/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import PlayQueueUpNextCollection from 'app/js/models/library/PlayQueueUpNextCollection';

const PlayQueueAfterUpNextCollection = PlayQueueUpNextCollection.extend({
  //
  // Init

  initialize: function (options: any) {
    this.playQueue = options.playQueue;

    this.addFilter(_.bind(this.filterAfterUpNext, this));
    this.listenForUpNextChanges();
  },

  //
  // Control

  filterAfterUpNext: function (playQueueItem: any) {
    const index = this.superset.indexOf(playQueueItem);
    const range = this.playQueue.getUpNextRange();

    return index > range.end;
  },
});

export default PlayQueueAfterUpNextCollection;
