import React from 'react';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import Modal from 'app/js/views/v3/components/core/modals/Modal';
import DVRSettingsModalContentConnector from './DVRSettingsModalContentConnector';

type DVRSettingsModalProps = {
  isOpen: boolean;
  metricsProperties: any;
  onModalClose: (...args: any[]) => any;
};

function DVRSettingsModal(props: DVRSettingsModalProps) {
  const { isOpen, metricsProperties, onModalClose, ...otherProps } = props;

  return (
    <Modal
      isOpen={isOpen}
      metricsPage={metricsPages.DVR_SETTINGS}
      metricsProperties={metricsProperties}
      onModalClose={onModalClose}
    >
      <DVRSettingsModalContentConnector
        onModalClose={onModalClose}
        {...otherProps}
      />
    </Modal>
  );
}

export default DVRSettingsModal;
