import React, { ReactElement, useCallback, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import serverFeatures from 'app/js/models/server/serverFeatures';
import {
  cancelReloadGuide,
  reloadGuide,
} from 'app/js/stores/actions/ui/dvrSettingsUIActions';
import * as metricsContexts from 'app/js/utils/constants/metrics/metricsContexts';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import isVersionOrNewer from 'app/js/utils/isVersionOrNewer';
import t from 'app/js/utils/t';
import createEPGLoadActivitySelector from 'app/js/v4/core/serverActivities/selectors/createEPGLoadActivitySelector';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import { useRedispatchLegacyServerAction } from 'app/js/v4/core/servers/hooks/useRedispatchLegacyServerAction';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import useDisclosure from 'app/js/v4/core/ui/hooks/useDisclosure';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import Kind from 'app/js/v4/core/ui/types/Kind';
import Size from 'app/js/v4/core/ui/types/Size';
import { DVR } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import DashSeparator from 'app/js/views/v3/components/core/base/DashSeparator';
import ConfirmModal from 'app/js/views/v3/components/core/modals/ConfirmModal';
import DVRSettingsModal from 'app/js/views/v3/components/livetv/dvr/DVRSettingsModal';
import IconReload from 'app/glyphs/reload-560.svg';
import IconRemove from 'app/glyphs/remove-560.svg';
import IconSettings from 'app/glyphs/settings-560.svg';
import styles from './LiveTVSettingsDVR.css';

const metricsProperties = {
  context: metricsContexts.SETTINGS,
};

interface LiveTVSettingsDVRProps {
  dvr: DVR;
  hasDVRFeature: boolean;
  hasLiveTVSetupFeature: boolean;
}

export default function LiveTVSettingsDVR({
  dvr,
  hasDVRFeature,
  hasLiveTVSetupFeature,
}: LiveTVSettingsDVRProps): ReactElement | null {
  const dispatch = useDispatch();
  const [server, provider] = useSourceContext();
  const removeDVRModalState = useDisclosure();
  const dvrSettingsModalState = useDisclosure();
  const selectEPGLoadActivity = useMemo(
    () => createEPGLoadActivitySelector(server, provider),
    [provider, server]
  );

  const epgLoadActivity = useSelector(selectEPGLoadActivity);
  const { isCancellingReloadGuide, isDeletingDVR } =
    useSelector(selectDVRSetup);

  const redispatchLegacyServerAction = useRedispatchLegacyServerAction({
    server,
    provider,
  });

  //
  // Callbacks

  const onRemoveDVRConfirm = useCallback(() => {
    dispatch(
      dvrSetupSlice.actions.dvrDeleteRequested({ server, dvrID: dvr.key })
    );

    removeDVRModalState.onClose();
  }, [dvr, server, dispatch, removeDVRModalState]);

  const onReloadGuidePress = useCallback(() => {
    redispatchLegacyServerAction(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
      reloadGuide({ dvrID: Number(dvr.key) })
    );
  }, [dvr, redispatchLegacyServerAction]);

  const onCancelReloadGuidePress = useCallback(() => {
    dispatch(dvrSetupSlice.actions.cancelReloadGuide());
    redispatchLegacyServerAction(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
      cancelReloadGuide({ dvrID: Number(dvr.key) })
    );
  }, [dispatch, redispatchLegacyServerAction, dvr]);

  //
  // Render

  const hasPrefs = !!dvr.Setting;
  const hasFreeLiveTVDVRSettings = isVersionOrNewer(
    server.version,
    serverFeatures.freeLiveTVDVRSettings
  );
  // DVR Settings button exposes XMLTV configuration setting that are
  // available to all Live TV users, even those without DVR capabilities.
  // Live TV & DVR users will hit `hasDVRFeature`. Free Live TV users will hit
  // `hasLiveTVSetupFeature` and `hasFreeLiveTVDVRSettings` if they have the
  // minimum server version.
  const hasDVRSettings =
    hasDVRFeature || (hasLiveTVSetupFeature && hasFreeLiveTVDVRSettings);
  const showDVRSettings = hasDVRSettings && hasPrefs;

  return (
    <div className={styles.container}>
      {showDVRSettings ? (
        <Link
          className={styles.action}
          kind={Kind.Primary}
          onPress={dvrSettingsModalState.onOpen}
        >
          <IconSettings className={styles.actionIcon} />
          {t('DVR Settings')}
        </Link>
      ) : null}

      {isCancellingReloadGuide ? (
        <div className={styles.spinnerContainer}>
          <Spinner className={styles.spinner} size={Size.Small} />
          {t('Cancelling guide refresh...')}
        </div>
      ) : null}

      {!isCancellingReloadGuide && epgLoadActivity ? (
        <Link
          className={styles.action}
          isDisabled={isDeletingDVR}
          kind={Kind.Default}
          title={t('Cancel refresh')}
          onPress={onCancelReloadGuidePress}
        >
          <IconRemove className={styles.actionIcon} />
          {epgLoadActivity.title} ({epgLoadActivity.progress}%)
          <DashSeparator className={styles.separator} />
          {epgLoadActivity.subtitle}
        </Link>
      ) : null}

      {!isCancellingReloadGuide && !epgLoadActivity ? (
        <Link
          className={styles.action}
          isDisabled={isDeletingDVR}
          kind={Kind.Primary}
          onPress={onReloadGuidePress}
        >
          <IconReload className={styles.actionIcon} />
          {t('Refresh Guide')}
        </Link>
      ) : null}

      {isDeletingDVR ? (
        <div className={styles.spinnerContainer}>
          <Spinner className={styles.spinner} size={Size.Small} />
          {t('Deleting DVR...')}
        </div>
      ) : (
        <Link kind={Kind.Primary} onPress={removeDVRModalState.onOpen}>
          {t('Delete DVR')}
        </Link>
      )}

      <ConfirmModal
        confirmLabel={t('Delete')}
        isOpen={removeDVRModalState.isOpen}
        kind={Kind.Danger}
        message={t(
          "Deleting your DVR will remove the Guide, and all scheduled recordings will be suspended. Programming you've previously recorded will remain in your library."
        )}
        metricsPage={metricsPages.CONFIRM_REMOVE_DVR}
        title={t('Delete DVR')}
        onCancel={removeDVRModalState.onClose}
        onConfirm={onRemoveDVRConfirm}
      />

      <DVRSettingsModal
        // @ts-expect-error ts-migrate(2322) FIXME: Type '{ isOpen: any; serverEntityID: string; dvrID... Remove this comment to see the full error message
        dvrID={Number(dvr.key)}
        isOpen={dvrSettingsModalState.isOpen}
        metricsProperties={metricsProperties}
        serverEntityID={server.entityID}
        onModalClose={dvrSettingsModalState.onClose}
      />
    </div>
  );
}
