/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import * as attributionIdentifiers from 'app/js/utils/constants/attributionIdentifiers';
import IconTidalLogo from 'app/glyphs/tidal-logo-560.svg';
import IconWatchTogether from 'app/glyphs/watch-together.svg';

const attributionIcons = {
  [attributionIdentifiers.TIDAL]: IconTidalLogo,
  [attributionIdentifiers.WATCH_TOGETHER]: IconWatchTogether,
};

interface AttributionBadgeProps {
  className?: string;
  attribution?: string;
}

function AttributionBadge(props: AttributionBadgeProps) {
  // @ts-expect-error ts-migrate(2538) FIXME: Type 'undefined' cannot be used as an index type.
  const Icon = attributionIcons[props.attribution];

  if (!Icon) {
    return null;
  }

  return (
    <div className={props.className}>
      <Icon />
    </div>
  );
}

export default AttributionBadge;
