import React, { ReactElement } from 'react';
import { MetadataItemResponse } from 'app/js/v4/core/metadata/metadataTypes';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import PosterCardImg from 'app/js/views/v3/components/core/images/PosterCardImg';
import styles from './TopPlayedColumnHeader.css';

interface TopPlayedColumnHeaderProps {
  title: string;
  isEmpty: boolean;
  metadataItem?: MetadataItemResponse;
}

export default function TopPlayedColumnHeader({
  title,
  isEmpty,
  metadataItem,
}: TopPlayedColumnHeaderProps): ReactElement {
  const [server] = useSourceContext();

  return (
    <div className={!isEmpty ? styles.tintedHeader : styles.header}>
      {!isEmpty && metadataItem ? (
        <div className={styles.imageContainer}>
          <PosterCardImg
            backgroundPosition="center top"
            height={100}
            serverEntityID={server.entityID}
            url={metadataItem.art || metadataItem.thumb}
            width={200}
          />
        </div>
      ) : null}

      <div
        className={
          isEmpty ? styles.emptyTitleBackground : styles.titleBackground
        }
      >
        <div className={styles.titleText}>{title}</div>
      </div>
    </div>
  );
}
