import React, { ChangeEvent, ReactElement, useCallback, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import Checkbox from 'app/js/v4/core/ui/forms/components/Checkbox';
import Kind from 'app/js/v4/core/ui/types/Kind';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import createDeviceChannelsSelector from 'app/js/v4/features/dvrManagement/selectors/createDeviceChannelsSelector';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import selectLineupChannels from 'app/js/v4/features/dvrManagement/selectors/selectLineupChannels';
import styles from './DeviceChannelMappingsHeader.css';

interface DeviceChannelMappingsHeaderProps {
  allChannelMappingsEnabled: boolean;
  allChannelMappingsDisabled: boolean;
  onAllChannelMappingsEnabledChange(event: ChangeEvent<HTMLInputElement>): void;
}

export default function DeviceChannelMappingsHeader({
  allChannelMappingsEnabled,
  allChannelMappingsDisabled,
  onAllChannelMappingsEnabledChange,
}: DeviceChannelMappingsHeaderProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const { selectedDevice, selectedDVR } = useSelector(selectDVRSetup);
  const selectDeviceChannels = useMemo(
    () => createDeviceChannelsSelector(selectedDevice?.key),
    [selectedDevice]
  );

  const lineupChannels = useSelector(selectLineupChannels);
  const { channels: deviceChannels } = useSelector(selectDeviceChannels);

  //
  // Callbacks

  const indeterminateSetter = useCallback(
    (element) => {
      if (element) {
        element.indeterminate =
          !allChannelMappingsEnabled && !allChannelMappingsDisabled;
      }
    },
    [allChannelMappingsEnabled, allChannelMappingsDisabled]
  );

  const onRescanPress = useCallback(() => {
    if (selectedDevice) {
      dispatch(
        dvrSetupSlice.actions.deviceChannelsScanRequested({
          server,
          deviceKey: selectedDevice.key,
          source: selectedDevice.source,
          country: selectedDVR?.dvr?.country,
        })
      );
    }
  }, [dispatch, server, selectedDevice, selectedDVR]);

  //
  // Render

  return (
    <div className={styles.container}>
      <div className={styles.device}>
        <Checkbox
          ref={indeterminateSetter}
          checked={allChannelMappingsEnabled}
          className={styles.checkbox}
          name="toggleAll"
          onChange={onAllChannelMappingsEnabledChange}
        />

        <div className={styles.titleContainer}>
          <div className={styles.title}>{t('Tuner Channels')}</div>
          <div className={styles.count}>
            {t('({count} Channels)', { count: deviceChannels.length })}
          </div>
          <Link
            className={styles.rescanButton}
            kind={Kind.Primary}
            onPress={onRescanPress}
          >
            {t('Rescan')}
          </Link>
        </div>
      </div>

      <div className={styles.lineup}>
        <div className={styles.titleContainer}>
          <div className={styles.title}>{t('EPG Channels')}</div>
          <div className={styles.count}>
            {t('({count} Channels)', { count: lineupChannels.length })}
          </div>
        </div>
      </div>
    </div>
  );
}
