/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import t from 'app/js/utils/t';
import TextInput from 'app/js/views/v3/components/core/forms/TextInput';
import { LARGE } from 'app/js/views/v3/helpers/props/sizes';
import styles from './ServerLogFilterInput.css';

type ServerLogFilterInputProps = {
  filter: string;
  onTextFilterChange: (...args: any[]) => any;
};

type ServerLogFilterInputState = any;

class ServerLogFilterInput extends Component<
  ServerLogFilterInputProps,
  ServerLogFilterInputState
> {
  // eslint-disable-next-line react/sort-comp
  _timeoutID: any;

  //
  // Static

  static getDerivedStateFromProps(nextProps: any, prevState: any) {
    // Respect external changes to the filter value, e.g. `clearFilter` action.
    if (nextProps.filter !== prevState.filterFromStore) {
      return {
        filter: nextProps.filter,
        filterFromStore: nextProps.filter,
      };
    }

    // No change
    return null;
  }

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      filter: props.filter,
      filterFromStore: props.filter,
    };
  }

  componentWillUnmount() {
    this._clearOnTextFilterChangeTimeout();
  }

  //
  // Control

  _clearOnTextFilterChangeTimeout() {
    if (this._timeoutID) {
      window.clearTimeout(this._timeoutID);
    }
  }

  //
  // Listeners

  onTextFilterChange = (filter: any) => {
    this._clearOnTextFilterChangeTimeout();

    this.setState({ filter }, () => {
      this._timeoutID = window.setTimeout(() => {
        this._timeoutID = null;
        this.props.onTextFilterChange(this.state.filter);
      }, 200);
    });
  };

  //
  // Render

  render() {
    const { filter } = this.state;

    return (
      <TextInput
        className={styles.input}
        initialValue={filter}
        name="filter"
        placeholder={t('Filter logs')}
        size={LARGE}
        value={filter}
        onChange={this.onTextFilterChange}
      />
    );
  }
}

export default ServerLogFilterInput;
