/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import _ from 'underscore';
import assert from 'app/js/utils/log/assert';
import isPlainObject from 'app/js/utils/object/isPlainObject';
import createChainedCallback from 'app/js/views/v3/helpers/createChainedCallback';
import getDisplayName from 'app/js/views/v3/helpers/getDisplayName';
import storeShape from 'app/js/views/v3/helpers/props/shapes/storeShape';
import shallowEqual from 'app/js/views/v3/helpers/shallowEqual';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';
import { actions } from './Form';

type FormInputProps<InputProps> = Omit<InputProps, 'onChange'> & {
  name: string;
  initialValue: number | string | boolean | any[] | object;
  errors: string[];
  validations: ((...args: any[]) => any)[];
  transformValue?: (...args: any[]) => any;
  onChange?: (...args: any[]) => any;
};

export default function formInput<InputProps>(WrappedComponent: any) {
  const displayName = `FormInput(${getDisplayName(WrappedComponent)})`;
  return class extends Component<FormInputProps<InputProps>> {
    static displayName = displayName;
    static contextTypes = {
      formStore: storeShape,
    };
    static defaultProps = {
      validations: [],
      errors: [],
    };
    //
    // Lifecycle
    constructor(props: any, context: any) {
      super(props, context);
      this.store = context.formStore;
      __DEV__ &&
        assert(
          !!this.store,
          `${displayName} has no formStore. Did you forget to make it a child of <Form>?`
        );
      this.actions = bindActionCreators(actions, this.store.dispatch);
      // @ts-expect-error ts-migrate(2322) FIXME: Type 'null' is not assignable to type 'Readonly<{}... Remove this comment to see the full error message
      this.state = null;
      this.hasBlurred = false;
    }
    // eslint-disable-next-line react/sort-comp
    UNSAFE_componentWillMount() {
      this.previousFormState = this.store.getState();
      this.unsubscribe = this.store.subscribe(this.onFormStoreChange);
      // Initialize the input state with the Form provider
      this.initializeInputState(this.props);
    }
    UNSAFE_componentWillReceiveProps(nextProps: any) {
      if (
        isPlainObject(nextProps.initialValue) &&
        isPlainObject(this.props.initialValue)
      ) {
        if (!shallowEqual(nextProps.initialValue, this.props.initialValue)) {
          this.handleStateChange(nextProps, nextProps.initialValue);
        }
      } else if (!_.isEqual(nextProps.initialValue, this.props.initialValue)) {
        this.handleStateChange(nextProps, nextProps.initialValue);
      }
    }
    shouldComponentUpdate(nextProps: any, nextState: any) {
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
      return shouldPureComponentUpdate(this, nextProps, nextState);
    }
    componentWillUnmount() {
      const { name } = this.props;
      if (this.unsubscribe) {
        this.unsubscribe();
      }
      // When a `formInput` is removed from the VDOM we no longer want its
      // value tracked in `Form`, so we clear out the form input state to let
      // `Form` clean it up.
      this.actions.change({
        name,
        change: null,
      });
    }
    static WrappedComponent = WrappedComponent;
    actions: any;
    hasBlurred: any;
    previousFormState: any;
    store: any;
    unsubscribe: any;
    //
    // Control
    initializeInputState(props: any) {
      const { name, initialValue } = props;
      const formState = this.store.getState();
      formState[name] = {
        errors: this.calculateErrors(initialValue, formState, props),
        showErrors: false,
        value: initialValue,
      };
      this.actions.replaceState(formState);
    }
    // `handleStateChange` is called when `onChange` is called or when
    // `formInput` receives an update from `formStore`. If `newValue` is set it will be used
    // in calculating the errors and compared to the previous value to determine if
    // a formState update is needed.
    handleStateChange(props: any, newValue: any) {
      const formState = this.store.getState();
      const { name } = props;
      const inputState = formState[name];
      const value =
        Array.isArray(newValue) || newValue != null
          ? newValue
          : inputState.value;
      const errors = this.calculateErrors(value, formState, props);
      if (
        !_.isEqual(inputState.value, value) ||
        !_.isEqual(inputState.errors, errors)
      ) {
        // If the value has changed, or the errors have changed,
        // call the `change` action to update the state.
        this.actions.change({
          name,
          change: {
            errors,
            showErrors: this.hasBlurred && errors.length !== 0,
            value,
          },
        });
      }
    }
    calculateErrors(value: any, formState: any, props: any) {
      const { validations } = props;
      const validationErrors = validations
        .map((fn: any) => fn(value, formState))
        .filter((error: any) => error != null);
      return validationErrors;
    }
    //
    // Listeners
    onFormStoreChange = () => {
      const { name } = this.props;
      const formState = this.store.getState();
      const inputState = formState[name];
      if (!inputState) {
        this.initializeInputState(this.props);
      }
      // If the formState has changed, trigger `handleStateChange` as
      // derived validations may have changed. Calling `handleStateChange` will
      // blow the stack if we don't do the shallowEqual check.
      if (!shallowEqual(formState, this.previousFormState)) {
        this.previousFormState = formState;
        // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
        this.handleStateChange(this.props);
      }
      if (this.state !== inputState && inputState != null) {
        this.setState(inputState);
      }
    };
    onChange = (value: any) => {
      const { transformValue } = this.props;
      this.handleStateChange(
        this.props,
        transformValue ? transformValue(value) : value
      );
    };
    onBlur = () => {
      if (this.hasBlurred) {
        return;
      }
      this.hasBlurred = true;
      const formState = this.store.getState();
      const { name } = this.props;
      const inputState = formState[name];
      const errors = this.calculateErrors(
        inputState.value,
        formState,
        this.props
      );
      this.actions.change({
        name,
        change: {
          ...inputState,
          errors,
          showErrors: errors.length !== 0,
        },
      });
    };
    //
    // Render
    render() {
      const {
        validations,
        errors: propsErrors,
        transformValue,
        onChange,
        ...otherProps
      } = this.props;
      // Prevent render when inputState is not set because this component
      // will set it's inputState on the `Form` `formStore` triggering a re-render.
      if (!this.state) {
        return null;
      }
      // If errors are passed in from outside `formInput` use them in place of
      // the calculated errors, and force the `showErrors` prop. Outside errors can
      // be from Server API errors or set from a parent component.
      const hasPropsErrors = propsErrors.length > 0;
      // @ts-expect-error ts-migrate(2339) FIXME: Property 'value' does not exist on type 'Readonly<... Remove this comment to see the full error message
      const { value, errors, showErrors } = this.state;
      return (
        <WrappedComponent
          {...otherProps}
          errors={hasPropsErrors ? propsErrors : errors}
          showErrors={hasPropsErrors ? true : showErrors}
          value={value}
          onBlur={this.onBlur}
          onChange={createChainedCallback(this.onChange, onChange)}
        />
      );
    }
  };
}
