/* eslint-plugin-disable @typescript-eslint */

import React, { useState } from 'react';
import { resolve } from 'app/js/adapters/adapters';
import DeprecatedPluginListModel from 'app/js/models/deprecatedPlugin/DeprecatedPluginListModel';
import t from 'app/js/utils/t';
import DeprecatedPluginsView from 'app/js/views/deprecatedPlugin/DeprecatedPluginsView';
import MarionetteComponent from 'app/js/views/v3/components/MarionetteComponent';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import EmptyPage from 'app/js/views/v3/components/pages/empty/EmptyPage';
import ErrorPage from 'app/js/views/v3/components/pages/empty/ErrorPage';
import useEffectOnce from 'app/js/views/v3/helpers/hooks/useEffectOnce';

type DeprecatedPluginsPageProps = {
  serverEntityID: string;
};

function DeprecatedPluginsPage(props: DeprecatedPluginsPageProps) {
  const { serverEntityID } = props;

  const [deprecatedPluginsState, setDeprecatedPluginsState] = useState({
    deprecatedPlugins: null,
    isDeprecatedPluginsPending: true,
    deprecatedPluginsError: false,
  });

  useEffectOnce(() => {
    const appModel = resolve('appModel');
    const server = appModel
      .get('entities')
      .get('servers')
      .get(serverEntityID)
      ?.get('model');
    const deprecatedPlugins = new DeprecatedPluginListModel({}, { server });

    deprecatedPlugins.fetch().then(
      () => {
        setDeprecatedPluginsState({
          deprecatedPlugins,
          isDeprecatedPluginsPending: false,
          deprecatedPluginsError: false,
        });
      },
      () => {
        setDeprecatedPluginsState({
          deprecatedPlugins: null,
          isDeprecatedPluginsPending: false,
          deprecatedPluginsError: true,
        });
      }
    );
  });

  if (deprecatedPluginsState.isDeprecatedPluginsPending) {
    return (
      <EmptyPage>
        <Spinner />
      </EmptyPage>
    );
  }

  if (deprecatedPluginsState.deprecatedPluginsError) {
    return <ErrorPage title={t('Plugins are not available.')} />;
  }

  return (
    <MarionetteComponent
      view={DeprecatedPluginsView}
      viewOptions={{
        deprecatedPlugins: deprecatedPluginsState.deprecatedPlugins,
      }}
    />
  );
}

export default DeprecatedPluginsPage;
