/* eslint-plugin-disable @typescript-eslint */

import createGridLayout, {
  VERTICAL,
} from 'app/js/views/v3/helpers/layouts/createGridLayout';

export default function createVerticalLayout(
  rowHeight = 40,
  overscan = 10,
  options: any
) {
  const verticalLayout = createGridLayout({
    direction: VERTICAL,
    columns: 1,
    overscan,

    getCellWidth: (viewportOptions: any) => viewportOptions.innerViewportWidth,
    getCellHeight: () => rowHeight,

    ...options,
  });

  return verticalLayout;
}
