/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import t from 'app/js/utils/t';
import LiveTVSettingsPage from 'app/js/v4/features/dvrManagement/components/settings/LiveTVSettingsPage';
import ManageLibrariesPane from 'app/js/v4/features/libraryManagement/components/ManageLibrariesPane';
import DeprecatedPluginsPage from 'app/js/views/v3/components/pages/deprecatedPlugins/DeprecatedPluginsPage';
import ServerLogPageConnector from 'app/js/views/v3/components/pages/serverSettings/panes/logs/ServerLogPageConnector';
import ServerTroubleshootingPaneConnector from 'app/js/views/v3/components/pages/serverSettings/panes/troubleshooting/ServerTroubleshootingPaneConnector';
import ActivityIcon from 'app/js/views/v3/components/sidebar/settings/ActivityIcon';
import RemoteAccessStateIcon from 'app/js/views/v3/components/sidebar/settings/RemoteAccessStateIcon';
import ServerIsClaimedIcon from 'app/js/views/v3/components/sidebar/settings/ServerIsClaimedIcon';

function isOwned(server: any) {
  return !server.isShared;
}

function hasSettings(server: any) {
  return !!server[__PLATFORM__].isSettingsPopulated;
}

function hasStreamingLogs(server: any) {
  const diagnostics = server.diagnostics;

  return !!(diagnostics && diagnostics.indexOf('streaminglogs') !== -1);
}

// @ts-expect-error ts-migrate(6133) FIXME: 'server' is declared but its value is never read.
function hasSessions(server: any, nowPlayingSessionsCount: any) {
  return nowPlayingSessionsCount > 0;
}

function hasPlugins(server: any) {
  return server.allowChannelAccess;
}

function hasConversions(server: any) {
  return server[__PLATFORM__].backgroundTranscodeJobs.length > 0;
}

function getRemoteAccessState(server: any) {
  const cloudAccount = server[__PLATFORM__].cloudAccount;
  return cloudAccount && cloudAccount.remoteAccessState;
}

const serverSettingsSections = [
  {
    key: 'status',
    title: () => t('Status'),
    isVisible: true,
    metricsPage: metricsPages.STATUS,
    metricsPanePrefix: '',
    groups: [
      {
        key: 'server-dashboard',
        title: () => t('Dashboard'),
        isVisible: isOwned,
        createStatusIndicator: (server: any, nowPlayingSessionsCount: any) => (
          <ActivityIcon
            isActive={hasSessions(server, nowPlayingSessionsCount)}
          />
        ),
      },
      {
        key: 'alerts',
        title: () => t('Alerts'),
        isVisible: isOwned,
      },
      {
        key: 'conversion',
        title: () => t('Conversions'),
        isVisible: isOwned,
        createStatusIndicator: (server: any) => (
          <ActivityIcon isActive={hasConversions(server)} />
        ),
      },
    ],
  },
  {
    key: 'settings',
    title: () => t('Settings'),
    isVisible: isOwned,
    metricsPage: metricsPages.SETTINGS,
    metricsPanePrefix: 'server:',
    groups: [
      {
        key: 'general',
        title: () => t('General'),
        isVisible: hasSettings,
        createStatusIndicator: (server: any) => (
          <ServerIsClaimedIcon isClaimed={!!server.isClaimed} />
        ),
      },
      {
        key: 'remoteAccess',
        title: () => t('Remote Access'),
        isVisible: (server: any) => hasSettings(server),
        createStatusIndicator: (server: any) => (
          <RemoteAccessStateIcon
            remoteAccessState={getRemoteAccessState(server)}
          />
        ),
      },
      {
        key: 'agents',
        title: () => t('Agents'),
        isVisible: hasSettings,
      },
      {
        key: 'library',
        title: () => t('Library'),
      },
      {
        key: 'channels',
        title: () => t('Plugins'),
        isVisible: hasSettings,
      },
      {
        key: 'network',
        title: () => t('Network'),
      },
      {
        key: 'transcoder',
        title: () => t('Transcoder'),
      },
      {
        key: 'languages',
        title: () => t('Languages'),
        isVisible: hasSettings,
      },
      {
        key: 'dlna',
        title: () => t('DLNA'),
      },
      {
        key: 'butler',
        title: () => t('Scheduled Tasks'),
      },
      {
        key: 'extras',
        title: () => t('Extras'),
      },
    ],
  },
  {
    key: 'manage',
    title: () => t('Manage'),
    isVisible: true,
    groups: [
      {
        key: 'libraries',
        title: () => t('Libraries'),
        isVisible: isOwned,
        metricsPage: metricsPages.SETTINGS,
        metricsPane: 'server:libraries',
        paneComponent: ManageLibrariesPane,
      },
      {
        key: 'versions',
        title: () => t('Optimized Versions'),
        isVisible: (server: any) => {
          return isOwned(server) && hasSettings(server);
        },
        metricsPage: metricsPages.STATUS,
        metricsPane: 'versions',
      },
      {
        key: 'dvr',
        title: () => t('Live TV & DVR'),
        isVisible: (server: any) =>
          isOwned(server) && server.livetv >= 1 && hasSettings(server),
        metricsPage: metricsPages.SETTINGS,
        metricsPane: 'server:dvr',
        paneComponent: LiveTVSettingsPage,
      },
      {
        key: 'help',
        title: () => t('Troubleshooting'),
        isVisible: (server: any) => isOwned(server) && hasSettings(server),
        metricsPage: metricsPages.SETTINGS,
        metricsPane: 'server:help',
        paneComponent: ServerTroubleshootingPaneConnector,
      },
      {
        key: 'console',
        title: () => t('Console'),
        isVisible: (server: any) => isOwned(server) && hasStreamingLogs(server),
        metricsPage: metricsPages.SETTINGS,
        metricsPane: 'server:console',
        pageComponent: ServerLogPageConnector,
      },
      {
        key: 'plugins',
        title: () => t('Plugins'),
        isVisible: hasPlugins,
        metricsPage: metricsPages.SETTINGS,
        metricsPane: 'server:plugins',
        paneComponent: DeprecatedPluginsPage,
      },
    ],
  },
];

export default serverSettingsSections;
