import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import ServerSettingCollection from 'app/js/models/server/ServerSettingCollection';
import dispatcher from 'app/js/utils/dispatcher';
import AgentInfoModal from 'app/js/views/modals/AgentInfoModal';
import DeprecatedPluginSettingsModal from 'app/js/views/modals/DeprecatedPluginSettingsModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/lists... Remove this comment to see the full error message
import tpl from 'app/templates/lists/settings/ContributorListItem.tpl';

const ContributorListItem = Marionette.ItemView.extend({
  //
  // Properties

  tagName: 'li',
  className: 'card card-sortable',
  template: tpl,

  events: {
    'change input[type=checkbox]': 'onEnabledChange',
    'click .settings-btn': 'onSettingsClick',
    'click .info-btn': 'onInfoClick',
  },

  //
  // Init

  initialize: function () {
    this.attribution = this.model.get('attribution');
    this.attribution.fetch();
  },

  //
  // Bindings

  bindings: {
    '.info-btn': {
      model: 'attribution',
      modelAttribute: 'content',
      manual: true,
      autohide: true,
    },

    '.settings-btn': {
      modelAttribute: 'hasPrefs',
      manual: true,
      autohide: true,
    },
  },

  //
  // Listeners

  onEnabledChange: function (event: any) {
    this.model.set('enabled', event.target.checked);
  },

  onSettingsClick: function (event: any) {
    event.preventDefault();

    const settings = new ServerSettingCollection([], {
      server: this.model.server,
      url: '/:/plugins/' + this.model.id + '/prefs',
    });

    settings.fetch().done(
      _.bind(function () {
        dispatcher.trigger(
          'show:modal',
          new DeprecatedPluginSettingsModal({
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            model: this.model,
            collection: settings,
          })
        );
      }, this)
    );
  },

  onInfoClick: function (event: any) {
    event.preventDefault();

    dispatcher.trigger('show:modal', new AgentInfoModal({ model: this.model }));
  },
});

export default ContributorListItem;
