import React, { ReactElement, useCallback, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import { ServerPlatform } from 'app/js/v4/core/servers/serversTypes';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import SpinnerButton from 'app/js/v4/core/ui/buttons/components/SpinnerButton';
import FormLabel from 'app/js/v4/core/ui/forms/components/FormLabel';
import TextInput from 'app/js/v4/core/ui/forms/components/TextInput';
import ModalBody from 'app/js/v4/core/ui/modals/components/ModalBody';
import ModalFooter from 'app/js/v4/core/ui/modals/components/ModalFooter';
import ModalFooterButtonBar from 'app/js/v4/core/ui/modals/components/ModalFooterButtonBar';
import Kind from 'app/js/v4/core/ui/types/Kind';
import {
  DVRSetupStep,
  DVRSetupStepProps,
} from 'app/js/v4/features/dvrManagement/components/setup/DVRSetupModal';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import selectSelectedDVRLineups from 'app/js/v4/features/dvrManagement/selectors/selectSelectedDVRLineups';
import selectSetupSelectedLineups from 'app/js/v4/features/dvrManagement/selectors/selectSetupSelectedLineups';
import FormGroup from 'app/js/views/v3/components/core/forms/FormGroup';
import DVRSetupLanguageSelect from './DVRSetupLanguageSelect';
import styles from './DVRSetupUseXMLTV.css';

interface FormData {
  xmltvPath: string;
  programGuideTitle: string;
}

export default function DVRSetupUseXMLTV({
  onSetStep,
}: DVRSetupStepProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const { selectedDevice } = useSelector(selectDVRSetup);
  const {
    selectedLineupGroups,
    languages: { isPopulated: isLanguagesPopulated },
    channelMappings: {
      isPending: isChannelMappingsPending,
      error: channelMappingsError,
    },
    selectedLanguage,
  } = useSelector(selectEPGSettings);
  const selectedSetupLineups = useSelector(selectSetupSelectedLineups);
  const selectedDVRLineups = useSelector(selectSelectedDVRLineups);
  const [isAddingLineup, setIsAddingLineup] = useState(false);
  const canSwitchLineupProvider =
    !selectedDVRLineups?.length && !selectedSetupLineups?.length;
  const canUseXMLTV =
    !selectedDVRLineups ||
    selectedDVRLineups?.find(({ uuid }): boolean =>
      uuid.includes(providerIdentifiers.XMLTV)
    );
  const hasLanguage = !!selectedLanguage;

  const isEverySelectedLineupChannelMappingsFetched =
    selectedLineupGroups.every(
      (selectedLineupGroup) => selectedLineupGroup.isChannelMappingsFetched
    );

  const { register, errors, handleSubmit } = useForm<FormData>({
    defaultValues: {
      programGuideTitle: t('My Guide'),
    },
  });

  //
  // Callbacks

  const onBackPress = useCallback(() => {
    onSetStep(
      selectedLineupGroups.length > 0
        ? DVRSetupStep.Mapping
        : DVRSetupStep.Device
    );
  }, [selectedLineupGroups.length, onSetStep]);

  const onContinuePress = handleSubmit(
    ({ xmltvPath, programGuideTitle }: FormData) => {
      dispatch(dvrSetupSlice.actions.setupErrorMessageRemoved());

      dispatch(
        epgSettingsSlice.actions.xmltvSelectedEPGLineupGroupAdded({
          path: xmltvPath,
          title: programGuideTitle,
        })
      );
    }
  );

  const onUseLocationPress = useCallback(() => {
    onSetStep(DVRSetupStep.AddLineup);
  }, [onSetStep]);

  //
  // Effects

  useEffect(() => {
    setIsAddingLineup(isChannelMappingsPending);
  }, [isChannelMappingsPending, setIsAddingLineup]);

  useEffect(() => {
    if (
      isAddingLineup &&
      channelMappingsError &&
      selectedLineupGroups.length > 0
    ) {
      // When channel mappings fail, remove the synthetic lineup group added
      // when this form was submitted. Doing so prevents populating the lineup
      // group list with faulty items.
      const latestLineupGroup =
        selectedLineupGroups[selectedLineupGroups.length - 1];

      scheduleImmediateUpdate(() => {
        dispatch(
          epgSettingsSlice.actions.selectedEPGLineupGroupRemoved({
            lineupGroupUUID: latestLineupGroup.uuid,
          })
        );
      });
    }
  }, [
    dispatch,
    channelMappingsError,
    selectedLineupGroups,
    isAddingLineup,
    setIsAddingLineup,
  ]);

  useEffect(() => {
    if (
      selectedDevice &&
      selectedSetupLineups &&
      !isAddingLineup &&
      !isEverySelectedLineupChannelMappingsFetched
    ) {
      dispatch(
        epgSettingsSlice.actions.channelMappingsFetchRequested({
          server,
          device: selectedDevice,
          lineups: selectedSetupLineups,
        })
      );
    }
  }, [
    dispatch,
    selectedDevice,
    selectedSetupLineups,
    isAddingLineup,
    isEverySelectedLineupChannelMappingsFetched,
    server,
  ]);

  useEffect(() => {
    if (!isLanguagesPopulated) {
      dispatch(
        epgSettingsSlice.actions.epgLanguagesFetchRequested({
          server,
        })
      );
    }
  }, [dispatch, server, isLanguagesPopulated]);

  useEffect(() => {
    if (!isChannelMappingsPending && !channelMappingsError && isAddingLineup) {
      onSetStep(DVRSetupStep.Mapping);
    }
  }, [
    channelMappingsError,
    isChannelMappingsPending,
    isAddingLineup,
    onSetStep,
  ]);

  useEffect(() => {
    if (!selectedDevice) {
      onSetStep(DVRSetupStep.Device);
    }
  }, [selectedDevice, onSetStep]);

  //
  // Render

  const xmltvPathPlaceholder =
    server.platform === ServerPlatform.Windows
      ? 'C:\\path\\to\\your\\xmltv_file.xml'
      : '/path/to/your/xmltv_file.xml';

  return (
    <>
      <ModalBody innerClassName={styles.innerModalBody}>
        {canUseXMLTV ? (
          <div className={styles.form}>
            <p className={styles.title}>{t('Use an XMLTV guide')}</p>
            <p>
              {!canSwitchLineupProvider
                ? t(
                    'Please specify a location to an XMLTV service to provide your guide data.'
                  )
                : t(
                    "If you are located somewhere that isn't covered by Plex DVR guide data, you can specify a location to an XMLTV service to provide your guide data."
                  )}
            </p>

            <FormGroup>
              <DVRSetupLanguageSelect />
            </FormGroup>

            <FormGroup>
              <FormLabel name="xmltvPath">{t('XMLTV Guide')}</FormLabel>
              <TextInput
                ref={register({ required: true })}
                hasError={!!errors.xmltvPath}
                name="xmltvPath"
                placeholder={xmltvPathPlaceholder}
              />
            </FormGroup>

            <FormGroup>
              <FormLabel name="programGuideTitle">{t('Guide Title')}</FormLabel>
              <TextInput
                ref={register({ required: true })}
                hasError={!!errors.programGuideTitle}
                name="programGuideTitle"
              />
            </FormGroup>
          </div>
        ) : (
          <div className={styles.form}>
            <p className={styles.title}>{t('Unsupported location')}</p>
            <p>
              {t(
                "This location isn't covered by Plex DVR guide data. Because you already set up a tuner using Plex DVR guide data, we are unable to offer the option to use a XMLTV guide. Please select a different location."
              )}
            </p>
          </div>
        )}

        {canSwitchLineupProvider ? (
          <Link kind={Kind.Primary} onPress={onUseLocationPress}>
            {t("Don't use XMLTV? Click here to select your location instead.")}
          </Link>
        ) : null}
      </ModalBody>

      <ModalFooter>
        <ModalFooterButtonBar className={styles.buttonBar}>
          <Button onPress={onBackPress}>{t('Back')}</Button>
          <SpinnerButton
            isDisabled={!canUseXMLTV || !hasLanguage}
            isSpinning={isChannelMappingsPending}
            kind={Kind.Primary}
            onPress={onContinuePress}
          >
            {t('Continue')}
          </SpinnerButton>
        </ModalFooterButtonBar>
      </ModalFooter>
    </>
  );
}
