/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import DashSeparator from 'app/js/views/v3/components/core/base/DashSeparator';
import DVROutdatedServerBannerConnector from 'app/js/views/v3/components/livetv/dvr/DVROutdatedServerBannerConnector';
import SettingsPageHeader from 'app/js/views/v3/components/pages/settings/SettingsPageHeader';
import getServerDetail from 'app/js/views/v3/helpers/getServerDetail';
import getServerTitle from 'app/js/views/v3/helpers/getServerTitle';

type ServerSettingsPageHeaderProps = {
  serverEntityID: string;
  friendlyName: string;
  sourceTitle?: string;
  settingsTitle: string;
  settingsGroup: string;
  settingsSection: string;
  isShared: boolean;
};

function ServerSettingsPageHeader(props: ServerSettingsPageHeaderProps) {
  const {
    serverEntityID,
    friendlyName,
    sourceTitle,
    settingsTitle,
    settingsGroup,
    settingsSection,
    isShared,
  } = props;

  const shouldRenderDVROutdatedServerBanner =
    settingsSection === 'manage' && settingsGroup === 'dvr';
  const serverTitleAttrs = { friendlyName, sourceTitle, isShared };
  const title = getServerTitle(serverTitleAttrs);
  const detail = getServerDetail(serverTitleAttrs);

  const serverTitle = title && detail ? `${title} (${detail})` : title;

  return (
    <SettingsPageHeader>
      {shouldRenderDVROutdatedServerBanner ? (
        <DVROutdatedServerBannerConnector serverEntityID={serverEntityID} />
      ) : null}
      {serverTitle}
      <DashSeparator />
      {settingsTitle}
    </SettingsPageHeader>
  );
}

export default ServerSettingsPageHeader;
