import BaseImage from 'app/js/views/images/BaseImage';
import BaseModal from 'app/js/views/modals/BaseModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/AgentInfoModal.tpl';

const AgentInfoModal = BaseModal.extend({
  //
  // Properties

  name: 'agentInfoModal',
  className: 'agent-info-modal modal fade',
  template: tpl,

  regions: {
    icon: '.agent-icon-container',
  },

  //
  // Control

  serializeData: function () {
    return {
      agent: this.model.toJSON(),
      attribution: this.model.get('attribution').get('content'),
    };
  },

  //
  // Listeners

  onRender: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.onRender.apply(this, arguments);

    this.icon.show(
      new BaseImage({
        model: this.model,
        url: '/system/agents/attribution/image?identifier=' + this.model.id,
        className: 'agent-icon',
        width: 100,
        height: 100,
      })
    );
  },
});

export default AgentInfoModal;
