import React, { ReactElement, useCallback, useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import t from 'app/js/utils/t';
import createChannelScanActivitySelector from 'app/js/v4/core/serverActivities/selectors/createChannelScanActivitySelector';
import createEPGLineupScanActivitySelector from 'app/js/v4/core/serverActivities/selectors/createEPGLineupScanActivitySelector';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import IconButton from 'app/js/v4/core/ui/buttons/components/IconButton';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import Kind from 'app/js/v4/core/ui/types/Kind';
import {
  DVRSetupStep,
  DVRSetupStepProps,
} from 'app/js/v4/features/dvrManagement/components/setup/DVRSetupModal';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import createChannelMappingsSelector from 'app/js/v4/features/dvrManagement/selectors/createChannelMappingsSelector';
import createDeviceChannelsSelector from 'app/js/v4/features/dvrManagement/selectors/createDeviceChannelsSelector';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import selectLineupChannels from 'app/js/v4/features/dvrManagement/selectors/selectLineupChannels';
import selectSetupSelectedLineups from 'app/js/v4/features/dvrManagement/selectors/selectSetupSelectedLineups';
import getMatchedChannelsPerLineupUUID, {
  MatchedChannelsPerLineupUUID,
} from 'app/js/v4/features/dvrManagement/utils/getMatchedChannelsPerLineupUUID';
import IconRemove from 'app/glyphs/remove-560.svg';
import EPGLineupSelect from './EPGLineupSelect';
import styles from './EPGLineups.css';

interface DeleteButtonProps {
  lineupGroupUUID: string;
  isDisabled: boolean;
  onDeletePress(lineupGroupUUID: string): void;
}

function DeleteButton({
  lineupGroupUUID,
  isDisabled,
  onDeletePress,
}: DeleteButtonProps): ReactElement {
  const onPress = useCallback(() => {
    onDeletePress(lineupGroupUUID);
  }, [lineupGroupUUID, onDeletePress]);

  return (
    <IconButton
      ariaLabel={t('Delete Guide')}
      className={styles.deleteButton}
      iconComponent={IconRemove}
      isDisabled={isDisabled}
      kind={Kind.Danger}
      onPress={onPress}
    />
  );
}

interface EPGLineupsProps {
  onSetStep: DVRSetupStepProps['onSetStep'];
}

const EPGLineups = React.forwardRef<HTMLDivElement, EPGLineupsProps>(
  ({ onSetStep }, forwardedRef) => {
    const dispatch = useDispatch();
    const [server] = useSourceContext();
    const {
      selectedLineupGroups,
      channelMappings: { isPending: isChannelMappingsPending },
    } = useSelector(selectEPGSettings);
    const { hasScannedChannels, selectedDevice } = useSelector(selectDVRSetup);
    const lineupChannels = useSelector(selectLineupChannels);
    const selectedLineups = useSelector(selectSetupSelectedLineups);
    const selectChannelMappings = useMemo(
      () => createChannelMappingsSelector(selectedDevice?.key),
      [selectedDevice]
    );
    const selectChannelScanActivity = useMemo(
      () => createChannelScanActivitySelector(server),
      [server]
    );
    const selectEPGLineupScanActivity = useMemo(
      () => createEPGLineupScanActivitySelector(server),
      [server]
    );
    const selectDeviceChannels = useMemo(
      () => createDeviceChannelsSelector(selectedDevice?.key),
      [selectedDevice]
    );
    const epgLineupScanActivity = useSelector(selectEPGLineupScanActivity);
    const channelScanActivity = useSelector(selectChannelScanActivity);
    const { isPending: isDeviceChannelsPending } =
      useSelector(selectDeviceChannels);
    const channelMappings = useSelector(selectChannelMappings);

    const matchedChannelsPerLineupUUID: MatchedChannelsPerLineupUUID = useMemo(
      () =>
        getMatchedChannelsPerLineupUUID(
          channelMappings,
          lineupChannels,
          selectedLineups
        ),
      [channelMappings, lineupChannels, selectedLineups]
    );

    //
    // Callbacks

    const onAddLineupPress = useCallback(() => {
      // PMS currently enforces that all lineups must be from the same provider.
      // If the user previously added an XMLTV guide, they can only add more
      // guides from that same provider.
      const hasXMLTVLineup = selectedLineups?.find(({ uuid }) =>
        uuid.includes(providerIdentifiers.XMLTV)
      );

      onSetStep(hasXMLTVLineup ? DVRSetupStep.XMLTV : DVRSetupStep.AddLineup);
    }, [selectedLineups, onSetStep]);

    const onRemoveLineupGroupPress = useCallback(
      (lineupGroupUUID: string) => {
        dispatch(
          epgSettingsSlice.actions.selectedEPGLineupGroupRemoved({
            lineupGroupUUID,
          })
        );
      },
      [dispatch]
    );

    //
    // Effects

    useEffect(() => {
      const lineupGroup = selectedLineupGroups[0];

      if (
        !hasScannedChannels ||
        !selectedDevice ||
        !selectedLineups?.length ||
        lineupGroup?.activityUUID ||
        lineupGroup?.isSelectedLineupPopulated
      ) {
        return;
      }

      dispatch(
        epgSettingsSlice.actions.bestEPGLineupSearchRequested({
          server,
          device: selectedDevice,
          lineupGroup,
        })
      );
    }, [
      dispatch,
      selectedDevice,
      selectedLineups,
      selectedLineupGroups,
      hasScannedChannels,
      server,
    ]);

    //
    // Render

    if (epgLineupScanActivity) {
      return null;
    }

    return (
      <div ref={forwardedRef} className={styles.container}>
        <div className={styles.label}>{t('EPG Location')}</div>
        <div className={styles.lineups}>
          {selectedLineupGroups.map((lineupGroup, index) => {
            const { selectedLineup, uuid } = lineupGroup;

            return (
              <div key={index} className={styles.lineup}>
                <div className={styles.lineupSelect}>
                  <EPGLineupSelect
                    isDisabled={!!channelScanActivity}
                    lineupGroup={lineupGroup}
                  />
                  {selectedLineupGroups.length > 1 ? (
                    <DeleteButton
                      isDisabled={!!channelScanActivity}
                      lineupGroupUUID={uuid}
                      onDeletePress={onRemoveLineupGroupPress}
                    />
                  ) : null}
                </div>

                {selectedLineup &&
                !isChannelMappingsPending &&
                !isDeviceChannelsPending &&
                !channelScanActivity
                  ? t('{count} channels matched', {
                      count: matchedChannelsPerLineupUUID[selectedLineup.uuid],
                    })
                  : null}
              </div>
            );
          })}

          <Link
            className={styles.addLineup}
            isDisabled={!!channelScanActivity}
            onPress={onAddLineupPress}
          >
            <span className={styles.addLineupCircle}>+</span>
            {t('Add channels from another lineup/location')}
          </Link>
        </div>
      </div>
    );
  }
);

export default EPGLineups;
