/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import getSelectedVideo from 'app/js/utils/media/getSelectedVideo';
import formatResolution from 'app/js/utils/metadata/formatResolution';
import t from 'app/js/utils/t';

interface VideoStreamTitleProps {
  separatorComponent: React.ReactElement;
  mediaItem?: {
    videoResolution?: string;
    videoCodec?: string;
    Part?: any[];
  };
}

function VideoStreamTitle(props: VideoStreamTitleProps) {
  const { mediaItem, separatorComponent: Separator } = props;

  let details;

  // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
  if (mediaItem.videoCodec) {
    // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
    details = [mediaItem.videoCodec.toUpperCase()];
  }

  const videoStream = getSelectedVideo(mediaItem);
  const scanType = videoStream && videoStream.scanType;
  const label =
    // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
    formatResolution(mediaItem.videoResolution, scanType) || t('Unknown');

  const finalDetails = details && details.length > 0 ? details.join(' ') : null;

  const ariaLabel = finalDetails
    ? t('{label}, {details}', { label, details: finalDetails })
    : label;

  return (
    <span aria-label={ariaLabel} data-testid="videoStream">
      {label}
      {/* @ts-expect-error ts-migrate(2604) FIXME: JSX element type 'Separator' does not have any con... Remove this comment to see the full error message */}
      {finalDetails ? <Separator /> : null}
      {finalDetails}
    </span>
  );
}

export default VideoStreamTitle;
