/* eslint-plugin-disable @typescript-eslint */

import commands from 'app/js/commands/commands';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import ServerSettingCollection from 'app/js/models/server/ServerSettingCollection';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import MediaPosterImage from 'app/js/views/images/MediaPosterImage';
import DeprecatedPluginSettingsModal from 'app/js/views/modals/DeprecatedPluginSettingsModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/depre... Remove this comment to see the full error message
import tpl from 'app/templates/deprecatedPlugin/DeprecatedPluginTileListItem.tpl';

const DeprecatedPluginTileListItem = Marionette.ItemView.extend({
  //
  // Properties

  tagName: 'li',
  className: 'poster-item media-tile-list-item channel',
  template: tpl,

  ui: {
    poster: '.media-poster',
  },

  events: {
    'click .settings-btn': 'onSettingsClick',
    'click .uninstall-btn': 'onUninstallClick',
  },

  //
  // Init

  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  initialize: function (options: any) {
    this.image = new MediaPosterImage({
      model: this.model,
      type: 'channel',
      width: 150,
    });

    this.shared = this.model.server.get('shared');
  },

  //
  // Control

  serializeData: function () {
    return {
      item: this.model.toJSON(),
      shared: this.shared,
    };
  },

  //
  // Bindings

  bindings: {
    '.media-title': {
      modelAttribute: 'title',
    },

    '.media-subtitle-1': {
      modelAttribute: 'type',
      autohide: true,

      formatter: (type: any) => {
        if (type === 'video') {
          return t('Video');
        } else if (type === 'music') {
          return t('Music');
        } else if (type === 'photos') {
          return t('Photo');
        }

        return t('Utility');
      },
    },
  },

  //
  // Listeners

  onRender: function () {
    this.image.setElement(this.ui.poster);
  },

  onClose: function () {
    // Manually close the poster image because we aren't actually showing it
    // so it won't be cleaned up automatically.
    this.image.close();
  },

  onSettingsClick: function (event: any) {
    event.preventDefault();

    if (this.$el.hasClass('active')) {
      return;
    }

    this.$el.addClass('active');

    const settings = new ServerSettingCollection([], {
      server: this.model.server,
      url: this.model.id + '/:/prefs',
    });

    const promise = settings.fetch();

    promise.done(() => {
      dispatcher.trigger(
        'show:modal',
        new DeprecatedPluginSettingsModal({
          model: this.model,
          collection: settings,
        })
      );
    });

    promise.always(() => {
      this.$el.removeClass('active');
    });
  },

  onUninstallClick: function (event: any) {
    event.preventDefault();

    if (this.$el.hasClass('active')) {
      return;
    }

    this.$el.addClass('active');

    const promise = commands.execute('uninstallChannel', {
      channel: this.model,
    });

    promise.done(() => {
      // Destroy the channel so it is removed from the channels list.
      this.model.trigger('destroy', this.model, this.model.collection);
    });

    promise.always(() => {
      this.$el.removeClass('active');
    });
  },
});

export default DeprecatedPluginTileListItem;
