/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';
import BaseModal from 'app/js/views/modals/BaseModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/depre... Remove this comment to see the full error message
import tpl from 'app/templates/deprecatedPlugin/DeprecatedPluginSettingsModal.tpl';

const DeprecatedPluginSettingsModal = BaseModal.extend({
  //
  // Properties

  name: 'DeprecatedPluginSettingsModal',
  className: 'channel-settings-modal modal fade',
  template: tpl,

  ui: {
    submitBtn: '.submit-btn',
  },

  events: _.extend(
    {
      'change input[name]': 'onInputChange',
      'change select[name]': 'onInputChange',
      'submit #channel-settings-form': 'onSubmit',
    },
    BaseModal.prototype.events
  ),

  //
  // Init

  initialize: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.initialize.apply(this, arguments);

    this.changes = {};
  },

  //
  // Control

  serializeData: function () {
    const title = this.model.get('title') || this.model.get('name');

    return {
      escapedTitle: _.escape(title ? title : 'Preferences...'),
      settings: this.collection.toJSON(),
    };
  },

  //
  // Listeners

  onInputChange: function (event: any) {
    const target = event.currentTarget;
    let value = target.value;

    if (target.type === 'checkbox') {
      value = target.checked;
    }

    this.changes[target.name] = value;
  },

  onSubmit: function (event: any) {
    event.preventDefault();

    // If no changes were made, just close the modal.
    if (_.isEmpty(this.changes)) {
      this.hide();
      return;
    }

    const submitBtn = this.ui.submitBtn;

    if (submitBtn.hasClass('active')) {
      return;
    }

    submitBtn.addClass('active');

    const setting = this.collection.first();
    const params = setting.getSaveParams(this.changes);
    const urlRoot = _.result(this.collection, 'url');
    const url = path.join([urlRoot, '/set'], params);

    const promise = $.ajax(setting.server.getXHROptions(url));

    promise.done(() => {
      this.hide();
    });

    promise.fail(() => {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem saving these settings.'),
      });
    });

    promise.always(() => {
      submitBtn.removeClass('active');
    });
  },
});

export default DeprecatedPluginSettingsModal;
