/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React from 'react';
import DisclosureArrow from 'app/js/views/v3/components/core/base/DisclosureArrow';
import Spinner from 'app/js/views/v3/components/core/base/Spinner';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import * as directions from 'app/js/views/v3/helpers/props/directions';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import styles from './SidebarMenuButton.css';

interface SidebarMenuButtonProps {
  className?: string;
  secondaryTitleClassName?: string;
  id?: string;
  isMenuOpen?: boolean;
  isLoading?: boolean;
  isSelected?: boolean;
  isDisabled?: boolean;
  primaryTitle: string;
  secondaryTitle?: string;
  children?: React.ReactNode;
  spinnerComponent?: React.ReactElement;
  onPress?: (...args: any[]) => any;
}

function SidebarMenuButton(props: SidebarMenuButtonProps) {
  const {
    className,
    secondaryTitleClassName,
    id,
    isLoading,
    isMenuOpen,
    isDisabled,
    isSelected,
    primaryTitle,
    secondaryTitle,
    children,
    spinnerComponent: CustomSpinner,
    onPress,
  } = props;

  return (
    <Link
      aria-haspopup={!isDisabled}
      className={classNames(className, isDisabled && styles.isDisabled)}
      data-testid="serverMenuButton"
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: Element; id: string | undefined;... Remove this comment to see the full error message
      id={id}
      isDisabled={isDisabled}
      isSelected={isSelected}
      onPress={onPress}
    >
      <div className={styles.sidebarMenuButtonContainer}>
        <span className={styles.sidebarMenuTitle}>
          <span className={styles.sidebarMenuPrimaryTitleContainer}>
            <span className={styles.sidebarMenuPrimaryTitle}>
              {primaryTitle}
            </span>

            {!isDisabled ? (
              <DisclosureArrow
                direction={isMenuOpen ? directions.UP : directions.DOWN}
                isSelected={isSelected}
              />
            ) : null}
          </span>
          {secondaryTitle ? (
            <span className={secondaryTitleClassName}>{secondaryTitle}</span>
          ) : null}
        </span>
        {isLoading ? (
          // @ts-expect-error ts-migrate(2604) FIXME: JSX element type 'CustomSpinner' does not have any... Remove this comment to see the full error message
          <CustomSpinner
            className={styles.sidebarMenuSpinner}
            size={sizes.SMALL}
          />
        ) : (
          children
        )}
      </div>
    </Link>
  );
}

SidebarMenuButton.defaultProps = {
  className: styles.sidebarMenuButton,
  secondaryTitleClassName: styles.sidebarMenuSecondaryTitle,
  isMenuOpen: false,
  isSelected: true,
  isDisabled: false,
  isLoading: false,
  spinnerComponent: Spinner,
};

export default SidebarMenuButton;
