/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import CheckInput from './CheckInput';
import FormGroup from './FormGroup';
import FormHelp from './FormHelp';
import FormLabel from './FormLabel';
import formInput from './formInput';

interface CheckInputGroupProps {
  formGroupClassName?: string;
  labelText?: React.ReactNode;
  checkboxText: React.ReactNode;
  helpText?: string;
  showErrors?: boolean;
  errors?: string[];
}

function CheckInputGroup({
  formGroupClassName,
  labelText,
  checkboxText,
  helpText,
  showErrors,
  errors,
  ...otherProps
}: CheckInputGroupProps) {
  // @ts-expect-error ts-migrate(2488) FIXME: Type 'string[] | undefined' must have a '[Symbol.i... Remove this comment to see the full error message
  const [firstError] = errors;

  return (
    <FormGroup className={formGroupClassName}>
      {labelText ? <FormLabel>{labelText}</FormLabel> : null}
      {/* @ts-expect-error ts-migrate(2739) FIXME: Type '{ hasError: boolean | undefined; checkboxTex... Remove this comment to see the full error message */}
      <CheckInput
        checkboxText={
          <span>
            {checkboxText}
            {showErrors && firstError ? `  ${firstError}` : null}
          </span>
        }
        hasError={showErrors}
        {...otherProps}
      />
      {helpText ? <FormHelp>{helpText}</FormHelp> : null}
    </FormGroup>
  );
}

CheckInputGroup.defaultProps = {
  showErrors: false,
  errors: [],
};

export default formInput(CheckInputGroup);
