/* eslint-plugin-disable @typescript-eslint */

// A product is considered unstoppable if playback cannot be stopped remotely.
const unstoppableProducts = [/alexa/i, /sonos/i];

const unstoppableProductMemo = {};

export default function isPlayerUnstoppable(player: any) {
  const { product } = player;

  if (!unstoppableProductMemo.hasOwnProperty(product)) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    unstoppableProductMemo[product] = unstoppableProducts.some((regex) =>
      regex.test(product)
    );
  }

  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  return unstoppableProductMemo[product];
}
