/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import getLogLevelLabel from 'app/js/utils/server/getLogLevelLabel';
import Scroller from 'app/js/views/v3/components/core/base/Scroller';

type ServerLogRawTextProps = {
  className: string;
  filter: string;
  logLevels: any[];
  lines: any[];
};

class ServerLogRawText extends Component<ServerLogRawTextProps> {
  //
  // Lifecycle

  shouldComponentUpdate(nextProps: ServerLogRawTextProps) {
    // Do not re-render when new lines are added. Only re-render when filters
    // are modified.

    return (
      nextProps.filter !== this.props.filter ||
      nextProps.logLevels !== this.props.logLevels
    );
  }

  //
  // Render

  render() {
    const { className, lines } = this.props;

    return (
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: Element; className: string; }' i... Remove this comment to see the full error message
      <Scroller className={className}>
        <pre>
          {lines.reduce((str, line) => {
            str += `${line.time} [${line.thread}] ${getLogLevelLabel(
              line.level
            )}  ${line.message}\n`;
            return str;
          }, '')}
        </pre>
      </Scroller>
    );
  }
}

export default ServerLogRawText;
