import React, {
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import ModalBody from 'app/js/v4/core/ui/modals/components/ModalBody';
import ModalFooter from 'app/js/v4/core/ui/modals/components/ModalFooter';
import ModalFooterButtonBar from 'app/js/v4/core/ui/modals/components/ModalFooterButtonBar';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import {
  DVRSetupStep,
  DVRSetupStepProps,
} from 'app/js/v4/features/dvrManagement/components/setup/DVRSetupModal';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import createDeviceChannelsSelector from 'app/js/v4/features/dvrManagement/selectors/createDeviceChannelsSelector';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import selectSetupSelectedLineups from 'app/js/v4/features/dvrManagement/selectors/selectSetupSelectedLineups';
import DVRSetupInfoStep from './DVRSetupInfoStep';
import styles from './DVRSetupInfo.css';

export enum LoadingStep {
  Data = 1,
  Channels = 2,
  Guide = 3,
}

export default function DVRSetupInfo({
  onSetStep,
}: DVRSetupStepProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const [loadingStep, setLoadingStep] = useState(LoadingStep.Data);

  const selectedLineups = useSelector(selectSetupSelectedLineups);
  const { selectedDevice } = useSelector(selectDVRSetup);
  const selectDeviceChannels = useMemo(
    () => createDeviceChannelsSelector(selectedDevice?.key),
    [selectedDevice]
  );
  const {
    channels,
    isPending: isChannelsPending,
    isPopulated: isChannelsPopulated,
  } = useSelector(selectDeviceChannels);

  const {
    postalCode,
    selectedCountry,
    selectedLanguage,
    selectedLineupGroups,
    selectedRegion,
    languages: { epgLanguages },
    channelMappings: { isPopulated: isChannelMappingsPopulated },
  } = useSelector(selectEPGSettings);

  const lineupGroup = selectedLineupGroups[0];

  //
  // Callbacks

  const onBackPress = useCallback(() => {
    onSetStep(DVRSetupStep.Device);
  }, [onSetStep]);

  //
  // Effects

  useEffect(() => {
    if (epgLanguages.length === 0) {
      dispatch(
        epgSettingsSlice.actions.epgLanguagesFetchRequested({
          server,
        })
      );
    }
  }, [dispatch, server, epgLanguages, onSetStep]);

  useEffect(() => {
    if (loadingStep === LoadingStep.Data) {
      if (selectedCountry && selectedLanguage) {
        setLoadingStep(LoadingStep.Channels);
      } else if (epgLanguages && selectedCountry && !selectedLanguage) {
        dispatch(
          epgSettingsSlice.actions.epgLanguageSelected({
            languageCode: selectedCountry.language,
          })
        );
      }
    }
  }, [dispatch, epgLanguages, loadingStep, selectedCountry, selectedLanguage]);

  useEffect(() => {
    if (loadingStep === LoadingStep.Channels) {
      if (isChannelsPopulated) {
        setLoadingStep(LoadingStep.Guide);
      } else if (selectedDevice && (!channels || !isChannelsPending)) {
        dispatch(
          dvrSetupSlice.actions.deviceChannelsFetchRequested({
            server,
            deviceKey: selectedDevice.key,
          })
        );
      }
    }
  }, [
    channels,
    dispatch,
    isChannelsPending,
    isChannelsPopulated,
    loadingStep,
    selectedDevice,
    server,
  ]);

  useEffect(() => {
    if (
      loadingStep === LoadingStep.Guide &&
      selectedCountry &&
      !selectedLineups &&
      !lineupGroup
    ) {
      dispatch(
        epgSettingsSlice.actions.epgLineupGroupFetchRequested({
          server,
          country: selectedCountry,
          region: selectedRegion,
          postalCode,
        })
      );
    }
  }, [
    dispatch,
    lineupGroup,
    loadingStep,
    postalCode,
    selectedCountry,
    selectedLineups,
    selectedRegion,
    server,
  ]);

  useEffect(() => {
    if (
      loadingStep === LoadingStep.Guide &&
      selectedDevice &&
      !lineupGroup?.activityUUID &&
      !lineupGroup?.isSelectedLineupPopulated &&
      !selectedLineups?.length
    ) {
      dispatch(
        epgSettingsSlice.actions.bestEPGLineupSearchRequested({
          server,
          device: selectedDevice,
          lineupGroup,
        })
      );
    }
  }, [
    dispatch,
    lineupGroup,
    loadingStep,
    selectedDevice,
    selectedLineups,
    server,
  ]);

  useEffect(() => {
    if (
      loadingStep === LoadingStep.Guide &&
      lineupGroup?.isSelectedLineupPopulated
    ) {
      onSetStep(DVRSetupStep.Mapping);
    }
  }, [
    isChannelMappingsPopulated,
    lineupGroup,
    loadingStep,
    onSetStep,
    selectedLineups,
  ]);

  //
  // Render

  return (
    <>
      <ModalBody
        className={styles.modalBody}
        innerClassName={styles.innerModalBody}
      >
        <p>{t('Setting up your tuner and channels...')}</p>
        <div className={styles.container}>
          <Spinner className={styles.spinner} />

          <div className={styles.steps}>
            <DVRSetupInfoStep
              activeStep={loadingStep}
              step={LoadingStep.Data}
              title={t('Gathering data')}
            />
            <DVRSetupInfoStep
              activeStep={loadingStep}
              step={LoadingStep.Channels}
              title={t('Scanning channels')}
            />
            <DVRSetupInfoStep
              activeStep={loadingStep}
              step={LoadingStep.Guide}
              title={t('Finding the best guide')}
            />
          </div>
        </div>
      </ModalBody>

      <ModalFooter>
        <ModalFooterButtonBar className={styles.buttonBar}>
          <Button onPress={onBackPress}>{t('Back')}</Button>
        </ModalFooterButtonBar>
      </ModalFooter>
    </>
  );
}
