import React, { ReactElement, useMemo } from 'react';
import t from 'app/js/utils/t';
import { ServerAccount } from 'app/js/v4/core/serverAccounts/serverAccountsTypes';
import {
  VirtualCellFactory,
  VirtualListRanges,
} from 'app/js/v4/core/ui/lists/listsTypes';
import createHorizontalLayout from 'app/js/v4/core/ui/lists/utils/createHorizontalLayout';
import { ScrollDirection } from 'app/js/v4/core/ui/scrollers/components/Scroller';
import VirtualListScroller from 'app/js/v4/core/ui/scrollers/components/VirtualListScroller';
import ServerDashboardSectionContent from 'app/js/v4/features/dashboard/components/ServerDashboardSectionContent';
import {
  FormattedTopUsersStatistics,
  TopUsersStatistics,
} from 'app/js/v4/features/dashboard/dashboardTopUsersTypes';
import { StatisticsMetadataType } from 'app/js/v4/features/dashboard/dashboardTypes';
import { TimePeriod } from 'app/js/v4/utils/timePeriods';
import TopUsersCell from './TopUsersCell';
import styles from './TopUsersList.css';

interface TopUsersListProps {
  activeTimePeriod: TimePeriod;
  accounts: ServerAccount[];
  statistics: TopUsersStatistics[];
  isSpinnerVisible: boolean;
  showList: boolean;
  errorMessage?: string;
  targetScrollIndex?: number;
  smoothScroll?: boolean;
  onListUpdate(ranges: VirtualListRanges): void;
}

const layout = createHorizontalLayout({
  columnWidth: 200,
  rowHeight: 290,
  innerHorizontalPadding: 40,
});

function formatData(
  accounts: ServerAccount[],
  statistics: TopUsersStatistics[]
): FormattedTopUsersStatistics[] {
  if (!statistics) {
    return [];
  }

  const hasGameStatistics = !!statistics.find(
    (userStatistics) =>
      userStatistics.metadataType === StatisticsMetadataType.Game
  );

  const formattedData = statistics.reduce(
    (acc: FormattedTopUsersStatistics[], userStatistics) => {
      const { accountID, metadataType, count, duration } = userStatistics;

      // Ignore metadata types we're not interested in and unknown accounts.
      if (!(metadataType in StatisticsMetadataType) || accountID === -1) {
        return acc;
      }

      const index = acc.findIndex((item) => item.accountID === accountID);
      const hasExistingEntry = index !== -1;

      let formattedStatistics: FormattedTopUsersStatistics;
      if (hasExistingEntry) {
        formattedStatistics = acc[index];
      } else {
        const account = accounts.find((account_) => account_.id === accountID);

        formattedStatistics = {
          accountID,
          username: account?.name,
          thumb: account?.thumb,
          playCount: 0,
          totalDuration: 0,
          durations: {
            [StatisticsMetadataType.Movie]: 0,
            [StatisticsMetadataType.TV]: 0,
            [StatisticsMetadataType.Music]: 0,
            [StatisticsMetadataType.Photo]: 0,
          },
        };

        if (hasGameStatistics) {
          formattedStatistics.durations[StatisticsMetadataType.Game] = 0;
        }
      }

      formattedStatistics.playCount = formattedStatistics.playCount + count;
      formattedStatistics.totalDuration =
        formattedStatistics.totalDuration + duration;
      formattedStatistics.durations[metadataType] =
        formattedStatistics.durations[metadataType] + duration;

      if (hasExistingEntry) {
        acc[index] = formattedStatistics;
      } else {
        acc.push(formattedStatistics);
      }

      return acc;
    },
    []
  );

  // Sort accounts by play count.
  return formattedData.sort((a, b) => {
    if (a.totalDuration > b.totalDuration) {
      return -1;
    }

    if (a.totalDuration < b.totalDuration) {
      return 1;
    }

    return 0;
  });
}

export default function TopUsersList({
  activeTimePeriod,
  accounts,
  statistics,
  isSpinnerVisible,
  showList,
  errorMessage,
  targetScrollIndex,
  smoothScroll,
  onListUpdate,
}: TopUsersListProps): ReactElement {
  const items = useMemo(() => {
    return formatData(accounts, statistics);
  }, [accounts, statistics]);

  const notEnoughDataMessage =
    showList && items.length === 0 ? t('No data') : undefined;
  const finalErrorMessage = errorMessage || notEnoughDataMessage;

  const finalShowList = showList && !finalErrorMessage;

  const cellComponentFactory: VirtualCellFactory<FormattedTopUsersStatistics> =
    useMemo(() => {
      return (cellProps) => {
        return (
          <TopUsersCell {...cellProps} activeTimePeriod={activeTimePeriod} />
        );
      };
    }, [activeTimePeriod]);

  return (
    <ServerDashboardSectionContent
      className={isSpinnerVisible ? styles.spinnerContainer : styles.container}
      errorMessage={finalErrorMessage}
      isSpinnerVisible={isSpinnerVisible}
    >
      {finalShowList ? (
        <VirtualListScroller
          cellComponentFactory={cellComponentFactory}
          className={styles.listContainer}
          items={items}
          keyPropName="accountID"
          layout={layout}
          scrollDirection={ScrollDirection.Horizontal}
          smoothScroll={smoothScroll}
          targetScrollIndex={targetScrollIndex}
          totalSize={items.length}
          onListUpdate={onListUpdate}
        />
      ) : null}
    </ServerDashboardSectionContent>
  );
}
