/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/depre... Remove this comment to see the full error message
import tpl from 'app/templates/deprecatedPlugin/DeprecatedPluginsView.tpl';
import DeprecatedPluginTileList from './DeprecatedPluginTileList';

const DeprecatedPluginsView = Marionette.Layout.extend({
  //
  // Properties

  className: 'container',
  template: tpl,

  ui: {
    navLinks: '.nav a',
  },

  regions: {
    list: '.channels-container',
  },

  events: {
    'click .nav a': 'onNavClick',
    'click .all-btn': 'onAllClick',
    'click .video-btn': 'onVideoClick',
    'click .music-btn': 'onMusicClick',
    'click .photo-btn': 'onPhotoClick',
  },

  //
  // Init

  initialize: function ({ deprecatedPlugins }: any) {
    this.collection = deprecatedPlugins.get('channels');
    this.videoCollection = this.collection.filterByType('video');
    this.musicCollection = this.collection.filterByType('music');
    this.photoCollection = this.collection.filterByType('photos');
  },

  //
  // Control

  serializeData: function () {
    return {
      serverID: this.collection.server.id,
      allCount: this.collection.length,
      hasInstalledChannels: this.collection.length > 0,
      videoCount: this.videoCollection.length,
      musicCount: this.musicCollection.length,
      photoCount: this.photoCollection.length,
    };
  },

  //
  // Listeners

  onRender: function () {
    this.list.show(
      new DeprecatedPluginTileList({ collection: this.collection })
    );
  },

  onNavClick: function (event: any) {
    // Wipe out any existing selected classes before we add the new one.
    this.ui.navLinks.removeClass('selected');

    $(event.currentTarget).addClass('selected');
  },

  onAllClick: function (event: any) {
    event.preventDefault();

    this.list.show(
      new DeprecatedPluginTileList({ collection: this.collection })
    );
  },

  onVideoClick: function (event: any) {
    event.preventDefault();

    this.list.show(
      new DeprecatedPluginTileList({ collection: this.videoCollection })
    );
  },

  onMusicClick: function (event: any) {
    event.preventDefault();

    this.list.show(
      new DeprecatedPluginTileList({ collection: this.musicCollection })
    );
  },

  onPhotoClick: function (event: any) {
    event.preventDefault();

    this.list.show(
      new DeprecatedPluginTileList({ collection: this.photoCollection })
    );
  },
});

export default DeprecatedPluginsView;
