import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import selectUser from 'app/js/stores/selectors/user/selectUser';
import ServerFeaturePlexPassUpsellButton from 'app/js/views/v3/components/core/buttons/ServerFeaturePlexPassUpsellButton';

function createMapStateToProps() {
  return createSelector(
    selectUser,
    createUnstableServerSelector(),
    (user, server) => {
      return {
        machineIdentifier: server.machineIdentifier,
        isClaimed: server.isClaimed,
        isSignedIn: !!user && user.signedIn,
        hasPlexSubscription: !!user && user.hasPlexSubscription,
      };
    }
  );
}
const ServerFeaturePlexPassUpsellButtonConnector = connect(
  createMapStateToProps
)(ServerFeaturePlexPassUpsellButton);
(ServerFeaturePlexPassUpsellButtonConnector as any).propTypes = {
  ...(ServerFeaturePlexPassUpsellButtonConnector as any).propTypes,
  serverEntityID: PropTypes.string.isRequired,
};
export default ServerFeaturePlexPassUpsellButtonConnector;
