/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import styles from './DashSeparator.css';

export const SEPARATOR = '';

interface DashSeparatorProps {
  className?: string;
  separator?: string;
}

function DashSeparator({ className, separator }: DashSeparatorProps) {
  return <span className={className}>{separator}</span>;
}

DashSeparator.defaultProps = {
  className: styles.separator,
  separator: SEPARATOR,
};

export default DashSeparator;
