/* eslint-plugin-disable @typescript-eslint */

function getPercentPlayed(viewOffset: any, duration: any) {
  if (viewOffset > duration) {
    // When the duration is untrustworthy fallback to a synthetic
    // percentage watched.
    // See: https://github.com/plexinc/plex-media-player/issues/104
    return 50;
  } else if (viewOffset <= 0 || duration <= 0) {
    return 0;
  } else {
    return parseFloat(((viewOffset / duration) * 100).toFixed(2));
  }
}

export default getPercentPlayed;
