import React, { ReactElement, ReactNode } from 'react';
import DisclosureArrowButton from 'app/js/views/v3/components/core/buttons/DisclosureArrowButton';
import * as directions from 'app/js/views/v3/helpers/props/directions';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import styles from './ServerDashboardMenuButton.css';

interface ServerDashboardMenuButtonProps {
  children: ReactNode;
  id: string;
  className?: string;
  isMenuOpen: boolean;
  onPress(): void;
}

export default function ServerDashboardMenuButton({
  children,
  id,
  className = styles.menuButton,
  isMenuOpen,
  onPress,
}: ServerDashboardMenuButtonProps): ReactElement {
  return (
    <DisclosureArrowButton
      className={className}
      direction={isMenuOpen ? directions.UP : directions.DOWN}
      // @ts-expect-error ts-migrate(2769) FIXME: No overload matches this call.
      id={id}
      isSelected={false}
      size={sizes.LARGE}
      onPress={onPress}
    >
      {children}
    </DisclosureArrowButton>
  );
}
