import classNames from 'classnames';
import React, {
  ReactElement,
  ReactNode,
  Ref,
  SelectHTMLAttributes,
} from 'react';
import Size from 'app/js/v4/core/ui/types/Size';
import styles from './Select.css';

interface InnerSelectProps
  extends Omit<SelectHTMLAttributes<HTMLSelectElement>, 'size'> {
  children: ReactNode;
  forwardedRef: Ref<HTMLSelectElement>;
  className?: string;
  selectClassName?: string;
  errorClassName?: string;
  disabledClassName?: string;
  name: string;
  value?: string;
  size?: Size;
  isDisabled?: boolean;
  hasError?: boolean;
}

export type SelectProps = Omit<InnerSelectProps, 'forwardedRef'>;

function Select({
  forwardedRef,
  className = styles.input,
  selectClassName = styles.select,
  errorClassName = styles.hasError,
  disabledClassName = styles.isDisabled,
  isDisabled = false,
  hasError = false,
  name,
  size = Size.Medium,
  children,
  ...inputProps
}: InnerSelectProps): ReactElement {
  return (
    <span
      className={classNames(
        className,
        styles[size],
        isDisabled && disabledClassName,
        hasError && errorClassName
      )}
    >
      <select
        ref={forwardedRef}
        className={selectClassName}
        disabled={isDisabled}
        name={name}
        {...inputProps}
      >
        {children}
      </select>
    </span>
  );
}

export default React.forwardRef<HTMLSelectElement, SelectProps>(
  (props, ref) => {
    return <Select {...props} forwardedRef={ref} />;
  }
);
