import React, { ReactElement } from 'react';
import { IconActivity } from '@plex/react-chroma-icon';

interface ActivityIconProps {
  isActive: boolean;
}

function ActivityIcon(props: ActivityIconProps): ReactElement | null {
  const { isActive } = props;

  if (!isActive) {
    return null;
  }

  return <IconActivity />;
}

export default ActivityIcon;
