/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import MetadataListModel from 'app/js/models/metadata/MetadataListModel';
import * as responseTypes from 'app/js/models/responseTypes';
import replaceMetadataXMLElementNames from 'app/js/utils/parser/replaceMetadataXMLElementNames';
import parser2 from 'app/js/utils/parser2';

const DeprecatedPluginModel = AbstractModel.extend({
  //
  // Properties
  name: 'DeprecatedPluginModel',
  context: ['server', 'channel'],
  idAttribute: 'key',
  url() {
    // While we usually have a key for a channel, requesting a channel in the
    // channel directory only provides an identifier.
    return this.id ? this.id : this.identifierUrl(this.get('identifier'));
  },
  identifierUrl(identifier: any) {
    return `/:/plugins/${identifier}/root`;
  },
  syncedAttrs: {
    key: attrTypes.string,
    identifier: attrTypes.string,
    type: attrTypes.string,
    name: attrTypes.string,
    title1: attrTypes.string,
    title2: attrTypes.string,
    message: attrTypes.string,
    summary: attrTypes.string,
    thumb: attrTypes.string,
    art: attrTypes.string,
    prefsKey: attrTypes.string,
    searchKey: attrTypes.string,
    installed: attrTypes.boolean,
    noCache: attrTypes.boolean,
    allowSync: attrTypes.boolean,
    hasPrefs: attrTypes.boolean,
    hasStoreServices: attrTypes.boolean,
    share: attrTypes.boolean,
    lastAccessedAt: attrTypes.integer,
  },
  syncedRelations: {
    metadataList: {
      relationClass: MetadataListModel,
      attrs(data: any) {
        return {
          containerKey: data.key
            ? data.key
            : (this as any).identifierUrl(data.identifier),
        };
      },
      // @ts-expect-error ts-migrate(6133) FIXME: 'data' is declared but its value is never read.
      options(data: any) {
        return {
          channel: this,
          skipCache: true,
        };
      },
    },
  },
  derivedAttrs: {
    title() {
      // The channel directory uses the `name` attribute, while browsing a
      // channel will use `title1` or `title2` depending on if you are at the
      // root of the channel.
      return (
        (this as any).get('name') ||
        (this as any).get('title2') ||
        (this as any).get('title1')
      );
    },
  },
  //
  // Control
  augmentResponseText(responseText: any, responseType: any) {
    return responseType === responseTypes.xml
      ? replaceMetadataXMLElementNames(responseText, {
          includeDirectories: true,
        })
      : responseText;
  },
  augmentResponse(response: any) {
    // The key does not come back when fetching the channel directly and we may
    // need this key for old channels like iTunes.
    if (this.has('key')) {
      response.key = this.get('key');
    }
    // View history in the channel directory returns channels with "none" for
    // their `key`, which causes issues within a collection since the `key` is
    // used as an identifier that should not conflict.
    if (response.key === 'none') {
      delete response.key;
    }
    if (response.key) {
      // Use the key to deduce a type for the channel, which can be used in a
      // listening of channels to differentiate channels with the same title but
      // different types.
      const typeMatch = response.key.match(/^\/(\w+)\//);
      if (typeMatch) {
        response.type = typeMatch[1];
      }
    }
    // The `title` derived attribute helps with other title discrepancies. Use
    // `name` for the synced attribute, which is standard in the channel
    // directory, but use the derived attribute for display in views.
    if (response.title) {
      response.name = response.title;
    }
    const metadataItems = parser2.parseArray(response.Metadata);
    for (let i = metadataItems.length - 1; i >= 0; i--) {
      const metadataItem = metadataItems[i];
      // Remove the settings and search items from the array. We do not want to
      // display these in a list.
      if (metadataItem.settings || metadataItem.search) {
        const keyName = metadataItem.settings ? 'prefsKey' : 'searchKey';
        response[keyName] = metadataItem.key;
        metadataItems.splice(i, 1);
      }
    }
    // A little tricky, but we want the metadata list model to know about the
    // root attributes, so pass the entire response to it.
    response.Metadata = metadataItems;
    response.metadataList = response;
    return response;
  },
});
export default DeprecatedPluginModel;
