/* eslint-plugin-disable @typescript-eslint */

import { isParentType } from 'app/js/utils/types/metadataTypes';
import { getTimePeriodAfter } from 'app/js/v4/utils/timePeriods';
import getFullMetadataTitle from 'app/js/views/v3/helpers/metadata/getFullMetadataTitle';
import getPlayHistoryRoute from './getPlayHistoryRoute';

export default function getMetadataItemPlayHistoryRoute(
  machineIdentifier: any,
  metadataItem: any,
  activeTimePeriod: any
) {
  let filters = `metadataItemID=${metadataItem.ratingKey}`;

  if (activeTimePeriod) {
    filters += `&viewedAt=${getTimePeriodAfter(activeTimePeriod)}`;
  }

  return getPlayHistoryRoute(machineIdentifier, {
    filters,
    filterTitle: getFullMetadataTitle(metadataItem),
    activeTimePeriod,
    isParentType: isParentType(metadataItem.type),
  });
}
