/* eslint-plugin-disable @typescript-eslint */

import React, { useLayoutEffect, useRef } from 'react';
import cssDurationToMilliseconds from 'app/js/views/v3/helpers/css/cssDurationToMilliseconds';
import animations from 'app/styles/variables/animations';
import BackgroundImage from './BackgroundImage';

function addImageToTop(src: any, containerEl: any, options: any) {
  const { className, style, styleProps, durationMilliseconds, useCrossFade } =
    options;

  const imageEl = document.createElement('div');

  imageEl.className = className;

  Object.assign(imageEl.style, {
    position: 'absolute',
    backgroundImage: `url(${src})`,
    width: '100%',
    height: '100%',
    ...styleProps,
    ...style,
  });

  containerEl.appendChild(imageEl);

  // Safari 12 does not support the Web Animation API, so we need to check if it
  // exists on the element before calling it
  if (useCrossFade && typeof imageEl.animate === 'function') {
    imageEl.animate(
      [
        {
          opacity: 0,
        },
        {
          opacity: 1,
        },
      ],
      durationMilliseconds
    );
  }
}

function removeImageFromBottom(containerEl: any) {
  containerEl.removeChild(containerEl.childNodes[0]);
}

/*
(ts-migrate) TODO: Migrate the remaining prop types
...BackgroundImage.propTypes
*/
interface CrossFadeImageProps {
  containerClassName?: string;
  containerStyle?: any;
  animateOnMount?: boolean;
  disableCrossFade?: boolean;
  durationMilliseconds: number;
}

function CrossFadeImage(props: CrossFadeImageProps) {
  const {
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'className' does not exist on type 'Cross... Remove this comment to see the full error message
    className,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'style' does not exist on type 'CrossFade... Remove this comment to see the full error message
    style,
    containerClassName,
    containerStyle,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'src' does not exist on type 'CrossFadeIm... Remove this comment to see the full error message
    src,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'width' does not exist on type 'CrossFade... Remove this comment to see the full error message
    width,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'height' does not exist on type 'CrossFad... Remove this comment to see the full error message
    height,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'backgroundSize' does not exist on type '... Remove this comment to see the full error message
    backgroundSize,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'backgroundPosition' does not exist on ty... Remove this comment to see the full error message
    backgroundPosition,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'backgroundRepeat' does not exist on type... Remove this comment to see the full error message
    backgroundRepeat,
    animateOnMount,
    disableCrossFade,
    durationMilliseconds,
  } = props;

  const containerElRef = useRef(null);
  const isInitialRenderRef = useRef(true);

  const finalContainerStyle = {
    position: 'relative',
    width,
    height,
    ...containerStyle,
  };

  // Prefer `useLayoutEffect` over `useEffect`. This ensures that an initial
  // image is added to the top synchronously after DOM mutations are performed.
  // Particularly if `animateOnMount={false}`, we want the initial image to be
  // added before the browser has a chance to paint.

  useLayoutEffect(() => {
    const isInitialRender = isInitialRenderRef.current;
    isInitialRenderRef.current = false;

    const addOptions = {
      className,
      style,
      styleProps: {
        backgroundSize,
        backgroundPosition,
        backgroundRepeat,
      },
      durationMilliseconds,
      useCrossFade: isInitialRender ? animateOnMount : true,
    };

    if (disableCrossFade) {
      addOptions.useCrossFade = false;
    }

    let animationFrameID: any;
    let timeoutID: any;

    if (isInitialRender) {
      addImageToTop(src, containerElRef.current, addOptions);
    } else {
      animationFrameID = window.requestAnimationFrame(() => {
        animationFrameID = null;

        addImageToTop(src, containerElRef.current, addOptions);

        // After the top image is able to animate in, remove the bottom image
        // that is no longer visible. Use a longer delay in case the animation
        // is not smooth and does not complete on time.

        timeoutID = window.setTimeout(() => {
          // @ts-expect-error ts-migrate(2531) FIXME: Object is possibly 'null'.
          while (containerElRef.current.childNodes.length > 1) {
            removeImageFromBottom(containerElRef.current);
          }
        }, durationMilliseconds * 2);
      });
    }

    return () => {
      if (animationFrameID) {
        window.cancelAnimationFrame(animationFrameID);
      }

      if (timeoutID) {
        window.clearTimeout(timeoutID);
      }
    };
    // Do not re-run the effect when a prop like `disableCrossFade` changes.
    // These only apply to new images.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    className,
    style,
    src,
    backgroundSize,
    backgroundPosition,
    backgroundRepeat,
  ]);

  return (
    <div
      ref={containerElRef}
      className={containerClassName}
      style={finalContainerStyle}
    />
  );
}

CrossFadeImage.defaultProps = {
  ...BackgroundImage.defaultProps,
  animateOnMount: true,
  disableCrossFade: false,
  durationMilliseconds: cssDurationToMilliseconds(animations.slowSpeed),
};

export default CrossFadeImage;
