import React, { ReactElement, useCallback, useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import SpinnerButton from 'app/js/v4/core/ui/buttons/components/SpinnerButton';
import Kind from 'app/js/v4/core/ui/types/Kind';
import DVRDeviceImage from 'app/js/v4/features/dvrManagement/components/setup/DVRDeviceImage';
import {
  DVRSetupStep,
  DVRSetupStepProps,
} from 'app/js/v4/features/dvrManagement/components/setup/DVRSetupModal';
import {
  DeviceChannelsState,
  EPGCountryFlavor,
  MediaGrabberDevice,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import createDeviceChannelsSelector from 'app/js/v4/features/dvrManagement/selectors/createDeviceChannelsSelector';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import selectEPGSettingsPostalCodeExample from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettingsPostalCodeExample';
import selectLatestEPGLineupGroup from 'app/js/v4/features/dvrManagement/selectors/selectLatestEPGLineupGroup';
import selectSelectedDVRLineups from 'app/js/v4/features/dvrManagement/selectors/selectSelectedDVRLineups';
import DashSeparator from 'app/js/views/v3/components/core/base/DashSeparator';
import DVRSetupCountryRegionSelectInput from './DVRSetupCountryRegionSelectInput';
import DVRSetupCountrySelectInput from './DVRSetupCountrySelectInput';
import DVRSetupDeviceSourceSelectInput from './DVRSetupDeviceSourceSelectInput';
import DVRSetupPostalCodeInput from './DVRSetupPostalCodeInput';
import styles from './DVRSetupDevice.css';

function getChannelsMessage(
  deviceChannels: DeviceChannelsState | undefined
): string {
  if (!deviceChannels || deviceChannels.isPending) {
    return t('Loading channels...');
  }

  const { channels, error } = deviceChannels;
  const channelCount = channels.length;

  if (error || channelCount === 0) {
    return t('0 Channels (scan required)');
  } else if (channelCount === 1) {
    return t('1 channel');
  } else {
    return t('{channelCount} channels', { channelCount });
  }
}

interface DVRSetupDeviceProps {
  device: MediaGrabberDevice;
  activeDeviceKey?: string;
  onSetupStart(deviceKey: MediaGrabberDevice['key']): void;
  onSetStep: DVRSetupStepProps['onSetStep'];
}

export default function DVRSetupDevice({
  device,
  activeDeviceKey,
  onSetupStart,
  onSetStep,
}: DVRSetupDeviceProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const selectDeviceChannels = useMemo(
    () => createDeviceChannelsSelector(device.key),
    [device]
  );
  const postalCodeExample = useSelector(selectEPGSettingsPostalCodeExample);
  const deviceChannels = useSelector(selectDeviceChannels);
  const {
    lineupGroups: { isPending: isLineupGroupsPending },
    selectedCountry,
    selectedRegion,
    postalCode,
  } = useSelector(selectEPGSettings);
  const lineupGroup = useSelector(selectLatestEPGLineupGroup);
  const selectedDVRLineups = useSelector(selectSelectedDVRLineups);
  const hasXMLTVLineup = selectedDVRLineups?.find(({ uuid }) =>
    uuid.includes(providerIdentifiers.XMLTV)
  );
  const canUseXMLTV = !selectedDVRLineups || hasXMLTVLineup;
  const { key, make, model, tuners, thumb, uri } = device;

  const hasValidLocation = !!(
    selectedCountry &&
    (postalCode ||
      selectedRegion ||
      selectedCountry.flavor === EPGCountryFlavor.SinglePostalCode ||
      hasXMLTVLineup)
  );

  const isActive = !!(activeDeviceKey && activeDeviceKey === key);
  const isDisabled = !!(activeDeviceKey && activeDeviceKey !== key);
  const canContinue = !!activeDeviceKey && hasValidLocation;

  //
  // Callbacks

  const onNextPress = useCallback(() => {
    if (!isActive || !canContinue) {
      return;
    }

    scheduleImmediateUpdate(() => {
      dispatch(dvrSetupSlice.actions.deviceSelected({ deviceKey: device.key }));

      if (!hasXMLTVLineup && lineupGroup && lineupGroup.epgLineups.length > 0) {
        dispatch(
          epgSettingsSlice.actions.selectedEPGLineupGroupAddRequested({
            lineupGroup,
          })
        );
      }
    });

    if (lineupGroup && lineupGroup.epgLineups.length > 0 && !hasXMLTVLineup) {
      onSetStep(DVRSetupStep.Info);
    } else {
      onSetStep(DVRSetupStep.XMLTV);
    }
  }, [
    dispatch,
    device,
    lineupGroup,
    hasXMLTVLineup,
    isActive,
    canContinue,
    onSetStep,
  ]);

  const onXMLTVPress = useCallback(() => {
    scheduleImmediateUpdate(() => {
      dispatch(dvrSetupSlice.actions.deviceSelected({ deviceKey: device.key }));
    });

    onSetStep(DVRSetupStep.XMLTV);
  }, [dispatch, device, onSetStep]);

  const onSetupStartPress = useCallback(() => {
    onSetupStart(key);
  }, [key, onSetupStart]);

  //
  // Effects

  useEffect(() => {
    dispatch(
      dvrSetupSlice.actions.deviceChannelsFetchRequested({
        server,
        deviceKey: key,
      })
    );
  }, [server, key, dispatch]);

  useEffect(() => {
    if (hasValidLocation && selectedCountry) {
      dispatch(
        epgSettingsSlice.actions.epgLineupGroupFetchRequested({
          server,
          country: selectedCountry,
          region: selectedRegion,
          postalCode,
        })
      );
    }
  }, [
    selectedRegion,
    postalCode,
    server,
    selectedCountry,
    hasValidLocation,
    dispatch,
  ]);

  //
  // Render

  const channelsMessage = getChannelsMessage(deviceChannels);

  return (
    <form className={styles.container} onSubmit={onNextPress}>
      <DVRDeviceImage height={110} model={model} thumb={thumb} width={200} />

      <div className={styles.section}>
        <div className={styles.make}>{make}</div>
        <div className={styles.model}>{model}</div>
        <div className={styles.moreInfo}>
          {uri}
          {uri && tuners ? <DashSeparator /> : null}
          {tuners > 1
            ? t('{tunersCount} tuners', { tunersCount: tuners })
            : t('1 tuner')}
        </div>
      </div>

      <div className={styles.section}>
        <DVRSetupDeviceSourceSelectInput
          device={device}
          isDisabled={isDisabled}
        />
        <div className={styles.channelsMessage}>{channelsMessage}</div>
      </div>

      {!isDisabled ? (
        <div className={styles.section}>
          <DVRSetupCountrySelectInput
            deviceKey={device.key}
            isActive={isActive}
            onEdit={onSetupStartPress}
          />
        </div>
      ) : null}

      {isActive &&
      selectedCountry?.flavor === EPGCountryFlavor.Regions &&
      !hasXMLTVLineup ? (
        <div className={styles.section}>
          <DVRSetupCountryRegionSelectInput />
        </div>
      ) : null}

      {isActive && postalCodeExample && !hasXMLTVLineup ? (
        <div className={styles.section}>
          <DVRSetupPostalCodeInput />
        </div>
      ) : null}

      {isActive && canUseXMLTV && !hasXMLTVLineup ? (
        <div className={styles.section}>
          <Link kind={Kind.Primary} onPress={onXMLTVPress}>
            {t('Have an XMLTV guide on your server? Click here to use it.')}
          </Link>
        </div>
      ) : null}

      {!isActive && !isDisabled ? (
        <Button
          className={styles.button}
          kind={Kind.Primary}
          onPress={onSetupStartPress}
        >
          {t('Setup Device')}
        </Button>
      ) : null}

      {isActive ? (
        <SpinnerButton
          className={styles.button}
          isDisabled={!canContinue}
          isSpinning={isLineupGroupsPending}
          kind={Kind.Primary}
          type="submit"
        >
          {t('Next')}
        </SpinnerButton>
      ) : null}

      {isDisabled ? (
        <div className={styles.disabledText}>
          {t('You can add this device later.')}
        </div>
      ) : null}
    </form>
  );
}
