/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { useCallback } from 'react';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import useVirtualCell from 'app/js/views/v3/helpers/hooks/useVirtualCell';
import styles from './ServerLogLine.css';

function getLineClassName(level: any) {
  return (
    [
      styles.levelError,
      styles.levelWarning,
      styles.levelInfo,
      styles.levelDebug,
      styles.levelVerbose,
    ][level] || styles.levelUnknown
  );
}

type ServerLogLineProps = {
  index?: number;
  level?: number;
  message?: string;
  thread: number | string;
  time?: string;
  isHighlighted?: boolean;
  cellX: number;
  cellY: number;
  cellWidth: number;
  cellHeight: number;
  onLogLinePress: (...args: any[]) => any;
};

function ServerLogLine(props: ServerLogLineProps) {
  const { index, level, message, thread, time, isHighlighted, onLogLinePress } =
    props;

  const cell = useVirtualCell(props);

  const onPress = useCallback(
    // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
    (event) => {
      onLogLinePress({
        index: index,
        time: time,
        thread: thread,
        level: level,
        message: message,
      });
    },
    [index, time, thread, level, message, onLogLinePress]
  );

  const lineClassName = getLineClassName(level);

  return (
    <div style={cell.style}>
      <Link
        className={classNames(
          lineClassName,
          isHighlighted && styles.isHighlighted
        )}
        // @ts-expect-error ts-migrate(2322) FIXME: Type 'number | undefined' is not assignable to typ... Remove this comment to see the full error message
        tabIndex={index}
        onPress={onPress}
      >
        <span className={styles.fieldTime}>{time}</span>

        <span className={styles.fieldThread}>[{thread}]</span>

        <span className={styles.fieldMessage}>{message}</span>
      </Link>
    </div>
  );
}

export default ServerLogLine;
