/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import selectStableProvider from 'app/js/stores/selectors/provider/selectStableProvider';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import createVisibleSettingsSectionsSelector from 'app/js/stores/selectors/settings/createVisibleSettingsSectionsSelector';
import ServerSettingsPage from 'app/js/views/v3/components/pages/serverSettings/ServerSettingsPage';

function findGroup(settingsSections: any, sectionKey: any, groupKey: any) {
  const section = settingsSections.find(
    (settingsSection: any) => settingsSection.key === sectionKey
  );
  if (!section) {
    return null;
  }

  return section.groups.find((group: any) => group.key === groupKey);
}

function createMapStateToProps() {
  return createSelector(
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState, { settingsSection }) => settingsSection,
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState, { settingsGroup }) => settingsGroup,
    createVisibleSettingsSectionsSelector(),
    createUnstableServerSelector(),
    selectStableProvider,
    (settingsSection, settingsGroup, visibleSections, server, provider) => {
      const group = findGroup(visibleSections, settingsSection, settingsGroup);

      return {
        machineIdentifier: server && server.machineIdentifier,
        providerIdentifier: provider && provider.identifier,
        isInstallingServerUpdate: !!(server && server.isInstallingServerUpdate),
        isUnavailable: !!(server && server.isUnavailable),
        isUnauthorized: !!(server && server.isUnauthorized),
        isSettingAccessible: !!group,
        isSettingsPending: !!(server && server[__PLATFORM__].isSettingsPending),
        pageComponent: group && group.pageComponent,
        paneComponent: group && group.paneComponent,
      };
    }
  );
}

export default connect(createMapStateToProps)(ServerSettingsPage);
