import $ from 'jquery';
import _ from 'underscore';
import commands from 'app/js/commands/commands';
import { REMOTE_ACCESS_STATE_CONNECTING } from 'app/js/models/server/ServerCloudAccountModel';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';
import ServerSettingsPane from 'app/js/views/settings/server/ServerSettingsPane';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setti... Remove this comment to see the full error message
import tpl from 'app/templates/settings/server/ServerSettingsRemoteAccessPane.tpl';

const ServerSettingsRemoteAccessPane = ServerSettingsPane.extend({
  //
  // Properties
  className: 'remote-access-container',
  template: tpl,
  ui: {
    manualPortMappingMode: '#ManualPortMappingMode',
    manualPortMappingPort: '#ManualPortMappingPort',
    wanTotalMaxUploadRate: '#WanTotalMaxUploadRate',
    uploadContainer: '.remote-access-upload-container',
    submitBtn: '.submit-btn',
    message: '.form-message',
  },
  events: {
    'change .remote-access-error-container input[id]': 'onErrorInputChange',
    'click .sign-in-link': 'onSignInClick',
    'click .remote-access-enable-btn': 'onEnableClick',
    'click .retry-btn': 'onRetryClick',
    'click .remote-access-disable-btn': 'onDisableClick',
    'change input[name=WanTotalMaxUploadRate]': 'onTotalRateChange',
    'keyup input[name=WanTotalMaxUploadRate]': 'onTotalRateChange',
    'change select[name=WanPerStreamMaxUploadRate]': 'onPerStreamRateChange',
    'submit #remote-access-upload-form': 'onUploadFormSubmit',
  },
  //
  // Init
  initialize: function (options: any) {
    this.cloudAccount = options.cloudAccount;
    this.changes = options.changes;
    this.listenTo(this.cloudAccount, {
      'change:signInState': this.render,
      'change:remoteAccessState': this.render,
    });
  },
  //
  // Control
  serializeData: function () {
    const publishSetting = this.collection.get('PublishServerOnPlexOnlineKey');
    const mappingModeSetting = this.collection.get('ManualPortMappingMode');
    const mappingPortSetting = this.collection.get('ManualPortMappingPort');
    const totalRateSetting = this.collection.get('WanTotalMaxUploadRate');
    const perStreamRateSetting = this.collection.get(
      'WanPerStreamMaxUploadRate'
    );
    const cloudAccount = this.cloudAccount.toJSON();
    const isDisabled = !publishSetting.get('value');
    const isSignedOut = !cloudAccount.signedIn;
    const isUnavailable = isDisabled || isSignedOut;
    const hasPrivateAddress = !!(
      cloudAccount.privateAddress && cloudAccount.privatePort != null
    );
    const hasPublicAddress = !!(
      cloudAccount.publicAddress && cloudAccount.publicPort != null
    );
    const data = {
      forceAdvanced: !isDisabled && !!cloudAccount.mappingError,
      isDisabled: isDisabled,
      isSignedOut: isSignedOut,
      isUnavailable: isUnavailable,
      canEditPort:
        !isUnavailable && mappingModeSetting && mappingModeSetting.get('value'),
      hasPrivateAddress,
      hasPublicAddress,
      cloudAccount: cloudAccount,
      hasMappingSettings: !!(mappingModeSetting && mappingPortSetting),
      hasUploadLimitSettings: !!(totalRateSetting && perStreamRateSetting),
      mappingModeSetting: mappingModeSetting
        ? mappingModeSetting.toJSON()
        : null,
      mappingPortSetting: mappingPortSetting
        ? mappingPortSetting.toJSON()
        : null,
      uploadSettings: [],
    };
    if (totalRateSetting) {
      (data as any).totalRateSettingID = totalRateSetting.id;
      (data as any).totalRateSettingValue =
        totalRateSetting.get('value') / 1000;
    }
    if (perStreamRateSetting) {
      // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'any' is not assignable to parame... Remove this comment to see the full error message
      data.uploadSettings.push(perStreamRateSetting.toJSON());
    }
    return data;
  },
  enable: function () {
    // Always keep the mapping form enabled.
  },
  enableUploadForm: function () {
    this.isDisabled = false;
    this.ui.message.text('');
    this.ui.message.removeClass('has-error');
    this.ui.submitBtn.removeClass('disabled');
  },
  updateUploadSetting: function (key: any, value: any) {
    // Extensions (LastPass) can trigger input changes we should ignore.
    if (!key) {
      return;
    }
    // Only recognize the change if we found a corresponding model, just as
    // an extra safeguard against extensions.
    const setting = this.updateModel(key, value);
    if (setting) {
      this.changes[key] = value;
      this.enable();
    }
  },
  connect: function () {
    return commands.execute('connectServer', {
      cloudAccount: this.cloudAccount,
      value: true,
    });
  },
  disconnect: function () {
    return commands.execute('connectServer', {
      cloudAccount: this.cloudAccount,
      value: false,
    });
  },
  reconnect: function () {
    return commands.execute('reconnectServer', {
      cloudAccount: this.cloudAccount,
    });
  },
  saveChanges: function () {
    if (_.isEmpty(this.changes)) {
      return $.Deferred().resolve().promise();
    }
    const promise = this.collection.first().save(this.changes);
    promise.done(
      _.bind(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.changes = {};
      }, this)
    );
    return promise;
  },
  //
  // Listeners
  onErrorInputChange: function () {
    // eslint-disable-next-line prefer-rest-params
    ServerSettingsPane.prototype.onInputChange.apply(this, arguments);
    this.ui.manualPortMappingPort.prop(
      'disabled',
      !this.ui.manualPortMappingMode.prop('checked')
    );
  },
  onSignInClick: function (event: any) {
    event.preventDefault();
    // Need to do a full navigation to ensure title and sidebar are
    // updated correctly.
    navigateToRoute('serverSettings', {
      machineIdentifier: this.model.id,
      settingsSection: 'settings',
      settingsGroup: 'general',
    });
  },
  onEnableClick: function (event: any) {
    event.preventDefault();
    this.connect();
  },
  onDisableClick: function (event: any) {
    event.preventDefault();

    const isPrivateConnection = this.model.get('isPrivateConnection');
    const hasManualPortMappingModeEnabled = this.collection
      .get('ManualPortMappingMode')
      .get('value');

    const confirmModal = new ConfirmModal({
      title: t('Disable Remote Access'),
      message: isPrivateConnection
        ? t(
            'This server will no longer be accessible outside this network. Are you sure you want to disable remote access?'
          )
        : t(
            "This server will no longer be accessible outside the local network. You're currently using a remote connection to this server and you may lose access if you continue. Are you sure you want to disable remote access?"
          ),
      warningMessage: hasManualPortMappingModeEnabled
        ? t(
            'Note: This will not affect any custom network configuration or manual port forwarding from your router. Please review your network settings to ensure complete removal of access to this host and port.'
          )
        : undefined,
      confirmLabel: t('Disable'),
      cancelLabel: t('Cancel'),
      hasDanger: true,
    });

    confirmModal.promise.then(() => {
      this.disconnect();
    });

    dispatcher.trigger('show:modal', confirmModal);
  },
  onRetryClick: function (event: any) {
    event.preventDefault();
    this.cloudAccount.set('remoteAccessState', REMOTE_ACCESS_STATE_CONNECTING);
    this.saveChanges().always(_.bind(this.reconnect, this));
  },
  onTotalRateChange: function (event: any) {
    const target = event.currentTarget;
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 2.
    const value = parseFloat(target.value, 10);
    const bitrate = _.isNaN(value) ? 0 : Math.max(value, 0);
    this.updateUploadSetting(target.name, bitrate * 1000);
    this.enableUploadForm();
  },
  onPerStreamRateChange: function (event: any) {
    const target = event.currentTarget;
    this.updateUploadSetting(target.name, target.value);
    this.enableUploadForm();
  },
  onUploadFormSubmit: function (event: any) {
    event.preventDefault();
    this.trigger('save');
    const totalRateSettingValue = this.collection
      .get('WanTotalMaxUploadRate')
      .get('value');
    const showWarning = !totalRateSettingValue;
    this.ui.wanTotalMaxUploadRate.val(totalRateSettingValue / 1000);
    this.ui.uploadContainer.toggleClass('show-warning', showWarning);
  },
});
export default ServerSettingsRemoteAccessPane;
