/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import _ from 'underscore';
import createDVRSelector from './createDVRSelector';

function createDVRDeviceSelector() {
  return createSelector(
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState, { deviceID }) => deviceID,
    createDVRSelector(),
    (deviceID, dvr) => {
      if (!dvr) {
        return;
      }

      return _.find(dvr.devices, { key: deviceID });
    }
  );
}

export default createDVRDeviceSelector;
