/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import path from 'app/js/utils/url/path';

export default function createServerDownloadUrlSelector(downloadPath: any) {
  return createSelector(createUnstableServerSelector(), (server) => {
    if (!server || !server.activeConnection) {
      return null;
    }

    const uri = server.activeConnection.uri;
    const params = server.accessToken
      ? { 'X-Plex-Token': server.accessToken }
      : {};

    return path.join([uri, downloadPath], params);
  });
}
