import addWeeks from 'date-fns/addWeeks';
import format from 'date-fns/format';
import fromUnixTime from 'date-fns/fromUnixTime';
import getUnixTime from 'date-fns/getUnixTime';
import startOfDay from 'date-fns/startOfDay';
import React, { ReactElement, useCallback, useMemo, useState } from 'react';
import isToday from 'app/js/utils/dateTime/isToday';
import formatBandwidthBitrate from 'app/js/utils/metadata/formatBandwidthBitrate';
import formatBytes from 'app/js/utils/metadata/formatBytes';
import t from 'app/js/utils/t';
import ServerDashboardGraph from 'app/js/v4/features/dashboard/components/ServerDashboardGraph';
import {
  BandwidthSourceType,
  BandwidthStatistics,
} from 'app/js/v4/features/dashboard/dashboardBandwidthTypes';
import formatRealTimeAgo from 'app/js/v4/features/dashboard/utils/formatRealTimeAgo';
import getBandwidthSourceTypeLabel from 'app/js/v4/features/dashboard/utils/getBandwidthSourceTypeLabel';
import {
  getTimePeriodDateFormat,
  getTimePeriodTimespan,
  TimePeriod,
  Timespan,
} from 'app/js/v4/utils/timePeriods';
import DashSeparator from 'app/js/views/v3/components/core/base/DashSeparator';
import BarChart from 'app/js/views/v3/components/core/charts/BarChart';
import LineChart from 'app/js/views/v3/components/core/charts/LineChart';

type Data = (PerTypeData & {
  at: number;
  date: number;
})[];

type PerTypeData = {
  [sourceType in BandwidthSourceType]?: number;
};

interface BandwidthGraphProps {
  statistics: BandwidthStatistics[];
  activeBandwidthSourceType: BandwidthSourceType;
  responseTimestampMilliseconds: number | null;
  activeTimePeriod: TimePeriod;
  isPending: boolean;
  isPopulated: boolean;
  errorMessage?: string;
  onGraphBandwidthSourceTypeSelect(
    BandwidthSourceType: BandwidthSourceType
  ): void;
  onGraphTooltipActive(): void;
  onGraphTooltipInactive(): void;
}

interface LegendSummaryProps {
  isRemoteVisible: boolean;
  isLocalVisible: boolean;
  summedData: PerTypeData;
}

const REAL_TIME_PERIOD_SPAN_SECONDS = 120;

function isTodayOrAfter(date: number): boolean {
  return isToday(date) || getUnixTime(startOfDay(new Date())) <= date;
}

function formatDate(
  date: number,
  activeTimePeriod: TimePeriod,
  dateFormat: string,
  toUpperCase = false
): string {
  const isDayToday =
    getTimePeriodTimespan(activeTimePeriod) === Timespan.Day && isToday(date);

  if (isDayToday) {
    const today = t('Today');
    return toUpperCase ? today.toUpperCase() : today;
  }

  return format(fromUnixTime(date), dateFormat);
}

function formatRealTimeData(
  statistics: BandwidthStatistics[],
  activeBandwidthSourceType: BandwidthSourceType,
  responseTimestampMilliseconds: number | null
): {
  data: Data;
  summedData: PerTypeData;
} {
  if (responseTimestampMilliseconds == null) {
    return {
      data: [],
      summedData: {},
    };
  }

  const isRemoteVisible =
    activeBandwidthSourceType === BandwidthSourceType.All ||
    activeBandwidthSourceType === BandwidthSourceType.Remote;

  const isLocalVisible =
    activeBandwidthSourceType === BandwidthSourceType.All ||
    activeBandwidthSourceType === BandwidthSourceType.Local;

  const startTimeSeconds =
    Math.floor(responseTimestampMilliseconds / 1000) -
    REAL_TIME_PERIOD_SPAN_SECONDS -
    1;

  const data = Array.from(
    new Array(REAL_TIME_PERIOD_SPAN_SECONDS + 1),
    (_, index) => {
      return {
        at: startTimeSeconds + index,
        date: REAL_TIME_PERIOD_SPAN_SECONDS - index,
        [BandwidthSourceType.Remote]: isRemoteVisible ? 0 : undefined,
        [BandwidthSourceType.Local]: isLocalVisible ? 0 : undefined,
      };
    }
  );

  const summedData: PerTypeData = {};

  statistics.forEach((bandwidthStatistic) => {
    const { at, lan, bytes } = bandwidthStatistic;

    const index = data.findIndex((entry) => entry.at === Number(at));

    if (index === -1) {
      return;
    }

    const timestampData = data[index];

    if (!lan && isRemoteVisible) {
      timestampData[BandwidthSourceType.Remote] =
        (timestampData[BandwidthSourceType.Remote] || 0) + bytes;
      summedData[BandwidthSourceType.Remote] =
        (summedData[BandwidthSourceType.Remote] || 0) + bytes;
    }

    if (lan && isLocalVisible) {
      timestampData[BandwidthSourceType.Local] =
        (timestampData[BandwidthSourceType.Local] || 0) + bytes;
      summedData[BandwidthSourceType.Local] =
        (summedData[BandwidthSourceType.Local] || 0) + bytes;
    }

    data[index] = timestampData;
  });

  return { data, summedData };
}

function formatHistoricalData(
  statistics: BandwidthStatistics[],
  activeBandwidthSourceType: BandwidthSourceType
): {
  data: Data;
  summedData: PerTypeData;
} {
  const isRemoteVisible =
    activeBandwidthSourceType === BandwidthSourceType.All ||
    activeBandwidthSourceType === BandwidthSourceType.Remote;
  const isLocalVisible =
    activeBandwidthSourceType === BandwidthSourceType.All ||
    activeBandwidthSourceType === BandwidthSourceType.Local;

  const aggregatedData: {
    [atKey: string]: PerTypeData;
  } = {};

  const summedData: PerTypeData = {};

  statistics.forEach((bandwidthStatistic) => {
    const { at, lan, bytes } = bandwidthStatistic;
    const atKey = String(at);

    if (!aggregatedData[atKey]) {
      aggregatedData[atKey] = {};
    }

    if (!lan && isRemoteVisible) {
      aggregatedData[atKey][BandwidthSourceType.Remote] =
        (aggregatedData[atKey][BandwidthSourceType.Remote] || 0) + bytes;
      summedData[BandwidthSourceType.Remote] =
        (summedData[BandwidthSourceType.Remote] || 0) + bytes;
    }

    if (lan && isLocalVisible) {
      aggregatedData[atKey][BandwidthSourceType.Local] =
        (aggregatedData[atKey][BandwidthSourceType.Local] || 0) + bytes;
      summedData[BandwidthSourceType.Local] =
        (summedData[BandwidthSourceType.Local] || 0) + bytes;
    }
  });

  const data = Object.keys(aggregatedData)
    .sort()
    .map((atKey) => {
      const aggregatedTimespan = aggregatedData[atKey];
      const at = parseInt(atKey, 10);

      return {
        ...aggregatedTimespan,
        at,
        date: at,
      };
    });

  return { data, summedData };
}

function AveragesLegendSummary({
  isRemoteVisible,
  isLocalVisible,
  summedData,
}: LegendSummaryProps): ReactElement {
  const isAllVisible = isRemoteVisible && isLocalVisible;

  return (
    <>
      {isAllVisible ? t('Averages') : t('Average')}
      {': '}

      {isAllVisible ? (
        <>
          {getBandwidthSourceTypeLabel(BandwidthSourceType.Remote)}
          <DashSeparator />
        </>
      ) : null}
      {isRemoteVisible
        ? formatBandwidthBitrate(
            ((summedData[BandwidthSourceType.Remote] || 0) * 8) /
              REAL_TIME_PERIOD_SPAN_SECONDS,
            0
          )
        : null}

      {isAllVisible ? (
        <>
          {' | '}
          {getBandwidthSourceTypeLabel(BandwidthSourceType.Local)}
          <DashSeparator />
        </>
      ) : null}
      {isLocalVisible
        ? formatBandwidthBitrate(
            ((summedData[BandwidthSourceType.Local] || 0) * 8) /
              REAL_TIME_PERIOD_SPAN_SECONDS,
            0
          )
        : null}
    </>
  );
}

function TotalsLegendSummary({
  isRemoteVisible,
  isLocalVisible,
  summedData,
}: LegendSummaryProps): ReactElement {
  const isAllVisible = isRemoteVisible && isLocalVisible;

  return (
    <>
      {isAllVisible ? t('Totals') : t('Total')}
      {': '}

      {isAllVisible ? (
        <>
          {getBandwidthSourceTypeLabel(BandwidthSourceType.Remote)}
          <DashSeparator />
        </>
      ) : null}
      {isRemoteVisible
        ? formatBytes(summedData[BandwidthSourceType.Remote], 0)
        : null}

      {isAllVisible ? (
        <>
          {' | '}
          {getBandwidthSourceTypeLabel(BandwidthSourceType.Local)}
          <DashSeparator />
        </>
      ) : null}
      {isLocalVisible
        ? formatBytes(summedData[BandwidthSourceType.Local], 0)
        : null}
    </>
  );
}

export default function BandwidthGraph({
  statistics,
  activeBandwidthSourceType,
  responseTimestampMilliseconds,
  activeTimePeriod,
  isPending,
  isPopulated,
  errorMessage,
  onGraphBandwidthSourceTypeSelect,
  onGraphTooltipActive,
  onGraphTooltipInactive,
}: BandwidthGraphProps): ReactElement {
  const [isChartReady, setIsChartReady] = useState(false);

  const isRealTime = activeTimePeriod === TimePeriod.RealTime;

  const { data, summedData } = useMemo(() => {
    return isRealTime
      ? formatRealTimeData(
          statistics,
          activeBandwidthSourceType,
          responseTimestampMilliseconds
        )
      : formatHistoricalData(statistics, activeBandwidthSourceType);
  }, [
    activeBandwidthSourceType,
    isRealTime,
    responseTimestampMilliseconds,
    statistics,
  ]);

  const isRemoteVisible =
    activeBandwidthSourceType === BandwidthSourceType.All ||
    activeBandwidthSourceType === BandwidthSourceType.Remote;
  const isLocalVisible =
    activeBandwidthSourceType === BandwidthSourceType.All ||
    activeBandwidthSourceType === BandwidthSourceType.Local;

  const series = [
    {
      dataKey: BandwidthSourceType.Remote,
      title: getBandwidthSourceTypeLabel(BandwidthSourceType.Remote),
      visible: isRemoteVisible,
      selected: activeBandwidthSourceType === BandwidthSourceType.Remote,
      color: '#366dfc',
    },
    {
      dataKey: BandwidthSourceType.Local,
      title: getBandwidthSourceTypeLabel(BandwidthSourceType.Local),
      visible: isLocalVisible,
      selected: activeBandwidthSourceType === BandwidthSourceType.Local,
      color: '#e69f23',
    },
  ];

  const ChartComponent = isRealTime ? LineChart : BarChart;
  const LegendSummary = isRealTime
    ? AveragesLegendSummary
    : TotalsLegendSummary;

  const isSpinnerVisible =
    !isChartReady || (isRealTime ? !isPopulated : isPending);

  const isGraphVisible = isRealTime ? isPopulated : !isPending && isPopulated;

  //
  // Callbacks

  const realTimeTooltipLabelFormatter = useCallback((seconds: number) => {
    return seconds === 0
      ? t('Now')
      : t('{unitOfTime} ago', { unitOfTime: formatRealTimeAgo(seconds) });
  }, []);

  const tooltipLabelFormatter = useCallback(
    (dateTimestamp: number) => {
      // For week long timespans show the period the timespan covers, rather than
      // just a single date.
      if (getTimePeriodTimespan(activeTimePeriod) === Timespan.Week) {
        const startDate = formatDate(
          dateTimestamp,
          activeTimePeriod,
          getTimePeriodDateFormat(activeTimePeriod, true)
        );

        const endDateTimestamp = getUnixTime(
          addWeeks(fromUnixTime(dateTimestamp), 1)
        );

        // Check to make sure the week doesn't start and end today/after.
        const isFutureTimePeriod =
          isToday(dateTimestamp) && isTodayOrAfter(endDateTimestamp);

        if (isFutureTimePeriod) {
          return t('Today');
        } else {
          const endDate = formatDate(
            endDateTimestamp,
            activeTimePeriod,
            getTimePeriodDateFormat(activeTimePeriod, true)
          );
          return `${startDate} - ${endDate}`;
        }
      }

      return formatDate(
        dateTimestamp,
        activeTimePeriod,
        getTimePeriodDateFormat(activeTimePeriod, true)
      );
    },
    [activeTimePeriod]
  );

  const realTimeTooltipValueFormatter = useCallback((bytes: number) => {
    return formatBandwidthBitrate(bytes * 8, 0);
  }, []);

  const tooltipValueFormatter = useCallback((bytes: number) => {
    return formatBytes(bytes, 0);
  }, []);

  const realTimeXAxisTickFormatter = useCallback((seconds: number) => {
    return formatRealTimeAgo(seconds);
  }, []);

  const xAxisTickFormatter = useCallback(
    (dateTimestamp: number) => {
      // For the month long timespans show the period the timespan covers, rather
      // than just a single date.
      if (activeTimePeriod === TimePeriod.Month) {
        const startDate = formatDate(
          dateTimestamp,
          activeTimePeriod,
          getTimePeriodDateFormat(activeTimePeriod)
        );

        const endDateTimestamp = getUnixTime(
          addWeeks(fromUnixTime(dateTimestamp), 1)
        );

        // Check to make sure the week doesn't start and end today/after.
        const isFutureTimePeriod =
          isToday(dateTimestamp) && isTodayOrAfter(endDateTimestamp);

        if (isFutureTimePeriod) {
          return t('Today').toUpperCase();
        } else {
          const endDate = formatDate(
            endDateTimestamp,
            activeTimePeriod,
            getTimePeriodDateFormat(activeTimePeriod)
          );
          return `${startDate} - ${endDate}`;
        }
      }

      return formatDate(
        dateTimestamp,
        activeTimePeriod,
        getTimePeriodDateFormat(activeTimePeriod),
        true
      );
    },
    [activeTimePeriod]
  );

  const yAxisTickFormatter = useCallback(
    (bytes: number) => {
      if (bytes === 0) {
        return '';
      }

      if (isRealTime) {
        return formatBandwidthBitrate(bytes * 8, 0);
      } else {
        return formatBytes(bytes, 0);
      }
    },
    [isRealTime]
  );

  const onChartReady = useCallback(() => {
    setIsChartReady(true);
  }, []);

  //
  // Render

  return (
    <ServerDashboardGraph
      errorMessage={errorMessage}
      isSpinnerVisible={isSpinnerVisible}
    >
      {isGraphVisible ? (
        <ChartComponent
          // @ts-expect-error ts-migrate(2322) FIXME: Type '{ width: string; height: number; series: { d... Remove this comment to see the full error message
          data={data}
          height={280}
          isAnimationActive={!isRealTime}
          legendSummary={
            <LegendSummary
              isLocalVisible={isLocalVisible}
              isRemoteVisible={isRemoteVisible}
              summedData={summedData}
            />
          }
          series={series}
          tooltipLabelFormatter={
            isRealTime ? realTimeTooltipLabelFormatter : tooltipLabelFormatter
          }
          tooltipValueFormatter={
            isRealTime ? realTimeTooltipValueFormatter : tooltipValueFormatter
          }
          width="100%"
          xAxisTickFormatter={
            isRealTime ? realTimeXAxisTickFormatter : xAxisTickFormatter
          }
          xAxisTickInterval={isRealTime ? 9 : null}
          yAxisTickFormatter={yAxisTickFormatter}
          onChartReady={onChartReady}
          onLegendPress={onGraphBandwidthSourceTypeSelect}
          onTooltipActive={onGraphTooltipActive}
          onTooltipInactive={onGraphTooltipInactive}
        />
      ) : null}
    </ServerDashboardGraph>
  );
}
