/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import t from 'app/js/utils/t';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import PlexPassModal from 'app/js/views/v3/components/user/modals/PlexPassModal';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';

type PlexPassUpsellButtonProps = {
  modalMetricsProperties: any;
  origin?: any; // TODO: PropTypes.oneOf(upsellOrigins.ALL)
};

type PlexPassUpsellButtonState = any;

class PlexPassUpsellButton extends Component<
  PlexPassUpsellButtonProps,
  PlexPassUpsellButtonState
> {
  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      isPlexPassModalOpen: false,
    };
  }

  //
  // Listeners

  onPlexPassPress = () => {
    this.setState({ isPlexPassModalOpen: true });
  };

  onPlexPassModalClose = () => {
    this.setState({ isPlexPassModalOpen: false });
  };

  //
  // Render

  render() {
    const { modalMetricsProperties, origin } = this.props;

    const { isPlexPassModalOpen } = this.state;

    return (
      <Button
        kind={kinds.PRIMARY}
        size={sizes.LARGE}
        onPress={this.onPlexPassPress}
      >
        {t('Get a Plex Pass')}
        <PlexPassModal
          isOpen={isPlexPassModalOpen}
          metricsProperties={modalMetricsProperties}
          origin={origin}
          onModalClose={this.onPlexPassModalClose}
        />
      </Button>
    );
  }
}

export default PlexPassUpsellButton;
