import { useEffect, useState } from 'react';

/**
 * This hook can be used when wanting to avoid showing a pending state quickly
 * between two populated states by not showing the pending state at all and
 * transitioning straight to the next populated state. Allow a certain amount of
 * time to pass before finally showing the pending state. The `delayValue` is
 * used to match on `currentValue` such that if they match, there is a delay
 * before that value is used.
 *
 * For example: `currentValue` becomes true, matching the `delayValue`, the
 * `forcedValue` doesn't become true until after the delay. If `currentValue`
 * becomes false, that change takes effect immediately.
 *
 * @param currentValue The currently rendered value
 * @param delayValue The value to compare to currentValue
 */
function usePreviousValueForAtMost<Value>(
  currentValue: Value,
  delayValue: Value,
  maxDuration = 500
): Value {
  const [forcedValue, setForcedValue] = useState(currentValue);

  useEffect(() => {
    let timeoutID: number | undefined;

    if (currentValue === delayValue) {
      timeoutID = window.setTimeout(() => {
        setForcedValue(currentValue);
      }, maxDuration);
    } else {
      setForcedValue(currentValue);
    }

    return () => window.clearTimeout(timeoutID);
  }, [currentValue, delayValue, maxDuration]);

  return forcedValue;
}

export default usePreviousValueForAtMost;
