import { Layout } from 'app/js/v4/core/ui/lists/listsTypes';
import createGridLayout, {
  GridDirection,
  GridLayoutOptions,
} from './createGridLayout';

export interface VerticalLayoutOptions
  extends Omit<
    GridLayoutOptions,
    'columns' | 'direction' | 'getCellHeight' | 'getCellWidth'
  > {
  rowHeight: number;
  rowWidth?: number;
}

export default function createVerticalLayout({
  rowHeight,
  rowWidth = 0,
  ...options
}: VerticalLayoutOptions): Layout {
  return createGridLayout({
    direction: GridDirection.Vertical,
    columns: 1,
    getCellWidth: (viewportOptions) =>
      Math.max(rowWidth, viewportOptions.innerViewportWidth),
    getCellHeight: () => rowHeight,
    ...options,
  });
}
