import React, { ReactElement } from 'react';
import t from 'app/js/utils/t';
import IconAlert from 'app/glyphs/alert-560.svg';
import styles from './FormSectionError.css';

export interface FormSectionErrorProps {
  errorMessage?: string;
}

export default function FormSectionError({
  errorMessage,
}: FormSectionErrorProps): ReactElement {
  return (
    <div className={styles.sectionError}>
      <IconAlert className={styles.sectionErrorIcon} />
      {errorMessage
        ? errorMessage
        : t('There was a problem saving your changes.')}
    </div>
  );
}
