import classNames from 'classnames';
import React, { ReactElement, useCallback } from 'react';
import { Draggable } from 'react-beautiful-dnd';
import { useDispatch } from 'react-redux';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import Checkbox from 'app/js/v4/core/ui/forms/components/Checkbox';
import useDisclosure from 'app/js/v4/core/ui/hooks/useDisclosure';
import useIsFocused from 'app/js/v4/core/ui/hooks/useIsFocused';
import ListRow from 'app/js/v4/core/ui/lists/components/ListRow';
import PromotionWarningModal from 'app/js/v4/features/libraryManagement/components/PromotionWarningModal';
import libraryManagementSlice from 'app/js/v4/features/libraryManagement/libraryManagementSlice';
import { HubOptions } from 'app/js/v4/features/libraryManagement/libraryManagementTypes';
import IconRemove from 'app/glyphs/remove-560.svg';
import IconReorder from 'app/glyphs/reorder-560.svg';
import styles from './LibraryHubListItem.css';

interface LibraryHubListItemProps {
  index: number;
  options: HubOptions;
  isDragging: boolean;
  sourceURI: string;
  onRemoveItem(index: number): void;
}

export default function LibraryHubListItem({
  sourceURI,
  index,
  options,
  isDragging,
  onRemoveItem,
}: LibraryHubListItemProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();

  // Show a dismissible warning when a hub is promoted to shared users' homes
  const hubWarningModalState = useDisclosure();

  const [isFocused, onFocus, onBlur] = useIsFocused();

  //
  // Callbacks

  const onDispatchPromote = useCallback(
    (promoted) => {
      dispatch(
        libraryManagementSlice.actions.promoteRequested({
          server,
          sourceURI,
          options,
          promoted,
        })
      );
    },
    [dispatch, options, server, sourceURI]
  );

  const onPromoteToRecommended = useCallback(
    (e) => {
      onDispatchPromote({ promotedToRecommended: e.target.checked });
    },
    [onDispatchPromote]
  );

  const onPromoteToOwnHome = useCallback(
    (e) => {
      onDispatchPromote({ promotedToOwnHome: e.target.checked });
    },
    [onDispatchPromote]
  );

  const onPromoteToSharedHome = useCallback(
    (e) => {
      onDispatchPromote({ promotedToSharedHome: e.target.checked });
    },
    [onDispatchPromote]
  );

  const onRemove = useCallback(
    () => onRemoveItem(index),
    [index, onRemoveItem]
  );

  const onAcceptWarning = useCallback(() => {
    if (hubWarningModalState.isOpen) {
      hubWarningModalState.onClose();
    }

    onDispatchPromote({ promotedToSharedHome: true });
  }, [hubWarningModalState, onDispatchPromote]);

  //
  // Render

  const rowIdentifier = `${sourceURI}-${options.identifier}`;
  const fieldRecommended = `${rowIdentifier}-recommended`;
  const fieldOwnHome = `${rowIdentifier}-ownHome`;
  const fieldSharedHome = `${rowIdentifier}-sharedHome`;

  return (
    <Draggable draggableId={options.identifier} index={index}>
      {(provided, snapshot) => (
        <div
          ref={provided.innerRef}
          {...provided.draggableProps}
          {...provided.dragHandleProps}
          onBlur={onBlur}
          onFocus={onFocus}
        >
          <ListRow isAlternate={!isDragging && index % 2 === 0}>
            <div
              className={classNames(
                snapshot.isDragging ? styles.draggingRow : styles.row,
                isFocused && styles.isActive
              )}
            >
              <div className={styles.handle}>
                <IconReorder />
              </div>
              <div className={styles.title}>
                <div className={styles.titleText}>{options.title}</div>
              </div>
              <div className={styles.inputs}>
                <label className={styles.input} htmlFor={fieldRecommended}>
                  <Checkbox
                    checked={options.promotedToRecommended}
                    name={fieldRecommended}
                    onChange={onPromoteToRecommended}
                  />
                </label>
                <label className={styles.input} htmlFor={fieldOwnHome}>
                  <Checkbox
                    checked={options.promotedToOwnHome}
                    name={fieldOwnHome}
                    onChange={onPromoteToOwnHome}
                  />
                </label>
                <label className={styles.input} htmlFor={fieldSharedHome}>
                  <Checkbox
                    checked={options.promotedToSharedHome}
                    name={fieldSharedHome}
                    onChange={
                      options.promotedToSharedHome
                        ? onPromoteToSharedHome
                        : hubWarningModalState.onOpen
                    }
                  />
                </label>
                <div className={styles.delete}>
                  {options.deletable ? (
                    <Link
                      aria-label={t('Remove Hub')}
                      className={styles.remove}
                      onPress={onRemove}
                    >
                      <IconRemove />
                    </Link>
                  ) : null}
                </div>
              </div>
            </div>
          </ListRow>
          <PromotionWarningModal
            isOpen={hubWarningModalState.isOpen}
            onAccept={onAcceptWarning}
            onClose={hubWarningModalState.onClose}
          />
        </div>
      )}
    </Draggable>
  );
}
