/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import t from 'app/js/utils/t';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import getServerSettingsRoute from 'app/js/views/v3/helpers/routes/getServerSettingsRoute';
import PlexPassUpsellButton from './PlexPassUpsellButton';

type ServerFeaturePlexPassUpsellButtonProps = {
  className?: string;
  machineIdentifier: string;
  origin?: any; // TODO: PropTypes.oneOf(upsellOrigins.ALL)
  modalMetricsProperties: any;
  isClaimed: boolean;
  isSignedIn: boolean;
  hasPlexSubscription: boolean;
};

function ServerFeaturePlexPassUpsellButton(
  props: ServerFeaturePlexPassUpsellButtonProps
) {
  const {
    className,
    origin,
    modalMetricsProperties,
    isSignedIn,
    isClaimed,
    hasPlexSubscription,
  } = props;

  if (hasPlexSubscription && isClaimed) {
    return null;
  }

  if (!hasPlexSubscription && isSignedIn) {
    return (
      <PlexPassUpsellButton
        modalMetricsProperties={modalMetricsProperties}
        origin={origin}
      />
    );
  }

  return (
    <Button
      className={className}
      kind={kinds.PRIMARY}
      size={sizes.LARGE}
      to={getServerSettingsRoute('settings', 'general')}
    >
      {isSignedIn ? t('Claim Server') : t('Sign In')}
    </Button>
  );
}

export default ServerFeaturePlexPassUpsellButton;
