import { RefObject, useCallback, useEffect, useRef, useState } from 'react';

export default function useIsRefHovered(): [
  boolean,
  RefObject<HTMLDivElement>
] {
  const [isHovered, setIsHovered] = useState(false);
  const ref = useRef<HTMLDivElement>(null);
  const onMouseEnter = useCallback(() => setIsHovered(true), []);
  const onMouseLeave = useCallback(() => setIsHovered(false), []);

  // Rather than relying on React's event system for detecting hovered state,
  // which can have unexpected behavior when elements are removed or portals are
  // used, use basic DOM event listeners.

  useEffect(() => {
    const node = ref.current;

    if (node) {
      node.addEventListener('mouseenter', onMouseEnter);
      node.addEventListener('mouseleave', onMouseLeave);
    }

    return () => {
      if (node) {
        node.removeEventListener('mouseenter', onMouseEnter);
        node.removeEventListener('mouseleave', onMouseLeave);
      }
    };
  }, [onMouseEnter, onMouseLeave]);

  return [isHovered, ref];
}
