import React from 'react';
import Scroller from 'app/js/views/v3/components/core/base/Scroller';
import styles from './SidebarScroller.css';

interface SidebarScrollerProps {
  className?: string;
}

function SidebarScroller(props: SidebarScrollerProps) {
  const { className } = props;

  // @ts-expect-error ts-migrate(2322) FIXME: Type '{ className: string | undefined; autoScroll:... Remove this comment to see the full error message
  return <Scroller autoScroll={true} className={className} {...props} />;
}

SidebarScroller.defaultProps = {
  className: styles.scroller,
};

export default SidebarScroller;
