import t from 'app/js/utils/t';
import { BandwidthSourceType } from 'app/js/v4/features/dashboard/dashboardBandwidthTypes';

export default function getBandwidthSourceTypeLabel(
  resourceType: BandwidthSourceType
): string {
  switch (resourceType) {
    case BandwidthSourceType.All:
      return t('All');
    case BandwidthSourceType.Remote:
      return t('Remote');
    case BandwidthSourceType.Local:
      return t('Local');
    default:
      return '';
  }
}
