import { Selector } from 'react-redux';
import { createSelector } from 'reselect';
import { LineupChannelSelectOption } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import getLineupChannelOptionLabel from 'app/js/v4/features/dvrManagement/utils/getLineupChannelOptionLabel';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import selectEPGSettings from './selectEPGSettings';
import selectSelectedLineups from './selectSelectedLineups';

type GroupedLineupChannels = Record<string, LineupChannelSelectOption[]>;

export const VALUE_SEPARATOR = ':';

const selectGroupedLineupChannels: Selector<
  LegacyRootState,
  GroupedLineupChannels
> = createSelector(
  selectEPGSettings,
  selectSelectedLineups,
  ({ lineupChannels }, selectedLineups) => {
    return selectedLineups.reduce<GroupedLineupChannels>((acc, lineup) => {
      const channels = lineupChannels.channelsByLineupUUID[lineup.uuid] ?? [];

      acc[lineup.title] = channels.map((channel) => ({
        value: `${channel.key}${VALUE_SEPARATOR}${channel.identifier}`,
        label: getLineupChannelOptionLabel(channel),
      }));

      return acc;
    }, {});
  }
);

export default selectGroupedLineupChannels;
