/* eslint-plugin-disable @typescript-eslint */

import getPartByAbsoluteOffset from './getPartByAbsoluteOffset';
import getRelativeOffset from './getRelativeOffset';
import getScreenshotIndex from './getScreenshotIndex';

function getScreenshotUrl(media: any, absoluteOffset: any, index: any) {
  const part = getPartByAbsoluteOffset(media, absoluteOffset);
  index = getScreenshotIndex(part, index);

  if (!index) {
    return;
  }

  const relativeOffset = getRelativeOffset(media, part, absoluteOffset);

  return (
    '/library/parts/' + part.id + '/indexes/' + index + '/' + relativeOffset
  );
}

export default getScreenshotUrl;
