/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import DeprecatedPluginModel from './DeprecatedPluginModel';

const DeprecatedPluginCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'DeprecatedPluginCollection',
  context: ['server', 'channel'],

  model: DeprecatedPluginModel,

  url() {
    return this.containerKey;
  },

  //
  // Control

  comparator(deprecatedPlugin: any) {
    return deprecatedPlugin.get('title').toLowerCase();
  },

  filterByType(type: any) {
    const nextDeprecatedPlugins = this.filter((deprecatedPlugin: any) => {
      return deprecatedPlugin.get('type') === type;
    });

    return this.clone(nextDeprecatedPlugins);
  },
});

export default DeprecatedPluginCollection;
