import React, { ReactElement, useCallback, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import SpinnerButton from 'app/js/v4/core/ui/buttons/components/SpinnerButton';
import FormLabel from 'app/js/v4/core/ui/forms/components/FormLabel';
import TextInput from 'app/js/v4/core/ui/forms/components/TextInput';
import Direction from 'app/js/v4/core/ui/types/Direction';
import Kind from 'app/js/v4/core/ui/types/Kind';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import usePrevious from 'app/js/v4/utils/hooks/usePrevious';
import DisclosureArrowButton from 'app/js/views/v3/components/core/buttons/DisclosureArrowButton';
import styles from './DVRSetupManualDevice.css';

interface FormData {
  address: string;
}

export default function DVRSetupManualDevice(): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const { setupErrorMessage, isAddingDevice } = useSelector(selectDVRSetup);
  const [showManualAddress, setShowManualAddress] = useState(false);
  const prevIsAddingDevice = usePrevious(isAddingDevice);
  const { register, handleSubmit } = useForm<FormData>();

  //
  // Callbacks

  const onShowDeviceAddressPress = useCallback(() => {
    setShowManualAddress(!showManualAddress);
  }, [showManualAddress, setShowManualAddress]);

  const onSubmit = handleSubmit(({ address }: FormData) => {
    dispatch(
      dvrSetupSlice.actions.deviceAddRequested({
        server,
        uri: address,
      })
    );
  });

  //
  // Effects

  useEffect(() => {
    if (
      showManualAddress &&
      prevIsAddingDevice &&
      !isAddingDevice &&
      !setupErrorMessage
    ) {
      setShowManualAddress(false);
    }
  }, [
    isAddingDevice,
    prevIsAddingDevice,
    setShowManualAddress,
    setupErrorMessage,
    showManualAddress,
  ]);

  //
  // Render

  return (
    <>
      <div className={styles.manualDeviceButtonContainer}>
        <DisclosureArrowButton
          direction={showManualAddress ? Direction.Up : Direction.Down}
          isSelected={false}
          kind={Kind.Primary}
          // @ts-expect-error ts-migrate(2769) FIXME: No overload matches this call.
          onPress={onShowDeviceAddressPress}
        >
          {t(
            "Don't see your HDHomeRun device? Enter its network address manually"
          )}
        </DisclosureArrowButton>
      </div>

      {showManualAddress ? (
        <div className={styles.manualDeviceContainer}>
          <form onSubmit={onSubmit}>
            <FormLabel>{t('HDHomeRun Device Address')}</FormLabel>
            <TextInput
              ref={register({ required: true })}
              className={styles.manualDeviceInput}
              name="address"
              placeholder={t('IP address or hostname')}
              type="text"
            />

            <SpinnerButton
              isSpinning={isAddingDevice}
              kind={Kind.Primary}
              type="submit"
            >
              {t('Connect')}
            </SpinnerButton>
          </form>
        </div>
      ) : null}
    </>
  );
}
