import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import normalizeProviderDataIdentifier from 'app/js/utils/provider/normalizeProviderDataIdentifier';
import { Provider } from 'app/js/v4/core/providers/providersTypes';

export default function isEPGProvider(provider: Provider): boolean {
  return providerIdentifiers.matches(
    normalizeProviderDataIdentifier(
      provider.identifier,
      provider.providerIdentifier
    ),
    [providerIdentifiers.CLOUD_EPG, providerIdentifiers.EPG]
  );
}
