/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import serverFeatures from 'app/js/models/server/serverFeatures';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import isVersionOrNewer from 'app/js/utils/isVersionOrNewer';
import DVROutdatedServerBanner from './DVROutdatedServerBanner';

function createMapStateToProps() {
  return createSelector(createUnstableServerSelector(), (server) => {
    const shouldRender =
      server.version &&
      !isVersionOrNewer(server.version, serverFeatures.v4DVRSetup);

    return {
      activeConnectionUri: server.activeConnection.uri,
      machineIdentifier: server.machineIdentifier,
      shouldRender,
    };
  });
}

export default connect(createMapStateToProps)(DVROutdatedServerBanner);
