import { createSelector, Selector } from 'reselect';
import createServerActivitiesSelector from 'app/js/v4/core/serverActivities/selectors/createServerActivitiesSelector';
import {
  ServerActivity,
  ServerActivityType,
} from 'app/js/v4/core/serverActivities/serverActivitiesTypes';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import { MediaGrabberDevice } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

const createDeviceActiveTunerCountSelector = (
  server: Server,
  deviceKey: MediaGrabberDevice['key']
): Selector<LegacyRootState, number> => {
  return createSelector(createServerActivitiesSelector(server), (activities) =>
    activities.reduce((acc: number, activity: ServerActivity) => {
      if (
        activity.type === ServerActivityType.GrabberGrab &&
        activity?.Context.deviceID === deviceKey
      ) {
        acc++;
      }

      return acc;
    }, 0)
  );
};

export default createDeviceActiveTunerCountSelector;
