import React, { Component } from 'react';
import makeCancelable from 'app/js/utils/promise/makeCancelable';
import getDisplayName from 'app/js/views/v3/helpers/getDisplayName';

function asyncProps(WrappedComponent: any, map: any) {
  const mapKeys = Object.keys(map);
  return class extends Component {
    static displayName = `AsyncProps(${getDisplayName(
      WrappedComponent
    )}:${mapKeys.join(',')})`;
    // eslint-disable-next-line react/sort-comp
    static WrappedComponent = WrappedComponent;
    _pairs: any;
    //
    // Lifecycle
    constructor(props: any, context: any) {
      super(props, context);
      this.state = mapKeys.reduce((acc, key) => {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        acc[key] = null;
        return acc;
      }, {});
    }
    componentDidMount() {
      this._pairs = mapKeys.map((key) => [key, makeCancelable(map[key]())]);
      // @ts-expect-error ts-migrate(7031) FIXME: Binding element 'key' implicitly has an 'any' type... Remove this comment to see the full error message
      this._pairs.forEach(([key, cancelable]) => {
        cancelable.promise.then((result: any) => {
          this.setState({ [key]: result });
        });
      });
    }
    componentWillUnmount() {
      // @ts-expect-error ts-migrate(6133) FIXME: 'key' is declared but its value is never read.
      this._pairs.forEach(([key, cancelable]) => {
        cancelable.cancel();
      });
    }
    //
    // Render
    render() {
      return <WrappedComponent {...this.props} {...this.state} />;
    }
  };
}
export default asyncProps;
