import React, { CSSProperties, ReactElement } from 'react';
import t from 'app/js/utils/t';
import * as metadataTypes from 'app/js/utils/types/metadataTypes';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import { TopPlayedMetadataItem } from 'app/js/v4/features/dashboard/dashboardTopPlayedTypes';
import { TimePeriod } from 'app/js/v4/utils/timePeriods';
import MetadataPosterCardImg from 'app/js/views/v3/components/metadata/posters/MetadataPosterCardImg';
import getMetadataItemPlayHistoryRoute from 'app/js/views/v3/helpers/routes/getMetadataItemPlayHistoryRoute';
import getPrePlayRoute from 'app/js/views/v3/helpers/routes/getPrePlayRoute';
import styles from './TopPlayedColumnRow.css';

interface TopPlayedColumnRowProps {
  className?: string;
  style?: CSSProperties;
  metadataItem: TopPlayedMetadataItem;
  activeTimePeriod: TimePeriod;
}

export default function TopPlayedColumnRow({
  className,
  style,
  metadataItem,
  activeTimePeriod,
}: TopPlayedColumnRowProps): ReactElement {
  const [server] = useSourceContext();

  const { key, type, title, thumb, globalViewCount, userCount } = metadataItem;

  const isArtist = type === metadataTypes.ARTIST;
  // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
  const prePlayRoute = getPrePlayRoute(
    server,
    { isLibraryProvider: true },
    key
  );

  return (
    <div className={className} style={style}>
      <Link
        className={isArtist ? styles.artistPoster : styles.poster}
        to={prePlayRoute}
      >
        <MetadataPosterCardImg
          height={isArtist ? 60 : 80}
          serverEntityID={server.entityID}
          type={type}
          url={thumb}
          width={isArtist ? 60 : 53}
        />
      </Link>

      <div className={styles.summary}>
        <Link className={styles.itemTitle} title={title} to={prePlayRoute}>
          {title}
        </Link>

        <Link
          to={getMetadataItemPlayHistoryRoute(
            server.machineIdentifier,
            metadataItem,
            activeTimePeriod
          )}
        >
          {globalViewCount === 1
            ? t('{count} play', { count: 1 })
            : t('{count} plays', { count: globalViewCount })}
        </Link>
        <div>
          {userCount === 1
            ? t('{count} user', { count: 1 })
            : t('{count} users', { count: userCount })}
        </div>
      </div>
    </div>
  );
}
