/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import AvatarImg from 'app/js/views/v3/components/core/images/AvatarImg';
import IconAlert from 'app/glyphs/alert-560.svg';
import styles from './ServerAvatar.css';

const AVATAR_SIZE = 60;

type ServerAvatarProps = {
  serverEntityID: string;
  avatarUrl?: string;
};

function ServerAvatar(props: ServerAvatarProps) {
  const { serverEntityID, avatarUrl } = props;

  return (
    <div className={styles.avatar}>
      {!avatarUrl ? <IconAlert className={styles.icon} /> : null}

      <AvatarImg
        height={AVATAR_SIZE}
        serverEntityID={serverEntityID}
        url={avatarUrl}
        width={AVATAR_SIZE}
      />
    </div>
  );
}

export default ServerAvatar;
