import React, {
  createContext,
  ReactElement,
  ReactNode,
  useContext,
  useMemo,
} from 'react';
import { useSelector } from 'react-redux';
import {
  ContentDirectory,
  Provider,
} from 'app/js/v4/core/providers/providersTypes';
import createProviderContentDirectorySelector from 'app/js/v4/core/providers/selectors/createProviderContentDirectorySelector';
import createProviderSelector from 'app/js/v4/core/providers/selectors/createProviderSelector';
import unknownProvider from 'app/js/v4/core/providers/utils/unknownProvider';
import createStableServerSelector from 'app/js/v4/core/servers/selectors/createStableServerSelector';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import unknownServer from 'app/js/v4/core/servers/utils/unknownServer';

interface SourceContextProviderProps {
  machineIdentifier: string;
  providerIdentifier: string;
  contentDirectoryID?: string;
  children: ReactNode;
}

type Source = [Server, Provider, ContentDirectory | undefined];

export const SourceContext = createContext<Source>([
  unknownServer,
  unknownProvider,
  undefined,
]);

export function useSourceContext(): Source {
  return useContext<Source>(SourceContext);
}

export default function SourceContextProvider({
  machineIdentifier,
  providerIdentifier,
  contentDirectoryID,
  children,
}: SourceContextProviderProps): ReactElement {
  const server = useSelector(createStableServerSelector(machineIdentifier));

  const provider = useSelector(
    createProviderSelector(machineIdentifier, providerIdentifier)
  );

  const contentDirectory = useSelector(
    createProviderContentDirectorySelector(
      machineIdentifier,
      providerIdentifier,
      contentDirectoryID
    )
  );

  const source = useMemo<Source>(() => {
    return [server, provider, contentDirectory];
  }, [server, provider, contentDirectory]);

  return (
    <SourceContext.Provider value={source}>{children}</SourceContext.Provider>
  );
}
