/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import t from 'app/js/utils/t';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import getServerSettingsRoute from 'app/js/views/v3/helpers/routes/getServerSettingsRoute';
import IconAlertRound from 'app/glyphs/alert-round-560.svg';
import styles from './DVROutdatedServerBanner.css';

type DVROutdatedServerBannerProps = {
  className: string;
  machineIdentifier: string;
  activeConnectionUri: string;
  shouldRender: boolean;
  showButton: boolean;
};

function DVROutdatedServerBanner({
  machineIdentifier,
  activeConnectionUri,
  shouldRender,
}: DVROutdatedServerBannerProps) {
  if (!shouldRender) {
    return null;
  }

  const encodedMachineIdentifier = encodeURIComponent(machineIdentifier);
  const dvrSettingsRoute = getServerSettingsRoute('manage', 'dvr');
  const localUrl = `${activeConnectionUri}/web/index.html${dvrSettingsRoute}`;
  const updateUrl = `#!/server/${encodedMachineIdentifier}/update`;

  return (
    <div className={styles.container}>
      <div className={styles.messageContainer}>
        <IconAlertRound className={styles.icon} />
        <div className={styles.message}>
          {t(
            "Your Plex Media Server doesn't support setting up tuner devices on this version of the Plex Web App."
          )}
        </div>
      </div>

      <Button
        className={styles.button}
        kind={kinds.DARK}
        target="_blank"
        to={updateUrl}
      >
        {t('Update Server')}
      </Button>

      <Button
        className={styles.button}
        kind={kinds.DARK}
        target="_blank"
        to={localUrl}
      >
        {t('Use Local Plex Web App')}
      </Button>
    </div>
  );
}

DVROutdatedServerBanner.defaultProps = {
  className: styles.container,
  showButton: true,
};
export default DVROutdatedServerBanner;
