import React, {
  ChangeEvent,
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import FormLabel from 'app/js/v4/core/ui/forms/components/FormLabel';
import Select from 'app/js/v4/core/ui/forms/components/Select';
import Kind from 'app/js/v4/core/ui/types/Kind';
import {
  EPGCountryFlavor,
  MediaGrabberDevice,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import styles from './DVRSetupCountrySelectInput.css';

interface DVRSetupCountrySelectInputProps {
  deviceKey: MediaGrabberDevice['key'];
  isActive: boolean;
  isDisabled?: boolean;
  hasError?: boolean;
  onEdit?(deviceKey: MediaGrabberDevice['key']): void;
}

interface CountryOption {
  value: string;
  label: string;
}

export default function DVRSetupCountrySelectInput({
  deviceKey,
  isActive,
  isDisabled,
  hasError,
  onEdit,
}: DVRSetupCountrySelectInputProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const {
    selectedCountry,
    countries: { epgCountries, isPending, isPopulated, error },
  } = useSelector(selectEPGSettings);
  const [hasAttemptedToSelectCountry, setHasAttemptedToSelectCountry] =
    useState(false);

  const selectedCountryCode = selectedCountry?.code ?? '';

  const countryOptions = useMemo(
    () =>
      epgCountries.map<CountryOption>((country) => ({
        value: country.code,
        label: country.title,
      })),
    [epgCountries]
  );

  //
  // Callbacks

  const onCountrySelect = useCallback(
    (event: ChangeEvent<HTMLSelectElement>) => {
      dispatch(
        epgSettingsSlice.actions.epgCountrySelected({
          countryCode: event.currentTarget.value,
        })
      );
    },
    [dispatch]
  );

  const onEditPress = useCallback(() => {
    onEdit?.(deviceKey);
  }, [deviceKey, onEdit]);

  //
  // Effects

  useEffect(() => {
    const { countryCode } = server;

    if (!isPopulated) {
      return;
    }

    const isCountryPresent = !!countryOptions.find(
      ({ value }) => value === countryCode
    );

    if (!selectedCountryCode && countryCode && isCountryPresent) {
      dispatch(
        epgSettingsSlice.actions.epgCountrySelected({
          countryCode,
        })
      );
    } else {
      setHasAttemptedToSelectCountry(true);
    }
  }, [
    server,
    selectedCountryCode,
    isPopulated,
    setHasAttemptedToSelectCountry,
    dispatch,
    countryOptions,
  ]);

  useEffect(() => {
    if (!!selectedCountryCode && !hasAttemptedToSelectCountry) {
      setHasAttemptedToSelectCountry(true);
    }
  }, [
    selectedCountryCode,
    hasAttemptedToSelectCountry,
    setHasAttemptedToSelectCountry,
  ]);

  useEffect(() => {
    if (selectedCountry) {
      const { flavor } = selectedCountry;

      if (flavor === EPGCountryFlavor.Regions) {
        dispatch(
          epgSettingsSlice.actions.epgCountryRegionsFetchRequested({
            server,
            country: selectedCountry,
          })
        );
      } else if (flavor === EPGCountryFlavor.SinglePostalCode) {
        // The API will return the postal code value in `example` for countries
        // with a single postal code.
        // See: https://docs.plex.tv/docs/api/pms#device-endpoints-epg-endpoints-get
        dispatch(
          epgSettingsSlice.actions.epgPostalCodeChanged({
            postalCode: selectedCountry.example,
          })
        );
      }
    }
  }, [dispatch, selectedCountry, server]);

  useEffect(() => {
    if (!isPopulated) {
      dispatch(epgSettingsSlice.actions.epgCountriesFetchRequested({ server }));
    }
  }, [dispatch, isPopulated, server]);

  //
  // Render

  const inputContent =
    isActive || !selectedCountryCode ? (
      <Select
        hasError={hasError}
        isDisabled={isDisabled}
        name="countrySelect"
        value={selectedCountryCode}
        onChange={onCountrySelect}
      >
        <option value="">{t('Select Country')}</option>
        {countryOptions.map(({ value, label }, index) => (
          <option key={index} value={value}>
            {label}
          </option>
        ))}
      </Select>
    ) : (
      <div className={styles.edit}>
        {selectedCountry?.title}
        {!isDisabled ? (
          <Link kind={Kind.Primary} onPress={onEditPress}>
            {t('Edit')}
          </Link>
        ) : null}
      </div>
    );

  return (
    <>
      <FormLabel className={styles.label} hasError={hasError}>
        {t('Country')}
      </FormLabel>

      {isPending || !hasAttemptedToSelectCountry
        ? t('Loading countries...')
        : null}
      {error ? t("We couldn't fetch countries at this moment.") : null}
      {isPopulated && hasAttemptedToSelectCountry ? inputContent : null}
    </>
  );
}
