import React, { ReactElement, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import serverFeatures from 'app/js/models/server/serverFeatures';
import {
  clearDVRSettingsUI,
  initDVRSettingsUI,
} from 'app/js/stores/actions/ui/dvrSettingsUIActions';
import * as metricsContexts from 'app/js/utils/constants/metrics/metricsContexts';
import * as features from 'app/js/utils/constants/user/features';
import isVersionOrNewer from 'app/js/utils/isVersionOrNewer';
import t from 'app/js/utils/t';
import createEPGProviderSelector from 'app/js/v4/core/providers/selectors/createEPGProviderSelector';
import SourceContextProvider from 'app/js/v4/core/servers/components/SourceContextProvider';
import createStableServerSelector from 'app/js/v4/core/servers/selectors/createStableServerSelector';
import useDisclosure from 'app/js/v4/core/ui/hooks/useDisclosure';
import EmptyPageContent from 'app/js/v4/core/ui/pages/components/empty/EmptyPageContent';
import ErrorPageContent from 'app/js/v4/core/ui/pages/components/empty/ErrorPageContent';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import createUserFeatureSelector from 'app/js/v4/core/user/selectors/createUserFeatureSelector';
import DVRSetupModal from 'app/js/v4/features/dvrManagement/components/setup/DVRSetupModal';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import selectLegacyDVRSettingsUI from 'app/js/v4/features/dvrManagement/selectors/selectLegacyDVRSettingsUI';
import selectMappedLiveTVChannels from 'app/js/v4/features/livetv/selectors/selectMappedLiveTVChannels';
import LiveTVSettingsChannelList from './LiveTVSettingsChannelList';
import LiveTVSettingsDVR from './LiveTVSettingsDVR';
import LiveTVSettingsDeviceList from './LiveTVSettingsDeviceList';
import LiveTVSettingsZeroState from './LiveTVSettingsZeroState';
import styles from './LiveTVSettingsPage.css';

interface LiveTVSettingsPageProps {
  machineIdentifier: string;
}

export default function LiveTVSettingsPage({
  machineIdentifier,
}: LiveTVSettingsPageProps): ReactElement {
  const dispatch = useDispatch();
  const setupModalState = useDisclosure();
  const server = useSelector(createStableServerSelector(machineIdentifier));
  const provider = useSelector(createEPGProviderSelector(machineIdentifier));
  const hasLiveTVSetupFeature = useSelector(
    createUserFeatureSelector(features.LIVE_TV_SETUP)
  );
  const channels = useSelector(selectMappedLiveTVChannels);
  const {
    selectedDVR: { dvr, isPending: isDVRPending, error: dvrError },
  } = useSelector(selectDVRSetup);
  const hasDVRFeature =
    !!server.ownerFeatures && server.ownerFeatures.indexOf('dvr') > -1;

  const hasUnsupportedServer = !isVersionOrNewer(
    server.version,
    serverFeatures.v4DVRSetup
  );

  const { isUIPopulated, isDvrsPending } = useSelector(
    selectLegacyDVRSettingsUI
  );
  const isPending = !isUIPopulated || isDvrsPending || isDVRPending;

  //
  // Effects

  useEffect(() => {
    dispatch(initDVRSettingsUI({ serverEntityID: server.entityID }));

    return () => {
      dispatch(dvrSetupSlice.actions.reset());
      dispatch(epgSettingsSlice.actions.reset());
      dispatch(clearDVRSettingsUI());
    };
  }, [server, dispatch]);

  useEffect(() => {
    if (isUIPopulated && !isDvrsPending) {
      dispatch(dvrSetupSlice.actions.dvrFetchRequested({ server }));
    }
  }, [server, dispatch, isUIPopulated, isDvrsPending]);

  //
  // Render

  return (
    <SourceContextProvider
      machineIdentifier={machineIdentifier}
      providerIdentifier={provider.identifier}
    >
      {isPending ? (
        <EmptyPageContent>
          <Spinner />
        </EmptyPageContent>
      ) : null}

      {!isPending && !dvr && !dvrError ? (
        <LiveTVSettingsZeroState
          hasDVRFeature={hasDVRFeature}
          hasLiveTVSetupFeature={hasLiveTVSetupFeature}
          hasUnsupportedServer={hasUnsupportedServer}
          onSetupPress={setupModalState.onOpen}
        />
      ) : null}

      {!isPending && dvrError ? (
        <ErrorPageContent title={t('Unable to load DVRs')} />
      ) : null}

      {!isPending && !dvrError && dvr ? (
        <div className={styles.container}>
          <div className={styles.deviceListContainer}>
            <div className={styles.sectionTitle}>{t('Channel Sources')}</div>
            <LiveTVSettingsDeviceList
              dvrID={dvr.key}
              onAddDevicePress={setupModalState.onOpen}
            />
          </div>
          <div className={styles.channelsListContainer}>
            <div className={styles.sectionTitle}>
              {t('My Channels')} ({channels.length})
            </div>
            <LiveTVSettingsChannelList />
          </div>
          <div className={styles.dvrContainer}>
            <div className={styles.sectionTitle}>{t('DVR')}</div>
            <LiveTVSettingsDVR
              dvr={dvr}
              hasDVRFeature={hasDVRFeature}
              hasLiveTVSetupFeature={hasLiveTVSetupFeature}
            />
          </div>
        </div>
      ) : null}

      <DVRSetupModal
        dvrID={dvr?.key}
        isOpen={setupModalState.isOpen}
        machineIdentifier={machineIdentifier}
        metricsContext={metricsContexts.SETTINGS}
        providerIdentifier={provider.identifier}
        onModalClose={setupModalState.onClose}
      />
    </SourceContextProvider>
  );
}
