import React, {
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import { ServerAccount } from 'app/js/v4/core/serverAccounts/serverAccountsTypes';
import serverAccountComparator from 'app/js/v4/core/serverAccounts/utils/serverAccountComparator';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import usePreviousValueForAtMost from 'app/js/v4/core/ui/hooks/usePreviousValueForAtMost';
import ServerDashboardPageHeader from 'app/js/v4/features/dashboard/components/ServerDashboardPageHeader';
import ServerDashboardAccountMenuButton from 'app/js/v4/features/dashboard/components/menuButtons/ServerDashboardAccountMenuButton';
import ServerDashboardFilterMenuButton from 'app/js/v4/features/dashboard/components/menuButtons/ServerDashboardFilterMenuButton';
import dashboardPlayHistorySlice from 'app/js/v4/features/dashboard/dashboardPlayHistorySlice';
import { StatisticsMetadataType } from 'app/js/v4/features/dashboard/dashboardTypes';
import selectDashboardPlayHistory from 'app/js/v4/features/dashboard/selectors/selectDashboardPlayHistory';
import getStatisticsMetadataTypeLabel from 'app/js/v4/features/dashboard/utils/getStatisticsMetadataTypeLabel';
import {
  getTimePeriodAfter,
  getTimePeriodLabel,
  getTimePeriodTimespan,
  TimePeriod,
} from 'app/js/v4/utils/timePeriods';
import DashSeparator from 'app/js/views/v3/components/core/base/DashSeparator';
import getPlayHistoryRoute from 'app/js/views/v3/helpers/routes/getPlayHistoryRoute';
import PlayHistoryGraph from './PlayHistoryGraph';
import styles from './PlayHistory.css';

export default function PlayHistory(): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();

  const [activeAccount, setActiveAccount] = useState<ServerAccount | null>(
    null
  );

  const [activeTimePeriod, setActiveTimePeriod] = useState(TimePeriod.Month);
  const [lockedTimePeriod, setLockedTimePeriod] = useState(activeTimePeriod);

  const timePeriods = useMemo(() => {
    return [
      TimePeriod.Week,
      TimePeriod.Month,
      TimePeriod.NinetyDays,
      TimePeriod.Year,
      TimePeriod.AllTime,
    ];
  }, []);

  const [activeStatisticsMetadataType, setActiveStatisticsMetadataType] =
    useState(StatisticsMetadataType.All);

  const { accounts, statistics, isPending, isPopulated, error } = useSelector(
    selectDashboardPlayHistory
  );

  const hasGameStatistics = !!statistics.find(
    (userStatistics) =>
      userStatistics.metadataType === StatisticsMetadataType.Game
  );

  const statisticsMetadataTypes = useMemo(() => {
    const result = [
      StatisticsMetadataType.All,
      StatisticsMetadataType.Movie,
      StatisticsMetadataType.TV,
      StatisticsMetadataType.Music,
      StatisticsMetadataType.Photo,
    ];

    return hasGameStatistics
      ? result.concat([StatisticsMetadataType.Game])
      : result;
  }, [hasGameStatistics]);

  // Rather than showing all the server accounts, show the subset of accounts
  // that have play history in the active time period.

  const sortedAccounts = useMemo(() => {
    return accounts.slice().sort(serverAccountComparator);
  }, [accounts]);

  const isDelayedPending = usePreviousValueForAtMost(isPending, true);

  //
  // Callbacks

  const onMetadataTypeSelect = useCallback(
    (statisticsMetadataType: StatisticsMetadataType) => {
      setActiveStatisticsMetadataType(statisticsMetadataType);
    },
    []
  );

  const onAccountSelect = useCallback((account: ServerAccount) => {
    setActiveAccount(account);
  }, []);

  const onTimePeriodSelect = useCallback((timePeriod: TimePeriod) => {
    setActiveTimePeriod(timePeriod);
  }, []);

  const onGraphMetadataTypeSelect = useCallback(
    (statisticsMetadataTypeKey: string) => {
      const statisticsMetadataType = parseInt(statisticsMetadataTypeKey, 10);
      const nextStatisticsMetadataType =
        statisticsMetadataType === activeStatisticsMetadataType
          ? StatisticsMetadataType.All
          : statisticsMetadataType;

      setActiveStatisticsMetadataType(nextStatisticsMetadataType);
    },
    [activeStatisticsMetadataType]
  );

  //
  // Effects

  useEffect(() => {
    dispatch(
      dashboardPlayHistorySlice.actions.playHistoryFetchRequested({
        server,
        timespan: getTimePeriodTimespan(activeTimePeriod),
        after: getTimePeriodAfter(activeTimePeriod),
      })
    );
  }, [activeTimePeriod, dispatch, server]);

  useEffect(() => {
    // Do not pass the new active time period to the graph with statistics for
    // the previous time period. Once the statistics are updated, then update
    // the time period that is passed to the graph.

    setLockedTimePeriod(activeTimePeriod);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statistics]);

  useEffect(() => {
    return () => {
      dispatch(dashboardPlayHistorySlice.actions.reset());
    };
  }, [dispatch]);

  //
  // Render

  return (
    <div className={styles.container}>
      <ServerDashboardPageHeader title={t('Play History')}>
        <div className={styles.link}>
          <DashSeparator />

          {/* @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1. */}
          <Link to={getPlayHistoryRoute(server.machineIdentifier)}>
            {t('View Full History')}
          </Link>
        </div>

        <div>
          <ServerDashboardFilterMenuButton
            activeValue={activeStatisticsMetadataType}
            formatter={getStatisticsMetadataTypeLabel}
            values={statisticsMetadataTypes}
            onSelect={onMetadataTypeSelect}
          />

          <ServerDashboardAccountMenuButton
            accounts={sortedAccounts}
            activeAccount={activeAccount}
            onAccountSelect={onAccountSelect}
          />

          <ServerDashboardFilterMenuButton
            activeValue={activeTimePeriod}
            formatter={getTimePeriodLabel}
            values={timePeriods}
            onSelect={onTimePeriodSelect}
          />
        </div>
      </ServerDashboardPageHeader>

      <PlayHistoryGraph
        activeAccount={activeAccount}
        activeStatisticsMetadataType={activeStatisticsMetadataType}
        activeTimePeriod={lockedTimePeriod}
        errorMessage={error ? t('Unable to load history') : undefined}
        hasGameStatistics={hasGameStatistics}
        isPending={isDelayedPending}
        isPopulated={isPopulated}
        statistics={statistics}
        onGraphMetadataTypeSelect={onGraphMetadataTypeSelect}
      />
    </div>
  );
}
