/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import FormGroup from './FormGroup';
import FormHelp from './FormHelp';
import InputGroupFormLabel from './InputGroupFormLabel';
import TextInput from './TextInput';
import formInput from './formInput';

interface TextInputGroupProps {
  formGroupClassName?: string;
  formLabelClassName?: string;
  name: string;
  children?: React.ReactNode;
  labelText: React.ReactNode;
  helpText?: string;
  showErrors?: boolean;
  errors: string[];
}

export function TextInputGroup({
  formGroupClassName,
  formLabelClassName,
  name,
  labelText,
  helpText,
  showErrors,
  errors,
  children,
  ...otherProps
}: TextInputGroupProps) {
  return (
    <FormGroup className={formGroupClassName}>
      {children}
      <InputGroupFormLabel
        className={formLabelClassName}
        errors={errors}
        labelText={labelText}
        name={name}
        showErrors={showErrors}
      />
      {/* @ts-expect-error ts-migrate(2769) FIXME: No overload matches this call. */}
      <TextInput hasError={showErrors} name={name} {...otherProps} />
      {helpText ? <FormHelp>{helpText}</FormHelp> : null}
    </FormGroup>
  );
}

TextInputGroup.defaultProps = {
  errors: [],
};

export default formInput(TextInputGroup);
