import Sortable from 'sortablejs';
import BaseList from 'app/js/views/lists/BaseList';
import ContributorListItem from 'app/js/views/lists/settings/ContributorListItem';

const ContributorList = BaseList.extend({
  //
  // Properties

  tagName: 'ul',
  className: 'list contributor-list',
  itemView: ContributorListItem,

  events: {
    'change input[type=checkbox]': 'save',
    update: 'save',
  },

  //
  // Control

  save: function () {
    const contributors: any = [];

    this.$(':checked').each(function () {
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      contributors.push(this.name);
    });

    this.trigger('save', contributors.join(','));
  },

  //
  // Listeners

  onRender: function () {
    this.sortable = new Sortable(this.$el[0], {
      draggable: '.card-sortable',
      animation: 200,
    });
  },

  onClose: function () {
    this.sortable.destroy();
  },
});

export default ContributorList;
