import React, { ReactElement } from 'react';
import { IconWarningBadge } from '@plex/react-chroma-icon';
import styles from './ServerIsClaimedIcon.css';

interface ServerIsClaimedIconProps {
  isClaimed: boolean;
}

function ServerIsClaimedIcon(
  props: ServerIsClaimedIconProps
): ReactElement | null {
  const { isClaimed } = props;

  if (isClaimed) {
    return null;
  }

  return (
    <div className={styles.icon}>
      <IconWarningBadge />
    </div>
  );
}

export default ServerIsClaimedIcon;
