import classNames from 'classnames';
import React, { ReactElement } from 'react';
import Button, {
  ButtonProps,
} from 'app/js/v4/core/ui/buttons/components/Button';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import Size from 'app/js/v4/core/ui/types/Size';
import styles from './SpinnerButton.css';

export interface SpinnerButtonProps extends ButtonProps {
  className?: string;
  spinnerSize?: Size;
  isSpinning: boolean;
}

export default function SpinnerButton({
  className = styles.button,
  spinnerSize = Size.Small,
  isDisabled = false,
  isSpinning = false,
  children,
  ...buttonProps
}: SpinnerButtonProps): ReactElement {
  return (
    <Button
      className={classNames(className, isSpinning && styles.isSpinning)}
      isDisabled={isDisabled || isSpinning}
      {...buttonProps}
    >
      <span className={styles.spinnerContainer}>
        <Spinner className={styles.spinner} size={spinnerSize} />
      </span>
      <span className={styles.label}>{children}</span>
    </Button>
  );
}
