import React, { ReactElement, useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import SpinnerButton from 'app/js/v4/core/ui/buttons/components/SpinnerButton';
import ModalBody from 'app/js/v4/core/ui/modals/components/ModalBody';
import ModalFooter from 'app/js/v4/core/ui/modals/components/ModalFooter';
import ModalFooterButtonBar from 'app/js/v4/core/ui/modals/components/ModalFooterButtonBar';
import Kind from 'app/js/v4/core/ui/types/Kind';
import {
  DVRSetupStep,
  DVRSetupStepProps,
} from 'app/js/v4/features/dvrManagement/components/setup/DVRSetupModal';
import DVRSetupCountryRegionSelectInput from 'app/js/v4/features/dvrManagement/components/setup/device/DVRSetupCountryRegionSelectInput';
import DVRSetupCountrySelectInput from 'app/js/v4/features/dvrManagement/components/setup/device/DVRSetupCountrySelectInput';
import DVRSetupPostalCodeInput from 'app/js/v4/features/dvrManagement/components/setup/device/DVRSetupPostalCodeInput';
import { EPGCountryFlavor } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import selectEPGSettingsPostalCodeExample from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettingsPostalCodeExample';
import selectLatestEPGLineupGroup from 'app/js/v4/features/dvrManagement/selectors/selectLatestEPGLineupGroup';
import selectSelectedDVRLineups from 'app/js/v4/features/dvrManagement/selectors/selectSelectedDVRLineups';
import selectSetupSelectedLineups from 'app/js/v4/features/dvrManagement/selectors/selectSetupSelectedLineups';
import usePrevious from 'app/js/v4/utils/hooks/usePrevious';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import styles from './DVRSetupAddLineup.css';

export default function DVRSetupAddLineup({
  onSetStep,
}: DVRSetupStepProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const { selectedDevice } = useSelector(selectDVRSetup);
  const {
    selectedCountry,
    selectedRegion,
    selectedLineupGroups,
    postalCode,
    channelMappings: { isPending: isChannelMappingsPending },
    lineupGroups: { isPending: isLineupGroupsPending },
  } = useSelector(selectEPGSettings);
  const lineupGroup = useSelector(selectLatestEPGLineupGroup);
  const selectedSetupLineups = useSelector(selectSetupSelectedLineups);
  const selectedDVRLineups = useSelector(selectSelectedDVRLineups);
  const postalCodeExample = useSelector(selectEPGSettingsPostalCodeExample);
  const [isAddingLineup, setIsAddingLineup] = useState(false);
  const prevSelectedCountry = usePrevious(selectedCountry);
  const prevSelectedRegion = usePrevious(selectedRegion);
  const prevPostalCode = usePrevious(postalCode);

  const canSwitchLineupProvider =
    !selectedDVRLineups?.length && !selectedSetupLineups?.length;

  const isEverySelectedLineupPopulated = selectedLineupGroups.every(
    (selectedLineupGroup) => selectedLineupGroup.isSelectedLineupPopulated
  );
  const isEverySelectedLineupChannelMappingsFetched =
    selectedLineupGroups.every(
      (selectedLineupGroup) => selectedLineupGroup.isChannelMappingsFetched
    );

  const hasValidLocation = !!(
    selectedCountry &&
    (postalCode ||
      selectedRegion ||
      selectedCountry.flavor === EPGCountryFlavor.SinglePostalCode)
  );

  const hasChangedLocation = !!(
    (prevSelectedCountry !== undefined &&
      prevSelectedCountry !== selectedCountry) ||
    (prevSelectedRegion !== undefined &&
      prevSelectedRegion !== selectedRegion) ||
    (prevPostalCode !== undefined && prevPostalCode !== postalCode)
  );

  //
  // Callbacks

  const onBackPress = useCallback(() => {
    if (selectedLineupGroups.length > 0) {
      onSetStep(DVRSetupStep.Mapping);
    } else {
      dispatch(dvrSetupSlice.actions.reset());
      dispatch(epgSettingsSlice.actions.reset());
    }
  }, [dispatch, selectedLineupGroups.length, onSetStep]);

  const onContinuePress = useCallback(() => {
    if (lineupGroup) {
      dispatch(
        epgSettingsSlice.actions.selectedEPGLineupGroupAddRequested({
          lineupGroup,
        })
      );
    }
  }, [dispatch, lineupGroup]);

  const onUseXMLTVPress = useCallback(() => {
    onSetStep(DVRSetupStep.XMLTV);
  }, [onSetStep]);

  //
  // Effects

  useEffect(() => {
    if (!selectedDevice) {
      onSetStep(DVRSetupStep.Device);
    }
  }, [selectedDevice, onSetStep]);

  useEffect(() => {
    if (hasChangedLocation && hasValidLocation && selectedCountry) {
      dispatch(
        epgSettingsSlice.actions.epgLineupGroupFetchRequested({
          server,
          country: selectedCountry,
          region: selectedRegion,
          postalCode,
        })
      );
    }
  }, [
    hasChangedLocation,
    hasValidLocation,
    selectedRegion,
    postalCode,
    server,
    selectedCountry,
    dispatch,
  ]);

  useEffect(() => {
    if (
      lineupGroup &&
      selectedDevice &&
      selectedLineupGroups &&
      !isAddingLineup &&
      !isEverySelectedLineupPopulated
    ) {
      dispatch(
        epgSettingsSlice.actions.bestEPGLineupSearchRequested({
          server,
          device: selectedDevice,
          lineupGroup: selectedLineupGroups[selectedLineupGroups.length - 1],
        })
      );
      setIsAddingLineup(true);
    }
  }, [
    dispatch,
    lineupGroup,
    selectedDevice,
    selectedLineupGroups,
    isAddingLineup,
    isEverySelectedLineupPopulated,
    server,
  ]);

  useEffect(() => {
    if (!isAddingLineup || !lineupGroup || !selectedDevice) {
      return;
    }

    if (
      isEverySelectedLineupPopulated &&
      (isEverySelectedLineupChannelMappingsFetched ||
        !selectedSetupLineups?.length)
    ) {
      onSetStep(DVRSetupStep.Mapping);
    } else if (!isChannelMappingsPending && selectedSetupLineups?.length) {
      dispatch(
        epgSettingsSlice.actions.channelMappingsFetchRequested({
          server,
          device: selectedDevice,
          lineups: selectedSetupLineups,
        })
      );
    }
  }, [
    dispatch,
    isAddingLineup,
    isChannelMappingsPending,
    isEverySelectedLineupPopulated,
    isEverySelectedLineupChannelMappingsFetched,
    lineupGroup,
    onSetStep,
    selectedDevice,
    selectedSetupLineups,
    server,
  ]);

  //
  // Render

  const canContinue =
    (!lineupGroup || lineupGroup.epgLineups.length > 0) && hasValidLocation;

  const isSubmitting =
    isAddingLineup ||
    isLineupGroupsPending ||
    isChannelMappingsPending ||
    !isEverySelectedLineupPopulated;

  const hasError =
    hasValidLocation &&
    !isLineupGroupsPending &&
    lineupGroup?.epgLineups.length === 0;

  return (
    <>
      <ModalBody innerClassName={styles.innerModalBody}>
        <p className={styles.header}>
          {!selectedSetupLineups?.length
            ? t('Add EPG data lineup or location')
            : t('Add another EPG data lineup or location')}
        </p>
        <p>
          {t(
            'If your tuner device can receive channels that are not present in your initial lineup, you can specify additional locations or lineups to fulfill your EPG.'
          )}
        </p>
        <p>
          {t(
            'Remember, your device will need to be able to tune these channels to be able to function.'
          )}
        </p>
        <p>
          {t(
            'Any unsaved changes to channel mappings will be lost when you add a new lineup.'
          )}
        </p>
        <div className={styles.formContainer}>
          <div className={styles.formElements}>
            {selectedDevice ? (
              <div className={styles.section}>
                <DVRSetupCountrySelectInput
                  deviceKey={selectedDevice.key}
                  hasError={hasError}
                  isActive={true}
                  isDisabled={isSubmitting}
                />
              </div>
            ) : null}

            {selectedCountry?.flavor === EPGCountryFlavor.Regions ? (
              <div className={styles.section}>
                <DVRSetupCountryRegionSelectInput
                  hasError={hasError}
                  isDisabled={isSubmitting}
                />
              </div>
            ) : null}

            {postalCodeExample ? (
              <div className={styles.section}>
                <DVRSetupPostalCodeInput
                  hasError={hasError}
                  isDisabled={isSubmitting}
                />
              </div>
            ) : null}
          </div>
          {hasError ? (
            <p className={styles.formError}>
              {t(
                "We couldn't find Plex DVR guide data for your location at this time. Please select a different location or use an XMLTV guide."
              )}
            </p>
          ) : null}
        </div>

        {canSwitchLineupProvider ? (
          <Link kind={Kind.Primary} onPress={onUseXMLTVPress}>
            {t(
              'Have an XMLTV guide on your server? Click here to use that instead.'
            )}
          </Link>
        ) : null}
      </ModalBody>

      <ModalFooter>
        <ModalFooterButtonBar className={styles.buttonBar}>
          <Button isDisabled={isChannelMappingsPending} onPress={onBackPress}>
            {t('Back')}
          </Button>
          <SpinnerButton
            isDisabled={!canContinue}
            isSpinning={isSubmitting}
            kind={Kind.Primary}
            onPress={onContinuePress}
          >
            {t('Continue')}
          </SpinnerButton>
        </ModalFooterButtonBar>
      </ModalFooter>
    </>
  );
}
