/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React from 'react';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import styles from './Badge.css';

interface BadgeProps {
  className?: string;
  children?: React.ReactNode;
  kind?: any; // TODO: PropTypes.oneOf(kinds.all)
}

function Badge({ className, children, kind }: BadgeProps) {
  return (
    <span
      className={classNames(
        className,
        styles.badge,
        styles[kind],
        !children && styles.isHidden
      )}
      data-testid="badgeElement"
    >
      {children}
    </span>
  );
}

Badge.defaultProps = {
  kind: kinds.DEFAULT,
};

export default Badge;
