import classNames from 'classnames';
import React, {
  memo,
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { useSelector } from 'react-redux';
import { IconX } from '@plex/react-chroma-icon';
import { Row } from '@plex/react-chroma-layout';
import { Align, Size } from '@plex/react-chroma-types';
import formatDuration from 'app/js/utils/dateTime/formatDuration';
import isPlayerUnstoppable from 'app/js/utils/isPlayerUnstoppable';
import getPercentPlayed from 'app/js/utils/media/getPercentPlayed';
import getScreenshotUrl from 'app/js/utils/media/getScreenshotUrl';
import getSeasonEpisodeIndex from 'app/js/utils/media/getSeasonEpisodeIndex';
import playerUtil from 'app/js/utils/playerUtil';
import t from 'app/js/utils/t';
import { MetadataType } from 'app/js/v4/core/metadata/metadataTypes';
import { Provider } from 'app/js/v4/core/providers/providersTypes';
import createServerNowPlayingSessionSelector from 'app/js/v4/core/serverNowPlayingSessions/selectors/createServerNowPlayingSessionSelector';
import { ServerNowPlayingSession } from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsTypes';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import useTranscodeImage from 'app/js/v4/core/servers/hooks/useTranscodeImage';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import useIsRefHovered from 'app/js/v4/core/ui/hooks/useIsRefHovered';
import CrossFadeImage, {
  ImageFit,
} from 'app/js/v4/core/ui/images/CrossFadeImage';
import AttributionBadge from 'app/js/views/v3/components/core/AttributionBadge';
import AvatarImg from 'app/js/views/v3/components/core/images/AvatarImg';
import MetadataPosterCardImg from 'app/js/views/v3/components/metadata/posters/MetadataPosterCardImg';
import { getWidth } from 'app/js/views/v3/helpers/metadata/posterRatios';
import * as metadataContexts from 'app/js/views/v3/helpers/props/metadataContext';
import getPrePlayRoute from 'app/js/views/v3/helpers/routes/getPrePlayRoute';
import SessionTileDetails from './SessionTileDetails';
import StopServerSessionModal from './StopServerSessionModal';
import styles from './SessionTile.css';

const TILE_POSTER_HEIGHT = 100;
const TILE_POSTER_HEIGHT_FULL = 104;

function isLiveTVSession(session: ServerNowPlayingSession): boolean {
  if (!session.Media) {
    return false;
  }

  const selectedMediaItem =
    session.Media.find((mediaItem) => mediaItem.selected) || session.Media[0];

  return selectedMediaItem.origin === 'livetv';
}

function getTilePrePlayRoute(
  server: Server,
  provider: Provider,
  session: ServerNowPlayingSession
): string | undefined {
  const {
    key,
    parentKey,
    grandparentKey,
    type,
    skipDetails,
    skipParent,
    extraType,
  } = session;

  if (
    extraType ||
    skipDetails ||
    type === MetadataType.Photo ||
    isLiveTVSession(session)
  ) {
    return;
  }

  let finalKey: string | undefined = key;

  // If the search result is a track, link to the album or artist.
  if (type === MetadataType.Track) {
    finalKey = skipParent ? grandparentKey : parentKey;
  }

  // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
  return getPrePlayRoute(server, provider, finalKey);
}

function getPosterUrl(session: ServerNowPlayingSession): string | undefined {
  if (session.type === MetadataType.Track) {
    return session.parentThumb;
  }

  if (session.type === MetadataType.Photo) {
    return session.thumb;
  }

  return session.grandparentThumb || session.parentThumb || session.thumb;
}

function getTitleText(session: ServerNowPlayingSession): string {
  const { type, grandparentTitle, parentTitle, title } = session;

  if (isLiveTVSession(session)) {
    return t('Live TV');
  }

  if (type === MetadataType.Track || type === MetadataType.Photo) {
    return title;
  }

  return grandparentTitle || parentTitle || title;
}

function getSubtitleText(session: ServerNowPlayingSession): string | undefined {
  const {
    type,
    index,
    parentIndex,
    originallyAvailableAt,
    grandparentTitle,
    parentTitle,
    title,
    year,
  } = session;

  if (type === MetadataType.Track || type === MetadataType.Photo) {
    return parentTitle;
  }

  const parts: string[] = [];

  if (type === MetadataType.Episode) {
    parts.push(
      getSeasonEpisodeIndex({
        seasonIndex: parentIndex,
        episodeIndex: index,
        date: originallyAvailableAt,
        noLeadingZero: true,
        separator: '  ',
      })
    );
  }

  if (grandparentTitle || parentTitle) {
    parts.push(title);
  } else if (year) {
    parts.push(String(year));
  }

  return parts.join(' ');
}

function getSecondSubtitleText(
  session: ServerNowPlayingSession
): string | undefined {
  const { type, grandparentTitle, parentTitle, year, duration } = session;

  if (type === MetadataType.Movie || type === MetadataType.Episode) {
    return formatDuration(duration);
  } else if (type === MetadataType.Album || type === MetadataType.Track) {
    return grandparentTitle || parentTitle;
  } else if (type === MetadataType.Photo) {
    return String(year);
  }

  return;
}

interface SessionTileProps {
  sessionKey: string;
  isDetailsExpanded: boolean;
  canStopSessions: boolean;
}

function SessionTile({
  sessionKey,
  isDetailsExpanded,
  canStopSessions,
}: SessionTileProps): ReactElement {
  const [server, provider] = useSourceContext();

  const selectSession = useMemo(() => {
    return createServerNowPlayingSessionSelector(
      server.machineIdentifier,
      sessionKey
    );
  }, [server.machineIdentifier, sessionKey]);

  const session = useSelector(selectSession);
  const isPlaying = session.Player.state === playerUtil.states.PLAYING;

  const [isHovered, hoverRef] = useIsRefHovered();
  const [internalViewOffset, setInternalViewOffset] = useState(
    session.viewOffset || 0
  );
  const [isStopSessionModalOpen, setIsStopSessionModalOpen] = useState(false);

  const {
    type,
    subtype,
    duration,
    attribution,
    Media,
    User,
    Session,
    TranscodeSession,
    Player,
  } = session;

  const showProgressBar = session.viewOffset != null;

  const isGame = type === MetadataType.Game;

  let screenshotUrl: string | undefined;

  if (Media) {
    screenshotUrl =
      isGame && TranscodeSession
        ? `${TranscodeSession.key}/frame?t=${Date.now()}`
        : getScreenshotUrl(Media[0], internalViewOffset, 'hd');
  }

  const showPreviewThumbnail = isHovered && screenshotUrl;
  const prePlayRoute = getTilePrePlayRoute(server, provider, session);
  const MediaBackgroundComponent = prePlayRoute ? Link : 'div';

  const canShowStopButton = !!(
    canStopSessions &&
    Session &&
    !isPlayerUnstoppable(Player)
  );

  const titleText = getTitleText(session);
  const subtitleText = getSubtitleText(session);
  const secondSubtitleText = getSecondSubtitleText(session);

  const posterUrl = getPosterUrl(session);
  const posterHeight = showProgressBar
    ? TILE_POSTER_HEIGHT
    : TILE_POSTER_HEIGHT_FULL;
  const posterWidth = getWidth(
    posterHeight,
    type,
    subtype,
    metadataContexts.NOW_PLAYING
  );
  const previewWidth = isGame ? 180 : 214;

  const imageSrc = useTranscodeImage({
    server,
    provider,
    width: previewWidth,
    height: posterHeight,
    url: screenshotUrl,
  });

  //
  // Callbacks

  const onStopPress = useCallback((event) => {
    event.preventDefault();
    event.stopPropagation();

    setIsStopSessionModalOpen(true);
  }, []);

  const onModalClose = useCallback(() => {
    setIsStopSessionModalOpen(false);
  }, []);

  //
  // Effects

  useEffect(() => {
    let intervalID: number;

    setInternalViewOffset(session.viewOffset || 0);

    // If the item is actively playing, keep ticking the current time even
    // without an update to the session.
    if (isPlaying) {
      const timestamp = Date.now();

      intervalID = window.setInterval(() => {
        setInternalViewOffset(
          (session.viewOffset || 0) + (Date.now() - timestamp)
        );
      }, 1000);
    }

    return () => window.clearInterval(intervalID);
  }, [isPlaying, session.viewOffset]);

  //
  // Render

  return (
    <div ref={hoverRef} className={styles.listItem}>
      <MediaBackgroundComponent
        className={styles.mediaInformationContainer}
        to={prePlayRoute}
      >
        <div className={styles.posterContainer} style={{ width: posterWidth }}>
          <MetadataPosterCardImg
            height={posterHeight}
            serverEntityID={server.entityID}
            type={type}
            url={posterUrl}
            width={posterWidth}
          />

          {attribution ? (
            <AttributionBadge
              attribution={attribution}
              className={styles.attribution}
            />
          ) : null}
        </div>

        <div
          className={classNames(
            styles.infoContainer,
            showProgressBar ? styles.visibleProgressBar : undefined
          )}
        >
          {showPreviewThumbnail ? (
            <div className={styles.previewThumbnailContainer}>
              <CrossFadeImage
                fit={isGame ? ImageFit.Contain : ImageFit.Cover}
                height={posterHeight}
                src={imageSrc}
                width={previewWidth}
              />
            </div>
          ) : (
            <div className={styles.metadataText}>
              <div className={styles.title} title={titleText}>
                {titleText}
              </div>
              <div className={styles.subtitle} title={subtitleText}>
                {subtitleText}
              </div>
              <div className={styles.subtitle} title={secondSubtitleText}>
                {secondSubtitleText}
              </div>
            </div>
          )}
        </div>

        {showProgressBar ? (
          <div className={styles.progressTrack}>
            <div
              className={styles.playProgressPercent}
              style={{
                width: `${getPercentPlayed(internalViewOffset, duration)}%`,
              }}
            />

            {TranscodeSession?.progress ? (
              <div
                className={styles.transcodeProgressPercent}
                style={{ width: `${TranscodeSession.progress}%` }}
              />
            ) : null}
          </div>
        ) : null}
      </MediaBackgroundComponent>

      <SessionTileDetails
        className={classNames(
          styles.detailsContainer,
          isDetailsExpanded ? styles.detailsContainerExpanded : undefined
        )}
        internalViewOffset={internalViewOffset}
        server={server}
        session={session}
      />

      <div className={styles.userContainer}>
        <AvatarImg
          className={styles.avatarImg}
          height={40}
          serverEntityID={server.entityID}
          url={User.thumb}
          width={40}
        />

        <div className={styles.username}>{User.title}</div>

        {canShowStopButton ? (
          <Link className={styles.stopButton} onPress={onStopPress}>
            <Row
              align={Align.SpaceBetween}
              gap={Size.L}
              verticalAlign={Align.Center}
            >
              <div className={styles.stopIcon}>
                <IconX aria-label={t('Stop playback')} />
              </div>

              {t('Stop playback')}
            </Row>
          </Link>
        ) : null}
      </div>

      <StopServerSessionModal
        isOpen={isStopSessionModalOpen}
        session={session}
        onModalClose={onModalClose}
      />
    </div>
  );
}

export default memo(SessionTile);
