import React, { ReactElement, useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import createEPGLoadActivitySelector from 'app/js/v4/core/serverActivities/selectors/createEPGLoadActivitySelector';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import { VirtualCellFactory } from 'app/js/v4/core/ui/lists/listsTypes';
import createVerticalLayout from 'app/js/v4/core/ui/lists/utils/createVerticalLayout';
import VirtualListScroller from 'app/js/v4/core/ui/scrollers/components/VirtualListScroller';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import liveTVSlice from 'app/js/v4/features/livetv/liveTVSlice';
import { GuideChannel } from 'app/js/v4/features/livetv/liveTVTypes';
import selectLiveTV from 'app/js/v4/features/livetv/selectors/selectLiveTV';
import selectMappedLiveTVChannels from 'app/js/v4/features/livetv/selectors/selectMappedLiveTVChannels';
import dimensions from 'app/js/views/v3/styles/variables/dimensions';
import LiveTVSettingsChannel from './LiveTVSettingsChannel';
import styles from './LiveTVSettingsChannelList.css';

export default function LiveTVSettingsChannelList(): ReactElement {
  const dispatch = useDispatch();
  const [server, provider] = useSourceContext();
  const selectEPGLoadActivity = useMemo(
    () => createEPGLoadActivitySelector(server, provider),
    [provider, server]
  );
  const {
    guideChannels: { isPending },
  } = useSelector(selectLiveTV);
  const {
    lineupChannels: { error: lineupChannelsError },
  } = useSelector(selectEPGSettings);

  const channels = useSelector(selectMappedLiveTVChannels);
  const isRefreshing = !!useSelector(selectEPGLoadActivity);

  //
  // Effects

  useEffect(() => {
    if (!isRefreshing) {
      dispatch(
        liveTVSlice.actions.guideChannelsFetchRequested({ server, provider })
      );
    }
  }, [dispatch, server, provider, isRefreshing]);

  //
  // Render

  const layout = useMemo(() => {
    return createVerticalLayout({
      rowHeight: parseInt(dimensions.liveTVSettingsChannelItemHeight, 10),
      innerVerticalPadding: 2,
      overscan: 1,
    });
  }, []);

  const cellComponentFactory: VirtualCellFactory<GuideChannel> = useMemo(() => {
    return (cellProps) => {
      // `cellProps` will never have a null item, but VirtualCellFactory
      // anticipates being used with a paginated list which this is not. Type
      // narrow the item so that typescript is happy with out having to adjust
      // the types for VirtualCellFactory
      if (!cellProps.item) {
        return <></>;
      }

      return <LiveTVSettingsChannel {...cellProps} />;
    };
  }, []);

  const showSpinner = isRefreshing || isPending;

  return (
    <div className={styles.container}>
      {showSpinner ? (
        <div className={styles.emptyContainer}>
          <Spinner />
          {isRefreshing ? (
            <>
              <div className={styles.emptyMessage}>
                {t("We're now building your Guide")}
              </div>
              <div className={styles.emptyMessage}>
                {t("Hang tight while we download all the data for what's on")}
              </div>
            </>
          ) : null}
        </div>
      ) : null}

      {!showSpinner && channels.length > 0 ? (
        <VirtualListScroller
          cellComponentFactory={cellComponentFactory}
          className={styles.scroller}
          items={channels}
          layout={layout}
          totalSize={channels.length}
        />
      ) : null}

      {!showSpinner && channels.length === 0 ? (
        <div className={styles.emptyContainer}>
          <div className={styles.emptyMessage}>{t('Your Guide is empty')}</div>
          <div className={styles.emptyMessage}>
            {lineupChannelsError
              ? t(
                  'No channels were found. Delete the DVR and recreate it with a new lineup.'
                )
              : t('Please verify your channel mappings and refresh the Guide')}
          </div>
        </div>
      ) : null}
    </div>
  );
}
