/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import isFramed from 'app/js/utils/window/isFramed';
import SidebarListLink from 'app/js/views/v3/components/core/sidebar/SidebarListLink';
import styles from './SettingsSidebarListLink.css';

type SettingsSidebarListLinkProps = {
  className?: string;
  children?: React.ReactNode;
  selectableKey?: string;
  selectedKey?: string;
  to: string;
  title: string;
};

function SettingsSidebarListLink(props: SettingsSidebarListLinkProps) {
  const { className, children, selectableKey, selectedKey, to, title } = props;

  const isSelected = selectedKey && selectedKey === selectableKey;

  let target;
  if (!/^#/.test(to)) {
    target = isFramed() ? '_blank' : __EXTERNAL_LINK_TARGET__;
  }

  return (
    <SidebarListLink
      key={selectableKey}
      className={className}
      iconClassName={isSelected ? styles.selectedIcon : styles.icon}
      isSelected={isSelected}
      target={target}
      title={title}
      to={to}
    >
      {children}
    </SidebarListLink>
  );
}

export default SettingsSidebarListLink;
