import React from 'react';
import t from 'app/js/utils/t';
import SidebarMenuButton from 'app/js/views/v3/components/sidebar/menu/SidebarMenuButton';
import ServerSettingsStatusIcon from 'app/js/views/v3/components/sidebar/settings/ServerSettingsStatusIcon';
import getServerDetail from 'app/js/views/v3/helpers/getServerDetail';
import getServerDetailWithStatus from 'app/js/views/v3/helpers/getServerDetailWithStatus';
import getServerTitle from 'app/js/views/v3/helpers/getServerTitle';
import styles from './ServerSettingsServerMenuButton.css';

type ServerSettingsServerMenuButtonProps = {
  id: string;
  friendlyName: string;
  sourceTitle?: string;
  isFullOwnedServer: boolean;
  isShared: boolean;
  isConnected: boolean;
  isSelected: boolean;
  isInstallingServerUpdate: boolean;
  isSettingsPending: boolean;
  isUnavailable: boolean;
  isSecure: boolean;
  isRelayed: boolean;
  isMenuOpen: boolean;
  onPress: (...args: any[]) => any;
};

function ServerSettingsServerMenuButton(
  props: ServerSettingsServerMenuButtonProps
) {
  const {
    id,
    friendlyName,
    sourceTitle,
    isFullOwnedServer,
    isShared,
    isConnected,
    isInstallingServerUpdate,
    isSelected,
    isUnavailable,
    isSecure,
    isSettingsPending,
    isRelayed,
    isMenuOpen,
    onPress,
  } = props;

  const serverTitleAttrs = { friendlyName, sourceTitle, isShared };
  const statusWithDetail = getServerDetailWithStatus(
    getServerDetail(serverTitleAttrs),
    isRelayed ? t('Indirect') : null
  );

  return (
    // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: Element; className: string; seco... Remove this comment to see the full error message
    <SidebarMenuButton
      className={styles.button}
      id={id}
      isMenuOpen={isMenuOpen}
      isSelected={isSelected}
      primaryTitle={getServerTitle(serverTitleAttrs)}
      secondaryTitle={statusWithDetail}
      secondaryTitleClassName={styles.secondaryTitle}
      onPress={onPress}
    >
      <ServerSettingsStatusIcon
        className={styles.icon}
        isConnected={isConnected}
        isFullOwnedServer={isFullOwnedServer}
        isInstallingServerUpdate={isInstallingServerUpdate}
        isRelayed={isRelayed}
        isSecure={isSecure}
        isSettingsPending={isSettingsPending}
        isUnavailable={isUnavailable}
      />
    </SidebarMenuButton>
  );
}

export default ServerSettingsServerMenuButton;
