import { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { PayloadAction } from '@reduxjs/toolkit';
import { redispatchLegacyServerAction } from 'app/js/stores/actions/serverActions';
import { Provider } from 'app/js/v4/core/providers/providersTypes';
import { Server } from 'app/js/v4/core/servers/serversTypes';

export interface LegacyActionOptions {
  server: Server;
  provider: Provider;
}

export function useRedispatchLegacyServerAction({
  server,
  provider,
}: LegacyActionOptions): (options: PayloadAction) => unknown {
  const dispatch = useDispatch();

  return useCallback(
    (legacyAction: PayloadAction) => {
      dispatch(
        redispatchLegacyServerAction({
          server,
          provider,
          legacyAction,
        })
      );
    },
    [dispatch, provider, server]
  );
}
