/* eslint-plugin-disable @typescript-eslint */

function channelNumberComparator(a: any, b: any) {
  if (a === b) {
    return 0;
  }

  const aSplit = a.split('.');
  const bSplit = b.split('.');
  const len = Math.max(aSplit.length, bSplit.length);

  for (let i = 0; i < len; i++) {
    // In the case of `a` being 2.1 and `b` being 2 we want 2 to come
    // before 2.1 in the list, returning 1 treats 2.1 as being greater
    // than 2 and orders the results properly.
    // If `a` is 2 and `b` is 2.1, returning -1 keeps their order.
    if (!bSplit[i]) {
      return 1;
    }

    if (!aSplit[i]) {
      return -1;
    }

    const aValue = parseInt(aSplit[i], 10);
    const bValue = parseInt(bSplit[i], 10);

    if (aValue > bValue) {
      return 1;
    } else if (aValue < bValue) {
      return -1;
    }
  }

  return 0;
}

export default channelNumberComparator;
