import { PayloadAction } from '@reduxjs/toolkit';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import { PlainError } from 'app/js/v4/utils/data/api';
import { AsyncState } from 'app/js/v4/utils/data/asyncStateMarkers';

export enum BandwidthSourceType {
  All = 'all',
  Remote = 'remote',
  Local = 'local',
}

export interface BandwidthState extends AsyncState {
  statistics: BandwidthStatistics[];
  requestTimestampMilliseconds: number | null;
  responseTimestampMilliseconds: number | null;
}

export interface BandwidthStatistics {
  accountID: number;
  deviceID: number;
  timespan: number;
  at: number;
  lan: boolean;
  bytes: number;
}

export type BandwidthFetchRequestedAction = PayloadAction<{
  server: Server;
  timespan?: number;
  after?: number;
}>;

export type BandwidthFetchSucceededAction = PayloadAction<{
  data: [];
  requestTimestampMilliseconds: number;
  responseTimestampMilliseconds: number;
}>;

export type BandwidthFetchFailedAction = PayloadAction<{
  error: PlainError;
}>;
