/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import _ from 'underscore';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import serverSettingsSections from 'app/js/utils/constants/serverSettingsSections';
import ServerSettingsPageHeader from './ServerSettingsPageHeader';

function createSettingsGroupTitleSelector() {
  return createSelector(
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState: any, { settingsSection }: any) => settingsSection,
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState: any, { settingsGroup }: any) => settingsGroup,
    (settingsSection, settingsGroup) => {
      const section = _.findWhere(serverSettingsSections, {
        key: settingsSection,
      });
      // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
      const group = _.findWhere(section.groups, { key: settingsGroup });

      // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
      return group.title();
    }
  );
}

function createMapStateToProps() {
  return createSelector(
    createUnstableServerSelector(),
    createSettingsGroupTitleSelector(),
    (server, settingsGroupTitle) => {
      return {
        friendlyName: server ? server.friendlyName : '',
        sourceTitle: server ? server.sourceTitle : '',
        isShared: !!(server && server.isShared),
        settingsTitle: settingsGroupTitle,
      };
    }
  );
}

export default connect(createMapStateToProps)(ServerSettingsPageHeader);
