/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

function getScreenshotIndex(part: any, index: any) {
  if (!part.indexes) {
    return;
  }

  const indexes = part.indexes.split(',');
  if (!indexes.length) {
    return;
  }

  if (_.indexOf(indexes, index) > -1) {
    return index;
  } else {
    return indexes[0];
  }
}

export default getScreenshotIndex;
