import { createSelector, OutputSelector } from 'reselect';
import channelNumberComparator from 'app/js/utils/livetv/channelNumberComparator';
import {
  DeviceChannelsState,
  EPGSettingsState,
  LineupChannel,
  MediaGrabberDevice,
  SelectedChannelMapping,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import memoize from 'app/js/v4/utils/memoize';
import createDVRDeviceSelector from './createDVRDeviceSelector';
import createDeviceChannelsSelector from './createDeviceChannelsSelector';
import selectEPGSettings from './selectEPGSettings';
import selectLineupChannels from './selectLineupChannels';

function createChannelMappingsSelector(
  deviceKey?: MediaGrabberDevice['key']
): OutputSelector<
  LegacyRootState,
  SelectedChannelMapping[],
  (
    deviceChannels: DeviceChannelsState,
    epgSettings: EPGSettingsState,
    lineupChannels: LineupChannel[],
    dvrDevice: MediaGrabberDevice | undefined
  ) => SelectedChannelMapping[]
> {
  return createSelector(
    createDeviceChannelsSelector(deviceKey),
    selectEPGSettings,
    selectLineupChannels,
    createDVRDeviceSelector(deviceKey),
    (deviceChannels, epgSettings, lineupChannels, dvrDevice) => {
      const channelMappings = dvrDevice?.ChannelMapping
        ? dvrDevice.ChannelMapping
        : epgSettings.channelMappings.mappings;

      if (!deviceChannels?.isPopulated || channelMappings.length === 0) {
        return [];
      }

      const deviceChannelIdentifiers = deviceChannels.channels.map<string>(
        (channel) => channel.key
      );

      const channelMappingDeviceIdentifiers = channelMappings.map<string>(
        (mapping) => mapping.deviceIdentifier
      );

      const channelMappingKeys = deviceChannelIdentifiers.concat(
        channelMappingDeviceIdentifiers
      );
      const uniqueChannelMappingKeys = Array.from(new Set(channelMappingKeys));

      const mappedChannels = uniqueChannelMappingKeys.map((key) => {
        const deviceChannel = deviceChannels.channels.find(
          (channel) => channel.key === key
        );

        const channelMapping = channelMappings.find(
          (mapping) => mapping.deviceIdentifier === key
        );

        const lineupChannel = lineupChannels.find((channel) => {
          return (
            channel?.key === channelMapping?.channelKey ||
            channel?.identifier === channelMapping?.lineupIdentifier
          );
        });

        // deviceChannel may be undefined, in that case use an empty
        // object to destructure from.
        const {
          name = '',
          identifier = '',
          drm = false,
          signalStrength,
          signalQuality,
          hd = false,
        } = deviceChannel || {};

        const enabled = !!(deviceChannel && !drm && channelMapping?.enabled);

        return {
          key,
          name,
          deviceIdentifier: identifier,
          channelKey: channelMapping?.channelKey || lineupChannel?.key,
          lineupIdentifier:
            channelMapping?.lineupIdentifier || lineupChannel?.identifier,
          signalStrength,
          signalQuality,
          enabled,
          drm,
          isChannelMissing: !deviceChannel,
          isDeviceChannelHD: hd,
          isLineupChannelHD: lineupChannel?.hd ?? false,
        };
      });

      return mappedChannels.sort((a, b) => {
        return channelNumberComparator(a.deviceIdentifier, b.deviceIdentifier);
      });
    }
  );
}

export default memoize(createChannelMappingsSelector);
