/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import getErrorMessageFromXHR from 'app/js/utils/getErrorMessageFromXHR';
import t from 'app/js/utils/t';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import SpinnerButton from 'app/js/views/v3/components/core/buttons/SpinnerButton';
import Form from 'app/js/views/v3/components/core/forms/Form';
import FormSectionError from 'app/js/views/v3/components/core/forms/FormSectionError';
import SettingInputGroup from 'app/js/views/v3/components/core/forms/SettingInputGroup';
import ModalBody from 'app/js/views/v3/components/core/modals/ModalBody';
import ModalContent from 'app/js/views/v3/components/core/modals/ModalContent';
import ModalFooter from 'app/js/views/v3/components/core/modals/ModalFooter';
import ModalFooterButtonBar from 'app/js/views/v3/components/core/modals/ModalFooterButtonBar';
import ModalHeader from 'app/js/views/v3/components/core/modals/ModalHeader';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import DVRDeviceImage from './DVRDeviceImage';
import styles from './DVRDeviceSettingsModalContent.css';

type DVRDeviceSettingsModalContentProps = {
  serverEntityID: string;
  model: string;
  uri: string;
  thumb?: string;
  prefs: any[];
  title?: string;
  isSaving: boolean;
  saveError?: any;
  onSubmit: (...args: any[]) => any;
  onModalClose: (...args: any[]) => any;
};

class DVRDeviceSettingsModalContent extends Component<DVRDeviceSettingsModalContentProps> {
  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = { deviceTitle: props.title || '' };
  }

  componentDidUpdate(
    prevProps: DVRDeviceSettingsModalContentProps,
    // @ts-expect-error ts-migrate(6133) FIXME: 'prevState' is declared but its value is never rea... Remove this comment to see the full error message
    prevState: any
  ) {
    const shouldCloseModal =
      prevProps.isSaving &&
      !this.props.isSaving &&
      this.props.saveError == null;

    if (shouldCloseModal) {
      this.props.onModalClose();
    }
  }

  //
  // Listeners

  onDeviceTitleChange = (value: any) => {
    this.setState({
      deviceTitle: value,
    });
  };

  //
  // Render

  render() {
    const {
      serverEntityID,
      model,
      uri,
      thumb,
      prefs,
      isSaving,
      saveError,
      onSubmit,
      onModalClose,
    } = this.props;

    return (
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: Element; className: string; onSu... Remove this comment to see the full error message
      <Form className={styles.form} onSubmit={onSubmit}>
        <ModalContent onModalClose={onModalClose}>
          <ModalHeader>{t('Device Settings')}</ModalHeader>

          {saveError ? (
            <FormSectionError
              errorMessage={getErrorMessageFromXHR(
                saveError,
                t('There was a problem saving your changes. Please try again.')
              )}
            />
          ) : null}

          <ModalBody>
            <div className={styles.container}>
              <DVRDeviceImage
                className={styles.deviceImage}
                height={150}
                model={model}
                serverEntityID={serverEntityID}
                thumb={thumb}
                width={150}
              />

              <div className={styles.info}>
                <div className={styles.model}>{model}</div>

                <div>{uri}</div>

                <div className={styles.settings}>
                  <SettingInputGroup
                    id="deviceTitle"
                    label={t('Device Name')}
                    type="text"
                    // @ts-expect-error ts-migrate(2322) FIXME: Type '{ name: string; value: any; labelText: strin... Remove this comment to see the full error message
                    value={this.state.deviceTitle}
                  />

                  {prefs.map((pref) => {
                    const { id } = pref;
                    return <SettingInputGroup key={id} {...pref} />;
                  })}
                </div>
              </div>
            </div>
          </ModalBody>

          <ModalFooter>
            <ModalFooterButtonBar>
              <Button
                kind={kinds.DEFAULT}
                size={sizes.MEDIUM}
                onPress={onModalClose}
              >
                {t('Cancel')}
              </Button>

              <SpinnerButton
                isSpinning={isSaving}
                kind={kinds.PRIMARY}
                size={sizes.MEDIUM}
                type="submit"
              >
                {t('Save')}
              </SpinnerButton>
            </ModalFooterButtonBar>
          </ModalFooter>
        </ModalContent>
      </Form>
    );
  }
}

export default DVRDeviceSettingsModalContent;
