import React, {
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import ServerDashboardPageHeader from 'app/js/v4/features/dashboard/components/ServerDashboardPageHeader';
import ServerDashboardPageSection from 'app/js/v4/features/dashboard/components/ServerDashboardPageSection';
import ServerDashboardFilterMenuButton from 'app/js/v4/features/dashboard/components/menuButtons/ServerDashboardFilterMenuButton';
import dashboardResourcesSlice from 'app/js/v4/features/dashboard/dashboardResourcesSlice';
import { ServerResourceType } from 'app/js/v4/features/dashboard/dashboardResourcesTypes';
import selectDashboardResources from 'app/js/v4/features/dashboard/selectors/selectDashboardResources';
import getServerResourceTypeLabel from 'app/js/v4/features/dashboard/utils/getServerResourceTypeLabel';
import ServerResourceGraph from './ServerResourceGraph';
import styles from './ServerResources.css';

const DATA_FETCH_MIN_INTERVAL = 5000;

export default function ServerResources(): ReactElement | null {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const realtimeTimeoutIDRef = useRef<number | null>(null);

  const [activeCPUResourceType, setActiveCPUResourceType] = useState(
    ServerResourceType.All
  );

  const [activeRAMResourceType, setActiveRAMResourceType] = useState(
    ServerResourceType.All
  );

  const resourceTypes = useMemo(() => {
    return [
      ServerResourceType.All,
      ServerResourceType.PlexMediaServer,
      ServerResourceType.System,
    ];
  }, []);

  const {
    statistics,
    isPopulated,
    isUnsupported,
    error,
    requestTimestampMilliseconds,
  } = useSelector(selectDashboardResources);

  //
  // Callbacks

  const onFetch = useCallback(() => {
    dispatch(
      dashboardResourcesSlice.actions.resourcesFetchRequested({
        server,
      })
    );
  }, [dispatch, server]);

  const onCPUSourceTypeSelect = useCallback(
    (cpuResourceType: ServerResourceType) => {
      setActiveCPUResourceType(cpuResourceType);
    },
    []
  );

  const onRAMSourceTypeSelect = useCallback(
    (ramResourceType: ServerResourceType) => {
      setActiveRAMResourceType(ramResourceType);
    },
    []
  );

  const onGraphCPUResourceTypeSelect = useCallback(
    (cpuResourceType: ServerResourceType) => {
      const nextCPUResourceType =
        cpuResourceType === activeCPUResourceType
          ? ServerResourceType.All
          : cpuResourceType;

      setActiveCPUResourceType(nextCPUResourceType);
    },
    [activeCPUResourceType]
  );

  const onGraphRAMResourceTypeSelect = useCallback(
    (ramResourceType: ServerResourceType) => {
      const nextRAMResourceType =
        ramResourceType === activeRAMResourceType
          ? ServerResourceType.All
          : ramResourceType;

      setActiveRAMResourceType(nextRAMResourceType);
    },
    [activeRAMResourceType]
  );

  const onTooltipActive = useCallback(() => {
    if (realtimeTimeoutIDRef.current) {
      window.clearTimeout(realtimeTimeoutIDRef.current);
    }
  }, []);

  //
  // Effects

  useEffect(() => {
    onFetch();
  }, [onFetch]);

  useEffect(() => {
    // If the server resources statistics have just been fetched, then fire off
    // the next request with a delay.
    if (requestTimestampMilliseconds) {
      const requestLatency = Date.now() - requestTimestampMilliseconds;
      const delay = Math.max(DATA_FETCH_MIN_INTERVAL - requestLatency, 0);

      realtimeTimeoutIDRef.current = window.setTimeout(onFetch, delay);
    }

    return () => {
      if (realtimeTimeoutIDRef.current) {
        window.clearTimeout(realtimeTimeoutIDRef.current);
      }
    };
  }, [requestTimestampMilliseconds, onFetch]);

  useEffect(() => {
    return () => {
      dispatch(dashboardResourcesSlice.actions.reset());
    };
  }, [dispatch]);

  //
  // Render

  if (isUnsupported) {
    return null;
  }

  return (
    <>
      <ServerDashboardPageSection>
        <div className={styles.container}>
          <ServerDashboardPageHeader title={t('CPU')}>
            <div>
              <ServerDashboardFilterMenuButton
                activeValue={activeCPUResourceType}
                formatter={getServerResourceTypeLabel}
                values={resourceTypes}
                onSelect={onCPUSourceTypeSelect}
              />
            </div>
          </ServerDashboardPageHeader>

          <div className={styles.graph}>
            <ServerResourceGraph
              activeResourceType={activeCPUResourceType}
              errorMessage={error ? t('Unable to load statistics') : undefined}
              hostColor="#9ad186"
              hostKey="hostCpuUtilization"
              isPopulated={isPopulated}
              processColor="#f1335e"
              processKey="processCpuUtilization"
              statistics={statistics}
              onGraphResourceTypeSelect={onGraphCPUResourceTypeSelect}
              onGraphTooltipActive={onTooltipActive}
              onGraphTooltipInactive={onFetch}
            />
          </div>
        </div>
      </ServerDashboardPageSection>

      <ServerDashboardPageSection>
        <div className={styles.container}>
          <ServerDashboardPageHeader title={t('RAM')}>
            <div>
              <ServerDashboardFilterMenuButton
                activeValue={activeRAMResourceType}
                formatter={getServerResourceTypeLabel}
                values={resourceTypes}
                onSelect={onRAMSourceTypeSelect}
              />
            </div>
          </ServerDashboardPageHeader>

          <div className={styles.graph}>
            <ServerResourceGraph
              activeResourceType={activeRAMResourceType}
              errorMessage={error ? t('Unable to load statistics') : undefined}
              hostColor="#33f1c5"
              hostKey="hostMemoryUtilization"
              isPopulated={isPopulated}
              processColor="#bd86d1"
              processKey="processMemoryUtilization"
              statistics={statistics}
              onGraphResourceTypeSelect={onGraphRAMResourceTypeSelect}
              onGraphTooltipActive={onTooltipActive}
              onGraphTooltipInactive={onFetch}
            />
          </div>
        </div>
      </ServerDashboardPageSection>
    </>
  );
}
