import React from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import createIsServerClaimedSelector from 'app/js/stores/selectors/server/createIsServerClaimedSelector';
import ClaimedServerConnector from './ClaimedServerConnector';
import UnclaimedServerConnector from './UnclaimedServerConnector';

function createMapStateToProps() {
  return createSelector(createIsServerClaimedSelector(), (isServerClaimed) => {
    return {
      isServerClaimed,
    };
  });
}

type ClaimServerConnectorProps = {
  isServerClaimed: boolean;
};

function ClaimServerConnector(props: ClaimServerConnectorProps) {
  const { isServerClaimed, ...otherProps } = props;

  if (isServerClaimed) {
    return <ClaimedServerConnector {...otherProps} />;
  } else {
    // @ts-expect-error ts-migrate(2739) FIXME: Type '{}' is missing the following properties from... Remove this comment to see the full error message
    return <UnclaimedServerConnector {...otherProps} />;
  }
}

export default connect(createMapStateToProps)(ClaimServerConnector);
