/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import { createServerSettingsSidebarKey } from 'app/js/utils/settingsSidebarKey';
import SidebarServerUnavailableMessageConnector from 'app/js/views/v3/components/sidebar/server/SidebarServerUnavailableMessageConnector';
import SettingsSidebarList from 'app/js/views/v3/components/sidebar/settings/SettingsSidebarList';
import SettingsSidebarListLink from 'app/js/views/v3/components/sidebar/settings/SettingsSidebarListLink';

type ServerSettingsSidebarListProps = {
  serverEntityID: string;
  machineIdentifier: string;
  friendlyName: string;
  selectedKey?: string;
  isShared: boolean;
  isUnavailable: boolean;
  isSettingsPending: boolean;
  isInstallingServerUpdate: boolean;
  settingsSections: {
    key: string;
    title: string;
    groups: {
      key: string;
      title: string;
      statusIndicator?: React.ReactNode;
      route: string;
    }[];
  }[];
};

function ServerSettingsSidebarList(props: ServerSettingsSidebarListProps) {
  const {
    serverEntityID,
    machineIdentifier,
    selectedKey,
    isShared,
    isInstallingServerUpdate,
    isUnavailable,
    isSettingsPending,
    settingsSections,
  } = props;

  if (isInstallingServerUpdate || isUnavailable) {
    return (
      <SidebarServerUnavailableMessageConnector
        serverEntityID={serverEntityID}
      />
    );
  }

  if (isSettingsPending) {
    return null;
  }

  return settingsSections.map((settingsSection, sectionIndex) => {
    if (isShared && settingsSection.groups.length === 1) {
      const group = settingsSection.groups[0];

      return (
        <SettingsSidebarListLink
          key={sectionIndex}
          selectableKey={createServerSettingsSidebarKey(
            machineIdentifier,
            settingsSection.key,
            group.key
          )}
          selectedKey={selectedKey}
          title={group.title}
          to={group.route}
        />
      );
    }

    return (
      <SettingsSidebarList key={sectionIndex} title={settingsSection.title}>
        {settingsSection.groups.map((settingsGroup, groupIndex) => (
          <SettingsSidebarListLink
            key={`${sectionIndex}${groupIndex}`}
            selectableKey={createServerSettingsSidebarKey(
              machineIdentifier,
              settingsSection.key,
              settingsGroup.key
            )}
            selectedKey={selectedKey}
            title={settingsGroup.title}
            to={settingsGroup.route}
          >
            {settingsGroup.statusIndicator}
          </SettingsSidebarListLink>
        ))}
      </SettingsSidebarList>
    );
  });
}

export default ServerSettingsSidebarList;
