/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import assert from 'app/js/utils/log/assert';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';
import styles from './Measure.css';

interface MeasureProps {
  id?: string;
  className?: string;
  style?: any;
  ignoreWidth?: boolean;
  ignoreHeight?: boolean;
  ignorePosition?: boolean;
  ignoreTransforms?: boolean;
  resizeTimeout: number;
  onMeasure?: (...args: any[]) => any;
  onContainerRef?: (...args: any[]) => any;
  onKeyDown?: (...args: any[]) => any;
}
type MeasureState = any;
class Measure extends Component<MeasureProps, MeasureState> {
  static defaultProps = {
    className: styles.container,
    ignoreWidth: false,
    ignoreHeight: false,
    ignorePosition: false,
    ignoreTransforms: false,
    resizeTimeout: 100,
  };
  // eslint-disable-next-line react/sort-comp
  _container: any;
  _expandContainer: any;
  _frameRequestID: any;
  _measureTimeoutID: any;
  _shrinkContainer: any;
  //
  // Lifecycle
  constructor(props: any, context: any) {
    super(props, context);
    const { ignoreWidth, ignoreHeight, ignorePosition } = props;
    // The `scrollWidth` property provides a consistent value for containers
    // which overflow their `offsetParent` on both X and Y axis. Other width
    // access methods return values influenced by the container's `position`
    // style in conjunction with the axis which they overflow their
    // `offsetParent`.
    this.state = {
      isMeasured: false,
      width: ignoreWidth ? undefined : 0,
      scrollWidth: ignoreWidth ? undefined : 0,
      height: ignoreHeight ? undefined : 0,
      scrollHeight: ignoreHeight ? undefined : 0,
      x: ignorePosition ? undefined : 0,
      y: ignorePosition ? undefined : 0,
    };
  }
  componentDidMount() {
    this._requestFrame();
  }
  shouldComponentUpdate(nextProps: MeasureProps, nextState: MeasureState) {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    return shouldPureComponentUpdate(this, nextProps, nextState);
  }
  componentWillUnmount() {
    this._cancelFrame();
    this._clearTimeout();
  }
  //
  // Control
  _requestFrame = () => {
    this._clearTimeout();
    this._cancelFrame();
    this._frameRequestID = window.requestAnimationFrame(this._measure);
  };
  _requestTimeout = () => {
    this._clearTimeout();
    this._cancelFrame();
    this._measureTimeoutID = window.setTimeout(
      this._requestFrame,
      this.props.resizeTimeout
    );
  };
  _cancelFrame() {
    if (this._frameRequestID) {
      this._frameRequestID = window.cancelAnimationFrame(this._frameRequestID);
    }
  }
  _clearTimeout() {
    if (this._measureTimeoutID) {
      this._measureTimeoutID = window.clearTimeout(this._measureTimeoutID);
    }
  }
  _measure = () => {
    const { ignoreWidth, ignoreHeight, ignorePosition, ignoreTransforms } =
      this.props;
    const { isMeasured, width, scrollWidth, height, scrollHeight, x, y } =
      this.state;
    const container = this._container;
    if (!container) {
      // container is null so do no work. _setContainerRef will get called with
      // `null` when the element changes or is unmounted. Guard when _measure
      // gets called during this case.
      return;
    }
    __DEV__ &&
      assert(
        window.getComputedStyle(container).position !== 'static',
        'The `Measure` container needs a `position` style other than `static`'
      );
    const boundingRect = container.getBoundingClientRect();
    // When there are transforms applied to the element, offsetWidth and
    // offsetHeight return its layout width and height, while
    // getBoundingClientRect() returns the rendering width and height.
    // See: https://developer.mozilla.org/en-US/docs/Web/API/CSS_Object_Model/Determining_the_dimensions_of_elements
    const containerWidth = ignoreTransforms
      ? container.offsetWidth
      : boundingRect.width;
    const containerHeight = ignoreTransforms
      ? container.offsetHeight
      : boundingRect.height;
    const hasMeasuredChanged = isMeasured !== true;
    const hasPositionChanged =
      !ignorePosition && (boundingRect.left !== x || boundingRect.top !== y);
    const hasWidthChanged =
      !ignoreWidth &&
      (containerWidth !== width || container.scrollWidth !== scrollWidth);
    const hasHeightChanged =
      !ignoreHeight &&
      (containerHeight !== height || container.scrollHeight !== scrollHeight);
    // Reset before notifying `onMeasure` to avoid any complications arising if
    // the `Measure` component is unmounted during the `onMeasure` callstack.
    this._reset();
    if (
      hasMeasuredChanged ||
      hasPositionChanged ||
      hasWidthChanged ||
      hasHeightChanged
    ) {
      const style = window.getComputedStyle(container);
      const measurements = { isMeasured: true };
      if (!ignorePosition) {
        (measurements as any).x = hasPositionChanged ? boundingRect.left : x;
        (measurements as any).y = hasPositionChanged ? boundingRect.top : y;
      }
      if (!ignoreWidth) {
        const paddingRight = parseInt(style.paddingRight, 10) || 0;
        const paddingLeft = parseInt(style.paddingLeft, 10) || 0;
        (measurements as any).width = hasWidthChanged
          ? containerWidth - paddingLeft - paddingRight
          : width;
        (measurements as any).scrollWidth = hasWidthChanged
          ? container.scrollWidth
          : scrollWidth;
      }
      if (!ignoreHeight) {
        const paddingTop = parseInt(style.paddingTop, 10) || 0;
        const paddingBottom = parseInt(style.paddingBottom, 10) || 0;
        (measurements as any).height = hasHeightChanged
          ? containerHeight - paddingTop - paddingBottom
          : height;
        (measurements as any).scrollHeight = hasHeightChanged
          ? container.scrollHeight
          : scrollHeight;
      }
      this.setState(measurements);
      if (this.props.onMeasure) {
        this.props.onMeasure(measurements);
      }
    }
  };
  _reset() {
    const expandContainer = this._expandContainer;
    const shrinkContainer = this._shrinkContainer;
    expandContainer.scrollTop = 100000;
    expandContainer.scrollLeft = 100000;
    shrinkContainer.scrollTop = 100000;
    shrinkContainer.scrollLeft = 100000;
  }
  _setContainerRef = (ref: any) => {
    this._container = ref;
    const { onContainerRef } = this.props;
    if (onContainerRef) {
      onContainerRef(ref);
    }
  };
  _setExpandContainerRef = (ref: any) => {
    this._expandContainer = ref;
  };
  _setShrinkContainerRef = (ref: any) => {
    this._shrinkContainer = ref;
  };
  //
  // Listeners
  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onScroll = (event: any) => {
    this._requestTimeout();
  };
  //
  // Render
  render() {
    const { id, className, children, style, onKeyDown, onMeasure } = this.props;
    const isControlled = typeof children !== 'function';
    if (__DEV__) {
      assert(
        onMeasure || !isControlled,
        `There are two ways to use the \`Measure\` component:
        1. Use a \`children\` function that returns a node, e.g. \`({ width, height }) => PropTypes.node\`
        2. Treat \`Measure\` as a controlled component with an \`onMeasure\` callback and \`children\` node`
      );
    }
    return (
      <div
        ref={this._setContainerRef}
        className={className}
        id={id}
        style={style}
        onKeyDown={onKeyDown}
      >
        {isControlled ? children : children(this.state)}

        <div
          ref={this._setExpandContainerRef}
          className={styles.scrollContainer}
          onScroll={this.onScroll}
        >
          <div className={styles.expandContent} />
        </div>

        <div
          ref={this._setShrinkContainerRef}
          className={styles.scrollContainer}
          onScroll={this.onScroll}
        >
          <div className={styles.shrinkContent} />
        </div>
      </div>
    );
  }
}
export default Measure;
