/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import * as serverActions from 'app/js/stores/actions/serverActions';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import createVisibleSettingsSectionsSelector from 'app/js/stores/selectors/settings/createVisibleSettingsSectionsSelector';
import ServerSettingsSidebarList from './ServerSettingsSidebarList';

function createMapStateToProps() {
  return createSelector(
    createUnstableServerSelector(),
    createVisibleSettingsSectionsSelector(),
    (server, settingsSections) => {
      return {
        machineIdentifier: server.machineIdentifier,
        friendlyName: server.friendlyName,
        isShared: server.isShared,
        isInstallingServerUpdate: server.isInstallingServerUpdate,
        isConnected: server.isConnected,
        isUnavailable: server.isUnavailable,
        isSettingsPending:
          server.isFullOwnedServer && server[__PLATFORM__].isSettingsPending,
        isFullOwnedServer: server.isFullOwnedServer,
        settingsSections,
      };
    }
  );
}

const mapDispatchToProps = {
  dispatchPopulateSettings: serverActions.populateSettings,
};

type ServerSettingsSidebarListConnectorProps = {
  serverEntityID: string;
  isConnected?: boolean;
  isFullOwnedServer?: boolean;
  dispatchPopulateSettings: (...args: any[]) => any;
};

class ServerSettingsSidebarListConnector extends Component<ServerSettingsSidebarListConnectorProps> {
  //
  // Lifecycle

  componentDidMount() {
    this._populate();
  }

  componentDidUpdate(prevProps: ServerSettingsSidebarListConnectorProps) {
    if (prevProps.serverEntityID !== this.props.serverEntityID) {
      this._populate();
    }
  }

  //
  // Control

  _populate() {
    const {
      serverEntityID,
      isConnected,
      isFullOwnedServer,
      dispatchPopulateSettings,
    } = this.props;

    if (isConnected && isFullOwnedServer) {
      dispatchPopulateSettings({ serverEntityID });
    }
  }

  //
  // Render

  render() {
    const {
      isConnected,
      isFullOwnedServer,
      dispatchPopulateSettings,
      ...otherProps
    } = this.props;

    // @ts-expect-error ts-migrate(2740) FIXME: Type '{ serverEntityID: string; children?: ReactNo... Remove this comment to see the full error message
    return <ServerSettingsSidebarList {...otherProps} />;
  }
}

export default connect(
  createMapStateToProps,
  mapDispatchToProps
)(ServerSettingsSidebarListConnector);
