import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import t from 'app/js/utils/t';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setti... Remove this comment to see the full error message
import tpl from 'app/templates/settings/server/ServerSettingsPane.tpl';

const ServerSettingsPane = Marionette.Layout.extend({
  //
  // Properties

  className: 'form-group',
  template: tpl,

  isDisabled: true,
  isActive: false,

  ui: {
    submitBtn: '.submit-btn',
    message: '.form-message',
  },

  events: {
    'change #server-settings-form input[name]': 'onInputChange',
    'input #server-settings-form input[type=text]': 'onInputChange',
    'change #server-settings-form select[name]': 'onInputChange',
    'submit #server-settings-form': 'onSubmit',
  },

  //
  // Init

  initialize: function (options: any) {
    this.changes = options.changes;
  },

  //
  // Control

  serializeData: function () {
    return _.where(this.collection.toJSON(), {
      group: this.options.group,
      hidden: false,
    });
  },

  enable: function () {
    this.isDisabled = false;
    this.ui.submitBtn.removeClass('disabled');
    this.ui.message.text('');
    this.ui.message.removeClass('has-error');
  },

  updateModel: function (key: any, value: any) {
    const setting = this.collection.get(key);
    if (setting) {
      return setting.set('value', value);
    }
  },

  //
  // Listeners

  onRender: function () {
    if (!_.isEmpty(this.changes)) {
      this.enable();
    }
  },

  onInputChange: function (event: any) {
    const target = event.currentTarget;
    const key = target.name;
    let change = target.value;
    let value = target.value;

    // Extensions (LastPass) can trigger input changes we should ignore.
    if (!key) {
      return;
    }

    if (target.type === 'checkbox') {
      value = target.checked;

      if (!value) {
        change = '0';
      }
    }

    // Only recognize the change if we found a corresponding model, just as
    // an extra safeguard against extensions.
    const setting = this.updateModel(key, value);
    if (setting) {
      this.changes[target.name] = change;
      this.enable();
    }
  },

  onSubmit: function (event: any) {
    event.preventDefault();

    this.trigger('save');
  },

  onBeforeSave: function () {
    this.isActive = true;
    this.ui.submitBtn.addClass('active');
  },

  onAfterSave: function (isSuccess: any) {
    this.isActive = false;
    this.ui.submitBtn.removeClass('active');

    if (isSuccess) {
      // Disable the form after a successful save until further changes.
      this.isDisabled = true;
      this.ui.submitBtn.addClass('disabled');
      this.ui.message.removeClass('has-error');
      this.ui.message.text(t('Your changes have been saved.'));
    } else {
      this.ui.message.addClass('has-error');
      this.ui.message.text(t('Your changes could not be saved.'));
    }
  },
});

export default ServerSettingsPane;
