import React, { ReactElement, ReactNode } from 'react';
import styles from './ModalFooterButtonBar.css';

export interface ModalFooterButtonBarProps {
  children: ReactNode;
  className?: string;
}

export default function ModalFooterButtonBar({
  children,
  className = styles.buttonBar,
}: ModalFooterButtonBarProps): ReactElement {
  return <div className={className}>{children}</div>;
}
