/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import ServerLogLine from './ServerLogLine';

function createMapStateToProps() {
  return createSelector(
    (appState) => appState.ui.serverLogPage.highlightedLineIndex,
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState: any, { index }: any) => index,
    (highlightedLineIndex, index) => {
      return {
        isHighlighted: highlightedLineIndex === index,
      };
    }
  );
}

export default connect(createMapStateToProps)(ServerLogLine);
