import React from 'react';
import PlexPassUpsell from 'app/js/views/v3/components/core/PlexPassUpsell';
import styles from './PlexPassUpsellInfo.css';

type PlexPassUpsellInfoProps = {
  className: string;
  moreInfoClassName: string;
  iconComponent: React.ReactElement;
  title: string;
  text: string;
  moreInfo: React.ReactNode;
  children?: React.ReactNode;
};

export default function PlexPassUpsellInfo(props: PlexPassUpsellInfoProps) {
  const {
    className,
    moreInfoClassName,
    iconComponent,
    title,
    text,
    moreInfo,
    children,
  } = props;

  return (
    <div className={className}>
      <PlexPassUpsell
        className={styles.upsell}
        iconComponent={iconComponent}
        text={text}
        title={title}
      />

      <div className={moreInfoClassName}>{moreInfo}</div>
      {children}
    </div>
  );
}

PlexPassUpsellInfo.defaultProps = {
  className: styles.info,
  moreInfoClassName: styles.moreInfo,
};
