import { createSelector, OutputSelector } from 'reselect';
import {
  DVRSetupState,
  MediaGrabberDevice,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import selectDVRSetup from './selectDVRSetup';

function createDVRDeviceSelector(
  deviceKey?: MediaGrabberDevice['key']
): OutputSelector<
  LegacyRootState,
  MediaGrabberDevice | undefined,
  (dvrSetupState: DVRSetupState) => MediaGrabberDevice | undefined
> {
  return createSelector(selectDVRSetup, ({ selectedDVR }) => {
    const { dvr } = selectedDVR;

    if (!dvr?.Device?.length) {
      return;
    }

    return dvr.Device?.find((device) => device.key === deviceKey);
  });
}

export default createDVRDeviceSelector;
