/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import { discoverServers } from 'app/js/stores/actions/appActions';
import * as serverActions from 'app/js/stores/actions/serverActions';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import selectCloudServer from 'app/js/stores/selectors/server/selectCloudServer';
import connectWithMetrics from 'app/js/views/v3/decorators/connectWithMetrics';
import SidebarServerUnavailableMessage from './SidebarServerUnavailableMessage';

function createMapStateToProps() {
  return createSelector(
    selectCloudServer,
    createUnstableServerSelector(),
    (cloudServer, server) => {
      return {
        machineIdentifier: server.machineIdentifier,
        friendlyName: server.friendlyName,
        presence: server.presence,
        isPending: server.isPending,
        isFullServer: server.isFullServer,
        isFullOwnedServer: server.isFullOwnedServer,
        isInstallingServerUpdate: server.isInstallingServerUpdate,
        isBundled: server.isBundled,
        isCloudServerResourcesPending: cloudServer.isResourcesPending,
        httpsRequired: server.httpsRequired,
        hasHttpsConnections: server.hasHttpsConnections,
        hasFallbackConnections: server.hasFallbackConnections,
      };
    }
  );
}

function createMapDispatchToProps(dispatch: any, props: any) {
  const payload = {
    serverEntityID: props.serverEntityID,
    metricsPage: props.metricsPage,
  };

  return {
    onDeleteServerConfirm() {
      dispatch(serverActions.deleteServer(payload));
    },

    onTryAgainPress() {
      dispatch(discoverServers());
      dispatch(serverActions.testServer(payload));
    },
  };
}

export default connectWithMetrics(
  createMapStateToProps,
  createMapDispatchToProps
)(SidebarServerUnavailableMessage);
