import { ReactElement, useEffect, useMemo, useRef } from 'react';
import {
  Layout,
  RangeOptions,
  VirtualCellFactory,
  VirtualCellProps,
  VirtualListRanges,
} from 'app/js/v4/core/ui/lists/listsTypes';
import usePrevious from 'app/js/views/v3/helpers/hooks/usePrevious';

// Keep the cells as a separate component from the list and memoize the cells to
// avoid re-rendering every time the scroll position changes. Instead, we should
// only re-render when the render / visible indices change.

export interface VirtualListCellsProps<
  ItemType,
  ItemKeyProp,
  ListMeasurementsType
> {
  keyPropName?: ItemKeyProp;
  items: (ItemType | undefined)[];
  measurements: ListMeasurementsType;
  layout: Layout<ListMeasurementsType>;
  totalSize: number;
  renderStartIndex: number;
  renderEndIndex: number;
  visibleStartIndex: number;
  visibleEndIndex: number;
  cellComponentFactory: VirtualCellFactory<ItemType>;
  minPageCount?: number;
  minPageSize?: number;
  pageRequestDelay?: number;
  onPageRequest?(rangeOptions: RangeOptions): void;
  onListUpdate?(ranges: VirtualListRanges): void;
}

function useVirtualListCells<
  ItemType,
  ItemKeyProp extends keyof ItemType,
  ListMeasurementsType
>({
  keyPropName,
  items,
  totalSize,
  layout,
  measurements,
  renderStartIndex,
  renderEndIndex,
  visibleStartIndex,
  visibleEndIndex,
  cellComponentFactory,
  minPageCount,
  minPageSize,
  pageRequestDelay,
  onPageRequest,
  onListUpdate,
}: VirtualListCellsProps<
  ItemType,
  ItemKeyProp,
  ListMeasurementsType
>): ReactElement<VirtualCellProps<ItemType>>[] {
  // Keep the previous start index to know whether the list is being scrolled
  // forward or backward.
  const prevRenderStartIndex = usePrevious(renderStartIndex);
  const isScrollingBackRef = useRef(false);

  // There may be a re-render in which `renderStartIndex` doesn't change and the
  // previous value would equal the current value. Keep using the last
  // `isScrollingBack` value in this case.

  if (renderStartIndex !== prevRenderStartIndex) {
    isScrollingBackRef.current =
      prevRenderStartIndex == null
        ? false
        : renderStartIndex < prevRenderStartIndex;
  }

  //
  // Effects

  useEffect(() => {
    let timeoutID: number;

    if (onPageRequest && minPageCount && minPageSize) {
      // In a virtualized list, not every cell will have an item immediately. An
      // empty cell is a signal that more items need to be fetched. Look past
      // the currently rendered range and see if more items will need to be
      // fetched.

      const isScrollingBack = isScrollingBackRef.current;
      const renderSize = renderEndIndex - renderStartIndex + 1;
      const pageSize = Math.max(renderSize * minPageCount, minPageSize);

      let hasMissingItem = false;
      let i: number;

      if (isScrollingBack) {
        const start = Math.max(renderEndIndex - pageSize + 1, 0);
        i = renderEndIndex;

        while (i >= start) {
          if (items[i] == null) {
            hasMissingItem = true;
            break;
          }

          i--;
        }
      } else {
        const end = Math.min(renderStartIndex + pageSize - 1, totalSize - 1);
        i = renderStartIndex;

        while (i <= end) {
          if (items[i] == null) {
            hasMissingItem = true;
            break;
          }

          i++;
        }
      }

      if (hasMissingItem) {
        timeoutID = window.setTimeout(() => {
          onPageRequest({
            startIndex: i,
            size: pageSize,
            isPrepend: isScrollingBack,
          });
        }, pageRequestDelay);
      }
    }

    return () => window.clearTimeout(timeoutID);
  }, [
    items,
    minPageCount,
    minPageSize,
    onPageRequest,
    pageRequestDelay,
    prevRenderStartIndex,
    renderEndIndex,
    renderStartIndex,
    totalSize,
  ]);

  useEffect(() => {
    if (onListUpdate) {
      onListUpdate({
        renderStartIndex,
        renderEndIndex,
        visibleStartIndex,
        visibleEndIndex,
      });
    }
  }, [
    onListUpdate,
    renderEndIndex,
    renderStartIndex,
    visibleEndIndex,
    visibleStartIndex,
  ]);

  //
  // Render

  return useMemo(() => {
    const cells = [];

    for (let i = renderStartIndex; i <= renderEndIndex; i++) {
      const item = items[i];
      const isVisible = i >= visibleStartIndex && i <= visibleEndIndex;
      const position = layout.getPositionForIndex(i, measurements);

      cells.push(
        cellComponentFactory({
          key: item && keyPropName ? String(item[keyPropName]) : String(i),
          isVirtual: true,
          isVisible,
          cellIndex: i,
          item,
          ...position,
        })
      );
    }

    return cells;
  }, [
    cellComponentFactory,
    items,
    keyPropName,
    layout,
    measurements,
    renderEndIndex,
    renderStartIndex,
    visibleEndIndex,
    visibleStartIndex,
  ]);
}

export default useVirtualListCells;
