import _ from 'underscore';
import alexaImg from 'app/img/devices/alexa.svg';
import androidImg from 'app/img/devices/android.svg';
import atvImg from 'app/img/devices/atv.svg';
import chromeImg from 'app/img/devices/chrome.svg';
import dashImg from 'app/img/devices/dash.svg';
import defaultImg from 'app/img/devices/default.svg';
import dlnaImg from 'app/img/devices/dlna.svg';
import edgeImg from 'app/img/devices/edge.svg';
import firefoxImg from 'app/img/devices/firefox.svg';
import gtvImg from 'app/img/devices/gtv.svg';
import ieImg from 'app/img/devices/ie.svg';
import iosImg from 'app/img/devices/ios.svg';
import linuxImg from 'app/img/devices/linux.svg';
import operaImg from 'app/img/devices/opera.svg';
import osxImg from 'app/img/devices/osx.svg';
import phtImg from 'app/img/devices/pht.svg';
import plexampImg from 'app/img/devices/plexamp.svg';
import pmsImg from 'app/img/devices/pms.svg';
import psxImg from 'app/img/devices/psx.svg';
import rokuImg from 'app/img/devices/roku.svg';
import safariImg from 'app/img/devices/safari.svg';
import samsungImg from 'app/img/devices/samsung.svg';
import sonosImg from 'app/img/devices/sonos.svg';
import vrImg from 'app/img/devices/vr.svg';
import wiiuImg from 'app/img/devices/wiiu.svg';
import win8Img from 'app/img/devices/win8.svg';
import wpImg from 'app/img/devices/wp.svg';
import xboxImg from 'app/img/devices/xbox.svg';

const iconMap = {
  [alexaImg]: /alexa/i,
  [androidImg]: /android/i,
  [atvImg]: /plex.*atv|tvos/i,
  [chromeImg]: /chrome/i,
  [dashImg]: /plex.*dash/i,
  [dlnaImg]: /dlna/i,
  [edgeImg]: /edge/i,
  [firefoxImg]: /firefox/i,
  [gtvImg]: /google.*tv/i,
  [ieImg]: /internet.*explorer|explorer.*windows/i,
  [iosImg]: /ios/i,
  [linuxImg]: /linux/i,
  [operaImg]: /opera/i,
  [osxImg]: /mac/i,
  [phtImg]: /konvergo|plex.*home.*theater|plex.*media.*center/i,
  [plexampImg]: /plexamp/i,
  [pmsImg]: /plex.*media.*server/i,
  [psxImg]: /WebMAF|playstation|mystery.*3/i,
  [rokuImg]: /roku/i,
  [safariImg]: /safari/i,
  [samsungImg]: /samsung/i,
  [sonosImg]: /sonos/i,
  [vrImg]: /plex.*vr/i,
  [wiiuImg]: /wii.*u/i,
  [win8Img]: /windows/i,
  [wpImg]: /windows.*phone|wince/i,
  [xboxImg]: /xbox|mystery.*5/i,
};

const iconPairs = _.pairs(iconMap);
const memo = {};

// @ts-expect-error ts-migrate(7030) FIXME: Not all code paths return a value.
function findIcon(str: any) {
  for (let i = 0, len = iconPairs.length; i < len; i++) {
    const [icon, regex] = iconPairs[i];

    if (regex.test(str)) {
      return icon;
    }
  }
}

export default function getDeviceIcon(product: any, platform: any) {
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  let icon = memo[product] || memo[platform];

  if (icon) {
    return icon;
  }

  icon = product && findIcon(product);

  if (icon) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    memo[product] = icon;
    return icon;
  }

  icon = platform && findIcon(platform);

  if (icon) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    memo[platform] = icon;
    return icon;
  }

  return defaultImg;
}
