/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import formatDays from './formatDays';
import formatDaysHours from './formatDaysHours';
import formatHoursMinutes from './formatHoursMinutes';
import formatMinutes from './formatMinutes';

const cutoffs: [number, (ms: number) => string][] = [
  [90 * 60 * 1000, formatMinutes], // 1 and a half minutes
  [24 * 60 * 60 * 1000, formatHoursMinutes], // 1 day
  [7 * 24 * 60 * 60 * 1000, formatDaysHours], // 1 week
  [Infinity, formatDays],
];

function formatDuration(milliseconds: any) {
  if (!milliseconds) {
    return '';
  }

  const cutoff = _.find(cutoffs, ([cutoff_]) => {
    return milliseconds <= cutoff_;
  });

  return cutoff && cutoff[1](milliseconds);
}

export default formatDuration;
