import { LineupChannel } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';

export default function getLineupChannelOptionLabel(
  lineupChannel: LineupChannel
): string {
  return lineupChannel.title
    ? `${lineupChannel.identifier} ${lineupChannel.title} (${lineupChannel.callSign})`
    : `${lineupChannel.identifier} ${lineupChannel.callSign}`;
}
