/* eslint-plugin-disable @typescript-eslint */

import durationOf from 'app/js/utils/dateTime/durationOf';
import t from 'app/js/utils/t';

function formatDaysHours(milliseconds: any) {
  if (milliseconds == null) {
    return '';
  }
  const duration = durationOf(milliseconds);
  const days = (duration as any).days;
  const hours = (duration as any).hours;
  const isPlural = days !== 1;
  if (days && hours) {
    return isPlural
      ? t('{1} days {2} hr', days, hours)
      : t('{1} day {2} hr', days, hours);
  } else if (days) {
    return isPlural ? t('{1} days', days) : t('{1} day', days);
  } else {
    return t('{1} hr', hours);
  }
}
export default formatDaysHours;
