import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';

export default function createLibraryProviderEntityID(serverEntityID: any) {
  return createEntityID(
    entityTypes.PROVIDER,
    providerIdentifiers.LIBRARY,
    serverEntityID
  );
}
