/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import serverLogLevels from 'app/js/utils/constants/serverLogLevels';
import getLogLevelLabel from 'app/js/utils/server/getLogLevelLabel';
import t from 'app/js/utils/t';
import PageHeaderBreadcrumbButton from 'app/js/views/v3/components/core/header/PageHeaderBreadcrumbButton';
import Menu from 'app/js/views/v3/components/core/menus/Menu';
import MenuSeparator from 'app/js/views/v3/components/core/menus/MenuSeparator';
import SelectedMenuItem from 'app/js/views/v3/components/core/menus/SelectedMenuItem';
import getUniqueElementID from 'app/js/views/v3/helpers/getUniqueElementID';
import { LARGE } from 'app/js/views/v3/helpers/props/sizes';

type ServerLogLevelMenuProps = {
  logLevels: any[];
  onResetLogLevels: (...args: any[]) => any;
  onToggleLogLevel: (...args: any[]) => any;
};

type ServerLogLevelMenuState = any;

class ServerLogLevelMenu extends Component<
  ServerLogLevelMenuProps,
  ServerLogLevelMenuState
> {
  // eslint-disable-next-line react/sort-comp
  _buttonID: any;

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this._buttonID = getUniqueElementID();

    this.state = {
      isMenuOpen: false,
    };
  }

  //
  // Listeners

  onMenuPress = () => {
    this.setState((prevState: any) => {
      return { isMenuOpen: !prevState.isMenuOpen };
    });
  };

  onMenuClose = () => {
    this.setState({ isMenuOpen: false });
  };

  onAllPress = () => {
    this.props.onResetLogLevels();
  };

  onLogLevelSelect = (logLevel: any) => {
    this.props.onToggleLogLevel(logLevel);
  };

  //
  // Render

  render() {
    const { logLevels } = this.props;

    const { isMenuOpen } = this.state;

    let label;

    if (!logLevels.length) {
      label = t('All');
    } else if (logLevels.length === 1) {
      label = getLogLevelLabel(logLevels[0]);
    } else {
      label = t('Custom');
    }

    return (
      <>
        <PageHeaderBreadcrumbButton
          hasMenu={true}
          // @ts-expect-error ts-migrate(2769) FIXME: No overload matches this call.
          id={this._buttonID}
          isMenuOpen={isMenuOpen}
          onPress={this.onMenuPress}
        >
          {label}
        </PageHeaderBreadcrumbButton>

        <Menu
          isOpen={isMenuOpen}
          size={LARGE}
          target={this._buttonID}
          onMenuClose={this.onMenuClose}
        >
          <SelectedMenuItem
            key="all"
            isSelected={!logLevels.length}
            onPress={this.onAllPress}
          >
            {t('All')}
          </SelectedMenuItem>

          <MenuSeparator />

          {serverLogLevels.map((logLevel, i) => (
            <SelectedMenuItem
              key={i}
              isSelected={logLevels.indexOf(logLevel) !== -1}
              value={logLevel}
              onSelect={this.onLogLevelSelect}
            >
              {getLogLevelLabel(logLevel)}
            </SelectedMenuItem>
          ))}
        </Menu>
      </>
    );
  }
}

export default ServerLogLevelMenu;
