import React, { ReactElement, useCallback, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import createChannelScanActivitySelector from 'app/js/v4/core/serverActivities/selectors/createChannelScanActivitySelector';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import SpinnerButton from 'app/js/v4/core/ui/buttons/components/SpinnerButton';
import Kind from 'app/js/v4/core/ui/types/Kind';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import createDeviceChannelsSelector from 'app/js/v4/features/dvrManagement/selectors/createDeviceChannelsSelector';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import CircularProgressBar from 'app/js/views/v3/components/core/CircularProgressBar';
import IconAlert from 'app/glyphs/alert-560.svg';
import IconDVRClockHour from 'app/glyphs/dvr-clock-hour-560.svg';
import IconDVRClockMinutes from 'app/glyphs/dvr-clock-minutes-560.svg';
import IconDVRClockSeparator from 'app/glyphs/dvr-clock-separator-560.svg';
import styles from './ChannelScanActivity.css';

interface ChannelScanActivityProps {
  className: string;
}

export default function ChannelScanActivity({
  className,
}: ChannelScanActivityProps): ReactElement | null {
  const dispatch = useDispatch();
  const [server] = useSourceContext();

  const { selectedDevice, hasScannedChannels, isCancellingChannelScan } =
    useSelector(selectDVRSetup);

  const { selectedCountry } = useSelector(selectEPGSettings);

  const selectChannelScanActivity = useMemo(
    () => createChannelScanActivitySelector(server),
    [server]
  );
  const selectDeviceChannels = useMemo(
    () => createDeviceChannelsSelector(selectedDevice?.key),
    [selectedDevice]
  );

  const channelScanActivity = useSelector(selectChannelScanActivity);
  const { channels: deviceChannels, isPopulated: isDeviceChannelsPopulated } =
    useSelector(selectDeviceChannels);

  //
  // Callbacks

  const onScanPress = useCallback(() => {
    if (selectedDevice) {
      dispatch(
        dvrSetupSlice.actions.deviceChannelsScanRequested({
          server,
          deviceKey: selectedDevice.key,
          source: selectedDevice.source,
          country: selectedCountry?.code,
        })
      );
    }
  }, [dispatch, server, selectedDevice, selectedCountry]);

  const onCancelPress = useCallback(() => {
    if (selectedDevice && !isCancellingChannelScan) {
      dispatch(
        dvrSetupSlice.actions.deviceChannelsCancelScanRequested({
          server,
          deviceKey: selectedDevice.key,
        })
      );
    }
  }, [dispatch, server, selectedDevice, isCancellingChannelScan]);

  //
  // Render

  if (channelScanActivity) {
    return (
      <div className={className}>
        <CircularProgressBar
          // @ts-expect-error ts-migrate(2322) FIXME: Type '{ size: number; lineWidth: number; progress:... Remove this comment to see the full error message
          progress={channelScanActivity?.progress}
          size={90}
        />

        <div className={styles.message}>{t('Scanning channels')}</div>
        <SpinnerButton
          isDisabled={isCancellingChannelScan}
          isSpinning={isCancellingChannelScan}
          onPress={onCancelPress}
        >
          {t('Stop scanning')}
        </SpinnerButton>
      </div>
    );
  }

  return isDeviceChannelsPopulated && deviceChannels.length === 0 ? (
    <div className={className}>
      <div className={styles.iconContainer}>
        {hasScannedChannels ? (
          <IconAlert className={styles.icon} />
        ) : (
          <>
            <IconDVRClockHour className={styles.clockTime} />
            <IconDVRClockSeparator className={styles.clock} />
            <IconDVRClockMinutes className={styles.clockTime} />
          </>
        )}
      </div>
      <div className={styles.message}>
        {hasScannedChannels
          ? t(
              'No channels found. Make sure your device has a signal from your antenna and try scanning again.'
            )
          : t('Awaiting channel scan')}
      </div>
      <SpinnerButton
        isDisabled={!!channelScanActivity}
        isSpinning={!!channelScanActivity}
        kind={Kind.Primary}
        onPress={onScanPress}
      >
        {t('Scan channels')}
      </SpinnerButton>
    </div>
  ) : null;
}
