import React, { memo, ReactElement } from 'react';
import formatDuration from 'app/js/utils/dateTime/formatDuration';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import useVirtualCell from 'app/js/v4/core/ui/lists/hooks/useVirtualCell';
import { VirtualCellProps } from 'app/js/v4/core/ui/lists/listsTypes';
import { FormattedTopUsersStatistics } from 'app/js/v4/features/dashboard/dashboardTopUsersTypes';
import getStatisticsMetadataTypeLabel from 'app/js/v4/features/dashboard/utils/getStatisticsMetadataTypeLabel';
import { getTimePeriodAfter, TimePeriod } from 'app/js/v4/utils/timePeriods';
import AvatarImg from 'app/js/views/v3/components/core/images/AvatarImg';
import getPlayHistoryRoute from 'app/js/views/v3/helpers/routes/getPlayHistoryRoute';
import colors from 'app/js/views/v3/styles/variables/colors';
import styles from './TopUsersCell.css';

const backgroundColors = [
  colors.serverDashboardTileRowBackgroundPrimaryColor,
  colors.serverDashboardTileRowBackgroundSecondaryColor,
  colors.serverDashboardTileRowBackgroundTertiaryColor,
  colors.serverDashboardTileRowBackgroundQuaternaryColor,
];

function getFormattedDuration(duration: number): string {
  return formatDuration(duration * 1000) || t('{1} min', 0);
}

interface TopUsersCellProps
  extends VirtualCellProps<FormattedTopUsersStatistics> {
  activeTimePeriod: TimePeriod;
}

function TopUsersCell({
  activeTimePeriod,
  item: userStatistics,
  ...virtualCellProps
}: TopUsersCellProps): ReactElement | null {
  const [server] = useSourceContext();

  const cell = useVirtualCell({
    ...virtualCellProps,
    // Move animations when changing the time period looks unexpected.
    moveDurationMilliseconds: 0,
  });

  if (!userStatistics) {
    return null;
  }

  const { accountID, username, thumb, playCount, totalDuration, durations } =
    userStatistics;

  // Sort metadata type rows by duration
  const metadataTypes = Object.keys(durations).sort((a, b) => {
    if (durations[a] > durations[b]) {
      return -1;
    }

    if (durations[a] < durations[b]) {
      return 1;
    }

    return 0;
  });

  return (
    <div ref={cell.cellRef} style={cell.style}>
      <div className={styles.summaryContainer}>
        <AvatarImg
          className={styles.avatar}
          height={50}
          serverEntityID={server.entityID}
          url={thumb}
          width={50}
        />

        <div className={styles.summary}>
          <div>
            {playCount === 1
              ? t('{playCount} play', { playCount })
              : t('{playCount} plays', { playCount })}
          </div>

          <div>{getFormattedDuration(totalDuration)}</div>
        </div>

        <Link
          className={styles.playHistoryLink}
          to={getPlayHistoryRoute(server.machineIdentifier, {
            filters: `accountID=${accountID}&viewedAt=${getTimePeriodAfter(
              activeTimePeriod
            )}`,
            activeTimePeriod,
          })}
        >
          {t('View Play History')}
        </Link>
      </div>

      <div className={styles.username} title={username}>
        {username}
      </div>

      {metadataTypes.map((metadataType, index) => {
        return (
          <div
            key={metadataType}
            className={styles.metadataRow}
            style={{
              backgroundColor: backgroundColors[index],
            }}
          >
            <div className={styles.metadataTitle}>
              {getStatisticsMetadataTypeLabel(parseInt(metadataType, 10))}
            </div>
            <div className={styles.duration}>
              {getFormattedDuration(durations[metadataType])}
            </div>
          </div>
        );
      })}
    </div>
  );
}

export default memo(TopUsersCell);
