import React, { ReactElement } from 'react';
import t from 'app/js/utils/t';
import { MetadataType } from 'app/js/v4/core/metadata/metadataTypes';
import ServerDashboardSectionContent from 'app/js/v4/features/dashboard/components/ServerDashboardSectionContent';
import { TopPlayedMetadataItem } from 'app/js/v4/features/dashboard/dashboardTopPlayedTypes';
import { TimePeriod } from 'app/js/v4/utils/timePeriods';
import TopPlayedColumn from './TopPlayedColumn';
import styles from './TopPlayedList.css';

interface Column {
  type: MetadataType;
  title: string;
  items: TopPlayedMetadataItem[];
}

interface TopPlayedListProps {
  moviesStatistics: TopPlayedMetadataItem[];
  tvStatistics: TopPlayedMetadataItem[];
  musicStatistics: TopPlayedMetadataItem[];
  gameStatistics: TopPlayedMetadataItem[];
  activeTimePeriod: TimePeriod;
  isSpinnerVisible: boolean;
  showList: boolean;
  errorMessage?: string;
}

function formatData(
  moviesStatistics: TopPlayedMetadataItem[],
  tvStatistics: TopPlayedMetadataItem[],
  musicStatistics: TopPlayedMetadataItem[],
  gameStatistics: TopPlayedMetadataItem[]
): Column[] {
  const result = [
    {
      type: MetadataType.Movie,
      title: t('Movies'),
      items: moviesStatistics,
    },
    {
      type: MetadataType.Show,
      title: t('TV'),
      items: tvStatistics,
    },
    {
      type: MetadataType.Artist,
      title: t('Artists'),
      items: musicStatistics,
    },
  ];

  return gameStatistics.length > 0
    ? result.concat({
        type: MetadataType.Game,
        title: t('Games'),
        items: gameStatistics,
      })
    : result;
}

export default function TopPlayedList({
  moviesStatistics,
  tvStatistics,
  musicStatistics,
  gameStatistics,
  activeTimePeriod,
  isSpinnerVisible,
  showList,
  errorMessage,
}: TopPlayedListProps): ReactElement {
  const finalShowList = showList && !errorMessage;
  const columns = formatData(
    moviesStatistics,
    tvStatistics,
    musicStatistics,
    gameStatistics
  );

  return (
    <ServerDashboardSectionContent
      className={isSpinnerVisible ? styles.spinnerContainer : styles.container}
      errorMessage={errorMessage}
      isSpinnerVisible={isSpinnerVisible}
    >
      {finalShowList
        ? columns.map((column) => (
            <TopPlayedColumn
              key={column.type}
              activeTimePeriod={activeTimePeriod}
              className={styles.column}
              items={column.items}
              title={column.title}
              type={column.type}
            />
          ))
        : null}
    </ServerDashboardSectionContent>
  );
}
