/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import serverSettingsSections from 'app/js/utils/constants/serverSettingsSections';

export default function getServerSettingsMetricsProps(
  settingsSection: any,
  settingsGroup: any
) {
  const section = _.findWhere(serverSettingsSections, { key: settingsSection });
  // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
  const group = _.findWhere(section.groups, { key: settingsGroup });
  if ((group as any).metricsPage) {
    return {
      metricsPage: (group as any).metricsPage,
      metricsPane: (group as any).metricsPane,
    };
  }
  return {
    // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
    metricsPage: section.metricsPage,
    // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
    metricsPane: section.metricsPanePrefix + group.key,
  };
}
