/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import FilteredCollection from 'app/js/models/FilteredCollection';

const PlayQueueUpNextCollection = FilteredCollection.extend({
  //
  // Init

  initialize: function (options: any) {
    this.playQueue = options.playQueue;
    this.includeSelectedItem = options.includeSelectedItem;

    this.addFilter(_.bind(this.filterUpNext, this));
    this.listenForUpNextChanges();
  },

  listenForUpNextChanges: function () {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2-3 arguments, but got 1.
    const debouncedPlayQueueChange = _.debounce(this.onPlayQueueChange);
    const events = [
      'change:playQueueSelectedItemID',
      'change:playQueueLastAddedItemID',
    ];

    this.listenTo(this.playQueue, events.join(' '), debouncedPlayQueueChange);
    this.listenTo(
      this.playQueue.get('collection'),
      'change:queueIndex',
      debouncedPlayQueueChange
    );
  },

  //
  // Control

  filterUpNext: function (playQueueItem: any) {
    const index = this.superset.indexOf(playQueueItem);
    const range = this.playQueue.getUpNextRange();
    let startIndex = range.start;
    const endIndex = range.end;

    // If we are not including the selected item as part of this collection,
    // don't include its index in the filtered range.
    if (!this.includeSelectedItem) {
      startIndex += 1;
    }

    return index >= startIndex && index <= endIndex;
  },

  //
  // Listeners

  onPlayQueueChange: function () {
    this.refilter();
  },
});

export default PlayQueueUpNextCollection;
