import { HorizontalLayout } from 'app/js/v4/core/ui/lists/listsTypes';
import createGridLayout, {
  GridDirection,
  GridLayoutOptions,
} from './createGridLayout';

export interface HorizontalLayoutOptions
  extends Omit<
    GridLayoutOptions,
    'direction' | 'getCellHeight' | 'getCellWidth' | 'rows'
  > {
  columnWidth: number;
  rowHeight: number;
}

export default function createHorizontalLayout({
  columnWidth,
  rowHeight,
  ...options
}: HorizontalLayoutOptions): HorizontalLayout {
  return {
    rowHeight,
    ...createGridLayout({
      direction: GridDirection.Horizontal,
      rows: 1,
      getCellWidth: () => columnWidth,
      getCellHeight: () => rowHeight,

      // An overscan of 4 helps avoid popping in items as they are scrolled into
      // view on CPU-constrained devices. The tradeoff is rendering more cells at
      // once.
      overscan: 4,

      ...options,
    }),
  };
}
