import React, { ReactElement } from 'react';
import t from 'app/js/utils/t';
import { MetadataType } from 'app/js/v4/core/metadata/metadataTypes';
import { TopPlayedMetadataItem } from 'app/js/v4/features/dashboard/dashboardTopPlayedTypes';
import { TimePeriod } from 'app/js/v4/utils/timePeriods';
import TopPlayedColumnHeader from './TopPlayedColumnHeader';
import TopPlayedColumnRow from './TopPlayedColumnRow';
import styles from './TopPlayedColumn.css';

interface TopPlayedColumnProps {
  className: string;
  type: MetadataType;
  title: string;
  items: TopPlayedMetadataItem[];
  activeTimePeriod: TimePeriod;
}

const backgroundOpacities = [0.5, 0.4, 0.3, 0.2, 0.1, 0.05];

function getEmptyTitle(type: MetadataType): string {
  switch (type) {
    case MetadataType.Movie:
      return t('No Movies Played');
    case MetadataType.Show:
      return t('No TV Played');
    case MetadataType.Artist:
      return t('No Artists Played');
    case MetadataType.Game:
      return t('No Games Played');
    default:
      return '';
  }
}

export default function TopPlayedColumn({
  className,
  type,
  title,
  items = [],
  activeTimePeriod,
}: TopPlayedColumnProps): ReactElement {
  const isEmpty = items.length === 0;

  return (
    <div className={className}>
      <TopPlayedColumnHeader
        isEmpty={isEmpty}
        metadataItem={items[0]}
        title={title}
      />

      {isEmpty ? (
        <div className={styles.emptyItem}>{getEmptyTitle(type)}</div>
      ) : (
        items.map((metadataItem, index) => (
          <TopPlayedColumnRow
            key={metadataItem.key}
            activeTimePeriod={activeTimePeriod}
            className={styles.item}
            metadataItem={metadataItem}
            style={{
              backgroundColor: `rgba(0, 0, 0, ${backgroundOpacities[index]})`,
            }}
          />
        ))
      )}
    </div>
  );
}
