/* eslint-plugin-disable @typescript-eslint */

const suffixes = ['bps', 'Kbps', 'Mbps', 'Gbps', 'Tbps'];

function formatBandwidthBitrate(bits: any, digits = 2) {
  if (bits === 0) {
    return `${(0).toFixed(digits)} ${suffixes[0]}`;
  }

  // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'number' is not assignable to par... Remove this comment to see the full error message
  const i = parseInt(Math.floor(Math.log(bits) / Math.log(1024)), 10);

  return `${(bits / Math.pow(1024, i)).toFixed(digits)} ${suffixes[i]}`;
}

export default formatBandwidthBitrate;
