import { Selector } from 'react-redux';
import { createSelector } from 'reselect';
import { EPGLineupGroup } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import selectEPGSettings from './selectEPGSettings';

const selectLatestEPGLineupGroup: Selector<
  LegacyRootState,
  EPGLineupGroup | null
> = createSelector(selectEPGSettings, ({ lineupGroups }) => {
  const { epgLineupGroups, isPending, isPopulated } = lineupGroups;

  if (isPending || !isPopulated || epgLineupGroups.length === 0) {
    return null;
  }

  return epgLineupGroups[epgLineupGroups.length - 1];
});

export default selectLatestEPGLineupGroup;
