import $ from 'jquery';
import _ from 'underscore';
import commands from 'app/js/commands/commands';
import stateMachineMixin from 'app/js/mixins/views/stateMachineMixin';
import BackgroundProcessingListCollection from 'app/js/models/library/convert/BackgroundProcessingListCollection';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';
import LoadingView from 'app/js/views/LoadingView';
import VersionGeneratorList from 'app/js/views/lists/library/convert/VersionGeneratorList';
import ServerSettingsPane from 'app/js/views/settings/server/ServerSettingsPane';
import ServerSettingsVersionsErrorView from 'app/js/views/settings/server/ServerSettingsVersionsErrorView';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setti... Remove this comment to see the full error message
import tpl from 'app/templates/settings/server/ServerSettingsVersionsPane.tpl';

const ServerSettingsVersionsPane = ServerSettingsPane.extend({
  mixins: [stateMachineMixin],
  //
  // Properties
  className: 'versions-container',
  template: tpl,
  ui: {
    message: '.versions-status-message',
  },
  regions: {
    listRegion: '.background-processing-list-region',
  },
  events: {
    'click .delete-all-btn': 'onDeleteAllVersionsClick',
    'click .status-conversion-btn': 'onStatusConversionClick',
  },
  //
  // Init
  initialize: function () {
    this.pausedSetting = this.collection.get('BackgroundQueueIdlePaused');
    this.playlists = new BackgroundProcessingListCollection([], {
      server: this.model,
      allowInCache: true,
    });
    this.transcodeJobs = this.model
      .get(__PLATFORM__)
      .get('backgroundTranscodeJobs');
  },
  //
  // States
  initialState: 'loading',
  states: {
    loading: {
      nextState: 'list',
      onEnter: function () {
        const isSupported = (this as any).model.supports('streamReady');
        const canTranscode = (this as any).model.get('transcoderVideo');
        (this as any).listRegion.show(new LoadingView({ inline: true }));
        if (!isSupported || !canTranscode) {
          (this as any).enterState('error');
          return;
        }
        let promise = $.when(
          (this as any).playlists.populate(),
          (this as any).transcodeJobs.fetch()
        );
        promise = promise.then(
          _.bind(function () {
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.playlist = this.playlists.first();
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            if (this.playlist) {
              // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
              this.generators = this.playlist.get('children');
              // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
              return this.generators.fetch();
            } else {
              return $.Deferred().reject().promise();
            }
          }, this)
        );
        promise.done(
          (this as any).whenRendered(function () {
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.enterNextState();
          })
        );
        promise.fail(
          (this as any).whenRendered(function () {
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.enterState('error');
          })
        );
      },
    },
    list: {
      onEnter: function () {
        (this as any).toggleMessage();
        (this as any).listenTo(
          (this as any).generators,
          'reset add remove',
          (this as any).toggleMessage
        );
        (this as any).listRegion.show(
          new VersionGeneratorList({
            collection: (this as any).generators,
            itemViewOptions: {
              transcodeJobs: (this as any).transcodeJobs,
              pausedSetting: (this as any).pausedSetting,
            },
          })
        );
      },
      onExit: function () {
        (this as any).stopListening((this as any).generators);
      },
    },
    error: {
      onEnter: function () {
        (this as any).listRegion.show(
          new ServerSettingsVersionsErrorView({ model: (this as any).model })
        );
      },
    },
  },
  //
  // Control
  toggleMessage: function () {
    this.ui.message.toggleClass('hidden', !this.generators.length);
  },
  //
  // Listeners
  onRender: function () {
    this.enterNextState();
  },
  onDeleteAllVersionsClick: function (event: any) {
    event.preventDefault();
    commands.execute('deleteAllVersionGenerators', { server: this.model }).then(
      _.bind(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        return this.generators.fetch();
      }, this)
    );
  },
  onStatusConversionClick: function (event: any) {
    event.preventDefault();
    navigateToRoute('serverSettings', {
      machineIdentifier: this.model.id,
      settingsSection: 'status',
      settingsGroup: 'conversion',
    });
  },
});
export default ServerSettingsVersionsPane;
