import React, { ReactElement } from 'react';
import { useSelector } from 'react-redux';
import serverFeatures from 'app/js/models/server/serverFeatures';
import isVersionOrNewer from 'app/js/utils/isVersionOrNewer';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import { DVR } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import selectSelectedDVRDevices from 'app/js/v4/features/dvrManagement/selectors/selectSelectedDVRDevices';
import IconToolbarFilterAdd from 'app/glyphs/toolbar-filter-add-560.svg';
import LiveTVSettingsDevice from './LiveTVSettingsDevice';
import styles from './LiveTVSettingsDeviceList.css';

interface LiveTVSettingsDeviceListProps {
  dvrID: DVR['key'];
  onAddDevicePress(): void;
}

export default function LiveTVSettingsDeviceList({
  dvrID,
  onAddDevicePress,
}: LiveTVSettingsDeviceListProps): ReactElement {
  const [server] = useSourceContext();
  const devices = useSelector(selectSelectedDVRDevices);

  const hasUnsupportedServer = !isVersionOrNewer(
    server.version,
    serverFeatures.v4DVRSetup
  );

  //
  // Render

  return (
    <div>
      {devices?.map((device) => (
        <LiveTVSettingsDevice
          key={device.key}
          canRemoveDevice={devices.length > 1}
          channelMappings={device.ChannelMapping}
          deviceKey={device.key}
          dvrID={dvrID}
          hasPrefs={!!device.Setting?.length}
          model={device.model}
          status={device.status}
          title={device.title}
          tunerCount={device.tuners}
          uri={device.uri}
        />
      ))}
      <Button
        className={styles.addDeviceButton}
        isDisabled={hasUnsupportedServer}
        onPress={onAddDevicePress}
      >
        <IconToolbarFilterAdd className={styles.addDeviceButtonIcon} />
        {t('Add another device')}
      </Button>
    </div>
  );
}
