/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import t from 'app/js/utils/t';
import ServerAvatar from './ServerAvatar';
import styles from './UnclaimedServer.css';

type UnclaimedServerProps = {
  serverEntityID: string;
  isBusy: boolean;
  isAnonymousUser: boolean;
  isUserRestricted: boolean;
  ClaimServerButtonConnectorComponent: React.ReactElement;
};

function UnclaimedServer(props: UnclaimedServerProps) {
  const {
    serverEntityID,
    isBusy,
    isAnonymousUser,
    isUserRestricted,
    ClaimServerButtonConnectorComponent,
  } = props;

  return (
    <div className={styles.container}>
      <ServerAvatar serverEntityID={serverEntityID} />

      {isAnonymousUser ? (
        <div className={styles.message}>
          <div className={styles.messageHeader}>
            {t('This server is unclaimed and not secure')}
          </div>
          <div className={styles.messageDetails}>
            {t('You need to sign in to claim this server.')}
          </div>
        </div>
      ) : null}

      {isUserRestricted ? (
        <div className={styles.message}>
          <div className={styles.messageHeader}>
            {t('This server is unclaimed and not secure')}
          </div>
          <div className={styles.messageDetails}>
            {t('You are signed in as a managed user.')}
          </div>
          <div className={styles.messageDetails}>
            {t('Switch to a full user account to claim this server.')}
          </div>
        </div>
      ) : null}

      {!isAnonymousUser && !isUserRestricted ? (
        <div className={styles.message}>
          <div className={styles.messageHeader}>
            {t('This server is unclaimed and not secure')}
          </div>
          <div className={styles.messageDetails}>
            {t(
              'Claiming this server will associate it with your Plex account. This helps your devices find each other and helps keep your media safe.'
            )}
          </div>
        </div>
      ) : null}

      {/* @ts-expect-error ts-migrate(2604) FIXME: JSX element type 'ClaimServerButtonConnectorCompon... Remove this comment to see the full error message */}
      <ClaimServerButtonConnectorComponent
        isBusy={isBusy}
        isDisabled={isAnonymousUser || isUserRestricted}
        serverEntityID={serverEntityID}
      />
    </div>
  );
}

export default UnclaimedServer;
