import React from 'react';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import Modal from 'app/js/views/v3/components/core/modals/Modal';
import DVRDeviceSettingsModalContentConnector from './DVRDeviceSettingsModalContentConnector';

type DVRDeviceSettingsModalProps = {
  isOpen: boolean;
  metricsProperties: any;
  onModalClose: (...args: any[]) => any;
};

function DVRDeviceSettingsModal(props: DVRDeviceSettingsModalProps) {
  const { isOpen, metricsProperties, onModalClose, ...otherProps } = props;

  return (
    <Modal
      isOpen={isOpen}
      metricsPage={metricsPages.DVR_DEVICE_SETTINGS}
      metricsProperties={metricsProperties}
      onModalClose={onModalClose}
    >
      <DVRDeviceSettingsModalContentConnector
        onModalClose={onModalClose}
        {...otherProps}
      />
    </Modal>
  );
}

export default DVRDeviceSettingsModal;
