import React, { ReactElement, useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { saveExperienceSettings } from 'app/js/stores/actions/userActions';
import * as features from 'app/js/utils/constants/user/features';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import t from 'app/js/utils/t';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import Checkbox from 'app/js/v4/core/ui/forms/components/Checkbox';
import Modal from 'app/js/v4/core/ui/modals/components/Modal';
import ModalBody from 'app/js/v4/core/ui/modals/components/ModalBody';
import ModalContent from 'app/js/v4/core/ui/modals/components/ModalContent';
import ModalFooter from 'app/js/v4/core/ui/modals/components/ModalFooter';
import ModalFooterButtonBar from 'app/js/v4/core/ui/modals/components/ModalFooterButtonBar';
import ModalHeader from 'app/js/v4/core/ui/modals/components/ModalHeader';
import Kind from 'app/js/v4/core/ui/types/Kind';
import Size from 'app/js/v4/core/ui/types/Size';
import createUserExperienceSettingSelector from 'app/js/v4/core/user/selectors/createUserExperienceSettingSelector';
import createUserFeatureSelector from 'app/js/v4/core/user/selectors/createUserFeatureSelector';
import styles from './PromotionWarningModal.css';

const DISMISS_SETTING = 'hasDismissedHubPromotionWarning';

interface PromotionWarningModalProps {
  isOpen: boolean;
  onClose: () => void;
  onAccept: () => void;
}

export default function PromotionWarningModal({
  isOpen,
  onClose,
  onAccept,
}: PromotionWarningModalProps): ReactElement | null {
  const dispatch = useDispatch();
  const [isDismissed, setIsDismissed] = useState(false);

  const hasDismissedWarning = useSelector(
    createUserExperienceSettingSelector(DISMISS_SETTING)
  );
  const hasHomeLibraryAccessLabels = useSelector(
    createUserFeatureSelector(features.HOME_LIBRARY_ACCESS_LABELS)
  );

  //
  // Effects

  useEffect(() => {
    if (isOpen && hasDismissedWarning) {
      onAccept();
    }
  }, [isOpen, hasDismissedWarning, onAccept, onClose]);

  //
  // Callbacks

  const onDismissChange = useCallback(
    () => setIsDismissed(!isDismissed),
    [isDismissed]
  );

  const onAcceptPress = useCallback(() => {
    if (isDismissed) {
      scheduleImmediateUpdate(() => {
        dispatch(saveExperienceSettings({ [DISMISS_SETTING]: true }));
      });
    }
    onAccept();
  }, [dispatch, isDismissed, onAccept]);

  //
  // Render

  if (hasDismissedWarning) {
    return null;
  }

  return (
    <Modal isOpen={isOpen} size={Size.Small} onModalClose={onClose}>
      <ModalContent onModalClose={onClose}>
        <ModalHeader>
          {hasHomeLibraryAccessLabels
            ? t("Add to Friends' Home?")
            : t("Add to Shared Users' Home?")}
        </ModalHeader>
        <ModalBody>
          {hasHomeLibraryAccessLabels
            ? t(
                'Matching items from this library will appear on your friends Home screens as long as they have this library pinned to the sidebar. Be nice.'
              )
            : t(
                'Matching items from this library will appear on your users Home screens as long as they have this library pinned to the sidebar. Be nice.'
              )}
        </ModalBody>
        <ModalFooter>
          <label key="dismiss" className={styles.dismiss}>
            <Checkbox
              checked={isDismissed}
              name="dismiss"
              onChange={onDismissChange}
            />
            <span>{t("Don't show again")}</span>
          </label>
          <ModalFooterButtonBar>
            <Button onPress={onClose}>{t('Cancel')}</Button>
            <Button kind={Kind.Primary} onPress={onAcceptPress}>
              {t('Accept')}
            </Button>
          </ModalFooterButtonBar>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
}
