import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module './CircleProgressBarView.tpl' o... Remove this comment to see the full error message
import tpl from './CircleProgressBarView.tpl';

const CircleProgressBarView = Marionette.ItemView.extend({
  //
  // Properties

  className: 'circle-progress',
  template: tpl,

  size: 30,
  lineWidth: 5,
  strokeColor: '#cc7b19',
  hasSize: false,

  ui: {
    svg: '.circle-progress-bar',
    svgCircle: '.circle-progress-bar circle',
  },

  bindings: {
    '.circle-progress-text': {
      modelAttribute: 'text',
    },
  },

  modelEvents: {
    'change:progress': 'onProgressChange',
  },

  //
  // Init

  initialize: function (options: any) {
    this.size = options.size || this.size;
    this.lineWidth = options.lineWidth || this.lineWidth;
    this.strokeColor = options.strokeColor || this.strokeColor;

    if (this.size) {
      this.$el.width(this.size);
      this.$el.height(this.size);
    }
  },

  //
  // Control

  drawProgressBar: function () {
    const size = this.size;
    const center = size / 2;
    const radius = center - this.lineWidth;
    const circumference = Math.PI * (radius * 2);

    if (!this.hasSize) {
      this.ui.svg.attr('width', size);
      this.ui.svg.attr('height', size);

      this.ui.svgCircle.attr('r', radius);
      this.ui.svgCircle.attr('cx', center);
      this.ui.svgCircle.attr('cy', center);
      this.ui.svgCircle.attr('stroke-dasharray', circumference);

      this.ui.svgCircle.css('stroke-width', this.lineWidth);
      this.ui.svgCircle.css('stroke', this.strokeColor);

      this.hasSize = true;
    }

    const progress = this.model.get('progress');
    const percent = ((100 - progress) / 100) * circumference;

    this.ui.svgCircle.css('stroke-dashoffset', percent);
  },

  //
  // Listeners

  onRender: function () {
    this.drawProgressBar();
  },

  onProgressChange: function () {
    this.drawProgressBar();
  },
});

export default CircleProgressBarView;
