import { createSelector, Selector } from 'reselect';
import createServerActivitiesSelector from 'app/js/v4/core/serverActivities/selectors/createServerActivitiesSelector';
import {
  ServerActivity,
  ServerActivityType,
} from 'app/js/v4/core/serverActivities/serverActivitiesTypes';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

function createEPGLineupScanActivitySelector(
  server: Server
): Selector<LegacyRootState, ServerActivity | undefined> {
  return createSelector(
    createServerActivitiesSelector(server),
    (activities) => {
      return activities.find((activity) => {
        return activity.type === ServerActivityType.ProviderEpgLineupScan;
      });
    }
  );
}

export default createEPGLineupScanActivitySelector;
