/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import BaseModal from 'app/js/views/modals/BaseModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/ReleaseNotesModal.tpl';

function splitList(list: any) {
  if (!list) {
    return [];
  }

  return list.split('\n');
}

const ReleaseNotesModal = BaseModal.extend({
  //
  // Properties

  name: 'releaseNotesModal',
  template: tpl,

  //
  // Control

  serializeData: function () {
    const data = this.model.toJSON();
    data.escapedVersion = _.escape(data.version.replace(/-.*/gi, ''));
    data.added = splitList(data.added);
    data.fixed = splitList(data.fixed);

    return data;
  },
});

export default ReleaseNotesModal;
