/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import selectManageableServerEntityIDs from 'app/js/stores/selectors/server/selectManageableServerEntityIDs';
import SettingsSidebar from './SettingsSidebar';

function createMapStateToProps() {
  return createSelector(
    selectManageableServerEntityIDs,

    (manageableServerEntityIDs) => {
      return {
        manageableServerEntityIDs,
      };
    }
  );
}

export default connect(createMapStateToProps)(SettingsSidebar);
