/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import * as serverLogPageActions from 'app/js/stores/actions/ui/serverLogPageActions';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import ServerLogPageHeader from './ServerLogPageHeader';

const mapStateToProps = createSelector(
  // @ts-expect-error ts-migrate(2571) FIXME: Object is of type 'unknown'.
  (appState) => appState.ui.serverLogPage,
  (serverLogPage) => {
    return {
      filter: serverLogPage.filter,
      timeFilterStart: serverLogPage.timeFilterStart,
      timeFilterEnd: serverLogPage.timeFilterEnd,
      isTimeFilterStartValid: serverLogPage.isTimeFilterStartValid,
      isTimeFilterEndValid: serverLogPage.isTimeFilterEndValid,
      showTimeFilter: serverLogPage.showTimeFilter,
      showRawText: serverLogPage.showRawText,
      logLines: serverLogPage.logLines,
      unfilteredLogLinesCount: serverLogPage.unfilteredLogLinesCount,
    };
  }
);

function createMapDispatchToProps(dispatch: any, props: any) {
  const { serverEntityID } = props;

  return {
    onTextFilterChange(filter: any) {
      // Avoid a race between debounced local changes and the state change.
      scheduleImmediateUpdate(() => {
        dispatch(
          serverLogPageActions.changeTextFilter({ serverEntityID, filter })
        );
      });
    },
    onTimeFilterChange(timeFilterStart: any, timeFilterEnd: any) {
      // Avoid a race between debounced local changes and the state change.
      scheduleImmediateUpdate(() => {
        dispatch(
          serverLogPageActions.changeTimeFilter({
            serverEntityID,
            timeFilterStart,
            timeFilterEnd,
          })
        );
      });
    },
    onToggleTimeFilter() {
      dispatch(serverLogPageActions.toggleTimeFilter({ serverEntityID }));
    },
    onToggleRaw() {
      dispatch(serverLogPageActions.toggleRaw());
    },
    onClearLog() {
      dispatch(serverLogPageActions.clearLog({ serverEntityID }));
    },
  };
}

export default connect(
  mapStateToProps,
  createMapDispatchToProps
)(ServerLogPageHeader);
