import { ServerAccount } from 'app/js/v4/core/serverAccounts/serverAccountsTypes';

const SERVER_OWNER_ID = 1;

export default function serverAccountComparator(
  accountA: ServerAccount,
  accountB: ServerAccount
): number {
  // Always sort the server owner first.
  if (accountA.id === SERVER_OWNER_ID) {
    return -1;
  }

  if (accountB.id === SERVER_OWNER_ID) {
    return 1;
  }

  // Otherwise sort alphabetically, ignoring case.
  const nameA = accountA.name.trim().toUpperCase();
  const nameB = accountB.name.trim().toUpperCase();

  if (nameA < nameB) {
    return -1;
  }

  if (nameA > nameB) {
    return 1;
  }

  return 0;
}
