import t from 'app/js/utils/t';
import { ServerResourceType } from 'app/js/v4/features/dashboard/dashboardResourcesTypes';

export default function getServerResourceTypeLabel(
  resourceType: ServerResourceType
): string {
  switch (resourceType) {
    case ServerResourceType.All:
      return t('All');
    case ServerResourceType.PlexMediaServer:
      return 'Plex Media Server';
    case ServerResourceType.System:
      return t('System');
    default:
      return '';
  }
}
