import { useEffect } from 'react';
import { resolve } from 'app/js/adapters/adapters';
import performance from 'app/js/utils/metrics/performance';
import { useMetricsContext } from 'app/js/v4/core/ui/metrics/components/MetricsContextProvider';
import { MetricsProperties } from 'app/js/v4/core/ui/metrics/metricsTypes';

export default function useMetricsPageViewEffect(
  metricsProperties?: Omit<MetricsProperties, 'context'>,
  isReady = true
): void {
  const { metricsPage, metricsContextTuple } = useMetricsContext();

  useEffect(() => {
    if (isReady) {
      // This ends the `client:view:${metricsPage}` period.
      resolve('metrics').trackView({
        ...metricsProperties,
        page: metricsPage,
        contextTuple: metricsContextTuple,
      });
    } else {
      performance.startPeriod(`client:view:${metricsPage}`);
    }

    // The `metricsProperties` reference will change with each render, but only
    // track a new page view if the property values change.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    isReady,
    metricsContextTuple,
    metricsPage,
    // eslint-disable-next-line react-hooks/exhaustive-deps
    JSON.stringify(metricsProperties),
  ]);
}
