/* eslint-plugin-disable @typescript-eslint */

import t from 'app/js/utils/t';
import formatDuration from './formatDuration';

function formatDurationSecondsLeft(seconds: any) {
  if (seconds == null) {
    return '';
  }

  const duration = formatDuration(seconds * 1000);

  return duration ? t('{1} left', duration) : '';
}

export default formatDurationSecondsLeft;
