import React, { ReactElement, useCallback, useMemo, useState } from 'react';
import t from 'app/js/utils/t';
import ServerDashboardGraph from 'app/js/v4/features/dashboard/components/ServerDashboardGraph';
import {
  ResourcesStatistics,
  ServerResourceType,
} from 'app/js/v4/features/dashboard/dashboardResourcesTypes';
import formatRealTimeAgo from 'app/js/v4/features/dashboard/utils/formatRealTimeAgo';
import getServerResourceTypeLabel from 'app/js/v4/features/dashboard/utils/getServerResourceTypeLabel';
import DashSeparator from 'app/js/views/v3/components/core/base/DashSeparator';
import LineChart from 'app/js/views/v3/components/core/charts/LineChart';

type HostKey = 'hostCpuUtilization' | 'hostMemoryUtilization';
type ProcessKey = 'processCpuUtilization' | 'processMemoryUtilization';

type Data = {
  at: number;
  date: number;
  [ServerResourceType.PlexMediaServer]: number | null;
  [ServerResourceType.System]: number | null;
}[];

interface SummedData {
  [ServerResourceType.PlexMediaServer]: number;
  [ServerResourceType.System]: number;
}

interface LegendSummaryProps {
  isPlexMediaServerVisible: boolean;
  isSystemVisible: boolean;
  summedData?: SummedData;
}

interface ServerResourceGraphProps {
  statistics: ResourcesStatistics[];
  activeResourceType: ServerResourceType;
  hostKey: HostKey;
  processKey: ProcessKey;
  hostColor: string;
  processColor: string;
  isPopulated: boolean;
  errorMessage?: string;
  onGraphResourceTypeSelect(resourceType: ServerResourceType): void;
  onGraphTooltipActive(): void;
  onGraphTooltipInactive(): void;
}

const TIME_PERIOD_SPAN_SECONDS = 120;
const TIME_PERIOD_INTERVAL_SECONDS = 5;
const TIME_PERIOD_ENTRIES_COUNT =
  TIME_PERIOD_SPAN_SECONDS / TIME_PERIOD_INTERVAL_SECONDS + 1;

function formatData(
  statistics: ResourcesStatistics[],
  hostKey: HostKey,
  processKey: ProcessKey,
  activeResourceType: ServerResourceType
): {
  data: Data;
  summedData: SummedData;
} {
  const isSystemVisible =
    activeResourceType === ServerResourceType.All ||
    activeResourceType === ServerResourceType.System;
  const isPlexMediaServerVisible =
    activeResourceType === ServerResourceType.All ||
    activeResourceType === ServerResourceType.PlexMediaServer;

  const summedData = {
    [ServerResourceType.System]: 0,
    [ServerResourceType.PlexMediaServer]: 0,
  };

  const data = statistics
    .slice(-TIME_PERIOD_ENTRIES_COUNT)
    .map((intervalStatistics, index) => {
      summedData[ServerResourceType.PlexMediaServer] =
        summedData[ServerResourceType.PlexMediaServer] +
        (intervalStatistics[processKey] || 0);

      summedData[ServerResourceType.System] =
        summedData[ServerResourceType.System] +
        (intervalStatistics[hostKey] || 0);

      return {
        at: intervalStatistics.at,
        date: TIME_PERIOD_SPAN_SECONDS - TIME_PERIOD_INTERVAL_SECONDS * index,
        [ServerResourceType.PlexMediaServer]: isPlexMediaServerVisible
          ? intervalStatistics[processKey]
          : null,
        [ServerResourceType.System]: isSystemVisible
          ? intervalStatistics[hostKey]
          : null,
      };
    });

  return { data, summedData };
}

function tooltipLabelFormatter(seconds: number): string {
  return seconds === 0
    ? t('Now')
    : t('{timeAgo} ago', { timeAgo: formatRealTimeAgo(seconds) });
}

function tooltipValueFormatter(utilization: number): string {
  return `${utilization.toFixed(2)}%`;
}

function xAxisTickFormatter(seconds: number): string {
  return formatRealTimeAgo(seconds);
}

function yAxisTickFormatter(utilization: number): string {
  if (utilization === 0) {
    return '';
  }

  return `${Math.round(utilization)}%`;
}

function LegendSummary({
  isPlexMediaServerVisible,
  isSystemVisible,
  summedData,
}: LegendSummaryProps): ReactElement | null {
  if (!summedData) {
    return null;
  }

  const isAllVisible = isPlexMediaServerVisible && isSystemVisible;

  return (
    <>
      {isAllVisible ? t('Averages') : t('Average')}
      {': '}

      {isAllVisible ? (
        <>
          {getServerResourceTypeLabel(ServerResourceType.PlexMediaServer)}
          <DashSeparator />
        </>
      ) : null}
      {isPlexMediaServerVisible
        ? `${(
            summedData[ServerResourceType.PlexMediaServer] /
            TIME_PERIOD_ENTRIES_COUNT
          ).toFixed(2)}%`
        : null}

      {isAllVisible ? (
        <>
          {' | '}
          {getServerResourceTypeLabel(ServerResourceType.System)}
          <DashSeparator />
        </>
      ) : null}
      {isSystemVisible
        ? `${(
            summedData[ServerResourceType.System] / TIME_PERIOD_ENTRIES_COUNT
          ).toFixed(2)}%`
        : null}
    </>
  );
}

export default function ServerResourceGraph({
  statistics,
  activeResourceType,
  hostKey,
  processKey,
  hostColor,
  processColor,
  isPopulated,
  errorMessage,
  onGraphResourceTypeSelect,
  onGraphTooltipActive,
  onGraphTooltipInactive,
}: ServerResourceGraphProps): ReactElement {
  const [isChartReady, setIsChartReady] = useState(false);

  const { data, summedData } = useMemo(() => {
    return formatData(statistics, hostKey, processKey, activeResourceType);
  }, [activeResourceType, hostKey, processKey, statistics]);

  const isPlexMediaServerVisible =
    activeResourceType === ServerResourceType.All ||
    activeResourceType === ServerResourceType.PlexMediaServer;
  const isSystemVisible =
    activeResourceType === ServerResourceType.All ||
    activeResourceType === ServerResourceType.System;

  const series = [
    {
      dataKey: ServerResourceType.PlexMediaServer,
      title: getServerResourceTypeLabel(ServerResourceType.PlexMediaServer),
      visible: isPlexMediaServerVisible,
      selected: activeResourceType === ServerResourceType.PlexMediaServer,
      color: hostColor,
    },
    {
      dataKey: ServerResourceType.System,
      title: getServerResourceTypeLabel(ServerResourceType.System),
      visible: isSystemVisible,
      selected: activeResourceType === ServerResourceType.System,
      color: processColor,
    },
  ];

  //
  // Callbacks

  const onChartReady = useCallback(() => {
    setIsChartReady(true);
  }, []);

  //
  // Render

  return (
    <ServerDashboardGraph
      errorMessage={errorMessage}
      isSpinnerVisible={!isPopulated || !isChartReady}
    >
      {isPopulated ? (
        <LineChart
          // @ts-expect-error ts-migrate(2322) FIXME: Type '{ width: string; height: number; series: { d... Remove this comment to see the full error message
          data={data}
          height={280}
          isAnimationActive={false}
          legendSummary={
            <LegendSummary
              isPlexMediaServerVisible={isPlexMediaServerVisible}
              isSystemVisible={isSystemVisible}
              summedData={summedData}
            />
          }
          series={series}
          showTooltipTotal={false}
          tooltipLabelFormatter={tooltipLabelFormatter}
          tooltipValueFormatter={tooltipValueFormatter}
          width="100%"
          xAxisTickFormatter={xAxisTickFormatter}
          xAxisTickInterval={1}
          yAxisDomain={[0, 100]}
          yAxisTickFormatter={yAxisTickFormatter}
          onChartReady={onChartReady}
          onLegendPress={onGraphResourceTypeSelect}
          onTooltipActive={onGraphTooltipActive}
          onTooltipInactive={onGraphTooltipInactive}
        />
      ) : null}
    </ServerDashboardGraph>
  );
}
