/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import styles from './SidebarList.css';

interface SidebarListProps {
  className?: string;
  headerContainerClassName?: string;
  headerClassName?: string;
  title: string;
  icon?: React.ReactNode;
  children?: React.ReactNode;
  onRef?: (...args: any[]) => any;
}

function SidebarList(props: SidebarListProps) {
  const {
    className,
    headerContainerClassName,
    headerClassName,
    children,
    title,
    icon,
    onRef,
  } = props;

  return (
    <div ref={onRef} className={className} role="navigation">
      <div className={headerContainerClassName}>
        <div className={headerClassName} role="header">
          {title}
        </div>
        {icon ? (
          <div className={styles.sidebarListHeaderIcon}>{icon}</div>
        ) : null}
      </div>

      {children}
    </div>
  );
}

SidebarList.defaultProps = {
  headerContainerClassName: styles.sidebarListHeaderContainer,
  headerClassName: styles.sidebarListHeader,
};

export default SidebarList;
