/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import styles from './FormHelp.css';

interface FormHelpProps {
  children: React.ReactNode;
  className?: string;
}

function FormHelp({ children, className, ...otherProps }: FormHelpProps) {
  return (
    <p {...otherProps} className={className}>
      {children}
    </p>
  );
}

FormHelp.defaultProps = {
  className: styles.help,
};

export default FormHelp;
