/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import {
  resetLogLevels,
  toggleLogLevel,
} from 'app/js/stores/actions/ui/serverLogPageActions';
import ServerLogLevelMenu from './ServerLogLevelMenu';

const mapStateToProps = createSelector(
  // @ts-expect-error ts-migrate(2571) FIXME: Object is of type 'unknown'.
  (appState) => appState.ui.serverLogPage.logLevels,
  (logLevels) => {
    return {
      logLevels,
    };
  }
);

function createMapDispatchToProps(dispatch: any, props: any) {
  const { serverEntityID } = props;

  return {
    onResetLogLevels() {
      dispatch(resetLogLevels({ serverEntityID }));
    },

    onToggleLogLevel(logLevel: any) {
      dispatch(toggleLogLevel({ serverEntityID, logLevel }));
    },
  };
}

export default connect(
  mapStateToProps,
  createMapDispatchToProps
)(ServerLogLevelMenu);
