import addWeeks from 'date-fns/addWeeks';
import format from 'date-fns/format';
import fromUnixTime from 'date-fns/fromUnixTime';
import getUnixTime from 'date-fns/getUnixTime';
import startOfDay from 'date-fns/startOfDay';
import React, { ReactElement, useCallback, useMemo, useState } from 'react';
import isToday from 'app/js/utils/dateTime/isToday';
import t from 'app/js/utils/t';
import { MetadataType } from 'app/js/v4/core/metadata/metadataTypes';
import { ServerAccount } from 'app/js/v4/core/serverAccounts/serverAccountsTypes';
import ServerDashboardGraph from 'app/js/v4/features/dashboard/components/ServerDashboardGraph';
import { PlayHistoryStatistics } from 'app/js/v4/features/dashboard/dashboardPlayHistoryTypes';
import { StatisticsMetadataType } from 'app/js/v4/features/dashboard/dashboardTypes';
import getStatisticsMetadataTypeLabel from 'app/js/v4/features/dashboard/utils/getStatisticsMetadataTypeLabel';
import {
  getTimePeriodDateFormat,
  getTimePeriodTimespan,
  TimePeriod,
  Timespan,
} from 'app/js/v4/utils/timePeriods';
import DashSeparator from 'app/js/views/v3/components/core/base/DashSeparator';
import BarChart from 'app/js/views/v3/components/core/charts/BarChart';

type Data = PerDateData[];

type PerDateData = PerTypeData & { date: number };

type PerTypeData = {
  [metadataType in StatisticsMetadataType]?: number;
};

interface PlayHistoryGraphProps {
  statistics: PlayHistoryStatistics[];
  activeStatisticsMetadataType: StatisticsMetadataType;
  activeAccount: ServerAccount | null;
  activeTimePeriod: TimePeriod;
  isPending: boolean;
  isPopulated: boolean;
  errorMessage?: string;
  hasGameStatistics: boolean;
  onGraphMetadataTypeSelect(type: MetadataType): void;
}

interface LegendSummaryProps {
  isMoviesVisible: boolean;
  isTVVisible: boolean;
  isMusicVisible: boolean;
  isPhotosVisible: boolean;
  isGamesVisible: boolean;
  summedData?: PerTypeData;
  hasGameStatistics: boolean;
}

function sortByDate(a: PerDateData, b: PerDateData): number {
  // The `StatisticsMedia` data entries returned by PMS are not necessarily
  // chronologically sorted.
  // See: https://github.com/plexinc/plex-media-server/issues/11218

  return a.date - b.date;
}

function isTodayOrAfter(date: number): boolean {
  return isToday(date) || getUnixTime(startOfDay(new Date())) <= date;
}

function formatDate(
  date: number,
  activeTimePeriod: TimePeriod,
  dateFormat: string,
  toUpperCase = false
): string {
  const isDayToday =
    getTimePeriodTimespan(activeTimePeriod) === Timespan.Day && isToday(date);

  if (isDayToday) {
    const today = t('Today');
    return toUpperCase ? today.toUpperCase() : today;
  }

  return format(fromUnixTime(date), dateFormat);
}

function formatDuration(seconds: number): string {
  const minutes = seconds / 60;
  const hours = minutes / 60;
  const days = hours / 24;
  const years = days / 365;

  let finalValue;
  let suffix;

  if (minutes < 1) {
    finalValue = seconds.toFixed();
    suffix = 's';
  } else if (hours < 1) {
    finalValue = minutes.toFixed();
    suffix = 'm';
  } else if (days < 1) {
    finalValue = hours.toFixed(1);
    suffix = 'h';
  } else if (years < 1) {
    finalValue = days.toFixed(1);
    suffix = 'd';
  } else {
    finalValue = years.toFixed(1);
    suffix = 'y';
  }

  return `${finalValue}${suffix}`;
}

function LegendSummary({
  isMoviesVisible,
  isTVVisible,
  isMusicVisible,
  isPhotosVisible,
  isGamesVisible,
  summedData,
  hasGameStatistics,
}: LegendSummaryProps): ReactElement | null {
  if (!summedData) {
    return null;
  }

  const isAllVisible =
    isMoviesVisible && isTVVisible && isMusicVisible && isPhotosVisible;

  return (
    <>
      {isAllVisible ? t('Totals') : t('Total')}
      {': '}

      {isAllVisible ? (
        <>
          {getStatisticsMetadataTypeLabel(StatisticsMetadataType.Movie)}
          <DashSeparator />
        </>
      ) : null}
      {isMoviesVisible
        ? formatDuration(summedData[StatisticsMetadataType.Movie] || 0)
        : null}

      {isAllVisible ? (
        <>
          {' | '}
          {getStatisticsMetadataTypeLabel(StatisticsMetadataType.TV)}
          <DashSeparator />
        </>
      ) : null}
      {isTVVisible
        ? formatDuration(summedData[StatisticsMetadataType.TV] || 0)
        : null}

      {isAllVisible ? (
        <>
          {' | '}
          {getStatisticsMetadataTypeLabel(StatisticsMetadataType.Music)}
          <DashSeparator />
        </>
      ) : null}
      {isMusicVisible
        ? formatDuration(summedData[StatisticsMetadataType.Music] || 0)
        : null}

      {isAllVisible ? (
        <>
          {' | '}
          {getStatisticsMetadataTypeLabel(StatisticsMetadataType.Photo)}
          <DashSeparator />
        </>
      ) : null}
      {isPhotosVisible
        ? formatDuration(summedData[StatisticsMetadataType.Photo] || 0)
        : null}

      {hasGameStatistics && isAllVisible ? (
        <>
          {' | '}
          {getStatisticsMetadataTypeLabel(StatisticsMetadataType.Game)}
          <DashSeparator />
        </>
      ) : null}
      {hasGameStatistics && isGamesVisible
        ? formatDuration(summedData[StatisticsMetadataType.Game] || 0)
        : null}
    </>
  );
}

function formatData(
  statistics: PlayHistoryStatistics[],
  activeStatisticsMetadataType: StatisticsMetadataType,
  activeAccount: ServerAccount | null
): {
  data: Data;
  summedData: PerTypeData;
} {
  const summedData: PerTypeData = {};

  const data = statistics.reduce((acc: Data, mediaStatistics) => {
    const { accountID, at, metadataType, duration } = mediaStatistics;

    if (!(metadataType in StatisticsMetadataType)) {
      // Skip over metadata types we're not interested in.
      return acc;
    }

    const index = acc.findIndex((item) => item.date === at);
    const hasExistingEntry = index !== -1;

    let dateStatistics;
    if (hasExistingEntry) {
      dateStatistics = acc[index];
    } else {
      dateStatistics = {
        date: at,
      };
    }

    if (activeAccount && activeAccount.id !== accountID) {
      // If a single user is selected, skip over any data that belongs to another
      // user.
      if (!hasExistingEntry) {
        // Make sure to insert the new data into the array before we return.
        acc.push(dateStatistics);
      }

      return acc;
    }

    if (
      activeStatisticsMetadataType === StatisticsMetadataType.All ||
      activeStatisticsMetadataType === metadataType
    ) {
      dateStatistics[metadataType] =
        (dateStatistics[metadataType] || 0) + duration;

      summedData[metadataType] = (summedData[metadataType] || 0) + duration;

      dateStatistics[StatisticsMetadataType.All] =
        (dateStatistics[StatisticsMetadataType.All] || 0) + duration;

      summedData[StatisticsMetadataType.All] =
        (summedData[StatisticsMetadataType.All] || 0) + duration;
    }

    if (hasExistingEntry) {
      acc[index] = dateStatistics;
    } else {
      acc.push(dateStatistics);
    }

    return acc;
  }, []);

  return { data: data.sort(sortByDate), summedData };
}

export default function PlayHistoryGraph({
  statistics,
  activeStatisticsMetadataType,
  activeAccount,
  activeTimePeriod,
  hasGameStatistics,
  isPending,
  isPopulated,
  errorMessage,
  onGraphMetadataTypeSelect,
}: PlayHistoryGraphProps): ReactElement {
  const [isChartReady, setIsChartReady] = useState(false);

  const { data, summedData } = useMemo(() => {
    return formatData(statistics, activeStatisticsMetadataType, activeAccount);
  }, [activeAccount, activeStatisticsMetadataType, statistics]);

  const isMoviesSelected =
    activeStatisticsMetadataType === StatisticsMetadataType.Movie;
  const isMoviesVisible =
    isMoviesSelected ||
    activeStatisticsMetadataType === StatisticsMetadataType.All;
  const isTVSelected =
    activeStatisticsMetadataType === StatisticsMetadataType.TV;
  const isTVVisible =
    isTVSelected || activeStatisticsMetadataType === StatisticsMetadataType.All;
  const isMusicSelected =
    activeStatisticsMetadataType === StatisticsMetadataType.Music;
  const isMusicVisible =
    isMusicSelected ||
    activeStatisticsMetadataType === StatisticsMetadataType.All;
  const isPhotosSelected =
    activeStatisticsMetadataType === StatisticsMetadataType.Photo;
  const isPhotosVisible =
    isPhotosSelected ||
    activeStatisticsMetadataType === StatisticsMetadataType.All;
  const isGamesSelected =
    activeStatisticsMetadataType === StatisticsMetadataType.Game;
  const isGamesVisible =
    isGamesSelected ||
    activeStatisticsMetadataType === StatisticsMetadataType.All;

  const series = [
    {
      dataKey: String(StatisticsMetadataType.Movie),
      title: getStatisticsMetadataTypeLabel(StatisticsMetadataType.Movie),
      visible: isMoviesVisible,
      selected: isMoviesSelected,
      color: '#9ad186',
    },
    {
      dataKey: String(StatisticsMetadataType.TV),
      title: getStatisticsMetadataTypeLabel(StatisticsMetadataType.TV),
      visible: isTVVisible,
      selected: isTVSelected,
      color: '#f1335e',
    },
    {
      dataKey: String(StatisticsMetadataType.Music),
      title: getStatisticsMetadataTypeLabel(StatisticsMetadataType.Music),
      visible: isMusicVisible,
      selected: isMusicSelected,
      color: '#366dfc',
    },
    {
      dataKey: String(StatisticsMetadataType.Photo),
      title: getStatisticsMetadataTypeLabel(StatisticsMetadataType.Photo),
      visible: isPhotosVisible,
      selected: isPhotosSelected,
      color: '#e69f23',
    },
  ];

  if (hasGameStatistics) {
    series.push({
      dataKey: String(StatisticsMetadataType.Game),
      title: getStatisticsMetadataTypeLabel(StatisticsMetadataType.Game),
      visible: isGamesVisible,
      selected: isGamesSelected,
      color: '#bc9bb7',
    });
  }

  //
  // Callbacks

  const tooltipLabelFormatter = useCallback(
    (dateTimestamp: number) => {
      // For week long timespans show the period the timespan covers, rather than
      // just a single date.
      if (getTimePeriodTimespan(activeTimePeriod) === Timespan.Week) {
        const startDate = formatDate(
          dateTimestamp,
          activeTimePeriod,
          getTimePeriodDateFormat(activeTimePeriod, true)
        );

        const endDateTimestamp = getUnixTime(
          addWeeks(fromUnixTime(dateTimestamp), 1)
        );

        // Check to make sure the week doesn't start and end today/after.
        const isFutureTimePeriod =
          isToday(dateTimestamp) && isTodayOrAfter(endDateTimestamp);

        if (isFutureTimePeriod) {
          return t('Today');
        } else {
          const endDate = formatDate(
            endDateTimestamp,
            activeTimePeriod,
            getTimePeriodDateFormat(activeTimePeriod, true)
          );
          return `${startDate} - ${endDate}`;
        }
      }

      return formatDate(
        dateTimestamp,
        activeTimePeriod,
        getTimePeriodDateFormat(activeTimePeriod, true)
      );
    },
    [activeTimePeriod]
  );

  const tooltipValueFormatter = useCallback((duration: number) => {
    return formatDuration(duration);
  }, []);

  const xAxisTickFormatter = useCallback(
    (dateTimestamp: number) => {
      // For the month long timespans show the period the timespan covers, rather
      // than just a single date.
      if (activeTimePeriod === TimePeriod.Month) {
        const startDate = formatDate(
          dateTimestamp,
          activeTimePeriod,
          getTimePeriodDateFormat(activeTimePeriod)
        );

        const endDateTimestamp = getUnixTime(
          addWeeks(fromUnixTime(dateTimestamp), 1)
        );

        // Check to make sure the week doesn't start and end today/after.
        const isFutureTimePeriod =
          isToday(dateTimestamp) && isTodayOrAfter(endDateTimestamp);

        if (isFutureTimePeriod) {
          return t('Today').toUpperCase();
        } else {
          const endDate = formatDate(
            endDateTimestamp,
            activeTimePeriod,
            getTimePeriodDateFormat(activeTimePeriod)
          );
          return `${startDate} - ${endDate}`;
        }
      }

      return formatDate(
        dateTimestamp,
        activeTimePeriod,
        getTimePeriodDateFormat(activeTimePeriod),
        true
      );
    },
    [activeTimePeriod]
  );

  const yAxisTickFormatter = useCallback((duration: number) => {
    if (duration === 0) {
      return '';
    }

    return formatDuration(duration);
  }, []);

  const onChartReady = useCallback(() => {
    setIsChartReady(true);
  }, []);

  //
  // Render

  return (
    <ServerDashboardGraph
      errorMessage={errorMessage}
      isSpinnerVisible={isPending || !isChartReady}
    >
      {!isPending && isPopulated ? (
        <BarChart
          // @ts-expect-error ts-migrate(2322) FIXME: Type '{ width: string; height: number; series: { d... Remove this comment to see the full error message
          data={data}
          height={280}
          legendSummary={
            <LegendSummary
              hasGameStatistics={hasGameStatistics}
              isGamesVisible={isGamesVisible}
              isMoviesVisible={isMoviesVisible}
              isMusicVisible={isMusicVisible}
              isPhotosVisible={isPhotosVisible}
              isTVVisible={isTVVisible}
              summedData={summedData}
            />
          }
          series={series}
          tooltipLabelFormatter={tooltipLabelFormatter}
          tooltipValueFormatter={tooltipValueFormatter}
          width="100%"
          xAxisTickFormatter={xAxisTickFormatter}
          yAxisTickFormatter={yAxisTickFormatter}
          onChartReady={onChartReady}
          onLegendPress={onGraphMetadataTypeSelect}
        />
      ) : null}
    </ServerDashboardGraph>
  );
}
