import React, { ReactElement, ReactNode, useEffect, useMemo } from 'react';
import useVirtualListCells, {
  VirtualListCellsProps,
} from 'app/js/v4/core/ui/lists/hooks/useVirtualListCells';
import { VirtualListMeasurements } from 'app/js/v4/core/ui/lists/listsTypes';
import { VerticalStackMeasurements } from 'app/js/v4/core/ui/lists/utils/createVerticalStackLayout';
import { PAGE_REQUEST_DELAY_MS } from 'app/js/v4/core/ui/lists/utils/pagination';

type OmittedCellsProps =
  | 'measurements'
  | 'renderEndIndex'
  | 'renderStartIndex'
  | 'visibleEndIndex'
  | 'visibleStartIndex';

export interface VirtualListProps<ItemType, ItemKeyProp, ListMeasurementsType>
  extends Omit<
    VirtualListCellsProps<ItemType, ItemKeyProp, ListMeasurementsType>,
    OmittedCellsProps
  > {
  className?: string;
  children?: ReactNode;
  totalSize: number;
  viewportWidth: number;
  viewportHeight: number;
  listTop?: number;
  listLeft?: number;
  scrollTop: number;
  scrollLeft: number;
  isWaitingToRestoreScrollPosition?: boolean;
  onListReady?(measurements: ListMeasurementsType): void;
}

export default function VirtualList<
  ItemType,
  ItemKeyProp extends keyof ItemType,
  ListMeasurementsType extends
    | VerticalStackMeasurements
    | VirtualListMeasurements
>({
  className,
  children,
  keyPropName,
  items,
  totalSize,
  layout,
  cellComponentFactory,
  viewportWidth,
  viewportHeight,
  listTop = 0,
  listLeft = 0,
  scrollTop,
  scrollLeft,
  isWaitingToRestoreScrollPosition = false,
  minPageCount = 3,
  minPageSize = 24,
  pageRequestDelay = PAGE_REQUEST_DELAY_MS,
  onPageRequest,
  onListReady,
  onListUpdate,
}: VirtualListProps<
  ItemType,
  ItemKeyProp,
  ListMeasurementsType
>): ReactElement {
  const measurements = useMemo(() => {
    return layout.getListMeasurements(
      {
        viewportWidth,
        viewportHeight,
        listTop,
        listLeft,
      },
      totalSize
    );
  }, [viewportWidth, viewportHeight, layout, listTop, listLeft, totalSize]);

  const {
    renderStartIndex,
    renderEndIndex,
    visibleStartIndex,
    visibleEndIndex,
  } = useMemo(() => {
    return layout.getRangesForPosition(
      scrollTop,
      scrollLeft,
      totalSize,
      measurements
    );
  }, [layout, measurements, scrollLeft, scrollTop, totalSize]);

  const cells = useVirtualListCells({
    keyPropName,
    items,
    totalSize,
    layout,
    measurements,
    renderStartIndex,
    renderEndIndex,
    visibleStartIndex,
    visibleEndIndex,
    cellComponentFactory,
    minPageCount,
    minPageSize,
    pageRequestDelay,
    onPageRequest,
    onListUpdate,
  });

  //
  // Effects

  useEffect(() => {
    if (onListReady) {
      onListReady(measurements);
    }
  }, [measurements, onListReady]);

  //
  // Render

  return (
    <div
      className={className}
      style={{
        width: measurements.width,
        height: measurements.height,
        position: 'relative',
      }}
    >
      {isWaitingToRestoreScrollPosition || !measurements.height ? null : cells}
      {children}
    </div>
  );
}
