/* eslint-plugin-disable @typescript-eslint */

import getSeasonEpisodeIndex from 'app/js/utils/media/getSeasonEpisodeIndex';
import * as metadataTypes from 'app/js/utils/types/metadataTypes';

export default function getFullMetadataTitle(
  metadataItem: any,
  separator = ''
) {
  const {
    type,
    title,
    parentTitle,
    grandparentTitle,
    index,
    parentIndex,
    originallyAvailableAt,
  } = metadataItem;

  switch (type) {
    case metadataTypes.EPISODE: {
      const seasonEpisodeIndex = getSeasonEpisodeIndex({
        seasonIndex: parentIndex,
        episodeIndex: index,
        date: originallyAvailableAt,
        noLeadingZero: true,
        separator: '  ',
      });

      let finalTitle = `${grandparentTitle} ${separator} ${seasonEpisodeIndex}`;

      if (title) {
        finalTitle += ` ${separator} ${title}`;
      }

      return finalTitle;
    }
    case metadataTypes.SEASON:
    case metadataTypes.ALBUM:
      return `${parentTitle} ${separator} ${title}`;
    case metadataTypes.TRACK:
      return `${grandparentTitle} ${separator} ${parentTitle} ${separator} ${title}`;
    default:
      return title;
  }
}
