/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { Component } from 'react';
import {
  IconExclamationCircledFilled,
  IconLockLockedCircledFilled,
  IconWarning,
} from '@plex/react-chroma-icon';
import t from 'app/js/utils/t';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';
import styles from './ServerConnectionStatusIcon.css';

type ServerConnectionStatusIconProps = {
  className?: string;
  isBadge?: boolean;
  isConnected?: boolean;
  isUnavailable?: boolean;
  isSecure?: boolean;
  isRelayed?: boolean;
};

class ServerConnectionStatusIcon extends Component<ServerConnectionStatusIconProps> {
  //
  // Lifecycle

  shouldComponentUpdate(
    nextProps: ServerConnectionStatusIconProps,
    nextState: any
  ) {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    return shouldPureComponentUpdate(this, nextProps, nextState);
  }

  //
  // Render

  render() {
    const {
      className,
      isBadge,
      isConnected,
      isUnavailable,
      isSecure,
      isRelayed,
    } = this.props;

    if (isRelayed) {
      return (
        <div
          className={classNames(
            className,
            styles.warningIcon,
            isBadge && styles.circleBadge
          )}
          title={t('Indirect')}
        >
          <IconExclamationCircledFilled aria-label={t('Indirect')} />
        </div>
      );
    } else if (isUnavailable) {
      return (
        <div
          className={classNames(
            className,
            styles.alertIcon,
            isBadge && styles.triangleBadge
          )}
          title={t('Unavailable')}
        >
          <IconWarning aria-label={t('Unavailable')} />
        </div>
      );
    } else if (isSecure && isConnected) {
      return (
        <div
          className={classNames(
            className,
            styles.secureIcon,
            isBadge && styles.circleBadge
          )}
          title={t('Secure')}
        >
          <IconLockLockedCircledFilled aria-label={t('Secure')} />
        </div>
      );
    }

    // If the server is connected, but not secure, don't show any icon.
    return null;
  }
}

export default ServerConnectionStatusIcon;
