import {
  ContentDirectory,
  Pivot,
  Provider,
} from 'app/js/v4/core/providers/providersTypes';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import isUnknownViewPivot from 'app/js/v4/features/directory/utils/isUnknownViewPivot';
import getSourceRoute from 'app/js/views/v3/helpers/routes/getSourceRoute';

function getPivotRoute(
  server: Server,
  provider: Provider,
  contentDirectory: ContentDirectory | undefined,
  pivot: Pivot
): string | undefined {
  if (!contentDirectory) {
    return;
  }

  if (isUnknownViewPivot(pivot)) {
    return;
  }

  const isInlinePivot = !contentDirectory.pivots?.includes(pivot);

  const params =
    // Pivots that come from Meta will not be included in the contentDirectory.
    // In order to instruct DirectoryPage to load the key with out any Selected
    // Pivot we intentionally don't use the `pivot` param, which will allow
    // DirectoryPage to load the key directly and display any inline pivots
    isInlinePivot
      ? {
          pageType: pivot.type,
          key: pivot.key,
        }
      : {
          pivot: pivot.id || pivot.key,
        };

  return getSourceRoute(server, provider, contentDirectory, params);
}

export default getPivotRoute;
