import classNames from 'classnames';
import React, { InputHTMLAttributes, ReactElement, Ref } from 'react';
import Size from 'app/js/v4/core/ui/types/Size';
import styles from './Checkbox.css';

interface InnerCheckboxProps
  extends Omit<InputHTMLAttributes<HTMLInputElement>, 'size' | 'value'> {
  forwardedRef: Ref<HTMLInputElement>;
  className?: string;
  errorClassName?: string;
  disabledClassName?: string;
  name: string;
  size?: Size;
  isDisabled?: boolean;
  hasError?: boolean;
}

export type CheckboxProps = Omit<InnerCheckboxProps, 'forwardedRef'>;

function Checkbox({
  forwardedRef,
  className = styles.input,
  errorClassName = styles.hasError,
  disabledClassName = styles.isDisabled,
  isDisabled = false,
  hasError = false,
  name,
  size = Size.Medium,
  ...inputProps
}: InnerCheckboxProps): ReactElement {
  return (
    <input
      ref={forwardedRef}
      className={classNames(
        className,
        styles[size],
        isDisabled && disabledClassName,
        hasError && errorClassName
      )}
      disabled={isDisabled}
      id={name}
      name={name}
      type="checkbox"
      {...inputProps}
    />
  );
}

export default React.forwardRef<HTMLInputElement, CheckboxProps>(
  (props, ref) => {
    return <Checkbox {...props} forwardedRef={ref} />;
  }
);
