/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { signOutServer } from 'app/js/stores/actions/server/claimServerActions';
import createServerCloudAccountSelector from 'app/js/stores/selectors/server/createServerCloudAccountSelector';
import selectAuthCodeExchange from 'app/js/stores/selectors/user/selectAuthCodeExchange';
import ClaimedServer from './ClaimedServer';

function createServerCloudAccountUsernameSelector() {
  return createSelector(
    createServerCloudAccountSelector(),
    (serverCloudAccount) =>
      (serverCloudAccount && serverCloudAccount.username) || null
  );
}

const selectIsBusy = createSelector(
  selectAuthCodeExchange,
  (authCodeExchangeProps) => authCodeExchangeProps.isBusy
);

function createMapStateToProps() {
  return createSelector(
    selectIsBusy,
    createServerCloudAccountUsernameSelector(),
    (isSigningOut, serverCloudAccountUsername) => {
      return {
        isSigningOut,
        username: serverCloudAccountUsername,
      };
    }
  );
}

const mapDispatchToProps = {
  dispatchSignOutServer: signOutServer,
};

type ClaimedServerConnectorProps = {
  serverEntityID: string;
  dispatchSignOutServer: (...args: any[]) => any;
};

class ClaimedServerConnector extends Component<ClaimedServerConnectorProps> {
  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onSignOutPress = (event: any) => {
    this.props.dispatchSignOutServer({
      serverEntityID: this.props.serverEntityID,
    });
  };

  //
  // Render

  render() {
    const { dispatchSignOutServer, ...otherProps } = this.props;

    return (
      // @ts-expect-error ts-migrate(2739) FIXME: Type '{ onSignOutPress: (event: any) => void; serv... Remove this comment to see the full error message
      <ClaimedServer {...otherProps} onSignOutPress={this.onSignOutPress} />
    );
  }
}

export default connect(
  createMapStateToProps,
  mapDispatchToProps
)(ClaimedServerConnector);
