import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { resolve } from 'app/js/adapters/adapters';
import AbstractReactView from 'app/js/views/AbstractReactView';
import ClaimServerConnector from 'app/js/views/v3/components/pages/serverSettings/ClaimServerConnector';

const ServerSettingsClaimViewReact = AbstractReactView.extend({
  //
  // Properties

  template: () => '',

  //
  // Control

  mountReact() {
    const appStore = resolve('appStore');

    const serverEntityID = this.model.entityID();

    ReactDOM.render(
      <Provider store={appStore}>
        <ClaimServerConnector serverEntityID={serverEntityID} />
      </Provider>,
      this.el
    );
  },
});

export default ServerSettingsClaimViewReact;
