import React, { ReactElement, useCallback, useMemo } from 'react';
import { useSelector } from 'react-redux';
import * as serverActions from 'app/js/stores/actions/serverActions';
import t from 'app/js/utils/t';
import createProviderContentDirectoriesSelector from 'app/js/v4/core/providers/selectors/createProviderContentDirectoriesSelector';
import createProviderSelector from 'app/js/v4/core/providers/selectors/createProviderSelector';
import createIsServerLibrariesUpdatingSelector from 'app/js/v4/core/serverActivities/selectors/createIsServerLibrariesUpdatingSelector';
import SourceContextProvider from 'app/js/v4/core/servers/components/SourceContextProvider';
import { useRedispatchLegacyServerAction } from 'app/js/v4/core/servers/hooks/useRedispatchLegacyServerAction';
import createStableServerSelector from 'app/js/v4/core/servers/selectors/createStableServerSelector';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import Size from 'app/js/v4/core/ui/types/Size';
import LibrarySettings from 'app/js/v4/features/libraryManagement/components/LibrarySettings';
import ManageLibrarySummary from 'app/js/v4/features/libraryManagement/components/ManageLibrarySummary';
import styles from './ManageLibrariesPane.css';

interface ManageLibrariesPaneProps {
  machineIdentifier: string;
  providerIdentifier: string;
}

export default function ManageLibrariesPane({
  machineIdentifier,
  providerIdentifier,
}: ManageLibrariesPaneProps): ReactElement {
  const directories = useSelector(
    createProviderContentDirectoriesSelector(
      machineIdentifier,
      providerIdentifier
    )
  );

  const server = useSelector(createStableServerSelector(machineIdentifier));
  const provider = useSelector(
    createProviderSelector(machineIdentifier, providerIdentifier)
  );

  const visibleDirectories = useMemo(
    () => directories.filter(({ key, hubKey, type }) => key && hubKey && type),
    [directories]
  );

  const redispatchLegacyServerAction = useRedispatchLegacyServerAction({
    server,
    provider,
  });

  const isScanning = useSelector(
    createIsServerLibrariesUpdatingSelector(server)
  );

  const onAddLibraryPress = useCallback(() => {
    redispatchLegacyServerAction(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
      serverActions.addLibrary({
        redirect: false,
      })
    );
  }, [redispatchLegacyServerAction]);

  const onScanLibrariesPress = useCallback(() => {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
    redispatchLegacyServerAction(serverActions.startLibrariesRefresh());
  }, [redispatchLegacyServerAction]);

  const onCancelScanLibrariesPress = useCallback(() => {
    redispatchLegacyServerAction(serverActions.cancelLibrariesRefresh());
  }, [redispatchLegacyServerAction]);

  return (
    <SourceContextProvider
      machineIdentifier={machineIdentifier}
      providerIdentifier={providerIdentifier}
    >
      <div className={styles.header}>
        <div className={styles.libraryCount}>
          {visibleDirectories.length === 1
            ? t('1 library')
            : t('{1} libraries', visibleDirectories.length)}
        </div>
        <Button
          className={styles.button}
          size={Size.Small}
          onPress={onAddLibraryPress}
        >
          {t('Add Library')}
        </Button>
        {isScanning ? (
          <Button
            className={styles.button}
            size={Size.Small}
            onPress={onCancelScanLibrariesPress}
          >
            {t('Cancel Scan')}
          </Button>
        ) : (
          <Button
            className={styles.button}
            size={Size.Small}
            onPress={onScanLibrariesPress}
          >
            {t('Scan Library Files')}
          </Button>
        )}
      </div>

      {visibleDirectories.map((contentDirectory, index) => (
        <ManageLibrarySummary
          key={index}
          contentDirectory={contentDirectory}
          index={index}
          machineIdentifier={machineIdentifier}
          providerEntityID={provider.entityID}
        />
      ))}

      <LibrarySettings />
    </SourceContextProvider>
  );
}
