/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { Component } from 'react';
// @ts-expect-error ts-migrate(6133) FIXME: 'SMALL' is declared but its value is never read.
import { LARGE, SMALL } from 'app/js/views/v3/helpers/props/sizes';
import styles from './TextInput.css';

interface TextInputProps {
  className?: string;
  errorClassName?: string;
  disabledClassName?: string;
  isDisabled?: boolean;
  initialValue?: string | number;
  value?: string | number;
  size?: any; // TODO: PropTypes.oneOf([SMALL, LARGE])
  type?: 'text' | 'password' | 'number' | 'email' | 'date';
  name: string;
  placeholder?: string;
  hasError?: boolean;
  errors?: string[];
  validations?: ((...args: any[]) => any)[];
  focusOnMount?: boolean;
  selectTextOnFocus?: boolean;
  onChange: (...args: any[]) => any;
}

class TextInput extends Component<TextInputProps> {
  static defaultProps = {
    className: styles.input,
    errorClassName: styles.hasError,
    disabledClassName: styles.isDisabled,
    isDisabled: false,
    size: LARGE,
    type: 'text',
    hasError: false,
    focusOnMount: false,
    selectTextOnFocus: false,
  };

  // eslint-disable-next-line react/sort-comp
  inputElement: any;

  //
  // Lifecycle

  componentDidMount() {
    if (this.props.focusOnMount) {
      this.inputElement.focus();
      this._select();
    }
  }

  //
  // Control

  _select() {
    if (this.inputElement && this.props.selectTextOnFocus) {
      const value = this.inputElement.value;
      const length = value ? value.length : 0;
      this.inputElement.selectionStart = 0;
      this.inputElement.selectionEnd = length;
    }
  }

  //
  // Listeners

  onChange = (event: any) => {
    this.props.onChange(event.target.value);
  };

  onFocus = () => {
    this._select();
  };

  onRef = (inputElement: any) => {
    this.inputElement = inputElement;
  };

  //
  // Render

  render() {
    const {
      className,
      errorClassName,
      disabledClassName,
      isDisabled,
      initialValue,
      value,
      size,
      type,
      name,
      placeholder,
      hasError,
      errors,
      validations,
      focusOnMount,
      selectTextOnFocus,
      ...otherProps
    } = this.props;

    return (
      <input
        {...otherProps}
        ref={focusOnMount ? this.onRef : null}
        className={classNames(
          className,
          styles[size],
          isDisabled && disabledClassName,
          hasError && errorClassName
        )}
        disabled={isDisabled}
        id={name}
        name={name}
        placeholder={placeholder}
        type={type}
        value={value}
        onChange={this.onChange}
        onFocus={this.onFocus}
      />
    );
  }
}

export default TextInput;
