import React, {
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import t from 'app/js/utils/t';
import createChannelScanActivitySelector from 'app/js/v4/core/serverActivities/selectors/createChannelScanActivitySelector';
import createEPGLineupScanActivitySelector from 'app/js/v4/core/serverActivities/selectors/createEPGLineupScanActivitySelector';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import SpinnerButton from 'app/js/v4/core/ui/buttons/components/SpinnerButton';
import useMeasure from 'app/js/v4/core/ui/hooks/useMeasure';
import ModalBody from 'app/js/v4/core/ui/modals/components/ModalBody';
import ModalFooter from 'app/js/v4/core/ui/modals/components/ModalFooter';
import ModalFooterButtonBar from 'app/js/v4/core/ui/modals/components/ModalFooterButtonBar';
import { ScrollPosition } from 'app/js/v4/core/ui/scrollers/scrollerTypes';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import Kind from 'app/js/v4/core/ui/types/Kind';
import ChannelScanActivity from 'app/js/v4/features/dvrManagement/components/ChannelScanActivity';
import ChannelScanPrefs from 'app/js/v4/features/dvrManagement/components/ChannelScanPrefs';
import DeviceChannelMappingsForm, {
  ChannelMappingChangeProps,
} from 'app/js/v4/features/dvrManagement/components/DeviceChannelMappingsForm';
import {
  DVRSetupStep,
  DVRSetupStepProps,
} from 'app/js/v4/features/dvrManagement/components/setup/DVRSetupModal';
import { ChannelMappingFormData } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import createChannelMappingsSelector from 'app/js/v4/features/dvrManagement/selectors/createChannelMappingsSelector';
import createDeviceChannelsSelector from 'app/js/v4/features/dvrManagement/selectors/createDeviceChannelsSelector';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import selectSetupSelectedLineups from 'app/js/v4/features/dvrManagement/selectors/selectSetupSelectedLineups';
import usePrevious from 'app/js/v4/utils/hooks/usePrevious';
import shallowEqual from 'app/js/views/v3/helpers/shallowEqual';
import dimensions from 'app/js/views/v3/styles/variables/dimensions';
import EPGLineups from './EPGLineups';
import styles from './DVRSetupMapping.css';

const FORM_ID = 'channel-mappings-form';

const DEVICE_CHANNEL_MAPPING_HEADER_HEIGHT = parseInt(
  dimensions.dvrSetupChannelMappingItemHeight,
  10
);

export default function DVRSetupMapping({
  dvrID,
  onSetStep,
}: DVRSetupStepProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const [epgLineupsMeasureRef, { height: epgLineupsHeight }] = useMeasure();
  const [modalBodyRef, { height: modalBodyHeight }] = useMeasure();
  const {
    selectedDevice,
    selectedDVR: { error: selectedDVRError },
    scanPrefs,
    scanNetworks,
    hasScannedChannels,
    isSavingDVR,
  } = useSelector(selectDVRSetup);
  const {
    channelMappings: { isPending: isChannelMappingsPending },
  } = useSelector(selectEPGSettings);

  const selectChannelScanActivity = useMemo(
    () => createChannelScanActivitySelector(server),
    [server]
  );
  const selectEPGLineupScanActivity = useMemo(
    () => createEPGLineupScanActivitySelector(server),
    [server]
  );
  const selectDeviceChannels = useMemo(
    () => createDeviceChannelsSelector(selectedDevice?.key),
    [selectedDevice]
  );
  const selectChannelMappings = useMemo(
    () => createChannelMappingsSelector(selectedDevice?.key),
    [selectedDevice]
  );

  const channelScanActivity = useSelector(selectChannelScanActivity);
  const epgLineupScanActivity = useSelector(selectEPGLineupScanActivity);
  const { channels: deviceChannels, isPending: isDeviceChannelsPending } =
    useSelector(selectDeviceChannels);

  const selectedLineups = useSelector(selectSetupSelectedLineups);
  const channelMappings = useSelector(selectChannelMappings);
  const prevIsSavingDVR = usePrevious(isSavingDVR);
  const prevSelectedLineups = usePrevious(selectedLineups);

  const [scrollPosition, setScrollPosition] = useState<ScrollPosition>({
    scrollTop: 0,
    scrollLeft: 0,
  });

  //
  // Effects

  useEffect(() => {
    if (prevIsSavingDVR && !isSavingDVR && !selectedDVRError) {
      onSetStep(DVRSetupStep.Download);
    }
  }, [prevIsSavingDVR, isSavingDVR, selectedDVRError, onSetStep]);

  useEffect(() => {
    if (selectedDevice && hasScannedChannels) {
      scheduleImmediateUpdate(() => {
        dispatch(
          dvrSetupSlice.actions.deviceChannelsFetchRequested({
            server,
            deviceKey: selectedDevice.key,
          })
        );
      });
    }
  }, [dispatch, server, selectedDevice, hasScannedChannels]);

  useEffect(() => {
    if (
      !selectedDevice ||
      !selectedLineups ||
      !selectedLineups?.length ||
      (prevSelectedLineups &&
        shallowEqual(prevSelectedLineups, selectedLineups))
    ) {
      return;
    }

    dispatch(
      epgSettingsSlice.actions.channelMappingsFetchRequested({
        server,
        device: selectedDevice,
        lineups: selectedLineups,
      })
    );
  }, [dispatch, selectedDevice, selectedLineups, prevSelectedLineups, server]);

  //
  // Callbacks

  const onChannelMappingChange = useCallback(
    ({ index, ...values }: ChannelMappingChangeProps): void => {
      dispatch(
        epgSettingsSlice.actions.channelMappingChanged({
          index,
          ...values,
        })
      );
    },
    [dispatch]
  );

  const onBackPress = useCallback(() => {
    onSetStep(DVRSetupStep.Device);
  }, [onSetStep]);

  const onSubmit = useCallback(
    (data: ChannelMappingFormData[]): void => {
      if (!selectedDevice) {
        return;
      }

      scheduleImmediateUpdate(() => {
        if (dvrID) {
          dispatch(
            dvrSetupSlice.actions.addDVRDeviceRequested({
              server,
              dvrID,
              deviceKey: selectedDevice.key,
              channelMappings: data,
            })
          );
        } else {
          dispatch(
            dvrSetupSlice.actions.dvrSaveRequested({
              server,
              device: selectedDevice,
              channelMappings: data,
            })
          );
        }
      });
    },
    [dispatch, selectedDevice, dvrID, server]
  );

  const onScrollPositionChange = useCallback((scrollUpdate: ScrollPosition) => {
    setScrollPosition(scrollUpdate);
  }, []);

  //
  // Render

  return (
    <>
      <ModalBody
        ref={modalBodyRef}
        className={styles.modalBody}
        innerClassName={styles.innerModalBody}
        onScrollPositionChange={onScrollPositionChange}
      >
        {!channelScanActivity &&
        !epgLineupScanActivity &&
        !isChannelMappingsPending &&
        deviceChannels.length > 0 ? (
          <EPGLineups ref={epgLineupsMeasureRef} onSetStep={onSetStep} />
        ) : null}

        {!channelScanActivity &&
        !epgLineupScanActivity &&
        !isDeviceChannelsPending &&
        !isChannelMappingsPending &&
        channelMappings.length > 0 &&
        deviceChannels.length > 0 &&
        selectedLineups &&
        selectedLineups.length > 0 ? (
          <DeviceChannelMappingsForm
            channelMappings={channelMappings}
            height={modalBodyHeight}
            id={FORM_ID}
            scrollLeft={scrollPosition.scrollLeft}
            scrollTop={
              scrollPosition.scrollTop -
              epgLineupsHeight -
              DEVICE_CHANNEL_MAPPING_HEADER_HEIGHT * 2
            }
            onChannelMappingChange={onChannelMappingChange}
            onSubmit={onSubmit}
          />
        ) : null}

        {!channelScanActivity &&
        !epgLineupScanActivity &&
        !isDeviceChannelsPending &&
        !isChannelMappingsPending &&
        deviceChannels.length > 0 &&
        !selectedLineups?.length ? (
          <div className={styles.container}>
            {t('Please select a guide to begin mapping your device channels.')}
          </div>
        ) : null}

        {!channelScanActivity &&
        (!!epgLineupScanActivity ||
          isDeviceChannelsPending ||
          isChannelMappingsPending) ? (
          <div className={styles.container}>
            <Spinner />
          </div>
        ) : null}

        {!channelScanActivity && (scanPrefs.length || scanNetworks.length) ? (
          <ChannelScanPrefs />
        ) : null}

        {!scanPrefs?.length &&
        !scanNetworks?.length &&
        !epgLineupScanActivity &&
        !isDeviceChannelsPending &&
        !isChannelMappingsPending ? (
          <ChannelScanActivity className={styles.container} />
        ) : null}
      </ModalBody>

      <ModalFooter>
        <ModalFooterButtonBar className={styles.buttonBar}>
          <Button onPress={onBackPress}>{t('Back')}</Button>
          <SpinnerButton
            form={FORM_ID}
            isDisabled={
              !!channelScanActivity ||
              isChannelMappingsPending ||
              channelMappings.length === 0
            }
            isSpinning={isSavingDVR}
            kind={Kind.Primary}
            type="submit"
          >
            {t('Continue')}
          </SpinnerButton>
        </ModalFooterButtonBar>
      </ModalFooter>
    </>
  );
}
