import classNames from 'classnames';
import React, { CSSProperties, FocusEvent, ReactNode } from 'react';
import styles from './ListRow.css';

export interface ListRowProps {
  className?: string;
  alternateClassName?: string;
  hoveredClassName?: string;
  tabIndex?: number;
  style?: CSSProperties;
  isAlternate?: boolean;
  isHovered?: boolean;
  children?: ReactNode;
  onFocus?(event: FocusEvent<HTMLElement>): void;
  onBlur?(event: FocusEvent<HTMLElement>): void;
}

export default React.forwardRef<HTMLDivElement, ListRowProps>(
  (
    {
      className = styles.row,
      alternateClassName = styles.alternateRow,
      hoveredClassName = styles.isHovered,
      tabIndex,
      style = {},
      isAlternate = false,
      isHovered = false,
      children,
      onFocus,
      onBlur,
    },
    forwardedRef
  ) => {
    return (
      <div
        ref={forwardedRef}
        className={classNames(
          isAlternate ? alternateClassName : className,
          isHovered && hoveredClassName
        )}
        style={style}
        tabIndex={tabIndex}
        onBlur={onBlur}
        onFocus={onFocus}
      >
        {children}
      </div>
    );
  }
);
