import { createSelector, Selector } from 'reselect';
import { ContentDirectory } from 'app/js/v4/core/providers/providersTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import memoize from 'app/js/v4/utils/memoize';
import createProviderContentDirectoriesSelector from './createProviderContentDirectoriesSelector';

// This selector may be created frequently in components and saga workers,
// but there is a limited number of possible selectors based on the arguments.
// Rather than independently memoize in each component or saga, add a layer of
// memoization on top of reselect here.

function createProviderContentDirectorySelector(
  machineIdentifier: string,
  providerIdentifier: string,
  contentDirectoryID?: string
): Selector<LegacyRootState, ContentDirectory | undefined> {
  return createSelector(
    createProviderContentDirectoriesSelector(
      machineIdentifier,
      providerIdentifier
    ),
    (contentDirectories) => {
      return contentDirectories.find(
        (contentDirectory) => contentDirectoryID === contentDirectory.id
      );
    }
  );
}

export default memoize(createProviderContentDirectorySelector);
