import { createSelector, Selector } from 'reselect';
import isAbsoluteUrl from 'app/js/utils/url/isAbsoluteUrl';
import path from 'app/js/utils/url/path';
import { ProviderFeatureType } from 'app/js/v4/core/providers/providersTypes';
import createProviderSelector from 'app/js/v4/core/providers/selectors/createProviderSelector';
import getProviderFeature from 'app/js/v4/core/providers/utils/getProviderFeature';
import createActiveConnectionURISelector from 'app/js/v4/core/servers/selectors/createActiveConnectionURISelector';
import createStableServerSelector from 'app/js/v4/core/servers/selectors/createStableServerSelector';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import memoize from 'app/js/v4/utils/memoize';

function createImageTranscodeURLSelector(
  machineIdentifier: string,
  providerIdentifier?: string,
  url?: string,
  urlParams?: object
): Selector<LegacyRootState, string | undefined> {
  if (!url) {
    return () => undefined;
  }

  return createSelector(
    createStableServerSelector(machineIdentifier),
    createActiveConnectionURISelector(machineIdentifier),
    createProviderSelector(machineIdentifier, providerIdentifier),
    (server, connectionURI, provider) => {
      let transcodeKey = '/photo/:/transcode';
      let isAbsoluteTranscodeKey = false;
      let isPublicTranscoder = false;
      let baseUrl;

      if (!connectionURI) {
        return;
      }

      if (provider) {
        const imageTranscoderFeature = getProviderFeature(
          provider,
          ProviderFeatureType.ImageTranscoder
        );

        if (imageTranscoderFeature?.key) {
          transcodeKey = imageTranscoderFeature.key;
          isAbsoluteTranscodeKey = isAbsoluteUrl(transcodeKey);
          isPublicTranscoder = !!imageTranscoderFeature.public;
        }
      }

      // Use a relative path if the transcoder is relative to the image's
      // server. If the transcoder is hosted separately or part of a
      // different server, use the absolute path.

      if (isAbsoluteTranscodeKey) {
        baseUrl = provider.baseURL ?? connectionURI;
      } else {
        baseUrl = '';
      }

      let imageUrl;

      if (isAbsoluteUrl(url)) {
        imageUrl = url;
      } else {
        const params = { 'X-Plex-Token': server.accessToken, ...urlParams };
        imageUrl = path.join([baseUrl, url], params);
      }

      const pathParts = isAbsoluteTranscodeKey
        ? transcodeKey
        : [connectionURI, transcodeKey];

      const transcodeUrl = path.join(pathParts, {
        url: imageUrl,
        'X-Plex-Token': isPublicTranscoder ? undefined : server.accessToken,
      });

      return transcodeUrl;
    }
  );
}

export default memoize(createImageTranscodeURLSelector);
