/* eslint-plugin-disable @typescript-eslint */

import durationOf from 'app/js/utils/dateTime/durationOf';
import t from 'app/js/utils/t';

function formatHoursMinutes(milliseconds: any) {
  if (milliseconds == null) {
    return '';
  }
  const duration = durationOf(milliseconds);
  const hr = (duration as any).hours + 24 * (duration as any).days;
  const min = (duration as any).minutes;
  const sec = (duration as any).seconds;
  if (hr && min) {
    return t('{1} hr {2} min', hr, min);
  } else if (hr) {
    return t('{1} hr', hr);
  } else if (min) {
    return t('{1} min', min);
  } else if (sec > 0) {
    return t('{1} sec', sec);
  } else {
    return '';
  }
}
export default formatHoursMinutes;
