/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import getSelectedVideo from 'app/js/utils/media/getSelectedVideo';
import t from 'app/js/utils/t';
import AudioStreamTitle from './AudioStreamTitle';
import SubtitlesStreamTitle from './SubtitlesStreamTitle';
import VideoStreamTitle from './VideoStreamTitle';

const streamTitleComponents = {
  audio: AudioStreamTitle,
  subtitles: SubtitlesStreamTitle,
  video: VideoStreamTitle,
};

interface StreamTitleProps {
  type: 'audio' | 'subtitles' | 'video';
  stream?: any;
  mediaItem?: any;
  isDisplayTitleSupported: boolean;
}

function StreamTitle(props: StreamTitleProps) {
  const { type, stream, mediaItem, isDisplayTitleSupported, ...otherProps } =
    props;

  if (!stream && !mediaItem) {
    return <span data-testid={`${type}Stream`}>{t('None')}</span>;
  }

  if (isDisplayTitleSupported) {
    let displayTitle;
    if (type === 'video') {
      const videoStream = getSelectedVideo(mediaItem);
      displayTitle = videoStream ? videoStream.displayTitle : t('None');
    } else {
      // `None` streams inserted by ourselves will not have a `displayTitle`
      // attribute, so fall back to the language for those.
      displayTitle = stream.displayTitle || stream.language;
    }

    return (
      <span aria-label={displayTitle} data-testid={`${type}Stream`}>
        {displayTitle}
      </span>
    );
  }

  const StreamTitleComponent = streamTitleComponents[type];

  return (
    // @ts-expect-error ts-migrate(2741) FIXME: Property 'separatorComponent' is missing in type '... Remove this comment to see the full error message
    <StreamTitleComponent
      {...otherProps}
      mediaItem={mediaItem}
      stream={stream}
    />
  );
}

export default StreamTitle;
