import React, { ReactElement } from 'react';
import {
  IconArtist,
  IconBook,
  IconCamera,
  IconGames,
  IconMovie,
  IconMusic,
  IconPlaylist,
  IconTv,
  IconVideo,
} from '@plex/react-chroma-icon';
import { MetadataType } from 'app/js/v4/core/metadata/metadataTypes';

function assertNever(
  metadataType:
    | MetadataType.Channel
    | MetadataType.Collection
    | MetadataType.Directory
    | MetadataType.Hub
    | MetadataType.List
    | MetadataType.Person
    | never
): never {
  throw new Error(`"${metadataType}" is not mapped to an icon`);
}

export interface MetadataIconProps {
  type: MetadataType;
}

export default function MetadataIcon({
  type,
}: MetadataIconProps): ReactElement {
  switch (type) {
    case MetadataType.Movie:
      return <IconMovie />;
    case MetadataType.Show:
    case MetadataType.Season:
    case MetadataType.Episode:
      return <IconTv />;
    case MetadataType.Artist:
      return <IconArtist />;
    case MetadataType.Album:
    case MetadataType.Track:
      return <IconMusic />;
    case MetadataType.Photo:
    case MetadataType.PhotoAlbum:
      return <IconCamera />;
    case MetadataType.Clip:
      return <IconVideo />;
    case MetadataType.Game:
      return <IconGames />;
    case MetadataType.Playlist:
      return <IconPlaylist />;
    case MetadataType.Author:
    case MetadataType.Audiobook:
    case MetadataType.Chapter:
      return <IconBook />;
  }

  assertNever(type);
}
