/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { claimServer } from 'app/js/stores/actions/server/claimServerActions';
import createUserAttributeSelector from 'app/js/stores/selectors/user/createUserAttributeSelector';
import selectAuthCodeExchange from 'app/js/stores/selectors/user/selectAuthCodeExchange';
import selectIsAnonymousUser from 'app/js/stores/selectors/user/selectIsAnonymousUser';
import shouldClientPoll from 'app/js/utils/shouldClientPoll';
import ClaimServerSignedInButtonConnector from './ClaimServerSignedInButtonConnector';
import ClaimServerSignedOutLaunchButtonConnector from './ClaimServerSignedOutLaunchButtonConnector';
import ClaimServerSignedOutPollButtonConnector from './ClaimServerSignedOutPollButtonConnector';
import UnclaimedServer from './UnclaimedServer';

const SIGNED_IN_BUTTON = 'SIGNED_IN_BUTTON';
const SIGNED_OUT_POLL_BUTTON = 'SIGNED_OUT_POLL_BUTTON';
const SIGNED_OUT_LAUNCH_BUTTON = 'SIGNED_OUT_LAUNCH_BUTTON';

function createMapStateToProps() {
  return createSelector(
    selectAuthCodeExchange,
    selectIsAnonymousUser,
    createUserAttributeSelector('isSignedIn'),
    createUserAttributeSelector('restricted'),
    (
      authCodeExchangeProps,
      isAnonymousUser,
      isUserSignedIn,
      isUserRestricted
    ) => {
      let componentType;
      if (isUserSignedIn) {
        componentType = SIGNED_IN_BUTTON;
      } else if (shouldClientPoll()) {
        componentType = SIGNED_OUT_POLL_BUTTON;
      } else {
        componentType = SIGNED_OUT_LAUNCH_BUTTON;
      }

      return {
        componentType,
        isUserRestricted: !!isUserRestricted,
        isAnonymousUser,
        ...authCodeExchangeProps,
      };
    }
  );
}

const mapDispatchToProps = {
  dispatchClaimServer: claimServer,
};

const claimComponents = {
  [SIGNED_IN_BUTTON]: ClaimServerSignedInButtonConnector,
  [SIGNED_OUT_POLL_BUTTON]: ClaimServerSignedOutPollButtonConnector,
  [SIGNED_OUT_LAUNCH_BUTTON]: ClaimServerSignedOutLaunchButtonConnector,
};

type UnclaimedServerConnectorProps = {
  serverEntityID: string;
  componentType: any; // TODO: PropTypes.oneOf(Object.keys(claimComponents))
  isBusy: boolean;
  isAnonymousUser: boolean;
  isUserRestricted: boolean;
  authToken?: string;
};

class UnclaimedServerConnector extends Component<UnclaimedServerConnectorProps> {
  //
  // Render

  render() {
    const {
      serverEntityID,
      componentType,
      isBusy,
      isAnonymousUser,
      isUserRestricted,
    } = this.props;

    return (
      <UnclaimedServer
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        ClaimServerButtonConnectorComponent={claimComponents[componentType]}
        isAnonymousUser={isAnonymousUser}
        isBusy={isBusy}
        isUserRestricted={isUserRestricted}
        serverEntityID={serverEntityID}
      />
    );
  }
}

export default connect(
  createMapStateToProps,
  mapDispatchToProps
)(UnclaimedServerConnector);
