import createKey from 'app/js/views/v3/helpers/createKey';

export function createWebClientSettingsSidebarKey(settingsSubset: any) {
  return createKey('web-client', 'settings', settingsSubset);
}
export function createServerSettingsSidebarKey(
  machineIdentifier: any,
  settingsGroup: any,
  settingsType: any
) {
  return createKey('server', machineIdentifier, settingsGroup, settingsType);
}
