import React, { ReactNode } from 'react';
import Scroller, {
  ScrollDirection,
  ScrollerProps,
} from 'app/js/v4/core/ui/scrollers/components/Scroller';
import styles from './ModalBody.css';

export interface ModalBodyProps extends ScrollerProps {
  children: ReactNode;
  className?: string;
  innerClassName?: string;
}

const ModalBody = React.forwardRef<HTMLDivElement, ModalBodyProps>(
  (
    {
      children,
      className,
      innerClassName = styles.innerModalBody,
      ...scrollerProps
    },
    forwardedRef
  ) => {
    const isScroller = scrollerProps.scrollDirection !== ScrollDirection.None;

    if (!className) {
      className = isScroller ? styles.modalScroller : styles.modalBody;
    }

    return (
      <Scroller ref={forwardedRef} {...scrollerProps} className={className}>
        {isScroller ? (
          <div className={innerClassName}>{children}</div>
        ) : (
          children
        )}
      </Scroller>
    );
  }
);

export default ModalBody;
