import React, { useCallback, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Dispatch } from 'redux';
import { upgradeLibrary } from 'app/js/stores/actions/provider/providerActions';
import selectAvailableLibraryUpgrades from 'app/js/stores/selectors/ui/selectAvailableLibraryUpgrades';
import { METADATA_AGENTS_SUPPORT_URL } from 'app/js/utils/constants/commonUrls';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import t from 'app/js/utils/t';
import ModalFooter from 'app/js/v4/core/ui/modals/components/ModalFooter';
import ModalFooterButtonBar from 'app/js/v4/core/ui/modals/components/ModalFooterButtonBar';
import TagInjector from 'app/js/views/v3/components/core/TagInjector';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import CheckInputGroup from 'app/js/views/v3/components/core/forms/CheckInputGroup';
import Form from 'app/js/views/v3/components/core/forms/Form';
import Modal from 'app/js/views/v3/components/core/modals/Modal';
import ModalBody from 'app/js/views/v3/components/core/modals/ModalBody';
import ModalContent from 'app/js/views/v3/components/core/modals/ModalContent';
import ModalHeader from 'app/js/views/v3/components/core/modals/ModalHeader';
import useModalState from 'app/js/views/v3/helpers/hooks/useModalState';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import { ContentDirectory } from 'app/js/views/v3/helpers/props/shapes/contentDirectoryShape';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import styles from './AgentUpgradeLibraryModals.css';

export interface FormValues {
  upgradeAllLibraries: boolean;
  refreshMetadata: boolean;
}

export function submitForm(
  dispatch: Dispatch<typeof upgradeLibrary>,
  availableUpgrades: ReturnType<typeof selectAvailableLibraryUpgrades>,
  formValues: FormValues,
  targetMachineIdentifier?: string,
  targetSourceDirectory?: ContentDirectory
): void {
  // Available upgrades always come in with all upgrades available for all
  // libraries across all servers.
  // In some cases we want to operate on a limited set of these;
  //  * one library on one server
  //  * all libraries on one server
  // By providing a `targetMachineIdentifier`, the available upgrades are
  // filtered to just those applying to a specific machine.
  // By providing a `targetSourceDirectory`, the available upgrades are
  // filtered to just those (that one) applying to a specific library.
  // Since the specific-library upgrade modal includes an option to upgrade all
  // libraries on a server, the presence of `upgradeAllLibraries` in the
  // formValues skips the per-library filtering step.
  availableUpgrades
    .filter(([availableMachineIdentifier, , , availableSourceDirectory]) => {
      if (
        targetMachineIdentifier &&
        availableMachineIdentifier !== targetMachineIdentifier
      ) {
        return false;
      }

      if (
        targetSourceDirectory &&
        availableSourceDirectory !== targetSourceDirectory &&
        !formValues.upgradeAllLibraries
      ) {
        return false;
      }

      return true;
    })
    .forEach(([, serverEntityID, providerEntityID, contentDirectory]) => {
      dispatch(
        upgradeLibrary({
          serverEntityID,
          providerEntityID,
          contentDirectory,
          options: {
            refreshMetadata: formValues.refreshMetadata,
          },
        })
      );
    });
}

interface AgentUpgradeLibraryModalProps {
  isOpen: boolean;
  onClose: () => void;
  onOpen: () => void;
  machineIdentifier: string;
  sourceDirectory: ContentDirectory;
}

function AgentUpgradeLibraryModal(
  props: AgentUpgradeLibraryModalProps
): React.ReactElement {
  const dispatch = useDispatch();
  const allAvailableUpgrades = useSelector(selectAvailableLibraryUpgrades);

  const availableUpgrades = useMemo(() => {
    return allAvailableUpgrades.filter(
      ([availableMachineIdentifier]) =>
        availableMachineIdentifier === props.machineIdentifier
    );
  }, [allAvailableUpgrades, props.machineIdentifier]);

  const { onClose } = props;
  const onSubmit = useCallback(
    (formValues: FormValues) => {
      onClose();
      submitForm(
        dispatch,
        availableUpgrades,
        formValues,
        props.machineIdentifier,
        props.sourceDirectory
      );
    },
    [
      availableUpgrades,
      dispatch,
      onClose,
      props.machineIdentifier,
      props.sourceDirectory,
    ]
  );

  return (
    <Modal
      containerClassName={styles.modalContainer}
      isOpen={props.isOpen}
      metricsPage={metricsPages.CONFIRM_UPGRADE_LIBRARY}
      size={sizes.SMALL}
      onModalClose={props.onClose}
    >
      <Form onSubmit={onSubmit}>
        <ModalContent onModalClose={props.onClose}>
          <ModalHeader>{t('Upgrade Library')}</ModalHeader>
          <ModalBody>
            <TagInjector
              message={t(
                'This library is currently using a legacy agent to match items and fetch artwork and other metadata. {openLink}There is a newer, faster, and more accurate agent available{closeLink} for this type of media. Would you like to upgrade the library now?'
              )}
              tags={[
                {
                  open: 'openLink',
                  close: 'closeLink',
                  component: Link,
                  kind: kinds.PRIMARY,
                  to: METADATA_AGENTS_SUPPORT_URL,
                  target: '_blank',
                },
              ]}
            />
            <div className={styles.note}>
              {t(
                'Note: Metadata for existing items in this library will not be affected until they are refreshed. We can refresh metadata immediately after updating this library or you can wait for the next scheduled update.'
              )}
            </div>

            <CheckInputGroup
              // @ts-expect-error
              checkboxText={t(
                'Refresh metadata for this library after upgrading'
              )}
              formGroupClassName={styles.checkInputGroup}
              initialValue={true}
              name="refreshMetadata"
              size={sizes.SMALL}
            />
          </ModalBody>
          <ModalFooter>
            {availableUpgrades.length > 1 ? (
              <CheckInputGroup
                // @ts-expect-error
                checkboxText={t('Upgrade all libraries')}
                formGroupClassName={styles.checkInputGroup}
                initialValue={false}
                name="upgradeAllLibraries"
                size={sizes.SMALL}
              />
            ) : null}
            <ModalFooterButtonBar>
              <Button onPress={props.onClose}>{t('No thanks')}</Button>
              <Button kind={kinds.PRIMARY} type="submit">
                {t('Upgrade')}
              </Button>
            </ModalFooterButtonBar>
          </ModalFooter>
        </ModalContent>
      </Form>
    </Modal>
  );
}

export function useAgentUpgradeLibraryModal(
  machineIdentifier: string,
  sourceDirectory: ContentDirectory,
  onMenuClose?: () => void
): {
  Modal: React.FC;
  onOpen: () => void;
} {
  const { isOpen, onClose: onModalClose, onOpen } = useModalState();

  const onClose = useCallback(() => {
    onModalClose();
    onMenuClose?.();
  }, [onMenuClose, onModalClose]);

  const ModalBuilder = useCallback(() => {
    return (
      <AgentUpgradeLibraryModal
        isOpen={isOpen}
        machineIdentifier={machineIdentifier}
        sourceDirectory={sourceDirectory}
        onClose={onClose}
        onOpen={onOpen}
      />
    );
  }, [isOpen, machineIdentifier, onClose, onOpen, sourceDirectory]);

  return {
    Modal: ModalBuilder,
    onOpen,
  };
}
