import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import serverFeatures from 'app/js/models/server/serverFeatures';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setti... Remove this comment to see the full error message
import tpl from 'app/templates/settings/server/ServerSettingsVersionsErrorView.tpl';

const ServerSettingsVersionsErrorView = Marionette.ItemView.extend({
  //
  // Properties

  template: tpl,

  //
  // Control

  serializeData: function () {
    return {
      isTranscoderRequired: !this.model.get('transcoderVideo'),
      isUpgradeRequired: !this.model.supports('streamReady'),
      escapedMinServerVersion: _.escape(serverFeatures.streamReady),
    };
  },
});

export default ServerSettingsVersionsErrorView;
