import React, { ReactElement, useCallback } from 'react';
import t from 'app/js/utils/t';
import { ServerAccount } from 'app/js/v4/core/serverAccounts/serverAccountsTypes';
import Menu from 'app/js/views/v3/components/core/menus/Menu';
import SelectedMenuItem from 'app/js/views/v3/components/core/menus/SelectedMenuItem';
import useMenuState from 'app/js/views/v3/helpers/hooks/useMenuState';
import ServerDashboardMenuButton from './ServerDashboardMenuButton';

interface ServerDashboardAccountMenuButtonProps {
  className?: string;
  accounts: ServerAccount[];
  activeAccount: ServerAccount | null;
  onAccountSelect(account?: ServerAccount): void;
}

export default function ServerDashboardAccountMenuButton({
  className,
  accounts,
  activeAccount,
  onAccountSelect: onAccountSelectFromProps,
}: ServerDashboardAccountMenuButtonProps): ReactElement {
  const { menuButtonID, isMenuOpen, onMenuButtonPress, onMenuClose } =
    useMenuState();

  const onSelect = useCallback(
    (accountID) => {
      const nextAccount = accounts.find((account) => account.id === accountID);

      onMenuClose();
      onAccountSelectFromProps(nextAccount);
    },
    [accounts, onMenuClose, onAccountSelectFromProps]
  );

  return (
    <>
      <ServerDashboardMenuButton
        className={className}
        id={menuButtonID}
        isMenuOpen={isMenuOpen}
        onPress={onMenuButtonPress}
      >
        {activeAccount ? activeAccount.name : t('All Users')}
      </ServerDashboardMenuButton>

      <Menu isOpen={isMenuOpen} target={menuButtonID} onMenuClose={onMenuClose}>
        <SelectedMenuItem
          isSelected={!activeAccount}
          value={undefined}
          onSelect={onSelect}
        >
          {t('All Users')}
        </SelectedMenuItem>

        {accounts.map((account) => {
          return (
            <SelectedMenuItem
              key={account.id}
              isSelected={activeAccount?.id === account.id}
              value={account.id}
              onSelect={onSelect}
            >
              {account.name}
            </SelectedMenuItem>
          );
        })}
      </Menu>
    </>
  );
}
