// https://github.com/react-bootstrap/react-bootstrap/blob/master/src/utils/ValidComponentChildren.js

import React from 'react';

/**
 * Return an array of "valid components" in the Children container.
 *
 * @param {?*} children Children tree container.
 * @returns {array} of children that are valid components
 */
export default function getValidComponents(children: any) {
  const result: any = [];

  React.Children.forEach(children, (child) => {
    if (!React.isValidElement(child)) {
      return;
    }

    result.push(child);
  });

  return result;
}
