import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import PlayQueueAfterUpNextCollection from 'app/js/models/library/PlayQueueAfterUpNextCollection';
import PlayQueueUpNextCollection from 'app/js/models/library/PlayQueueUpNextCollection';
import serverFeatures from 'app/js/models/server/serverFeatures';
import t from 'app/js/utils/t';
import ConversionQueueList from 'app/js/views/lists/activity/convert/ConversionQueueList';
import ConversionQueueListEmptyView from 'app/js/views/lists/activity/convert/ConversionQueueListEmptyView';
import getServerSettingsRoute from 'app/js/views/v3/helpers/routes/getServerSettingsRoute';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/activ... Remove this comment to see the full error message
import tpl from 'app/templates/activity/ConversionView.tpl';

const ConversionView = Marionette.Layout.extend({
  //
  // Properties

  className: 'conversion-container',
  template: tpl,

  ui: {
    filterBar: '.filter-bar',
    pauseBtn: '.pause-btn',
    resumeBtn: '.resume-btn',
    divider: '.conversion-queue-divider',
  },

  regions: {
    filterBar: '.filter-bar',
    upNextList: '.conversion-queue-up-next-list-region',
    afterUpNextList: '.conversion-queue-after-up-next-list-region',
    emptyRegion: '.conversion-queue-empty-region',
  },

  events: {
    'click .pause-btn': 'onTogglePauseClick',
    'click .resume-btn': 'onTogglePauseClick',
  },

  //
  // Init

  initialize: function (options: any) {
    this.playQueue = options.playQueue;
    this.transcodeJobs = options.transcodeJobs;
    this.generators = options.generators;

    this.items = this.playQueue.get('collection');

    this.upNextItems = new PlayQueueUpNextCollection(this.items, {
      playQueue: this.playQueue,
      includeSelectedItem: true,
    });

    this.afterUpNextItems = new PlayQueueAfterUpNextCollection(this.items, {
      playQueue: this.playQueue,
    });

    this.settings = this.model.get(__PLATFORM__).get('settings');
    this.pausedSetting = this.settings.get('BackgroundQueueIdlePaused');

    this.listenTo(this.upNextItems, 'reset add remove', this.onUpNextChange);
    this.listenTo(
      this.afterUpNextItems,
      'reset add remove',
      this.onAfterUpNextChange
    );
  },

  //
  // Control

  serializeData: function () {
    return {
      shouldShowList: this.model.supports('streamReady'),
      shouldShowDivider: !!this.afterUpNextItems.length,
      escapedMinServerVersion: _.escape(serverFeatures.streamReady),
      hasPausedSetting: !!this.pausedSetting,
      isPaused: this.pausedSetting?.get('value'),
      errorMessage: this.getErrorMessage(),
      manageVersionsRoute: getServerSettingsRoute('manage', 'versions'),
    };
  },

  toggleEmptyView: function () {
    const hasItems = this.upNextItems.length + this.afterUpNextItems.length > 0;

    if (hasItems) {
      this.emptyRegion.close();
    } else {
      this.emptyRegion.show(
        new ConversionQueueListEmptyView({ model: this.model })
      );
    }

    this.$el.toggleClass('is-empty', !hasItems);
    this.ui.filterBar.toggleClass('hide-actions', !hasItems);
  },

  getErrorMessage: function () {
    const failedCount = this.generators.reduce(function (
      count: any,
      generator: any
    ) {
      const status = generator.get('status');

      if (status.get('state') === 'failed') {
        // Always increment by at least one, in case the generator has no
        // items because of a failed path or similar issue.
        count += Math.max(status.get('itemsCount'), 1);
      } else if (status.get('itemsFailedCount')) {
        count += status.get('itemsFailedCount');
      }

      return count;
    },
    0);

    if (failedCount) {
      return failedCount === 1
        ? t('There was a problem converting 1 item.')
        : t('There were problems converting {1} items.', failedCount);
    }

    return;
  },

  //
  // Listeners

  onRender: function () {
    const listOptions = {
      playQueue: this.playQueue,
    };

    const itemViewOptions = {
      playQueue: this.playQueue,
      transcodeJobs: this.transcodeJobs,
      pausedSetting: this.pausedSetting,
    };

    this.upNextList.show(
      new ConversionQueueList(
        _.extend({}, listOptions, {
          collection: this.upNextItems,
          itemViewOptions: itemViewOptions,
          sortableOptions: {
            sort: true,
            group: {
              name: 'upNext',
              pull: false,
              put: ['afterUpNext'],
            },
          },
        })
      )
    );

    this.afterUpNextList.show(
      new ConversionQueueList(
        _.extend({}, listOptions, {
          collection: this.afterUpNextItems,
          itemViewOptions: _.extend({}, itemViewOptions, {
            hideDragHandle: true,
          }),
          sortableOptions: {
            sort: false,
            group: {
              name: 'afterUpNext',
              pull: true,
              put: false,
            },
          },
        })
      )
    );

    this.toggleEmptyView();
  },

  onClose: function () {
    this.upNextItems.destroy();
    this.afterUpNextItems.destroy();
  },

  onUpNextChange: function () {
    this.toggleEmptyView();
  },

  onAfterUpNextChange: function () {
    this.ui.divider.toggleClass('hidden', !this.afterUpNextItems.length);
    this.toggleEmptyView();
  },

  onTogglePauseClick: function (event: any) {
    event.preventDefault();

    const isPaused = this.pausedSetting.get('value');

    this.ui.pauseBtn.toggleClass('hidden', !isPaused);
    this.ui.resumeBtn.toggleClass('hidden', isPaused);

    this.pausedSetting.set('value', !isPaused);
    this.settings.first().save(this.pausedSetting.id, isPaused ? 0 : 1);
  },
});

export default ConversionView;
