import React, { ReactElement, useCallback, useMemo, useState } from 'react';
import { useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import Select, { SelectProps } from 'app/js/v4/core/ui/forms/components/Select';
import selectGroupedLineupChannels, {
  VALUE_SEPARATOR,
} from 'app/js/v4/features/dvrManagement/selectors/selectGroupedLineupChannels';
import selectLineupChannels from 'app/js/v4/features/dvrManagement/selectors/selectLineupChannels';
import getLineupChannelOptionLabel from 'app/js/v4/features/dvrManagement/utils/getLineupChannelOptionLabel';

interface LineupChannelSelectProps
  extends Omit<SelectProps, 'children' | 'name' | 'onChange'> {
  index: number;
  selectedChannelKey?: string;
  selectedLineupIdentifier?: string;
  onChange(channelKey: string, lineupIdentifier: string, index: number): void;
}

export default function LineupChannelSelect({
  className,
  index,
  selectedChannelKey,
  selectedLineupIdentifier,
  onChange,
  isDisabled,
}: LineupChannelSelectProps): ReactElement {
  const lineupChannels = useSelector(selectLineupChannels);
  const groupedLineupChannels = useSelector(selectGroupedLineupChannels);
  const [showSelectOptions, setShowSelectOptions] = useState(false);
  const selectedLineupChannel = useMemo(() => {
    return lineupChannels?.find(
      (lineupChannel) =>
        lineupChannel.key === selectedChannelKey &&
        lineupChannel.identifier === selectedLineupIdentifier
    );
  }, [lineupChannels, selectedChannelKey, selectedLineupIdentifier]);

  //
  // Callbacks

  const onSelectPress = useCallback(() => {
    // Defer rendering the options until the select is opened to improve
    // performance.
    setShowSelectOptions(true);
  }, [setShowSelectOptions]);

  const onSelect = useCallback(
    (event) => {
      const [key, identifier] = event.target.value.split(VALUE_SEPARATOR);
      const nextSelectedChannel = lineupChannels.find(
        (channel) => channel.key === key && channel.identifier === identifier
      );

      if (nextSelectedChannel) {
        onChange(key, identifier, index);
      }
    },
    [index, lineupChannels, onChange]
  );

  //
  // Render

  const selectedValue = `${selectedChannelKey}${VALUE_SEPARATOR}${selectedLineupIdentifier}`;

  return (
    <Select
      className={className}
      isDisabled={isDisabled}
      name={`[${index}].channelKey`}
      value={selectedValue}
      onChange={onSelect}
      onMouseDown={onSelectPress}
      onTouchStart={onSelectPress}
    >
      <option key="" value="">
        {t('Select Channel')}
      </option>

      {showSelectOptions ? (
        Object.keys(groupedLineupChannels).map((group, groupIndex) => (
          <optgroup key={groupIndex} label={group}>
            {groupedLineupChannels[group].map(
              ({ value, label }, channelIndex) => (
                <option key={channelIndex} value={value}>
                  {label}
                </option>
              )
            )}
          </optgroup>
        ))
      ) : (
        <option key="computed" value={selectedValue}>
          {selectedLineupChannel
            ? getLineupChannelOptionLabel(selectedLineupChannel)
            : null}
        </option>
      )}
    </Select>
  );
}
