import React, { ReactElement } from 'react';
import { useSelector } from 'react-redux';
import * as features from 'app/js/utils/constants/user/features';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import createUserExperienceSettingSelector from 'app/js/v4/core/user/selectors/createUserExperienceSettingSelector';
import Bandwidth from 'app/js/v4/features/dashboard/components/bandwidth/Bandwidth';
import NowPlaying from 'app/js/v4/features/dashboard/components/nowPlaying/NowPlaying';
import PlayHistory from 'app/js/v4/features/dashboard/components/playHistory/PlayHistory';
import ServerResources from 'app/js/v4/features/dashboard/components/serverResources/ServerResources';
import TopPlayed from 'app/js/v4/features/dashboard/components/topPlayed/TopPlayed';
import TopUsers from 'app/js/v4/features/dashboard/components/topUsers/TopUsers';
import ServerDashboardPageSection from './ServerDashboardPageSection';
import ServerDashboardPageUpsell from './ServerDashboardPageUpsell';

export default function ServerDashboardPage(): ReactElement {
  const [server] = useSourceContext();

  const hasChartsFeature = !!server.ownerFeatures?.includes(
    features.SERVER_DASHBOARD_CHARTS
  );

  const hasDismissedUpsell = useSelector(
    createUserExperienceSettingSelector('hasDismissedServerDashboardUpsell')
  );

  //
  // Render

  return (
    <>
      {!hasChartsFeature && !hasDismissedUpsell ? (
        <ServerDashboardPageUpsell />
      ) : null}

      <ServerDashboardPageSection>
        <NowPlaying />
      </ServerDashboardPageSection>

      {hasChartsFeature ? (
        <>
          <ServerDashboardPageSection>
            <Bandwidth />
          </ServerDashboardPageSection>

          <ServerResources />

          <ServerDashboardPageSection>
            <TopUsers />
          </ServerDashboardPageSection>

          <ServerDashboardPageSection>
            <PlayHistory />
          </ServerDashboardPageSection>

          <ServerDashboardPageSection>
            <TopPlayed />
          </ServerDashboardPageSection>
        </>
      ) : null}
    </>
  );
}
