import React, { ReactElement } from 'react';
import signalQuality1 from 'app/img/signal-quality/signal-quality-1.svg';
import signalQuality2 from 'app/img/signal-quality/signal-quality-2.svg';
import signalQuality3 from 'app/img/signal-quality/signal-quality-3.svg';
import signalQuality4 from 'app/img/signal-quality/signal-quality-4.svg';

function getImage(signalQuality: number): string {
  if (signalQuality <= 25) {
    return signalQuality1;
  } else if (signalQuality <= 50) {
    return signalQuality2;
  } else if (signalQuality <= 75) {
    return signalQuality3;
  } else {
    return signalQuality4;
  }
}

interface SignalQualityProps {
  className?: string;
  quality?: number;
}

export default function SignalQuality({
  className,
  quality,
}: SignalQualityProps): ReactElement | null {
  if (quality === undefined) {
    return null;
  }

  const image = getImage(quality);

  return (
    <img className={className} height={16} src={String(image)} width={18} />
  );
}
