/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React from 'react';
import styles from './SettingsPageHeader.css';

type SettingsPageHeaderProps = {
  className: string;
  children: React.ReactNode;
  hasCommunityFeatures?: boolean;
};

function SettingsPageHeader(props: SettingsPageHeaderProps) {
  return (
    <h2
      className={classNames(
        props.className,
        props.hasCommunityFeatures && styles.communityFeatures
      )}
    >
      {props.children}
    </h2>
  );
}

SettingsPageHeader.defaultProps = {
  className: styles.header,
};

export default SettingsPageHeader;
