import assert from 'app/js/utils/log/assert';
import {
  Layout,
  VirtualCellPosition,
  VirtualListDimensions,
  VirtualListMeasurements,
  VirtualListRanges,
} from 'app/js/v4/core/ui/lists/listsTypes';
import clampListIndex from 'app/js/views/v3/helpers/layouts/clampListIndex';

export enum GridDirection {
  Vertical = 'vertical',
  Horizontal = 'horizontal',
}

export interface GridLayoutOptions {
  direction: GridDirection;
  rows?: number;
  columns?: number;
  overscan?: number;
  innerHorizontalPadding?: number;
  innerVerticalPadding?: number;
  outerPaddingTop?: number;
  outerPaddingRight?: number;
  outerPaddingBottom?: number;
  outerPaddingLeft?: number;
  getCellWidth: (options: {
    innerViewportWidth: number;
    innerViewportHeight: number;
    innerHorizontalPadding: number;
    innerVerticalPadding: number;
  }) => number;
  getCellHeight: (options: {
    cellWidth: number;
    innerViewportWidth: number;
    innerViewportHeight: number;
    innerHorizontalPadding: number;
    innerVerticalPadding: number;
  }) => number;
}

export default function createGridLayout({
  direction = GridDirection.Vertical,
  rows,
  columns,
  overscan = 0,
  innerHorizontalPadding = 0,
  innerVerticalPadding = 0,
  outerPaddingTop = 0,
  outerPaddingRight = 0,
  outerPaddingBottom = 0,
  outerPaddingLeft = 0,
  getCellWidth,
  getCellHeight,
}: GridLayoutOptions): Layout<VirtualListMeasurements> {
  interface RowColumnOptions {
    cellsPerRow: number;
    cellsPerColumn: number;
  }

  function getRow(index: number, options: RowColumnOptions): number {
    return direction === GridDirection.Vertical
      ? Math.floor(index / options.cellsPerRow)
      : index % options.cellsPerColumn;
  }

  function getColumn(index: number, options: RowColumnOptions): number {
    return direction === GridDirection.Vertical
      ? index % options.cellsPerRow
      : Math.floor(index / options.cellsPerColumn);
  }

  return {
    getListMeasurements(
      dimensions: VirtualListDimensions,
      size: number
    ): VirtualListMeasurements {
      __DEV__ &&
        assert(
          rows || dimensions.viewportHeight != null,
          'A grid list must have a viewport height or a fixed number of rows'
        );

      __DEV__ &&
        assert(
          columns || dimensions.viewportWidth != null,
          'A grid list must have a viewport width or a fixed number of columns'
        );

      const listTop = dimensions.listTop;
      const listLeft = dimensions.listLeft;

      let innerViewportWidth =
        (dimensions.viewportWidth || 0) - outerPaddingRight - outerPaddingLeft;
      let innerViewportHeight =
        (dimensions.viewportHeight || 0) - outerPaddingTop - outerPaddingBottom;

      if (direction === GridDirection.Vertical) {
        innerViewportWidth -= listLeft;
      } else {
        innerViewportHeight -= listTop;
      }

      innerViewportWidth = Math.max(0, innerViewportWidth);
      innerViewportHeight = Math.max(0, innerViewportHeight);

      const cellWidth = getCellWidth({
        innerViewportWidth,
        innerViewportHeight,
        innerHorizontalPadding,
        innerVerticalPadding,
      });

      const cellHeight = getCellHeight({
        cellWidth,
        innerViewportWidth,
        innerViewportHeight,
        innerHorizontalPadding,
        innerVerticalPadding,
      });

      const cellsPerColumn = Math.max(
        0,
        rows ||
          Math.floor(
            (innerViewportHeight + innerVerticalPadding) /
              (cellHeight + innerVerticalPadding)
          )
      );
      const cellsPerRow = Math.max(
        0,
        columns ||
          Math.floor(
            (innerViewportWidth + innerHorizontalPadding) /
              (cellWidth + innerHorizontalPadding)
          )
      );
      const cellsPerPage = cellsPerRow * cellsPerColumn;

      let width = 0;
      let height = 0;

      if (direction === GridDirection.Vertical && cellsPerRow) {
        width =
          cellsPerRow * (cellWidth + innerHorizontalPadding) -
          innerHorizontalPadding;
        height =
          Math.ceil(size / cellsPerRow) * (cellHeight + innerVerticalPadding) -
          innerVerticalPadding;
      } else if (direction === GridDirection.Horizontal && cellsPerColumn) {
        width =
          Math.ceil(size / cellsPerColumn) *
            (cellWidth + innerHorizontalPadding) -
          innerHorizontalPadding;
        height =
          cellsPerColumn * (cellHeight + innerVerticalPadding) -
          innerVerticalPadding;
      }

      return {
        width: width + outerPaddingRight + outerPaddingLeft,
        height: height + outerPaddingTop + outerPaddingBottom,
        outerPaddingTop,
        outerPaddingRight,
        outerPaddingBottom,
        outerPaddingLeft,
        listTop,
        listLeft,
        cellWidth,
        cellHeight,
        cellsPerColumn,
        cellsPerRow,
        cellsPerPage,
      };
    },

    getPositionForIndex(
      index: number,
      measurements: VirtualListMeasurements
    ): VirtualCellPosition {
      const { listTop, listLeft, cellWidth, cellHeight } = measurements;

      const column = getColumn(index, measurements);
      const row = getRow(index, measurements);

      return {
        column,
        row,
        cellX:
          column * (cellWidth + innerHorizontalPadding) +
          listLeft +
          outerPaddingLeft,
        cellY:
          row * (cellHeight + innerVerticalPadding) + listTop + outerPaddingTop,
        cellWidth,
        cellHeight,
      };
    },

    getRangesForPosition(
      scrollTop: number,
      scrollLeft: number,
      size: number,
      measurements: VirtualListMeasurements
    ): VirtualListRanges {
      const {
        listTop,
        listLeft,
        cellWidth,
        cellHeight,
        cellsPerColumn,
        cellsPerRow,
        cellsPerPage,
      } = measurements;

      // The render range represents cells that will be rendered in the list. The
      // visible range represents cells that can actually be seen in the list.
      // Cells within the render range, but outside the visible range may defer
      // loading an image, for example. Clamp the ranges to indices that actually
      // exist in the list of items.

      const finalCellHeight = cellHeight + innerVerticalPadding;
      const finalCellWidth = cellWidth + innerHorizontalPadding;
      const finalScrollTop = scrollTop - listTop;
      const finalScrollLeft = scrollLeft - listLeft;

      const cellsPerGroup =
        direction === GridDirection.Vertical ? cellsPerRow : cellsPerColumn;

      const groupIndex =
        direction === GridDirection.Vertical
          ? finalScrollTop / finalCellHeight
          : finalScrollLeft / finalCellWidth;

      const maxRenderedCells =
        cellsPerPage + cellsPerGroup * 2 + overscan * cellsPerGroup * 2;

      const visibleStartIndex = Math.ceil(groupIndex) * cellsPerGroup;
      const visibleEndIndex = visibleStartIndex + cellsPerPage - 1;

      let renderStartIndex = visibleStartIndex - cellsPerGroup * overscan;
      const renderEndIndex =
        clampListIndex(renderStartIndex, size) + maxRenderedCells - 1;

      if (renderEndIndex >= size) {
        renderStartIndex = size - maxRenderedCells;
      }

      return {
        visibleStartIndex: clampListIndex(visibleStartIndex, size),
        visibleEndIndex: clampListIndex(visibleEndIndex, size),
        renderStartIndex: clampListIndex(renderStartIndex, size),
        renderEndIndex: clampListIndex(renderEndIndex, size),
      };
    },
  };
}
