import React, {
  ChangeEvent,
  ReactElement,
  useCallback,
  useEffect,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import FormLabel from 'app/js/v4/core/ui/forms/components/FormLabel';
import TextInput from 'app/js/v4/core/ui/forms/components/TextInput';
import Size from 'app/js/v4/core/ui/types/Size';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import selectEPGSettingsPostalCodeExample from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettingsPostalCodeExample';
import usePrevious from 'app/js/v4/utils/hooks/usePrevious';
import styles from './DVRSetupPostalCodeInput.css';

interface DVRSetupPostalCodeInputProps {
  isDisabled?: boolean;
  hasError?: boolean;
}

let epgPostalCodeTimeoutID: number | undefined;

export default function DVRSetupPostalCodeInput({
  isDisabled,
  hasError,
}: DVRSetupPostalCodeInputProps): ReactElement | null {
  const dispatch = useDispatch();
  const { selectedCountry } = useSelector(selectEPGSettings);
  const prevSelectedCountry = usePrevious(selectedCountry);
  const postalCodeExample = useSelector(selectEPGSettingsPostalCodeExample);
  const [postalCodeInput, setPostalCodeInput] = useState('');

  //
  // Callbacks

  const onPostalCodeChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      setPostalCodeInput(event.currentTarget.value);
    },
    [setPostalCodeInput]
  );

  //
  // Effects

  useEffect(() => {
    if (
      prevSelectedCountry &&
      prevSelectedCountry.key !== selectedCountry?.key
    ) {
      setPostalCodeInput('');
    }
  }, [selectedCountry, prevSelectedCountry]);

  useEffect(() => {
    if (epgPostalCodeTimeoutID) {
      window.clearTimeout(epgPostalCodeTimeoutID);
    }

    epgPostalCodeTimeoutID = window.setTimeout(() => {
      dispatch(
        epgSettingsSlice.actions.epgPostalCodeChanged({
          postalCode: postalCodeInput,
        })
      );
    }, 300);

    () => {
      if (epgPostalCodeTimeoutID) {
        window.clearTimeout(epgPostalCodeTimeoutID);
      }
    };
  }, [dispatch, postalCodeInput]);

  //
  // Render

  return postalCodeExample ? (
    <>
      <FormLabel className={styles.label} hasError={hasError}>
        {t('Postal Code')}
      </FormLabel>
      <TextInput
        hasError={hasError}
        isDisabled={isDisabled}
        name="postalCodeInput"
        placeholder={t('e.g. {postalCodeExample}', { postalCodeExample })}
        size={Size.Medium}
        value={postalCodeInput}
        onChange={onPostalCodeChange}
      />
    </>
  ) : null;
}
