/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import TranscodeImageConnector from 'app/js/views/v3/components/core/images/TranscodeImageConnector';
import hdHomeRunConnectImg from 'app/img/dvr-devices/device-hdhomerun-connect-560.svg';
import hdHomeRunExpandImg from 'app/img/dvr-devices/device-hdhomerun-expand-560.svg';
import hdHomeRunExtendImg from 'app/img/dvr-devices/device-hdhomerun-extend-560.svg';
import hdHomeRunPrimeImg from 'app/img/dvr-devices/device-hdhomerun-prime-560.svg';
import styles from './DVRDeviceImage.css';

type DVRDeviceImageProps = {
  className?: string;
  serverEntityID: string;
  providerEntityID?: string;
  model?: string;
  thumb?: string;
  width: number;
  height: number;
};

function DVRDeviceImage(props: DVRDeviceImageProps) {
  const {
    className,
    serverEntityID,
    providerEntityID,
    model,
    thumb,
    width,
    height,
  } = props;

  let fallbackImg;

  switch (model) {
    case 'HDHomeRun CONNECT':
      fallbackImg = hdHomeRunConnectImg;
      break;
    case 'HDHomeRun EXPAND':
      fallbackImg = hdHomeRunExpandImg;
      break;
    case 'HDHomeRun EXTEND':
      fallbackImg = hdHomeRunExtendImg;
      break;
    case 'HDHomeRun PRIME':
      fallbackImg = hdHomeRunPrimeImg;
  }

  return (
    <TranscodeImageConnector
      className={className}
      emptySrc={fallbackImg}
      height={height}
      providerEntityID={providerEntityID}
      serverEntityID={serverEntityID}
      url={thumb}
      width={width}
    />
  );
}

DVRDeviceImage.defaultProps = {
  className: styles.image,
};

export default DVRDeviceImage;
