import { PayloadAction } from '@reduxjs/toolkit';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import { PlainError } from 'app/js/v4/utils/data/api';
import { AsyncState } from 'app/js/v4/utils/data/asyncStateMarkers';

export enum ServerResourceType {
  All = 'all',
  System = 'system',
  PlexMediaServer = 'plex-media-server',
}

export interface ResourcesState extends AsyncState {
  statistics: ResourcesStatistics[];
  isUnsupported: boolean;
  requestTimestampMilliseconds: number | null;
}

export interface ResourcesStatistics {
  timespan: number;
  at: number;
  hostCpuUtilization: number;
  processCpuUtilization: number;
  hostMemoryUtilization: number;
  processMemoryUtilization: number;
}

export type ResourcesFetchRequestedAction = PayloadAction<{
  server: Server;
}>;

export type ResourcesFetchSucceededAction = PayloadAction<{
  data: [];
  requestTimestampMilliseconds: number;
}>;

export type ResourcesFetchFailedAction = PayloadAction<{
  error: PlainError;
}>;
