import {
  ChannelMapping,
  EPGLineup,
  LineupChannel,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';

export type MatchedChannelsPerLineupUUID = Record<string, number>;

export default function getMatchedChannelsPerLineupUUID(
  channelMappings: ChannelMapping[],
  lineupChannels: LineupChannel[],
  selectedLineups?: EPGLineup[]
): MatchedChannelsPerLineupUUID {
  const initialValues = selectedLineups?.reduce<MatchedChannelsPerLineupUUID>(
    (acc, { uuid }) => {
      acc[uuid] = 0;

      return acc;
    },
    {}
  );

  return channelMappings.reduce<MatchedChannelsPerLineupUUID>(
    (acc, { enabled, channelKey }) => {
      if (enabled && channelKey) {
        const match = lineupChannels.find(({ key }) => key === channelKey);

        if (match?.lineupUUID) {
          acc[match.lineupUUID]++;
        }
      }

      return acc;
    },
    initialValues ?? {}
  );
}
