import React, { ReactElement } from 'react';
import Badge from 'app/js/v4/core/ui/badges/components/Badge';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import IconSelected from 'app/glyphs/selected-560.svg';
import { LoadingStep } from './DVRSetupInfo';
import styles from './DVRSetupInfoStep.css';

interface DVRSetupInfoStepProps {
  activeStep: LoadingStep;
  step: LoadingStep;
  title: string;
}

export default function DVRSetupInfoStep({
  activeStep,
  step,
  title,
}: DVRSetupInfoStepProps): ReactElement {
  const isActive = activeStep === step;
  const isDone = activeStep > step;
  const isWaiting = activeStep < step;

  return (
    <div className={styles.container}>
      {isDone ? (
        <Badge className={styles.done} iconComponent={IconSelected} />
      ) : null}
      {isActive ? <Spinner className={styles.active} /> : null}
      {isWaiting ? <div className={styles.waiting} /> : null}
      {title}
    </div>
  );
}
