import React, {
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import * as metricsContexts from 'app/js/utils/constants/metrics/metricsContexts';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import t from 'app/js/utils/t';
import createChannelScanActivitySelector from 'app/js/v4/core/serverActivities/selectors/createChannelScanActivitySelector';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import SpinnerButton from 'app/js/v4/core/ui/buttons/components/SpinnerButton';
import useMeasure from 'app/js/v4/core/ui/hooks/useMeasure';
import MetricsContextProvider from 'app/js/v4/core/ui/metrics/components/MetricsContextProvider';
import Modal from 'app/js/v4/core/ui/modals/components/Modal';
import ModalBody from 'app/js/v4/core/ui/modals/components/ModalBody';
import ModalContent from 'app/js/v4/core/ui/modals/components/ModalContent';
import ModalFooter from 'app/js/v4/core/ui/modals/components/ModalFooter';
import ModalFooterButtonBar from 'app/js/v4/core/ui/modals/components/ModalFooterButtonBar';
import ModalHeader from 'app/js/v4/core/ui/modals/components/ModalHeader';
import { ScrollPosition } from 'app/js/v4/core/ui/scrollers/scrollerTypes';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import Kind from 'app/js/v4/core/ui/types/Kind';
import ChannelScanActivity from 'app/js/v4/features/dvrManagement/components/ChannelScanActivity';
import ChannelScanPrefs from 'app/js/v4/features/dvrManagement/components/ChannelScanPrefs';
import DeviceChannelMappingsForm from 'app/js/v4/features/dvrManagement/components/DeviceChannelMappingsForm';
import {
  ChannelMappingFormData,
  MediaGrabberDevice,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import createChannelMappingsSelector from 'app/js/v4/features/dvrManagement/selectors/createChannelMappingsSelector';
import createDeviceChannelsSelector from 'app/js/v4/features/dvrManagement/selectors/createDeviceChannelsSelector';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import selectLineupChannels from 'app/js/v4/features/dvrManagement/selectors/selectLineupChannels';
import selectSelectedDVRLineups from 'app/js/v4/features/dvrManagement/selectors/selectSelectedDVRLineups';
import getMatchedChannelsPerLineupUUID, {
  MatchedChannelsPerLineupUUID,
} from 'app/js/v4/features/dvrManagement/utils/getMatchedChannelsPerLineupUUID';
import usePrevious from 'app/js/views/v3/helpers/hooks/usePrevious';
import dimensions from 'app/js/views/v3/styles/variables/dimensions';
import styles from './DVRManageChannelsModal.css';

interface DVRManageChannelsModalProps {
  deviceKey: MediaGrabberDevice['key'];
  isOpen: boolean;
  onModalClose(): void;
}

const FORM_ID = 'channel-mappings-form';

const DEVICE_CHANNEL_MAPPING_HEADER_HEIGHT = parseInt(
  dimensions.dvrSetupChannelMappingItemHeight,
  10
);

export default function DVRManageChannelsModal({
  deviceKey,
  isOpen,
  onModalClose,
}: DVRManageChannelsModalProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const [epgLineupsMeasureRef, { height: epgLineupsHeight }] = useMeasure();
  const [modalBodyRef, { height: modalBodyHeight }] = useMeasure();
  const {
    hasScannedChannels,
    isSavingChannelMappings,
    channelMappingsSaveError,
    scanPrefs,
    scanNetworks,
  } = useSelector(selectDVRSetup);
  const {
    lineupChannels: {
      isPending: isLineupChannelsPending,
      isPopulated: isLineupChannelsPopulated,
      error: lineupChannelsError,
    },
  } = useSelector(selectEPGSettings);
  const selectChannelScanActivity = useMemo(
    () => createChannelScanActivitySelector(server),
    [server]
  );
  const selectDeviceChannels = useMemo(
    () => createDeviceChannelsSelector(deviceKey),
    [deviceKey]
  );
  const selectChannelMappings = useMemo(
    () => createChannelMappingsSelector(deviceKey),
    [deviceKey]
  );

  const channelMappings = useSelector(selectChannelMappings);
  const channelScanActivity = useSelector(selectChannelScanActivity);
  const lineupChannels = useSelector(selectLineupChannels);
  const selectedLineups = useSelector(selectSelectedDVRLineups);
  const { channels: deviceChannels, isPending: isDeviceChannelsPending } =
    useSelector(selectDeviceChannels);

  const prevIsSavingChannelMappings = usePrevious(isSavingChannelMappings);
  const matchedChannelsPerLineupUUID: MatchedChannelsPerLineupUUID = useMemo(
    () =>
      getMatchedChannelsPerLineupUUID(
        channelMappings,
        lineupChannels,
        selectedLineups
      ),
    [channelMappings, lineupChannels, selectedLineups]
  );

  const [scrollPosition, setScrollPosition] = useState<ScrollPosition>({
    scrollTop: 0,
    scrollLeft: 0,
  });

  //
  // Effects

  useEffect(() => {
    dispatch(dvrSetupSlice.actions.deviceSelected({ deviceKey }));
  }, [dispatch, deviceKey]);

  useEffect(() => {
    if (hasScannedChannels) {
      scheduleImmediateUpdate(() => {
        dispatch(
          dvrSetupSlice.actions.deviceChannelsFetchRequested({
            server,
            deviceKey,
          })
        );
      });
    }
  }, [dispatch, server, deviceKey, hasScannedChannels]);

  useEffect(() => {
    if (
      !isLineupChannelsPending &&
      !isLineupChannelsPopulated &&
      !lineupChannelsError
    ) {
      dispatch(
        epgSettingsSlice.actions.selectedDVRLineupChannelsFetchRequested({
          server,
        })
      );
    }
  }, [
    dispatch,
    server,
    isLineupChannelsPending,
    isLineupChannelsPopulated,
    lineupChannelsError,
  ]);

  useEffect(() => {
    if (
      prevIsSavingChannelMappings &&
      !isSavingChannelMappings &&
      !channelMappingsSaveError
    ) {
      onModalClose();
    }
  }, [
    isSavingChannelMappings,
    prevIsSavingChannelMappings,
    channelMappingsSaveError,
    onModalClose,
  ]);

  //
  // Callbacks

  const onSubmit = useCallback(
    (data: ChannelMappingFormData[]): void => {
      scheduleImmediateUpdate(() => {
        dispatch(
          dvrSetupSlice.actions.deviceChannelMappingsSaveRequested({
            server,
            deviceKey,
            mappings: data,
          })
        );
      });
    },
    [dispatch, server, deviceKey]
  );

  const onScrollPositionChange = useCallback((scrollUpdate: ScrollPosition) => {
    setScrollPosition(scrollUpdate);
  }, []);

  //
  // Render

  return (
    <MetricsContextProvider
      metricsPage={metricsPages.DVR_SETUP}
      name={metricsContexts.SETTINGS}
    >
      <Modal
        className={styles.modal}
        isOpen={isOpen}
        onModalClose={onModalClose}
      >
        <ModalContent onModalClose={onModalClose}>
          <ModalHeader>{t('Channel Matching')}</ModalHeader>
          <ModalBody
            ref={modalBodyRef}
            className={styles.modalBody}
            innerClassName={styles.innerModalBody}
            onScrollPositionChange={onScrollPositionChange}
          >
            {!channelScanActivity &&
            !isDeviceChannelsPending &&
            !isLineupChannelsPending &&
            deviceChannels.length > 0 ? (
              <div
                ref={epgLineupsMeasureRef}
                className={styles.lineupsContainer}
              >
                <div className={styles.lineupsLabel}>{t('EPG Location')}</div>
                <div className={styles.lineups}>
                  {Object.keys(matchedChannelsPerLineupUUID).map(
                    (lineupUUID) => {
                      const matchCount =
                        matchedChannelsPerLineupUUID[lineupUUID];
                      const lineup = selectedLineups?.find(
                        (selectedLineup) => selectedLineup.uuid === lineupUUID
                      );

                      if (matchCount === 0 || !lineup) {
                        return;
                      }

                      return (
                        <div key={lineupUUID} className={styles.lineup}>
                          {lineup.title}
                        </div>
                      );
                    }
                  )}
                </div>
              </div>
            ) : null}

            {!channelScanActivity &&
            !isLineupChannelsPending &&
            !isDeviceChannelsPending &&
            deviceChannels.length > 0 ? (
              <DeviceChannelMappingsForm
                channelMappings={channelMappings}
                height={modalBodyHeight}
                id={FORM_ID}
                scrollLeft={scrollPosition.scrollLeft}
                scrollTop={
                  scrollPosition.scrollTop -
                  epgLineupsHeight -
                  DEVICE_CHANNEL_MAPPING_HEADER_HEIGHT * 2
                }
                onSubmit={onSubmit}
              />
            ) : null}

            {!channelScanActivity &&
            (isDeviceChannelsPending || isLineupChannelsPending) ? (
              <div className={styles.innerContainer}>
                <Spinner />
              </div>
            ) : null}

            {!channelScanActivity &&
            (scanPrefs.length || scanNetworks.length) ? (
              <ChannelScanPrefs />
            ) : null}

            {!scanPrefs?.length &&
            !scanNetworks?.length &&
            !isDeviceChannelsPending &&
            !isLineupChannelsPending ? (
              <ChannelScanActivity className={styles.innerContainer} />
            ) : null}
          </ModalBody>
          <ModalFooter>
            <ModalFooterButtonBar className={styles.buttonBar}>
              <Button onPress={onModalClose}>{t('Cancel')}</Button>
              <SpinnerButton
                form={FORM_ID}
                isDisabled={
                  isSavingChannelMappings ||
                  !!channelScanActivity ||
                  channelMappings.length === 0
                }
                isSpinning={isSavingChannelMappings}
                kind={Kind.Primary}
                type="submit"
              >
                {t('Save')}
              </SpinnerButton>
            </ModalFooterButtonBar>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </MetricsContextProvider>
  );
}
