import $ from 'jquery';
import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import AgentCollection from 'app/js/models/server/AgentCollection';
import * as agentIdentifiers from 'app/js/utils/constants/agentIdentifiers';
import {
  ALBUM,
  ARTIST,
  getMetadataType,
  MOVIE,
  PHOTO,
  SHOW,
} from 'app/js/utils/types/metadataTypes';
import AgentList from 'app/js/views/lists/settings/AgentList';
import ContributorList from 'app/js/views/lists/settings/ContributorList';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setti... Remove this comment to see the full error message
import tpl from 'app/templates/settings/server/ServerSettingsAgentsPane.tpl';

const ServerSettingsAgentsPane = Marionette.Layout.extend({
  //
  // Properties

  template: tpl,

  ui: {
    agentLabel: '.agent-label',
    typeNavItems: '.agent-types-nav li',
    agentNavItems: '.agents-nav li',
  },

  regions: {
    agentsList: '.agents-list-container',
    contributorsList: '.contributors-list-container',
  },

  events: {
    'click .agent-types-nav a': 'onAgentTypeClick',
    'click .agents-nav a': 'onAgentClick',
  },

  //
  // Init

  initialize: function () {
    this.bindAllWhenRendered('onAgentsReset', 'onContributorsReset');

    this.agentTypes = [];
    this.agentTypes.push(getMetadataType(MOVIE));
    this.agentTypes.push(getMetadataType(SHOW));
    this.agentTypes.push(getMetadataType(ARTIST));
    this.agentTypes.push(getMetadataType(ALBUM));
    this.agentTypes.push(getMetadataType(PHOTO));

    this.currentType = _.first(this.agentTypes);

    this.populateAgents();
  },

  //
  // Control

  serializeData: function () {
    return {
      currentType: this.currentType,
      agentTypes: this.agentTypes,
    };
  },

  populateAgents: function () {
    const agents = new AgentCollection([], {
      server: this.model,
      mediaTypeID: this.currentType.id,
    });

    agents.populate({ reset: true }).then(() => {
      const filteredAgents = agents.filter(
        (agent: any) =>
          agent.get('identifier') !== agentIdentifiers.PLEXMUSIC &&
          agent.get('identifier') !== agentIdentifiers.PLEXMOVIE &&
          agent.get('identifier') !== agentIdentifiers.PLEXSERIES
      );

      this.agents = agents.clone(filteredAgents);

      this.onAgentsReset();
    });
  },

  selectAgent: function (agent: any) {
    if (this.currentAgent) {
      this.currentAgent.set('selected', false);
    }

    this.currentAgent = agent;
    agent.set('selected', true);

    this.ui.agentLabel.text(agent.get('name'));

    this.additionalAgents = new AgentCollection([], {
      server: this.model,
      mediaTypeID: this.currentType.id,
      relatedAgentID: agent.id,
    });

    this.contributors = new AgentCollection([], {
      server: this.model,
      mediaTypeID: this.currentType.id,
      primaryAgentID: agent.id,
    });

    this.contributorsList.show(
      new ContributorList({ collection: this.contributors })
    );
    this.listenTo(
      this.contributorsList.currentView,
      'save',
      this.onContributorsSave
    );

    this.populateContributors();
  },

  populateContributors: function () {
    const requests = [];

    requests.push(this.additionalAgents.populate({ reset: true }));
    requests.push(this.contributors.populate({ reset: true }));

    // eslint-disable-next-line prefer-spread
    const promise = $.when.apply($, requests);
    promise.done(_.bind(this.onContributorsReset, this));
  },

  //
  // Listeners

  onAgentsReset: function () {
    this.agentsList.show(new AgentList({ collection: this.agents }));

    if (this.agents.length) {
      const selectedAgent =
        this.agents.findWhere({ selected: true }) || this.agents.first();

      this.selectAgent(selectedAgent);
    }
  },

  onContributorsReset: function () {
    // We need to find where additional agents and contributors intersect.
    // The primary agent should always be included as a contributor.

    const filterByContributors = _.bind(function (additionalAgent: any) {
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      if (additionalAgent.id === this.currentAgent.id) {
        additionalAgent.set('isPrimary', true);
        return true;
      }

      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      return !!this.contributors.get(additionalAgent.id);
    }, this);

    this.contributors.reset(this.additionalAgents.filter(filterByContributors));
  },

  onAgentTypeClick: function (event: any) {
    event.preventDefault();

    const $target = $(event.currentTarget);
    const type = $target.data('type');

    this.currentType = getMetadataType(type);
    this.populateAgents();

    this.ui.typeNavItems.removeClass('active');
    $target.parent('li').addClass('active');
  },

  onAgentClick: function (event: any) {
    event.preventDefault();

    const $target = $(event.target);
    const id = $target.data('identifier');
    const agent = this.agents.get(id);

    if (agent) {
      this.selectAgent(agent);
    }
  },

  onContributorsSave: function (order: any) {
    this.currentAgent.url =
      'system/agents/' +
      this.currentAgent.id +
      '/config/' +
      this.currentType.id;

    const promise = this.currentAgent.save({ order: order });

    promise.done(
      _.bind(function () {
        // We don't want to update the list immediately, but we want to make
        // sure the collections are up-to-date if the user navigates away
        // and back to this agent.
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.additionalAgents.fetch({ silent: true });
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.contributors.fetch({ silent: true });
      }, this)
    );
  },
});

export default ServerSettingsAgentsPane;
