/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React from 'react';
import ServerMenuItem from 'app/js/views/v3/components/sidebar/menu/ServerMenuItem';
import ServerSettingsStatusIcon from 'app/js/views/v3/components/sidebar/settings/ServerSettingsStatusIcon';
import getServerDetail from 'app/js/views/v3/helpers/getServerDetail';
import getServerDetailWithStatus from 'app/js/views/v3/helpers/getServerDetailWithStatus';
import getServerStatus from 'app/js/views/v3/helpers/getServerStatus';
import getServerTitle from 'app/js/views/v3/helpers/getServerTitle';
import getServerSettingsRoute from 'app/js/views/v3/helpers/routes/getServerSettingsRoute';
import styles from './ServerSettingsServerMenuItem.css';

type ServerSettingsServerMenuItemProps = {
  machineIdentifier: string;
  settingsSection?: string;
  settingsGroup?: string;
  friendlyName?: string;
  sourceTitle?: string;
  activeConnection?: any;
  isFullOwnedServer: boolean;
  isInstallingServerUpdate: boolean;
  isSettingsPending: boolean;
  isConnected: boolean;
  isShared: boolean;
  isUnavailable: boolean;
  isSecure: boolean;
  isRelayed: boolean;
  isSelected: boolean;
};

function ServerSettingsServerMenuItem(
  props: ServerSettingsServerMenuItemProps
) {
  const {
    isSelected,
    activeConnection,
    isConnected,
    isShared,
    isUnavailable,
    isSecure,
    isRelayed,
    machineIdentifier,
    friendlyName,
    sourceTitle,
    isFullOwnedServer,
    isInstallingServerUpdate,
    isSettingsPending,
    settingsSection,
    settingsGroup,
  } = props;

  // To determine the server status; we pass through only the props that matter.
  const serverTitleAttrs = {
    activeConnection,
    friendlyName,
    sourceTitle,
    isFullOwnedServer,
    isShared,
    isConnected,
    isRelayed,
    isUnavailable,
  };

  const { isConnecting, status } = getServerStatus(serverTitleAttrs);
  const statusWithDetail = getServerDetailWithStatus(
    getServerDetail(serverTitleAttrs),
    status
  );

  const isDisabled =
    (isFullOwnedServer && isSettingsPending) || isConnecting || isUnavailable;

  return (
    <ServerMenuItem
      className={classNames(isDisabled && styles.disabled)}
      details={statusWithDetail}
      detailsClassName={styles.details}
      friendlyNameClassName={styles.friendlyName}
      identifier={machineIdentifier}
      isDisabled={isConnecting}
      isSelected={isSelected}
      title={getServerTitle(serverTitleAttrs)}
      to={getServerSettingsRoute(
        settingsSection || 'settings',
        settingsGroup || 'general'
      )}
    >
      <ServerSettingsStatusIcon
        className={styles.icon}
        isConnected={isConnected}
        isFullOwnedServer={isFullOwnedServer}
        isInstallingServerUpdate={isInstallingServerUpdate}
        isRelayed={isRelayed}
        isSecure={isSecure}
        isSettingsPending={isSettingsPending}
        isUnavailable={isUnavailable}
      />
    </ServerMenuItem>
  );
}

export default ServerSettingsServerMenuItem;
