import React, { ReactElement } from 'react';
import {
  IconCheckForm,
  IconExclamationCircledFilled,
  IconQuestionCircledFilled,
} from '@plex/react-chroma-icon';
import {
  REMOTE_ACCESS_STATE_CONNECTING,
  REMOTE_ACCESS_STATE_DISCONNECTING,
  REMOTE_ACCESS_STATE_OK,
  REMOTE_ACCESS_STATE_UNREACHABLE,
} from 'app/js/models/server/ServerCloudAccountModel';
import styles from './RemoteAccessStateIcon.css';

const remoteAccessStateIconProps: { [key: string]: ReactElement } = {
  [REMOTE_ACCESS_STATE_CONNECTING]: (
    <div className={styles.unknown}>
      <IconQuestionCircledFilled />
    </div>
  ),
  [REMOTE_ACCESS_STATE_DISCONNECTING]: (
    <div className={styles.unknown}>
      <IconQuestionCircledFilled />
    </div>
  ),
  [REMOTE_ACCESS_STATE_UNREACHABLE]: (
    <div className={styles.error}>
      <IconExclamationCircledFilled />
    </div>
  ),
  [REMOTE_ACCESS_STATE_OK]: (
    <div className={styles.ok}>
      <IconCheckForm />
    </div>
  ),
};

interface RemoteAccessStateIconProps {
  remoteAccessState?: string;
}

function RemoteAccessStateIcon(
  props: RemoteAccessStateIconProps
): ReactElement | null {
  const { remoteAccessState } = props;

  if (!remoteAccessState) {
    return null;
  }

  const icon = remoteAccessStateIconProps[remoteAccessState];

  return icon || null;
}

export default RemoteAccessStateIcon;
