/* eslint-plugin-disable @typescript-eslint */

import * as metadataSubtypes from 'app/js/utils/types/metadataSubtypes';
import * as metadataContexts from 'app/js/views/v3/helpers/props/metadataContext';

export const PORTRAIT = 1 / 1.5;
export const PORTRAIT_WIDE = 3 / 4;
export const SQUARE = 1;
export const LANDSCAPE = 16 / 9;

export function getAspectRatio(type: any, subtype: any, context: any) {
  if (context) {
    switch (context) {
      case metadataContexts.QUICK_SEARCH:
        switch (type) {
          case 'genre':
            return SQUARE;
          case 'channel':
            return SQUARE;
        }
        break;

      case metadataContexts.PRE_PLAY:
      case metadataContexts.WATCH_TOGETHER_LOBBY:
        switch (type) {
          case 'episode':
            return LANDSCAPE;
        }
        break;

      case metadataContexts.CONTINUE_WATCHING:
      case metadataContexts.WATCH_TOGETHER:
        return LANDSCAPE;

      case metadataContexts.PLAYER:
        switch (type) {
          case 'movie':
            return LANDSCAPE;
          case 'episode':
            return LANDSCAPE;
          case 'game':
            return LANDSCAPE;
        }
        break;

      case metadataContexts.PLAY_QUEUE:
      case metadataContexts.PLAYLIST:
      case metadataContexts.DOWNLOADS:
      case metadataContexts.SHARED:
        switch (type) {
          case 'episode':
            return LANDSCAPE;
        }
        break;

      case metadataContexts.NOW_PLAYING:
        switch (type) {
          case 'clip':
            return SQUARE;
        }
        break;

      case metadataContexts.LIVE_TV_GRID:
      case metadataContexts.LIVE_TV_TIMELINE:
        switch (type) {
          case 'clip':
            return PORTRAIT;
        }
        break;
    }
  }

  switch (subtype) {
    case metadataSubtypes.PODCAST:
    case metadataSubtypes.WEBSHOW:
      switch (type) {
        case 'show':
          return SQUARE;
        case 'episode':
          return LANDSCAPE;
      }
  }

  switch (type) {
    case 'channels':
      return SQUARE;
    case 'movie':
      return PORTRAIT;
    case 'show':
      return PORTRAIT;
    case 'season':
      return PORTRAIT;
    case 'episode':
      return PORTRAIT;
    case 'artist':
      return SQUARE;
    case 'album':
      return SQUARE;
    case 'track':
      return SQUARE;
    case 'photo':
      return SQUARE;
    case 'photoalbum':
      return SQUARE;
    case 'clip':
      return LANDSCAPE;
    case 'collection':
      return PORTRAIT;
    case 'extra':
      return LANDSCAPE;
    case 'musicVideo':
      return LANDSCAPE;

    // Lists with a "mixed" type should be portrait by default, with non-
    // portrait items having shorter posters.
    case 'mixed':
      return PORTRAIT;

    // Game platforms are `directory` containers.
    case 'platform':
    case 'directory':
      return LANDSCAPE;

    case 'genre':
      return LANDSCAPE;

    case 'content':
    case 'channel':
    case 'hub':
    case 'list':
      return SQUARE;

    case 'game':
      return PORTRAIT_WIDE;

    default:
      return SQUARE;
  }
}

export function getWidth(height: any, type: any, subtype: any, context: any) {
  const aspectRatio = getAspectRatio(type, subtype, context);

  return Math.round(height * aspectRatio);
}

export function getHeight(width: any, type: any, subtype: any, context: any) {
  const aspectRatio = getAspectRatio(type, subtype, context);

  return Math.round(width / aspectRatio);
}
