/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import styles from './LegendLabel.css';

type LegendLabelProps = {
  className?: string;
  dataKey?: string;
  title: string;
  labelColor: string;
  onPress?: (...args: any[]) => any;
  onMouseEnter?: (...args: any[]) => any;
  onMouseLeave?: (...args: any[]) => any;
};

class LegendLabel extends Component<LegendLabelProps> {
  static defaultProps = {
    className: styles.legendLabel,
  };

  //
  // Listeners

  onPress = () => {
    const { dataKey, onPress } = this.props;

    if (onPress) {
      onPress(dataKey);
    }
  };

  onMouseEnter = () => {
    const { dataKey, onMouseEnter } = this.props;

    if (onMouseEnter) {
      onMouseEnter(dataKey);
    }
  };

  //
  // Render

  render() {
    const { className, title, labelColor, onMouseLeave } = this.props;

    return (
      <Link
        className={className}
        // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: (string | Element)[]; className:... Remove this comment to see the full error message
        onMouseEnter={this.onMouseEnter}
        onMouseLeave={onMouseLeave}
        onPress={this.onPress}
      >
        <span
          className={styles.dot}
          style={{
            backgroundColor: labelColor,
          }}
        />
        {title}
      </Link>
    );
  }
}

export default LegendLabel;
