import React, { ReactElement } from 'react';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import useTranscodeImage from 'app/js/v4/core/servers/hooks/useTranscodeImage';
import CrossFadeImage, {
  ImageFit,
} from 'app/js/v4/core/ui/images/CrossFadeImage';
import genericImg from 'app/img/dvr-devices/device-generic-560.svg';
import hdHomeRunConnectImg from 'app/img/dvr-devices/device-hdhomerun-connect-560.svg';
import hdHomeRunExpandImg from 'app/img/dvr-devices/device-hdhomerun-expand-560.svg';
import hdHomeRunExtendImg from 'app/img/dvr-devices/device-hdhomerun-extend-560.svg';
import hdHomeRunPrimeImg from 'app/img/dvr-devices/device-hdhomerun-prime-560.svg';
import styles from './DVRDeviceImage.css';

interface DVRDeviceImageProps {
  className?: string;
  thumb?: string;
  model: string;
  width: number;
  height: number;
}

const deviceFallbackImgs: Record<string, string> = {
  'HDHomeRun CONNECT': hdHomeRunConnectImg,
  'HDHomeRun EXPAND': hdHomeRunExpandImg,
  'HDHomeRun EXTEND': hdHomeRunExtendImg,
  'HDHomeRun PRIME': hdHomeRunPrimeImg,
};

export default function DVRDeviceImage({
  className = styles.image,
  thumb,
  model,
  width,
  height,
}: DVRDeviceImageProps): ReactElement {
  const [server, provider] = useSourceContext();

  const fallbackImg = deviceFallbackImgs[model] || genericImg;

  const imageSrc = useTranscodeImage({
    server,
    provider,
    width,
    height,
    url: thumb,
  });

  return (
    <CrossFadeImage
      className={className}
      fit={ImageFit.Cover}
      height={height}
      src={imageSrc ?? fallbackImg}
      width={width}
    />
  );
}
