/* eslint-plugin-disable @typescript-eslint */

import languages, {
  sortedLanguageCodes,
} from 'app/js/utils/language/languages';
import t from 'app/js/utils/t';
import ServerSettingsPane from 'app/js/views/settings/server/ServerSettingsPane';

const ServerSettingsLanguagesPane = ServerSettingsPane.extend({
  //
  // Properties

  prefix: 'account.',

  //
  // Control

  serializeData: function () {
    const account = this.options.account;
    const languageSettings = [];

    const enumLanguages = sortedLanguageCodes.map((languageCode) => {
      return {
        value: languageCode,
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        label: languages[languageCode],
      };
    });

    languageSettings.push({
      id: this.prefix + 'autoSelectAudio',
      label: t('Automatically select audio and subtitle tracks'),
      summary: t(
        'Automatically select audio and subtitle tracks in your preferred language when possible.'
      ),
      default: false,
      value: account.get('autoSelectAudio'),
      type: 'bool',
      group: 'account.language',
      advanced: false,
      hidden: false,
    });

    languageSettings.push({
      id: this.prefix + 'defaultAudioLanguage',
      label: t('Prefer audio tracks in'),
      default: 'en',
      value: account.get('defaultAudioLanguage'),
      type: 'text',
      group: 'account.language',
      advanced: false,
      hidden: false,
      enumValues: enumLanguages,
    });

    languageSettings.push({
      id: this.prefix + 'subtitleMode',
      label: t('Subtitle mode'),
      default: 1,
      value: parseInt(account.get('subtitleMode'), 10),
      type: 'int',
      group: 'account.language',
      advanced: false,
      hidden: false,
      enumValues: [
        { value: 0, label: t('Manually selected') },
        { value: 1, label: t('Shown with foreign audio') },
        { value: 2, label: t('Always enabled') },
      ],
    });

    languageSettings.push({
      id: this.prefix + 'defaultSubtitleLanguage',
      label: t('Prefer subtitles in'),
      default: 'en',
      value: account.get('defaultSubtitleLanguage'),
      type: 'text',
      group: 'account.language',
      advanced: false,
      hidden: false,
      enumValues: enumLanguages,
    });

    return languageSettings;
  },

  updateModel: function (key: any, value: any) {
    // If the setting is found in the main collection, update that model.
    const setting = ServerSettingsPane.prototype.updateModel.apply(
      this,
      // eslint-disable-next-line prefer-rest-params
      arguments
    );
    if (setting) {
      return setting;
    }

    // Otherwise, look for one of our special settings.
    const unprefixedKey = key.replace(this.prefix, '');
    const account = this.options.account;

    if (account.has(unprefixedKey)) {
      return account.set(unprefixedKey, value);
    }
  },
});

export default ServerSettingsLanguagesPane;
