import { createSelector, Selector } from 'reselect';
import {
  MediaGrabberDevice,
  MediaGrabberDeviceStatus,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

const selectAvailableMediaGrabberDevices: Selector<
  LegacyRootState,
  MediaGrabberDevice[]
> = createSelector(selectDVRSetup, ({ devices }) => {
  return devices.mediaGrabberDevices.filter(
    (mediaGrabberDevice) =>
      !mediaGrabberDevice.parentID &&
      mediaGrabberDevice.status === MediaGrabberDeviceStatus.Alive
  );
});

export default selectAvailableMediaGrabberDevices;
