import commands from 'app/js/commands/commands';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import generatorStates from 'app/js/utils/constants/convert/generatorStates';
import dispatcher from 'app/js/utils/dispatcher';
import posterSizes from 'app/js/utils/posterSizes';
import t from 'app/js/utils/t';
import CompositeImage from 'app/js/views/images/CompositeImage';
import ConvertModal from 'app/js/views/modals/convert/ConvertModal';
import VersionGeneratorMetadataModal from 'app/js/views/modals/convert/VersionGeneratorMetadataModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/lists... Remove this comment to see the full error message
import tpl from 'app/templates/lists/library/convert/VersionGeneratorListItem.tpl';

const VersionGeneratorListItem = Marionette.ItemView.extend({
  //
  // Properties
  className:
    'striped-list-item media-list-item playlist-item version-generator-item',
  tagName: 'li',
  template: tpl,
  ui: {
    poster: '.media-poster',
    status: '.generator-status',
  },
  events: {
    'click .playlist-inner-item': 'onClick',
    'click .retry-btn': 'onRetryClick',
    'click .edit-btn': 'onEditClick',
    'click .delete-btn': 'onDeleteClick',
  },
  attributes: function () {
    return {
      'data-generator-id': this.model.id,
    };
  },
  //
  // Init
  initialize: function (options: any) {
    this.transcodeJobs = options.transcodeJobs;
    this.pausedSetting = options.pausedSetting;
    this.status = this.model.get('status');
    const posterHeight = 60;
    this.compositeImage = new CompositeImage({
      model: this.model,
      width: posterSizes.getPosterWidth('movie', posterHeight),
      height: posterHeight,
      rows: 1,
      columns: 1,
    });
    this.listenTo(
      this.transcodeJobs,
      'reset add remove',
      this.onTranscodeJobsChange
    );
  },
  //
  // Control
  resetState: function () {
    const states = [
      'converting',
      generatorStates.PENDING,
      generatorStates.COMPLETE,
      generatorStates.FAILED,
    ];
    this.ui.status.removeClass(states.join(' '));
  },
  getState: function (status: any) {
    // `converting` is not a real generator state, but we will use it to
    // distinguish between waiting and waiting with an active transcode job.
    if (this.transcodeJobs.findWhere({ generatorID: this.model.id })) {
      return 'converting';
    }
    // A generator can be in a complete state with failed items, but we
    // want to show a failed state in that case.
    if (status.get('itemsFailedCount') > 0) {
      return generatorStates.FAILED;
    }
    return status.get('state');
  },
  //
  // Bindings
  bindings: {
    '.generator-title': {
      modelAttribute: 'title',
    },
    '.generator-target': {
      modelAttribute: 'target',
    },
    '.generator-status': {
      model: 'status',
      modelAttribute: 'state',
      modelListeners: ['state', 'itemsFailedCount'],
      manual: true,
      // @ts-expect-error ts-migrate(6133) FIXME: 'state' is declared but its value is never read.
      formatter: function (state: any, $el: any) {
        (this as any).resetState();
        $el.addClass((this as any).getState((this as any).status));
      },
    },
    '.generator-status-failed-message': {
      model: 'status',
      modelAttribute: 'itemsFailedCount',
      formatter: function (itemsFailedCount: any) {
        return itemsFailedCount === 1
          ? t('1 failed item')
          : t('{1} failed items', itemsFailedCount);
      },
    },
    '.retry-btn': {
      model: 'status',
      modelAttribute: 'state',
      modelListeners: ['state', 'itemsFailedCount'],
      manual: true,
      autohide: function () {
        return (
          (this as any).getState((this as any).status) ===
          generatorStates.FAILED
        );
      },
    },
  },
  //
  // Listeners
  onRender: function () {
    this.compositeImage.setElement(this.ui.poster);
  },
  onClose: function () {
    this.compositeImage.close();
  },
  onTranscodeJobsChange: function () {
    const state = this.getState(this.status);
    this.resetState();
    this.ui.status.addClass(state);
  },
  onClick: function (event: any) {
    event.preventDefault();
    dispatcher.trigger(
      'show:modal',
      new VersionGeneratorMetadataModal({
        model: this.model,
        listModel: this.model.get('children'),
        transcodeJobs: this.transcodeJobs,
        pausedSetting: this.pausedSetting,
      })
    );
  },
  onRetryClick: function (event: any) {
    event.preventDefault();
    event.stopImmediatePropagation();
    commands.execute('reprocessVersionGenerator', { generator: this.model });
  },
  onEditClick: function (event: any) {
    event.preventDefault();
    event.stopImmediatePropagation();
    dispatcher.trigger('show:modal', new ConvertModal({ model: this.model }));
  },
  onDeleteClick: function (event: any) {
    event.preventDefault();
    event.stopImmediatePropagation();
    commands.execute('deleteVersionGenerator', { generator: this.model });
  },
});
export default VersionGeneratorListItem;
