import { createSelector, Selector } from 'reselect';
import selectAllProviderEntities from 'app/js/stores/selectors/provider/selectAllProviderEntities';
import { Provider } from 'app/js/v4/core/providers/providersTypes';
import isEPGProvider from 'app/js/v4/core/providers/utils/isEPGProvider';
import unknownProvider from 'app/js/v4/core/providers/utils/unknownProvider';
import createUnstableServerSelector from 'app/js/v4/core/servers/selectors/createUnstableServerSelector';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import memoize from 'app/js/v4/utils/memoize';

// This selector may be created frequently in components and saga workers,
// but there is a limited number of possible selectors based on the arguments.
// Rather than independently memoize in each component or saga, add a layer of
// memoization on top of reselect here.

function createEPGProviderSelector(
  machineIdentifier: string
): Selector<LegacyRootState, Provider> {
  return createSelector(
    selectAllProviderEntities,
    createUnstableServerSelector(machineIdentifier),
    (providerEntities, unstableServer) => {
      const matchingProviderEntityID = unstableServer.providers.find(
        (providerEntityID) => isEPGProvider(providerEntities[providerEntityID])
      );

      return matchingProviderEntityID
        ? providerEntities[matchingProviderEntityID]
        : unknownProvider;
    }
  );
}

export default memoize(createEPGProviderSelector);
