import { createSelector, Selector } from 'reselect';
import {
  ContentDirectory,
  ProviderFeatureType,
} from 'app/js/v4/core/providers/providersTypes';
import getProviderFeature from 'app/js/v4/core/providers/utils/getProviderFeature';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import memoize from 'app/js/v4/utils/memoize';
import createProviderSelector from './createProviderSelector';

// This selector may be created frequently in components and saga workers,
// but there is a limited number of possible selectors based on the arguments.
// Rather than independently memoize in each component or saga, add a layer of
// memoization on top of reselect here.

function createProviderContentDirectoriesSelector(
  machineIdentifier: string,
  providerIdentifier: string
): Selector<LegacyRootState, ContentDirectory[]> {
  return createSelector(
    createProviderSelector(machineIdentifier, providerIdentifier),
    (provider) => {
      const contentFeature = getProviderFeature(
        provider,
        ProviderFeatureType.Content
      );

      return contentFeature?.directories ?? [];
    }
  );
}

export default memoize(createProviderContentDirectoriesSelector);
