import { createSelector, Selector } from 'reselect';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import selectEPGSettings from './selectEPGSettings';

const selectEPGSettingsPostalCodeExample: Selector<
  LegacyRootState,
  string | undefined
> = createSelector(selectEPGSettings, ({ selectedCountry }) => {
  return selectedCountry && selectedCountry.flavor === 1
    ? selectedCountry.example
    : undefined;
});

export default selectEPGSettingsPostalCodeExample;
