import { useSelector } from 'react-redux';
import getPixelRatio from 'app/js/utils/constants/getPixelRatio';
import * as imageSizes from 'app/js/utils/constants/imageSizes';
import path from 'app/js/utils/url/path';
import { Provider } from 'app/js/v4/core/providers/providersTypes';
import createImageTranscodeURLSelector from 'app/js/v4/core/servers/selectors/createImageTranscodeURLSelector';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import assert from 'app/js/v4/utils/assert';

const sizeMemo: Record<string, ImageSize> = {};
const widthMemo: Record<string, number> = {};

type ImageSize = typeof imageSizes[keyof typeof imageSizes];

const sortedImageSizes = [
  imageSizes.SMALL_60,
  imageSizes.SMALL_120,
  imageSizes.MEDIUM_240,
  imageSizes.MEDIUM_360,
  imageSizes.LARGE_480,
  imageSizes.LARGE_720,
  imageSizes.LARGE_1280,
  imageSizes.LARGE_1920,
].sort((a, b) => getWidthForSize(a) - getWidthForSize(b)) as ImageSize[];

function getWidthForSize(imageSize: string): number {
  if (widthMemo.hasOwnProperty(imageSize)) {
    return widthMemo[imageSize];
  }

  const width = parseInt(imageSize.split('-')[1], 10);
  widthMemo[imageSize] = width;

  return width;
}

function getSizeForWidth(width: number): ImageSize {
  if (sizeMemo.hasOwnProperty(width)) {
    return sizeMemo[width];
  }

  const size =
    sortedImageSizes.find((imageSize) => getWidthForSize(imageSize) >= width) ||
    sortedImageSizes[sortedImageSizes.length - 1];

  sizeMemo[width] = size;
  return size;
}

interface BaseParams {
  format?: string;
  quality?: number;
}

interface DimensionsParams extends BaseParams {
  width: number;
  height: number;
  minSize: 1 | undefined;
  upscale: 1 | undefined;
}

interface SizeParams extends BaseParams {
  size: ImageSize;
  scale: number;
}

type TranscodeOptions = TranscodeImageOptions & {
  supportsSize: boolean;
};

function getDimensionsParams(
  options: TranscodeOptions
): DimensionsParams | SizeParams {
  const {
    allowPixelRatioSize = true,
    minSize = true,
    upscale = true,
  } = options;

  if (__DEV__) {
    assert(
      'size' in options ||
        (options.width && (options.height || options.supportsSize)),
      'TranscodeImage requires `width` to determine `size` or both `width` and `height`'
    );
  }

  if (__DEV__) {
    assert(
      !('size' in options) || options.supportsSize,
      'TranscodeImage cannot use `size` directly if the server does not support the param'
    );
  }

  const pxRatio = allowPixelRatioSize ? getPixelRatio() : 1;
  const size =
    'size' in options ? options.size : getSizeForWidth(options.width);

  if (options.supportsSize) {
    return {
      size,
      scale: Math.ceil(pxRatio),
    };
  } else {
    assert(
      !('size' in options) && options.height != null,
      'Both width and height required'
    );
    const bracketedWidth = getWidthForSize(size);
    const aspectRatio = options.height / options.width;

    return {
      width: Math.ceil(bracketedWidth * pxRatio),
      height: Math.ceil(aspectRatio * bracketedWidth * pxRatio),
      minSize: minSize ? 1 : undefined,
      upscale: upscale ? 1 : undefined,
    };
  }
}

function appendTranscodeParams(
  url: string | undefined,
  options: TranscodeOptions
): string | undefined {
  if (!url) {
    return;
  }

  const params = Object.assign(getDimensionsParams(options), options.filters);

  if (options.format) {
    params.format = options.format;
  }

  if (options.quality) {
    params.quality = options.quality;
  }

  // Move existing params to the end of the url. Plex Media Server expects
  // X-Plex- params at the end of the query string because they can be headers
  // as well as params.

  const queryIndex = url.indexOf('?');

  let urlWithParams = url;

  if (queryIndex > -1) {
    urlWithParams = path.join(url.substring(0, queryIndex), params);
    urlWithParams += urlWithParams.indexOf('?') === -1 ? '?' : '&';
    urlWithParams += url.substring(queryIndex + 1);
  } else {
    urlWithParams = path.join(urlWithParams, params);
  }

  return urlWithParams;
}

interface BaseTranscodeImageOptions {
  url?: string;
  minSize?: boolean;
  upscale?: boolean;
  format?: string;
  quality?: number;
  filters?: {
    blur?: number;
    background?: string;
    opacity?: number;
    saturation?: number;
  };
  allowPixelRatioSize?: boolean;
}

export interface SizeTranscodeImageOptions extends BaseTranscodeImageOptions {
  size: ImageSize;
}

export interface WidthSizeTranscodeImageOptions
  extends BaseTranscodeImageOptions {
  width: number;
  height?: number;
}

export interface DimensionTranscodeImageOptions
  extends BaseTranscodeImageOptions {
  width: number;
  height: number;
}

type TranscodeImageOptions =
  | DimensionTranscodeImageOptions
  | SizeTranscodeImageOptions
  | WidthSizeTranscodeImageOptions;

export type UseTranscodeImageProps = TranscodeImageOptions & {
  server: Server;
  provider?: Provider;
};

export default function useTranscodeImage({
  server,
  provider,
  url,
  ...options
}: UseTranscodeImageProps): string | undefined {
  const transcodeImageUrl = useSelector(
    createImageTranscodeURLSelector(
      server.machineIdentifier,
      provider?.identifier,
      url
    )
  );

  return appendTranscodeParams(transcodeImageUrl, {
    ...options,
    // The size parameter is only supported by the provider cloud transcoder
    // currently, not the plex.tv or PMS transcoders.
    supportsSize: server.isCloud && !!provider,
  });
}
