import t from 'app/js/utils/t';
import { StatisticsMetadataType } from 'app/js/v4/features/dashboard/dashboardTypes';

export default function getStatisticsMetadataTypeLabel(
  statisticsMetadataType: StatisticsMetadataType
): string {
  switch (statisticsMetadataType) {
    case StatisticsMetadataType.All:
      return t('All Media');
    case StatisticsMetadataType.Movie:
      return t('Movies');
    case StatisticsMetadataType.TV:
      return t('TV');
    case StatisticsMetadataType.Music:
      return t('Music');
    case StatisticsMetadataType.Photo:
      return t('Photos');
    case StatisticsMetadataType.Game:
      return t('Games');
    default:
      return '';
  }
}
