import { useEffect } from 'react';
import { resolve } from 'app/js/adapters/adapters';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import { useMetricsContext } from 'app/js/v4/core/ui/metrics/components/MetricsContextProvider';
import usePrevious from 'app/js/views/v3/helpers/hooks/usePrevious';

export default function useMetricsModalDismissEffect(
  isOpen: boolean,
  shouldTrackOnClose = false
): void {
  const { metricsPage } = useMetricsContext();

  const prevIsOpen = usePrevious(isOpen);

  useEffect(() => {
    if (prevIsOpen && !isOpen && shouldTrackOnClose) {
      resolve('metrics').trackView({
        page: metricsPage,
        action: metricsActions.DISMISS,
      });
    }
  }, [isOpen, prevIsOpen, shouldTrackOnClose, metricsPage]);
}
