import classNames from 'classnames';
import React, { ReactElement, useCallback } from 'react';
import { useSelector } from 'react-redux';
import {
  IconEditFilled,
  IconOverflowVerticalAlt,
  IconUpCircledFilled,
} from '@plex/react-chroma-icon';
import { Row } from '@plex/react-chroma-layout';
import { Align, Size } from '@plex/react-chroma-types';
import * as providerActions from 'app/js/stores/actions/provider/providerActions';
import * as agentIdentifiers from 'app/js/utils/constants/agentIdentifiers';
import * as features from 'app/js/utils/constants/user/features';
import createSourceFromIdentifiers from 'app/js/utils/metadata/createSourceFromIdentifiers';
import getDirectoryUpgrade from 'app/js/utils/server/getDirectoryUpgrade';
import t from 'app/js/utils/t';
import MetadataIcon from 'app/js/v4/core/metadata/components/icons/MetadataIcon';
import { MetadataType } from 'app/js/v4/core/metadata/metadataTypes';
import { getMetadataTypeChild } from 'app/js/v4/core/metadata/utils/getMetadataTypeRelations';
import { ContentDirectory } from 'app/js/v4/core/providers/providersTypes';
import useLibraryUpdating from 'app/js/v4/core/serverActivities/hooks/useLibraryUpdating';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import { useRedispatchLegacyServerAction } from 'app/js/v4/core/servers/hooks/useRedispatchLegacyServerAction';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import useDisclosure from 'app/js/v4/core/ui/hooks/useDisclosure';
import useIsFocused from 'app/js/v4/core/ui/hooks/useIsFocused';
import useIsRefHovered from 'app/js/v4/core/ui/hooks/useIsRefHovered';
import usePreviousValueForAtMost from 'app/js/v4/core/ui/hooks/usePreviousValueForAtMost';
import ListRow from 'app/js/v4/core/ui/lists/components/ListRow';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import Kind from 'app/js/v4/core/ui/types/Kind';
import { default as CoreSize } from 'app/js/v4/core/ui/types/Size';
import createUserFeatureSelector from 'app/js/v4/core/user/selectors/createUserFeatureSelector';
import LibraryActionsMenu from 'app/js/v4/features/libraryManagement/components/LibraryActionsMenu';
import LibraryHubList from 'app/js/v4/features/libraryManagement/components/LibraryHubList';
import createLibraryHubsSelector from 'app/js/v4/features/libraryManagement/selectors/createLibraryHubsSelector';
import getHubManagementKey from 'app/js/v4/features/libraryManagement/utils/getHubManagementKey';
import DisclosureArrow from 'app/js/views/v3/components/core/base/DisclosureArrow';
import { useAgentUpgradeLibraryModal } from 'app/js/views/v3/components/upgrade/AgentUpgradeLibraryModals';
import useUniqueElementID from 'app/js/views/v3/helpers/hooks/useUniqueElementID';
import * as directions from 'app/js/views/v3/helpers/props/directions';
import getSourceRoute from 'app/js/views/v3/helpers/routes/getSourceRoute';
import styles from './ManageLibrarySummary.css';

interface ManageLibrarySummaryProps {
  index: number;
  contentDirectory: ContentDirectory;
  machineIdentifier: string;
  providerEntityID: string;
}

export default function ManageLibrarySummary(
  props: ManageLibrarySummaryProps
): ReactElement {
  const { index, contentDirectory } = props;

  const [server, provider] = useSourceContext();
  const moreButtonID = useUniqueElementID();
  const [isFocused, onFocus, onBlur] = useIsFocused();
  const [isHovered, hoverRef] = useIsRefHovered();
  const actionsMenuState = useDisclosure();
  const hubListState = useDisclosure();

  const sourceURI = createSourceFromIdentifiers({
    serverIdentifier: server.machineIdentifier,
    key: getHubManagementKey(contentDirectory.id),
  });

  const isActionsVisible =
    isFocused || isHovered || actionsMenuState.isOpen || hubListState.isOpen;

  const route = getSourceRoute(server, provider, contentDirectory);
  const { title } = contentDirectory;

  const redispatchLegacyServerAction = useRedispatchLegacyServerAction({
    server,
    provider,
  });

  const isScanning = useLibraryUpdating(server, contentDirectory);
  const libraryHubs = useSelector(createLibraryHubsSelector(sourceURI));
  const isLoading = usePreviousValueForAtMost(libraryHubs?.isPending, true);
  const canManageHubs = server.ownerFeatures?.includes(features.MANAGE_HUBS);
  const hasHomeLibraryAccessLabels = useSelector(
    createUserFeatureSelector(features.HOME_LIBRARY_ACCESS_LABELS)
  );

  const { Modal, onOpen: onUpgradeLibraryPress } = useAgentUpgradeLibraryModal(
    props.machineIdentifier,
    props.contentDirectory
  );

  const onEditPress = useCallback(() => {
    redispatchLegacyServerAction(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
      providerActions.editLibrary({
        contentDirectory,
      })
    );
  }, [contentDirectory, redispatchLegacyServerAction]);

  // The icon is based off the child items and if the library is 'other videos'
  // This can be removed once subtype is added for this library type
  // https://github.com/plexinc/plex-media-server/issues/12144
  const leafType = contentDirectory.type
    ? getMetadataTypeChild(contentDirectory.type)
    : null;
  const isOtherVideosLibrary =
    contentDirectory.agent === agentIdentifiers.NONE &&
    contentDirectory.type === MetadataType.Movie;
  const contentDirectoryType = isOtherVideosLibrary
    ? MetadataType.Clip
    : contentDirectory.type;
  const type = leafType ?? contentDirectoryType;

  const metadataIcon = type ? <MetadataIcon type={type} /> : null;

  const { hasPlexMusicUpgrade, hasPlexMovieUpgrade, hasPlexSeriesUpgrade } =
    getDirectoryUpgrade(server, contentDirectory);
  const isLibraryUpgradeAvailable =
    hasPlexMusicUpgrade || hasPlexMovieUpgrade || hasPlexSeriesUpgrade;

  return (
    <>
      <ListRow
        ref={hoverRef}
        alternateClassName={styles.alternateRow}
        className={styles.row}
        isAlternate={!!(index % 2)}
        isHovered={isHovered || isFocused}
        onBlur={onBlur}
        onFocus={onFocus}
      >
        <Link
          aria-label={t('Manage {libraryTitle}', {
            libraryTitle: title,
          })}
          className={styles.underlay}
          onPress={canManageHubs ? hubListState.onToggle : onEditPress}
        />

        <div className={styles.overlay}>
          <div className={styles.iconContainer}>
            {isScanning ? <Spinner size={CoreSize.Small} /> : metadataIcon}
          </div>

          <Link className={styles.title} title={title} to={route}>
            {title}
          </Link>

          {isLibraryUpgradeAvailable && !hubListState.isOpen ? (
            <Link
              className={styles.action}
              kind={Kind.Primary}
              onPress={onUpgradeLibraryPress}
            >
              <Row gap={Size.XS} verticalAlign={Align.Center}>
                <IconUpCircledFilled />
                <span
                  className={
                    isHovered ? styles.upgradeTextHovered : styles.upgradeText
                  }
                >
                  {t('Upgrade Library')}
                </span>
              </Row>
            </Link>
          ) : null}

          <div
            className={classNames(
              styles.actions,
              isActionsVisible && styles.visible
            )}
          >
            {hubListState.isOpen ? (
              <div className={styles.header}>
                {libraryHubs?.options.length ? (
                  <>
                    <span
                      className={styles.columnHeader}
                      title={t('Library Recommended')}
                    >
                      {t('Library Recommended')}
                    </span>
                    <span className={styles.columnHeader} title={t('Home')}>
                      {t('Home')}
                    </span>
                    <span
                      className={styles.columnHeader}
                      title={
                        hasHomeLibraryAccessLabels
                          ? t("Friends' Home")
                          : t("Shared Users' Home")
                      }
                    >
                      {hasHomeLibraryAccessLabels
                        ? t("Friends' Home")
                        : t("Shared Users' Home")}
                    </span>
                  </>
                ) : null}
                <Link className={styles.action} onPress={hubListState.onClose}>
                  <span className={styles.headerIcon}>
                    {isLoading ? (
                      <Spinner size={CoreSize.Small} />
                    ) : (
                      <DisclosureArrow direction={directions.UP} />
                    )}
                  </span>
                </Link>
              </div>
            ) : (
              <div className={styles.header}>
                {canManageHubs ? (
                  <Link className={styles.action} onPress={hubListState.onOpen}>
                    <DisclosureArrow direction={directions.DOWN} />
                    {t('Manage Recommendations')}
                  </Link>
                ) : null}
                <Link className={styles.action} onPress={onEditPress}>
                  <Row gap={Size.XXS} verticalAlign={Align.Center}>
                    <IconEditFilled />
                    {t('Edit Library')}
                  </Row>
                </Link>
                <Link
                  aria-label={t('More Actions')}
                  className={styles.action}
                  data-testid="metadataRowMoreButton"
                  id={moreButtonID}
                  onPress={actionsMenuState.onToggle}
                >
                  <span className={styles.headerIcon}>
                    <IconOverflowVerticalAlt />
                  </span>
                </Link>
              </div>
            )}
          </div>
        </div>

        <LibraryActionsMenu
          contentDirectory={contentDirectory}
          isOpen={actionsMenuState.isOpen}
          machineIdentifier={props.machineIdentifier}
          providerEntityID={props.providerEntityID}
          target={moreButtonID}
          onMenuClose={actionsMenuState.onClose}
        />
      </ListRow>
      {hubListState.isOpen ? <LibraryHubList sourceURI={sourceURI} /> : null}
      <Modal />
    </>
  );
}
