/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import t from 'app/js/utils/t';
import DashSeparator from 'app/js/views/v3/components/core/base/DashSeparator';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import ToolbarButton from 'app/js/views/v3/components/core/buttons/ToolbarButton';
import PageHeader from 'app/js/views/v3/components/core/header/PageHeader';
import PageHeaderLeft from 'app/js/views/v3/components/core/header/PageHeaderLeft';
import PageHeaderRight from 'app/js/views/v3/components/core/header/PageHeaderRight';
import ServerLogLevelMenuConnector from 'app/js/views/v3/components/pages/serverSettings/panes/logs/ServerLogLevelMenuConnector';
import IconCircleBackslash from 'app/glyphs/circle-backslash-560.svg';
import IconTimer from 'app/glyphs/timer-560.svg';
import ServerLogFilterInput from './ServerLogFilterInput';
import ServerLogTimeFilterInputs from './ServerLogTimeFilterInputs';
import styles from './ServerLogPageHeader.css';

type ServerLogPageHeaderProps = {
  filter: string;
  timeFilterStart: string;
  timeFilterEnd: string;
  isTimeFilterStartValid: boolean;
  isTimeFilterEndValid: boolean;
  showTimeFilter: boolean;
  showRawText: boolean;
  title: string;
  logLines: any[];
  unfilteredLogLinesCount: number;
  onTextFilterChange: (...args: any[]) => any;
  onTimeFilterChange: (...args: any[]) => any;
  onToggleTimeFilter: (...args: any[]) => any;
  onToggleRaw: (...args: any[]) => any;
  onClearLog: (...args: any[]) => any;
};

class ServerLogPageHeader extends Component<ServerLogPageHeaderProps> {
  //
  // Render

  render() {
    const {
      filter,
      timeFilterStart,
      timeFilterEnd,
      isTimeFilterStartValid,
      isTimeFilterEndValid,
      showTimeFilter,
      showRawText,
      title,
      unfilteredLogLinesCount,
      logLines,
      onTextFilterChange,
      onTimeFilterChange,
      onToggleTimeFilter,
      onToggleRaw,
      onClearLog,
    } = this.props;

    const hiddenLinesCount = unfilteredLogLinesCount - logLines.length;

    return (
      <>
        <PageHeader>
          <PageHeaderLeft>
            {title}
            <DashSeparator />
            {t('Plex Media Server Logs')}

            {hiddenLinesCount > 0 ? (
              <span className={styles.controlsHiddenLinesCount}>
                <DashSeparator />
                {t('{hiddenLinesCount} items hidden by filters', {
                  hiddenLinesCount,
                })}
              </span>
            ) : null}
          </PageHeaderLeft>

          <PageHeaderRight>
            <ServerLogLevelMenuConnector />

            <ServerLogFilterInput
              filter={filter}
              onTextFilterChange={onTextFilterChange}
            />

            <ToolbarButton
              isSelected={showTimeFilter}
              tooltip={t('Time Filter')}
              onPress={onToggleTimeFilter}
            >
              <IconTimer />
            </ToolbarButton>

            <Link
              className={styles.rawButton}
              isSelected={showRawText}
              onPress={onToggleRaw}
            >
              {t('Raw')}
            </Link>

            <ToolbarButton tooltip={t('Clear Logs')} onPress={onClearLog}>
              <IconCircleBackslash />
            </ToolbarButton>
          </PageHeaderRight>
        </PageHeader>

        {showTimeFilter ? (
          <ServerLogTimeFilterInputs
            isTimeFilterEndValid={isTimeFilterEndValid}
            isTimeFilterStartValid={isTimeFilterStartValid}
            timeFilterEnd={timeFilterEnd}
            timeFilterStart={timeFilterStart}
            onTimeFilterChange={onTimeFilterChange}
          />
        ) : null}
      </>
    );
  }
}

export default ServerLogPageHeader;
