/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import asyncProps from 'app/js/views/v3/decorators/asyncProps';
import Bar from './Bar';
import ChartTooltip from './ChartTooltip';
import LegendLabel from './LegendLabel';
import styles from './BarChart.css';

function areAllSeriesVisible(series: any) {
  return series.every((points: any) => points.visible);
}

function createTooltip(props: any) {
  return function renderTooltip(tooltipProps: any) {
    return <ChartTooltip {...tooltipProps} {...props} />;
  };
}

function createBar(props: any) {
  return function renderBar(barProps: any) {
    return <Bar {...barProps} {...props} />;
  };
}

type BarChartProps = {
  className?: string;
  recharts?: any;
  width?: number | string;
  height?: number | string;
  series: any[];
  data: any[];
  legendSummary?: React.ReactNode;
  xAxisKey: string;
  stacked?: boolean;
  isAnimationActive?: boolean;
  showTooltipTotal?: boolean;
  chartMargin?: any;
  gridLinesStyles?: any;
  barGap?: number;
  legendHeight?: number;
  tooltipLabelFormatter: (...args: any[]) => any;
  tooltipValueFormatter: (...args: any[]) => any;
  xAxisDomain?: any[];
  xAxisStyle?: any | boolean;
  xAxisPadding?: any;
  xAxisTick?: boolean | ((...args: any[]) => any) | any;
  xAxisTickMargin?: number;
  xAxisTickLineStyle?: any | boolean;
  xAxisTickFormatter: (...args: any[]) => any;
  yAxisDomain?: any[];
  yAxisWidth?: number;
  yAxisStyle?: any | boolean;
  yAxisPadding?: any;
  yAxisTick?: boolean | ((...args: any[]) => any) | any;
  yAxisTickMargin?: number;
  yAxisTickLineStyle?: any | boolean;
  yAxisTickFormatter: (...args: any[]) => any;
  onLegendPress?: (...args: any[]) => any;
  onChartReady?: (...args: any[]) => any;
};

type BarChartState = any;

class BarChart extends Component<BarChartProps, BarChartState> {
  static defaultProps = {
    xAxisKey: 'date',
    stacked: true,
    isAnimationActive: true,
    chartMargin: {
      top: 15,
      right: 20,
      bottom: 10,
      left: 0,
    },
    gridLinesStyles: {
      horizontal: true,
      vertical: false,
      stroke: 'rgba(255, 255, 255, 0.1)',
    },
    barGap: 2,
    legendHeight: 20,
    xAxisStyle: false,
    xAxisPadding: {
      left: 0,
      right: 0,
    },
    xAxisTick: {
      fill: 'rgba(255, 255, 255, 0.45)',
      fontSize: '10px',
    },
    xAxisTickMargin: 15,
    xAxisTickLineStyle: {
      fill: 'rgba(255, 255, 255, 0.1)',
      strokeWidth: 5,
    },
    yAxisWidth: 60,
    yAxisStyle: false,
    yAxisPadding: {
      top: 0,
      bottom: 0,
    },
    yAxisTick: {
      fill: 'rgba(255, 255, 255, 0.45)',
      fontSize: '10px',
    },
    yAxisTickMargin: 5,
    yAxisTickLineStyle: false,
  };

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      hoveredDataKey: null,
    };
  }

  // @ts-expect-error ts-migrate(6133) FIXME: 'prevState' is declared but its value is never rea... Remove this comment to see the full error message
  componentDidUpdate(prevProps: BarChartProps, prevState: BarChartState) {
    const { recharts, onChartReady } = this.props;

    if (!prevProps.recharts && recharts && onChartReady) {
      onChartReady();
    }
  }

  //
  // Listeners

  onMouseEnter = (hoveredDataKey: any) => {
    this.setState({ hoveredDataKey });
  };

  onMouseLeave = () => {
    this.setState({ hoveredDataKey: null });
  };

  //
  // Render

  renderLegend = () => {
    const { series, legendSummary, onLegendPress } = this.props;

    const { hoveredDataKey } = this.state;

    return (
      <div className={styles.legendContainer}>
        <div className={styles.legendLabels}>
          {series.map((points) => {
            const isTranslucent =
              !points.visible ||
              (hoveredDataKey &&
                hoveredDataKey !== points.dataKey &&
                !points.selected);

            return (
              <LegendLabel
                key={points.dataKey}
                className={
                  isTranslucent
                    ? styles.translucentLegendLabel
                    : styles.legendLabel
                }
                dataKey={points.dataKey}
                labelColor={points.color}
                title={points.title}
                onMouseEnter={this.onMouseEnter}
                onMouseLeave={this.onMouseLeave}
                onPress={onLegendPress}
              />
            );
          })}
        </div>

        {legendSummary ? (
          <div className={styles.legendSummary}>{legendSummary}</div>
        ) : null}
      </div>
    );
  };

  render() {
    const {
      className,
      recharts,
      width,
      height,
      series,
      data,
      xAxisKey,
      stacked,
      isAnimationActive,
      showTooltipTotal,
      chartMargin,
      gridLinesStyles,
      barGap,
      legendHeight,
      tooltipLabelFormatter,
      tooltipValueFormatter,
      xAxisDomain,
      xAxisStyle,
      xAxisPadding,
      xAxisTick,
      xAxisTickMargin,
      xAxisTickLineStyle,
      xAxisTickFormatter,
      yAxisDomain,
      yAxisWidth,
      yAxisStyle,
      yAxisPadding,
      yAxisTick,
      yAxisTickMargin,
      yAxisTickLineStyle,
      yAxisTickFormatter,
    } = this.props;

    const { hoveredDataKey } = this.state;

    if (!recharts) {
      return null;
    }

    const {
      CartesianGrid,
      Legend,
      BarChart: RechartsBarChart,
      Bar: RechartsBar,
      ResponsiveContainer,
      Tooltip,
      XAxis,
      YAxis,
    } = recharts;

    const seriesOrdering = series.map((points) => points.dataKey);

    return (
      <ResponsiveContainer className={className} height={height} width={width}>
        <RechartsBarChart data={data} margin={chartMargin}>
          <CartesianGrid {...gridLinesStyles} />

          <XAxis
            axisLine={xAxisStyle}
            dataKey={xAxisKey}
            domain={xAxisDomain}
            minTickGap={0}
            padding={xAxisPadding}
            tick={xAxisTick}
            tickFormatter={xAxisTickFormatter}
            tickLine={xAxisTickLineStyle}
            tickMargin={xAxisTickMargin}
          />

          <YAxis
            axisLine={yAxisStyle}
            domain={yAxisDomain}
            padding={yAxisPadding}
            tick={yAxisTick}
            tickCount={6}
            tickFormatter={yAxisTickFormatter}
            tickLine={yAxisTickLineStyle}
            tickMargin={yAxisTickMargin}
            width={yAxisWidth}
          />

          <Tooltip
            content={createTooltip({
              series,
              showTotal: showTooltipTotal,
              labelFormatter: tooltipLabelFormatter,
              valueFormatter: tooltipValueFormatter,
            })}
            cursor={false}
            isAnimationActive={false}
          />

          {series.map((points, index) => (
            /* eslint-disable react/jsx-no-bind */
            <RechartsBar
              key={points.dataKey}
              className={
                hoveredDataKey &&
                hoveredDataKey !== points.dataKey &&
                areAllSeriesVisible(series)
                  ? styles.translucentBar
                  : styles.bar
              }
              dataKey={points.dataKey}
              fill={points.color}
              isAnimationActive={isAnimationActive}
              radius={2}
              shape={createBar({
                barGap: stacked ? barGap : 0,
                data,
                dataKey: xAxisKey,
                seriesOrdering,
                seriesIndex: index,
              })}
              stackId={stacked ? 'stacked' : null}
              onMouseEnter={this.onMouseEnter.bind(null, points.dataKey)}
              onMouseLeave={this.onMouseLeave}
            />
            /* eslint-enable react/jsx-no-bind */
          ))}

          <Legend
            content={this.renderLegend}
            height={legendHeight}
            wrapperStyle={{
              paddingLeft: `${yAxisWidth}px`,
            }}
          />
        </RechartsBarChart>
      </ResponsiveContainer>
    );
  }
}

export default asyncProps(BarChart, {
  // @ts-expect-error ts-migrate(7016) FIXME: Could not find a declaration file for module 'rech... Remove this comment to see the full error message
  recharts: () => import('recharts'),
});
