/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import ServerConnectionStatusIcon from 'app/js/views/v3/components/core/icons/ServerConnectionStatusIcon';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import styles from './ServerSettingsStatusIcon.css';

type ServerSettingsStatusIconProps = {
  className?: string;
  isFullOwnedServer: boolean;
  isInstallingServerUpdate: boolean;
  isUnavailable: boolean;
  isSettingsPending: boolean;
  isRelayed: boolean;
  isSecure: boolean;
  isConnected: boolean;
};

function ServerSettingsStatusIcon(props: ServerSettingsStatusIconProps) {
  const {
    className,
    isFullOwnedServer,
    isInstallingServerUpdate,
    isUnavailable,
    isSettingsPending,
  } = props;

  if (
    isInstallingServerUpdate ||
    (isFullOwnedServer && isSettingsPending && !isUnavailable)
  ) {
    return (
      <div className={className}>
        <Spinner className={styles.spinner} size={sizes.SMALL} />
      </div>
    );
  }

  return <ServerConnectionStatusIcon {...props} />;
}

export default ServerSettingsStatusIcon;
