import classNames from 'classnames';
import React, { memo, ReactElement, useCallback } from 'react';
import t from 'app/js/utils/t';
import Checkbox from 'app/js/v4/core/ui/forms/components/Checkbox';
import useVirtualCell from 'app/js/v4/core/ui/lists/hooks/useVirtualCell';
import { VirtualCellProps } from 'app/js/v4/core/ui/lists/listsTypes';
import { SelectedChannelMapping } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import Badge from 'app/js/views/v3/components/core/base/Badge';
import LineupChannelSelect from './LineupChannelSelect';
import SignalQuality from './SignalQuality';
import styles from './DeviceChannelMapping.css';

interface DeviceChannelMappingProps
  extends VirtualCellProps<SelectedChannelMapping> {
  enabled: boolean;
  channelKey?: string;
  lineupIdentifier?: string;
  onEnabledChange(enabled: boolean, index: number): void;
  onChannelKeyChange(
    channelKey: string,
    lineupIdentifier: string,
    index: number
  ): void;
}

function DeviceChannelMapping({
  item: channelMappingItem,
  enabled,
  channelKey,
  lineupIdentifier,
  onEnabledChange,
  onChannelKeyChange,
  ...virtualCellProps
}: DeviceChannelMappingProps): ReactElement | null {
  const cell = useVirtualCell(virtualCellProps);
  const index = virtualCellProps.row;

  const {
    name,
    deviceIdentifier,
    signalStrength,
    signalQuality,
    drm,
    isDeviceChannelHD,
    isLineupChannelHD,
  } = channelMappingItem || {};

  //
  // Callbacks

  const onChange = useCallback(
    (event) => {
      onEnabledChange(event.currentTarget.checked, index);
    },
    [index, onEnabledChange]
  );

  //
  // Render

  if (!channelMappingItem) {
    return null;
  }

  return (
    <div ref={cell.cellRef} className={styles.container} style={cell.style}>
      <label className={styles.deviceChannel}>
        <div className={styles.channelEnabled}>
          {!drm ? (
            <Checkbox
              checked={enabled}
              className={styles.channelEnabledCheckbox}
              name={`[${virtualCellProps.row}].enabled`}
              onChange={onChange}
            />
          ) : null}
        </div>

        <div
          className={classNames(
            styles.deviceChannelInfo,
            !enabled && styles.mappingDisabled
          )}
        >
          <div className={styles.deviceChannelIdentifier}>
            {deviceIdentifier}
          </div>
          <div className={styles.deviceChannelName}>{name}</div>
        </div>

        <SignalQuality
          className={styles.signalQuality}
          quality={signalQuality || signalStrength}
        />
      </label>

      {drm ? (
        <div className={styles.drmChannel}>
          <Badge className={styles.drmChannelBadge}>{t('Protected')}</Badge>
        </div>
      ) : (
        <div className={styles.lineupChannel}>
          <LineupChannelSelect
            className={styles.lineupChannelSelect}
            index={index}
            isDisabled={!enabled}
            selectedChannelKey={channelKey}
            selectedLineupIdentifier={lineupIdentifier}
            onChange={onChannelKeyChange}
          />
          {isDeviceChannelHD || isLineupChannelHD ? (
            <Badge className={styles.hdBadge}>{t('HD')}</Badge>
          ) : null}
        </div>
      )}
    </div>
  );
}

export default memo(DeviceChannelMapping);
