import React, { ReactElement } from 'react';
import { IconUpCircledFilled } from '@plex/react-chroma-icon';
import getDirectoryUpgrade from 'app/js/utils/server/getDirectoryUpgrade';
import t from 'app/js/utils/t';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import MenuItem from 'app/js/views/v3/components/core/menus/MenuItem';
import MenuSeparator from 'app/js/views/v3/components/core/menus/MenuSeparator';
import { useAgentUpgradeLibraryModal } from 'app/js/views/v3/components/upgrade/AgentUpgradeLibraryModals';
import { ContentDirectory } from 'app/js/views/v3/helpers/props/shapes/contentDirectoryShape';
import styles from './LibraryUpgradeMenuItem.css';

interface LibraryUpgradeMenuItemProps {
  server: Server;
  machineIdentifier: string;
  onMenuClose?: () => void;
  sourceDirectory: ContentDirectory;
}

export default function LibraryUpgradeMenuItem(
  props: LibraryUpgradeMenuItemProps
): ReactElement {
  const { hasPlexMovieUpgrade, hasPlexSeriesUpgrade } = getDirectoryUpgrade(
    props.server,
    props.sourceDirectory
  );

  const { Modal, onOpen } = useAgentUpgradeLibraryModal(
    props.machineIdentifier,
    props.sourceDirectory,
    props.onMenuClose
  );

  return hasPlexMovieUpgrade || hasPlexSeriesUpgrade ? (
    <>
      <MenuItem onPress={onOpen}>
        {t('Upgrade Library')}
        <span className={styles.icon}>
          <IconUpCircledFilled />
        </span>
      </MenuItem>
      <MenuSeparator />
      <Modal />
    </>
  ) : (
    <></>
  );
}
