/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import {
  IconLockUnlockedFilled,
  IconQuestionCircledFilled,
  IconRefresh,
  IconX,
} from '@plex/react-chroma-icon';
import { SECURE_CONNECTIONS_SUPPORT_URL } from 'app/js/utils/constants/commonUrls';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/v3/components/core/modals/ConfirmModal';
import SidebarListLink from 'app/js/views/v3/components/core/sidebar/SidebarListLink';
import SidebarMessage from 'app/js/views/v3/components/core/sidebar/SidebarMessage';
import AllowHttpFallbackModalConnector from 'app/js/views/v3/components/pages/empty/AllowHttpFallbackModalConnector';
import {
  shouldAllowInsecureFallback,
  shouldShowInsecureAppMessage,
} from 'app/js/views/v3/helpers/insecureFallbackHelpers';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';

type SidebarServerUnavailableMessageProps = {
  serverEntityID: string;
  machineIdentifier?: string;
  friendlyName?: string;
  presence?: boolean;
  isPending?: boolean;
  isFullServer?: boolean;
  isFullOwnedServer?: boolean;
  isInstallingServerUpdate?: boolean;
  isBundled?: boolean;
  isCloudServerResourcesPending?: boolean;
  httpsRequired?: boolean;
  hasHttpsConnections?: boolean;
  hasFallbackConnections?: boolean;
  onTryAgainPress: (...args: any[]) => any;
  onDeleteServerConfirm: (...args: any[]) => any;
};

type SidebarServerUnavailableMessageState = any;

class SidebarServerUnavailableMessage extends Component<
  SidebarServerUnavailableMessageProps,
  SidebarServerUnavailableMessageState
> {
  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      isRemoveModalOpen: false,
      isAllowInsecureConnectionModalOpen: false,
    };
  }

  shouldComponentUpdate(
    nextProps: SidebarServerUnavailableMessageProps,
    nextState: SidebarServerUnavailableMessageState
  ) {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    return shouldPureComponentUpdate(this, nextProps, nextState);
  }

  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onRemoveServerPress = (event: any) => {
    this.setState({ isRemoveModalOpen: true });
  };

  onRemoveConfirm = () => {
    this.setState({ isRemoveModalOpen: false });
    this.props.onDeleteServerConfirm(this.props.machineIdentifier);
  };

  onRemoveCancel = () => {
    this.setState({ isRemoveModalOpen: false });
  };

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onAllowInsecureConnectionPress = (event: any) => {
    this.setState({ isAllowInsecureConnectionModalOpen: true });
  };

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onAllowInsecureConnectionCancel = (event: any) => {
    this.setState({ isAllowInsecureConnectionModalOpen: false });
  };

  //
  // Render

  render() {
    const {
      serverEntityID,
      friendlyName,
      presence,
      isPending,
      isBundled,
      isCloudServerResourcesPending,
      isFullServer,
      isFullOwnedServer,
      isInstallingServerUpdate,
      httpsRequired,
      hasHttpsConnections,
      hasFallbackConnections,
      onTryAgainPress,
    } = this.props;

    const { isRemoveModalOpen, isAllowInsecureConnectionModalOpen } =
      this.state;

    let showAllowFallbackButton;
    let showFallback;
    let showWhereIsMyServer;
    let showRemoveServer;
    let message;

    if (isInstallingServerUpdate) {
      showAllowFallbackButton = false;
      showFallback = false;
      showWhereIsMyServer = false;
      showRemoveServer = false;
      message = t(
        'Once the server "{serverName}" has finished installing the update, Plex will automatically try to reconnect to this server.',
        { serverName: friendlyName }
      );
    } else {
      const server = {
        isFullServer,
        presence,
        httpsRequired,
        hasHttpsConnections,
        hasFallbackConnections,
      };

      showAllowFallbackButton = shouldAllowInsecureFallback(server);
      showFallback =
        shouldShowInsecureAppMessage(server) || showAllowFallbackButton;
      showWhereIsMyServer = !showFallback;
      showRemoveServer = isFullOwnedServer && !isBundled;

      message = showFallback
        ? t('Unable to connect to "{serverName}" securely.', {
            serverName: friendlyName,
          })
        : t(
            'The server "{serverName}" is unreachable. Make sure it\'s running, double check your network, and try again.',
            { serverName: friendlyName }
          );
    }

    const isConnecting = isCloudServerResourcesPending || isPending;
    const tryAgainLabel = isConnecting ? t('Connecting') : t('Try again');

    return (
      <SidebarMessage message={message}>
        <SidebarListLink
          iconComponent={IconRefresh}
          isDisabled={isConnecting}
          showSpinner={isConnecting}
          title={tryAgainLabel}
          onPress={onTryAgainPress}
        />
        {showAllowFallbackButton ? (
          <>
            <SidebarListLink
              iconComponent={IconLockUnlockedFilled}
              title={t('Allow insecure connection')}
              onPress={this.onAllowInsecureConnectionPress}
            />
            <AllowHttpFallbackModalConnector
              isOpen={isAllowInsecureConnectionModalOpen}
              serverEntityID={serverEntityID}
              onModalClose={this.onAllowInsecureConnectionCancel}
            />
          </>
        ) : null}
        {showFallback ? (
          <SidebarListLink
            iconComponent={IconQuestionCircledFilled}
            target="_blank"
            title={t('Learn more')}
            to={SECURE_CONNECTIONS_SUPPORT_URL}
          />
        ) : null}
        {showWhereIsMyServer ? (
          <SidebarListLink
            iconComponent={IconQuestionCircledFilled}
            target="_blank"
            title={t('Where is my server?')}
            to="https://help.plex.tv/noserver"
          />
        ) : null}
        {showRemoveServer ? (
          <>
            <SidebarListLink
              iconComponent={IconX}
              title={t('Remove server')}
              onPress={this.onRemoveServerPress}
            />
            <ConfirmModal
              confirmLabel={t('Remove')}
              isOpen={isRemoveModalOpen}
              kind={kinds.DANGER}
              message={t(
                'If this server is restarted, it will automatically be added to your account again.'
              )}
              metricsPage={metricsPages.CONFIRM_DELETE_SERVER}
              title={t('Remove {serverName}', { serverName: friendlyName })}
              onCancel={this.onRemoveCancel}
              onConfirm={this.onRemoveConfirm}
            />
          </>
        ) : null}
      </SidebarMessage>
    );
  }
}

export default SidebarServerUnavailableMessage;
