import { FocusEvent, useCallback, useEffect, useRef, useState } from 'react';

type FocusCallback = (event: FocusEvent<HTMLElement>) => void;

// Use a timeout to defer the blur to the next tick, to give the focus event
// time to clear the timeout.
export default function useIsFocused(): [
  boolean,
  FocusCallback,
  FocusCallback
] {
  const [isFocused, setIsFocused] = useState(false);

  const timeoutRef = useRef<number | undefined>(undefined);
  const elRef = useRef<HTMLElement | null>(null);

  const onFocus = useCallback<FocusCallback>((event) => {
    window.clearTimeout(timeoutRef.current);
    elRef.current = event.currentTarget;

    setIsFocused(true);
  }, []);

  const onBlur = useCallback<FocusCallback>(() => {
    timeoutRef.current = window.setTimeout(() => {
      elRef.current = null;

      setIsFocused(false);
    });
  }, []);

  useEffect(() => {
    function onBodyClick(event: MouseEvent): void {
      const target = event.currentTarget;

      // According to the DOM API, EventTarget does not extend HTMLElement  In
      // fact, it is the other way around. HTMLElement extends Element, which
      // extends Node, which extends EventTarget. See:
      // https://github.com/microsoft/TypeScript/issues/29540

      setIsFocused(
        target instanceof HTMLElement && target.contains(elRef.current)
      );
    }

    document.addEventListener('click', onBodyClick);

    return () => {
      document.removeEventListener('click', onBodyClick);
    };
  }, []);

  return [isFocused, onFocus, onBlur];
}
