import React, { ReactElement, useCallback, useEffect, useMemo } from 'react';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';
import { useDispatch, useSelector } from 'react-redux';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import LibraryHubListItem from 'app/js/v4/features/libraryManagement/components/LibraryHubListItem';
import libraryManagementSlice from 'app/js/v4/features/libraryManagement/libraryManagementSlice';
import createLibraryHubsSelector from 'app/js/v4/features/libraryManagement/selectors/createLibraryHubsSelector';
import IconReload from 'app/glyphs/reload-560.svg';
import styles from './LibraryHubList.css';

interface LibraryHubListProps {
  sourceURI: string;
}

export default function LibraryHubList({
  sourceURI,
}: LibraryHubListProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();

  const libraryHubs = useSelector(createLibraryHubsSelector(sourceURI));
  const options = useMemo(
    () => libraryHubs?.options ?? [],
    [libraryHubs?.options]
  );

  //
  // Callbacks

  const onDragEnd = useCallback(
    (result) => {
      const fromIndex = options.findIndex(
        ({ identifier }) => identifier === result.draggableId
      );
      const toIndex = result.destination.index;

      const newOptions = Array.from(options);
      const [option] = newOptions.splice(fromIndex, 1);
      newOptions.splice(toIndex, 0, option);

      scheduleImmediateUpdate(() =>
        dispatch(
          libraryManagementSlice.actions.reorderRequested({
            server,
            sourceURI,
            options: newOptions,
            fromIndex,
            toIndex,
          })
        )
      );
    },
    [dispatch, options, server, sourceURI]
  );

  const onRemoveItem = useCallback(
    (index) => {
      const newOptions = Array.from(options);
      const [removed] = newOptions.splice(index, 1);

      dispatch(
        libraryManagementSlice.actions.removeRequested({
          server,
          sourceURI,
          options: newOptions,
          removed,
          index,
        })
      );
    },
    [dispatch, options, server, sourceURI]
  );

  const onResetPress = useCallback(() => {
    dispatch(
      libraryManagementSlice.actions.resetRequested({
        server,
        sourceURI,
      })
    );
  }, [dispatch, server, sourceURI]);

  //
  // Effects

  useEffect(() => {
    dispatch(
      libraryManagementSlice.actions.hubsFetchRequested({
        server,
        sourceURI,
      })
    );
  }, [dispatch, server, sourceURI]);

  return (
    <div>
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId={sourceURI}>
          {(provided, snapshot) => (
            <div ref={provided.innerRef} {...provided.droppableProps}>
              {options.map((hubOptions, index) => (
                <LibraryHubListItem
                  key={hubOptions.identifier}
                  index={index}
                  isDragging={snapshot.isDraggingOver}
                  options={hubOptions}
                  sourceURI={sourceURI}
                  onRemoveItem={onRemoveItem}
                />
              ))}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
      {options.length ? (
        <div className={styles.footer}>
          <div className={styles.info}>
            {t(
              'Sources need to be pinned to the apps sidebar for selected recommendations to display on the Home screen'
            )}
          </div>
          <div className={styles.reset}>
            <Link className={styles.action} onPress={onResetPress}>
              <IconReload className={styles.resetIcon} />
              {t('Reset Customization')}
            </Link>
          </div>
        </div>
      ) : null}
    </div>
  );
}
