import React, { ReactElement } from 'react';
import t from 'app/js/utils/t';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import Kind from 'app/js/v4/core/ui/types/Kind';
import Size from 'app/js/v4/core/ui/types/Size';
import DVRDeviceImage from 'app/js/v4/features/dvrManagement/components/setup/DVRDeviceImage';
import { MediaGrabberDevice } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import styles from './AdditionalDevice.css';

interface AdditionalDeviceProps {
  device: MediaGrabberDevice;
  onSetupPress(): void;
}

export default function AdditionalDevice({
  device,
  onSetupPress,
}: AdditionalDeviceProps): ReactElement {
  const { make, model, thumb } = device;

  //
  // Render

  return (
    <div className={styles.container}>
      <DVRDeviceImage height={73} model={model} thumb={thumb} width={130} />
      <div className={styles.info}>
        <div className={styles.make}>{make}</div>
        <div className={styles.model}>{model}</div>
        <Button
          className={styles.button}
          kind={Kind.Primary}
          size={Size.Small}
          onPress={onSetupPress}
        >
          {t('Setup Device')}
        </Button>
      </div>
    </div>
  );
}
