/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { Component } from 'react';
// @ts-expect-error ts-migrate(6133) FIXME: 'SMALL' is declared but its value is never read.
import { LARGE, SMALL } from 'app/js/views/v3/helpers/props/sizes';
import styles from './CheckInput.css';

interface CheckInputProps {
  className?: string;
  inputClassName?: string;
  errorClassName?: string;
  disabledClassName?: string;
  isDisabled?: boolean;
  size?: any; // TODO: PropTypes.oneOf([SMALL, LARGE])
  checkboxText?: React.ReactNode;
  name: string;
  initialValue?: string | boolean | number;
  value?: string | boolean | number;
  uncheckedValue?: string | boolean | number;
  checkedValue?: string | boolean | number;
  hasError?: boolean;
  showErrors?: boolean;
  errors?: string[];
  validations?: ((...args: any[]) => any)[];
  onChange: (...args: any[]) => any;
}

class CheckInput extends Component<CheckInputProps> {
  static defaultProps = {
    className: styles.label,
    inputClassName: styles.input,
    errorClassName: styles.hasError,
    disabledClassName: styles.isDisabled,
    isDisabled: false,
    size: LARGE,
    uncheckedValue: false,
    checkedValue: true,
    value: false,
    hasError: false,
  };

  // eslint-disable-next-line react/sort-comp
  _checkbox: any;

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this._checkbox = null;
  }

  componentDidMount() {
    this.setIndeterminate();
  }

  componentDidUpdate() {
    this.setIndeterminate();
  }

  //
  // Control

  setIndeterminate() {
    if (!this._checkbox) {
      return;
    }

    const { value, uncheckedValue, checkedValue } = this.props;

    this._checkbox.indeterminate =
      value !== uncheckedValue && value !== checkedValue;
  }

  setRef = (ref: any) => {
    this._checkbox = ref;
  };

  //
  // Listeners

  onChange = (event: any) => {
    const checked = event.target.checked;
    const newValue = checked
      ? this.props.checkedValue
      : this.props.uncheckedValue;

    if (this.props.value !== newValue) {
      this.props.onChange(newValue);
    }
  };

  //
  // Render

  render() {
    const {
      className,
      inputClassName,
      errorClassName,
      disabledClassName,
      isDisabled,
      size,
      checkboxText,
      name,
      initialValue,
      value,
      checkedValue,
      uncheckedValue,
      showErrors,
      hasError,
      errors,
      validations,
      ...otherProps
    } = this.props;

    return (
      <label
        className={classNames(
          className,
          styles[size],
          isDisabled && disabledClassName,
          hasError && errorClassName
        )}
        htmlFor={name}
      >
        <input
          {...otherProps}
          ref={this.setRef}
          checked={value === checkedValue}
          className={inputClassName}
          disabled={isDisabled}
          id={name}
          type="checkbox"
          onChange={this.onChange}
        />
        {checkboxText}
      </label>
    );
  }
}

export default CheckInput;
