import commands from 'app/js/commands/commands';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import backgroundProcessingStates from 'app/js/utils/constants/convert/backgroundProcessingStates';
import getSeasonEpisodeIndex from 'app/js/utils/media/getSeasonEpisodeIndex';
import t from 'app/js/utils/t';
import MediaPosterImage from 'app/js/views/images/MediaPosterImage';
import ConversionStatusView from 'app/js/views/lists/activity/convert/ConversionStatusView';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/lists... Remove this comment to see the full error message
import tpl from 'app/templates/lists/library/convert/VersionGeneratorMetadataListItem.tpl';

const VersionGeneratorMetadataListItem = Marionette.Layout.extend({
  //
  // Properties
  tagName: 'li',
  className: 'striped-list-item version-generator-metadata-list-item',
  template: tpl,
  ui: {
    poster: '.media-poster',
  },
  regions: {
    statusRegion: '.conversion-status-region',
  },
  events: {
    'click .enable-btn': 'onEnableClick',
    'click .retry-btn': 'onRetryClick',
  },
  modelEvents: {
    'change:processingState': 'onProcessingStateChange',
  },
  //
  // Init
  initialize: function (options: any) {
    this.generator = options.generator;
    this.transcodeJobs = options.transcodeJobs;
    this.pausedSetting = options.pausedSetting;
    this.type = this.model.get('type');
    this.image = new MediaPosterImage({
      model: this.model,
      type: 'movie',
      height: 60,
      modelAttribute: this.type === 'episode' ? 'grandparentThumb' : 'thumb',
    });
  },
  //
  // Bindings
  bindings: {
    '.generator-metadata-title': {
      modelAttribute: 'title',
      formatter: function (title: any) {
        return (
          (this as any).model.get('grandparentTitle') ||
          (this as any).model.get('parentTitle') ||
          title
        );
      },
    },
    '.generator-metadata-episode-index': {
      modelAttribute: 'index',
      autohide: true,
      formatter: function (index: any) {
        if ((this as any).type === 'episode') {
          return getSeasonEpisodeIndex({
            seasonIndex: (this as any).model.get('parentIndex'),
            episodeIndex: index,
            date: (this as any).model.get('originallyAvailableAt'),
            noLeadingZero: true,
            separator: '  ',
          });
        }

        return;
      },
    },
    '.generator-metadata-year': {
      modelAttribute: 'year',
      autohide: true,
      formatter: function (year: any) {
        if ((this as any).type === 'movie') {
          return year;
        }
      },
    },
    '.conversion-status-failed-message': {
      modelAttribute: 'processingStateContext',
      formatter: function (processingStateContext: any) {
        return (this as any).getErrorMessage(processingStateContext);
      },
    },
  },
  //
  // Control
  getErrorMessage: function (processingStateContext: any) {
    const contexts = backgroundProcessingStates.contexts;
    // Provide some friendly messages for common errors.
    switch (processingStateContext) {
      case contexts.FILE_UNAVAILABLE:
        return t('File unavailable');
      case contexts.METADATA_ERROR:
        return t('Invalid metadata');
      case contexts.PROFILE_ERROR:
        return t('Invalid profile');
      case contexts.ANALYSIS_ERROR:
        return t('Unable to analyze');
    }
    return t('Unable to convert');
  },
  //
  // Listeners
  onRender: function () {
    this.image.setElement(this.ui.poster);
    this.statusRegion.show(
      new ConversionStatusView({
        model: this.model,
        generator: this.generator,
        transcodeJobs: this.transcodeJobs,
        pausedSetting: this.pausedSetting,
        initialState: this.model.get('processingState'),
        initialErrorMessage: this.getErrorMessage(
          this.model.get('processingStateContext')
        ),
      })
    );
  },
  onClose: function () {
    this.image.close();
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'model' is declared but its value is never read.
  onProcessingStateChange: function (model: any, processingState: any) {
    if (this.statusRegion.currentView) {
      this.statusRegion.currentView.enterState(processingState);
    }
  },
  onEnableClick: function (event: any) {
    event.preventDefault();
    commands.execute('enableVersionGenerator', {
      generator: this.model.generator,
      ratingKey: this.model.id,
    });
  },
  onRetryClick: function (event: any) {
    event.preventDefault();
    commands.execute('reprocessVersionGenerator', {
      generator: this.model.generator,
      ratingKey: this.model.id,
    });
  },
});
export default VersionGeneratorMetadataListItem;
