/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import BaseModel from 'app/js/models/BaseModel';
import parser from 'app/js/utils/parser';

const ServerAccountModel = BaseModel.extend({
  //
  // Properties

  idAttribute: 'key',

  whitelistedAttrs: [
    'key',
    'name',
    'defaultAudioLanguage',
    'autoSelectAudio',
    'defaultSubtitleLanguage',
    'subtitleMode',
  ],

  url: function () {
    return this.get('key');
  },

  //
  // Control

  convert: function (xml: any) {
    let json = _.isObject(xml) ? xml : parser.parseXML(xml);
    if (json.Account) {
      json = json.Account;
    }

    parser.parseBoolean(json, 'autoSelectAudio');

    return json;
  },

  parse: function (response: any) {
    let json = this.convert(response);

    json = _.pick(json, this.whitelistedAttrs);

    return json;
  },
});

export default ServerAccountModel;
