import $ from 'jquery';
import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import {
  checkForServerUpdate,
  dismissServerUpdate,
  downloadServerUpdate,
  fetchServerUpdateStatus,
  installServerUpdate,
} from 'app/js/stores/actions/serverUpdateActions';
import * as serverUpdateStates from 'app/js/utils/constants/serverUpdateStates';
import dispatcher from 'app/js/utils/dispatcher';
import shortVersion from 'app/js/utils/string/shortVersion';
import t from 'app/js/utils/t';
import ReleaseNotesModal from 'app/js/views/modals/ReleaseNotesModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setti... Remove this comment to see the full error message
import tpl from 'app/templates/settings/server/ServerSettingsUpdateView.tpl';

const viewStates = {
  ...serverUpdateStates,
  CHECKING: 'checking',
};
const ServerSettingsUpdateView = Marionette.ItemView.extend({
  //
  // Properties
  tagName: 'span',
  className: 'check-for-updates-container',
  template: tpl,
  ui: {
    checkForUpdatesBtn: '.check-for-updates-btn',
    downloadUpdatesContainer: '.downloading-updates-container',
    downloadProgressContainer: '.downloading-progress-container',
    downloadProgressLabel: '.downloading-progress-label',
    updateProgressLabel: '.downloading-updates-label',
    updateErrorMessage: '.update-error-message',
    installManualLink: '.install-manual-link',
  },
  modelEvents: {
    'change:isChecking': 'onIsCheckingChange',
    'change:state': 'onStateChange',
    'change:progress': 'onProgressChange',
  },
  events: {
    'click .check-for-updates-btn': 'onCheckForUpdatesClick',
    'click .available-updates-btn': 'onDownloadUpdatesClick',
    'click .install-updates-btn': 'onInstallUpdatesClick',
    'click .install-manual-link': 'onInstallManualClick',
    'click .release-notes-btn': 'onInfoClick',
  },
  //
  // Initialize
  initialize: function () {
    adapters.resolve('appStore').dispatch(
      fetchServerUpdateStatus({
        serverEntityID: this.model.server.entityID(),
      })
    );
  },
  //
  // Control
  serializeData: function () {
    return {
      canCheckForUpdate: this.canCheckForUpdate(this.model),
    };
  },
  checkForUpdates: function (options = {}) {
    adapters.resolve('appStore').dispatch(
      checkForServerUpdate({
        serverEntityID: this.model.server.entityID(),
        download: (options as any).download,
        initialState: (options as any).initialState,
      })
    );
  },
  dismissUpdates: function () {
    adapters.resolve('appStore').dispatch(
      dismissServerUpdate({
        serverEntityID: this.model.server.entityID(),
        version: shortVersion(this.model.get('version')),
      })
    );
  },
  downloadUpdates: function () {
    adapters
      .resolve('appStore')
      .dispatch(
        downloadServerUpdate({ serverEntityID: this.model.server.entityID() })
      );
  },
  installUpdates: function () {
    adapters
      .resolve('appStore')
      .dispatch(
        installServerUpdate({ serverEntityID: this.model.server.entityID() })
      );
  },
  waitForInstall: function () {
    this.onWaitingForInstallCheck();
    // When the server announces it's going to restart, we need to be
    // notified when it becomes available again.
    this.listenTo(
      this.model.server,
      'change:isConnected',
      this.onServerConnectedChange
    );
  },
  setCurrentViewState: function (viewState: any) {
    this.$el.removeClass(this.currentViewState);
    this.currentViewState = viewState;
    this.$el.addClass(this.currentViewState);
  },
  showUpdateState: function () {
    const state = this.model.get('state');
    if (this.model.previous('state') === serverUpdateStates.INSTALLING) {
      this.stopListening(
        this.model.server,
        'change:isConnected',
        this.onServerConnectedChange
      );
    }
    // Older media servers will use the available state instead of notify
    // if a release is available but the server cannot install it.
    let viewState = state;
    if (
      !this.model.get('canInstall') &&
      state === serverUpdateStates.AVAILABLE
    ) {
      viewState = viewStates.NOTIFY;
    } else if (this.model.get('isChecking')) {
      viewState = viewStates.CHECKING;
    }
    this.setCurrentViewState(viewState);
    const hasReleaseNotes = !!(
      this.model.get('added') || this.model.get('fixed')
    );
    this.$el.toggleClass('has-release-notes', hasReleaseNotes);
    if (viewState === viewStates.INSTALLING) {
      this.waitForInstall();
    } else if (
      viewState === viewStates.ERROR ||
      viewState === viewStates.NOTIFY
    ) {
      let errorMessage;
      if (this.model.get('error')) {
        // If the server provided an error message, display only that
        // message.
        errorMessage = this.model.get('error');
        this.ui.installManualLink.addClass('hidden');
      } else if (!this.model.get('canInstall')) {
        // If the server does not support installing updates automatically,
        // show a friendly message and a link to download the update
        // manually.
        errorMessage = t('An update is available.');
        this.ui.installManualLink.removeClass('hidden');
      } else {
        // Otherwise, show a generic error and a link to download the update
        // manually.
        errorMessage = t('Something went wrong.');
        this.ui.installManualLink.removeClass('hidden');
      }
      // Use the update's downloadURL if available, otherwise use the
      // generic download page.
      const appModel = adapters.resolve('appModel');
      const cloudServer = appModel.get('user').server;
      const url = this.model.get('downloadURL') || cloudServer.get('appsUrl');
      this.ui.installManualLink.attr('href', url);
      this.ui.updateErrorMessage.text(errorMessage);
    }
  },
  canCheckForUpdate: function (model: any) {
    const state = model.get('state');
    const isChecking = model.get('isChecking');
    return (
      !isChecking &&
      (state === serverUpdateStates.DONE ||
        state === serverUpdateStates.ERROR ||
        state === serverUpdateStates.NONE)
    );
  },
  onViewStateChange: function (model: any) {
    if (this.isRendered) {
      this.showUpdateState();
      this.ui.checkForUpdatesBtn.toggleClass(
        'active',
        !this.canCheckForUpdate(model)
      );
    }
  },
  //
  // Listeners
  onRender: function () {
    this.showUpdateState();
  },
  onIsCheckingChange: function (model: any) {
    this.onViewStateChange(model);
  },
  onStateChange: function (model: any) {
    this.onViewStateChange(model);
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'model' is declared but its value is never read.
  onProgressChange: function (model: any, progress: any) {
    if (_.isNumber(progress)) {
      this.ui.downloadUpdatesContainer.css({
        backgroundSize: progress + '% 100%',
      });
      this.ui.downloadProgressContainer.removeClass('hidden');
      this.ui.downloadProgressLabel.text(progress);
    } else {
      this.ui.downloadProgressContainer.addClass('hidden');
    }
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'server' is declared but its value is never read.
  onServerConnectedChange: function (server: any, isConnected: any) {
    if (isConnected) {
      this.checkForUpdates();
    } else if (this._waitingTimeoutID) {
      this._waitingTimeoutID = window.clearTimeout(this._waitingTimeoutID);
    }
  },
  onWaitingForInstallCheck: function () {
    const server = this.model.server;
    if (
      server.get('isConnected') &&
      this.model.get('state') === serverUpdateStates.INSTALLING
    ) {
      const xhrOptions = server.getXHROptions('/updater/check', {
        type: 'PUT',
      });
      const promise = $.ajax(xhrOptions).promise();
      promise.always(() => {
        this._waitingTimeoutID = window.setTimeout(
          this.onWaitingForInstallCheck.bind(this),
          2000
        );
      });
    }
  },
  onCheckForUpdatesClick: function (event: any) {
    event.preventDefault();
    if (!this.ui.checkForUpdatesBtn.isDisabled()) {
      this.ui.checkForUpdatesBtn.addClass('active');
      this.checkForUpdates({ download: true });
    }
  },
  onDownloadUpdatesClick: function (event: any) {
    event.preventDefault();
    this.downloadUpdates();
  },
  onInstallUpdatesClick: function (event: any) {
    event.preventDefault();
    this.installUpdates();
  },
  onInstallManualClick: function () {
    this.dismissUpdates();
  },
  onInfoClick: function (event: any) {
    event.preventDefault();
    dispatcher.trigger(
      'show:modal',
      new ReleaseNotesModal({ model: this.model })
    );
  },
});
export default ServerSettingsUpdateView;
