import _ from 'underscore';
import ServerSettingsPane from 'app/js/views/settings/server/ServerSettingsPane';

const ServerSettingsChannelsPane = ServerSettingsPane.extend({
  //
  // Properties

  prefix: 'channel.',

  //
  // Control

  serializeData: function () {
    const settings = ServerSettingsPane.prototype.serializeData.apply(
      this,
      // eslint-disable-next-line prefer-rest-params
      arguments
    );
    const channelSettings = this.options.channelSettings.toJSON();

    _.each(
      channelSettings,
      function (channelSetting) {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        channelSetting.id = this.prefix + channelSetting.id;
      },
      this
    );

    // eslint-disable-next-line prefer-spread
    settings.unshift.apply(settings, channelSettings);

    return settings;
  },

  updateModel: function (key: any, value: any) {
    // If the setting is found in the main collection, update that model.
    const setting = ServerSettingsPane.prototype.updateModel.apply(
      this,
      // eslint-disable-next-line prefer-rest-params
      arguments
    );
    if (setting) {
      return setting;
    }

    // Otherwise, look for one of our special settings.
    const unprefixedKey = key.replace(this.prefix, '');
    const channelSetting = this.options.channelSettings.get(unprefixedKey);
    if (channelSetting) {
      return channelSetting.set('value', value);
    }
  },
});

export default ServerSettingsChannelsPane;
