/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import asyncProps from 'app/js/views/v3/decorators/asyncProps';
import ChartTooltip from './ChartTooltip';
import LegendLabel from './LegendLabel';
import XAxisTick from './XAxisTick';
import styles from './LineChart.css';

function areAllSeriesVisible(series: any) {
  return series.every((points: any) => points.visible);
}

function createTooltip(props: any) {
  return function renderTooltip(tooltipProps: any) {
    return <ChartTooltip {...tooltipProps} {...props} />;
  };
}

function createXAxisTick(props: any) {
  return function renderXAxisTick(tickProps: any) {
    return <XAxisTick {...tickProps} {...props} />;
  };
}

type LineChartProps = {
  className?: string;
  recharts?: any;
  width: number | string;
  height: number | string;
  series: any[];
  data: any[];
  legendSummary?: React.ReactNode;
  xAxisKey: string;
  connectNulls?: boolean;
  lineType: string;
  lineStrokeWidth?: number;
  isAnimationActive?: boolean;
  showTooltipTotal?: boolean;
  chartMargin?: any;
  gridLinesStyles?: any;
  legendHeight?: number;
  tooltipLabelFormatter: (...args: any[]) => any;
  tooltipValueFormatter: (...args: any[]) => any;
  dot?: any | React.ReactElement | ((...args: any[]) => any) | boolean;
  xAxisDomain?: any[];
  xAxisStyle?: any | boolean;
  xAxisPadding?: any;
  xAxisTick?: boolean | ((...args: any[]) => any) | any;
  xAxisTickInterval?: string | number;
  xAxisTickMargin?: number;
  xAxisTickLineStyle?: any | boolean;
  xAxisTickFormatter: (...args: any[]) => any;
  yAxisDomain?: any[];
  yAxisWidth?: number;
  yAxisStyle?: any | boolean;
  yAxisPadding?: any;
  yAxisTick?: boolean | ((...args: any[]) => any) | any;
  yAxisTickMargin?: number;
  yAxisTickLineStyle?: any | boolean;
  yAxisTickFormatter: (...args: any[]) => any;
  onChartReady?: (...args: any[]) => any;
  onLegendPress?: (...args: any[]) => any;
  onTooltipActive?: (...args: any[]) => any;
  onTooltipInactive?: (...args: any[]) => any;
};

type LineChartState = any;

class LineChart extends Component<LineChartProps, LineChartState> {
  static defaultProps = {
    xAxisKey: 'date',
    connectNulls: false,
    lineType: 'monotone',
    lineStrokeWidth: 3,
    isAnimationActive: true,
    chartMargin: {
      top: 15,
      right: 20,
      bottom: 10,
      left: 0,
    },
    gridLinesStyles: {
      horizontal: true,
      vertical: false,
      stroke: 'rgba(255, 255, 255, 0.1)',
    },
    legendHeight: 20,
    dot: false,
    xAxisStyle: false,
    xAxisPadding: {
      left: 0,
      right: 0,
    },
    xAxisTick: {
      fill: 'rgba(255, 255, 255, 0.45)',
      fontSize: '10px',
    },
    xAxisTickMargin: 20,
    xAxisTickLineStyle: {
      fill: 'rgba(255, 255, 255, 0.1)',
      strokeWidth: 5,
    },
    yAxisWidth: 75,
    yAxisStyle: false,
    yAxisPadding: {
      top: 0,
      bottom: 0,
    },
    yAxisTick: {
      fill: 'rgba(255, 255, 255, 0.45)',
      fontSize: '10px',
    },
    yAxisTickMargin: 5,
    yAxisTickLineStyle: false,
  };

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      hoveredDataKey: null,
    };
  }

  // @ts-expect-error ts-migrate(6133) FIXME: 'prevState' is declared but its value is never rea... Remove this comment to see the full error message
  componentDidUpdate(prevProps: LineChartProps, prevState: LineChartState) {
    const { recharts, onChartReady } = this.props;

    if (!prevProps.recharts && recharts && onChartReady) {
      onChartReady();
    }
  }

  //
  // Listeners

  onMouseEnter = (hoveredDataKey: any) => {
    this.setState({ hoveredDataKey });
  };

  onMouseLeave = () => {
    this.setState({ hoveredDataKey: null });
  };

  //
  // Render

  renderLegend = () => {
    const { series, legendSummary, onLegendPress } = this.props;

    const { hoveredDataKey } = this.state;

    return (
      <div className={styles.legendContainer}>
        <div className={styles.legendLabels}>
          {series.map((points) => {
            const isTranslucent =
              !points.visible ||
              (hoveredDataKey &&
                hoveredDataKey !== points.dataKey &&
                !points.selected);

            return (
              <LegendLabel
                key={points.dataKey}
                className={
                  isTranslucent
                    ? styles.translucentLegendLabel
                    : styles.legendLabel
                }
                dataKey={points.dataKey}
                labelColor={points.color}
                title={points.title}
                onMouseEnter={this.onMouseEnter}
                onMouseLeave={this.onMouseLeave}
                onPress={onLegendPress}
              />
            );
          })}
        </div>

        {legendSummary ? (
          <div className={styles.legendSummary}>{legendSummary}</div>
        ) : null}
      </div>
    );
  };

  render() {
    const {
      className,
      recharts,
      width,
      height,
      series,
      data,
      xAxisKey,
      connectNulls,
      lineType,
      lineStrokeWidth,
      isAnimationActive,
      showTooltipTotal,
      chartMargin,
      gridLinesStyles,
      legendHeight,
      tooltipLabelFormatter,
      tooltipValueFormatter,
      dot,
      xAxisDomain,
      xAxisStyle,
      xAxisPadding,
      xAxisTick,
      xAxisTickInterval,
      xAxisTickMargin,
      xAxisTickLineStyle,
      xAxisTickFormatter,
      yAxisDomain,
      yAxisWidth,
      yAxisStyle,
      yAxisPadding,
      yAxisTick,
      yAxisTickMargin,
      yAxisTickLineStyle,
      yAxisTickFormatter,
      onTooltipActive,
      onTooltipInactive,
    } = this.props;

    const { hoveredDataKey } = this.state;

    if (!recharts) {
      return null;
    }

    const {
      CartesianGrid,
      Legend,
      LineChart: RechartsLineChart,
      Line,
      ResponsiveContainer,
      Tooltip,
      XAxis,
      YAxis,
    } = recharts;

    // SVG path rendering order is important. The last to be rendered will
    // overlay previous paths, and so we want to ensure that the 'primary' path
    // is rendered last.
    const reorderedSeries = [...series].reverse();

    return (
      <ResponsiveContainer className={className} height={height} width={width}>
        <RechartsLineChart data={data} margin={chartMargin}>
          <CartesianGrid {...gridLinesStyles} />

          <XAxis
            axisLine={xAxisStyle}
            dataKey={xAxisKey}
            domain={xAxisDomain}
            interval={xAxisTickInterval}
            minTickGap={0}
            padding={xAxisPadding}
            tick={createXAxisTick({
              ...xAxisTick,
              margin: xAxisTickMargin,
              tickFormatter: xAxisTickFormatter,
            })}
            tickLine={xAxisTickLineStyle}
          />

          <YAxis
            axisLine={yAxisStyle}
            domain={yAxisDomain}
            padding={yAxisPadding}
            tick={yAxisTick}
            tickCount={6}
            tickFormatter={yAxisTickFormatter}
            tickLine={yAxisTickLineStyle}
            tickMargin={yAxisTickMargin}
            width={yAxisWidth}
          />

          <Tooltip
            content={createTooltip({
              series,
              showTotal: showTooltipTotal,
              labelFormatter: tooltipLabelFormatter,
              valueFormatter: tooltipValueFormatter,
              onActive: onTooltipActive,
              onInactive: onTooltipInactive,
            })}
            cursor={false}
            isAnimationActive={false}
          />

          {reorderedSeries.map((points) => (
            /* eslint-disable react/jsx-no-bind */
            <Line
              key={points.dataKey}
              className={
                hoveredDataKey &&
                hoveredDataKey !== points.dataKey &&
                areAllSeriesVisible(series)
                  ? styles.translucentLine
                  : styles.line
              }
              connectNulls={connectNulls}
              dataKey={points.dataKey}
              dot={dot}
              isAnimationActive={isAnimationActive}
              stroke={points.color}
              strokeWidth={lineStrokeWidth}
              type={lineType}
              onMouseEnter={this.onMouseEnter.bind(null, points.dataKey)}
              onMouseLeave={this.onMouseLeave}
            />
            /* eslint-enable react/jsx-no-bind */
          ))}

          <Legend
            content={this.renderLegend}
            height={legendHeight}
            wrapperStyle={{
              paddingLeft: `${yAxisWidth}px`,
            }}
          />
        </RechartsLineChart>
      </ResponsiveContainer>
    );
  }
}

export default asyncProps(LineChart, {
  // @ts-expect-error ts-migrate(7016) FIXME: Could not find a declaration file for module 'rech... Remove this comment to see the full error message
  recharts: () => import('recharts'),
});
