/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { resolve } from 'app/js/adapters/adapters';
import MediaServerEventConnectionRetryManager from 'app/js/managers/MediaServerEventConnectionRetryManager';
import ServerEventManager from 'app/js/managers/ServerEventManager';
import {
  clearLog,
  loadLogFile,
} from 'app/js/stores/actions/ui/serverLogPageActions';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import MediaServerLogEventConnection from 'app/js/utils/pubsub/MediaServerLogEventConnection';
import ServerLogPage from './ServerLogPage';

function createMapStateToProps() {
  return createSelector(createUnstableServerSelector(), (server) => {
    return {
      title: server.friendlyName,
    };
  });
}

const mapDispatchToProps = {
  dispatchClearLog: clearLog,
  dispatchLoadLogFile: loadLogFile,
};

type UnconnectedServerLogPageConnectorProps = {
  serverEntityID: string;
  dispatchClearLog: (...args: any[]) => any;
  dispatchLoadLogFile: (...args: any[]) => any;
};

export class UnconnectedServerLogPageConnector extends Component<UnconnectedServerLogPageConnectorProps> {
  // eslint-disable-next-line react/sort-comp
  _isServerEventManagerStopped: any;
  _serverEventManager: any;

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    const appModel = resolve('appModel');

    this._serverEventManager = new ServerEventManager({
      name: 'MediaServerLogEventManager',
      appModel,
      isValidServer: (server: any) =>
        server.entityID() === props.serverEntityID,
      supportsLongPolling: false,
      ServerEventConnection: MediaServerLogEventConnection,
      ServerRetryManager: MediaServerEventConnectionRetryManager,
    });
  }

  componentDidMount() {
    this.props.dispatchClearLog();

    this._serverEventManager.start();
    const appModel = resolve('appModel');

    const servers = appModel.get('servers');

    servers.forEach((server: any) => {
      const isResolved = server.get('isResolved');
      if (isResolved) {
        this._serverEventManager.onServerResolvedChange(server, isResolved);
      }
    });
  }

  componentWillUnmount() {
    this._stopServerEventManager();
  }

  //
  // Control

  _stopServerEventManager() {
    if (!this._isServerEventManagerStopped) {
      this._serverEventManager.stop();

      // Clear out old log lines to free up memory
      this.props.dispatchClearLog();

      this._isServerEventManagerStopped = true;
    }
  }

  //
  // Listeners

  onDrop = (...args: any[]) => {
    this._stopServerEventManager();
    this.props.dispatchLoadLogFile(...args);
  };

  //
  // Render

  render() {
    // @ts-expect-error ts-migrate(2739) FIXME: Type '{ serverEntityID: string; dispatchClearLog: ... Remove this comment to see the full error message
    return <ServerLogPage onDrop={this.onDrop} {...this.props} />;
  }
}

export default connect(
  createMapStateToProps,
  mapDispatchToProps
)(UnconnectedServerLogPageConnector);
