/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  createPIN,
  signInThenClaimServer,
} from 'app/js/stores/actions/server/claimServerActions';
import { readStoredPIN } from 'app/js/stores/actions/user/authCodeExchangeActions';
import selectAuthCodeExchange from 'app/js/stores/selectors/user/selectAuthCodeExchange';
import RedirectToConnector from 'app/js/views/v3/components/pages/serverSettings/RedirectToConnector';
import ServiceAuthUrlConnector from 'app/js/views/v3/components/pages/serverSettings/ServiceAuthUrlConnector';
import ClaimServerButton from './ClaimServerButton';

const mapDispatchToProps = {
  dispatchReadStoredPIN: readStoredPIN,
  dispatchCreatePIN: createPIN,
  dispatchSignInThenClaimServer: signInThenClaimServer,
};

type ClaimServerSignedOutLaunchButtonConnectorProps = {
  serverEntityID: string;
  isBusy: boolean;
  isDisabled: boolean;
  pinID?: number;
  code?: string;
  authToken?: string;
  dispatchReadStoredPIN: (...args: any[]) => any;
  dispatchCreatePIN: (...args: any[]) => any;
  dispatchSignInThenClaimServer: (...args: any[]) => any;
};

class ClaimServerSignedOutLaunchButtonConnector extends Component<ClaimServerSignedOutLaunchButtonConnectorProps> {
  //
  // Lifecycle

  componentDidMount() {
    this.props.dispatchReadStoredPIN({ shouldCreatePIN: false });
  }

  componentDidUpdate(
    prevProps: ClaimServerSignedOutLaunchButtonConnectorProps
  ) {
    const didReceiveAuthToken =
      this.props.authToken !== prevProps.authToken && this.props.authToken;

    if (didReceiveAuthToken) {
      this.props.dispatchSignInThenClaimServer({
        serverEntityID: this.props.serverEntityID,
        authToken: this.props.authToken,
      });
    }
  }

  //
  // Listeners

  onClaimServerPress = () => {
    this.props.dispatchCreatePIN();
  };

  //
  // Render

  render() {
    const { isBusy, isDisabled, pinID, code } = this.props;

    if (pinID) {
      return (
        <ServiceAuthUrlConnector
          code={code}
          component={RedirectToConnector}
          pinID={pinID}
        />
      );
    }

    return (
      <ClaimServerButton
        isDisabled={isDisabled}
        isSpinning={isBusy}
        onPress={this.onClaimServerPress}
      />
    );
  }
}

export default connect(
  selectAuthCodeExchange,
  mapDispatchToProps
)(ClaimServerSignedOutLaunchButtonConnector);
