import React, {
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import usePreviousValueForAtMost from 'app/js/v4/core/ui/hooks/usePreviousValueForAtMost';
import ServerDashboardPageHeader from 'app/js/v4/features/dashboard/components/ServerDashboardPageHeader';
import ServerDashboardFilterMenuButton from 'app/js/v4/features/dashboard/components/menuButtons/ServerDashboardFilterMenuButton';
import dashboardBandwidthSlice from 'app/js/v4/features/dashboard/dashboardBandwidthSlice';
import { BandwidthSourceType } from 'app/js/v4/features/dashboard/dashboardBandwidthTypes';
import selectDashboardBandwidth from 'app/js/v4/features/dashboard/selectors/selectDashboardBandwidth';
import getBandwidthSourceTypeLabel from 'app/js/v4/features/dashboard/utils/getBandwidthSourceTypeLabel';
import {
  getTimePeriodAfter,
  getTimePeriodLabel,
  getTimePeriodTimespan,
  TimePeriod,
} from 'app/js/v4/utils/timePeriods';
import BandwidthGraph from './BandwidthGraph';
import styles from './Bandwidth.css';

const REAL_TIME_DATA_FETCH_MIN_INTERVAL = 1000;

export default function Bandwidth(): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const realtimeTimeoutIDRef = useRef<number | null>(null);

  const [activeBandwidthSourceType, setActiveBandwidthSourceType] = useState(
    BandwidthSourceType.All
  );

  const bandwidthSourceTypes = useMemo(() => {
    return [
      BandwidthSourceType.All,
      BandwidthSourceType.Remote,
      BandwidthSourceType.Local,
    ];
  }, []);

  const [activeTimePeriod, setActiveTimePeriod] = useState(TimePeriod.RealTime);
  const [lockedTimePeriod, setLockedTimePeriod] = useState(activeTimePeriod);

  const timePeriods = useMemo(() => {
    return [
      TimePeriod.RealTime,
      TimePeriod.TwelveHours,
      TimePeriod.Day,
      TimePeriod.Week,
      TimePeriod.Month,
      TimePeriod.NinetyDays,
      TimePeriod.Year,
      TimePeriod.AllTime,
    ];
  }, []);

  const {
    statistics,
    isPending,
    isPopulated,
    error,
    requestTimestampMilliseconds,
    responseTimestampMilliseconds,
  } = useSelector(selectDashboardBandwidth);

  const isDelayedPending = usePreviousValueForAtMost(isPending, true);

  //
  // Callbacks

  const onFetch = useCallback(() => {
    dispatch(
      dashboardBandwidthSlice.actions.bandwidthFetchRequested({
        server,
        timespan: getTimePeriodTimespan(activeTimePeriod),
        after: getTimePeriodAfter(activeTimePeriod),
      })
    );
  }, [activeTimePeriod, dispatch, server]);

  const onAbort = useCallback(() => {
    dispatch(dashboardBandwidthSlice.actions.bandwidthAbortRequested());
  }, [dispatch]);

  const onBandwidthSourceTypeSelect = useCallback(
    (bandwidthSourceType: BandwidthSourceType) => {
      setActiveBandwidthSourceType(bandwidthSourceType);
    },
    []
  );

  const onTimePeriodSelect = useCallback((timePeriod: TimePeriod) => {
    setActiveTimePeriod(timePeriod);
  }, []);

  const onGraphBandwidthSourceTypeSelect = useCallback(
    (bandwidthSourceType: BandwidthSourceType) => {
      const nextBandwidthSourceType =
        bandwidthSourceType === activeBandwidthSourceType
          ? BandwidthSourceType.All
          : bandwidthSourceType;

      setActiveBandwidthSourceType(nextBandwidthSourceType);
    },
    [activeBandwidthSourceType]
  );

  const onGraphTooltipActive = useCallback(() => {
    if (activeTimePeriod === TimePeriod.RealTime) {
      if (realtimeTimeoutIDRef.current) {
        window.clearTimeout(realtimeTimeoutIDRef.current);
      }
      onAbort();
    }
  }, [activeTimePeriod, onAbort]);

  const onGraphTooltipInactive = useCallback(() => {
    if (activeTimePeriod === TimePeriod.RealTime) {
      onFetch();
    }
  }, [activeTimePeriod, onFetch]);

  //
  // Effects

  useEffect(() => {
    onFetch();

    return onAbort;
  }, [onFetch, onAbort]);

  useEffect(() => {
    // If some real time bandwidth statistics have just been fetched, then fire
    // off the next request with a delay.
    if (
      activeTimePeriod === TimePeriod.RealTime &&
      requestTimestampMilliseconds
    ) {
      const requestLatency = Date.now() - requestTimestampMilliseconds;
      const delay = Math.max(
        REAL_TIME_DATA_FETCH_MIN_INTERVAL - requestLatency,
        0
      );

      realtimeTimeoutIDRef.current = window.setTimeout(onFetch, delay);
    }

    return () => {
      if (realtimeTimeoutIDRef.current) {
        window.clearTimeout(realtimeTimeoutIDRef.current);
      }
    };
  }, [requestTimestampMilliseconds, onFetch, activeTimePeriod]);

  useEffect(() => {
    // Do not pass the new active time period to the graph with statistics for
    // the previous time period. Once the statistics are updated, then update
    // the time period that is passed to the graph.

    setLockedTimePeriod(activeTimePeriod);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statistics]);

  useEffect(() => {
    return () => {
      dispatch(dashboardBandwidthSlice.actions.reset());
    };
  }, [dispatch]);

  //
  // Render

  return (
    <div className={styles.container}>
      <ServerDashboardPageHeader title={t('Bandwidth')}>
        <div>
          <ServerDashboardFilterMenuButton
            activeValue={activeBandwidthSourceType}
            formatter={getBandwidthSourceTypeLabel}
            values={bandwidthSourceTypes}
            onSelect={onBandwidthSourceTypeSelect}
          />

          <ServerDashboardFilterMenuButton
            activeValue={activeTimePeriod}
            formatter={getTimePeriodLabel}
            values={timePeriods}
            onSelect={onTimePeriodSelect}
          />
        </div>
      </ServerDashboardPageHeader>

      <div className={styles.graph}>
        <BandwidthGraph
          activeBandwidthSourceType={activeBandwidthSourceType}
          activeTimePeriod={lockedTimePeriod}
          errorMessage={error ? t('Unable to load statistics') : undefined}
          isPending={isDelayedPending}
          isPopulated={isPopulated}
          responseTimestampMilliseconds={responseTimestampMilliseconds}
          statistics={statistics}
          onGraphBandwidthSourceTypeSelect={onGraphBandwidthSourceTypeSelect}
          onGraphTooltipActive={onGraphTooltipActive}
          onGraphTooltipInactive={onGraphTooltipInactive}
        />
      </div>
    </div>
  );
}
