/* eslint-plugin-disable @typescript-eslint */

import durationOf, {
  DAYS_PER_MONTH,
  DAYS_PER_YEAR,
} from 'app/js/utils/dateTime/durationOf';
import t from 'app/js/utils/t';

function formatDays(milliseconds: any) {
  if (milliseconds == null) {
    return '';
  }
  const duration = durationOf(milliseconds);
  const years = (duration as any).years * DAYS_PER_YEAR;
  const months = (duration as any).months * DAYS_PER_MONTH;
  const days = years + months + (duration as any).days;
  return days !== 1 ? t('{1} days', days) : t('{1} day', days);
}
export default formatDays;
