/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import PosterCardImg from 'app/js/views/v3/components/core/images/PosterCardImg';
import MetadataPosterCardIcon from './MetadataPosterCardIcon';

type MetadataPosterCardImgProps = {
  serverEntityID?: string;
  providerEntityID?: string;
  itemServerEntityID?: string;
  itemProviderEntityID?: string;
  type?: string;
  icon?: string;
  width: number;
  height: number;
  url?: string;
  src?: string;
  backgroundSize?: string;
  isOffscreen?: boolean;
  isFolder?: boolean;
  disableCrossFade?: boolean;
  onLoad?: (...args: any[]) => any;
  onError?: (...args: any[]) => any;
};

function MetadataPosterCardImg(props: MetadataPosterCardImgProps) {
  const {
    serverEntityID,
    providerEntityID,
    itemServerEntityID,
    itemProviderEntityID,
    type,
    isOffscreen,
    url,
    src,
    width,
    height,
    icon,
    backgroundSize,
    isFolder,
    disableCrossFade,
    onLoad,
    onError,
  } = props;

  if (isOffscreen) {
    return null;
  }

  if (!url && !src) {
    return (
      <MetadataPosterCardIcon
        height={height}
        icon={icon}
        isFolder={isFolder}
        type={type}
        width={width}
      />
    );
  }

  return (
    <PosterCardImg
      backgroundPosition={type === 'artist' ? 'center top' : 'center'}
      backgroundSize={type === 'game' ? 'contain' : backgroundSize}
      disableCrossFade={disableCrossFade}
      height={height}
      providerEntityID={itemProviderEntityID || providerEntityID}
      serverEntityID={itemServerEntityID || serverEntityID}
      src={src}
      url={url}
      width={width}
      onError={onError}
      onLoad={onLoad}
    />
  );
}

export default MetadataPosterCardImg;
