/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { Component } from 'react';
// @ts-expect-error ts-migrate(6133) FIXME: 'SMALL' is declared but its value is never read.
import { LARGE, SMALL } from 'app/js/views/v3/helpers/props/sizes';
import styles from './TextArea.css';

interface TextAreaProps {
  className?: string;
  errorClassName?: string;
  disabledClassName?: string;
  initialValue?: string;
  isDisabled?: boolean;
  size?: any; // TODO: PropTypes.oneOf([SMALL, LARGE])
  name: string;
  hasError?: boolean;
  onChange: (...args: any[]) => any;
  rows?: number;
  value?: string;
}

class TextArea extends Component<TextAreaProps> {
  static defaultProps = {
    className: styles.textArea,
    errorClassName: styles.hasError,
    disabledClassName: styles.isDisabled,
    isDisabled: false,
    size: LARGE,
    hasError: false,
    rows: 2,
  };

  //
  // Listeners

  onChange = (event: any) => {
    this.props.onChange(event.target.value);
  };

  //
  // Render

  render() {
    const {
      className,
      errorClassName,
      disabledClassName,
      initialValue,
      isDisabled,
      size,
      name,
      hasError,
      ...otherProps
    } = this.props;

    return (
      <textarea
        {...otherProps}
        className={classNames(
          className,
          styles[size],
          isDisabled && disabledClassName,
          hasError && errorClassName
        )}
        disabled={isDisabled}
        id={name}
        name={name}
        onChange={this.onChange}
      />
    );
  }
}

export default TextArea;
