/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import { createWebClientSettingsSidebarKey } from 'app/js/utils/settingsSidebarKey';
import Measure from 'app/js/views/v3/components/core/base/Measure';
import MetricsPageTracker from 'app/js/views/v3/components/core/metrics/MetricsPageTracker';
import SidebarContainer from 'app/js/views/v3/components/core/sidebar/SidebarContainer';
import SidebarScroller from 'app/js/views/v3/components/core/sidebar/SidebarScroller';
import ServerSettingsServerMenu from 'app/js/views/v3/components/sidebar/settings/ServerSettingsServerMenu';
import ServerSettingsServerMenuButtonConnector from 'app/js/views/v3/components/sidebar/settings/ServerSettingsServerMenuButtonConnector';
import ServerSettingsSidebarListConnector from 'app/js/views/v3/components/sidebar/settings/ServerSettingsSidebarListConnector';
import getUniqueElementID from 'app/js/views/v3/helpers/getUniqueElementID';
import pageStorage from 'app/js/views/v3/helpers/pageStorage';
import styles from './SettingsSidebar.css';

const SCROLL_TOP_KEY = 'SettingsSidebarScrollTop';

type SettingsSidebarProps = {
  manageableServerEntityIDs: any[];
  serverMenuServerEntityID?: string;
  selectedKey?: string;
  settingsSection?: string;
  settingsGroup?: string;
};

type SettingsSidebarState = {
  isMeasured: boolean;
  scrollTop: number;
  isMenuOpen: boolean;
};

class SettingsSidebar extends Component<
  SettingsSidebarProps,
  SettingsSidebarState
> {
  // eslint-disable-next-line react/sort-comp
  _serverMenuButtonID: any;

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    const scrollTop = pageStorage.get(SCROLL_TOP_KEY);

    this.state = {
      isMeasured: false,
      scrollTop: scrollTop || 0,
      isMenuOpen: false,
    };

    this._serverMenuButtonID = getUniqueElementID();
  }

  //
  // Listeners

  onMeasure = () => {
    this.setState({ isMeasured: true });
  };

  onScrollPositionChange = ({ scrollTop }: any) => {
    this.setState({ scrollTop }, () => {
      // Ideally persisting the scrollTop would live in componentWillUnmount.
      // And it should once all the settings pages are full react.  Until then
      // react does not get a chance to unmount when user navigates between
      // settings pages.  So for now we persist the scrollTop as it changes.

      pageStorage.set(SCROLL_TOP_KEY, scrollTop);
    });
  };

  onMenuButtonPress = () => {
    this.setState({ isMenuOpen: !this.state.isMenuOpen });
  };

  onMenuClose = () => {
    this.setState({ isMenuOpen: false });
  };

  //
  // Render

  render() {
    const {
      manageableServerEntityIDs,
      serverMenuServerEntityID,
      selectedKey,
      settingsSection,
      settingsGroup,
    } = this.props;

    const { scrollTop, isMenuOpen } = this.state;

    // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 0.
    const webClientSettingsSidebarKey = createWebClientSettingsSidebarKey();

    return (
      <MetricsPageTracker
        isDelegated={true}
        page={metricsPages.SETTINGS_SIDEBAR}
      >
        <Measure
          ignorePosition={true}
          ignoreWidth={true}
          onMeasure={this.onMeasure}
        >
          <SidebarContainer>
            {this.state.isMeasured ? (
              // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: Element; smoothScroll: boolean; ... Remove this comment to see the full error message
              <SidebarScroller
                scrollTop={scrollTop}
                smoothScroll={false}
                onScrollPositionChange={this.onScrollPositionChange}
              >
                <div className={styles.sidebarContent}>
                  {serverMenuServerEntityID ? (
                    <>
                      <ServerSettingsServerMenuButtonConnector
                        id={this._serverMenuButtonID}
                        isMenuOpen={isMenuOpen}
                        serverEntityID={serverMenuServerEntityID}
                        onPress={this.onMenuButtonPress}
                      />
                      <ServerSettingsServerMenu
                        isOpen={isMenuOpen}
                        selectedServerEntityID={serverMenuServerEntityID}
                        serverEntityIDs={manageableServerEntityIDs}
                        settingsGroup={settingsGroup}
                        settingsSection={settingsSection}
                        target={this._serverMenuButtonID}
                        onMenuClose={this.onMenuClose}
                      />
                      <ServerSettingsSidebarListConnector
                        selectedKey={selectedKey}
                        serverEntityID={serverMenuServerEntityID}
                      />
                    </>
                  ) : null}
                </div>
              </SidebarScroller>
            ) : null}
          </SidebarContainer>
        </Measure>
      </MetricsPageTracker>
    );
  }
}

export default SettingsSidebar;
