import React, { ReactElement, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import Kind from 'app/js/v4/core/ui/types/Kind';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import Form from 'app/js/views/v3/components/core/forms/Form';
import SelectInputGroup from 'app/js/views/v3/components/core/forms/SelectInputGroup';
import SettingInputGroup from 'app/js/views/v3/components/core/forms/SettingInputGroup';
import styles from './ChannelScanPrefs.css';

export default function ChannelScanPrefs(): ReactElement | null {
  const dispatch = useDispatch();
  const [server] = useSourceContext();

  const { selectedDevice, scanPrefs, scanNetworks } =
    useSelector(selectDVRSetup);

  const { selectedCountry } = useSelector(selectEPGSettings);

  //
  // Callbacks

  const onSubmit = useCallback(
    (formState: any) => {
      const payload = scanPrefs?.length ? { prefs: formState } : formState;

      if (selectedDevice) {
        dispatch(
          dvrSetupSlice.actions.deviceChannelsScanRequested({
            server,
            deviceKey: selectedDevice.key,
            source: selectedDevice.source,
            country: selectedCountry?.code,
            ...payload,
          })
        );
      }
    },
    [dispatch, server, selectedDevice, selectedCountry, scanPrefs]
  );

  //
  // Render

  const title = scanPrefs?.length
    ? t(
        'We need some more information before proceeding with the channel scan.'
      )
    : t('Please select a network before proceeding with the channel scan.');

  const networks = !scanPrefs?.length
    ? scanNetworks.reduce<Record<string, string>[]>(
        (acc, network) => {
          acc.push({ [network.key]: network.title });

          return acc;
        },
        [{ '': t('Select Network') }]
      )
    : undefined;

  return (
    <div className={styles.container}>
      <Form onSubmit={onSubmit}>
        <div className={styles.titleContainer}>
          <div className={styles.title}>{title}</div>
          <Button
            className={styles.scanButton}
            kind={Kind.Primary}
            type="submit"
          >
            {t('Scan Channels')}
          </Button>
        </div>

        {scanPrefs?.map((pref) => {
          const { id, value, enumValues, label } = pref;
          const values = Array.isArray(enumValues)
            ? [
                { label: t('Select {label}', { label }), value: '' },
                ...enumValues,
              ]
            : undefined;

          return (
            <SettingInputGroup
              key={id}
              {...pref}
              enumValues={values}
              value={value == null ? '' : value}
            />
          );
        })}

        {networks ? (
          <SelectInputGroup
            initialValue=""
            // @ts-expect-error ts-migrate(2322) FIXME: Type '{ name: string; labelText: string; initialVa... Remove this comment to see the full error message
            labelText={t('Network')}
            name="network"
            values={networks}
          />
        ) : null}
      </Form>
    </div>
  );
}
