/* eslint-plugin-disable @typescript-eslint */

import React from 'react';

type XAxisTickProps = {
  x: number;
  y: number;
  margin: number;
  fill?: string;
  fontSize?: string;
  index: number;
  visibleTicksCount: number;
  payload: any;
  tickFormatter?: (...args: any[]) => any;
};

function XAxisTick(props: XAxisTickProps) {
  const {
    x,
    y,
    margin,
    fill,
    fontSize,
    index,
    visibleTicksCount,
    payload,
    tickFormatter,
  } = props;

  // Align the start and end labels with the axis ends.
  let textAnchor = 'middle';
  if (index === 0) {
    textAnchor = 'start';
  } else if (index + 1 === visibleTicksCount) {
    textAnchor = 'end';
  }

  const finalLabel = tickFormatter
    ? tickFormatter(payload.value)
    : payload.value;

  return (
    <g transform={`translate(${x},${y})`}>
      <text dy={margin} fill={fill} fontSize={fontSize} textAnchor={textAnchor}>
        {finalLabel}
      </text>
    </g>
  );
}

export default XAxisTick;
