/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import _ from 'underscore';
import * as activityTypes from 'app/js/utils/server/activityTypes';
import createServerActivitiesSelector from './createServerActivitiesSelector';

export default function createServerActivitySelector(type: any) {
  return createSelector(createServerActivitiesSelector(), (activities) =>
    _.find(activities, { type })
  );
}

//
// Shortcuts

export function createServerOptimizeDatabaseActivitySelector() {
  return createServerActivitySelector(activityTypes.OPTIMIZE_DATABASE);
}

export function createServerCleanBundlesActivitySelector() {
  return createServerActivitySelector(activityTypes.CLEAN_BUNDLES);
}

export function createServerLibraryDeleteActivitySelector() {
  return createServerActivitySelector(activityTypes.LIBRARY_DELETE);
}
