/* eslint-plugin-disable @typescript-eslint */

import t from 'app/js/utils/t';

function formatAudioChannels(channels: any) {
  channels = parseInt(channels, 10);

  if (!channels) {
    return '';
  }

  if (channels < 2) {
    return t('Mono');
  } else if (channels < 3) {
    return t('Stereo');
  } else {
    // Note, this approach returns an incorrect value in some situations.
    // See: https://github.com/plexinc/plex-web-client/issues/11833
    return (channels - 1).toString() + '.1';
  }
}

export default formatAudioChannels;
