/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import Menu from 'app/js/views/v3/components/core/menus/Menu';
import ServerSettingsServerMenuItemConnector from 'app/js/views/v3/components/sidebar/settings/ServerSettingsServerMenuItemConnector';
import * as scrollDirections from 'app/js/views/v3/helpers/props/scrollDirections';
import dimensions from 'app/js/views/v3/styles/variables/dimensions';
import styles from './ServerSettingsServerMenu.css';

const MENU_ITEM_HEIGHT = parseInt(dimensions.serverMenuItemHeight, 10);
const MAX_HEIGHT = MENU_ITEM_HEIGHT * 5;

const scrollerProps = {
  className: styles.serverMenuScroller,
  scrollDirection: scrollDirections.VERTICAL,
  autoScroll: true,
};

type ServerSettingsServerMenuProps = {
  target: string;
  serverEntityIDs: any[];
  selectedServerEntityID?: string;
  settingsSection?: string;
  settingsGroup?: string;
  isOpen?: boolean;
  onMenuClose: (...args: any[]) => any;
};

function ServerSettingsServerMenu(props: ServerSettingsServerMenuProps) {
  const {
    target,
    serverEntityIDs,
    selectedServerEntityID,
    settingsSection,
    settingsGroup,
    isOpen,
    onMenuClose,
  } = props;

  return (
    <Menu
      className={styles.serverMenu}
      // @ts-expect-error ts-migrate(2322) FIXME: Type 'boolean | undefined' is not assignable to ty... Remove this comment to see the full error message
      isOpen={isOpen}
      maxHeight={MAX_HEIGHT}
      scrollerProps={scrollerProps}
      target={target}
      onMenuClose={onMenuClose}
    >
      {serverEntityIDs.map((serverEntityID) => {
        return (
          <ServerSettingsServerMenuItemConnector
            key={serverEntityID}
            isSelected={serverEntityID === selectedServerEntityID}
            serverEntityID={serverEntityID}
            settingsGroup={settingsGroup}
            settingsSection={settingsSection}
          />
        );
      })}
    </Menu>
  );
}

export default ServerSettingsServerMenu;
