import classNames from 'classnames';
import React, {
  ComponentType,
  ReactElement,
  ReactNode,
  SVGAttributes,
} from 'react';
import { IconProps } from '@plex/react-chroma-icon';
import RequireOnlyOne from 'app/js/v4/types/RequireOnlyOne';
import Link, { LinkProps } from './Link';
import styles from './IconButton.css';

export type IconButtonProps = LinkProps &
  RequireOnlyOne<
    {
      // Accept children to support IconButton within a TooltipTrigger.
      children?: ReactNode;
      className?: string;
      iconComponent:
        | ComponentType<IconProps>
        | ComponentType<SVGAttributes<SVGElement>>;
      iconClassName?: string;
      activeClassName?: string;
      disabledClassName?: string;
      isDisabled?: boolean;
      isActive?: boolean;
      ariaLabel?: string;
      title?: string;
    },
    'ariaLabel' | 'title'
  >;

export default function IconButton({
  // Accept children to support IconButton within a TooltipTrigger.
  children,
  className = styles.iconButton,
  activeClassName = styles.isActive,
  disabledClassName = styles.isDisabled,
  iconClassName,
  iconComponent: Icon,
  isDisabled = false,
  isActive = false,
  title,
  ariaLabel,
  ...linkProps
}: IconButtonProps): ReactElement {
  const finalAriaLabel = ariaLabel || title;

  return (
    <Link
      {...linkProps}
      aria-label={finalAriaLabel}
      className={classNames(
        className,
        isDisabled && disabledClassName,
        isActive && activeClassName
      )}
      isDisabled={isDisabled}
      title={title}
    >
      <Icon className={iconClassName} />

      {children}
    </Link>
  );
}
