/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import FormGroup from 'app/js/views/v3/components/core/forms/FormGroup';
import FormHelp from 'app/js/views/v3/components/core/forms/FormHelp';
import InputGroupFormLabel from 'app/js/views/v3/components/core/forms/InputGroupFormLabel';
import formInput from 'app/js/views/v3/components/core/forms/formInput';
import NumberInput from './NumberInput';
import styles from './NumberInputGroup.css';

interface NumberInputGroupProps {
  formGroupClassName?: string;
  formLabelClassName?: string;
  inputClassName?: string;
  name: string;
  labelText: React.ReactNode;
  helpText?: string;
  showErrors?: boolean;
  errors: string[];
}

function NumberInputGroup({
  formGroupClassName,
  formLabelClassName,
  inputClassName,
  name,
  labelText,
  helpText,
  showErrors,
  errors,
  ...otherProps
}: NumberInputGroupProps) {
  return (
    <FormGroup className={formGroupClassName}>
      <InputGroupFormLabel
        className={formLabelClassName}
        errors={errors}
        labelText={labelText}
        name={name}
        showErrors={showErrors}
      />
      <NumberInput
        // @ts-expect-error ts-migrate(2769) FIXME: No overload matches this call.
        className={inputClassName}
        hasError={showErrors}
        name={name}
        type="number"
        {...otherProps}
      />
      {helpText ? <FormHelp>{helpText}</FormHelp> : null}
    </FormGroup>
  );
}

NumberInputGroup.defaultProps = {
  inputClassName: styles.input,
};

export default formInput(NumberInputGroup);
