/* eslint-plugin-disable @typescript-eslint */

import durationOf from 'app/js/utils/dateTime/durationOf';
import t from 'app/js/utils/t';

function formatMinutes(milliseconds: any) {
  if (milliseconds == null) {
    return '';
  }
  const duration = durationOf(milliseconds);
  const days = (duration as any).days * 24 * 60;
  const hours = (duration as any).hours * 60;
  const min = (duration as any).minutes + hours + days;
  const sec = (duration as any).seconds;
  if (min) {
    return t('{1} min', min);
  } else if (sec > 0) {
    return t('{1} sec', sec);
  } else {
    return '';
  }
}
export default formatMinutes;
