import React, { ReactElement, useCallback, useState } from 'react';
import { useDispatch } from 'react-redux';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import getSeasonEpisodeIndex from 'app/js/utils/media/getSeasonEpisodeIndex';
import t from 'app/js/utils/t';
import * as metadataTypes from 'app/js/utils/types/metadataTypes';
import serverNowPlayingSessionsSlice from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsSlice';
import { ServerNowPlayingSession } from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsTypes';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import MetricsContextProvider from 'app/js/v4/core/ui/metrics/components/MetricsContextProvider';
import Modal from 'app/js/v4/core/ui/modals/components/Modal';
import ModalBody from 'app/js/v4/core/ui/modals/components/ModalBody';
import ModalContent from 'app/js/v4/core/ui/modals/components/ModalContent';
import ModalFooter from 'app/js/v4/core/ui/modals/components/ModalFooter';
import ModalFooterButtonBar from 'app/js/v4/core/ui/modals/components/ModalFooterButtonBar';
import ModalHeader from 'app/js/v4/core/ui/modals/components/ModalHeader';
import Kind from 'app/js/v4/core/ui/types/Kind';
import Size from 'app/js/v4/core/ui/types/Size';
import DashSeparator from 'app/js/views/v3/components/core/base/DashSeparator';
import Form from 'app/js/views/v3/components/core/forms/Form';
import FormLabel from 'app/js/views/v3/components/core/forms/FormLabel';
import TextArea from 'app/js/views/v3/components/core/forms/TextArea';
import AvatarImg from 'app/js/views/v3/components/core/images/AvatarImg';
import styles from './StopServerSessionModal.css';

interface StopServerSessionModalProps {
  session: ServerNowPlayingSession;
  isOpen: boolean;
  onModalClose(): void;
}

export default function StopServerSessionModal({
  session,
  isOpen,
  onModalClose,
}: StopServerSessionModalProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const [message, setMessage] = useState('');

  const {
    type,
    grandparentTitle,
    parentTitle,
    title,
    parentIndex,
    index,
    originallyAvailableAt,
    User,
  } = session;

  const seasonEpisodeIndex =
    type === metadataTypes.EPISODE
      ? getSeasonEpisodeIndex({
          seasonIndex: parentIndex,
          episodeIndex: index,
          date: originallyAvailableAt,
          noLeadingZero: true,
          separator: '  ',
        })
      : null;

  //
  // Callbacks

  const onStop = useCallback(() => {
    dispatch(
      serverNowPlayingSessionsSlice.actions.stopRequested({
        server,
        session,
        reason: message,
      })
    );

    onModalClose();
  }, [dispatch, message, server, session, onModalClose]);

  const onFormSubmit = useCallback(
    (event) => {
      event.preventDefault();
      event.stopPropagation();

      onStop();
    },
    [onStop]
  );

  //
  // Render

  return (
    <MetricsContextProvider metricsPage={metricsPages.STOP_SERVER_SESSION}>
      <Modal isOpen={isOpen} size={Size.Small} onModalClose={onModalClose}>
        <ModalContent onModalClose={onModalClose}>
          <ModalHeader>
            {t('Stop {title}', {
              title: grandparentTitle || parentTitle || title,
            })}
            {seasonEpisodeIndex ? (
              <>
                <DashSeparator />
                {seasonEpisodeIndex}
              </>
            ) : null}
          </ModalHeader>

          <ModalBody>
            <div className={styles.topContainer}>
              <AvatarImg
                className={styles.avatarImg}
                height={76}
                serverEntityID={server.entityID}
                url={User.thumb}
                width={76}
              />

              <div className={styles.description}>
                <p>{t("Do you wish to stop this item's playback?")}</p>
                <p>
                  {t(
                    'You may supply a message that will be displayed to {username}.',
                    { username: User.title }
                  )}
                </p>
              </div>
            </div>
          </ModalBody>

          <ModalFooter className={styles.modalFooter}>
            <FormLabel className={styles.formLabel} name="message">
              {t('Message')}
            </FormLabel>

            {/* @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: Element; className: string; onSu... Remove this comment to see the full error message */}
            <Form className={styles.form} onSubmit={onFormSubmit}>
              <TextArea
                className={styles.textArea}
                data-autofocus={true}
                name="message"
                rows={4}
                value={message}
                onChange={setMessage}
              />
            </Form>

            <ModalFooterButtonBar className={styles.buttonBar}>
              <Button onPress={onModalClose}>{t('Cancel')}</Button>
              <Button kind={Kind.Primary} onPress={onStop}>
                {t('Stop')}
              </Button>
            </ModalFooterButtonBar>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </MetricsContextProvider>
  );
}
