import $ from 'jquery';
import Sortable from 'sortablejs';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import BaseList from 'app/js/views/lists/BaseList';
import ConversionQueueListItem from 'app/js/views/lists/activity/convert/ConversionQueueListItem';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

const ConversionQueueList = BaseList.extend({
  //
  // Properties

  tagName: 'ol',
  className: 'list media-list playlist-items',
  itemView: ConversionQueueListItem,
  sorted: true,

  itemEvents: {
    sort: 'onSort',
  },

  //
  // Init

  initialize: function () {
    _.bindAll(this, 'onItemMove');
  },

  //
  // Listeners

  onRender: function () {
    if (this.sortable) {
      this.sortable.destroy();
    }

    this.sortable = new Sortable(
      this.el,
      _.extend(
        {
          onAdd: this.onItemMove,
          onUpdate: this.onItemMove,
          handle: '.sortable-handle',
          animation: 200,
          scroll: true,
          scrollSensitivity: 100,
          scrollSpeed: 2,
        },
        this.options.sortableOptions
      )
    );
  },

  onClose: function () {
    if (this.sortable) {
      this.sortable.destroy();
    }
  },

  onSort: function () {
    this.render();
  },

  onItemMove: function (event: any) {
    const movedItemID = $(event.item).data('queue-item-id');
    const afterItemID = $(event.item).prev().data('queue-item-id');
    const playQueue = this.options.playQueue;

    if (afterItemID) {
      playQueue.moveItem(movedItemID, afterItemID);
      return;
    }

    // If the item is being moved above the current item, we should confirm
    // before performing the move since conversion progress will be lost.

    const selectedMetadataItem = playQueue
      .get('selectedItem')
      .get('metadataItem');
    const selectedMetadataItemTitle = selectedMetadataItem.get('title');
    const confirmModal = new ConfirmModal({
      title: t('Convert Immediately'),
      message: t(
        "Do you want to start converting this item immediately? Any conversion progress for '{1}' will be lost.",
        selectedMetadataItemTitle
      ),
    });

    const confirmPromise = confirmModal.promise;

    confirmPromise.done(function () {
      playQueue.moveItem(movedItemID);
    });

    // Re-render the list so we restore the order before the move.
    confirmPromise.fail(_.bind(this.render, this));

    dispatcher.trigger('show:modal', confirmModal);
  },
});

export default ConversionQueueList;
