import classNames from 'classnames';
import React, { ReactElement, useCallback, useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import * as metricsContexts from 'app/js/utils/constants/metrics/metricsContexts';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import IconButton from 'app/js/v4/core/ui/buttons/components/IconButton';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import useDisclosure from 'app/js/v4/core/ui/hooks/useDisclosure';
import Kind from 'app/js/v4/core/ui/types/Kind';
import DVRManageChannelsModal from 'app/js/v4/features/dvrManagement/components/settings/DVRManageChannelsModal';
import {
  ChannelMapping,
  DVR,
  MediaGrabberDevice,
  MediaGrabberDeviceStatus,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import createDeviceActiveTunerCountSelector from 'app/js/v4/features/dvrManagement/selectors/createDeviceActiveTunerCountSelector';
import createDeviceChannelsSelector from 'app/js/v4/features/dvrManagement/selectors/createDeviceChannelsSelector';
import DVRDeviceSettingsModal from 'app/js/views/v3/components/livetv/device/DVRDeviceSettingsModal';
import IconAlert from 'app/glyphs/alert-560.svg';
import IconRemove from 'app/glyphs/remove-560.svg';
import IconSettings from 'app/glyphs/settings-560.svg';
import styles from './LiveTVSettingsDevice.css';

const metricsProperties = {
  context: metricsContexts.SETTINGS,
};

interface LiveTVSettingsDeviceProps {
  dvrID: DVR['key'];
  deviceKey: MediaGrabberDevice['key'];
  model: MediaGrabberDevice['model'];
  title: MediaGrabberDevice['title'];
  status: MediaGrabberDevice['status'];
  uri: MediaGrabberDevice['uri'];
  tunerCount: MediaGrabberDevice['tuners'];
  channelMappings?: ChannelMapping[];
  canRemoveDevice: boolean;
  hasPrefs: boolean;
}

interface Tuner {
  active: boolean;
}

function createTuners(tunerCount: number, activeTunerCount: number): Tuner[] {
  return Array.from({ length: tunerCount }).map((_, i) => {
    return { active: i < activeTunerCount };
  });
}

export default function LiveTVSettingsDevice({
  dvrID,
  deviceKey,
  model,
  title,
  status,
  uri,
  tunerCount,
  channelMappings,
  canRemoveDevice,
  hasPrefs,
}: LiveTVSettingsDeviceProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const deviceSettingsModalState = useDisclosure();
  const manageChannelsModalState = useDisclosure();

  const selectActiveTunerCount = useMemo(
    () => createDeviceActiveTunerCountSelector(server, deviceKey),
    [server, deviceKey]
  );
  const selectDeviceChannels = useMemo(
    () => createDeviceChannelsSelector(deviceKey),
    [deviceKey]
  );

  const activeTunerCount = useSelector(selectActiveTunerCount);
  const { channels, isPending: isDeviceChannelsPending } =
    useSelector(selectDeviceChannels);

  const tuners = useMemo(
    () => createTuners(tunerCount, activeTunerCount),
    [tunerCount, activeTunerCount]
  );

  const isDead = status === MediaGrabberDeviceStatus.Dead;
  const enabledChannelsCount =
    channelMappings?.filter((mapping) => mapping.enabled).length ?? 0;

  //
  // Callbacks

  const onRemoveDevicePress = useCallback(() => {
    dispatch(
      dvrSetupSlice.actions.removeDVRDeviceRequested({
        server,
        dvrID,
        deviceKey,
      })
    );
  }, [dispatch, server, dvrID, deviceKey]);

  const onManageChannelsModalClose = useCallback(() => {
    manageChannelsModalState.onClose();

    // Fetch DVR again to get any potential new channel mappings.
    dispatch(dvrSetupSlice.actions.dvrFetchRequested({ server }));
  }, [dispatch, server, manageChannelsModalState]);

  const onDeviceSettingsModalClose = useCallback(() => {
    deviceSettingsModalState.onClose();

    // Fetch DVR again to get any potential changes to the device.
    dispatch(dvrSetupSlice.actions.dvrFetchRequested({ server }));
  }, [dispatch, server, deviceSettingsModalState]);

  //
  // Effects

  useEffect(() => {
    dispatch(
      dvrSetupSlice.actions.deviceChannelsFetchRequested({
        server,
        deviceKey,
      })
    );
  }, [dispatch, server, deviceKey]);

  //
  // Render

  return (
    <div className={styles.container}>
      <div className={styles.modelContainer}>
        <div className={styles.title}>{title ?? model}</div>
        {hasPrefs ? (
          <IconButton
            className={styles.deviceActionButton}
            iconComponent={IconSettings}
            title={t('Device Settings')}
            onPress={deviceSettingsModalState.onOpen}
          />
        ) : null}
        {canRemoveDevice ? (
          <IconButton
            className={styles.deviceActionButton}
            iconComponent={IconRemove}
            kind={Kind.Danger}
            title={t('Remove Device')}
            onPress={onRemoveDevicePress}
          />
        ) : null}
      </div>
      {isDead ? (
        <div className={styles.deviceWarning}>
          <div className={styles.deviceWarningIconContainer}>
            <IconAlert />
          </div>

          {t(
            'Device not found. Ensure your device is powered on and connected to your network.'
          )}
        </div>
      ) : (
        <>
          <div className={styles.attribute}>
            <div className={styles.attributeTitle}>{t('Tuner status')}</div>
            <div className={styles.attributeValue}>
              {tuners.map((tuner, index) => {
                return (
                  <div
                    key={index}
                    className={classNames(
                      styles.tuner,
                      tuner.active && styles.activeTuner
                    )}
                  />
                );
              })}
            </div>
          </div>
          <div className={styles.attribute}>
            <div className={styles.attributeTitle}>{t('Channels')}</div>
            <div className={styles.attributeValue}>
              {isDeviceChannelsPending ? (
                t('Loading channels...')
              ) : (
                <>
                  ({channels.length})
                  <Link
                    className={styles.enabledChannelsCount}
                    kind={Kind.Primary}
                    onPress={manageChannelsModalState.onOpen}
                  >
                    {t('{enabledChannelsCount} enabled', {
                      enabledChannelsCount,
                    })}
                  </Link>
                </>
              )}
            </div>
          </div>
          {uri ? (
            <div className={styles.attribute}>
              <div className={styles.attributeTitle}>{t('IP address')}</div>
              <div className={styles.attributeValue}>{uri}</div>
            </div>
          ) : null}

          {title ? (
            <div className={styles.attribute}>
              <div className={styles.attributeTitle}>{t('Model')}</div>
              <div className={styles.attributeValue}>{model}</div>
            </div>
          ) : null}
        </>
      )}

      {!isDead ? (
        <DVRManageChannelsModal
          deviceKey={deviceKey}
          isOpen={manageChannelsModalState.isOpen}
          onModalClose={onManageChannelsModalClose}
        />
      ) : null}

      {!isDead ? (
        <DVRDeviceSettingsModal
          // @ts-expect-error ts-migrate(2322) FIXME: Type '{ serverEntityID: string; dvrID: number; dev... Remove this comment to see the full error message
          deviceID={Number(deviceKey)}
          dvrID={Number(dvrID)}
          isOpen={deviceSettingsModalState.isOpen}
          metricsProperties={metricsProperties}
          serverEntityID={server.entityID}
          onModalClose={onDeviceSettingsModalClose}
        />
      ) : null}
    </div>
  );
}
