import t from 'app/js/utils/t';

export default function formatRealTimeAgo(secondsAgo?: number): string {
  if (secondsAgo == null) {
    return '';
  }

  if (secondsAgo === 0) {
    return t('Now').toUpperCase();
  } else if (secondsAgo % 60 === 0) {
    return `${Math.floor(secondsAgo / 60)}m`;
  } else if (secondsAgo > 60) {
    return `${Math.floor(secondsAgo / 60)}m ${secondsAgo % 60}s`;
  } else {
    return `${secondsAgo}s`;
  }
}
