import React, { ReactElement } from 'react';
import {
  COMMUNITY_TUNERS_SUPPORT_URL,
  SUPPORTED_TUNERS_SUPPORT_URL,
} from 'app/js/utils/constants/commonUrls';
import * as metricsContexts from 'app/js/utils/constants/metrics/metricsContexts';
import * as upsellOrigins from 'app/js/utils/constants/upsellOrigins';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import PlexPassUpsellInfo from 'app/js/views/v3/components/core/PlexPassUpsellInfo';
import TagInjector from 'app/js/views/v3/components/core/TagInjector';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import ServerFeaturePlexPassUpsellButtonConnector from 'app/js/views/v3/components/core/buttons/ServerFeaturePlexPassUpsellButtonConnector';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import IconMarketingDvr from 'app/glyphs/marketing-dvr-560.svg';
import styles from './LiveTVSettingsZeroState.css';

interface LiveTVSettingsZeroStateProps {
  hasDVRFeature: boolean;
  hasLiveTVSetupFeature: boolean;
  hasUnsupportedServer: boolean;
  onSetupPress(): void;
}

export default function LiveTVSettingsZeroState({
  hasDVRFeature,
  hasLiveTVSetupFeature,
  hasUnsupportedServer,
  onSetupPress,
}: LiveTVSettingsZeroStateProps): ReactElement {
  const [server] = useSourceContext();
  const hasDVRSetup = hasDVRFeature || hasLiveTVSetupFeature;

  return (
    <PlexPassUpsellInfo
      // @ts-expect-error ts-migrate(2322) FIXME: Type 'ComponentType<SVGAttributes<SVGElement>>' is... Remove this comment to see the full error message
      iconComponent={IconMarketingDvr}
      moreInfo={
        <TagInjector
          message={t(
            "To get started recording TV, connect a tuner device to your network. Here's a {openLinkSupported}list of supported devices{closeLinkSupported}. However, some tuners not officially supported {openLinkUnsupported}work as well{closeLinkUnsupported}!"
          )}
          tags={[
            {
              open: 'openLinkSupported',
              close: 'closeLinkSupported',
              component: Link,
              kind: kinds.PRIMARY,
              to: SUPPORTED_TUNERS_SUPPORT_URL,
              target: '_blank',
            },
            {
              open: 'openLinkUnsupported',
              close: 'closeLinkUnsupported',
              component: Link,
              kind: kinds.PRIMARY,
              to: COMMUNITY_TUNERS_SUPPORT_URL,
              target: '_blank',
            },
          ]}
        />
      }
      moreInfoClassName={styles.moreInfo}
      text={t(
        'With Plex Pass and a digital tuner, you can record over-the-air broadcast TV and watch your favorite shows, movies, news, and sports on any device, anywhere in the world. Cut the cord and love TV again.'
      )}
      title={t('Live TV & DVR')}
    >
      {hasDVRSetup ? (
        <Button
          className={styles.button}
          isDisabled={hasUnsupportedServer}
          kind={kinds.PRIMARY}
          size={sizes.LARGE}
          onPress={onSetupPress}
        >
          {t('Set Up Plex DVR')}
        </Button>
      ) : (
        <ServerFeaturePlexPassUpsellButtonConnector
          className={styles.button}
          modalMetricsProperties={{
            context: metricsContexts.DVR_SETUP,
          }}
          origin={upsellOrigins.DVR_SETUP}
          serverEntityID={server.entityID}
        />
      )}
    </PlexPassUpsellInfo>
  );
}
