/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import t from 'app/js/utils/t';
import SpinnerButton from 'app/js/views/v3/components/core/buttons/SpinnerButton';
import { DEFAULT } from 'app/js/views/v3/helpers/props/kinds';
import { MEDIUM } from 'app/js/views/v3/helpers/props/sizes';
import ClaimedServerAvatarConnector from './ClaimedServerAvatarConnector';
import styles from './ClaimedServer.css';

type ClaimedServerProps = {
  isSigningOut: boolean;
  username: string;
  serverEntityID: string;
  onSignOutPress: (...args: any[]) => any;
};

function ClaimedServer(props: ClaimedServerProps) {
  const { isSigningOut, username, serverEntityID, onSignOutPress } = props;

  return (
    <div className={styles.container}>
      <ClaimedServerAvatarConnector serverEntityID={serverEntityID} />

      <div className={styles.message}>
        <div className={styles.messageHeader}>{t('Server signed in as')}</div>
        <div className={styles.messageDetails}>{username}</div>
      </div>

      <SpinnerButton
        className={styles.button}
        isSpinning={isSigningOut}
        kind={DEFAULT}
        size={MEDIUM}
        onPress={onSignOutPress}
      >
        {t('Sign Out')}
      </SpinnerButton>
    </div>
  );
}

export default ClaimedServer;
