import { createSelector, Selector } from 'reselect';
import { ServerNowPlayingSession } from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import createServerNowPlayingSessionsSelector from './createServerNowPlayingSessionsSelector';

export default function createServerNowPlayingSessionSelector(
  machineIdentifier: string,
  sessionKey: string
): Selector<LegacyRootState, ServerNowPlayingSession> {
  return createSelector(
    createServerNowPlayingSessionsSelector(machineIdentifier),
    (serverNowPlayingSessions) =>
      serverNowPlayingSessions.sessionsByKey[sessionKey]
  );
}
