/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import Slider from 'app/js/views/v3/components/core/Slider';
import FormGroup from 'app/js/views/v3/components/core/forms/FormGroup';
import InputGroupFormLabel from 'app/js/views/v3/components/core/forms/InputGroupFormLabel';
import formInput from 'app/js/views/v3/components/core/forms/formInput';
import styles from './SliderInputGroup.css';

type SliderInputGroupProps = {
  name: string;
  value: string | number;
  min: number;
  max: number;
  stepSize: number;
  labelText: React.ReactNode;
  showErrors?: boolean;
  errors: string[];
  valueModifier: (...args: any[]) => any;
  onChange: (...args: any[]) => any;
  onBlur: (...args: any[]) => any;
};

class SliderInputGroup extends Component<SliderInputGroupProps> {
  static defaultProps: any;

  //
  // Listeners

  onChange = ({ steppedValue }: any) => {
    const stepSizeString = String(this.props.stepSize);
    const [, fractional] = stepSizeString.split('.');
    const fixedValue =
      fractional != null
        ? parseFloat(steppedValue.toFixed(fractional.length))
        : steppedValue;

    this.props.onChange(fixedValue);
  };

  //
  // Render

  render() {
    const {
      name,
      value,
      min,
      max,
      stepSize,
      labelText,
      showErrors,
      errors,
      valueModifier,
    } = this.props;

    return (
      <FormGroup>
        <InputGroupFormLabel
          errors={errors}
          labelText={labelText}
          name={name}
          showErrors={showErrors}
        />
        <div className={styles.sliderContainer}>
          <Slider
            className={styles.slider}
            max={max}
            min={min}
            stepSize={stepSize}
            // @ts-expect-error ts-migrate(2322) FIXME: Type 'string | number' is not assignable to type '... Remove this comment to see the full error message
            value={value}
            onChange={this.onChange}
          />
          <div className={styles.sliderValue}>{valueModifier(value)}</div>
        </div>
      </FormGroup>
    );
  }
}

SliderInputGroup.defaultProps = {
  valueModifier(value: any) {
    return value;
  },
};

export default formInput(SliderInputGroup);
