import React, {
  CSSProperties,
  FocusEvent,
  MouseEvent,
  ReactElement,
  ReactNode,
  Ref,
  TouchEvent,
  useCallback,
  useEffect,
  useState,
} from 'react';
import ReactDOM from 'react-dom';

// A portal can be used to transport a component rendered in the component tree
// to the document body. This allows us to create modals, menus, etc. where they
// are needed, but render them in a way that they overlay the entire
// application.

interface InnerPortalProps {
  forwardedRef: Ref<HTMLDivElement>;
  children?: ReactNode;
  className?: string;
  style?: CSSProperties;
  isOpen?: boolean;
  closeDelayMilliseconds?: number;
  allowInteractionEventBubbling?: boolean;
  onOpen?(): void;
  onClose?(): void;
}

export type PortalProps = Omit<InnerPortalProps, 'forwardedRef'>;

function PortalWithRef({
  className,
  style,
  children,
  forwardedRef,
  isOpen = true,
  closeDelayMilliseconds = 0,
  allowInteractionEventBubbling = false,
  onOpen,
  onClose,
}: InnerPortalProps): ReactElement | null {
  const [isOpenInternal, setIsOpenInternal] = useState(isOpen);

  //
  // Callbacks

  const onInteractionEvent = useCallback(
    (event: FocusEvent | MouseEvent | TouchEvent) => {
      // React 16 portals allow events to bubble up through the component tree, so
      // a click event can bubble up to the button that opened the portal. This
      // can lead to unexpected behavior, so stop propagation of interaction
      // events up from the portal.

      if (!allowInteractionEventBubbling) {
        event.stopPropagation();
      }
    },
    [allowInteractionEventBubbling]
  );

  //
  // Effects

  useEffect(() => {
    let timeoutID: number;

    if (isOpen) {
      setIsOpenInternal(true);
      onOpen?.();
    } else {
      timeoutID = window.setTimeout(() => {
        setIsOpenInternal(false);
        onClose?.();
      }, closeDelayMilliseconds);
    }

    return () => {
      if (timeoutID) {
        window.clearTimeout(timeoutID);
      }
    };
  }, [closeDelayMilliseconds, isOpen, onClose, onOpen]);

  //
  // Render

  if (!isOpenInternal) {
    return null;
  }

  return ReactDOM.createPortal(
    <div
      ref={forwardedRef}
      className={className}
      style={style}
      onBlur={onInteractionEvent}
      onClick={onInteractionEvent}
      onFocus={onInteractionEvent}
      onMouseDown={onInteractionEvent}
      onMouseUp={onInteractionEvent}
      onTouchCancel={onInteractionEvent}
      onTouchEnd={onInteractionEvent}
      onTouchMove={onInteractionEvent}
      onTouchStart={onInteractionEvent}
    >
      {children}
    </div>,
    document.body
  );
}

export default React.forwardRef<HTMLDivElement, PortalProps>((props, ref) => {
  return <PortalWithRef {...props} forwardedRef={ref} />;
});
