import React, { ReactElement, ReactNode } from 'react';
import formatPlayerTime from 'app/js/utils/dateTime/formatPlayerTime';
import getDeviceIcon from 'app/js/utils/getDeviceIcon';
import getSelectedAudio from 'app/js/utils/media/getSelectedAudio';
import getSelectedSubtitles from 'app/js/utils/media/getSelectedSubtitles';
import getStreams from 'app/js/utils/media/getStreams';
import formatBandwidthBitrate from 'app/js/utils/metadata/formatBandwidthBitrate';
import formatResolution from 'app/js/utils/metadata/formatResolution';
import isEdge from 'app/js/utils/predicates/device/isEdge';
import toTitleCase from 'app/js/utils/string/case/toTitleCase';
import toUpperCase from 'app/js/utils/string/case/toUpperCase';
import t from 'app/js/utils/t';
import streamTypes from 'app/js/utils/types/streamTypes';
import { ServerNowPlayingSession } from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsTypes';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import DashSeparator, {
  SEPARATOR,
} from 'app/js/views/v3/components/core/base/DashSeparator';
import ServerConnectionStatusIcon from 'app/js/views/v3/components/core/icons/ServerConnectionStatusIcon';
import StreamTitle from 'app/js/views/v3/components/metadata/titles/stream/StreamTitle';
import styles from './SessionTileDetails.css';

interface StreamOutputRowProps {
  children: ReactNode;
}

function StreamOutputRow({ children }: StreamOutputRowProps): ReactElement {
  return (
    <span>
      <span
        className={isEdge() ? styles.rightArrowForEdge : styles.rightArrow}
      />
      {children}
    </span>
  );
}

interface PlayerLocationProps {
  local: boolean;
  relayed: boolean;
}

function PlayerLocation({ local, relayed }: PlayerLocationProps): ReactElement {
  let label;

  if (relayed) {
    label = t('Indirect');
  } else if (local) {
    label = 'Local';
  } else {
    label = t('Remote');
  }

  return <span>{label}</span>;
}

interface SessionTileDetailsProps {
  className?: string;
  server: Server;
  session: ServerNowPlayingSession;
  internalViewOffset: number;
}

export default function SessionTileDetails({
  className,
  server,
  session,
  internalViewOffset,
}: SessionTileDetailsProps): ReactElement {
  const { duration, Media = [], Player, Session, TranscodeSession } = session;

  const selectedMediaItem =
    Media.find((mediaItem) => mediaItem.selected) || Media[0];
  const selectedSubtitles = getSelectedSubtitles(selectedMediaItem);
  let selectedAudio = getSelectedAudio(selectedMediaItem);

  if (!selectedAudio) {
    // In some cases the session metadataItem will only contain a single audio
    // track that is unselected.
    // See: https://github.com/plexinc/plex-media-server/issues/9561
    const streamType = streamTypes.get('audio').id;
    selectedAudio = getStreams(selectedMediaItem, { streamType })[0];
  }

  const showVideo = !!selectedMediaItem.videoCodec;
  const showAudio = !!selectedMediaItem.audioCodec;
  const showSubtitles = !!selectedSubtitles;

  return (
    <div className={className}>
      <div className={styles.playerContainer}>
        <div className={styles.playerIcon}>
          <img
            className={styles.productLogo}
            height={44}
            src={getDeviceIcon(Player.product, Player.platform)}
            width={44}
          />

          <ServerConnectionStatusIcon
            className={styles.statusIcon}
            isBadge={true}
            isConnected={true}
            isRelayed={Player.relayed}
            isSecure={Player.secure}
          />
        </div>

        <div className={styles.playerInformation}>
          <div
            className={styles.title}
            title={`${Player.product} ${SEPARATOR} ${Player.title}`}
          >
            {Player.product} <DashSeparator /> {Player.title}
          </div>
          <div className={styles.subtitle}>
            {toTitleCase(Player.state)}
            {duration ? (
              <>
                <DashSeparator />
                {`${formatPlayerTime(internalViewOffset)} / ${formatPlayerTime(
                  duration
                )}`}
              </>
            ) : null}
          </div>

          <div className={styles.subtitle}>
            <PlayerLocation local={Player.local} relayed={Player.relayed} />

            {!Player.relayed ? ` (${Player.address})` : null}

            {Session ? (
              <>
                <DashSeparator />
                <span
                  title={t(
                    'The amount of outgoing bandwidth reserved for this playback session'
                  )}
                >
                  {formatBandwidthBitrate(session.Session.bandwidth * 1024, 0)}
                </span>
              </>
            ) : null}
          </div>
        </div>
      </div>

      {showVideo ? (
        <div className={styles.videoStreamInformation}>
          <div className={styles.streamType}>{t('Video')}</div>

          <div className={styles.streams}>
            <div className={styles.title}>
              <StreamTitle
                mediaItem={selectedMediaItem}
                serverEntityID={server.entityID}
                type="video"
              />
              {TranscodeSession?.transcodeHwDecoding ? ' (hw)' : null}
            </div>

            <div className={styles.title}>
              {!!Session &&
              (!TranscodeSession || TranscodeSession.videoDecision == null) ? (
                <StreamOutputRow>{t('Direct Play')}</StreamOutputRow>
              ) : null}

              {TranscodeSession && TranscodeSession.videoDecision === 'copy' ? (
                <StreamOutputRow>{t('Direct Stream')}</StreamOutputRow>
              ) : null}

              {TranscodeSession &&
              TranscodeSession.videoDecision === 'transcode' ? (
                <StreamOutputRow>
                  {`${
                    formatResolution(
                      selectedMediaItem.videoResolution,
                      TranscodeSession.scanType
                    ) || t('Unknown')
                  } `}
                  ({toUpperCase(TranscodeSession.videoCodec)})
                  <DashSeparator />
                  {t('Transcode')}
                  {TranscodeSession.transcodeHwEncoding ? ' (hw)' : null}
                </StreamOutputRow>
              ) : null}
            </div>
          </div>
        </div>
      ) : null}

      {showAudio ? (
        <div className={styles.audioStreamInformation}>
          <div className={styles.streamType}>{t('Audio')}</div>

          <div className={styles.streams}>
            <div className={styles.title}>
              <StreamTitle
                serverEntityID={server.entityID}
                stream={selectedAudio}
                type="audio"
              />
            </div>

            <div className={styles.title}>
              {!!Session &&
              (!TranscodeSession || TranscodeSession.audioDecision == null) ? (
                <StreamOutputRow>{t('Direct Play')}</StreamOutputRow>
              ) : null}

              {TranscodeSession && TranscodeSession.audioDecision === 'copy' ? (
                <StreamOutputRow>{t('Direct Stream')}</StreamOutputRow>
              ) : null}

              {TranscodeSession &&
              TranscodeSession.audioDecision === 'transcode' ? (
                <StreamOutputRow>
                  {toUpperCase(TranscodeSession.audioCodec)}
                  <DashSeparator />
                  {t('Transcode')}
                </StreamOutputRow>
              ) : null}
            </div>
          </div>
        </div>
      ) : null}

      {showSubtitles ? (
        <div className={styles.subtitlesStreamInformation}>
          <div className={styles.streamType}>{t('Subtitles')}</div>

          <div className={styles.title}>
            <StreamTitle
              serverEntityID={server.entityID}
              stream={getSelectedSubtitles(selectedMediaItem)}
              type="subtitles"
            />
          </div>
        </div>
      ) : null}
    </div>
  );
}
