import { createSelector, Selector } from 'reselect';
import { GuideChannel } from 'app/js/v4/features/livetv/liveTVTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import selectLiveTV from './selectLiveTV';

const selectLiveTVChannels: Selector<LegacyRootState, GuideChannel[]> =
  createSelector(selectLiveTV, (livetv) => {
    const { guideChannels } = livetv;

    const usesGridKey = !!guideChannels.channels?.[0]?.gridKey;
    const usesVcn = !!guideChannels.channels?.[0]?.vcn;

    return guideChannels.channels.reduce<GuideChannel[]>(
      (acc, guideChannel) => {
        const existingChannelIndex = acc.findIndex(({ gridKey, id, vcn }) => {
          if (usesGridKey) {
            return gridKey === guideChannel.gridKey;
          } else if (usesVcn) {
            return vcn === guideChannel.vcn;
          } else {
            return id === guideChannel.id;
          }
        });

        if (existingChannelIndex === -1) {
          acc.push(guideChannel);
        } else if (!acc[existingChannelIndex].isHd && guideChannel.isHd) {
          acc[existingChannelIndex] = guideChannel;
        }

        return acc;
      },
      []
    );
  });

export default selectLiveTVChannels;
