import React, {
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import createServerAccountsSortedUsersSelector from 'app/js/v4/core/serverAccounts/selectors/createServerAccountsSortedUsersSelector';
import serverAccountsSlice from 'app/js/v4/core/serverAccounts/serverAccountsSlice';
import { ServerAccount } from 'app/js/v4/core/serverAccounts/serverAccountsTypes';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import usePreviousValueForAtMost from 'app/js/v4/core/ui/hooks/usePreviousValueForAtMost';
import ServerDashboardPageHeader from 'app/js/v4/features/dashboard/components/ServerDashboardPageHeader';
import ServerDashboardAccountMenuButton from 'app/js/v4/features/dashboard/components/menuButtons/ServerDashboardAccountMenuButton';
import ServerDashboardFilterMenuButton from 'app/js/v4/features/dashboard/components/menuButtons/ServerDashboardFilterMenuButton';
import dashboardTopPlayedSlice from 'app/js/v4/features/dashboard/dashboardTopPlayedSlice';
import selectDashboardTopPlayed from 'app/js/v4/features/dashboard/selectors/selectDashboardTopPlayed';
import {
  getTimePeriodAfter,
  getTimePeriodLabel,
  TimePeriod,
} from 'app/js/v4/utils/timePeriods';
import TopPlayedList from './TopPlayedList';

export default function TopPlayed(): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();

  const [activeAccount, setActiveAccount] = useState<ServerAccount | null>(
    null
  );

  const [activeTimePeriod, setActiveTimePeriod] = useState(TimePeriod.Month);

  const timePeriods = useMemo(() => {
    return [
      TimePeriod.Week,
      TimePeriod.Month,
      TimePeriod.NinetyDays,
      TimePeriod.Year,
      TimePeriod.AllTime,
    ];
  }, []);

  const {
    moviesStatistics,
    tvStatistics,
    musicStatistics,
    gameStatistics,
    isPending,
    isPopulated,
    error,
  } = useSelector(selectDashboardTopPlayed);

  const isDelayedPending = usePreviousValueForAtMost(isPending, true);

  const sortedAccounts = useSelector(
    createServerAccountsSortedUsersSelector(server.machineIdentifier)
  );

  //
  // Callbacks

  const onAccountSelect = useCallback((account: ServerAccount) => {
    setActiveAccount(account);
  }, []);

  const onTimePeriodSelect = useCallback((timePeriod: TimePeriod) => {
    setActiveTimePeriod(timePeriod);
  }, []);

  //
  // Effects

  useEffect(() => {
    dispatch(serverAccountsSlice.actions.populateRequested({ server }));
  }, [dispatch, server]);

  useEffect(() => {
    dispatch(
      dashboardTopPlayedSlice.actions.topPlayedFetchRequested({
        server,
        accountID: activeAccount ? activeAccount.id : undefined,
        viewedAt: getTimePeriodAfter(activeTimePeriod),
        limit: 6,
      })
    );
  }, [activeTimePeriod, activeAccount, dispatch, server]);

  useEffect(() => {
    return () => {
      dispatch(dashboardTopPlayedSlice.actions.reset());
    };
  }, [dispatch]);

  //
  // Render

  return (
    <>
      <ServerDashboardPageHeader title={t('Top Played')}>
        <div>
          <ServerDashboardAccountMenuButton
            accounts={sortedAccounts}
            activeAccount={activeAccount}
            onAccountSelect={onAccountSelect}
          />

          <ServerDashboardFilterMenuButton
            activeValue={activeTimePeriod}
            formatter={getTimePeriodLabel}
            values={timePeriods}
            onSelect={onTimePeriodSelect}
          />
        </div>
      </ServerDashboardPageHeader>

      <TopPlayedList
        activeTimePeriod={activeTimePeriod}
        errorMessage={error ? t('Unable to load statistics') : undefined}
        gameStatistics={gameStatistics}
        isSpinnerVisible={isDelayedPending}
        moviesStatistics={moviesStatistics}
        musicStatistics={musicStatistics}
        showList={!isDelayedPending && isPopulated}
        tvStatistics={tvStatistics}
      />
    </>
  );
}
