/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import createServerCloudAccountSelector from 'app/js/stores/selectors/server/createServerCloudAccountSelector';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';

function createIsServerClaimedSelector() {
  return createSelector(
    createUnstableServerSelector(),
    createServerCloudAccountSelector(),
    (server, serverCloudAccount) =>
      !!(
        // After claiming a server, the local server model will consider itself
        // claimed, but its serverCloudAccount will not be marked as signed-in,
        // and its connections cannot be relied upon until it appears in the
        // plex.tv resources response.
        (
          server &&
          server.isClaimed &&
          serverCloudAccount &&
          serverCloudAccount.signedIn
        )
      )
  );
}

export default createIsServerClaimedSelector;
