/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { Component } from 'react';
import assert from 'app/js/utils/log/assert';
import getPercent from 'app/js/utils/number/getPercent';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';
import styles from './Slider.css';

type SliderProps = {
  style?: any;
  className?: string;
  trackClassName?: string;
  fillClassName?: string;
  secondaryFillClassName?: string;
  thumbClassName?: string;
  thumbTrackClassName?: string;
  label?: string;
  tabIndex?: string;
  value: number;
  min: number;
  max: number;
  secondaryMin?: number;
  secondaryMax?: number;
  stepSize: number;
  stickiness: number;
  showFill: boolean;
  showSecondaryFill: boolean;
  autoHideThumb: boolean;
  isDisabled: boolean;
  isVertical: boolean;
  onMove?: (...args: any[]) => any;
  onChange: (...args: any[]) => any;
  onDragStart?: (...args: any[]) => any;
  onDragComplete?: (...args: any[]) => any;
  onMouseEnter?: (...args: any[]) => any;
  onMouseLeave?: (...args: any[]) => any;
  onWheel?: (...args: any[]) => any;
};

type SliderState = any;

class Slider extends Component<SliderProps, SliderState> {
  static defaultProps = {
    className: styles.slider,
    trackClassName: styles.track,
    fillClassName: styles.fill,
    secondaryFillClassName: styles.secondaryFill,
    thumbClassName: styles.thumb,
    thumbTrackClassName: styles.thumbTrack,
    min: 0,
    max: 100,
    stepSize: 0,
    stickiness: 10,
    showFill: false,
    showSecondaryFill: false,
    autoHideThumb: false,
    isDisabled: false,
    isDragging: false,
    isVertical: false,
  };

  // eslint-disable-next-line react/sort-comp
  _trackRef: any;

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this._trackRef = React.createRef();

    this.state = {
      isDragging: false,
    };
  }

  shouldComponentUpdate(nextProps: SliderProps, nextState: SliderState) {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    return shouldPureComponentUpdate(this, nextProps, nextState);
  }

  componentWillUnmount() {
    this._stopListeningForDrag();
  }

  //
  // Control

  _listenForDrag(isTouch: any) {
    if (isTouch) {
      document.addEventListener('touchmove', this.onDocumentTouchMove);
      document.addEventListener('touchcancel', this.onDocumentTouchEnd);
      document.addEventListener('touchend', this.onDocumentTouchEnd);
    } else {
      document.addEventListener('mousemove', this.onDocumentMouseMove);
      document.addEventListener('mouseup', this.onDocumentMouseUp);
    }
  }

  _stopListeningForDrag() {
    document.removeEventListener('mousemove', this.onDocumentMouseMove);
    document.removeEventListener('touchmove', this.onDocumentTouchMove);
    document.removeEventListener('mouseup', this.onDocumentMouseUp);
    document.removeEventListener('touchcancel', this.onDocumentTouchEnd);
    document.removeEventListener('touchend', this.onDocumentTouchEnd);
  }

  _startDrag(pageX: any, pageY: any, isTouch: any) {
    this._changeValue(this._getValueForPosition(pageX, pageY));
    this._listenForDrag(isTouch);

    if (this.props.onDragStart) {
      this.props.onDragStart();
    }
  }

  _drag(pageX: any, pageY: any) {
    if (!this.state.isDragging) {
      document.body.classList.add(styles.isDragging);
      this.setState({ isDragging: true });
    }

    this._changeValue(this._getValueForPosition(pageX, pageY));
  }

  _stopDrag(pageX: any, pageY: any) {
    document.body.classList.remove(styles.isDragging);

    this._stopListeningForDrag();

    const positionValue = this._getValueForPosition(pageX, pageY);
    const steppedValue = this._getSteppedValue(positionValue);

    this._changeValue(positionValue);
    this.setState({ isDragging: false });

    if (this.props.onDragComplete) {
      this.props.onDragComplete({
        positionValue,
        steppedValue,
      });
    }
  }

  _getValueForPosition(pageX: any, pageY: any) {
    const { isVertical, min, max } = this.props;

    __DEV__ &&
      assert(
        max > min,
        `The max, ${max}, must be greater than the min, ${min}`
      );

    const track = this._trackRef.current;
    const trackRect = track.getBoundingClientRect();

    let value;

    if (isVertical) {
      const viewportY = pageY - document.body.scrollTop;

      value = (trackRect.bottom - viewportY) / track.offsetHeight;
    } else {
      const viewportX = pageX - document.body.scrollLeft;

      value = (viewportX - trackRect.left) / track.offsetWidth;
    }

    value = Math.min(Math.max(value, 0), 1);

    return (max - min) * value + min;
  }

  _getSteppedValue(value: any) {
    const { min, max, stepSize } = this.props;

    if (!stepSize) {
      return value;
    }

    const steppedValue = Math.round(value / stepSize) * stepSize;

    return Math.min(Math.max(steppedValue, min), max);
  }

  _getStickyValue(value: any) {
    const { max, min, stepSize, stickiness } = this.props;

    if (!stepSize || !stickiness) {
      return value;
    }

    const steppedValue = this._getSteppedValue(value);
    const proximity = Math.abs(steppedValue - value) / (max - min);

    return proximity < stickiness / 100 ? steppedValue : value;
  }

  _changeValue(nextValue: any) {
    const { value } = this.props;

    if (nextValue !== value) {
      this.props.onChange({
        positionDelta: nextValue - value,
        positionValue: nextValue,
        steppedValue: this._getSteppedValue(nextValue),
      });
    }
  }

  //
  // Listeners

  onMouseDown = (event: any) => {
    event.preventDefault();

    if (event.button === 0) {
      this._startDrag(event.pageX, event.pageY, false);
    }
  };

  onMouseMove = ({ pageX, pageY }: any) => {
    if (this.state.isDragging) {
      return;
    }

    const value = this._getValueForPosition(pageX, pageY);
    const track = this._trackRef.current;
    const trackRect = track.getBoundingClientRect();

    const position = this.props.isVertical
      ? { x: trackRect.left, y: pageY }
      : { x: pageX, y: trackRect.top };

    // @ts-expect-error ts-migrate(2722) FIXME: Cannot invoke an object which is possibly 'undefin... Remove this comment to see the full error message
    this.props.onMove({
      position,
      positionValue: value,
      steppedValue: this._getSteppedValue(value),
    });
  };

  onTouchStart = (event: any) => {
    event.preventDefault();

    const touch = event.touches && event.touches[0];

    if (touch) {
      this._startDrag(touch.pageX, touch.pageY, true);
    }
  };

  onDocumentMouseMove = (event: any) => {
    this._drag(event.pageX, event.pageY);
  };

  onDocumentTouchMove = (event: any) => {
    const touch = event.touches && event.touches[0];

    if (touch) {
      this._drag(touch.pageX, event.pageY);
    }
  };

  onDocumentMouseUp = (event: any) => {
    this._stopDrag(event.pageX, event.pageY);
  };

  onDocumentTouchEnd = (event: any) => {
    const touch = event.changedTouches && event.changedTouches[0];

    if (touch) {
      this._stopDrag(touch.pageX, touch.pageY);
    }
  };

  //
  // Render

  render() {
    const {
      style,
      className,
      trackClassName,
      secondaryFillClassName,
      fillClassName,
      thumbClassName,
      thumbTrackClassName,
      label,
      tabIndex,
      value,
      min,
      max,
      secondaryMin,
      secondaryMax,
      showSecondaryFill,
      showFill,
      autoHideThumb,
      isDisabled,
      isVertical,
      onMove,
      onMouseEnter,
      onMouseLeave,
      onWheel,
    } = this.props;

    const { isDragging } = this.state;

    const percentage = getPercent(this._getStickyValue(value), min, max);

    let scaleProperty = 'scaleX';
    let translateProperty = 'translateX';

    if (isVertical) {
      // Compose around the `VerticalSlider` so that necessary CSS class updates
      // are also provided.
      scaleProperty = 'scaleY';
      translateProperty = 'translateY';
    }

    const thumbTransform = `${translateProperty}(${
      isVertical ? -percentage : -(100 - percentage)
    }%)`;
    const secondaryStartPercentage = secondaryMin
      ? getPercent(this._getStickyValue(secondaryMin), min, max)
      : 0;
    const secondaryEndPercentage = secondaryMax
      ? getPercent(this._getStickyValue(secondaryMax), min, max)
      : 0;
    const fillTransformValue = Math.max(
      (percentage - secondaryStartPercentage) / 100,
      0
    );
    const secondaryScaleTransformValue = Math.max(
      (secondaryEndPercentage - secondaryStartPercentage) / 100,
      0
    );
    const fillTransform = `${scaleProperty}(${fillTransformValue})`;
    const secondaryScaleTransform = `${scaleProperty}(${secondaryScaleTransformValue})`;
    const position = secondaryMin ? secondaryStartPercentage + '%' : undefined;

    const positionStyle = isVertical ? { top: position } : { left: position };
    const fillStyle = { transform: fillTransform, ...positionStyle };
    const secondaryFillStyle = {
      transform: secondaryScaleTransform,
      ...positionStyle,
    };

    return (
      <div
        className={classNames(
          className,
          isDisabled && styles.isDisabled,
          isDragging && styles.isDragging,
          autoHideThumb && styles.hasAutoHideThumb
        )}
        style={style}
        // @ts-expect-error ts-migrate(2322) FIXME: Type '((...args: any[]) => any) | null | undefined... Remove this comment to see the full error message
        onMouseDown={isDisabled ? null : this.onMouseDown}
        // @ts-expect-error ts-migrate(2322) FIXME: Type '((...args: any[]) => any) | null | undefined... Remove this comment to see the full error message
        onMouseEnter={isDisabled ? null : onMouseEnter}
        // @ts-expect-error ts-migrate(2322) FIXME: Type '((...args: any[]) => any) | null | undefined... Remove this comment to see the full error message
        onMouseLeave={isDisabled ? null : onMouseLeave}
        // @ts-expect-error ts-migrate(2322) FIXME: Type '((event: any) => void) | null' is not assign... Remove this comment to see the full error message
        onMouseMove={isDisabled || !onMove ? null : this.onMouseMove}
        // @ts-expect-error ts-migrate(2322) FIXME: Type '(({ pageX, pageY }: any) => void) | null' is... Remove this comment to see the full error message
        onTouchStart={isDisabled ? null : this.onTouchStart}
        // @ts-expect-error ts-migrate(2322) FIXME: Type '((event: any) => void) | null' is not assign... Remove this comment to see the full error message
        onWheel={isDisabled ? null : onWheel}
      >
        <div ref={this._trackRef} className={trackClassName}>
          {showSecondaryFill ? (
            <div
              className={secondaryFillClassName}
              style={secondaryFillStyle}
            />
          ) : null}

          {showFill ? (
            <div className={fillClassName} style={fillStyle} />
          ) : null}
        </div>

        <div
          className={thumbTrackClassName}
          style={{
            transform: thumbTransform,
          }}
        >
          <Link
            aria-labelledby={label}
            aria-valuemax={max}
            aria-valuemin={min}
            aria-valuenow={value}
            className={thumbClassName}
            role="slider"
            tabIndex={tabIndex}
            onMouseDown={this.onMouseDown}
            // @ts-expect-error ts-migrate(2322) FIXME: Type '{ className: string | undefined; role: strin... Remove this comment to see the full error message
            onMouseMove={onMove && this.onMouseMove}
            onTouchStart={this.onTouchStart}
          />
        </div>
      </div>
    );
  }
}

export default Slider;
