/* eslint-plugin-disable @typescript-eslint */

import escapeRegExp from 'app/js/utils/string/escapeRegExp';

function formatThreadFilterText(filterText: any, thread: any) {
  // Remove the thread id if it's already present in the filter
  const filterWithThreadRemoved = filterText
    .replace(new RegExp(`\\s?${escapeRegExp(thread)}`), '')
    .trim();

  // Prepend the thread id to the filter text
  const filter = filterWithThreadRemoved.length
    ? `${thread} ${filterWithThreadRemoved}`
    : thread;

  return filter;
}

export default formatThreadFilterText;
