/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import t from 'app/js/utils/t';
import TextInput from 'app/js/views/v3/components/core/forms/TextInput';
import { LARGE } from 'app/js/views/v3/helpers/props/sizes';
import styles from './ServerLogTimeFilterInputs.css';

type ServerLogTimeFilterInputsProps = {
  timeFilterStart: string;
  timeFilterEnd: string;
  isTimeFilterStartValid: boolean;
  isTimeFilterEndValid: boolean;
  onTimeFilterChange: (...args: any[]) => any;
};

type ServerLogTimeFilterInputsState = any;

class ServerLogTimeFilterInputs extends Component<
  ServerLogTimeFilterInputsProps,
  ServerLogTimeFilterInputsState
> {
  // eslint-disable-next-line react/sort-comp
  _timeoutID: any;

  //
  // Static

  static getDerivedStateFromProps(nextProps: any, prevState: any) {
    // Respect external changes to the filter value, e.g. `clearFilter` action.
    if (
      nextProps.timeFilterStart !== prevState.timeFilterStartFromStore ||
      nextProps.timeFilterEnd !== prevState.timeFilterEndFromStore
    ) {
      return {
        timeFilterStart: nextProps.timeFilterStart,
        timeFilterStartFromStore: nextProps.timeFilterStart,
        timeFilterEnd: nextProps.timeFilterEnd,
        timeFilterEndFromStore: nextProps.timeFilterEnd,
      };
    }

    // No change
    return null;
  }

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      timeFilterStart: props.timeFilterStart,
      timeFilterStartFromStore: props.timeFilterStart,
      timeFilterEnd: props.timeFilterEnd,
      timeFilterEndFromStore: props.timeFilterEnd,
    };
  }

  componentWillUnmount() {
    this._clearOnTextFilterChangeTimeout();
  }

  //
  // Control

  _flushValues = () => {
    this._timeoutID = window.setTimeout(() => {
      this._timeoutID = null;
      this.props.onTimeFilterChange(
        this.state.timeFilterStart,
        this.state.timeFilterEnd
      );
    }, 200);
  };

  _clearOnTextFilterChangeTimeout() {
    if (this._timeoutID) {
      window.clearTimeout(this._timeoutID);
    }
  }

  //
  // Listeners

  onFilterStartChange = (timeFilterStart: any) => {
    this._clearOnTextFilterChangeTimeout();

    this.setState({ timeFilterStart }, this._flushValues);
  };

  onFilterEndChange = (timeFilterEnd: any) => {
    this._clearOnTextFilterChangeTimeout();

    this.setState({ timeFilterEnd }, this._flushValues);
  };

  //
  // Render

  render() {
    const { isTimeFilterStartValid, isTimeFilterEndValid } = this.props;

    const { timeFilterStart, timeFilterEnd } = this.state;

    return (
      <div className={styles.container}>
        <TextInput
          className={styles.input}
          hasError={!isTimeFilterStartValid}
          initialValue={timeFilterStart}
          name="timeFilterStart"
          placeholder="Jun 15, 2018 09:00:00"
          size={LARGE}
          value={timeFilterStart}
          onChange={this.onFilterStartChange}
        />

        <span className={styles.separator}>{t('to')}</span>

        <TextInput
          className={styles.input}
          hasError={!isTimeFilterEndValid}
          initialValue={timeFilterEnd}
          name="timeFilterEnd"
          placeholder="Jun 15, 2018 09:01:00"
          size={LARGE}
          value={timeFilterEnd}
          onChange={this.onFilterEndChange}
        />
      </div>
    );
  }
}

export default ServerLogTimeFilterInputs;
