/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import ConnectionModel from 'app/js/models/server/ConnectionModel';
import { addLogLine } from 'app/js/stores/actions/ui/serverLogPageActions';
import BaseMediaServerEventConnection from 'app/js/utils/pubsub/BaseMediaServerEventConnection';
import * as messageTypes from 'app/js/utils/pubsub/messageTypes';

const MESSAGE_TYPES = [messageTypes.LOG];

const MediaServerLogEventConnection = BaseMediaServerEventConnection.extend({
  //
  // Properties

  name: 'MediaServerLogEventConnection',

  url() {
    const activeConnection = this.server.get('activeConnection');
    const socketConnectionUri = activeConnection.id.replace(/^http/, 'ws');

    // If the the app is being served over https we want to use wss://
    // instead of ws:// to prevent warnings about insecure connections.
    return this.server.resolveUrl('/:/websockets/notifications', {
      params: {
        filters: 'log',
      },
      connection: new ConnectionModel({ uri: socketConnectionUri }),
      appendToken: true,
    });
  },

  //
  // Messages

  getMessageTypes() {
    return MESSAGE_TYPES;
  },

  [messageTypes.LOG](message: any) {
    const appStore = resolve('appStore');
    appStore.dispatch(
      addLogLine({ message, serverEntityID: this.server.entityID() })
    );
  },
});

export default MediaServerLogEventConnection;
