/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import selectUser from 'app/js/stores/selectors/user/selectUser';
import ServerAvatar from './ServerAvatar';

const mapStateToProps = createSelector(selectUser, (user) => {
  const avatarUrl = user.isSignedIn ? user.thumb : null;

  return {
    avatarUrl,
  };
});

export default connect(mapStateToProps)(ServerAvatar);
