/* eslint-plugin-disable @typescript-eslint */

import t from 'app/js/utils/t';

function getServerStatus(props: any) {
  const activeConnection = props.activeConnection;
  let isConnecting = false;
  let status;

  if (props.isConnected && activeConnection) {
    if (props.isOutOfDate) {
      status = t('Needs Update');
    } else if (activeConnection.relay) {
      status = t('Indirect');
    } else if (activeConnection.isPrivate) {
      status = t('Nearby');
    } else {
      status = t('Remote');
    }
  } else if (props.isRelayed) {
    status = t('Indirect');
  } else if (props.isUnavailable) {
    status = t('Unavailable');
  } else if (props.isUnauthorized) {
    status = t('Unauthorized');
  } else {
    isConnecting = true;
    status = t('Connecting');
  }

  return { isConnecting, status };
}

export default getServerStatus;
