/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { Component } from 'react';
import _ from 'underscore';
// @ts-expect-error ts-migrate(6133) FIXME: 'SMALL' is declared but its value is never read.
import { LARGE, SMALL } from 'app/js/views/v3/helpers/props/sizes';
import styles from './SelectInput.css';

interface SelectInputProps {
  className?: string;
  errorClassName?: string;
  disabledClassName?: string;
  isDisabled?: boolean;
  size?: any; // TODO: PropTypes.oneOf([SMALL, LARGE])
  name: string;
  initialValue?: string | number;
  value: string | number;
  values?: string[] | any[];
  hasError?: boolean;
  errors?: string[];
  validations?: ((...args: any[]) => any)[];
  onChange: (...args: any[]) => any;
}

class SelectInput extends Component<SelectInputProps> {
  static defaultProps = {
    className: styles.input,
    errorClassName: styles.hasError,
    disabledClassName: styles.isDisabled,
    isDisabled: false,
    size: LARGE,
    hasError: false,
  };

  //
  // Listeners

  onChange = (event: any) => {
    this.props.onChange(event.target.value);
  };

  //
  // Render

  render() {
    const {
      className,
      errorClassName,
      disabledClassName,
      isDisabled,
      size,
      name,
      initialValue,
      value,
      values,
      hasError,
      errors,
      validations,
      ...otherProps
    } = this.props;

    return (
      <div
        className={classNames(
          className,
          styles[size],
          isDisabled && disabledClassName,
          hasError && errorClassName
        )}
      >
        <select
          {...otherProps}
          className={styles.select}
          disabled={isDisabled}
          id={name}
          name={name}
          value={value}
          onChange={this.onChange}
        >
          {/* @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'. */}
          {values.map((valueItem: any, i: any) => {
            if (_.isString(valueItem)) {
              return (
                <option key={i} value={valueItem}>
                  {valueItem}
                </option>
              );
            }

            const [key] = _.keys(valueItem);

            return (
              <option key={i} value={key}>
                {_.result(valueItem, key)}
              </option>
            );
          })}
        </select>
      </div>
    );
  }
}

export default SelectInput;
