/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import Link, { LinkProps } from 'app/js/views/v3/components/core/buttons/Link';
import TooltipTrigger from 'app/js/views/v3/components/core/tooltips/TooltipTrigger';
import * as tooltipAttachments from 'app/js/views/v3/helpers/props/tooltipAttachments';
import styles from './ToolbarButton.css';

interface ToolbarButtonProps extends LinkProps {
  tooltip?: string;
  className?: string;
  hasLabel?: boolean;
  tooltipAttachment?: string;
  hideOnPress?: boolean;
  onPress?: (...args: any[]) => any;
}

class ToolbarButton extends Component<ToolbarButtonProps> {
  static defaultProps = {
    className: styles.toolbarButton,
    tooltipAttachment: tooltipAttachments.BOTTOM_CENTER,
  };

  //
  // Render

  render() {
    const {
      children,
      tooltip,
      className,
      hasLabel,
      tooltipAttachment,
      hideOnPress,
      onPress,
      ...otherProps
    } = this.props;

    // @ts-expect-error ts-migrate(2538) FIXME: Type 'undefined' cannot be used as an index type.
    const tooltipClassName = styles[tooltipAttachment];
    const tooltipFocusedClassName = `${tooltipClassName} ${styles.hasFocus}`;

    return (
      <TooltipTrigger
        aria-label={tooltip}
        className={className}
        // @ts-expect-error ts-migrate(2739) FIXME: Type 'typeof Link' is missing the following proper... Remove this comment to see the full error message
        component={Link}
        hideOnPress={hideOnPress}
        tooltip={tooltip}
        tooltipClassName={tooltipClassName}
        tooltipFocusedClassName={tooltipFocusedClassName}
        onPress={onPress}
        {...otherProps}
      >
        {children}
      </TooltipTrigger>
    );
  }
}

export default ToolbarButton;
