/* eslint-plugin-disable @typescript-eslint */

import t from 'app/js/utils/t';

function getLogLevelLabel(level: any) {
  switch (level) {
    case 0:
      return t('Error');
    case 1:
      return t('Warning');
    case 2:
      return t('Info');
    case 3:
      return t('Debug');
    case 4:
      return t('Verbose');
    default:
      return t('Unknown');
  }
}

export default getLogLevelLabel;
