import { useCallback, useEffect, useRef, useState } from 'react';
import { ScrollPosition } from 'app/js/v4/core/ui/scrollers/scrollerTypes';
import pageStorage from 'app/js/views/v3/helpers/pageStorage';

interface ScrollRestoreOptions {
  key: string;
  shouldRestoreImmediately?: boolean;
  onScrollPositionChange?(position: ScrollPosition): void;
}

interface ScrollRestoreResult {
  isWaitingToRestoreScrollPosition: boolean;
  scrollRestoreProps: {
    targetScrollTop?: number;
    targetScrollLeft?: number;
    smoothScroll?: boolean;
    onScrollPositionChange(scrollPosition: ScrollPosition): void;
  };
  onScrollPositionReady(): void;
}

export default function useScrollRestore({
  key,
  shouldRestoreImmediately = false,
  onScrollPositionChange: onScrollPositionChangeFromProps,
}: ScrollRestoreOptions): ScrollRestoreResult {
  const storageKey = pageStorage.getKey(`${key}!scrollPosition`);
  const lastScrollPositionRef = useRef<ScrollPosition | null>(null);

  const [isRestoring, setIsRestoring] = useState(!!pageStorage.get(storageKey));
  const [targetScrollTop, setTargetScrollTop] = useState(undefined);
  const [targetScrollLeft, setTargetScrollLeft] = useState(undefined);

  //
  // Callbacks

  const onScrollPositionReady = useCallback(() => {
    const restorePosition = pageStorage.get(storageKey);

    if (restorePosition) {
      setTargetScrollTop(restorePosition.scrollTop);
      setTargetScrollLeft(restorePosition.scrollLeft);
    }
  }, [storageKey]);

  const onScrollPositionChange = useCallback(
    (scrollPosition: ScrollPosition) => {
      lastScrollPositionRef.current = scrollPosition;

      if (onScrollPositionChangeFromProps) {
        onScrollPositionChangeFromProps(scrollPosition);
      }
    },
    [onScrollPositionChangeFromProps]
  );

  //
  // Effects

  useEffect(() => {
    const restorePosition = pageStorage.get(storageKey);

    if (restorePosition) {
      setIsRestoring(true);

      if (shouldRestoreImmediately) {
        setTargetScrollTop(restorePosition.scrollTop);
        setTargetScrollLeft(restorePosition.scrollLeft);
      }
    }

    return () => {
      const lastScrollPosition = lastScrollPositionRef.current;
      lastScrollPositionRef.current = null;

      if (
        lastScrollPosition &&
        (lastScrollPosition.scrollTop || lastScrollPosition.scrollLeft)
      ) {
        pageStorage.set(storageKey, lastScrollPosition);
      } else {
        pageStorage.remove(storageKey);
      }
    };
  }, [shouldRestoreImmediately, storageKey]);

  useEffect(() => {
    if (targetScrollTop || targetScrollLeft) {
      setIsRestoring(false);
      setTargetScrollTop(undefined);
      setTargetScrollLeft(undefined);
    }
  }, [targetScrollLeft, targetScrollTop]);

  //
  // Return

  return {
    isWaitingToRestoreScrollPosition: isRestoring,

    scrollRestoreProps: isRestoring
      ? {
          targetScrollTop,
          targetScrollLeft,
          smoothScroll: false,
          onScrollPositionChange,
        }
      : { onScrollPositionChange },

    onScrollPositionReady,
  };
}
