/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import t from 'app/js/utils/t';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import PageConnector from 'app/js/views/v3/components/pages/PageConnector';
import ServerLogPageHeaderConnector from 'app/js/views/v3/components/pages/serverSettings/panes/logs/ServerLogPageHeaderConnector';
import * as scrollDirections from 'app/js/views/v3/helpers/props/scrollDirections';
import IconCircleBackslash from 'app/glyphs/circle-backslash-560.svg';
import ServerLogConnector from './ServerLogConnector';
import styles from './ServerLogPage.css';

const TOO_MANY_FILES = 1;
const ONLY_LOG_FILES_ALLOWED = 2;

// @ts-expect-error ts-migrate(7030) FIXME: Not all code paths return a value.
function getFileDragError(event: any) {
  const items = event.dataTransfer.items;
  const item = items[0];

  if (item.type !== '' || item.kind !== 'file') {
    return ONLY_LOG_FILES_ALLOWED;
  } else if (items.length > 1) {
    return TOO_MANY_FILES;
  }
}

type ServerLogPageProps = {
  title: string;
  metricsPage: string;
  metricsPane?: string;
  onDrop: (...args: any[]) => any;
};

type ServerLogPageState = any;

class ServerLogPage extends Component<ServerLogPageProps, ServerLogPageState> {
  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      title: props.title,
      isDraggingFile: false,
      isDroppingFile: false,
      fileDragError: null,
    };
  }

  //
  // Listeners

  onDragOver = (event: any) => {
    this.setState({
      isDraggingFile: true,
      fileDragError: getFileDragError(event),
    });

    event.preventDefault();
  };

  onDragLeave = () => {
    this.setState({
      isDraggingFile: false,
      fileDragError: null,
    });
  };

  onDrop = (event: any) => {
    event.preventDefault();

    const fileDragError = getFileDragError(event);

    if (fileDragError) {
      // If a file drop can be rejected before parsing, treat it as a no-op
      this.setState({
        isDraggingFile: false,
        fileDragError: null,
      });

      return;
    }

    this.setState({
      isDroppingFile: true,
    });

    const file = event.dataTransfer.files[0];
    const fileReader = new window.FileReader();
    fileReader.onload = () => {
      this.setState({
        title: file.name,
        isDraggingFile: false,
        isDroppingFile: false,
      });

      this.props.onDrop({ result: fileReader.result });
    };
    fileReader.readAsText(file);
  };

  //
  // Render

  render() {
    const { metricsPage, metricsPane } = this.props;

    const { title, isDraggingFile, isDroppingFile, fileDragError } = this.state;

    const draggingFileMessage =
      isDraggingFile &&
      !isDroppingFile &&
      ((fileDragError === TOO_MANY_FILES && t('Only one file at a time')) ||
        (fileDragError === ONLY_LOG_FILES_ALLOWED &&
          t('Only log files are allowed')) ||
        t('Load log file'));

    return (
      <PageConnector
        metricsPage={metricsPage}
        metricsProperties={{ pane: metricsPane }}
        scrollDirection={scrollDirections.NONE}
        onDragLeave={this.onDragLeave}
        onDragOver={this.onDragOver}
        onDrop={this.onDrop}
      >
        <ServerLogPageHeaderConnector title={title} />
        <ServerLogConnector />

        {isDraggingFile ? (
          <div className={styles.dropFileOverlay}>
            {fileDragError ? (
              <IconCircleBackslash className={styles.icon} />
            ) : null}
            {draggingFileMessage}
            {isDroppingFile ? <Spinner /> : null}
          </div>
        ) : null}
      </PageConnector>
    );
  }
}

export default ServerLogPage;
