import React, { ReactElement, ReactNode } from 'react';
import styles from './ServerDashboardPageMessage.css';

interface ServerDashboardPageMessageProps {
  children: ReactNode;
  className?: string;
}

export default function ServerDashboardPageMessage({
  children,
  className = styles.container,
}: ServerDashboardPageMessageProps): ReactElement {
  return (
    <div className={className}>
      <div className={styles.message}>{children}</div>
    </div>
  );
}
