import { createSelector, Selector } from 'reselect';
import * as activityTypes from 'app/js/utils/server/activityTypes';
import createServerActivitiesSelector from 'app/js/v4/core/serverActivities/selectors/createServerActivitiesSelector';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

function createIsServerLibrariesUpdatingSelector(
  server: Server
): Selector<LegacyRootState, boolean> {
  return createSelector(createServerActivitiesSelector(server), (activities) =>
    activities.some(({ type }) => type === activityTypes.LIBRARY_UPDATE_SECTION)
  );
}

export default createIsServerLibrariesUpdatingSelector;
