/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import * as serverActions from 'app/js/stores/actions/serverActions';
import * as serverActivitySelectors from 'app/js/stores/selectors/server/createServerActivitySelector';
import createServerDownloadUrlSelector from 'app/js/stores/selectors/server/createServerDownloadUrlSelector';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import ServerTroubleshootingPane from './ServerTroubleshootingPane';

function createMapStateToProps() {
  return createSelector(
    createUnstableServerSelector(),
    createServerDownloadUrlSelector('/diagnostics/databases'),
    createServerDownloadUrlSelector('/diagnostics/logs'),
    serverActivitySelectors.createServerCleanBundlesActivitySelector(),
    serverActivitySelectors.createServerOptimizeDatabaseActivitySelector(),
    (
      server,
      downloadDatabasesUrl,
      downloadLogsUrl,
      cleanBundlesActivity,
      optimizeDatabaseActivity
    ) => {
      const diagnostics = server.diagnostics;

      return {
        machineIdentifier: server.machineIdentifier,
        downloadDatabasesUrl,
        downloadLogsUrl,
        cleanBundlesError: server[__PLATFORM__].cleanBundlesError,
        optimizeDatabaseError: server[__PLATFORM__].optimizeDatabaseError,
        isCleanBundlesInProgress: !!cleanBundlesActivity,
        isOptimizeDatabaseInProgress: !!optimizeDatabaseActivity,
        showDownloadDatabases: !!(
          downloadDatabasesUrl &&
          diagnostics &&
          diagnostics.indexOf('databases') !== -1
        ),
        showDownloadLogs: !!(
          downloadLogsUrl &&
          diagnostics &&
          diagnostics.indexOf('logs') !== -1
        ),
      };
    }
  );
}

const mapDispatchToProps = {
  dispatchCleanBundles: serverActions.cleanBundles,
  dispatchOptimizeDatabase: serverActions.optimizeDatabase,
};

type ServerTroubleshootingPaneConnectorProps = {
  serverEntityID: string;
  dispatchCleanBundles: (...args: any[]) => any;
  dispatchOptimizeDatabase: (...args: any[]) => any;
};

export class ServerTroubleshootingPaneConnector extends Component<ServerTroubleshootingPaneConnectorProps> {
  //
  // Control

  onCleanBundlesPress = () => {
    this.props.dispatchCleanBundles({
      serverEntityID: this.props.serverEntityID,
      hideAlerts: true,
    });
  };

  onOptimizeDatabasePress = () => {
    this.props.dispatchOptimizeDatabase({
      serverEntityID: this.props.serverEntityID,
      hideAlerts: true,
    });
  };

  //
  // Render

  render() {
    const { dispatchCleanBundles, dispatchOptimizeDatabase, ...otherProps } =
      this.props;

    return (
      // @ts-expect-error ts-migrate(2739) FIXME: Type '{ serverEntityID: string; children?: ReactNo... Remove this comment to see the full error message
      <ServerTroubleshootingPane
        onCleanBundlesPress={this.onCleanBundlesPress}
        onOptimizeDatabasePress={this.onOptimizeDatabasePress}
        {...otherProps}
      />
    );
  }
}

export default connect(
  createMapStateToProps,
  mapDispatchToProps
)(ServerTroubleshootingPaneConnector);
