/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import t from 'app/js/utils/t';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import VirtualListScroller from 'app/js/views/v3/components/core/lists/VirtualListScroller';
import createVerticalLayout from 'app/js/views/v3/helpers/layouts/createVerticalLayout';
import ServerLogLineConnector from './ServerLogLineConnector';
import ServerLogLineModal from './ServerLogLineModal';
import ServerLogRawText from './ServerLogRawText';
import styles from './ServerLog.css';

const LINE_HEIGHT = 26;
const layout = createVerticalLayout(LINE_HEIGHT, 10, {
  outerPaddingTop: 10,
  outerPaddingBottom: 10,
});

function getInitialState() {
  return {
    isSticky: true,
    scrollIndex: 0,
    scrollIndexAfterFilterChange: null,
    selectedLogLine: null,
  };
}

function hasFilter(props: any) {
  return !!(
    props.filter ||
    props.timeFilterStart ||
    props.timeFilterEnd ||
    props.logLevels.length
  );
}

type ServerLogProps = {
  filter: string;
  timeFilterStart: string;
  timeFilterEnd: string;
  logLevels: any[];
  showRawText: boolean;
  logLines: any[];
  onTextFilterChange: (...args: any[]) => any;
  onTimeFilterChange: (...args: any[]) => any;
  onHighlightLine: (...args: any[]) => any;
  onClearFilter: (...args: any[]) => any;
};

type ServerLogState = any;

class ServerLog extends Component<ServerLogProps, ServerLogState> {
  // eslint-disable-next-line react/sort-comp
  _lastScrollTop: any;
  _scrollerRef: any;

  //
  // Static

  static getDerivedStateFromProps(nextProps: any, prevState: any) {
    if (
      prevState.scrollIndexAfterFilterChange != null &&
      !hasFilter(nextProps)
    ) {
      return {
        isSticky: false,
        scrollIndex: prevState.scrollIndexAfterFilterChange,
        scrollIndexAfterFilterChange: null,
      };
    }

    if (
      nextProps.showRawText &&
      (!prevState.isSticky ||
        prevState.scrollIndex ||
        prevState.selectedLogLine)
    ) {
      return getInitialState();
    }

    return null;
  }

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = getInitialState();
    this._scrollerRef = React.createRef();
  }

  //
  // Listeners

  onScroll = () => {
    const scrollerRef = this._scrollerRef.current;

    if (
      scrollerRef.scrollTop + scrollerRef.clientHeight >=
      scrollerRef.scrollHeight
    ) {
      this.setState({
        isSticky: true,
        scrollIndex: null,
      });
    } else if (scrollerRef.scrollTop < this._lastScrollTop) {
      this.setState({ isSticky: false });
    }

    this._lastScrollTop = scrollerRef.scrollTop;
  };

  onJumpToContext = (scrollIndex: any) => {
    // If there is an active filter, we need to clear in order to scroll to the
    // correct index.

    if (hasFilter(this.props)) {
      this.setState(
        {
          isSticky: false,
          scrollIndexAfterFilterChange: scrollIndex,
          selectedLogLine: null,
        },
        () => {
          this.props.onClearFilter();
        }
      );
    } else {
      this.setState({
        isSticky: false,
        scrollIndex,
        selectedLogLine: null,
      });
    }
  };

  onTextFilterChange = (...args: any[]) => {
    this.setState({ scrollIndex: 0, selectedLogLine: null });
    this.props.onTextFilterChange(...args);
  };

  onTimeFilterChange = (...args: any[]) => {
    this.setState({ scrollIndex: 0, selectedLogLine: null });
    this.props.onTimeFilterChange(...args);
  };

  onLogLinePress = (logLine: any) => {
    this.setState({ selectedLogLine: logLine });
    this.props.onHighlightLine(logLine);
  };

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onModalClose = (event: any) => {
    this.setState({ selectedLogLine: null });
  };

  onJumpToLatestPress = () => {
    this.setState({ isSticky: true });
  };

  //
  // Render

  renderLogLine = (cellProps: any) => {
    return (
      <ServerLogLineConnector
        {...cellProps}
        onLogLinePress={this.onLogLinePress}
      />
    );
  };

  render() {
    const {
      filter,
      timeFilterStart,
      timeFilterEnd,
      logLevels,
      showRawText,
      logLines,
    } = this.props;

    const { isSticky, scrollIndex, selectedLogLine } = this.state;

    const showSelectedLogLineModal = !!selectedLogLine;
    const hideLogLinesScroller = showSelectedLogLineModal || showRawText;

    return (
      <>
        {showRawText ? (
          <ServerLogRawText
            className={styles.rawTextScroller}
            filter={filter}
            lines={logLines}
            logLevels={logLevels}
          />
        ) : null}
        <VirtualListScroller
          ref={this._scrollerRef}
          // @ts-expect-error ts-migrate(2322) FIXME: Type '{ className: string; scrollerClassName: stri... Remove this comment to see the full error message
          cellRenderer={this.renderLogLine}
          className={
            hideLogLinesScroller ? styles.hiddenPageContent : styles.pageContent
          }
          items={logLines}
          keyPropName="index"
          layout={layout}
          scrollIndex={isSticky ? logLines.length - 1 : scrollIndex}
          scrollerClassName={styles.pageContentScroller}
          size={logLines.length}
          smoothScroll={false}
          onScroll={this.onScroll}
        />
        <Link
          className={isSticky ? styles.jumpLinkHidden : styles.jumpLinkVisible}
          onPress={this.onJumpToLatestPress}
        >
          {t('Jump to latest')}
        </Link>
        {showSelectedLogLineModal ? (
          <ServerLogLineModal
            {...selectedLogLine}
            filter={filter}
            timeFilterEnd={timeFilterEnd}
            timeFilterStart={timeFilterStart}
            onClose={this.onModalClose}
            onJumpToContext={this.onJumpToContext}
            onTextFilterChange={this.onTextFilterChange}
            onTimeFilterChange={this.onTimeFilterChange}
          />
        ) : null}
      </>
    );
  }
}

export default ServerLog;
