import React, { ChangeEvent, ReactElement, useCallback, useState } from 'react';
import { resolve } from 'app/js/adapters/adapters';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import t from 'app/js/utils/t';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import SpinnerButton from 'app/js/views/v3/components/core/buttons/SpinnerButton';
import MetricsClickTracker from 'app/js/views/v3/components/core/metrics/MetricsClickTracker';
import Modal from 'app/js/views/v3/components/core/modals/Modal';
import ModalBody from 'app/js/views/v3/components/core/modals/ModalBody';
import ModalContent from 'app/js/views/v3/components/core/modals/ModalContent';
import ModalFooter from 'app/js/views/v3/components/core/modals/ModalFooter';
import ModalFooterButtonBar from 'app/js/views/v3/components/core/modals/ModalFooterButtonBar';
import ModalHeader from 'app/js/views/v3/components/core/modals/ModalHeader';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import styles from './ConfirmModal.css';

interface ConfirmModalProps {
  className?: string;
  containerClassName?: string;
  isCancelButtonDisabled?: boolean;
  isConfirmButtonSpinning?: boolean;
  isOpen: boolean;
  metricsPage: string;
  metricsProperties?: any;
  kind?: typeof kinds[keyof typeof kinds];
  title: string;
  message: React.ReactNode | string;
  infoMessage?: string;
  accordMessage?: string;
  confirmLabel?: string;
  cancelLabel?: string;
  hideCancelButton?: boolean;
  onConfirm: (...args: any[]) => any;
  onCancel: (...args: any[]) => any;
}

function ConfirmModal(props: ConfirmModalProps): ReactElement {
  const { onCancel: onCancelFromProps } = props;
  const [isAccordGiven, setIsAccordGiven] = useState(false);

  const onCancel = useCallback(() => {
    const metrics = resolve('metrics');

    metrics.trackClick({
      ...props.metricsProperties,
      page: props.metricsPage,
      action: metricsActions.CANCEL,
    });

    onCancelFromProps();
    // The `metricsProperties` reference will change with each render, but only
    // update the callback if the property values change.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    onCancelFromProps,
    props.metricsPage,
    // eslint-disable-next-line react-hooks/exhaustive-deps
    JSON.stringify(props.metricsProperties),
  ]);

  const onAccordChange = useCallback((event: ChangeEvent<HTMLInputElement>) => {
    setIsAccordGiven(event.target.checked);
  }, []);

  const {
    containerClassName,
    isCancelButtonDisabled,
    isConfirmButtonSpinning = false,
    isOpen,
    metricsPage,
    metricsProperties,
    kind = kinds.PRIMARY,
    title,
    message,
    accordMessage,
    confirmLabel,
    cancelLabel,
    hideCancelButton,
    infoMessage,
    onConfirm,
  } = props;

  const isAccordRequired = !!accordMessage;
  const isConfirmButtonDisabled =
    isConfirmButtonSpinning || (isAccordRequired && !isAccordGiven);

  //
  // Render

  return (
    <Modal
      containerClassName={containerClassName}
      isOpen={isOpen}
      metricsPage={metricsPage}
      metricsProperties={metricsProperties}
      size={sizes.SMALL}
      onModalClose={onCancel}
    >
      <ModalContent onModalClose={onCancel}>
        <ModalHeader>{title}</ModalHeader>

        <ModalBody className={styles.confirmBody}>
          <p>{message}</p>
          {infoMessage ? <p>{infoMessage}</p> : null}
          {isAccordRequired ? (
            <label className={styles.accordLabel}>
              <input
                checked={isAccordGiven}
                className={styles.accordInput}
                type="checkbox"
                onChange={onAccordChange}
              />
              {accordMessage}
            </label>
          ) : null}
        </ModalBody>

        <ModalFooter>
          <ModalFooterButtonBar>
            {!hideCancelButton ? (
              <Button
                className={styles.cancelButton}
                isDisabled={isCancelButtonDisabled}
                kind={kinds.DEFAULT}
                onPress={onCancel}
              >
                {cancelLabel || t('Cancel')}
              </Button>
            ) : null}

            <MetricsClickTracker
              {...metricsProperties}
              action={metricsActions.CONFIRM}
            >
              <SpinnerButton
                className={styles.confirmButton}
                data-autofocus={true}
                isDisabled={isConfirmButtonDisabled}
                isSpinning={isConfirmButtonSpinning}
                kind={kind}
                onPress={onConfirm}
              >
                {confirmLabel || t('OK')}
              </SpinnerButton>
            </MetricsClickTracker>
          </ModalFooterButtonBar>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
}

export default ConfirmModal;
