/* eslint-plugin-disable @typescript-eslint */

import React from 'react';

function getPath(
  x: any,
  y: any,
  width: any,
  height: any,
  initialRadius: any,
  offset: any
) {
  const finalY = y - offset;

  // Code adapted from https://github.com/recharts/recharts/blob/master/src/shape/Rectangle.js#L12
  const sign = height >= 0 ? 1 : -1;
  const clockWise = height >= 0 ? 1 : 0;
  const maxRadius = Math.min(Math.abs(width) / 2, Math.abs(height) / 2);
  const radius = Math.min(maxRadius, initialRadius);

  return `M ${x},${finalY + sign * radius}
    A ${radius},${radius},0,0,${clockWise},${x + radius},${finalY}
    L ${x + width - radius},${finalY}
    A ${radius},${radius},0,0,${clockWise},${x + width},${
    finalY + sign * radius
  }
    L ${x + width},${finalY + height - sign * radius}
    A ${radius},${radius},0,0,${clockWise},${x + width - radius},${
    finalY + height
  }
    L ${x + radius},${finalY + height}
    A ${radius},${radius},0,0,${clockWise},${x},${
    finalY + height - sign * radius
  } Z`;
}

type BarProps = {
  x: number;
  y: number;
  width: number;
  height: number;
  radius?: number;
  fill?: string;
  barGap?: number;
  data: any[];
  dataKey: string;
  seriesOrdering: any[];
  seriesIndex: number;
};

function Bar(props: BarProps) {
  const {
    x,
    y,
    width,
    height,
    radius,
    fill,
    barGap,
    data,
    dataKey,
    seriesOrdering,
    seriesIndex,
  } = props;

  // In order to add a gap between stacked bar chart segments we need to
  // calculate the offset each segment is shifted by. This offset depends on
  // how many previous segments were rendered.
  const previousSeries = seriesOrdering.slice(0, seriesIndex);
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  const barValues = data.find((values) => values[dataKey] === props[dataKey]);

  // Count how many previous non-zero value bar segments were rendered.
  const previousBarSegmentsCount = previousSeries.reduce((acc, seriesKey) => {
    if (barValues[seriesKey] !== 0) {
      acc++;
    }

    return acc;
  }, 0);

  // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
  const offset = previousBarSegmentsCount * barGap;

  return <path d={getPath(x, y, width, height, radius, offset)} fill={fill} />;
}

export default Bar;
