import React, {
  ChangeEvent,
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { FormProvider, useForm } from 'react-hook-form';
import useMeasure from 'app/js/v4/core/ui/hooks/useMeasure';
import VirtualList from 'app/js/v4/core/ui/lists/components/VirtualList';
import { VirtualCellFactory } from 'app/js/v4/core/ui/lists/listsTypes';
import createVerticalLayout from 'app/js/v4/core/ui/lists/utils/createVerticalLayout';
import {
  ChannelMappingFormData,
  SelectedChannelMapping,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import dimensions from 'app/js/views/v3/styles/variables/dimensions';
import DeviceChannelMapping from './DeviceChannelMapping';
import DeviceChannelMappingsHeader from './DeviceChannelMappingsHeader';
import styles from './DeviceChannelMappingsForm.css';

const layout = createVerticalLayout({
  rowHeight: parseInt(dimensions.dvrSetupChannelMappingItemHeight, 10),
  overscan: 1,
});

export interface ChannelMappingChangeProps {
  index: number;
  enabled?: boolean;
  channelKey?: string;
  lineupIdentifier?: string;
}

interface DeviceChannelMappingsFormProps {
  id: string;
  height: number;
  scrollTop: number;
  scrollLeft: number;
  channelMappings: SelectedChannelMapping[];
  onChannelMappingChange?({
    index,
    enabled,
    channelKey,
    lineupIdentifier,
  }: ChannelMappingChangeProps): void;
  onSubmit(data: ChannelMappingFormData[]): void;
}

export default function DeviceChannelMappingsForm({
  id,
  height,
  scrollTop,
  scrollLeft,
  channelMappings,
  onChannelMappingChange,
  onSubmit,
}: DeviceChannelMappingsFormProps): ReactElement {
  const [allChannelMappingsEnabled, setAllChannelMappingsEnabled] =
    useState(false);
  const [allChannelMappingsDisabled, setAllChannelMappingsDisabled] =
    useState(false);

  const [measureRef, { width }] = useMeasure();

  const formMethods = useForm({
    defaultValues: channelMappings,
  });
  const { handleSubmit, register, setValue, watch } = formMethods;

  const watchedFields = watch();

  //
  // Effects

  useEffect(() => {
    let allDisabled = true;
    let allEnabled = true;

    Object.values(watchedFields).forEach(({ drm, enabled }) => {
      if (enabled) {
        allDisabled = false;
      } else if (!drm) {
        allEnabled = false;
      }
    });

    setAllChannelMappingsDisabled(allDisabled);
    setAllChannelMappingsEnabled(allEnabled);
  }, [
    watchedFields,
    setAllChannelMappingsDisabled,
    setAllChannelMappingsEnabled,
  ]);

  useEffect(() => {
    channelMappings.forEach((_mapping, index) => {
      register(`[${index}].enabled`);
      register(`[${index}].channelKey`);
      register(`[${index}].key`);
      register(`[${index}].lineupIdentifier`);
      register(`[${index}].drm`);
    });
  }, [channelMappings, register]);

  //
  // Callbacks

  const onAllChannelMappingsEnabledChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>): void => {
      channelMappings.forEach((mapping, index) => {
        const enabled = event.currentTarget.checked;

        if (!mapping.drm) {
          setValue(`[${index}]`, { enabled });

          onChannelMappingChange?.({ index, enabled });
        }
      });
    },
    [channelMappings, setValue, onChannelMappingChange]
  );

  const onChannelMappingEnabledChange = useCallback(
    (enabled: boolean, index: number): void => {
      setValue(`[${index}]`, { enabled });

      onChannelMappingChange?.({ index, enabled });
    },
    [setValue, onChannelMappingChange]
  );

  const onChannelMappingChannelKeyChange = useCallback(
    (channelKey: string, lineupIdentifier: string, index: number): void => {
      setValue(`[${index}]`, { channelKey, lineupIdentifier });

      onChannelMappingChange?.({ index, channelKey, lineupIdentifier });
    },
    [setValue, onChannelMappingChange]
  );

  const onFormSubmit = handleSubmit((data): void => {
    const parsedData = Object.values(data).map<ChannelMappingFormData>(
      ({ key, lineupIdentifier, channelKey, enabled }) => ({
        key,
        lineupIdentifier,
        channelKey,
        enabled,
      })
    );

    onSubmit(parsedData);
  });

  //
  // Render

  const cellComponentFactory: VirtualCellFactory<SelectedChannelMapping> =
    useMemo(() => {
      return (cellProps) => {
        // `cellProps` will never have a null item, but VirtualCellFactory
        // anticipates being used with a paginated list which this is not. Type
        // narrow the item so that typescript is happy with out having to adjust
        // the types for VirtualCellFactory
        if (!cellProps.item) {
          return <></>;
        }

        const enabled: boolean = watch(`[${cellProps.row}].enabled`);
        const channelKey: string = watch(`[${cellProps.row}].channelKey`);
        const lineupIdentifier: string = watch(
          `[${cellProps.row}].lineupIdentifier`
        );

        return (
          <DeviceChannelMapping
            {...cellProps}
            channelKey={channelKey}
            enabled={enabled}
            lineupIdentifier={lineupIdentifier}
            onChannelKeyChange={onChannelMappingChannelKeyChange}
            onEnabledChange={onChannelMappingEnabledChange}
          />
        );
      };
    }, [
      watch,
      onChannelMappingEnabledChange,
      onChannelMappingChannelKeyChange,
    ]);

  return (
    <FormProvider {...formMethods}>
      <form
        ref={measureRef}
        className={styles.container}
        id={id}
        onSubmit={onFormSubmit}
      >
        <DeviceChannelMappingsHeader
          allChannelMappingsDisabled={allChannelMappingsDisabled}
          allChannelMappingsEnabled={allChannelMappingsEnabled}
          onAllChannelMappingsEnabledChange={onAllChannelMappingsEnabledChange}
        />

        <VirtualList
          cellComponentFactory={cellComponentFactory}
          className={styles.scroller}
          items={channelMappings}
          layout={layout}
          scrollLeft={scrollLeft}
          scrollTop={scrollTop}
          totalSize={channelMappings.length}
          viewportHeight={height}
          viewportWidth={width}
        />
      </form>
    </FormProvider>
  );
}
