import React, { ChangeEvent, ReactElement, useCallback, useMemo } from 'react';
import { useDispatch } from 'react-redux';
import t from 'app/js/utils/t';
import Select from 'app/js/v4/core/ui/forms/components/Select';
import { SelectedEPGLineupGroup } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import styles from './EPGLineupSelect.css';

interface EPGLineupOption {
  value: string;
  label: string;
}

interface EPGLineupSelectProps {
  lineupGroup: SelectedEPGLineupGroup;
  isDisabled: boolean;
}

export default function EPGLineupSelect({
  lineupGroup,
  isDisabled,
}: EPGLineupSelectProps): ReactElement {
  const dispatch = useDispatch();
  const { epgLineups, selectedLineup, uuid } = lineupGroup;

  const epgLineupOptions = useMemo(
    () =>
      epgLineups.map<EPGLineupOption>((epgLineup) => ({
        value: epgLineup.uuid,
        label: epgLineup.title,
      })),
    [epgLineups]
  );

  //
  // Callbacks

  const onChange = useCallback(
    (event: ChangeEvent<HTMLSelectElement>) => {
      const selectedLineupUUID = event.currentTarget.value;

      if (!selectedLineupUUID) {
        return;
      }

      dispatch(
        epgSettingsSlice.actions.selectedLineupSelected({
          lineupGroupUUID: uuid,
          selectedLineupUUID,
        })
      );
    },
    [dispatch, uuid]
  );

  //
  // Effects

  //
  // Render

  return (
    <Select
      className={styles.input}
      isDisabled={isDisabled || epgLineupOptions.length === 1}
      name={uuid}
      value={selectedLineup?.uuid}
      onChange={onChange}
    >
      <option value="">{t('Select Guide')}</option>
      {epgLineupOptions.map(({ value, label }, index) => (
        <option key={index} value={value}>
          {label}
        </option>
      ))}
    </Select>
  );
}
