import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import t from 'app/js/utils/t';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/lists... Remove this comment to see the full error message
import tpl from 'app/templates/lists/activity/AlertListItem.tpl';

const AlertListItem = Marionette.ItemView.extend({
  //
  // Properties

  tagName: 'li',
  className: 'card-collapsed striped-list-item alert-list-item',
  template: tpl,

  //
  // Control

  serializeData: function () {
    const experienceSettingsModel = resolve('appModel')
      .get('user')
      .get('experienceSettings');
    const event = this.model.toJSON();

    const data = {
      serverID: this.model.server.id,
      itemKey: '/library/metadata/' + this.model.get('itemID'),
      event: event,
      escapedServerName: _.escape(this.model.server.get('friendlyName')),
      escapedEventLabel: _.escape(event.label),
      escapedEventValue: _.escape(event.value),
      escapedEventUsername: _.escape(event.username),
      escapedEventTitle: _.escape(event.title),
      escapedEventPlayer: _.escape(event.player),
      timeFormat: experienceSettingsModel.get('timeFormat'),
    };

    // These values are used as arguments in translate calls, which are not
    // automatically escaped.
    event.label = _.escape(event.label);
    event.value = _.escape(event.value);

    if (event._type === 'playing') {
      event.username = _.escape(event.username);
      event.title = _.escape(event.title);
      event.player = _.escape(event.player);
    }

    if (event._type === 'timeline') {
      event.mediaTitle = this.model.get('title') || 'an item';

      switch (this.model.get('state')) {
        case 0:
          event.stateString = t('Created');
          break;
        case 1:
          event.stateString = t('Progress for');
          break;
        case 2:
          event.stateString = t('Matching');
          break;
        case 3:
          event.stateString = t('Downloading metadata for');
          break;
        case 4:
          event.stateString = t('Loading metadata for');
          break;
        case 5:
          event.stateString = t('Finished processing');
          break;
        case 6:
          event.stateString = t('Analyzing');
          break;
        case 9:
          event.stateString = t('Deleted');
          break;
      }
    }

    return data;
  },
});

export default AlertListItem;
