/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { signInThenClaimServer } from 'app/js/stores/actions/server/claimServerActions';
import {
  beginPolling,
  preloadPIN,
  stopPolling,
} from 'app/js/stores/actions/user/authCodeExchangeActions';
import selectAuthCodeExchange from 'app/js/stores/selectors/user/selectAuthCodeExchange';
import t from 'app/js/utils/t';
import TagInjector from 'app/js/views/v3/components/core/TagInjector';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import ClaimServerButton from './ClaimServerButton';
import ServiceAuthUrlConnector from './ServiceAuthUrlConnector';

const mapDispatchToProps = {
  dispatchPreloadPIN: preloadPIN,
  dispatchBeginPolling: beginPolling,
  dispatchStopPolling: stopPolling,
  dispatchSignInThenClaimServer: signInThenClaimServer,
};

type ClaimServerSignedOutPollButtonConnectorProps = {
  serverEntityID: string;
  isBusy: boolean;
  isDisabled: boolean;
  pinID?: number;
  code?: string;
  authToken?: string;
  dispatchPreloadPIN: (...args: any[]) => any;
  dispatchBeginPolling: (...args: any[]) => any;
  dispatchStopPolling: (...args: any[]) => any;
  dispatchSignInThenClaimServer: (...args: any[]) => any;
};

class ClaimServerSignedOutPollButtonConnector extends Component<ClaimServerSignedOutPollButtonConnectorProps> {
  //
  // Lifecycle

  componentDidMount() {
    this.props.dispatchPreloadPIN();
  }

  componentDidUpdate(prevProps: ClaimServerSignedOutPollButtonConnectorProps) {
    const didReceiveAuthToken =
      this.props.authToken !== prevProps.authToken && this.props.authToken;

    if (didReceiveAuthToken) {
      this.props.dispatchSignInThenClaimServer({
        serverEntityID: this.props.serverEntityID,
        authToken: this.props.authToken,
      });
    }
  }

  componentWillUnmount() {
    this.props.dispatchStopPolling();
  }

  //
  // Listeners

  onClaimServerPress = () => {
    this.props.dispatchBeginPolling({ pinID: this.props.pinID });
  };

  onStillTherePress = () => {
    this.props.dispatchPreloadPIN();
  };

  //
  // Render

  render() {
    const { code, pinID, isBusy, isDisabled } = this.props;

    const showTimedOut = !(isBusy || this.props.code);

    if (showTimedOut) {
      return (
        <TagInjector
          message={t(
            'Server claiming timed out. {openLink}Try again?{closeLink}'
          )}
          tags={[
            {
              open: 'openLink',
              close: 'closeLink',
              component: Link,
              kind: kinds.PRIMARY,
              onPress: this.onStillTherePress,
            },
          ]}
        />
      );
    }

    return (
      <ServiceAuthUrlConnector
        code={code}
        component={ClaimServerButton}
        isDisabled={isDisabled || !pinID}
        isSpinning={isBusy}
        pinID={pinID}
        onPress={this.onClaimServerPress}
      />
    );
  }
}

export default connect(
  selectAuthCodeExchange,
  mapDispatchToProps
)(ClaimServerSignedOutPollButtonConnector);
