import classNames from 'classnames';
import React, { memo, ReactElement, useMemo } from 'react';
import { useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import useTranscodeImage from 'app/js/v4/core/servers/hooks/useTranscodeImage';
import CrossFadeImage, {
  ImageFit,
} from 'app/js/v4/core/ui/images/CrossFadeImage';
import useVirtualCell from 'app/js/v4/core/ui/lists/hooks/useVirtualCell';
import { VirtualCellProps } from 'app/js/v4/core/ui/lists/listsTypes';
import createDeviceIndicesMatchingGuideChannelSelector from 'app/js/v4/features/dvrManagement/selectors/createDeviceIndicesMatchingGuideChannelSelector';
import { GuideChannel } from 'app/js/v4/features/livetv/liveTVTypes';
import Badge from 'app/js/views/v3/components/core/base/Badge';
import colors from 'app/js/views/v3/styles/variables/colors';
import dimensions from 'app/js/views/v3/styles/variables/dimensions';
import styles from './LiveTVSettingsChannel.css';

const THUMB_WIDTH = parseInt(dimensions.liveTVSettingsChannelThumbWidth, 10);
const THUMB_HEIGHT = parseInt(dimensions.liveTVSettingsChannelThumbHeight, 10);

const tunerColors = [
  colors.liveTVSettingsTuner1Color,
  colors.liveTVSettingsTuner2Color,
  colors.liveTVSettingsTuner3Color,
  colors.liveTVSettingsTuner4Color,
  colors.liveTVSettingsTuner5Color,
];

export type LiveTVSettingsChannelProps = VirtualCellProps<GuideChannel>;

function LiveTVSettingsChannel({
  item: guideChannel,
  ...virtualCellProps
}: LiveTVSettingsChannelProps): ReactElement | null {
  const [server, provider] = useSourceContext();
  const cell = useVirtualCell(virtualCellProps);

  const selectDeviceIndices = useMemo(
    () => createDeviceIndicesMatchingGuideChannelSelector(guideChannel?.vcn),
    [guideChannel]
  );

  const deviceIndices = useSelector(selectDeviceIndices);

  const imageSrc = useTranscodeImage({
    server,
    provider,
    width: THUMB_WIDTH,
    height: THUMB_WIDTH,
    url: guideChannel?.thumb,
  });

  // Channel that have no mappings should not be shown.
  if (!guideChannel || deviceIndices.length === 0) {
    return null;
  }

  const { callSign, thumb, vcn, isHd } = guideChannel;

  return (
    <div ref={cell.cellRef} className={styles.container} style={cell.style}>
      <div className={styles.channelSourceContainer}>
        {deviceIndices.map((index) => {
          const moduloIndex = (tunerColors.length + index) % tunerColors.length;

          return (
            <div
              key={index}
              className={styles.channelSource}
              style={{ backgroundColor: tunerColors[moduloIndex] }}
            />
          );
        })}
      </div>
      {thumb ? (
        <CrossFadeImage
          // Key the image to allow the grid row to reuse all other row title
          // DOM while requiring a fresh image. This prevents a stale image
          // from disabling until the next thumb loads.
          key={thumb}
          className={styles.thumb}
          fit={ImageFit.Contain}
          height={THUMB_HEIGHT}
          src={imageSrc}
          width={THUMB_WIDTH}
        />
      ) : null}
      <div className={classNames(styles.vcn, !thumb && styles.vcnGap)}>
        {vcn}
      </div>
      <div className={styles.title}>{callSign}</div>
      {isHd ? <Badge className={styles.hdBadge}>{t('HD')}</Badge> : null}
    </div>
  );
}

export default memo(LiveTVSettingsChannel);
