import React, { ReactElement, ReactNode, useCallback } from 'react';
import Menu from 'app/js/views/v3/components/core/menus/Menu';
import SelectedMenuItem from 'app/js/views/v3/components/core/menus/SelectedMenuItem';
import useMenuState from 'app/js/views/v3/helpers/hooks/useMenuState';
import ServerDashboardMenuButton from './ServerDashboardMenuButton';

interface ServerDashboardFilterMenuButtonProps<T> {
  className?: string;
  values: T[];
  activeValue: T;
  formatter(item: T): ReactNode;
  onSelect(item: T): void;
}

export default function ServerDashboardFilterMenuButton<
  T extends number | string
>({
  className,
  values,
  activeValue,
  formatter,
  onSelect: onSelectFromProps,
}: ServerDashboardFilterMenuButtonProps<T>): ReactElement {
  const { menuButtonID, isMenuOpen, onMenuButtonPress, onMenuClose } =
    useMenuState();

  const onSelect = useCallback(
    (value) => {
      onMenuClose();
      onSelectFromProps(value);
    },
    [onMenuClose, onSelectFromProps]
  );

  return (
    <>
      <ServerDashboardMenuButton
        className={className}
        id={menuButtonID}
        isMenuOpen={isMenuOpen}
        onPress={onMenuButtonPress}
      >
        {formatter(activeValue)}
      </ServerDashboardMenuButton>

      <Menu isOpen={isMenuOpen} target={menuButtonID} onMenuClose={onMenuClose}>
        {values.map((value) => {
          return (
            <SelectedMenuItem
              key={String(value)}
              isSelected={activeValue === value}
              value={value}
              onSelect={onSelect}
            >
              {formatter(value)}
            </SelectedMenuItem>
          );
        })}
      </Menu>
    </>
  );
}
