import _ from 'underscore';
import stateMachineMixin from 'app/js/mixins/views/stateMachineMixin';
import bytesToSize from 'app/js/utils/number/bytesToSize';
import t from 'app/js/utils/t';
import VersionGeneratorMetadataList from 'app/js/views/lists/library/convert/VersionGeneratorMetadataList';
import BaseModal from 'app/js/views/modals/BaseModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/convert/VersionGeneratorMetadataModal.tpl';

const VersionGeneratorMetadataModal = BaseModal.extend({
  mixins: [stateMachineMixin],
  //
  // Properties
  name: 'versionGeneratorMetadataModal',
  className: 'version-generator-metadata-modal modal fade',
  template: tpl,
  pageSize: 20,
  scrollThreshold: 50,
  ui: {
    body: '.modal-body',
    loading: '.loading',
    errorMessage: '.error-message',
  },
  regions: {
    listRegion: '.list-region',
  },
  //
  // Init
  initialize: function (options: any) {
    this.status = this.model.get('status');
    this.listModel = options.listModel;
    this.collection = this.listModel.get('metadataItems');
    this.transcodeJobs = options.transcodeJobs;
    this.pausedSetting = options.pausedSetting;
    this.handleScroll = _.throttle(this.whenRendered(this.onScroll), 100);
  },
  //
  // States
  states: {
    base: {},
    loading: {
      onEnter: function () {
        (this as any).ui.loading.removeClass('hidden');
      },
      onExit: function () {
        (this as any).ui.loading.addClass('hidden');
      },
    },
    error: {
      onEnter: function () {
        (this as any).ui.errorMessage.removeClass('hidden');
      },
      onExit: function () {
        (this as any).ui.errorMessage.addClass('hidden');
      },
    },
  },
  //
  // Bindings
  bindings: {
    '.generator-title': {
      modelAttribute: 'title',
    },
    '.generator-items-count': {
      model: 'status',
      modelAttribute: 'itemsCount',
      formatter: function (itemsCount: any) {
        return itemsCount === 1
          ? t('{1} item', itemsCount)
          : t('{1} items', itemsCount);
      },
    },
    '.generator-total-size': {
      model: 'status',
      modelAttribute: 'totalSize',
      formatter: function (totalSize: any) {
        if (totalSize) {
          // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
          return bytesToSize(totalSize);
        } else {
          return t('0 files');
        }
      },
    },
    '.generator-failure-container': {
      model: 'status',
      modelAttribute: 'failure',
      manual: true,
      autohide: function (failure: any) {
        return !!failure;
      },
    },
    '.generator-failure-message': {
      model: 'status',
      modelAttribute: 'failure',
      formatter: function (failure: any) {
        return failure;
      },
    },
  },
  //
  // Control
  populatePage: function () {
    this.enterState('loading');
    const promise = this.listModel.populateRange({
      start: 0,
      end: this.listModel.get('populatedRange').end + this.pageSize,
    });
    promise.done(
      this.whenRendered(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.enterState('base');
      })
    );
    promise.fail(
      this.whenRendered(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.enterState('error');
      })
    );
    return promise;
  },
  //
  // Listeners
  onRender: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.onRender.apply(this, arguments);
    // Scroll event doesn't bubble up so we can't declare it in events
    // delegate hash.
    this.ui.body.on('scroll', this.handleScroll);
    // Fetch the generator in case the number of files has changed.
    this.model.fetch();
    // Populate the first page of items.
    this.populatePage();
    this.listRegion.show(
      new VersionGeneratorMetadataList({
        collection: this.collection,
        itemViewOptions: {
          generator: this.model,
          transcodeJobs: this.transcodeJobs,
          pausedSetting: this.pausedSetting,
        },
      })
    );
  },
  onClose: function () {
    this.ui.body.off('scroll', this.handleScroll);
    // Reset so if the modal is opened again, we populate a fresh set of
    // items.
    this.collection.reset();
  },
  onScroll: function () {
    // Don't keep trying to request items if there was an error.
    if (this.currentState === 'error') {
      return;
    }
    const containerHeight =
      this.ui.body.prop('scrollHeight') - this.ui.body.innerHeight();
    const atEnd =
      containerHeight - this.ui.body.scrollTop() < this.scrollThreshold;
    if (
      atEnd &&
      this.listModel.get('populatedRange').end < this.listModel.get('totalSize')
    ) {
      this.populatePage();
    }
  },
});
export default VersionGeneratorMetadataModal;
