import { createSelector, OutputSelector } from 'reselect';
import {
  ServerAccount,
  ServerAccountsPerServerState,
} from 'app/js/v4/core/serverAccounts/serverAccountsTypes';
import serverAccountComparator from 'app/js/v4/core/serverAccounts/utils/serverAccountComparator';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import memoize from 'app/js/v4/utils/memoize';
import createServerAccountsSelector from './createServerAccountsSelector';

// This selector may be created frequently in components and saga workers,
// but there is a limited number of possible selectors based on the arguments.
// Rather than independently memoize in each component or saga, add a layer of
// memoization on top of reselect here.

function createServerAccountsSortedUsersSelector(
  machineIdentifier: string
): OutputSelector<
  LegacyRootState,
  ServerAccount[],
  (serverAccounts: ServerAccountsPerServerState) => ServerAccount[]
> {
  return createSelector(
    createServerAccountsSelector(machineIdentifier),
    (serverAccounts) =>
      serverAccounts.accounts.slice().sort(serverAccountComparator)
  );
}

export default memoize(createServerAccountsSortedUsersSelector);
