/* eslint-plugin-disable @typescript-eslint */

function getRelativeOffset(media: any, part: any, absoluteOffset: any) {
  let relativeOffset = absoluteOffset;

  // Get the offset for a specific part based on the offset for the
  // entire video.

  if (media.Part.length > 1) {
    for (let i = 0, len = media.Part.length; i < len; i++) {
      const earlierPart = media.Part[i];

      // If the part doesn't have a duration, we can't accurately
      // estimate which part should be returned.

      if (!earlierPart.duration) {
        break;
      }

      // Once we reach the part the absolute offset falls within,
      // we don't need to go any further.

      if (part.key === earlierPart.key) {
        break;
      }

      relativeOffset -= earlierPart.duration;
    }
  }

  return relativeOffset;
}

export default getRelativeOffset;
