import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import t from 'app/js/utils/t';
import AlertList from 'app/js/views/lists/activity/AlertList';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/activ... Remove this comment to see the full error message
import tpl from 'app/templates/activity/AlertsView.tpl';

const AlertsView = Marionette.Layout.extend({
  //
  // Properties

  className: 'alerts-container',
  template: tpl,

  ui: {
    pageCount: '.page-count',
    previousBtn: '.previous-btn',
    nextBtn: '.next-btn',
  },

  regions: {
    serverDropdown: '.filter-bar',
    list: '.alert-list-container',
  },

  modelEvents: {
    'change:shared': 'onSharedChange',
  },

  collectionEvents: {
    add: 'onAlertAdd',
  },

  events: {
    'click .previous-btn': 'onPreviousClick',
    'click .next-btn': 'onNextClick',
  },

  onlyAfterRender: ['onAlertAdd'],

  //
  // Initialize

  initialize: function () {
    this.shared = this.model.get('shared');
    this.page = 1;
    this.listCollection = this.collection.clone(
      this.collection.page(this.page, { reverse: true })
    );
    this.listCollection.stopListening();
  },

  //
  // Control

  serializeData: function () {
    const pages = this.collection.pages();

    return {
      shared: this.shared,
      escapedPage: _.escape(this.page),
      escapedPages: _.escape(pages),
      enablePrevious: this.page > 1,
      enableNext: this.page < pages,
    };
  },

  updatePages: function () {
    const pages = this.collection.pages();

    this.ui.pageCount.text(t('{1} of {2}', this.page, pages));
    this.ui.previousBtn.toggleClass('disabled', this.page < 2);
    this.ui.nextBtn.toggleClass('disabled', this.page >= pages);
  },

  updateList: function () {
    this.listCollection.reset(
      this.collection.page(this.page, { reverse: true })
    );
  },

  //
  // Listeners

  onRender: function () {
    if (!this.shared) {
      this.list.append(new AlertList({ collection: this.listCollection }));
    }
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'server' is declared but its value is never read.
  onSharedChange: function (server: any, shared: any) {
    this.shared = shared;
    this.render();
  },

  onAlertAdd: function () {
    if (this.shared) {
      return;
    }

    this.updatePages();
    this.updateList();
  },

  onPreviousClick: function (event: any) {
    event.preventDefault();

    if (this.ui.previousBtn.isDisabled()) {
      return;
    }

    this.page--;
    this.updatePages();
    this.updateList();
  },

  onNextClick: function (event: any) {
    event.preventDefault();

    if (this.ui.nextBtn.isDisabled()) {
      return;
    }

    this.page++;
    this.updatePages();
    this.updateList();
  },
});

export default AlertsView;
