import {
  Provider,
  ProviderFeature,
  ProviderFeatureType,
  ProviderFlavorType,
} from 'app/js/v4/core/providers/providersTypes';

export default function getProviderFeature(
  provider: Provider,
  featureType: ProviderFeatureType,
  featureFlavor?: ProviderFlavorType
): ProviderFeature | undefined {
  return provider.features.find(
    (feature) =>
      feature.type === featureType &&
      (!featureFlavor || feature.flavor === featureFlavor)
  );
}
