import { createSelector, Selector } from 'reselect';
import {
  EPGLineup,
  EPGLineupType,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import selectDVRSetup from './selectDVRSetup';

const selectSelectedDVRLineups: Selector<
  LegacyRootState,
  EPGLineup[] | undefined
> = createSelector(selectDVRSetup, ({ selectedDVR }) => {
  return selectedDVR?.dvr?.Lineup?.map((lineup) => ({
    uuid: lineup.id,
    title: lineup.title,
    lineupType: EPGLineupType.Unknown,
  }));
});

export default selectSelectedDVRLineups;
