/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { SUPPORT_URL } from 'app/js/utils/constants/commonUrls';
import t from 'app/js/utils/t';
import TagInjector from 'app/js/views/v3/components/core/TagInjector';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import SpinnerButton from 'app/js/views/v3/components/core/buttons/SpinnerButton';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import getServerSettingsRoute from 'app/js/views/v3/helpers/routes/getServerSettingsRoute';
import IconRemove from 'app/glyphs/remove-560.svg';
import IconSelected from 'app/glyphs/selected-560.svg';
import styles from './ServerTroubleshootingPane.css';

type ServerTroubleshootingPaneProps = {
  machineIdentifier: string;
  downloadDatabasesUrl?: string;
  downloadLogsUrl?: string;
  cleanBundlesError?: string;
  optimizeDatabaseError?: string;
  isCleanBundlesInProgress: boolean;
  isOptimizeDatabaseInProgress: boolean;
  showDownloadDatabases: boolean;
  showDownloadLogs: boolean;
  onCleanBundlesPress: (...args: any[]) => any;
  onOptimizeDatabasePress: (...args: any[]) => any;
};
type ServerTroubleshootingPaneState = any;
class ServerTroubleshootingPane extends Component<
  ServerTroubleshootingPaneProps,
  ServerTroubleshootingPaneState
> {
  //
  // Static
  static getDerivedStateFromProps(nextProps: any, prevState: any) {
    const cleanBundlesProgressChanged =
      nextProps.isCleanBundlesInProgress !== prevState.isCleanBundlesInProgress;
    const optimizeDatabaseProgressChanged =
      nextProps.isOptimizeDatabaseInProgress !==
      prevState.isOptimizeDatabaseInProgress;
    if (!cleanBundlesProgressChanged && !optimizeDatabaseProgressChanged) {
      return null;
    }
    const derivedState = {};
    if (cleanBundlesProgressChanged) {
      (derivedState as any).isCleanBundlesInProgress =
        nextProps.isCleanBundlesInProgress;
      (derivedState as any).isCleanBundlesComplete =
        prevState.isCleanBundlesInProgress &&
        !nextProps.isCleanBundlesInProgress;
    }
    if (optimizeDatabaseProgressChanged) {
      (derivedState as any).isOptimizeDatabaseInProgress =
        nextProps.isOptimizeDatabaseInProgress;
      (derivedState as any).isOptimizeDatabaseComplete =
        prevState.isOptimizeDatabaseInProgress &&
        !nextProps.isOptimizeDatabaseInProgress;
    }
    return derivedState;
  }
  //
  // Lifecycle
  constructor(props: any, context: any) {
    super(props, context);
    this.state = {
      isCleanBundlesInProgress: false,
      isOptimizeDatabaseInProgress: false,
      isCleanBundlesComplete: false,
      isOptimizeDatabaseComplete: false,
    };
  }
  //
  // Render
  render() {
    const {
      downloadDatabasesUrl,
      downloadLogsUrl,
      cleanBundlesError,
      optimizeDatabaseError,
      isCleanBundlesInProgress,
      isOptimizeDatabaseInProgress,
      showDownloadDatabases,
      showDownloadLogs,
      onCleanBundlesPress,
      onOptimizeDatabasePress,
    } = this.props;
    const { isCleanBundlesComplete, isOptimizeDatabaseComplete } = this.state;
    return (
      <div>
        <div className={styles.troubleshootingSection}>
          <div className={styles.buttonContainer}>
            <SpinnerButton
              className={styles.button}
              isSpinning={isOptimizeDatabaseInProgress}
              onPress={onOptimizeDatabasePress}
            >
              {t('Optimize Database')}
            </SpinnerButton>
            {isOptimizeDatabaseComplete && !optimizeDatabaseError ? (
              <div className={styles.buttonStatusSuccess}>
                <IconSelected />
                <div className={styles.textStatus}>
                  {t('Optimize database complete')}
                </div>
              </div>
            ) : null}
            {isOptimizeDatabaseComplete && optimizeDatabaseError ? (
              <div className={styles.buttonStatusError}>
                <IconRemove />
                <div className={styles.textStatus}>{optimizeDatabaseError}</div>
              </div>
            ) : null}
          </div>

          <div className={styles.description}>
            {t(
              "Cleans up the library database and ensures it is operating as quickly as possible. This is especially useful when you've added or removed a large amount of media from the library."
            )}
          </div>
        </div>

        <div className={styles.troubleshootingSection}>
          <div className={styles.buttonContainer}>
            <SpinnerButton
              className={styles.button}
              isSpinning={isCleanBundlesInProgress}
              onPress={onCleanBundlesPress}
            >
              {t('Clean Bundles')}
            </SpinnerButton>
            {isCleanBundlesComplete && !cleanBundlesError ? (
              <div className={styles.buttonStatusSuccess}>
                <IconSelected />
                <div>{t('Bundles cleaned')}</div>
              </div>
            ) : null}
            {isCleanBundlesComplete && cleanBundlesError ? (
              <div className={styles.buttonStatusError}>
                <IconRemove />
                <div>{cleanBundlesError}</div>
              </div>
            ) : null}
          </div>

          <div className={styles.description}>
            <TagInjector
              className={styles.description}
              message={t(
                'Poster images and other metadata for items in your library are kept in "bundle" packages. When you remove items from your library, these bundles arent immediately removed. Removing them can reduce the size of your install. By default, your server will automatically clean up old bundles once a week as part of {openScheduledTass}Scheduled Tasks{closeScheduledTass}.'
              )}
              tags={[
                {
                  open: 'openScheduledTass',
                  close: 'closeScheduledTass',
                  component: Link,
                  kind: kinds.PRIMARY,
                  to: getServerSettingsRoute('settings', 'butler'),
                },
              ]}
            />
          </div>
        </div>

        {showDownloadLogs || showDownloadDatabases ? (
          <div className={styles.troubleshootingSection}>
            <div className={styles.buttonContainer}>
              {showDownloadLogs ? (
                <Button
                  className={styles.button}
                  target={__EXTERNAL_LINK_TARGET__}
                  to={downloadLogsUrl}
                >
                  {t('Download Logs')}
                </Button>
              ) : null}
              {showDownloadDatabases ? (
                <Button
                  className={styles.button}
                  target={__EXTERNAL_LINK_TARGET__}
                  to={downloadDatabasesUrl}
                >
                  {t('Download Database')}
                </Button>
              ) : null}
            </div>

            <div className={styles.description}>
              {t(
                'The Plex Media Server logs and database can be helpful to developers and forum community members in diagnosing server issues.'
              )}
            </div>
          </div>
        ) : null}

        <div className={styles.description}>
          <TagInjector
            className={styles.description}
            message={t(
              'You can find additional documentation, guides and FAQs as well as our friendly Community Forums among our {openSupportLink}support resources{closeSupportLink}.'
            )}
            tags={[
              {
                open: 'openSupportLink',
                close: 'closeSupportLink',
                component: Link,
                kind: kinds.PRIMARY,
                to: SUPPORT_URL,
                target: '_blank',
              },
            ]}
          />
        </div>
      </div>
    );
  }
}
export default ServerTroubleshootingPane;
