/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React from 'react';
import * as metadataIcons from 'app/js/utils/constants/metadataIcons';
import MetadataIcon from './MetadataIcon';
import styles from './MetadataPosterCardIcon.css';

type MetadataPosterCardIconProps = {
  className?: string;
  type?: string;
  icon?: string;
  width: number;
  height: number;
  isFolder?: boolean;
};

function MetadataPosterCardIcon(props: MetadataPosterCardIconProps) {
  const { className, type, icon, width, height, isFolder } = props;

  return (
    <MetadataIcon
      className={classNames(className, styles.placeholderIcon)}
      height={height}
      icon={isFolder ? metadataIcons.FOLDER : icon}
      type={type}
      width={width}
    />
  );
}

export default MetadataPosterCardIcon;
