import $ from 'jquery';
import Sortable from 'sortablejs';
import _ from 'underscore';
import BaseList from 'app/js/views/lists/BaseList';
import VersionGeneratorListEmptyView from 'app/js/views/lists/library/convert/VersionGeneratorListEmptyView';
import VersionGeneratorListItem from 'app/js/views/lists/library/convert/VersionGeneratorListItem';

const VersionGeneratorList = BaseList.extend({
  //
  // Properties

  tagName: 'ol',
  className: 'list media-list playlist-items',
  itemView: VersionGeneratorListItem,
  emptyView: VersionGeneratorListEmptyView,
  sorted: true,

  //
  // Listeners

  onRender: function () {
    if (this.sortable) {
      this.sortable.destroy();
    }

    this.sortable = new Sortable(this.el, {
      onUpdate: _.bind(this.onItemMove, this),
      handle: '.sortable-handle',
      animation: 200,
      scroll: true,
      scrollSensitivity: 100,
      scrollSpeed: 2,
    });
  },

  onClose: function () {
    if (this.sortable) {
      this.sortable.destroy();
    }
  },

  onItemMove: function (event: any) {
    const movedItemID = $(event.item).data('generator-id');
    const previousItemID = $(event.item).prev().data('generator-id');

    this.collection.parent.moveItem(movedItemID, previousItemID);
  },
});

export default VersionGeneratorList;
