/* eslint-plugin-disable @typescript-eslint */

import { useEffect, useMemo, useReducer, useRef } from 'react';
import usePrevious from 'app/js/views/v3/helpers/hooks/usePrevious';
import animations from 'app/styles/variables/animations';

function getCellStyle({
  cellX,
  cellY,
  cellWidth,
  cellHeight,
  style,
  isMoving,
  moveDurationMilliseconds,
  isVirtual,
}: any) {
  if (!isVirtual) {
    return {
      position: 'relative',
      ...style,
    };
  }

  return {
    position: 'absolute',
    width: cellWidth,
    height: cellHeight,
    transform: `translate3d(${cellX}px, ${cellY}px, 0)`,
    zIndex: isMoving ? 1 : 0,
    transition: isMoving ? `transform ${animations.defaultSpeed}` : 'none',
    transitionDuration: isMoving ? `${moveDurationMilliseconds}ms` : 0,
    ...style,
  };
}

function isCellPlaceholder({ isVirtual, isVisible, isScrolling }: any) {
  return (isVirtual && !isVisible) || isScrolling;
}

export default function useVirtualCell(props: any) {
  const {
    cellIndex,
    cellX,
    cellY,
    cellWidth,
    cellHeight,
    style,
    keyPropName,
    moveDurationMilliseconds = 400,
    isVirtual,
  } = props;

  const item = props[keyPropName];
  const prevItem = usePrevious(item);
  const prevCellIndex = usePrevious(cellIndex);

  const isSameItemAtDifferentIndex = !!(
    prevItem != null &&
    prevCellIndex != null &&
    item === prevItem &&
    props.cellIndex !== prevCellIndex
  );

  // Be wary of editing this to destructure the `forceRender` callback.
  // See: https://github.com/plexinc/plex-web-client/issues/14883
  const reducer = useReducer((state) => state + 1, 0);
  const forceRender = reducer[1];

  const isMovingRef = useRef(isSameItemAtDifferentIndex);
  const isMoving = isMovingRef.current || isSameItemAtDifferentIndex;

  const finalStyle = useMemo(() => {
    return getCellStyle({
      cellX,
      cellY,
      cellWidth,
      cellHeight,
      style,
      moveDurationMilliseconds,
      isMoving,
      isVirtual,
    });
  }, [
    cellX,
    cellY,
    cellWidth,
    cellHeight,
    style,
    moveDurationMilliseconds,
    isMoving,
    isVirtual,
  ]);

  const lastVisibleItemRef = useRef(isCellPlaceholder(props) ? null : item);

  // Once an item is visible, do not switch to an offscreen placeholder if it is
  // scrolled out of view. Keep the item visible until the cell is reused.

  let isPlaceholder;

  if (isCellPlaceholder(props)) {
    isPlaceholder = lastVisibleItemRef.current !== item;

    if (isPlaceholder) {
      lastVisibleItemRef.current = null;
    }
  } else {
    isPlaceholder = false;
    lastVisibleItemRef.current = item;
  }

  // Prefer a ref for isMoving over state in order to avoid a re-render when a
  // cell starts the move transition. Otherwise, the cell's position would
  // change before the transition styles could be applied. Then, force a
  // re-render when the move transition completes to clean up the transition
  // styles. Give the transition extra time in case it stutters.

  useEffect(() => {
    let timeoutID: any;

    if (isMoving) {
      isMovingRef.current = true;

      timeoutID = window.setTimeout(() => {
        isMovingRef.current = false;
        // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
        forceRender({});
      }, moveDurationMilliseconds * 2);
    }

    return () => {
      if (timeoutID) {
        window.clearTimeout(timeoutID);
      }
    };
  }, [moveDurationMilliseconds, isMoving, forceRender]);

  return {
    style: finalStyle,
    isPlaceholder,
  };
}
