/* eslint-plugin-disable @typescript-eslint */
/* eslint-disable prefer-rest-params, prefer-spread */

import _ from 'underscore';
import Backbone from 'app/js/initializers/extensions/BackboneExtension';
import BaseClass from 'app/js/utils/BaseClass';
import eventMixin from 'app/js/utils/mixins/eventMixin';

// This is a band-aid over some Obscura issues that were making things more
// difficult than they needed to be. Obscura.FilteredCollection cannot be
// extended and used independently easily and Obscura has some worrying
// performance issues. For example, Obscura resets by default (we've patched
// this), sort events trigger refilters, and SortedCollection listeners will
// add and remove models from the collection even when we aren't using any
// sorts. Our only use of Obscura so far is filters, so this tries to
// isolate and simplify what we need, improving performance where possible.

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const FilteredCollection = BaseClass.extend({
  mixins: [eventMixin],

  //
  // Properties

  defaultFilterName: '__default',

  whitelistedEvents: [
    'add',
    'remove',
    'reset',
    'sort',
    'destroy',
    'sync',
    'request',
    'error',
  ],

  blacklistedMethods: [
    '_onModelEvent',
    '_prepareModel',
    '_removeReference',
    '_reset',
    'add',
    'initialize',
    'sync',
    'remove',
    'reset',
    'set',
    'push',
    'pop',
    'unshift',
    'shift',
    'sort',
    'parse',
    'fetch',
    'create',
    'model',
    'off',
    'on',
    'listenTo',
    'listenToOnce',
    'bind',
    'trigger',
    'once',
    'stopListening',
  ],

  //
  // Init

  constructor: function (superset: any, options: any) {
    this.superset = superset;

    this._collection = new Backbone.Collection(superset.toArray());
    this._proxyCollection(this._collection);

    this.resetFilters();

    this.listenTo(superset, {
      add: this.onSupersetAdd,
      remove: this.onSupersetRemove,
      reset: this.onSupersetReset,
    });

    this.initialize(options);
  },

  _proxyCollection: function (collection: any) {
    this.models = collection.models;

    this._proxyCollectionMethods(collection);
    this._proxyCollectionLength(collection);
    this._proxyCollectionEvents(collection);
  },

  _proxyCollectionMethods: function (collection: any) {
    const methods = {};
    _.each(
      _.functions(Backbone.Collection.prototype),
      function (method) {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        if (!_.contains(this.blacklistedMethods, method)) {
          // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
          methods[method] = function () {
            return collection[method].apply(collection, arguments);
          };
        }
      },
      this
    );

    _.extend(this, methods);
  },

  _proxyCollectionLength: function (collection: any) {
    const updateLength = function () {
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      this.length = collection.length;
    };

    this.listenTo(collection, 'all', updateLength);
    updateLength.call(this);
  },

  _proxyCollectionEvents: function (collection: any) {
    const pipeEvents = function (eventName: any) {
      const args = _.toArray(arguments);
      const isChangeEvent =
        eventName === 'change' || eventName.slice(0, 7) === 'change:';
      if (eventName === 'reset') {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.models = collection.models;
      }
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      if (_.contains(this.whitelistedEvents, eventName)) {
        if (_.contains(['add', 'remove', 'destroy'], eventName)) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          args[2] = this;
        } else if (_.contains(['reset', 'sort'], eventName)) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          args[1] = this;
        }
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.trigger.apply(this, args);
      } else if (isChangeEvent) {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        if (this.contains(args[1])) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.trigger.apply(this, args);
        }
      }
    };

    this.listenTo(collection, 'all', pipeEvents);
  },

  //
  // Control

  addFilter: function (filterName: any, filter: any) {
    if (!filter) {
      filter = filterName;
      filterName = this.defaultFilterName;
    }

    if (_.has(this._filters, filterName)) {
      this._invalidateCacheForFilter(filterName);
    }
    this._filters[filterName] = this._createFilter(filter);

    this._execFilters();
    this.trigger('filtered:add', filterName);

    return this;
  },

  removeFilter: function (filterName: any) {
    if (!filterName) {
      filterName = this.defaultFilterName;
    }

    delete this._filters[filterName];
    this._invalidateCacheForFilter(filterName);

    this._execFilters();
    this.trigger('filtered:remove', filterName);

    return this;
  },

  resetFilters: function () {
    this._filters = {};
    this._invalidateCache();
    this._execFilters();
    this.trigger('filtered:reset');

    return this;
  },

  getFilters: function () {
    return _.keys(this._filters);
  },

  hasFilter: function (name: any) {
    return _.contains(this.getFilters(), name);
  },

  refilter: function (arg: any) {
    if (_.isObject(arg) && arg.cid) {
      this.onSupersetAdd(arg);
    } else {
      this._invalidateCache();
      this._execFilters();
    }

    return this;
  },

  destroy: function () {
    this.stopListening();
    this.superset = null;
    this._collection.reset([]);
    this.length = 0;
    this.trigger('filtered:destroy');

    return this;
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'keys' is declared but its value is never read.
  _createFilter: function (filter: any, keys: any) {
    if (_.isFunction(filter)) {
      return filter;
    }

    throw new Error('Unsupported filter type');
  },

  _execFilters: function (options: any) {
    options || (options = {});

    let filtered = [];
    if (this.superset) {
      filtered = this.superset.filter(_.bind(this._execFiltersOnModel, this));
    }

    if (options.reset) {
      this._collection.reset(filtered);
    } else {
      this._collection.set(filtered);
    }

    this.length = this._collection.length;
  },

  _execFiltersOnModel: function (model: any) {
    if (!this._filterResultCache[model.cid]) {
      this._filterResultCache[model.cid] = {};
    }
    const cache = this._filterResultCache[model.cid];
    for (const filterName in this._filters) {
      if (_.has(this._filters, filterName)) {
        // If the result isn't already in the cache, run the filter function
        // and store the result.
        if (!_.has(cache, filterName)) {
          cache[filterName] = this._filters[filterName](model);
        }

        if (!cache[filterName]) {
          return false;
        }
      }
    }
    return true;
  },

  _invalidateCache: function () {
    this._filterResultCache = {};
  },

  _invalidateCacheForFilter: function (filterName: any) {
    for (const cid in this._filterResultCache) {
      if (_.has(this._filterResultCache, cid)) {
        delete this._filterResultCache[cid][filterName];
      }
    }
  },

  //
  // Listeners

  onSupersetAdd: function (model: any) {
    this._filterResultCache[model.cid] = {};

    if (this._execFiltersOnModel(model)) {
      if (!this._collection.get(model.cid)) {
        const index = this.superset.indexOf(model);
        let filteredIndex = 0;

        for (let i = index - 1; i >= 0; i -= 1) {
          if (this.contains(this.superset.at(i))) {
            filteredIndex = this.indexOf(this.superset.at(i)) + 1;
            break;
          }
        }

        this._collection.add(model, { at: filteredIndex });
      }
    } else if (this._collection.get(model.cid)) {
      this._collection.remove(model);
    }

    this.length = this._collection.length;
  },

  onSupersetRemove: function (model: any) {
    if (this.contains(model)) {
      this._collection.remove(model);
    }

    this.length = this._collection.length;
  },

  onSupersetReset: function () {
    this._execFilters({ reset: true });
  },
});

export default FilteredCollection;
