import React, {
  FunctionComponent,
  ReactElement,
  useCallback,
  useEffect,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import t from 'app/js/utils/t';
import SourceContextProvider from 'app/js/v4/core/servers/components/SourceContextProvider';
import FormSectionError from 'app/js/v4/core/ui/forms/components/FormSectionError';
import MetricsContextProvider from 'app/js/v4/core/ui/metrics/components/MetricsContextProvider';
import Modal from 'app/js/v4/core/ui/modals/components/Modal';
import ModalContent from 'app/js/v4/core/ui/modals/components/ModalContent';
import ModalHeader from 'app/js/v4/core/ui/modals/components/ModalHeader';
import DVRSetupAddLineup from 'app/js/v4/features/dvrManagement/components/setup/addLineup/DVRSetupAddLineup';
import DVRSetupDeviceList from 'app/js/v4/features/dvrManagement/components/setup/device/DVRSetupDeviceList';
import DVRSetupDownload from 'app/js/v4/features/dvrManagement/components/setup/download/DVRSetupDownload';
import DVRSetupInfo from 'app/js/v4/features/dvrManagement/components/setup/info/DVRSetupInfo';
import DVRSetupMapping from 'app/js/v4/features/dvrManagement/components/setup/mapping/DVRSetupMapping';
import DVRSetupUseXMLTV from 'app/js/v4/features/dvrManagement/components/setup/xmltv/DVRSetupUseXMLTV';
import { DVR } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import usePrevious from 'app/js/views/v3/helpers/hooks/usePrevious';
import styles from './DVRSetupModal.css';

interface DVRSetupModalProps {
  machineIdentifier: string;
  providerIdentifier: string;
  dvrID?: DVR['key'];
  metricsContext: string;
  isOpen: boolean;
  onModalClose(): void;
}

export enum DVRSetupStep {
  Device = 'device',
  XMLTV = 'xmltv',
  Info = 'info',
  Mapping = 'mapping',
  Download = 'download',
  AddLineup = 'addLineup',
}

export interface DVRSetupStepProps {
  dvrID?: DVR['key'];
  onModalClose(): void;
  onSetStep(step: DVRSetupStep): void;
}

function getDVRSetupStepComponent(
  step: DVRSetupStep
): FunctionComponent<DVRSetupStepProps> {
  switch (step) {
    case DVRSetupStep.Device:
      return DVRSetupDeviceList;
    case DVRSetupStep.XMLTV:
      return DVRSetupUseXMLTV;
    case DVRSetupStep.Info:
      return DVRSetupInfo;
    case DVRSetupStep.Mapping:
      return DVRSetupMapping;
    case DVRSetupStep.Download:
      return DVRSetupDownload;
    case DVRSetupStep.AddLineup:
      return DVRSetupAddLineup;
    default:
      throw new Error('Unmatched DVR setup modal step: ' + step);
  }
}

export default function DVRSetupModal({
  machineIdentifier,
  providerIdentifier,
  dvrID,
  metricsContext: metricsContextName,
  isOpen,
  onModalClose,
}: DVRSetupModalProps): ReactElement {
  const [step, setStep] = useState(DVRSetupStep.Device);
  const dispatch = useDispatch();
  const prevIsOpen = usePrevious(isOpen);
  const {
    selectedDVR: { dvr },
    setupErrorMessage,
  } = useSelector(selectDVRSetup);

  //
  // Callbacks

  const onSetStep = useCallback(
    (nextStep: DVRSetupStep) => {
      dispatch(dvrSetupSlice.actions.setupErrorMessageRemoved());
      setStep(nextStep);
    },
    [dispatch, setStep]
  );

  const onClose = useCallback(() => {
    dispatch(dvrSetupSlice.actions.setupReset());
    onModalClose();
  }, [dispatch, onModalClose]);

  //
  // Effects

  useEffect(() => {
    if (prevIsOpen && !isOpen) {
      setStep(DVRSetupStep.Device);
    }
  }, [setStep, isOpen, prevIsOpen]);

  //
  // Render

  const DVRSetupStepComponent = getDVRSetupStepComponent(step);

  return (
    <MetricsContextProvider
      metricsPage={metricsPages.DVR_SETUP}
      name={metricsContextName}
    >
      <SourceContextProvider
        machineIdentifier={machineIdentifier}
        providerIdentifier={dvr?.epgIdentifier ?? providerIdentifier}
      >
        <Modal
          className={styles.modal}
          isOpen={isOpen}
          shouldTrackOnClose={true}
          onModalClose={onClose}
        >
          <ModalContent onModalClose={onClose}>
            <ModalHeader>
              {step === DVRSetupStep.Download
                ? t('Setup complete')
                : t('Tuner Setup')}
            </ModalHeader>

            {setupErrorMessage ? (
              <FormSectionError errorMessage={setupErrorMessage} />
            ) : null}

            <DVRSetupStepComponent
              dvrID={dvrID}
              onModalClose={onClose}
              onSetStep={onSetStep}
            />
          </ModalContent>
        </Modal>
      </SourceContextProvider>
    </MetricsContextProvider>
  );
}
