import React, {
  ReactElement,
  ReactNode,
  useCallback,
  useMemo,
  useState,
} from 'react';
import useResizeObserver from 'app/js/v4/core/ui/hooks/useResizeObserver';
import VirtualList, {
  VirtualListProps,
} from 'app/js/v4/core/ui/lists/components/VirtualList';
import {
  VirtualCellPosition,
  VirtualListMeasurements,
} from 'app/js/v4/core/ui/lists/listsTypes';
import { VerticalStackMeasurements } from 'app/js/v4/core/ui/lists/utils/createVerticalStackLayout';
import useScrollRestore from 'app/js/v4/core/ui/scrollers/hooks/useScrollRestore';
import { ScrollPosition } from 'app/js/v4/core/ui/scrollers/scrollerTypes';
import Scroller, { ScrollDirection } from './Scroller';
import styles from './VirtualListScroller.css';

type OmittedListProps =
  | 'isWaitingToRestoreScrollPosition'
  | 'scrollLeft'
  | 'scrollTop'
  | 'viewportHeight'
  | 'viewportWidth';

export interface VirtualListScrollerProps<
  ItemType,
  ItemKeyProp,
  ListMeasurementsType
> extends Omit<
    VirtualListProps<ItemType, ItemKeyProp, ListMeasurementsType>,
    OmittedListProps
  > {
  className?: string;
  scrollerKey?: string;
  scrollerStyle?: object;
  virtualListClassName?: string;
  resizeTimeout?: number;
  scrollDirection?: ScrollDirection;
  targetScrollIndex?: number;
  shouldCenterScrollIndex?: boolean;
  autoScroll?: boolean;
  smoothScroll?: boolean;
  renderAboveList?: (measurements: Measurements) => ReactNode;
  renderBelowList?: (measurements: Measurements) => ReactNode;
}

interface Measurements {
  width: number;
  height: number;
}

export function calculateScrollPositions<
  ListMeasurementsType extends {
    outerPaddingTop: number;
    outerPaddingLeft: number;
  }
>(
  scrollDirection: ScrollDirection,
  cellPosition: VirtualCellPosition,
  options: {
    shouldCenterCell: boolean;
    measurements: Measurements;
    listMeasurements: ListMeasurementsType;
  }
): ScrollPosition {
  const { shouldCenterCell, measurements, listMeasurements } = options;

  const scrollTop =
    scrollDirection === ScrollDirection.Vertical ||
    scrollDirection === ScrollDirection.Omni
      ? calculateScrollPosition(cellPosition.cellY, {
          shouldCenterCell,
          viewportDimension: measurements.height,
          offset: listMeasurements.outerPaddingTop,
          cellDimension: cellPosition.cellHeight,
        })
      : 0;

  const scrollLeft =
    scrollDirection === ScrollDirection.Horizontal ||
    scrollDirection === ScrollDirection.Omni
      ? calculateScrollPosition(cellPosition.cellX, {
          shouldCenterCell,
          viewportDimension: measurements.width,
          offset: listMeasurements.outerPaddingLeft,
          cellDimension: cellPosition.cellWidth,
        })
      : 0;

  return {
    scrollTop,
    scrollLeft,
  };
}

function calculateScrollPosition(
  cellPositionValue: number,
  options: {
    shouldCenterCell: boolean;
    viewportDimension: number;
    offset: number;
    cellDimension: number;
  }
): number {
  if (options.shouldCenterCell) {
    const halfAvailableDimension =
      (options.viewportDimension - options.cellDimension) / 2;
    const centeredScrollPosition = Math.round(
      cellPositionValue - halfAvailableDimension
    );

    return Math.max(centeredScrollPosition, 0);
  }

  return cellPositionValue - (options.offset || 0);
}

export default function VirtualListScroller<
  ItemType,
  ItemKeyProp extends keyof ItemType,
  ListMeasurementsType extends
    | VerticalStackMeasurements
    | VirtualListMeasurements
>({
  className = styles.scroller,
  scrollerKey = 'VirtualListScroller',
  scrollerStyle,
  virtualListClassName,
  layout,
  resizeTimeout,
  scrollDirection = ScrollDirection.Vertical,
  targetScrollIndex,
  shouldCenterScrollIndex = false,
  autoScroll = true,
  smoothScroll = true,
  renderAboveList,
  renderBelowList,
  onListReady: onListReadyFromProps,
  ...virtualListProps
}: VirtualListScrollerProps<
  ItemType,
  ItemKeyProp,
  ListMeasurementsType
>): ReactElement {
  const [scrollPosition, setScrollPosition] = useState<ScrollPosition>({
    scrollTop: 0,
    scrollLeft: 0,
  });
  const [measurements, setMeasurements] = useState<Measurements>({
    width: 0,
    height: 0,
  });
  const isListMeasured = !!(measurements.width || measurements.height);

  const [listMeasurements, setIsListMeasurements] = useState<
    ListMeasurementsType | undefined
  >(undefined);

  const onResize = useCallback<ResizeObserverCallback>(([entry]) => {
    // The `useMeasure` hook uses `el.getBoundingClientRect()` which measures
    // the width of the element including the scrollbar. We want to measure
    // the area inside Scroller with out the scrollbar, so we use
    // `clientWidth` and `clientHeight` here to get accurate measurements
    // See: https://developer.mozilla.org/en-US/docs/Web/API/Element/getBoundingClientRect
    // See: https://developer.mozilla.org/en-US/docs/Web/API/Element/clientWidth
    setMeasurements({
      width: entry.target.clientWidth,
      height: entry.target.clientHeight,
    });
  }, []);
  const measureRef = useResizeObserver<HTMLDivElement>(onResize);

  const scrollPositions = useMemo(() => {
    if (!listMeasurements || !measurements || targetScrollIndex == null) {
      return;
    }

    const position = layout.getPositionForIndex(
      Math.max(targetScrollIndex, 0),
      listMeasurements
    );

    return calculateScrollPositions(scrollDirection, position, {
      shouldCenterCell: shouldCenterScrollIndex,
      measurements,
      listMeasurements,
    });
  }, [
    layout,
    listMeasurements,
    measurements,
    scrollDirection,
    shouldCenterScrollIndex,
    targetScrollIndex,
  ]);

  let targetScrollTop;
  let targetScrollLeft;

  if (scrollPositions) {
    targetScrollTop = scrollPositions.scrollTop;
    targetScrollLeft = scrollPositions.scrollLeft;
  }

  //
  // Callbacks

  const onScrollPositionChange = useCallback(({ scrollLeft, scrollTop }) => {
    setScrollPosition({
      scrollTop,
      scrollLeft,
    });
  }, []);

  const {
    isWaitingToRestoreScrollPosition,
    scrollRestoreProps,
    onScrollPositionReady,
  } = useScrollRestore({
    key: scrollerKey,
    onScrollPositionChange,
  });

  const onListReady = useCallback(
    (listMeasurements_: ListMeasurementsType) => {
      setIsListMeasurements(listMeasurements_);
      onScrollPositionReady();

      if (onListReadyFromProps) {
        onListReadyFromProps(listMeasurements_);
      }
    },
    [onListReadyFromProps, onScrollPositionReady]
  );

  //
  // Render

  return (
    <Scroller
      ref={measureRef}
      autoScroll={autoScroll}
      className={className}
      scrollDirection={scrollDirection}
      smoothScroll={smoothScroll}
      style={scrollerStyle}
      targetScrollLeft={targetScrollLeft}
      targetScrollTop={targetScrollTop}
      {...scrollRestoreProps}
    >
      {renderAboveList && !isWaitingToRestoreScrollPosition
        ? renderAboveList(measurements)
        : null}

      {isListMeasured ? (
        <VirtualList
          className={virtualListClassName}
          isWaitingToRestoreScrollPosition={isWaitingToRestoreScrollPosition}
          layout={layout}
          scrollLeft={scrollPosition.scrollLeft}
          scrollTop={scrollPosition.scrollTop}
          viewportHeight={measurements.height}
          viewportWidth={measurements.width}
          onListReady={onListReady}
          {...virtualListProps}
        />
      ) : null}

      {renderBelowList && !isWaitingToRestoreScrollPosition
        ? renderBelowList(measurements)
        : null}
    </Scroller>
  );
}
