import _ from 'underscore';
import commands from 'app/js/commands/commands';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import VersionGeneratorModel from 'app/js/models/library/convert/VersionGeneratorModel';
import contentUtil from 'app/js/utils/contentUtil';
import posterPlaceholders from 'app/js/utils/posterPlaceholders';
import MediaPosterImage from 'app/js/views/images/MediaPosterImage';
import ConversionStatusView from 'app/js/views/lists/activity/convert/ConversionStatusView';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/lists... Remove this comment to see the full error message
import tpl from 'app/templates/lists/activity/convert/ConversionQueueListItem.tpl';

const ConversionQueueListItem = Marionette.Layout.extend({
  //
  // Properties
  className:
    'striped-list-item media-list-item playlist-item version-generator-item',
  tagName: 'li',
  template: tpl,
  ui: {
    poster: '.media-poster',
  },
  events: {
    'click .promote-btn': 'onPromoteClick',
    'click .delete-btn': 'onDeleteClick',
  },
  regions: {
    statusRegion: '.conversion-status-region',
  },
  attributes: function () {
    return {
      'data-queue-item-id': this.model.id,
    };
  },
  //
  // Init
  initialize: function (options: any) {
    this.metadataItem = options.model.get('metadataItem');
    this.playQueue = options.playQueue;
    this.transcodeJobs = options.transcodeJobs;
    this.pausedSetting = options.pausedSetting;
    this.hideDragHandle = options.hideDragHandle;
    this.type = this.metadataItem.get('type');
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    let placeholderClass = posterPlaceholders[this.type];
    if (this.type === 'movie') {
      placeholderClass = posterPlaceholders.art;
    }
    const posterType = this.type === 'episode' ? 'show' : this.type;
    this.image = new MediaPosterImage({
      model: this.metadataItem,
      type: posterType,
      placeholderClass: placeholderClass,
      height: 60,
      modelAttribute: 'thumb',
      getUrl: this.getPosterUrl,
    });
  },
  //
  // Bindings
  bindings: {
    '.generator-title': {
      model: 'metadataItem',
      modelAttribute: 'title',
      formatter: function () {
        // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
        return contentUtil.title((this as any).metadataItem);
      },
    },
    '.generator-target': {
      modelAttribute: 'target',
      formatter: function (target: any) {
        return target;
      },
    },
    '.sortable-handle': {
      model: 'playQueue',
      modelAttribute: 'playQueueSelectedItemID',
      manual: true,
      autohide: function (playQueueSelectedItemID: any) {
        // The selected item in the background processing queue cannot be
        // reordered. However, other items can be moved above it.
        return (
          (this.model as any).get('playQueueItemID') !== playQueueSelectedItemID
        );
      },
    },
    '.promote-btn': {
      modelAttribute: 'queueIndex',
      manual: true,
      autohide: function (queueIndex: any) {
        const currentItem = (this as any).playQueue.getCurrentItem();
        // Show the promote button if the drag handle is being hidden or if
        // the item is not already the next item in the up next region.
        return (
          (this as any).hideDragHandle ||
          queueIndex - currentItem.get('queueIndex') > 1
        );
      },
    },
  },
  //
  // Control
  serializeData: function () {
    return {
      hideDragHandle: this.hideDragHandle,
    };
  },
  getPosterUrl: function (thumb: any) {
    if (this.model.get('type') === 'episode') {
      return (
        this.model.get('grandparentThumb') ||
        this.model.get('parentThumb') ||
        thumb
      );
    } else {
      return thumb;
    }
  },
  deleteGenerator: function () {
    const generator = new VersionGeneratorModel(
      {
        id: this.model.get('generatorID'),
      },
      {
        server: this.model.server,
        playlistID: this.metadataItem.get('playlistID'),
      }
    );
    return commands.execute('disableVersionGenerator', {
      generator: generator,
      ratingKey: this.metadataItem.get('ratingKey'),
    });
  },
  //
  // Listeners
  onRender: function () {
    this.image.setElement(this.ui.poster);
    this.statusRegion.show(
      new ConversionStatusView({
        model: this.model,
        transcodeJobs: this.transcodeJobs,
        pausedSetting: this.pausedSetting,
      })
    );
  },
  onClose: function () {
    this.image.close();
  },
  onCollectionSort: function () {
    this.trigger('sort');
  },
  onPromoteClick: function (event: any) {
    event.preventDefault();
    // The list will not detect a change in the order, and we don't want to
    // refilter on every sort event. We know that we will not refilter after
    // this particular batch of sorts events though. Collection filtering on
    // play queue changes is debouncd, so we need to debounce our sort event
    // as well.
    const collection = this.playQueue.get('collection');
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2-3 arguments, but got 1.
    this.listenToOnce(collection, 'sort', _.debounce(this.onCollectionSort));
    const itemID = this.model.get('playQueueItemID');
    const selectedItemID = this.playQueue.get('playQueueSelectedItemID');
    this.playQueue.moveItem(itemID, selectedItemID);
  },
  onDeleteClick: function (event: any) {
    event.preventDefault();
    this.deleteGenerator();
  },
});
export default ConversionQueueListItem;
