import { CSSProperties, Ref, useLayoutEffect, useMemo, useRef } from 'react';
import { VirtualCellProps } from 'app/js/v4/core/ui/lists/listsTypes';
import usePrevious from 'app/js/v4/utils/hooks/usePrevious';

export type UseVirtualCellProps<T> = Partial<
  VirtualCellProps<T> & {
    style: CSSProperties;
    moveDurationMilliseconds: number;
  }
>;

export default function useVirtualCell<T>(props: UseVirtualCellProps<T>): {
  cellRef: Ref<HTMLDivElement>;
  style: CSSProperties;
  isOffscreenPlaceholder: boolean;
} {
  const {
    style,
    moveDurationMilliseconds = 200,
    isVirtual,
    isVisible,
    cellIndex,
    cellX,
    cellY,
    cellWidth,
    cellHeight,
  } = props;

  const cellRef = useRef<HTMLDivElement>(null);
  const prevCellIndex = usePrevious(cellIndex);
  const prevCellX = usePrevious(cellX);
  const prevCellY = usePrevious(cellY);

  const isDifferentIndex = prevCellIndex != null && cellIndex !== prevCellIndex;

  const isPlaceholderRef = useRef(true);

  // Once an item is visible, do not switch to an offscreen placeholder if it is
  // scrolled out of view.

  if (!isVirtual || isVisible) {
    isPlaceholderRef.current = false;
  }

  const finalStyle = useMemo<CSSProperties>(() => {
    if (!isVirtual) {
      return {
        position: 'relative',
        ...style,
      };
    }

    return {
      // `willChange: 'transform'` instructs browsers that this element should
      // be in it's own compositing layer. This allows the browser to optimize
      // the placement of the cells during movement
      // See: https://developer.mozilla.org/en-US/docs/Web/CSS/will-change
      willChange: 'transform',
      position: 'absolute',
      width: cellWidth,
      height: cellHeight,
      top: cellY,
      left: cellX,
      ...style,
    };
  }, [cellX, cellY, cellWidth, cellHeight, style, isVirtual]);

  useLayoutEffect(() => {
    const el = cellRef.current;

    if (
      !el ||
      prevCellX == null ||
      prevCellY == null ||
      cellX == null ||
      cellY == null
    ) {
      return;
    }

    let animation: Animation | undefined;

    // Safari 12 does not support the Web Animation API, so we need to check if it
    // exists on the element before calling it
    if (isDifferentIndex && typeof el.animate === 'function') {
      el.style.zIndex = '1';
      // During a move we don't want the element to interact with the mouse at
      // all so we disable events while the animation occurs
      el.style.pointerEvents = 'none';

      // When React reorders a list of elements it removes the DOM element
      // from the document and reinserts the element in the correct position.
      // This process makes it so that transitions on the element will not get
      // applied. Here we apply the FLIP method where we translate the cell to
      // the last position after the list has been reordered in the DOM, but
      // before the DOM changes have been flushed to the document. We set up
      // the cell so that we animate the old position to the new left/right
      // position. By triggering this using the animate API it allows us to
      // programmatically start the transition with out relying on extra
      // rerenders
      // See: https://css-tricks.com/everything-you-need-to-know-about-flip-animations-in-react/
      animation = el.animate(
        [
          {
            transform: `translate(${prevCellX - cellX}px, ${
              prevCellY - cellY
            }px)`,
          },
          {
            transform: 'none',
          },
        ],
        moveDurationMilliseconds
      );

      // Clean up the pointerEvents from the move
      animation.oncancel = animation.onfinish = (): void => {
        el.style.zIndex = '0';
        el.style.pointerEvents = 'unset';
      };
    }
  }, [
    cellX,
    cellY,
    isDifferentIndex,
    moveDurationMilliseconds,
    prevCellX,
    prevCellY,
  ]);

  return {
    cellRef,
    style: finalStyle,
    isOffscreenPlaceholder: isPlaceholderRef.current,
  };
}
