import { createSelector, Selector } from 'reselect';
import createDeepEqualSelector from 'app/js/stores/selectors/createDeepEqualSelector';
import selectServers from 'app/js/stores/selectors/server/selectServers';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import toStableServerObject from 'app/js/v4/core/servers/utils/toStableServerObject';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

const selectStableServers: Selector<LegacyRootState, Server[]> =
  createDeepEqualSelector(
    createSelector(selectServers, (servers) => {
      return servers.map(toStableServerObject);
    }),
    (servers: Server[]) => servers
  );

export default selectStableServers;
