/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import getServerSettingsMetricsProps from 'app/js/utils/getServerSettingsMetricsProps';
import t from 'app/js/utils/t';
import ServerSettingsInnerView from 'app/js/views/settings/server/ServerSettingsInnerView';
import MarionetteComponent from 'app/js/views/v3/components/MarionetteComponent';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import ServerUnauthorizedErrorPage from 'app/js/views/v3/components/pages/ServerUnauthorizedErrorPage';
import ServerUnavailableErrorPageConnector from 'app/js/views/v3/components/pages/ServerUnavailableErrorPageConnector';
import EmptyPage from 'app/js/views/v3/components/pages/empty/EmptyPage';
import ErrorPage from 'app/js/views/v3/components/pages/empty/ErrorPage';
import InstallingServerUpdatePageConnector from 'app/js/views/v3/components/pages/empty/InstallingServerUpdatePageConnector';
import ServerSettingsPageHeaderConnector from 'app/js/views/v3/components/pages/serverSettings/ServerSettingsPageHeaderConnector';
import SettingsPage from 'app/js/views/v3/components/pages/settings/SettingsPage';

type ServerSettingsPageProps = {
  serverEntityID: string;
  providerEntityID: string;
  machineIdentifier: string;
  providerIdentifier: string;
  viewDataErrorMessage?: string;
  isViewDataPending: boolean;
  isUnavailable: boolean;
  isUnauthorized: boolean;
  isInstallingServerUpdate: boolean;
  isSettingAccessible: boolean;
  isSettingsPending: boolean;
  settingsSection?: string;
  settingsGroup?: string;
  pageComponent?: React.ReactElement;
  paneComponent?: React.ReactElement;
  contentView?: (...args: any[]) => any;
};

function ServerSettingsPage(props: ServerSettingsPageProps) {
  const {
    serverEntityID,
    providerEntityID,
    machineIdentifier,
    providerIdentifier,
    viewDataErrorMessage,
    isViewDataPending,
    isInstallingServerUpdate,
    isUnavailable,
    isUnauthorized,
    isSettingAccessible,
    isSettingsPending,
    settingsSection,
    settingsGroup,
    pageComponent: Page,
    paneComponent: Pane,
    contentView,
    ...otherOptions
  } = props;

  if (isInstallingServerUpdate) {
    return (
      <InstallingServerUpdatePageConnector serverEntityID={serverEntityID} />
    );
  }

  if (isViewDataPending) {
    return (
      <EmptyPage>
        <Spinner />
      </EmptyPage>
    );
  }

  if (isUnavailable) {
    return (
      <ServerUnavailableErrorPageConnector
        serverEntityID={props.serverEntityID}
      />
    );
  }

  if (isUnauthorized) {
    return <ServerUnauthorizedErrorPage />;
  }

  if (viewDataErrorMessage) {
    return <ErrorPage title={viewDataErrorMessage} />;
  }

  if (!isSettingAccessible) {
    return isSettingsPending ? (
      <EmptyPage>
        <Spinner />
      </EmptyPage>
    ) : (
      <ErrorPage
        title={t('There was an unexpected error loading the settings')}
      />
    );
  }

  if (Page) {
    return (
      // @ts-expect-error ts-migrate(2604) FIXME: JSX element type 'Page' does not have any construc... Remove this comment to see the full error message
      <Page
        serverEntityID={serverEntityID}
        {...getServerSettingsMetricsProps(settingsSection, settingsGroup)}
      />
    );
  }

  return (
    <SettingsPage
      {...getServerSettingsMetricsProps(settingsSection, settingsGroup)}
    >
      <ServerSettingsPageHeaderConnector
        serverEntityID={serverEntityID}
        settingsGroup={settingsGroup}
        settingsSection={settingsSection}
      />
      {Pane ? (
        // @ts-expect-error ts-migrate(2604) FIXME: JSX element type 'Pane' does not have any construc... Remove this comment to see the full error message
        <Pane
          machineIdentifier={machineIdentifier}
          providerEntityID={providerEntityID}
          providerIdentifier={providerIdentifier}
          serverEntityID={serverEntityID}
        />
      ) : (
        <MarionetteComponent
          view={contentView || ServerSettingsInnerView}
          viewOptions={otherOptions}
        />
      )}
    </SettingsPage>
  );
}

export default ServerSettingsPage;
