import React, {
  CSSProperties,
  memo,
  ReactElement,
  useLayoutEffect,
  useRef,
} from 'react';
import cssDurationToMilliseconds from 'app/js/views/v3/helpers/css/cssDurationToMilliseconds';
import animations from 'app/styles/variables/animations';

const DEFAULT_DURATION = cssDurationToMilliseconds(animations.slowSpeed);

export enum ImageFit {
  Fill = 'fill',
  Contain = 'contain',
  Cover = 'cover',
  ScaleDown = 'scale-down',
  None = 'none',
}

export interface CrossFadeImageProps {
  className?: string;
  src?: string;
  alt?: string;
  width?: CSSProperties['width'];
  height?: CSSProperties['height'];
  fit?: ImageFit;
  durationMilliseconds?: number;
}

function CrossFadeImage({
  className,
  src,
  alt,
  width,
  height,
  fit,
  durationMilliseconds = DEFAULT_DURATION,
}: CrossFadeImageProps): ReactElement {
  const containerRef = useRef<HTMLDivElement>(null);

  //
  // Effects

  useLayoutEffect(() => {
    const containerEl = containerRef.current;

    if (!containerEl || !src) {
      return;
    }

    const imgEl = document.createElement('img');

    if (alt) {
      imgEl.alt = alt;
    }

    Object.assign<CSSStyleDeclaration, CSSProperties>(imgEl.style, {
      position: 'absolute',
      width: '100%',
      height: '100%',
      objectFit: fit,
      opacity: '0',
    });

    containerEl.appendChild(imgEl);

    imgEl.onload = () => {
      // If the onLoad handler fires in less than 50 milliseconds assume that it
      // was loaded from cache and immediately display the image with out any
      // animation
      const loadTime = Date.now() - loadTimeStart;
      const isCached = isComplete || loadTime < 50;
      const shouldCrossFade = containerEl.childNodes.length > 1;

      if (
        typeof imgEl.animate !== 'function' ||
        (isCached && !shouldCrossFade)
      ) {
        imgEl.style.opacity = '1';
      } else {
        const animated = imgEl.animate(
          [{ opacity: 0 }, { opacity: 1 }],
          durationMilliseconds
        );

        animated.onfinish = animated.oncancel = () => {
          imgEl.style.opacity = '1';

          while (containerEl.childNodes.length > 1) {
            containerEl.removeChild(containerEl.childNodes[0]);
          }
        };
      }
    };

    imgEl.onerror = () => {
      while (containerEl.childNodes.length > 0) {
        containerEl.removeChild(containerEl.childNodes[0]);
      }
    };

    imgEl.src = src;
    const loadTimeStart = Date.now();
    const isComplete = imgEl.complete;

    return () => {
      imgEl.onload = imgEl.onerror = null;
    };
  }, [src, alt, fit, durationMilliseconds]);

  //
  // Render

  return (
    <div
      ref={containerRef}
      className={className}
      style={{
        position: 'relative',
        overflow: 'hidden',
        width,
        height,
      }}
    />
  );
}

export default memo(CrossFadeImage);
