import { createSelector, Selector } from 'reselect';
import createShallowEqualSelector from 'app/js/stores/selectors/createShallowEqualSelector';
import { EPGLineup } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import selectSelectedDVRLineups from './selectSelectedDVRLineups';
import selectSetupSelectedLineups from './selectSetupSelectedLineups';

const selectSelectedLineupsUnmemoized: Selector<LegacyRootState, EPGLineup[]> =
  createSelector(
    selectSelectedDVRLineups,
    selectSetupSelectedLineups,
    (dvrLineups, setupLineups) => {
      // `setupLineups` is only ever set during tuner setup. Outside the
      // setup, we want lineups associated with the selected DVR, if any exists.
      return setupLineups ?? dvrLineups ?? [];
    }
  );

const selectSelectedLineups = createShallowEqualSelector(
  selectSelectedLineupsUnmemoized,
  (selectedLineups) => selectedLineups
);

export default selectSelectedLineups;
