import React, { ReactElement, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { saveExperienceSettings } from 'app/js/stores/actions/userActions';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import t from 'app/js/utils/t';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import Kind from 'app/js/v4/core/ui/types/Kind';
import IconRemove from 'app/glyphs/remove-560.svg';
import dashboardGraph from 'app/img/upsell/upsell-server-dashboard-graph.svg';
import dashboardList from 'app/img/upsell/upsell-server-dashboard-list.svg';
import styles from './ServerDashboardPageUpsell.css';

export default function ServerDashboardPageUpsell(): ReactElement {
  const dispatch = useDispatch();

  const onDismissPress = useCallback(() => {
    scheduleImmediateUpdate(() => {
      dispatch(
        saveExperienceSettings({ hasDismissedServerDashboardUpsell: true })
      );
    });
  }, [dispatch]);

  return (
    <div className={styles.upsell}>
      <div className={styles.description}>
        {`${t(
          "Want more data? Get stats like top users, what's popular, and bandwidth usage with the new server Dashboard for Plex Pass holders!"
        )} `}

        <Link
          kind={Kind.Primary}
          target="_blank"
          to="https://www.plex.tv/plex-pass"
        >
          {t('Learn more')}
        </Link>
      </div>

      <div className={styles.chartImageContainer}>
        <img className={styles.image} src={String(dashboardGraph)} />
      </div>

      <div className={styles.listImageContainer}>
        <img className={styles.image} src={String(dashboardList)} />
      </div>

      <Link className={styles.button} onPress={onDismissPress}>
        <IconRemove />
      </Link>
    </div>
  );
}
