/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import * as keyCodes from 'app/js/utils/constants/keyCodes';
import getLogLevelLabel from 'app/js/utils/server/getLogLevelLabel';
import t from 'app/js/utils/t';
import DisclosureArrow from 'app/js/views/v3/components/core/base/DisclosureArrow';
import Scroller from 'app/js/views/v3/components/core/base/Scroller';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import Menu from 'app/js/views/v3/components/core/menus/Menu';
import MenuItem from 'app/js/views/v3/components/core/menus/MenuItem';
import getUniqueElementID from 'app/js/views/v3/helpers/getUniqueElementID';
import * as directions from 'app/js/views/v3/helpers/props/directions';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import IconRemove from 'app/glyphs/remove-560.svg';
import formatThreadFilterText from './formatThreadFilterText';
import styles from './ServerLogLineModal.css';

function getLogContainerStyle(level: any) {
  return (
    [
      styles.containerError,
      styles.containerWarning,
      styles.containerInfo,
      styles.containerDebug,
      styles.containerVerbose,
    ][level] || styles.containerUnknown
  );
}

function getLevelLabelStyle(level: any) {
  return [styles.levelError, styles.levelWarning, styles.levelInfo][level];
}

type ServerLogLineModalProps = {
  index?: number;
  level?: number;
  time?: string;
  thread?: string;
  message?: string;
  filter?: string;
  timeFilterStart: string;
  timeFilterEnd: string;
  onJumpToContext: (...args: any[]) => any;
  onTextFilterChange: (...args: any[]) => any;
  onTimeFilterChange: (...args: any[]) => any;
  onClose: (...args: any[]) => any;
};

type ServerLogLineModalState = any;

class ServerLogLineModal extends Component<
  ServerLogLineModalProps,
  ServerLogLineModalState
> {
  // eslint-disable-next-line react/sort-comp
  _timeMenuButtonID: any;

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this._timeMenuButtonID = getUniqueElementID();

    this.state = {
      isTimeMenuOpen: false,
    };
  }

  componentDidMount() {
    document.addEventListener('keydown', this.onKeyDown);
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.onKeyDown);
  }

  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onClosePress = (event: any) => {
    this.props.onClose();
  };

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onShowContextPress = (event: any) => {
    // Reveal 10 preceding log lines.
    // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
    this.props.onJumpToContext(Math.max(this.props.index - 10, 0));
  };

  onFilterFromPress = () => {
    this.props.onTimeFilterChange({
      timeFilterStart: this.props.time,
      timeFilterEnd: this.props.timeFilterEnd,
    });
  };

  onFilterToPress = () => {
    this.props.onTimeFilterChange({
      timeFilterStart: this.props.timeFilterStart,
      timeFilterEnd: this.props.time,
    });
  };

  onThreadPress = () => {
    this.props.onTextFilterChange({
      filter: formatThreadFilterText(this.props.filter, this.props.thread),
    });
  };

  onTimeMenuPress = () => {
    this.setState({ isTimeMenuOpen: !this.state.isTimeMenuOpen });
  };

  onTimeMenuClose = () => {
    this.setState({ isTimeMenuOpen: false });
  };

  onKeyDown = (event: any) => {
    const keyCode = event.keyCode;

    if (keyCode === keyCodes.ESCAPE) {
      event.preventDefault();
      event.stopPropagation();

      this.props.onClose();
    }
  };

  //
  // Render

  render() {
    const { level, time, thread, message } = this.props;

    const { isTimeMenuOpen } = this.state;

    return (
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: Element[]; className: string; au... Remove this comment to see the full error message
      <Scroller autoScroll={true} className={getLogContainerStyle(level)}>
        <Link
          aria-label={t('Close')}
          className={styles.closeButton}
          onPress={this.onClosePress}
        >
          <IconRemove />
        </Link>

        <p>
          <span className={styles.label}>{t('Time')}</span>
          <Link
            // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: (string | Element | undefined)[]... Remove this comment to see the full error message
            id={this._timeMenuButtonID}
            kind={kinds.PRIMARY}
            onPress={this.onTimeMenuPress}
          >
            {time}
            <DisclosureArrow
              direction={isTimeMenuOpen ? directions.UP : directions.DOWN}
              kind={kinds.PRIMARY}
            />
          </Link>
          <Menu
            isOpen={isTimeMenuOpen}
            size={sizes.LARGE}
            target={this._timeMenuButtonID}
            onMenuClose={this.onTimeMenuClose}
          >
            <MenuItem onPress={this.onFilterFromPress}>
              {t('Filter from this time')}
            </MenuItem>
            <MenuItem onPress={this.onFilterToPress}>
              {t('Filter to this time')}
            </MenuItem>
          </Menu>
        </p>

        <p>
          <span className={styles.label}>{t('Level')}</span>
          <span className={getLevelLabelStyle(level)}>
            {getLogLevelLabel(level)}
          </span>
        </p>

        <p>
          <span className={styles.label}>{t('Thread')}</span>
          <Link kind={kinds.PRIMARY} onPress={this.onThreadPress}>
            {thread}
          </Link>
        </p>

        <p>
          <span className={styles.label}>{t('Message')}</span>
          {message}
        </p>

        <div className={styles.action}>
          <Button onPress={this.onShowContextPress}>
            {t('Clear Filters and Jump to Context')}
          </Button>
        </div>
      </Scroller>
    );
  }
}

export default ServerLogLineModal;
