/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

export default function createKeyPool(prefix = '') {
  const pool: any = [];
  const itemsByKey = {};
  const keysByItem = {};
  let counter = 0;

  function generateKey() {
    let key;

    if (pool.length) {
      key = pool.shift();
    } else {
      key = prefix + counter;
      counter++;
    }

    return key;
  }

  function deleteItemByKey(key: any) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    delete itemsByKey[key];
  }

  return {
    getKeys(
      startIndex: any,
      endIndex: any,
      items: any,
      offset = 0,
      keyPropName: any
    ) {
      // Recycle keys for placeholder cells or cells that are now outside the
      // rendered range. Don't remove the recycled keys from the `itemsByKey`
      // object immediately. This keeps items consistently ordered between
      // renders, which reduces the number of DOM operations.

      _.each(itemsByKey, (value, key) => {
        // @ts-expect-error ts-migrate(2571) FIXME: Object is of type 'unknown'.
        const item = value.item;

        if (item === null) {
          pool.push(key);
        } else {
          const itemKey = _.isString(item) ? item : item[keyPropName];
          const itemIndex = items.indexOf(item) + offset;

          if (itemIndex < startIndex || itemIndex > endIndex) {
            pool.push(key);
            // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
            delete keysByItem[itemKey];
          }
        }
      });

      // Loop through the rendered range and either generate new keys or reuse
      // keys that were just recycled.

      for (let i = startIndex; i <= endIndex; i++) {
        const item = items[i - offset] || null;
        const itemKey = item && (_.isString(item) ? item : item[keyPropName]);
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        let key = item && keysByItem[itemKey];

        if (!key) {
          key = generateKey();

          if (item) {
            // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
            keysByItem[itemKey] = key;
          }
        }

        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        itemsByKey[key] = { index: i, item: item };
      }

      // Finally, clean up unused keys in the pool so they are not listed in the
      // `itemsByKey` object and empty the pool. Holding on to excess keys
      // introduces inconsistent key ordering.

      pool.forEach(deleteItemByKey);

      pool.length = 0;

      return _.extend({}, itemsByKey);
    },
  };
}
