/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import FormLabel from './FormLabel';

interface InputGroupFormLabelProps {
  className?: string;
  name: string;
  labelText: React.ReactNode;
  showErrors?: boolean;
  errors: string[];
}

function InputGroupFormLabel(props: InputGroupFormLabelProps) {
  const { className, name, labelText, showErrors, errors } = props;

  const [firstError] = errors;

  return (
    <FormLabel className={className} hasError={showErrors} name={name}>
      {labelText}
      {showErrors && firstError ? `  ${firstError}` : null}
    </FormLabel>
  );
}

export default InputGroupFormLabel;
