import React, { ReactElement, useCallback } from 'react';
import * as providerActions from 'app/js/stores/actions/provider/providerActions';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import t from 'app/js/utils/t';
import { ContentDirectory } from 'app/js/v4/core/providers/providersTypes';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import { useRedispatchLegacyServerAction } from 'app/js/v4/core/servers/hooks/useRedispatchLegacyServerAction';
import useDisclosure from 'app/js/v4/core/ui/hooks/useDisclosure';
import LibraryUpgradeMenuItem from 'app/js/v4/features/libraryManagement/LibraryUpgradeMenuItem';
import Menu from 'app/js/views/v3/components/core/menus/Menu';
import MenuItem from 'app/js/views/v3/components/core/menus/MenuItem';
import ConfirmModal from 'app/js/views/v3/components/core/modals/ConfirmModal';
import createChainedCallback from 'app/js/views/v3/helpers/createChainedCallback';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';

interface LibraryActionsMenuProps {
  target: string;
  isOpen: boolean;
  contentDirectory: ContentDirectory;
  machineIdentifier: string;
  providerEntityID: string;
  onMenuClose(): void;
  onMenuCloseComplete?(): void;
}

export default function LibraryActionsMenu(
  props: LibraryActionsMenuProps
): ReactElement {
  const { target, isOpen, contentDirectory, onMenuClose, onMenuCloseComplete } =
    props;

  const [server, provider] = useSourceContext();

  const analyzeModalState = useDisclosure();
  const refreshAllModalState = useDisclosure();
  const emptyTrashModalState = useDisclosure();
  const deleteModalState = useDisclosure();

  const redispatchLegacyServerAction = useRedispatchLegacyServerAction({
    server,
    provider,
  });

  //
  // Callbacks

  const onScanPress = useCallback(() => {
    redispatchLegacyServerAction(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
      providerActions.scanLibraryFiles({
        contentDirectory,
      })
    );
  }, [contentDirectory, redispatchLegacyServerAction]);

  const onAnalyzePress = useCallback(() => {
    redispatchLegacyServerAction(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
      providerActions.analyzeLibrary({
        contentDirectory,
      })
    );
  }, [contentDirectory, redispatchLegacyServerAction]);

  const onRefreshAllPress = useCallback(() => {
    redispatchLegacyServerAction(
      providerActions.refreshAllLibraryMetadata({
        contentDirectory,
      })
    );
  }, [contentDirectory, redispatchLegacyServerAction]);

  const onEmptyTrashPress = useCallback(() => {
    redispatchLegacyServerAction(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
      providerActions.emptyLibraryTrash({
        contentDirectory,
      })
    );
  }, [contentDirectory, redispatchLegacyServerAction]);

  const onDeletePress = useCallback(() => {
    redispatchLegacyServerAction(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
      providerActions.deleteLibrary({
        contentDirectory,
      })
    );
  }, [contentDirectory, redispatchLegacyServerAction]);

  return (
    <>
      <Menu
        isOpen={isOpen}
        target={target}
        onMenuClose={onMenuClose}
        onMenuCloseComplete={onMenuCloseComplete}
      >
        <LibraryUpgradeMenuItem
          machineIdentifier={props.machineIdentifier}
          server={server}
          sourceDirectory={props.contentDirectory}
          onMenuClose={onMenuClose}
        />
        <MenuItem onPress={createChainedCallback(onMenuClose, onScanPress)}>
          {t('Scan Library Files')}
        </MenuItem>
        <MenuItem
          onPress={createChainedCallback(onMenuClose, analyzeModalState.onOpen)}
        >
          {t('Analyze')}
        </MenuItem>

        <MenuItem
          onPress={createChainedCallback(
            onMenuClose,
            refreshAllModalState.onOpen
          )}
        >
          {t('Refresh All Metadata')}
        </MenuItem>
        <MenuItem
          onPress={createChainedCallback(
            onMenuClose,
            emptyTrashModalState.onOpen
          )}
        >
          {t('Empty Trash')}
        </MenuItem>
        <MenuItem
          kind={kinds.DANGER}
          onPress={createChainedCallback(onMenuClose, deleteModalState.onOpen)}
        >
          {t('Delete')}
        </MenuItem>
      </Menu>

      <ConfirmModal
        confirmLabel={t('Analyze')}
        isOpen={analyzeModalState.isOpen}
        message={t(
          'Analyzes all the media files in the library. The analysis captures information about the media which helps Plex decide how to optimally play back the media.'
        )}
        metricsPage={metricsPages.CONFIRM_ANALYZE_LIBRARY}
        title={t('Analyze Library')}
        onCancel={analyzeModalState.onClose}
        onConfirm={createChainedCallback(
          analyzeModalState.onClose,
          onAnalyzePress
        )}
      />

      <ConfirmModal
        confirmLabel={t('Refresh')}
        isOpen={refreshAllModalState.isOpen}
        message={t(
          'Forces a download of fresh media information from the internet. This can take a long time. Any locked fields are not modified.'
        )}
        metricsPage={metricsPages.CONFIRM_FORCE_REFRESH_LIBRARY}
        title={t('Refresh All Metadata')}
        onCancel={refreshAllModalState.onClose}
        onConfirm={createChainedCallback(
          refreshAllModalState.onClose,
          onRefreshAllPress
        )}
      />

      <ConfirmModal
        confirmLabel={t('Empty')}
        isOpen={emptyTrashModalState.isOpen}
        message={t(
          "By default the media server doesn't remove information for media right away. This helps if a drive is temporarily disconnected. When you empty the trash for a section, all information about missing media is deleted."
        )}
        metricsPage={metricsPages.CONFIRM_EMPTY_TRASH_LIBRARY}
        title={t('Empty Trash')}
        onCancel={emptyTrashModalState.onClose}
        onConfirm={createChainedCallback(
          emptyTrashModalState.onClose,
          onEmptyTrashPress
        )}
      />

      <ConfirmModal
        accordMessage={t('Yes, delete the library "{libraryName}"', {
          libraryName: contentDirectory.title,
        })}
        confirmLabel={t('Delete')}
        isOpen={deleteModalState.isOpen}
        kind={kinds.DANGER}
        message={t(
          'The library "{libraryName}" will be removed from this server. Your media files will not be affected. This can\'t be undone. Continue?',
          { libraryName: contentDirectory.title }
        )}
        metricsPage={metricsPages.CONFIRM_DELETE_LIBRARY}
        title={t('Delete Library')}
        onCancel={deleteModalState.onClose}
        onConfirm={createChainedCallback(
          deleteModalState.onClose,
          onDeletePress
        )}
      />
    </>
  );
}
