import React, {
  ChangeEvent,
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import FormLabel from 'app/js/v4/core/ui/forms/components/FormLabel';
import Select from 'app/js/v4/core/ui/forms/components/Select';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';

interface LanguageOption {
  value: string;
  label: string;
}

export default function DVRSetupLanguageSelect(): ReactElement {
  const dispatch = useDispatch();
  const {
    selectedCountry,
    selectedLanguage,
    languages: { epgLanguages, isPending, isPopulated, error },
  } = useSelector(selectEPGSettings);
  const selectedLanguageCode = selectedLanguage?.code ?? '';

  const languageOptions = useMemo(
    () =>
      epgLanguages.map<LanguageOption>((language) => ({
        value: language.code,
        label: language.title,
      })),
    [epgLanguages]
  );

  //
  // Callbacks

  const onLanguageSelect = useCallback(
    (event: ChangeEvent<HTMLSelectElement>) => {
      dispatch(
        epgSettingsSlice.actions.epgLanguageSelected({
          languageCode: event.currentTarget.value,
        })
      );
    },
    [dispatch]
  );

  //
  // Effects

  useEffect(() => {
    if (isPopulated && selectedCountry && !selectedLanguage) {
      dispatch(
        epgSettingsSlice.actions.epgLanguageSelected({
          languageCode: selectedCountry.language,
        })
      );
    }
  }, [dispatch, isPopulated, selectedCountry, selectedLanguage]);

  //
  // Render

  return (
    <>
      <FormLabel>{t('Language')}</FormLabel>

      {isPending ? t('Loading languages...') : null}
      {error ? t("We couldn't fetch languages at this moment.") : null}
      {isPopulated ? (
        <Select
          name="languageSelect"
          value={selectedLanguageCode}
          onChange={onLanguageSelect}
        >
          <option value="">{t('Select Language')}</option>
          {languageOptions.map(({ value, label }, index) => (
            <option key={index} value={value}>
              {label}
            </option>
          ))}
        </Select>
      ) : null}
    </>
  );
}
