import { createSelector, Selector } from 'reselect';
import { ChannelMapping } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import selectSelectedDVRDevices from 'app/js/v4/features/dvrManagement/selectors/selectSelectedDVRDevices';
import { GuideChannel } from 'app/js/v4/features/livetv/liveTVTypes';
import selectLiveTVChannels from 'app/js/v4/features/livetv/selectors/selectLiveTVChannels';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

const selectMappedLiveTVChannels: Selector<LegacyRootState, GuideChannel[]> =
  createSelector(
    selectLiveTVChannels,
    selectSelectedDVRDevices,
    (channels, devices) => {
      const channelMappings = devices.reduce<ChannelMapping[]>(
        (acc, device) => {
          if (device.ChannelMapping) {
            acc.push(...device.ChannelMapping);
          }

          return acc;
        },
        []
      );

      return channels.filter((channel) =>
        channelMappings.find(
          (mapping) =>
            mapping.enabled && mapping.lineupIdentifier === channel.vcn
        )
      );
    }
  );

export default selectMappedLiveTVChannels;
