import React, {
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import t from 'app/js/utils/t';
import createChannelScanActivitySelector from 'app/js/v4/core/serverActivities/selectors/createChannelScanActivitySelector';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import ModalBody from 'app/js/v4/core/ui/modals/components/ModalBody';
import Scroller, {
  ScrollDirection,
} from 'app/js/v4/core/ui/scrollers/components/Scroller';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import { DVRSetupStepProps } from 'app/js/v4/features/dvrManagement/components/setup/DVRSetupModal';
import { MediaGrabberDevice } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import selectAvailableMediaGrabberDevices from 'app/js/v4/features/dvrManagement/selectors/selectAvailableMediaGrabberDevices';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import DVRSetupDevice from './DVRSetupDevice';
import DVRSetupManualDevice from './DVRSetupManualDevice';
import styles from './DVRSetupDeviceList.css';

function getStatusMessage(
  devices: MediaGrabberDevice[],
  isPending: boolean,
  isPopulated: boolean
): string {
  if (isPending) {
    return t('Looking for compatible devices...');
  }
  if (isPopulated && devices.length > 0) {
    return t('We found hardware we recognize!');
  } else if (isPopulated && devices.length === 0) {
    return t(
      "We couldn't find any hardware we recognize, but will continue to look for available devices."
    );
  } else {
    return t('Something went wrong. Please try again later.');
  }
}

export default function DVRSetupDeviceList({
  onSetStep,
}: DVRSetupStepProps): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();
  const [activeDeviceKey, setActiveDeviceKey] = useState<
    MediaGrabberDevice['key'] | undefined
  >(undefined);

  const {
    devices: { isPending: isDevicesPending, isPopulated: isDevicesPopulated },
    isDiscoveringDevices,
    isCancellingChannelScan,
  } = useSelector(selectDVRSetup);
  const availableDevices = useSelector(selectAvailableMediaGrabberDevices);

  const selectChannelScanActivity = useMemo(
    () => createChannelScanActivitySelector(server),
    [server]
  );

  const channelScanActivity = useSelector(selectChannelScanActivity);

  //
  // Callbacks

  const onSetupStart = useCallback(
    (deviceKey) => {
      setActiveDeviceKey(deviceKey);
    },
    [setActiveDeviceKey]
  );

  //
  // Effects

  useEffect(() => {
    scheduleImmediateUpdate(() => {
      dispatch(dvrSetupSlice.actions.setupReset());
    });
  }, [dispatch]);

  useEffect(() => {
    if (isCancellingChannelScan) {
      return;
    }

    if (channelScanActivity) {
      dispatch(
        dvrSetupSlice.actions.deviceChannelsCancelScanRequested({
          server,
          deviceKey: channelScanActivity.Context.deviceID,
        })
      );
    } else if (!isDevicesPopulated) {
      dispatch(dvrSetupSlice.actions.devicesFetchRequested({ server }));
    }
  }, [
    dispatch,
    server,
    channelScanActivity,
    isCancellingChannelScan,
    isDevicesPopulated,
  ]);

  useEffect(() => {
    if (
      activeDeviceKey &&
      !availableDevices.some((device) => device.key === activeDeviceKey)
    ) {
      setActiveDeviceKey(undefined);
    } else if (availableDevices.length === 1 && !activeDeviceKey) {
      setActiveDeviceKey(availableDevices[0].key);
    }
  }, [availableDevices, activeDeviceKey, setActiveDeviceKey]);

  useEffect(() => {
    let devicesDiscoverTimeoutID: number;

    if (!isDiscoveringDevices && !isCancellingChannelScan) {
      devicesDiscoverTimeoutID = window.setTimeout(() => {
        dispatch(dvrSetupSlice.actions.devicesDiscoverRequested({ server }));
      }, 2000);
    }

    return () => {
      if (devicesDiscoverTimeoutID) {
        window.clearTimeout(devicesDiscoverTimeoutID);
      }
    };
  }, [dispatch, server, isDiscoveringDevices, isCancellingChannelScan]);

  //
  // Render

  const message = getStatusMessage(
    availableDevices,
    isDevicesPending || isCancellingChannelScan,
    isDevicesPopulated
  );

  const showSpinner =
    isCancellingChannelScan ||
    isDevicesPending ||
    !availableDevices.length ||
    (availableDevices.length === 1 && !activeDeviceKey);

  return (
    <ModalBody
      className={styles.modalBody}
      innerClassName={styles.innerModalBody}
    >
      <p>{message}</p>
      <DVRSetupManualDevice />
      {showSpinner ? (
        <div className={styles.spinnerContainer}>
          <Spinner />
        </div>
      ) : (
        <Scroller
          className={styles.devicesContainer}
          scrollDirection={ScrollDirection.Horizontal}
        >
          {availableDevices.map((device) => (
            <DVRSetupDevice
              key={device.key}
              activeDeviceKey={activeDeviceKey}
              device={device}
              onSetStep={onSetStep}
              onSetupStart={onSetupStart}
            />
          ))}
        </Scroller>
      )}
    </ModalBody>
  );
}
