/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import DisclosureArrowButton from 'app/js/views/v3/components/core/buttons/DisclosureArrowButton';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import * as directions from 'app/js/views/v3/helpers/props/directions';
import styles from './PageHeaderBreadcrumbButton.css';

interface PageHeaderBreadcrumbButtonProps {
  linkClassName?: string;
  buttonClassName?: string;
  to?: string;
  hasMenu?: boolean;
  isMenuOpen?: boolean;
  isSelected?: boolean;
  isDisabled?: boolean;
}

class PageHeaderBreadcrumbButton extends Component<PageHeaderBreadcrumbButtonProps> {
  static defaultProps = {
    linkClassName: styles.link,
    buttonClassName: styles.button,
    hasMenu: false,
    isMenuOpen: false,
    isSelected: false,
    isDisabled: false,
  };

  //
  // Render

  render() {
    const {
      linkClassName,
      buttonClassName,
      children,
      to,
      hasMenu,
      isMenuOpen,
      isSelected,
      isDisabled,
      ...otherProps
    } = this.props;

    if (!hasMenu && !to) {
      return <span className={linkClassName}>{children}</span>;
    }

    if (!hasMenu) {
      return (
        <Link className={linkClassName} isDisabled={isDisabled} to={to}>
          {children}
        </Link>
      );
    }

    return (
      <DisclosureArrowButton
        className={buttonClassName}
        data-testid="typeDropdownButton"
        direction={isMenuOpen ? directions.UP : directions.DOWN}
        // @ts-expect-error ts-migrate(2769) FIXME: No overload matches this call.
        isDisabled={isDisabled}
        isSelected={isSelected}
        {...otherProps}
      >
        {children}
      </DisclosureArrowButton>
    );
  }
}

export default PageHeaderBreadcrumbButton;
