/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import React from 'react';
import * as adapters from 'app/js/adapters/adapters';
import BackgroundProcessingListCollection from 'app/js/models/library/convert/BackgroundProcessingListCollection';
import BackgroundProcessingQueueCollection from 'app/js/models/library/convert/BackgroundProcessingQueueCollection';
import ServerAccountModel from 'app/js/models/server/ServerAccountModel';
import ServerSettingCollection from 'app/js/models/server/ServerSettingCollection';
import { createServerSettingsSidebarKey } from 'app/js/utils/settingsSidebarKey';
import createEntityID from 'app/js/utils/state/createEntityID';
import createLibraryProviderEntityID from 'app/js/utils/state/createLibraryProviderEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import t from 'app/js/utils/t';
import AbstractReactView from 'app/js/views/AbstractReactView';
import AlertsView from 'app/js/views/activity/AlertsView';
import ConversionView from 'app/js/views/activity/ConversionView';
import ServerDashboardView from 'app/js/views/activity/ServerDashboardView';
import FullPage from 'app/js/views/v3/components/core/pages/FullPage';
import ServerSettingsPageConnector from 'app/js/views/v3/components/pages/serverSettings/ServerSettingsPageConnector';
import SettingsSidebarConnector from 'app/js/views/v3/components/sidebar/settings/SettingsSidebarConnector';

const ServerSettingsView = AbstractReactView.extend({
  //
  // Init

  initialize(options: any) {
    const { serverID, settingsSection, settingsGroup } = options;

    const appModel = adapters.resolve('appModel');
    const servers = appModel.get('servers');

    this.options = {
      settingsSection,
      settingsGroup,
      machineIdentifier: serverID,
      isViewDataPending: true,
    };

    servers
      .findServer(serverID)
      .done(
        this.whenRendered((server: any) => {
          // Ensure server is in the state tree before render
          // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 0.
          scheduleImmediateUpdate();

          // When server is specified; make sure it is the primary server.  From this
          // point we *try* to use the primaryServer as the selected settings server.
          appModel.set('primaryServer', server);

          if (!server.get('isConnected')) {
            this.options.isViewDataPending =
              !server.get('isUnavailable') && !server.get('isUnauthorized');
            this.render();

            return;
          }

          if (server.get('isShared')) {
            Object.assign(this.options, {
              viewDataErrorMessage: t('You do not have access to this server'),
              isViewDataPending: false,
            });

            this.render();
            return;
          }

          if (settingsSection === 'settings' || settingsSection === 'manage') {
            this.initializeServerSettingsView(server, options);
          } else if (
            settingsSection === 'status' &&
            settingsGroup === 'alerts'
          ) {
            this.initializeAlertsView(server);
          } else if (
            settingsSection === 'status' &&
            settingsGroup === 'conversion'
          ) {
            this.initializeConversionView(server);
          } else if (
            settingsSection === 'status' &&
            settingsGroup === 'server-dashboard'
          ) {
            this.initializeServerDashboardView(server);
          } else {
            // Nothing to initialize for this settings section
            this.options.isViewDataPending = false;
            this.render();
          }
        })
      )
      .fail(
        this.whenRendered(() => {
          Object.assign(this.options, {
            viewDataErrorMessage: t(
              "The server you're trying to access doesn't seem to exist."
            ),
            isViewDataPending: false,
          });

          this.render();
        })
      );
  },

  initializeServerSettingsView(server: any) {
    const group = this.options.settingsGroup;

    const serverFlexModel = server.get(__PLATFORM__);
    const settings = serverFlexModel.get('settings');
    const account = new ServerAccountModel(
      { key: '/accounts/1' },
      { server: server }
    );
    const cloudAccount = serverFlexModel.get('cloudAccount');
    const channelSettings = new ServerSettingCollection([], {
      server: server,
      url: '/system/:/prefs',
    });

    // Don't use a scope with the settings request since we generally want
    // that collection to be updated for the rest of the app as well. It
    // doesn't help us to abort it.

    const settingsPromise = $.when(
      settings.fetch(),
      account.fetch(),
      cloudAccount.fetch(),
      channelSettings.fetch()
    );

    settingsPromise.done(
      this.whenRendered(() => {
        Object.assign(this.options, {
          model: server,
          collection: settings,
          account,
          cloudAccount,
          channelSettings,
          subset: 'server',
          group,
          isViewDataPending: false,
        });

        this.render();
      })
    );

    settingsPromise.fail(
      this.whenRendered(() => {
        Object.assign(this.options, {
          viewDataErrorMessage: t('Server settings are unavailable.'),
          isViewDataPending: false,
        });

        this.render();
      })
    );
  },

  initializeAlertsView(server: any) {
    const serverFlexModel = server.get(__PLATFORM__);

    Object.assign(this.options, {
      model: server,
      collection: serverFlexModel.get('alerts'),
      subset: 'alerts',
      contentView: AlertsView,
      isViewDataPending: false,
    });

    this.render();
  },

  initializeConversionView(server: any) {
    const serverFlexModel = server.get(__PLATFORM__);

    this.options.contentView = ConversionView;

    const playQueues = new BackgroundProcessingQueueCollection([], {
      server: server,
      allowInCache: true,
    });

    const transcodeJobs = serverFlexModel.get('backgroundTranscodeJobs');

    const playlists = new BackgroundProcessingListCollection([], {
      server: server,
      allowInCache: true,
    });

    let playQueue: any;
    let generators: any;

    const playQueuePromise = $.when(
      // We don't expect the background processing play queue to change,
      // so just populate here in case it is already fetched.
      playQueues.populate(),
      transcodeJobs.fetch(),
      // Fetch settings so we have an updated `BackgroundQueueIdlePaused`.
      serverFlexModel.get('settings').fetch(),
      // We need the list of generators to check for failed items.
      playlists.populate()
    ).then(() => {
      playQueue = playQueues.first();
      const playlist = playlists.first();

      if (playQueue && playlist) {
        generators = playlist.get('children');

        return $.when(playQueue.fetch(), generators.fetch());
      } else {
        return $.Deferred().reject().promise();
      }
    });

    playQueuePromise.done(
      this.whenRendered(() => {
        Object.assign(this.options, {
          model: server,
          playQueue,
          transcodeJobs,
          generators,
          subset: 'conversion',
          isViewDataPending: false,
        });

        this.render();
      })
    );

    playQueuePromise.fail(
      this.whenRendered(() => {
        Object.assign(this.options, {
          viewDataErrorMessage: t('The conversion queue is unavailable.'),
          isViewDataPending: false,
        });

        this.render();
      })
    );
  },

  initializeServerDashboardView(server: any) {
    Object.assign(this.options, {
      server,
      subset: 'server-dashboard',
      contentView: ServerDashboardView,
      isViewDataPending: false,
    });

    this.render();
  },

  //
  // Control

  component(options: any) {
    const {
      machineIdentifier,
      settingsSection,
      settingsGroup,
      sidebarKey,
      ...otherOptions
    } = options;

    const serverEntityID = createEntityID(
      entityTypes.SERVER,
      machineIdentifier
    );

    const selectedKey = sidebarKey
      ? sidebarKey
      : createServerSettingsSidebarKey(
          machineIdentifier,
          settingsSection,
          settingsGroup
        );

    return (
      <FullPage>
        <SettingsSidebarConnector
          selectedKey={selectedKey}
          serverMenuServerEntityID={serverEntityID}
          settingsGroup={settingsGroup}
          settingsSection={settingsSection}
        />
        <ServerSettingsPageConnector
          providerEntityID={createLibraryProviderEntityID(serverEntityID)}
          serverEntityID={serverEntityID}
          settingsGroup={settingsGroup}
          settingsSection={settingsSection}
          {...otherOptions}
        />
      </FullPage>
    );
  },
});

export default ServerSettingsView;
