import { useMemo } from 'react';
import { useSelector } from 'react-redux';
import { ContentDirectory } from 'app/js/v4/core/providers/providersTypes';
import createLibraryUpdateActivitySelector from 'app/js/v4/core/serverActivities/selectors/createLibraryUpdateActivitySelector';
import { Server } from 'app/js/v4/core/servers/serversTypes';

export default function useLibraryUpdating(
  server: Server,
  contentDirectory?: ContentDirectory
): boolean {
  const selectProviderContentDirectoryUpdateActivity = useMemo(
    () =>
      contentDirectory
        ? createLibraryUpdateActivitySelector(server, contentDirectory)
        : () => undefined,
    [contentDirectory, server]
  );
  const libraryUpdateActivity = useSelector(
    selectProviderContentDirectoryUpdateActivity
  );

  // PMS Version 1.20.5 includes the needed Context to match to the content
  // directory, so fallback to the contentDirectory attribute, which may be
  // removed at a later date to make provider objects more stable
  return !!libraryUpdateActivity || !!contentDirectory?.refreshing;
}
