import React, { ReactElement, useCallback, useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { createSelector, Selector } from 'reselect';
import { IconListRow } from '@plex/react-chroma-icon';
import { saveExperienceSettings } from 'app/js/stores/actions/userActions';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import t from 'app/js/utils/t';
import createServerNowPlayingSessionsSelector from 'app/js/v4/core/serverNowPlayingSessions/selectors/createServerNowPlayingSessionsSelector';
import serverNowPlayingSessionsSlice from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsSlice';
import { ServerNowPlayingSessionsPerServerState } from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsTypes';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Link from 'app/js/v4/core/ui/buttons/components/Link';
import useMeasure from 'app/js/v4/core/ui/hooks/useMeasure';
import createUserExperienceSettingSelector from 'app/js/v4/core/user/selectors/createUserExperienceSettingSelector';
import ServerDashboardPageHeader from 'app/js/v4/features/dashboard/components/ServerDashboardPageHeader';
import ServerDashboardPageMessage from 'app/js/v4/features/dashboard/components/ServerDashboardPageMessage';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import { PlainError } from 'app/js/v4/utils/data/api';
import TooltipTrigger from 'app/js/views/v3/components/core/tooltips/TooltipTrigger';
import NowPlayingErrorModal from './NowPlayingErrorModal';
import SessionTile from './SessionTile';
import styles from './NowPlaying.css';

const COLUMN_WIDTH = 280;
const COLUMN_MARGIN = 40;
const SERVER_SESSIONS_POLL_INTERVAL = 5000;

type SessionKeys = ServerNowPlayingSessionsPerServerState['sessionKeys'];

function mapSessionKeysToColumns(
  sessionKeys: SessionKeys,
  width: number
): SessionKeys[] {
  const numberOfColumns = Math.max(
    Math.floor((width + COLUMN_MARGIN) / (COLUMN_WIDTH + COLUMN_MARGIN)),
    1
  );

  const columns = Array.from(new Array(numberOfColumns), () => []);

  return sessionKeys.reduce<string[][]>((acc, sessionKey, index) => {
    acc[index % numberOfColumns].push(sessionKey);

    return acc;
  }, columns);
}

function createServerNowPlayingSessionKeysSelector(
  machineIdentifier: string
): Selector<LegacyRootState, SessionKeys> {
  return createSelector(
    createServerNowPlayingSessionsSelector(machineIdentifier),
    (serverNowPlayingSessions) => serverNowPlayingSessions.sessionKeys
  );
}

function createServerNowPlayingStopSessionErrorSelector(
  machineIdentifier: string
): Selector<LegacyRootState, PlainError | null> {
  return createSelector(
    createServerNowPlayingSessionsSelector(machineIdentifier),
    (serverNowPlayingSessions) => serverNowPlayingSessions.stopSessionError
  );
}

export default function NowPlaying(): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();

  const [ref, { width }] = useMeasure();

  const selectServerNowPlayingSessionKeys = useMemo(() => {
    return createServerNowPlayingSessionKeysSelector(server.machineIdentifier);
  }, [server.machineIdentifier]);

  const sessionKeys = useSelector(selectServerNowPlayingSessionKeys);

  const selectServerNowPlayingStopSessionError = useMemo(() => {
    return createServerNowPlayingStopSessionErrorSelector(
      server.machineIdentifier
    );
  }, [server.machineIdentifier]);

  const stopSessionError = useSelector(selectServerNowPlayingStopSessionError);

  const isDetailsExpanded = useSelector(
    createUserExperienceSettingSelector('dashboardNowPlayingShowDetails')
  );

  const canStopSessions = !!server.ownerFeatures?.includes('session_kick');
  const hasSessions = sessionKeys.length > 0;

  //
  // Callbacks

  const onStopErrorDismiss = useCallback(() => {
    scheduleImmediateUpdate(() => {
      dispatch(
        serverNowPlayingSessionsSlice.actions.stopErrorDismissed({ server })
      );
    });
  }, [dispatch, server]);

  const onTogglePress = useCallback(() => {
    scheduleImmediateUpdate(() => {
      dispatch(
        saveExperienceSettings({
          dashboardNowPlayingShowDetails: !isDetailsExpanded,
        })
      );
    });
  }, [dispatch, isDetailsExpanded]);

  //
  // Effects

  useEffect(() => {
    let intervalID: number;

    if (hasSessions) {
      // Socket notifications will accurately tell us when sessions come and go,
      // but during the lifetime of a session periodically poll to ensure the
      // sessions are kept up to date.

      intervalID = window.setInterval(
        () =>
          dispatch(
            serverNowPlayingSessionsSlice.actions.fetchRequested({ server })
          ),
        SERVER_SESSIONS_POLL_INTERVAL
      );
    }

    return () => window.clearInterval(intervalID);
  }, [dispatch, hasSessions, server]);

  //
  // Render

  return (
    <div ref={ref}>
      <NowPlayingErrorModal
        errorMessage={t('There was a problem stopping playback')}
        isOpen={!!stopSessionError}
        metricsPage={metricsPages.STOP_SERVER_SESSION_ERROR}
        onModalClose={onStopErrorDismiss}
      />

      <ServerDashboardPageHeader title={t('Now Playing')}>
        {hasSessions ? (
          <TooltipTrigger
            className={styles.toggle}
            // @ts-expect-error ts-migrate(2739) FIXME: Type 'ForwardRefExoticComponent<Pick<InnerLinkProp... Remove this comment to see the full error message
            component={Link}
            data-testid="toolbarToggleNowPlayingDetails"
            hideOnPress={false}
            tooltip={isDetailsExpanded ? t('Hide Details') : t('Show Details')}
            onPress={onTogglePress}
          >
            <IconListRow />
          </TooltipTrigger>
        ) : null}
      </ServerDashboardPageHeader>

      <div className={styles.tilesContainer}>
        {hasSessions && width != null ? (
          <div className={styles.nowPlayingList}>
            {mapSessionKeysToColumns(sessionKeys, width).map(
              (column, index) => {
                return (
                  <div
                    key={index}
                    className={styles.column}
                    style={{
                      width: `${COLUMN_WIDTH}px`,
                    }}
                  >
                    {column.map((sessionKey) => {
                      return (
                        <SessionTile
                          key={sessionKey}
                          canStopSessions={canStopSessions}
                          isDetailsExpanded={isDetailsExpanded}
                          sessionKey={sessionKey}
                        />
                      );
                    })}
                  </div>
                );
              }
            )}
          </div>
        ) : null}

        {!hasSessions ? (
          <ServerDashboardPageMessage className={styles.emptyMessage}>
            {t('Nothing is currently being played')}
          </ServerDashboardPageMessage>
        ) : null}
      </div>
    </div>
  );
}
