/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { Component } from 'react';
import t from 'app/js/utils/t';
import MenuItem from 'app/js/views/v3/components/core/menus/MenuItem';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';
import IconSelected from 'app/glyphs/selected-560.svg';
import styles from './ServerMenuItem.css';

type ServerMenuItemProps = {
  className?: string;
  friendlyNameClassName?: string;
  detailsClassName?: string;
  to?: string;
  isSelected?: boolean;
  isDisabled?: boolean;
  identifier: string;
  title?: string;
  details?: string;
  onPress?: (...args: any[]) => any;
};

class ServerMenuItem extends Component<ServerMenuItemProps> {
  static defaultProps = {
    friendlyNameClassName: styles.serverMenuFriendlyName,
    detailsClassName: styles.serverMenuDetails,
  };

  //
  // Lifecycle

  shouldComponentUpdate(nextProps: ServerMenuItemProps, nextState: any) {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    return shouldPureComponentUpdate(this, nextProps, nextState);
  }

  //
  // Render

  render() {
    const {
      children,
      className,
      friendlyNameClassName,
      detailsClassName,
      to,
      isDisabled,
      isSelected,
      identifier,
      title,
      details,
      onPress,
    } = this.props;

    return (
      <MenuItem
        className={classNames(
          className,
          styles.serverMenuItem,
          isSelected && styles.selectedServerMenuItem
        )}
        data-testid={'serverMenuItem--' + identifier}
        isDisabled={isDisabled}
        to={to}
        onPress={onPress}
      >
        <div className={styles.serverMenuItemContainer}>
          <div className={styles.serverMenuTitle}>
            <div className={friendlyNameClassName}>{title}</div>
            <div className={detailsClassName}>{details}</div>
          </div>
          <div className={styles.serverMenuIconContainer}>
            {isSelected ? (
              <IconSelected
                aria-label={t('Selected')}
                className={classNames(
                  styles.serverMenuIcon,
                  styles.selectedIcon
                )}
              />
            ) : null}
            {children}
          </div>
        </div>
      </MenuItem>
    );
  }
}

export default ServerMenuItem;
