import { createSelector, Selector } from 'reselect';
import * as activityTypes from 'app/js/utils/server/activityTypes';
import { Provider } from 'app/js/v4/core/providers/providersTypes';
import createServerActivitiesSelector from 'app/js/v4/core/serverActivities/selectors/createServerActivitiesSelector';
import { ServerActivity } from 'app/js/v4/core/serverActivities/serverActivitiesTypes';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

function createEPGLoadActivitySelector(
  server: Server,
  provider: Provider
): Selector<LegacyRootState, ServerActivity | undefined> {
  return createSelector(
    createServerActivitiesSelector(server),
    (activities) => {
      return activities.find((activity) => {
        return (
          activity.type === activityTypes.PROVIDER_EPG_LOAD &&
          activity.Context.identifier === provider.identifier
        );
      });
    }
  );
}

export default createEPGLoadActivitySelector;
