import { createSelector, Selector } from 'reselect';
import { MediaGrabberDevice } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import selectDVRManagement from './selectDVRManagement';

const selectSelectedDVRDevices: Selector<
  LegacyRootState,
  MediaGrabberDevice[]
> = createSelector(
  selectDVRManagement,
  (dvrManagement) => dvrManagement.dvrSetup.selectedDVR.dvr?.Device ?? []
);

export default selectSelectedDVRDevices;
