import React, {
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { IconChevronLeft, IconChevronRight } from '@plex/react-chroma-icon';
import { Row } from '@plex/react-chroma-layout';
import { Align, Size } from '@plex/react-chroma-types';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import IconButton from 'app/js/v4/core/ui/buttons/components/IconButton';
import usePreviousValueForAtMost from 'app/js/v4/core/ui/hooks/usePreviousValueForAtMost';
import { VirtualListRanges } from 'app/js/v4/core/ui/lists/listsTypes';
import ServerDashboardPageHeader from 'app/js/v4/features/dashboard/components/ServerDashboardPageHeader';
import ServerDashboardFilterMenuButton from 'app/js/v4/features/dashboard/components/menuButtons/ServerDashboardFilterMenuButton';
import dashboardTopUsersSlice from 'app/js/v4/features/dashboard/dashboardTopUsersSlice';
import selectDashboardTopUsers from 'app/js/v4/features/dashboard/selectors/selectDashboardTopUsers';
import {
  getTimePeriodAfter,
  getTimePeriodLabel,
  getTimePeriodTimespan,
  TimePeriod,
} from 'app/js/v4/utils/timePeriods';
import TopUsersList from './TopUsersList';
import styles from './TopUsers.css';

export default function TopUsers(): ReactElement {
  const dispatch = useDispatch();
  const [server] = useSourceContext();

  const rangesRef = useRef<VirtualListRanges | null>(null);
  const [isPreviousEnabled, setIsPreviousEnabled] = useState(false);
  const [isNextEnabled, setIsNextEnabled] = useState(false);
  const [targetScrollIndex, setTargetScrollIndex] = useState(0);
  const [smoothScroll, setSmoothScroll] = useState(false);
  const [activeTimePeriod, setActiveTimePeriod] = useState(TimePeriod.Week);

  const { accounts, statistics, isPending, isPopulated, error } = useSelector(
    selectDashboardTopUsers
  );

  const isDelayedPending = usePreviousValueForAtMost(isPending, true);

  const timePeriods = useMemo(() => {
    return [
      TimePeriod.Day,
      TimePeriod.Week,
      TimePeriod.Month,
      TimePeriod.NinetyDays,
      TimePeriod.Year,
      TimePeriod.AllTime,
    ];
  }, []);

  //
  // Callbacks

  const onTimePeriodSelect = useCallback((timePeriod: TimePeriod) => {
    setActiveTimePeriod(timePeriod);
  }, []);

  const onListUpdate = useCallback((ranges: VirtualListRanges) => {
    const { visibleStartIndex, visibleEndIndex, renderEndIndex } = ranges;

    rangesRef.current = ranges;

    setIsPreviousEnabled(visibleStartIndex > 0);
    setIsNextEnabled(renderEndIndex > visibleEndIndex);
  }, []);

  const onPreviousPress = useCallback(() => {
    const ranges = rangesRef.current;

    if (ranges) {
      const numVisibleItems = ranges.visibleEndIndex - ranges.visibleStartIndex;

      setTargetScrollIndex(
        Math.max(ranges.visibleStartIndex - numVisibleItems - 1, 0)
      );
      setSmoothScroll(true);
    }
  }, []);

  const onNextPress = useCallback(() => {
    const ranges = rangesRef.current;

    if (ranges) {
      setTargetScrollIndex(ranges.visibleEndIndex + 1);
      setSmoothScroll(true);
    }
  }, []);

  //
  // Effects

  useEffect(() => {
    dispatch(
      dashboardTopUsersSlice.actions.topUsersFetchRequested({
        server,
        timespan: getTimePeriodTimespan(activeTimePeriod),
        after: getTimePeriodAfter(activeTimePeriod),
      })
    );
  }, [activeTimePeriod, dispatch, server]);

  useEffect(() => {
    // Reset the scroll index to the beginning when the dataset changes.
    setTargetScrollIndex(0);
    setSmoothScroll(false);
  }, [statistics]);

  useEffect(() => {
    return () => {
      dispatch(dashboardTopUsersSlice.actions.reset());
    };
  }, [dispatch]);

  //
  // Render

  return (
    <div className={styles.container}>
      <ServerDashboardPageHeader title={t('Top Users')}>
        <Row gap={Size.XL} verticalAlign={Align.Center}>
          <ServerDashboardFilterMenuButton
            activeValue={activeTimePeriod}
            formatter={getTimePeriodLabel}
            values={timePeriods}
            onSelect={onTimePeriodSelect}
          />

          <div className={styles.listActions}>
            <IconButton
              className={styles.listScrollButton}
              iconComponent={IconChevronLeft}
              isDisabled={!isPreviousEnabled}
              title={t('Previous Page')}
              onPress={onPreviousPress}
            />
            <IconButton
              className={styles.listScrollButton}
              iconComponent={IconChevronRight}
              isDisabled={!isNextEnabled}
              title={t('Next Page')}
              onPress={onNextPress}
            />
          </div>
        </Row>
      </ServerDashboardPageHeader>

      <TopUsersList
        accounts={accounts}
        activeTimePeriod={activeTimePeriod}
        errorMessage={error ? t('Unable to load statistics') : undefined}
        isSpinnerVisible={isDelayedPending}
        showList={!isDelayedPending && isPopulated}
        smoothScroll={smoothScroll}
        statistics={statistics}
        targetScrollIndex={targetScrollIndex}
        onListUpdate={onListUpdate}
      />
    </div>
  );
}
