/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import {
  clearDeviceSettingsSaveError,
  saveDeviceSettings,
  saveDeviceTitle,
} from 'app/js/stores/actions/ui/dvrSetupActions';
import createDVRDeviceSelector from 'app/js/stores/selectors/livetv/createDVRDeviceSelector';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import DVRDeviceSettingsModalContent from './DVRDeviceSettingsModalContent';

function createMapStateToProps() {
  return createSelector(
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState, { dvrID }) => dvrID,
    createDVRDeviceSelector(),
    (dvrID, device) => {
      const {
        key: deviceID,
        model,
        uri,
        prefs,
        title,
        thumb,
        isSaving,
        saveError,
      } = device;

      return {
        dvrID,
        deviceID,
        model,
        uri,
        prefs,
        title,
        thumb,
        isSaving,
        saveError,
      };
    }
  );
}

type DVRDeviceSettingsModalContentConnectorProps = {
  serverEntityID: string;
  dvrID: number;
  deviceID: number;
  onModalClose: (...args: any[]) => any;
  saveDeviceSettings: (...args: any[]) => any;
  saveDeviceTitle: (...args: any[]) => any;
  clearDeviceSettingsSaveError: (...args: any[]) => any;
};

class DVRDeviceSettingsModalContentConnector extends Component<DVRDeviceSettingsModalContentConnectorProps> {
  //
  // Listeners

  onSubmit = (formValues: any) => {
    const { serverEntityID, dvrID, deviceID } = this.props;
    const { deviceTitle, ...prefs } = formValues;

    scheduleImmediateUpdate(() => {
      this.props.saveDeviceSettings({ serverEntityID, dvrID, deviceID, prefs });
      this.props.saveDeviceTitle({
        serverEntityID,
        dvrID,
        deviceID,
        title: deviceTitle,
      });
    });
  };

  onModalClose = () => {
    const { dvrID, deviceID } = this.props;

    this.props.clearDeviceSettingsSaveError({ dvrID, deviceID });
    this.props.onModalClose();
  };

  //
  // Render

  render() {
    return (
      // @ts-expect-error ts-migrate(2769) FIXME: No overload matches this call.
      <DVRDeviceSettingsModalContent
        {...this.props}
        onModalClose={this.onModalClose}
        onSubmit={this.onSubmit}
      />
    );
  }
}

export default connect(createMapStateToProps, {
  saveDeviceSettings,
  saveDeviceTitle,
  clearDeviceSettingsSaveError,
})(DVRDeviceSettingsModalContentConnector);
