/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import ServerSettingsServerMenuButton from './ServerSettingsServerMenuButton';

function createMapStateToProps() {
  return createSelector(createUnstableServerSelector(), (server) => {
    return {
      machineIdentifier: server.machineIdentifier,
      friendlyName: server.friendlyName,
      isFullOwnedServer: server.isFullOwnedServer,
      isInstallingServerUpdate: server.isInstallingServerUpdate,
      isShared: server.isShared,
      isConnected: server.isConnected,
      isUnavailable: server.isUnavailable,
      isSettingsPending: server[__PLATFORM__].isSettingsPending,
      isSecure: server.isSecure,
      isRelayed: server.isRelayed,
      sourceTitle: server.sourceTitle,
    };
  });
}

export default connect(createMapStateToProps)(ServerSettingsServerMenuButton);
