import React, { ReactElement, ReactNode } from 'react';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import Size from 'app/js/v4/core/ui/types/Size';
import ServerDashboardPageMessage from './ServerDashboardPageMessage';
import styles from './ServerDashboardSectionContent.css';

export interface ServerDashboardSectionContentProps {
  children: ReactNode;
  className?: string;
  errorClassName?: string;
  isSpinnerVisible?: boolean;
  errorMessage?: string;
}

export default function ServerDashboardSectionContent({
  children,
  className = styles.container,
  errorClassName = styles.errorMessage,
  isSpinnerVisible,
  errorMessage,
}: ServerDashboardSectionContentProps): ReactElement {
  return (
    <div className={className}>
      {errorMessage ? (
        <ServerDashboardPageMessage className={errorClassName}>
          {errorMessage}
        </ServerDashboardPageMessage>
      ) : (
        <>
          {isSpinnerVisible ? (
            <Spinner className={styles.spinner} size={Size.Large} />
          ) : null}

          {children}
        </>
      )}
    </div>
  );
}
