import React, { ReactElement, useCallback } from 'react';
import { useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import {
  PivotView,
  ProviderFeatureType,
} from 'app/js/v4/core/providers/providersTypes';
import getProviderFeature from 'app/js/v4/core/providers/utils/getProviderFeature';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Button from 'app/js/v4/core/ui/buttons/components/Button';
import ModalBody from 'app/js/v4/core/ui/modals/components/ModalBody';
import ModalFooter from 'app/js/v4/core/ui/modals/components/ModalFooter';
import ModalFooterButtonBar from 'app/js/v4/core/ui/modals/components/ModalFooterButtonBar';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import Kind from 'app/js/v4/core/ui/types/Kind';
import getPivotRoute from 'app/js/v4/features/directory/utils/getPivotRoute';
import {
  DVRSetupStep,
  DVRSetupStepProps,
} from 'app/js/v4/features/dvrManagement/components/setup/DVRSetupModal';
import selectAvailableMediaGrabberDevices from 'app/js/v4/features/dvrManagement/selectors/selectAvailableMediaGrabberDevices';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import AdditionalDevice from './AdditionalDevice';
import styles from './DVRSetupDownload.css';

export default function DVRSetupDownload({
  onModalClose,
  onSetStep,
}: DVRSetupStepProps): ReactElement {
  const [server, provider] = useSourceContext();
  const {
    devices: { isPending: isDevicesPending },
  } = useSelector(selectDVRSetup);
  const { epgDownloadActivityID } = useSelector(selectDVRSetup);
  const availableDevices = useSelector(selectAvailableMediaGrabberDevices);

  const contentFeature = getProviderFeature(
    provider,
    ProviderFeatureType.Content
  );
  const sourceDirectory = contentFeature?.directories?.find(
    (contentDirectory) => {
      return contentDirectory.hubKey && !contentDirectory.key;
    }
  );
  const guidePivot = sourceDirectory?.pivots?.find(
    (pivot) => pivot.key === PivotView.Guide
  );

  const guideRoute =
    guidePivot && getPivotRoute(server, provider, sourceDirectory, guidePivot);

  //
  // Callbacks

  const onSetupPress = useCallback(() => {
    onSetStep(DVRSetupStep.Device);
  }, [onSetStep]);

  //
  // Render

  return (
    <>
      <ModalBody innerClassName={styles.innerModalBody}>
        {!isDevicesPending && availableDevices.length > 0 ? (
          <div className={styles.additionalDevices}>
            <p className={styles.accentText}>{t('Setup another device?')}</p>
            {availableDevices.map((device) => (
              <AdditionalDevice
                key={device.key}
                device={device}
                onSetupPress={onSetupPress}
              />
            ))}
          </div>
        ) : null}

        <p className={styles.text}>
          {t(
            'You can now find your Guide and Channels in the sidebar under "Live TV". While we\'re grabbing the guide data, you can start browsing; it will update automatically as we download whats on.'
          )}
        </p>

        {epgDownloadActivityID ? (
          <div className={styles.spinnerContainer}>
            <Spinner className={styles.spinner} />
            <p>{t('Downloading EPG data')}</p>
          </div>
        ) : null}
      </ModalBody>

      <ModalFooter>
        <ModalFooterButtonBar>
          <Button onPress={onModalClose}>{t('Close')}</Button>
          <Button
            isDisabled={!guideRoute}
            kind={Kind.Primary}
            to={guideRoute}
            onPress={onModalClose}
          >
            {t('View Guide')}
          </Button>
        </ModalFooterButtonBar>
      </ModalFooter>
    </>
  );
}
