import { AnimatePresence, motion } from 'framer-motion';
import React, {
  ReactElement,
  MouseEvent as ReactMouseEvent,
  ReactNode,
  TouchEvent as ReactTouchEvent,
  useCallback,
  useEffect,
  useRef,
} from 'react';
import ReactDOM from 'react-dom';
import * as keyCodes from 'app/js/utils/constants/keyCodes';
import useMetricsModalDismissEffect from 'app/js/v4/core/ui/metrics/hooks/useMetricsModalDismissEffect';
import useMetricsPageViewEffect from 'app/js/v4/core/ui/metrics/hooks/useMetricsPageViewEffect';
import Portal from 'app/js/v4/core/ui/portals/components/Portal';
import Size from 'app/js/v4/core/ui/types/Size';
import cssDurationToSeconds from 'app/js/views/v3/helpers/css/cssDurationToSeconds';
import * as focusManager from 'app/js/views/v3/helpers/focus/focusManager';
import animations from 'app/styles/variables/animations';
import easings from 'app/styles/variables/easings';
import styles from './Modal.css';

export interface ModalProps {
  children: ReactNode;
  className?: string;
  backdropClassName?: string;
  containerClassName?: string;
  size?: Size;
  isOpen: boolean;
  shouldTrackOnClose?: boolean;
  preventCloseOnBackdropPress?: boolean;
  onModalClose(): void;
}

const modalSpeedSeconds = cssDurationToSeconds(animations.modalSpeed);
const modalSpeedMilliseconds = modalSpeedSeconds * 1000;

const opacityTransition = {
  ease: 'easeOut',
  duration: modalSpeedSeconds,
};

const scaleTransition = {
  ease: easings.motionEasing,
  duration: modalSpeedSeconds,
};

const backdropTransition = {
  opacity: opacityTransition,
};

const backdropVariants = {
  enter: {
    opacity: 1,
    transition: backdropTransition,
  },
  exit: {
    opacity: 0,
    transition: backdropTransition,
  },
};

const containerTransition = {
  scale: scaleTransition,
  opacity: opacityTransition,
};

const modalContainerVariants = {
  enter: {
    opacity: 1,
    scale: 1,
    transition: containerTransition,
  },
  exit: {
    opacity: 0,
    scale: 0.95,
    transition: containerTransition,
  },
};

function isMouseEvent(
  event: ReactMouseEvent | ReactTouchEvent
): event is ReactMouseEvent {
  return event.nativeEvent instanceof MouseEvent;
}

function findEventTarget(
  event: ReactMouseEvent | ReactTouchEvent
): Element | null {
  if (isMouseEvent(event)) {
    return event.target as Element;
  }

  const changedTouches = event.changedTouches;

  if (changedTouches.length === 1) {
    const touch = changedTouches[0];

    return document.elementFromPoint(touch.clientX, touch.clientY);
  }

  return null;
}

function isBackdropTarget(
  event: ReactMouseEvent | ReactTouchEvent,
  modalElement: HTMLDivElement | null
): boolean {
  const targetElement = findEventTarget(event);

  if (targetElement) {
    const modalNode = ReactDOM.findDOMNode(modalElement);

    return !modalNode || !modalNode.contains(targetElement);
  }

  return false;
}

export default function Modal({
  children,
  className,
  backdropClassName = styles.modalBackdrop,
  containerClassName = styles.modalContainer,
  size = Size.Medium,
  isOpen,
  shouldTrackOnClose = false,
  preventCloseOnBackdropPress = false,
  onModalClose,
}: ModalProps): ReactElement {
  const modalRef = useRef<HTMLDivElement | null>(null);
  const isBackdropPressedRef = useRef(false);

  //
  // Callbacks

  const onBackdropBeginPress = useCallback(
    (event: ReactMouseEvent | ReactTouchEvent) => {
      isBackdropPressedRef.current = isBackdropTarget(event, modalRef.current);
    },
    []
  );

  const onBackdropEndPress = useCallback(
    (event: ReactMouseEvent | ReactTouchEvent) => {
      if (
        !preventCloseOnBackdropPress &&
        isBackdropPressedRef.current &&
        isBackdropTarget(event, modalRef.current)
      ) {
        onModalClose();
      }

      isBackdropPressedRef.current = false;
    },
    [onModalClose, preventCloseOnBackdropPress]
  );

  const onTouchMove = useCallback((_event: ReactTouchEvent) => {
    isBackdropPressedRef.current = false;
  }, []);

  const onTouchCancel = useCallback((_event: ReactTouchEvent) => {
    isBackdropPressedRef.current = false;
  }, []);

  const onKeyDown = useCallback(
    (event) => {
      const keyCode = event.keyCode;

      if (keyCode === keyCodes.TAB) {
        focusManager.scopeTabEvent(event);
      } else if (keyCode === keyCodes.ESCAPE) {
        event.preventDefault();
        event.stopPropagation();

        onModalClose();

        // Since the user used the keyboard to close the modal, attempt to
        // automatically restore the previously focused element to retain the
        // focus position.

        focusManager.restorePreviousFocus();
      }
    },
    [onModalClose]
  );

  const onRef = useCallback((node: HTMLDivElement | null) => {
    modalRef.current = node;

    if (node) {
      focusManager.startScopedFocus(ReactDOM.findDOMNode(node));
    }
  }, []);

  //
  // Effects

  useMetricsPageViewEffect({}, isOpen);

  useMetricsModalDismissEffect(isOpen, shouldTrackOnClose);

  useEffect(() => {
    let autoFocusTimeoutID: number;

    if (isOpen) {
      autoFocusTimeoutID = window.setTimeout(
        focusManager.scopeAutoFocus,
        modalSpeedMilliseconds
      );

      document.addEventListener('keydown', onKeyDown);
    }

    return () => {
      if (autoFocusTimeoutID) {
        window.clearTimeout(autoFocusTimeoutID);
      }

      if (isOpen) {
        document.removeEventListener('keydown', onKeyDown);
      }
    };
  }, [isOpen, onKeyDown]);

  //
  // Render

  return (
    <Portal
      className={containerClassName}
      closeDelayMilliseconds={modalSpeedMilliseconds}
      isOpen={isOpen}
    >
      <AnimatePresence>
        {isOpen ? (
          <motion.div
            key="backdrop"
            animate="enter"
            className={backdropClassName}
            exit="exit"
            initial="exit"
            variants={backdropVariants}
            onMouseDown={onBackdropBeginPress}
            onMouseUp={onBackdropEndPress}
            onTouchCancel={onTouchCancel}
            onTouchEnd={onBackdropEndPress}
            onTouchMove={onTouchMove}
            onTouchStart={onBackdropBeginPress}
          >
            <motion.div
              key="modal"
              ref={onRef}
              animate="enter"
              className={className || styles[size]}
              exit="exit"
              initial="exit"
              variants={modalContainerVariants}
            >
              {children}
            </motion.div>
          </motion.div>
        ) : // PoseGroup expects React.ReactElement<T> | false | void
        undefined}
      </AnimatePresence>
    </Portal>
  );
}
