/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import paginatedListModelMixin from 'app/js/models/mixins/paginatedListModelMixin';
import path from 'app/js/utils/url/path';
import DeprecatedPluginCollection from './DeprecatedPluginCollection';

const DeprecatedPluginListModel = AbstractModel.extend({
  mixins: [paginatedListModelMixin],

  //
  // Properties

  name: 'DeprecatedPluginListModel',
  context: ['server'],

  idAttribute: 'key',
  collectionName: 'channels',

  url() {
    const key = this.get('key');
    const url = key ? key : path.join(['/channels', this.get('subset')]);

    return this.getPaginatedUrl(url);
  },

  sessionAttrs: {
    key: attrTypes.string,
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 2.
    subset: attrTypes.oneOf('all', 'recentlyViewed'),
    currentPageStart: attrTypes.integer,
    currentPageSize: attrTypes.integer,
    populatedRange: attrTypes.object,
  },

  syncedAttrs: {
    totalSize: attrTypes.integer,
    viewGroup: attrTypes.string,
    title1: attrTypes.string,
    title2: attrTypes.string,
  },

  syncedRelations: {
    channels: {
      relationClass: DeprecatedPluginCollection,
      rootPropertyName: 'Directory',

      setOptions(parseOptions: any) {
        // `fetchRange` will add these options, which we'll need to provide when
        // setting models for the collection.

        return {
          remove: parseOptions.remove,
          at: parseOptions.at,
        };
      },
    },
  },

  defaults() {
    return {
      subset: 'all',
      populatedRange: {
        start: 0,
        end: 0,
        size: 0,
      },
    };
  },

  //
  // Init

  initialize() {
    this.startPopulatedRangeMonitor();
  },
});

export default DeprecatedPluginListModel;
