import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import { findUserModel } from 'app/js/utils/state/modelFinders';
import t from 'app/js/utils/t';
import ServerSettingsClaimViewReact from 'app/js/views/settings/server/ServerSettingsClaimViewReact';
import ServerSettingsPane from 'app/js/views/settings/server/ServerSettingsPane';
import ServerSettingsUpdateView from 'app/js/views/settings/server/ServerSettingsUpdateView';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setti... Remove this comment to see the full error message
import tpl from 'app/templates/settings/server/ServerSettingsGeneralPane.tpl';

const ServerSettingsGeneralPane = ServerSettingsPane.extend({
  //
  // Properties

  template: tpl,

  regions: {
    signIn: '.sign-in-region',
    update: '.update-region',
  },

  //
  // Control

  serializeData: function () {
    const settings = ServerSettingsPane.prototype.serializeData.apply(
      this,
      // eslint-disable-next-line prefer-rest-params
      arguments
    );

    // Show the server's name as placeholder for the friendly name if not
    // explicitly set.
    const friendlyName = _.findWhere(settings, { id: 'FriendlyName' });
    if (friendlyName) {
      friendlyName.placeholder = this.model.get('friendlyName');
    }

    return settings;
  },

  //
  // Bindings

  bindings: {
    '.version': {
      modelAttribute: 'version',

      formatter: function (version: any) {
        const appModel = resolve('appModel');
        const user = findUserModel(appModel);
        const isNinja = user.isNinja();

        return t(
          'Version {1}',
          isNinja ? version : (this as any).model.get('shortVersion')
        );
      },
    },
  },

  //
  // Listeners

  onRender: function () {
    // eslint-disable-next-line prefer-rest-params
    ServerSettingsPane.prototype.onRender.apply(this, arguments);
    this.signIn.show(
      new ServerSettingsClaimViewReact({
        model: this.model,
      })
    );

    if (this.model.get('updater')) {
      this.update.show(
        new ServerSettingsUpdateView({
          model: this.model.get(__PLATFORM__).get('update'),
          collection: this.collection,
        })
      );
    }
  },
});

export default ServerSettingsGeneralPane;
