/* eslint-plugin-disable @typescript-eslint */

import { useCallback, useState } from 'react';
import useUniqueElementID from 'app/js/views/v3/helpers/hooks/useUniqueElementID';

export default function useMenuState() {
  const menuButtonID = useUniqueElementID();

  const [isMenuOpen, setMenuState] = useState(false);

  const onMenuButtonPress = useCallback(() => {
    setMenuState(!isMenuOpen);
  }, [isMenuOpen, setMenuState]);

  const onMenuClose = useCallback(() => {
    setMenuState(false);
  }, [setMenuState]);

  return {
    menuButtonID,
    isMenuOpen,
    onMenuButtonPress,
    onMenuClose,
  };
}
