/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import {
  changeTextFilter,
  changeTimeFilter,
  clearFilter,
  highlightLine,
} from 'app/js/stores/actions/ui/serverLogPageActions';
import ServerLog from './ServerLog';

const mapStateToProps = createSelector(
  // @ts-expect-error ts-migrate(2571) FIXME: Object is of type 'unknown'.
  (appState) => appState.ui.serverLogPage,
  (serverLogPage) => {
    return {
      filter: serverLogPage.filter,
      timeFilterStart: serverLogPage.timeFilterStart,
      timeFilterEnd: serverLogPage.timeFilterEnd,
      logLevels: serverLogPage.logLevels,
      showRawText: serverLogPage.showRawText,
      logLines: serverLogPage.logLines,
    };
  }
);

function createMapDispatchToProps(dispatch: any, props: any) {
  const { serverEntityID } = props;

  return {
    onTextFilterChange(...args: any[]) {
      dispatch(changeTextFilter(...args));
    },

    onTimeFilterChange(...args: any[]) {
      dispatch(changeTimeFilter(...args));
    },

    onHighlightLine(logLine: any) {
      dispatch(highlightLine(logLine));
    },

    onClearFilter() {
      dispatch(clearFilter({ serverEntityID }));
    },
  };
}

const ServerLogConnector = connect(
  mapStateToProps,
  createMapDispatchToProps
)(ServerLog);

export default ServerLogConnector;
