/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import _ from 'underscore';
import createServerNowPlayingSessionsCountSelector from 'app/js/stores/selectors/server/createServerNowPlayingSessionsCountSelector';
import createServerSettingsSelector from 'app/js/stores/selectors/server/createServerSettingsSelector';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import selectUser from 'app/js/stores/selectors/user/selectUser';
import serverSettingsSections from 'app/js/utils/constants/serverSettingsSections';
import getServerSettingsRoute from 'app/js/views/v3/helpers/routes/getServerSettingsRoute';

function createAvailableGroupsSelector() {
  return createSelector(createServerSettingsSelector(), (settings) => {
    return settings.reduce((acc: any, setting: any) => {
      if (setting.group && !acc.includes(setting.group) && !setting.hidden) {
        acc.push(setting.group);
      }

      return acc;
    }, []);
  });
}

function evaluateIsVisible(isVisible: any, server: any, user: any) {
  return (
    isVisible === true || (_.isFunction(isVisible) && isVisible(server, user))
  );
}

export default function createVisibleSettingsSectionsSelector() {
  return createSelector(
    createUnstableServerSelector(),
    createServerNowPlayingSessionsCountSelector(),
    selectUser,
    createAvailableGroupsSelector(),
    (server, nowPlayingSessionsCount, user, availableGroups) => {
      if (!server || server.isUnavailable) {
        return [];
      }

      return serverSettingsSections.reduce((acc, section) => {
        if (!evaluateIsVisible(section.isVisible, server, user)) {
          return acc;
        }

        // @ts-expect-error ts-migrate(2349) FIXME: This expression is not callable.
        const visibleItems = section.groups.reduce(
          (groupAcc: any, group: any) => {
            if (
              evaluateIsVisible(group.isVisible, server, user) ||
              (group.isVisible !== false && availableGroups.includes(group.key))
            ) {
              groupAcc.push({
                key: group.key,
                title: group.title(),
                statusIndicator:
                  group.createStatusIndicator &&
                  group.createStatusIndicator(server, nowPlayingSessionsCount),
                pageComponent: group.pageComponent,
                paneComponent: group.paneComponent,
                route: getServerSettingsRoute(section.key, group.key),
              });
            }

            return groupAcc;
          },
          []
        );

        if (visibleItems.length > 0) {
          acc.push({
            // @ts-expect-error ts-migrate(2322) FIXME: Type 'string' is not assignable to type 'never'.
            key: section.key,
            // @ts-expect-error ts-migrate(2322) FIXME: Type 'any' is not assignable to type 'never'.
            title: section.title(),
            // @ts-expect-error ts-migrate(2322) FIXME: Type 'any' is not assignable to type 'never'.
            groups: visibleItems,
          });
        }

        return acc;
      }, []);
    }
  );
}
