import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import Backbone from 'app/js/initializers/extensions/BackboneExtension';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import { checkForServerUpdate } from 'app/js/stores/actions/serverUpdateActions';
import { saveExperienceSettings } from 'app/js/stores/actions/userActions';
import dispatcher from 'app/js/utils/dispatcher';
import path from 'app/js/utils/url/path';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';
import ServerSettingsAgentsPane from 'app/js/views/settings/server/ServerSettingsAgentsPane';
import ServerSettingsChannelsPane from 'app/js/views/settings/server/ServerSettingsChannelsPane';
import ServerSettingsGeneralPane from 'app/js/views/settings/server/ServerSettingsGeneralPane';
import ServerSettingsLanguagesPane from 'app/js/views/settings/server/ServerSettingsLanguagesPane';
import ServerSettingsPane from 'app/js/views/settings/server/ServerSettingsPane';
import ServerSettingsRemoteAccessPane from 'app/js/views/settings/server/ServerSettingsRemoteAccessPane';
import ServerSettingsVersionsPane from 'app/js/views/settings/server/ServerSettingsVersionsPane';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setti... Remove this comment to see the full error message
import tpl from 'app/templates/settings/server/ServerSettingsView.tpl';

const ServerSettingsInnerView = Marionette.Layout.extend({
  //
  // Properties

  className: 'server-settings-container',
  template: tpl,

  remoteAccessDecorators: {
    connecting: 'unknown',
    disconnecting: 'unknown',
    unreachable: 'error',
    ok: 'success',
  },

  regions: {
    serverDropdown: '.filter-bar',
    nav: '.nav-region',
    pane: '.pane-region',
  },

  events: {
    'click .toggle-advanced-btn': 'onToggleAdvancedClick',
  },

  panes: {
    general: {
      constructor: ServerSettingsGeneralPane,
      group: 'general',
      title: 'General',
      isVisible: true,
    },
    remoteAccess: {
      constructor: ServerSettingsRemoteAccessPane,
      group: 'remoteAccess',
      title: 'Remote Access',
      isVisible: true,
    },
    versions: {
      constructor: ServerSettingsVersionsPane,
      group: 'versions',
      title: 'Optimized Versions',
      isVisible: true,
    },
    agents: {
      constructor: ServerSettingsAgentsPane,
      group: 'agents',
      title: 'Agents',
      isVisible: true,
    },
    library: {
      constructor: ServerSettingsPane,
      group: 'library',
      title: 'Library',
    },
    channels: {
      constructor: ServerSettingsChannelsPane,
      group: 'channels',
      title: 'Plugins',
      isVisible: true,
    },
    network: {
      constructor: ServerSettingsPane,
      group: 'network',
      title: 'Network',
    },
    transcoder: {
      constructor: ServerSettingsPane,
      group: 'transcoder',
      title: 'Transcoder',
    },
    languages: {
      constructor: ServerSettingsLanguagesPane,
      group: 'account.language',
      title: 'Languages',
      isVisible: true,
    },
    dlna: {
      constructor: ServerSettingsPane,
      group: 'dlna',
      title: 'DLNA',
    },
    butler: {
      constructor: ServerSettingsPane,
      group: 'butler',
      title: 'Scheduled Tasks',
    },
    extras: {
      constructor: ServerSettingsPane,
      group: 'extras',
      title: 'Extras',
    },
  },

  //
  // Init

  initialize: function (options: any) {
    this.defaultGroup = options.group || 'general';
    this.account = options.account;
    this.cloudAccount = options.cloudAccount;
    this.channelSettings = options.channelSettings;

    // Work with a temporary settings collection, so we can make changes,
    // which may be discarded if the user does not save, without affecting
    // the working setting models.
    this.tempSettings = this.collection.cloneDeep();
    this.tempAccount = this.account.clone();
    this.tempChannelSettings = this.channelSettings.cloneDeep();
    this.changes = {};

    const experienceSettingsModel = resolve('appModel')
      .get('user')
      .get('experienceSettings');

    const showAdvanced = experienceSettingsModel.get('showAdvancedSettings');
    this.$el.toggleClass('show-advanced', showAdvanced);

    this.initGroups();

    // Check if the plex.tv can reach the server, so our Remote Access page
    // reflects reality.
    if (this.model.supports('reachability')) {
      const url = '/myplex/refreshReachability';
      const xhrOptions = _.extend(this.model.getXHROptions(url), {
        type: 'PUT',
      });

      $.ajax(xhrOptions);
    }

    this.listenTo(dispatcher, 'shortcut:save', this.saveChanges);
    this.listenTo(this.collection, 'change:value', this.onSettingValueChange);
  },

  initGroups: function () {
    const availableGroups = _.keys(this.panes);
    const representedGroups = _.unique(
      this.collection
        .filter(
          (setting: any) => setting.get('group') && !setting.get('hidden')
        )
        .map((setting: any) => setting.get('group'))
    );

    this.groups = new Backbone.Collection();

    // Only show custom panes or panes that actually have settings returned
    // by the server.

    availableGroups.forEach((availableGroup) => {
      const paneDef = this.panes[availableGroup];
      const isVisible = paneDef.isVisible;

      // isVisible true forces visibility
      // isVisible false forces suppression
      // isVisible false-y falls back to inclusion in server-provided groups
      if (
        isVisible === true ||
        (isVisible !== false && _.contains(representedGroups, availableGroup))
      ) {
        this.addGroup(availableGroup, paneDef);
      }
    });

    // Create new pane definitions for groups that were returned by the
    // server, but not represented by a pane.

    _.each(
      representedGroups,
      function (representedGroup) {
        if (!_.contains(availableGroups, representedGroup)) {
          const paneDef = {
            constructor: ServerSettingsPane,
            group: representedGroup,
            title: representedGroup,
          };

          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.panes[representedGroup] = paneDef;
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.addGroup(representedGroup, paneDef);
        }
      },
      this
    );
  },

  addGroup: function (id: any, paneDef: any) {
    const attrs = _.omit(paneDef, 'constructor');
    attrs.id = id;

    this.groups.add(attrs);
  },

  //
  // Control

  showPane: function (paneID: any) {
    if (this.currentPane) {
      // Avoid switching panes while we are waiting for changes to save.
      if (this.currentPane.isActive) {
        return this.currentPane;
      }

      this.stopListening(this.currentPane);
      this.flushChanges();
    }

    const paneDef = this.panes[paneID];

    if (!paneDef || !this.groups.get(paneID)) {
      // Need to do a full navigation so the side bar and title
      // are updated.
      navigateToRoute('serverSettings', {
        machineIdentifier: this.model.id,
        settingsSection: 'settings',
        settingsGroup: 'general',
      });
    }

    const PaneViewConstructor = paneDef.constructor;
    const view = new PaneViewConstructor({
      model: this.model,
      collection: this.tempSettings,
      account: this.tempAccount,
      cloudAccount: this.cloudAccount,
      channelSettings: this.tempChannelSettings,
      group: paneDef.group,
      changes: this.changes,
    });

    this.currentPane = view;
    this.pane.show(view);

    this.listenTo(this.currentPane, 'save', this.saveChanges);

    return view;
  },

  flushChanges: function () {
    if (this.currentPane.changes) {
      _.extend(this.changes, this.currentPane.changes);
    }
  },

  saveChanges: function () {
    this.flushChanges();

    if (this.currentPane.isDisabled || this.currentPane.isActive) {
      return;
    }

    this.currentPane.triggerMethod('before:save');

    const requests = [];
    const channelChanges = this.getPrefixedChanges('channel.');
    const accountChanges = this.getPrefixedChanges('account.');

    // The settings collections do not have a save method, but we can call
    // save on a single model and pass it everything we want to change.

    if (!_.isEmpty(channelChanges)) {
      const channelParams = this.channelSettings
        .first()
        .getSaveParams(channelChanges);
      const channelRootUrl = _.result(this.channelSettings, 'url');
      const channelUrl = path.join([channelRootUrl, '/set'], channelParams);

      requests.push($.ajax(this.model.getXHROptions(channelUrl)));
    }

    if (!_.isEmpty(accountChanges)) {
      requests.push(this.account.save(accountChanges));
    }

    if (!_.isEmpty(this.changes)) {
      requests.push(this.collection.first().save(this.changes));
    }

    // eslint-disable-next-line prefer-spread
    const promise = $.when.apply($, requests);

    promise.done(
      _.bind(function () {
        // Propagate the changes from the temp models to the models from the
        // working settings collection. We do not need to do this with other
        // temp models or collections since those are temporarily created for
        // this view, while the settings collection lifespan extends past this
        // view's lifespan.

        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        const changedKeys = _.keys(this.changes);
        _.each(
          changedKeys,
          function (changedKey) {
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            const tempModel = this.tempSettings.get(changedKey);
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            const setting = this.collection.get(changedKey);

            setting.set('value', tempModel.get('value'));

            if (changedKey === 'ButlerUpdateChannel') {
              // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
              this.onButlerUpdateChannelChange();
            }
          },
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this
        );

        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.changes = {};

        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        if (this.currentPane) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          if (this.currentPane.changes) {
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.currentPane.changes = {};
          }

          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.currentPane.triggerMethod('after:save', true);
        }
      }, this)
    );

    promise.fail(
      _.bind(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        if (this.currentPane) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.currentPane.triggerMethod('after:save', false);
        }
      }, this)
    );
  },

  getPrefixedChanges: function (prefix: any) {
    const prefixChanges = {};

    _.each(
      this.changes,
      function (value, key) {
        const hasPrefix = key.lastIndexOf(prefix, 0) === 0;

        if (hasPrefix) {
          // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
          prefixChanges[key.substring(prefix.length)] = value;
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          delete this.changes[key];
        }
      },
      this
    );

    return prefixChanges;
  },

  //
  // Listeners

  onRender: function () {
    this.showPane(this.defaultGroup);
  },

  onClose: function () {
    this.currentPane = null;
  },

  onSettingValueChange: function (setting: any, value: any) {
    this.tempSettings.get(setting.id).set('value', value);
  },

  onServerConnectedChange: function (server: any, connected: any) {
    if (!connected) {
      return;
    }

    // We no longer care about this server's availability changes, since
    // it's back and in good shape.
    this.stopListening(
      server,
      'change:connected',
      this.onServerConnectedChange
    );

    // If the user is still viewing settings, make sure the server that
    // installed the update is retained as the primary.
    const appModel = resolve('appModel');
    appModel.set('primaryServer', server);
  },

  onToggleAdvancedClick: function (event: any) {
    event.preventDefault();

    const showingAdvanced = this.$el.hasClass('show-advanced');
    const appStore = resolve('appStore');
    appStore.dispatch(
      saveExperienceSettings({ showAdvancedSettings: !showingAdvanced })
    );

    this.$el.toggleClass('show-advanced', !showingAdvanced);
  },

  onButlerUpdateChannelChange: function () {
    const autoCheckSetting = this.collection.get('ButlerTaskCheckForUpdates');
    const canAutoUpdate = autoCheckSetting?.get('value');

    if (canAutoUpdate) {
      resolve('appStore').dispatch(
        checkForServerUpdate({
          serverEntityID: this.model.entityID(),
          download: false,
        })
      );
    }
  },
});

export default ServerSettingsInnerView;
