/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

function getPartByAbsoluteOffset(media: any, absoluteOffset: any) {
  let part;

  if (media.Part.length > 1) {
    let duration = 0;

    for (let i = 0, len = media.Part.length; i < len; i++) {
      part = media.Part[i];

      // If the part doesn't have a duration, we can't accurately
      // estimate which part should be returned.

      if (!part.duration) {
        break;
      }

      // Add to the duration until we figure out which part the
      // absolute offset falls under.

      duration += part.duration;

      if (duration > absoluteOffset) {
        break;
      }
    }
  } else {
    part = _.first(media.Part);
  }

  return part;
}

export default getPartByAbsoluteOffset;
