import React from 'react';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import SourceContextProvider from 'app/js/v4/core/servers/components/SourceContextProvider';
import ServerDashboardPage from 'app/js/v4/features/dashboard/components/ServerDashboardPage';
import AbstractReactView from 'app/js/views/AbstractReactView';

const ServerDashboardView = AbstractReactView.extend({
  //
  // Control

  component(options: any) {
    const server = options.server;

    return (
      <SourceContextProvider
        machineIdentifier={server.get('machineIdentifier')}
        providerIdentifier={providerIdentifiers.LIBRARY}
      >
        <ServerDashboardPage />
      </SourceContextProvider>
    );
  },
});

export default ServerDashboardView;
