import Backbone from 'app/js/initializers/extensions/BackboneExtension';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import stateMachineMixin from 'app/js/mixins/views/stateMachineMixin';
import formatDurationSecondsLeft from 'app/js/utils/dateTime/formatDurationSecondsLeft';
import t from 'app/js/utils/t';
import CircleProgressBarView from 'app/js/views/activity/CircleProgressBarView';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/lists... Remove this comment to see the full error message
import tpl from 'app/templates/lists/activity/convert/ConversionStatusView.tpl';

const ConversionStatusView = Marionette.Layout.extend({
  mixins: [stateMachineMixin],
  //
  // Properties
  className: 'conversion-status',
  template: tpl,
  regions: {
    progressBarRegion: '.progress-bar-region',
  },
  //
  // Init
  initialize: function (options: any) {
    this.initialState = options.initialState;
    this.initialErrorMessage = options.initialErrorMessage;
    this.generator = options.generator;
    this.transcodeJobs = options.transcodeJobs;
    this.pausedSetting = options.pausedSetting;
    this.progressModel = new Backbone.Model({
      progress: 0,
      remaining: 0,
      speed: 0,
    });
  },
  //
  // States
  states: {
    pending: {
      onEnter: function () {
        (this as any).$el.addClass('pending');
      },
      onExit: function () {
        (this as any).$el.removeClass('pending');
      },
    },
    converting: {
      onEnter: function () {
        (this as any).$el.addClass('converting');
      },
      onExit: function () {
        (this as any).$el.removeClass('converting');
      },
    },
    processed: {
      onEnter: function () {
        (this as any).$el.addClass('processed');
      },
      onExit: function () {
        (this as any).$el.removeClass('processed');
      },
    },
    disabled: {
      onEnter: function () {
        (this as any).$el.addClass('disabled');
      },
      onExit: function () {
        (this as any).$el.removeClass('disabled');
      },
    },
    error: {
      onEnter: function () {
        (this as any).$el.addClass('error');
      },
      onExit: function () {
        (this as any).$el.removeClass('error');
      },
    },
  },
  //
  // Bindings
  bindingsModel: 'progressModel',
  bindings: {
    '.conversion-status-converting-label': {
      model: 'pausedSetting',
      modelAttribute: 'value',
      formatter: function (value: any) {
        return value ? t('Paused') : t('Converting');
      },
    },
    '.conversion-status-pending-label': {
      model: 'pausedSetting',
      modelAttribute: 'value',
      formatter: function (value: any) {
        return value ? t('Paused') : t('Waiting');
      },
    },
    '.conversion-status-converting-speed': {
      modelAttribute: 'speed',
      autohide: true,
      // @ts-expect-error ts-migrate(7030) FIXME: Not all code paths return a value.
      formatter: function (speed: any) {
        if (speed) {
          let factor = Math.round(speed * 10) / 10;
          if (factor > 10) {
            factor = Math.round(factor);
          }
          return '(' + factor + 'x)';
        }
      },
    },
    '.conversion-status-converting-percentage': {
      modelAttribute: 'progress',
      formatter: function (progress: any) {
        return Math.round(progress) + '%';
      },
    },
    '.conversion-status-converting-progress-separator': {
      modelAttribute: 'remaining',
      manual: true,
      autohide: function (remaining: any) {
        return remaining && (this as any).progressModel.get('speed');
      },
    },
    '.conversion-status-converting-remaining': {
      modelAttribute: 'remaining',
      autohide: true,
      formatter: function (remaining: any) {
        if ((this as any).progressModel.get('speed')) {
          return formatDurationSecondsLeft(remaining);
        }

        return;
      },
    },
  },
  //
  // Control
  serializeData: function () {
    return {
      initialErrorMessage: this.initialErrorMessage,
    };
  },
  findTranscodeJob: function () {
    // Find the transcode job that matches the background play queue item
    // and set up some listeners.
    this.transcodeJob = this.transcodeJobs.find(function (transcodeJob: any) {
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      const ratingKey = this.model.get('ratingKey');
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      const generatorID = this.generator
        ? // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.generator.id
        : // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.model.get('generatorID');
      const isItemMatch =
        ratingKey && ratingKey === transcodeJob.get('ratingKey');
      const isGeneratorMatch =
        generatorID && generatorID === transcodeJob.get('generatorID');
      return isItemMatch && isGeneratorMatch;
    }, this);
    if (this.transcodeJob) {
      // If we find a match, we don't need to continue to listen for new
      // jobs being added.
      this.stopListening(this.transcodeJobs);
      this.enterState('converting');
      this.progressModel.set({
        progress: this.transcodeJob.get('progress'),
        speed: this.transcodeJob.get('speed'),
        remaining: this.transcodeJob.get('remaining'),
      });
      this.listenTo(this.transcodeJob, {
        remove: this.onTranscodeJobRemove,
        'change:progress': this.onTranscodeProgressChange,
        'change:speed': this.onTranscodeSpeedChange,
        'change:remaining': this.onTranscodeRemainingChange,
      });
    }
    return this.transcodeJob;
  },
  //
  // Listeners
  onRender: function () {
    this.enterState(this.initialState || 'pending');
    if (!this.findTranscodeJob()) {
      this.listenTo(this.transcodeJobs, 'reset add', this.findTranscodeJob);
    }
    this.progressBarRegion.append(
      new CircleProgressBarView({
        model: this.progressModel,
        size: 30,
        lineWidth: 3,
      })
    );
  },
  onTranscodeJobRemove: function () {
    this.enterState('pending');
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'transcodeJob' is declared but its value is never ... Remove this comment to see the full error message
  onTranscodeProgressChange: function (transcodeJob: any, progress: any) {
    this.progressModel.set('progress', progress);
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'transcodeJob' is declared but its value is never ... Remove this comment to see the full error message
  onTranscodeSpeedChange: function (transcodeJob: any, speed: any) {
    this.progressModel.set('speed', speed);
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'transcodeJob' is declared but its value is never ... Remove this comment to see the full error message
  onTranscodeRemainingChange: function (transcodeJob: any, remaining: any) {
    this.progressModel.set('remaining', remaining);
  },
});
export default ConversionStatusView;
