import React, { ReactElement } from 'react';
import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import selectServer from 'app/js/v4/core/servers/selectors/selectServer';
import ServerSettingsView from 'app/js/views/settings/server/ServerSettingsView';
import MarionetteComponent from 'app/js/views/v3/components/MarionetteComponent';
import UserPageConnector from 'app/js/views/v3/components/UserPageConnector';
import createKey from 'app/js/views/v3/helpers/createKey';

export default function ServerSettingsViewPage(): ReactElement {
  const { settingsSection, settingsGroup } = useParams<{
    settingsSection: string;
    settingsGroup: string;
  }>();

  const server = useSelector(selectServer);

  // When server is connected again we need to refresh the data required for the
  // views. Easiest way is change the key so the Marionette view re-renders.
  const key = createKey(
    'ServerSettingsView',
    server.machineIdentifier,
    settingsSection,
    settingsGroup,
    server.connectionTestResult
  );

  return (
    // @ts-expect-error
    <UserPageConnector>
      <MarionetteComponent
        key={key}
        view={ServerSettingsView}
        viewOptions={{
          settingsSection,
          settingsGroup,
          serverID: server.machineIdentifier,
        }}
      />
    </UserPageConnector>
  );
}
