/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { bindActionCreators, createStore } from 'redux';
import _ from 'underscore';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import storeShape from 'app/js/views/v3/helpers/props/shapes/storeShape';

export const actionTypes = {
  CHANGE: 'CHANGE',
  REPLACE_STATE: 'REPLACE_STATE',
  RESET: 'RESET',
  SHOW_ERRORS: 'SHOW_ERRORS',
};

export const actions = {
  change: createActionCreator(actionTypes.CHANGE),
  replaceState: createActionCreator(actionTypes.REPLACE_STATE),
  reset: createActionCreator(actionTypes.RESET),
  showErrors: createActionCreator(actionTypes.SHOW_ERRORS),
};

function handleFormState(state = {}, { type, payload }: any) {
  switch (type) {
    case actionTypes.CHANGE: {
      const { name, change } = payload;

      if (change == null) {
        const newState = {
          ...state,
        };

        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        delete newState[name];

        return newState;
      }

      return {
        ...state,
        [name]: change,
      };
    }

    case actionTypes.REPLACE_STATE:
      return payload;

    case actionTypes.RESET:
      return {};

    case actionTypes.SHOW_ERRORS:
      return _.mapObject(state, (inputState: { errors: string[] }) => {
        return {
          ...inputState,
          showErrors: inputState.errors.length !== 0,
        };
      });

    default:
      return state;
  }
}

interface FormProps {
  formState?: any;
  onSubmit?: (...args: any[]) => any;
  onChange?: (...args: any[]) => any;
}

class Form extends Component<FormProps> {
  static childContextTypes = {
    formStore: storeShape,
  };

  static defaultProps = {
    onSubmit() {},
    onChange() {},
  };

  // eslint-disable-next-line react/sort-comp
  actions: any;
  prevState: any;
  store: any;
  unsubscribe: any;

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.store = createStore(handleFormState);
    this.prevState = this.store.getState();
    this.actions = bindActionCreators(actions, this.store.dispatch);
  }

  getChildContext() {
    return {
      formStore: this.store,
    };
  }

  UNSAFE_componentWillMount() {
    this.unsubscribe = this.store.subscribe(this.onFormStoreChange);
  }

  UNSAFE_componentWillReceiveProps(nextProps: FormProps) {
    if (nextProps.formState && this.store.getState() !== nextProps.formState) {
      this.actions.replaceState(nextProps.formState);
    }
  }

  componentWillUnmount() {
    if (this.unsubscribe) {
      this.unsubscribe();
      // While the children are unmounting freeze the current state of the form,
      // so that as `formInput`s remove themselves from `formState` we don't
      // cause errors in validation functions.
      this.store.replaceReducer((state: any) => state);
    }
  }

  //
  // Listeners

  onFormStoreChange = () => {
    const state = this.store.getState();

    if (state !== this.prevState) {
      this.prevState = state;
      // @ts-expect-error ts-migrate(2722) FIXME: Cannot invoke an object which is possibly 'undefin... Remove this comment to see the full error message
      this.props.onChange(state);
    }
  };

  onSubmit = (event: any) => {
    event.preventDefault();
    event.nativeEvent.preventDefault();

    const state = this.store.getState();

    // @ts-expect-error ts-migrate(6133) FIXME: 'key' is declared but its value is never read.
    const hasErrors = _.some(state, (val, key) => {
      return val.errors.length !== 0;
    });

    if (!hasErrors) {
      // @ts-expect-error ts-migrate(6133) FIXME: 'key' is declared but its value is never read.
      const data = _.mapObject(state, (val, key) => {
        return val.value;
      });

      // @ts-expect-error ts-migrate(2722) FIXME: Cannot invoke an object which is possibly 'undefin... Remove this comment to see the full error message
      this.props.onSubmit(data, state);
    } else {
      this.actions.showErrors();
    }
  };

  //
  // Render

  render() {
    const { children, formState, onChange, ...otherProps } = this.props;

    return (
      <form {...otherProps} onSubmit={this.onSubmit}>
        {children}
      </form>
    );
  }
}

export default Form;
