import {
  MetadataSubtype,
  MetadataType,
} from 'app/js/v4/core/metadata/metadataTypes';

export function getMetadataTypeChild(
  type: MetadataType,
  subtype?: MetadataSubtype
): MetadataType | undefined {
  switch (subtype) {
    case MetadataSubtype.Podcast:
      switch (type) {
        case MetadataType.Show:
          return MetadataType.Track;
      }
  }

  switch (type) {
    case MetadataType.Show:
      return MetadataType.Season;
    case MetadataType.Season:
      return MetadataType.Episode;
    case MetadataType.Artist:
      return MetadataType.Album;
    case MetadataType.Author:
      return MetadataType.Audiobook;
    case MetadataType.Audiobook:
      return MetadataType.Chapter;
    case MetadataType.Album:
      return MetadataType.Track;
    case MetadataType.PhotoAlbum:
      return MetadataType.Photo;
    case MetadataType.Collection:
      switch (subtype) {
        case MetadataSubtype.Movie:
          return MetadataType.Movie;
        case MetadataSubtype.Show:
          return MetadataType.Show;
        case MetadataSubtype.Season:
          return MetadataType.Season;
        case MetadataSubtype.Episode:
          return MetadataType.Episode;
        case MetadataSubtype.Artist:
          return MetadataType.Artist;
        case MetadataSubtype.Album:
          return MetadataType.Album;
        case MetadataSubtype.Track:
          return MetadataType.Track;
        case MetadataSubtype.Author:
          return MetadataType.Author;
        case MetadataSubtype.Audiobook:
          return MetadataType.Audiobook;
        case MetadataSubtype.Chapter:
          return MetadataType.Chapter;
      }
  }

  return;
}

export function getMetadataTypeLeaf(
  type: MetadataType,
  subtype?: MetadataSubtype
): MetadataType | undefined {
  switch (subtype) {
    case MetadataSubtype.Podcast:
      switch (type) {
        case MetadataType.Show:
          return MetadataType.Track;
      }
  }

  switch (type) {
    case MetadataType.Show:
    case MetadataType.Season:
      return MetadataType.Episode;
    case MetadataType.Author:
    case MetadataType.Audiobook:
      return MetadataType.Chapter;
    case MetadataType.Artist:
    case MetadataType.Album:
      return MetadataType.Track;
    case MetadataType.PhotoAlbum:
      return MetadataType.Photo;
    case MetadataType.Collection:
      switch (subtype) {
        case MetadataSubtype.Movie:
          return MetadataType.Movie;
        case MetadataSubtype.Show:
        case MetadataSubtype.Season:
        case MetadataSubtype.Episode:
          return MetadataType.Episode;
        case MetadataSubtype.Artist:
        case MetadataSubtype.Album:
        case MetadataSubtype.Track:
          return MetadataType.Track;
      }
  }

  return;
}
