/* eslint-plugin-disable @typescript-eslint */

import assert from 'app/js/utils/log/assert';
import clampListIndex from './clampListIndex';

export const VERTICAL = 'vertical';
export const HORIZONTAL = 'horizontal';

export default function createGridLayout(options = {}) {
  const {
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'direction' does not exist on type '{}'.
    direction = VERTICAL,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'rows' does not exist on type '{}'.
    rows,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'columns' does not exist on type '{}'.
    columns,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'overscan' does not exist on type '{}'.
    overscan = 0,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'innerHorizontalPadding' does not exist o... Remove this comment to see the full error message
    innerHorizontalPadding = 0,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'innerVerticalPadding' does not exist on ... Remove this comment to see the full error message
    innerVerticalPadding = 0,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'outerPaddingTop' does not exist on type ... Remove this comment to see the full error message
    outerPaddingTop = 0,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'outerPaddingRight' does not exist on typ... Remove this comment to see the full error message
    outerPaddingRight = 0,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'outerPaddingBottom' does not exist on ty... Remove this comment to see the full error message
    outerPaddingBottom = 0,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'outerPaddingLeft' does not exist on type... Remove this comment to see the full error message
    outerPaddingLeft = 0,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'getCellWidth' does not exist on type '{}... Remove this comment to see the full error message
    getCellWidth,
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'getCellHeight' does not exist on type '{... Remove this comment to see the full error message
    getCellHeight,
  } = options;

  let listTop: any;
  let listLeft: any;
  let cellWidth: any;
  let cellHeight: any;

  // These are the maximum potential values.
  let cellsPerColumn: any;
  let cellsPerRow: any;
  let cellsPerPage: any;

  let getListStyleMemoKey: any;
  let getListStyleMemo: any;
  let getPositionForIndexCache: any;

  function getListStyle(measurements: any, size: any) {
    // `getListStyle()` is a hot method which is frequently presented args
    // with the same values, although different objects. Coerce and concatenate
    // the args into a unique memoization flags.
    const fields = [
      outerPaddingTop,
      outerPaddingRight,
      outerPaddingBottom,
      outerPaddingLeft,
      size,
    ];

    for (const key in measurements) {
      fields.push(measurements[key]);
    }

    const memoKey = fields.join(':');

    if (memoKey === getListStyleMemoKey) {
      return getListStyleMemo;
    }

    __DEV__ &&
      assert(
        rows || measurements.viewportHeight != null,
        'A grid list must have a viewport height or a fixed number of rows'
      );

    __DEV__ &&
      assert(
        columns || measurements.viewportWidth != null,
        'A grid list must have a viewport width or a fixed number of columns'
      );

    listTop = measurements.listTop;
    listLeft = measurements.listLeft;

    let innerViewportWidth =
      (measurements.viewportWidth || 0) - outerPaddingRight - outerPaddingLeft;
    let innerViewportHeight =
      (measurements.viewportHeight || 0) - outerPaddingTop - outerPaddingBottom;

    if (direction === VERTICAL) {
      innerViewportWidth -= listLeft;
    } else {
      innerViewportHeight -= listTop;
    }

    innerViewportWidth = Math.max(0, innerViewportWidth);
    innerViewportHeight = Math.max(0, innerViewportHeight);

    cellWidth = getCellWidth({
      innerViewportWidth,
      innerViewportHeight,
      innerHorizontalPadding,
      innerVerticalPadding,
    });

    cellHeight = getCellHeight({
      cellWidth,
      innerViewportWidth,
      innerViewportHeight,
      innerHorizontalPadding,
      innerVerticalPadding,
    });

    cellsPerColumn = Math.max(
      0,
      rows ||
        Math.floor(
          (innerViewportHeight + innerVerticalPadding) /
            (cellHeight + innerVerticalPadding)
        )
    );
    cellsPerRow = Math.max(
      0,
      columns ||
        Math.floor(
          (innerViewportWidth + innerHorizontalPadding) /
            (cellWidth + innerHorizontalPadding)
        )
    );
    cellsPerPage = cellsPerRow * cellsPerColumn;

    let width = 0;
    let height = 0;

    if (direction === VERTICAL && cellsPerRow) {
      width =
        cellsPerRow * (cellWidth + innerHorizontalPadding) -
        innerHorizontalPadding;
      height =
        Math.ceil(size / cellsPerRow) * (cellHeight + innerVerticalPadding) -
        innerVerticalPadding;
    } else if (direction === HORIZONTAL && cellsPerColumn) {
      width =
        Math.ceil(size / cellsPerColumn) *
          (cellWidth + innerHorizontalPadding) -
        innerHorizontalPadding;
      height =
        cellsPerColumn * (cellHeight + innerVerticalPadding) -
        innerVerticalPadding;
    }

    // `getListStyle()` sets stateful values for `getPositionForIndex`. Empty
    // the cache for each call to `getListStyle()` which passes the above
    // `getListStyleMemo` check.
    getPositionForIndexCache = {};

    getListStyleMemoKey = memoKey;

    getListStyleMemo = {
      width: width + outerPaddingRight + outerPaddingLeft,
      height: height + outerPaddingTop + outerPaddingBottom,
    };

    return getListStyleMemo;
  }

  function getPositionForIndex(index: any) {
    __DEV__ &&
      assert(
        cellWidth !== undefined,
        '`getListStyle` must be called before `getPositionForIndex`'
      );

    const cacheKey = index;

    if (getPositionForIndexCache[cacheKey]) {
      return getPositionForIndexCache[cacheKey];
    }

    const column = getColumn(index);
    const row = getRow(index);
    const position = {
      column,
      row,
      cellX:
        column * (cellWidth + innerHorizontalPadding) +
        listLeft +
        outerPaddingLeft,
      cellY:
        row * (cellHeight + innerVerticalPadding) + listTop + outerPaddingTop,
      cellWidth,
      cellHeight,
    };

    getPositionForIndexCache[cacheKey] = position;

    return position;
  }

  function getRangesForPosition(scrollTop: any, scrollLeft: any, size: any) {
    __DEV__ &&
      assert(
        cellWidth !== undefined,
        '`getListStyle` must be called before `getRangesForPosition`'
      );

    // The render range represents cells that will be rendered in the list. The
    // visible range represents cells that can actually be seen in the list.
    // Cells within the render range, but outside the visible range may defer
    // loading an image, for example. Clamp the ranges to indices that actually
    // exist in the list of items.

    const finalCellHeight = cellHeight + innerVerticalPadding;
    const finalCellWidth = cellWidth + innerHorizontalPadding;
    const finalScrollTop = scrollTop - listTop;
    const finalScrollLeft = scrollLeft - listLeft;

    const cellsPerGroup = direction === VERTICAL ? cellsPerRow : cellsPerColumn;

    const groupIndex =
      direction === VERTICAL
        ? finalScrollTop / finalCellHeight
        : finalScrollLeft / finalCellWidth;

    const maxRenderedCells =
      cellsPerPage + cellsPerGroup * 2 + overscan * cellsPerGroup * 2;

    const visibleStartIndex = Math.ceil(groupIndex) * cellsPerGroup;
    const visibleEndIndex = visibleStartIndex + cellsPerPage - 1;

    const partiallyVisibleStartIndex = Math.floor(groupIndex) * cellsPerGroup;
    const partiallyVisibleEndIndex =
      visibleStartIndex + cellsPerPage + cellsPerGroup - 1;

    let renderStartIndex =
      partiallyVisibleStartIndex - cellsPerGroup * overscan;
    const renderEndIndex =
      clampListIndex(renderStartIndex, size) + maxRenderedCells - 1;

    if (renderEndIndex >= size) {
      renderStartIndex = size - maxRenderedCells;
    }

    return {
      visibleStartIndex: clampListIndex(visibleStartIndex, size),
      visibleEndIndex: clampListIndex(visibleEndIndex, size),
      partiallyVisibleStartIndex: clampListIndex(
        partiallyVisibleStartIndex,
        size
      ),
      partiallyVisibleEndIndex: clampListIndex(partiallyVisibleEndIndex, size),
      renderStartIndex: clampListIndex(renderStartIndex, size),
      renderEndIndex: clampListIndex(renderEndIndex, size),
    };
  }

  function getRelativeIndex(
    index: any,
    delta: any,
    relativeDirection: any,
    size: any
  ) {
    __DEV__ &&
      assert(
        cellsPerRow !== undefined && cellsPerColumn !== undefined,
        '`getListStyle` must be called before `getRelativeIndex`'
      );

    const directionsAlign = direction === relativeDirection;
    const isSingleDimension =
      (direction === VERTICAL && cellsPerRow === 1) ||
      (direction === HORIZONTAL && cellsPerColumn === 1);

    if (directionsAlign && isSingleDimension) {
      return clampListIndex(index + delta, size);
    }

    let workingDelta;
    let firstIndex;
    let lastIndex;

    if (relativeDirection === VERTICAL) {
      workingDelta = directionsAlign ? cellsPerRow * delta : delta;
      firstIndex = getFirstCellInColumn(index, size);
      lastIndex = getLastCellInColumn(index, size);
    }

    if (relativeDirection === HORIZONTAL) {
      workingDelta = directionsAlign ? cellsPerColumn * delta : delta;
      firstIndex = getFirstCellInRow(index, size);
      lastIndex = getLastCellInRow(index, size);
    }

    const workingIndex = index + workingDelta;

    return firstIndex || lastIndex
      ? // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'number | undefined' is not assig... Remove this comment to see the full error message
        Math.max(Math.min(workingIndex, lastIndex), firstIndex)
      : index;
  }

  function getRow(index: any) {
    return direction === VERTICAL
      ? Math.floor(index / cellsPerRow)
      : index % cellsPerColumn;
  }

  function getColumn(index: any) {
    return direction === VERTICAL
      ? index % cellsPerRow
      : Math.floor(index / cellsPerColumn);
  }

  // @ts-expect-error ts-migrate(6133) FIXME: 'size' is declared but its value is never read.
  function getFirstCellInRow(index: any, size: any) {
    const row = getRow(index);

    return direction === VERTICAL ? row * cellsPerRow : row;
  }

  // @ts-expect-error ts-migrate(6133) FIXME: 'size' is declared but its value is never read.
  function getFirstCellInColumn(index: any, size: any) {
    const column = getColumn(index);

    return direction === VERTICAL ? column : column * cellsPerColumn;
  }

  function getLastCellInRow(index: any, size: any) {
    if (direction === VERTICAL) {
      const firstColumn = getFirstCellInRow(index, size);
      return clampListIndex(firstColumn + (cellsPerRow - 1), size);
    } else {
      const lastIndex = size - 1;
      const row = getRow(index);
      const lastColumn = getColumn(lastIndex);
      const lastLogicalRowIndex =
        (lastColumn + 1) * cellsPerColumn - (cellsPerColumn - row);

      let rowLastIndex;

      if (lastLogicalRowIndex < size) {
        rowLastIndex = lastLogicalRowIndex;
      } else {
        rowLastIndex =
          lastIndex - (size % cellsPerColumn) - (cellsPerColumn - 1 - row);
      }

      return rowLastIndex;
    }
  }

  function getLastCellInColumn(index: any, size: any) {
    if (direction === VERTICAL) {
      const lastIndex = size - 1;
      const column = getColumn(index);
      const lastRow = getRow(lastIndex);
      const lastLogicalColumnIndex =
        (lastRow + 1) * cellsPerRow - (cellsPerRow - column);

      let columnLastIndex;

      if (lastLogicalColumnIndex < size) {
        columnLastIndex = lastLogicalColumnIndex;
      } else {
        columnLastIndex =
          lastIndex - (size % cellsPerRow) - (cellsPerRow - 1 - column);
      }

      return columnLastIndex;
    } else {
      const firstRow = getFirstCellInColumn(index, size);
      return clampListIndex(firstRow + (cellsPerColumn - 1), size);
    }
  }

  function getCoordinateDistance(indexA: any, indexB: any) {
    const x1 = getRow(indexA);
    const x2 = getRow(indexB);
    const y1 = getColumn(indexA);
    const y2 = getColumn(indexB);

    return Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2));
  }

  return {
    options,
    getListStyle,
    getRelativeIndex,
    getCoordinateDistance,
    getPositionForIndex,
    getRangesForPosition,
  };
}
