import classNames from 'classnames';
import React, {
  AnchorHTMLAttributes,
  ButtonHTMLAttributes,
  InputHTMLAttributes,
  MouseEvent,
  ReactElement,
  Ref,
  TouchEvent,
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react';
import Kind from 'app/js/v4/core/ui/types/Kind';
import Size from 'app/js/v4/core/ui/types/Size';
import {
  IS_ACTIVE,
  IS_DISABLED,
  IS_FOCUSED,
} from 'app/js/views/v3/helpers/modifierClassNames';
import styles from './Link.css';

export type LinkElement =
  | HTMLButtonElement
  | HTMLInputElement
  | HTMLLinkElement;
export type HTMLLink = AnchorHTMLAttributes<HTMLLinkElement> &
  ButtonHTMLAttributes<HTMLButtonElement> &
  InputHTMLAttributes<HTMLInputElement>;

interface InnerLinkProps extends Omit<HTMLLink, 'size' | 'value'> {
  forwardedRef: Ref<LinkElement>;
  to?: string;
  component?: string;
  role?: string;
  className?: string;
  kind?: Kind;
  size?: Size;
  isDisabled?: boolean;
  onPress?(event: MouseEvent | TouchEvent): void;
}

export type LinkProps = Omit<InnerLinkProps, 'forwardedRef'>;

function Link({
  forwardedRef,
  children,
  to,
  component = to ? 'a' : 'button',
  type = component === 'button' || component === 'input' ? 'button' : undefined,
  role = to ? 'link' : 'button',
  className = styles.link,
  kind = Kind.Default,
  size, // No default size, instead inheriting font-size by default.
  isDisabled = false,
  onPress,
  onTouchStart: onTouchStartFromProps,
  onTouchMove: onTouchMoveFromProps,
  onTouchEnd: onTouchEndFromProps,
  onTouchCancel: onTouchCancelFromProps,
  onMouseDown: onMouseDownFromProps,
  onMouseUp: onMouseUpFromProps,
  onMouseLeave: onMouseLeaveFromProps,
  onClick: onClickFromProps,
  onFocus: onFocusFromProps,
  onBlur: onBlurFromProps,
  ...linkProps
}: InnerLinkProps): ReactElement {
  const [isActive, setIsActive] = useState(false);
  const [isFocused, setIsFocused] = useState(false);

  // Focus is suppressed after a touch or mouse event in order to prevent the
  // focused style on press. Focused style is still used when an item is focused
  // after keyboard events, like tab focus.

  const isFocusSuppressed = useRef(false);

  // Touch events trigger an immediate `onPress`, avoiding the click delay that
  // can happen in mobile browsers. If a press is being handled as part of a
  // touch event sequence, avoid also triggering `onPress` in `onClick`.

  const isClickSuppressed = useRef(false);
  const isTouchPress = useRef(false);

  //
  // Listeners

  const onTouchStart = useCallback(
    (event) => {
      isFocusSuppressed.current = true;
      isClickSuppressed.current = true;

      if (event.touches.length === 1) {
        isTouchPress.current = true;
        setIsActive(true);
      }

      onTouchStartFromProps?.(event);
    },
    [onTouchStartFromProps]
  );

  const onTouchMove = useCallback(
    (event) => {
      isTouchPress.current = false;
      setIsActive(false);

      onTouchMoveFromProps?.(event);
    },
    [onTouchMoveFromProps]
  );

  const onTouchEnd = useCallback(
    (event) => {
      setIsActive(false);

      if (isTouchPress.current) {
        isTouchPress.current = false;
        onPress?.(event);
      }

      onTouchEndFromProps?.(event);
    },
    [onPress, onTouchEndFromProps]
  );

  const onTouchCancel = useCallback(
    (event) => {
      isFocusSuppressed.current = false;
      isClickSuppressed.current = false;
      isTouchPress.current = false;
      setIsActive(false);

      onTouchCancelFromProps?.(event);
    },
    [onTouchCancelFromProps]
  );

  const onMouseDown = useCallback(
    (event) => {
      isFocusSuppressed.current = true;
      setIsActive(true);

      onMouseDownFromProps?.(event);
    },
    [onMouseDownFromProps]
  );

  const onMouseUp = useCallback(
    (event) => {
      setIsActive(false);

      onMouseUpFromProps?.(event);
    },
    [onMouseUpFromProps]
  );

  const onMouseLeave = useCallback(
    (event) => {
      isFocusSuppressed.current = false;
      setIsActive(false);

      onMouseLeaveFromProps?.(event);
    },
    [onMouseLeaveFromProps]
  );

  const onFocus = useCallback(
    (event) => {
      if (isFocusSuppressed.current) {
        isFocusSuppressed.current = false;
        return;
      }

      setIsFocused(true);
      onFocusFromProps?.(event);
    },
    [onFocusFromProps]
  );

  const onBlur = useCallback(
    (event) => {
      isFocusSuppressed.current = false;
      setIsFocused(false);

      onBlurFromProps?.(event);
    },
    [onBlurFromProps]
  );

  const onClick = useCallback(
    (event) => {
      isFocusSuppressed.current = false;
      setIsActive(false);

      if (isClickSuppressed.current) {
        isClickSuppressed.current = false;
      } else {
        onPress?.(event);
        onClickFromProps?.(event);
      }
    },
    [onPress, onClickFromProps]
  );

  const onWindowMouseMove = useCallback(() => {
    // If the element is focused without a mouse or touch interaction, like
    // when refocusing the browser window, suppress the focus style when the
    // mouse moves within the window.

    setIsFocused(false);
  }, []);

  //
  // Effects

  useEffect(() => {
    if (isFocused) {
      window.addEventListener('mousemove', onWindowMouseMove);
    }

    return () => {
      if (isFocused) {
        window.removeEventListener('mousemove', onWindowMouseMove);
      }
    };
  }, [isFocused, onWindowMouseMove]);

  //
  // Render

  const callbacks = isDisabled
    ? {}
    : {
        onTouchStart,
        onTouchMove,
        onTouchEnd,
        onTouchCancel,
        onMouseDown,
        onMouseUp,
        onMouseLeave,
        onFocus,
        onBlur,
        onClick,
      };

  return React.createElement(component, {
    ...linkProps,
    ...callbacks,
    ref: forwardedRef,
    children,
    type,
    href: to,
    role,
    className: classNames(
      className,
      styles[kind],
      size && styles[size],
      to && styles.isHrefLink,
      isActive && IS_ACTIVE,
      isFocused && IS_FOCUSED,
      isDisabled && IS_DISABLED
    ),
    disabled: isDisabled,
  });
}

export default React.forwardRef<LinkElement, LinkProps>((props, ref) => {
  return <Link {...props} forwardedRef={ref} />;
});
