import React, {
  ChangeEvent,
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
} from 'react';
import { useDispatch } from 'react-redux';
import t from 'app/js/utils/t';
import Select from 'app/js/v4/core/ui/forms/components/Select';
import {
  MediaGrabberDevice,
  MediaGrabberDeviceSourceType,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';

interface DVRSetupDeviceSourceSelectInputProps {
  device: MediaGrabberDevice;
  isDisabled: boolean;
}

interface SourceOption {
  value: string;
  label: string;
}

function getSourceTypeTitle(source: MediaGrabberDeviceSourceType): string {
  switch (source) {
    case MediaGrabberDeviceSourceType.Unknown:
      return t('Unknown');
    case MediaGrabberDeviceSourceType.Antenna:
      return t('Antenna');
    case MediaGrabberDeviceSourceType.Cable:
      return t('Cable');
    case MediaGrabberDeviceSourceType.Satellite:
      return t('Satellite');
  }
}

function getSourceOptions(
  sources: MediaGrabberDeviceSourceType[] | undefined,
  activeSource: MediaGrabberDeviceSourceType
): SourceOption[] {
  if (!sources) {
    return [
      {
        value: activeSource,
        label: getSourceTypeTitle(activeSource),
      },
    ];
  }

  return sources.map((source) => ({
    value: source,
    label: getSourceTypeTitle(source),
  }));
}

export default function DVRSetupDeviceSourceSelectInput({
  device,
  isDisabled,
}: DVRSetupDeviceSourceSelectInputProps): ReactElement {
  const dispatch = useDispatch();

  const { key, source, sources } = device;

  const sourceOptions = useMemo(
    () => getSourceOptions(sources, source),
    [sources, source]
  );

  //
  // Callbacks

  const onDeviceSourceSelect = useCallback(
    (event: ChangeEvent<HTMLSelectElement>) => {
      dispatch(
        dvrSetupSlice.actions.deviceSourceSelected({
          deviceKey: key,
          source: event.currentTarget.value as MediaGrabberDeviceSourceType,
        })
      );
    },
    [dispatch, key]
  );

  //
  // Effects

  useEffect(() => {
    if (sources && source === undefined) {
      dispatch(
        dvrSetupSlice.actions.deviceSourceSelected({
          deviceKey: key,
          source: sources[0],
        })
      );
    }
  }, [dispatch, key, sources, source]);

  //
  // Render

  return (
    <Select
      isDisabled={isDisabled || sourceOptions.length === 1}
      name="deviceSource"
      value={source}
      onChange={onDeviceSourceSelect}
    >
      {sourceOptions.map(({ value, label }, index) => (
        <option key={index} value={value}>
          {label}
        </option>
      ))}
    </Select>
  );
}
