import { createSelector, Selector } from 'reselect';
import { SettingsState } from 'app/js/v4/features/libraryManagement/libraryManagementTypes';
import selectLibraryManagement from 'app/js/v4/features/libraryManagement/selectors/selectLibraryManagement';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

const createMergeRecentlyAddedSettingSelector = (
  machineIdentifier: string
): Selector<LegacyRootState, SettingsState | undefined> => {
  return createSelector(
    selectLibraryManagement,
    (libraryManagement) => libraryManagement.settings[machineIdentifier]
  );
};

export default createMergeRecentlyAddedSettingSelector;
