/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import Spinner from 'app/js/views/v3/components/core/base/Spinner';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import getValidComponents from 'app/js/views/v3/helpers/getValidComponents';
import styles from './SidebarLink.css';

interface SidebarLinkProps {
  className?: string;
  children?: React.ReactNode;
  iconClassName?: string;
  titleClassName?: string;
  iconSrc?: string;
  iconComponent?: ((...args: any[]) => any) | string;
  iconNode?: React.ReactNode;
  title?: string;
  showSpinner?: boolean;
}

function SidebarLink(props: SidebarLinkProps) {
  const {
    className,
    children,
    iconClassName,
    titleClassName,
    iconSrc,
    iconComponent: Icon,
    iconNode,
    title,
    showSpinner,
    ...otherProps
  } = props;

  let sidebarIcon;
  if (iconSrc) {
    sidebarIcon = <img className={styles.iconImg} src={iconSrc} />;
  } else if (Icon) {
    sidebarIcon = <Icon />;
  } else if (iconNode) {
    sidebarIcon = iconNode;
  }

  return (
    <Link className={className} {...otherProps}>
      <div className={styles.container}>
        <div className={iconClassName}>
          {showSpinner ? <Spinner className={styles.spinner} /> : sidebarIcon}
        </div>
        {title ? <div className={titleClassName}>{title}</div> : null}
        {getValidComponents(children).length ? (
          <div className={styles.children}>{children}</div>
        ) : null}
      </div>
    </Link>
  );
}

SidebarLink.defaultProps = {
  className: styles.sidebarLink,
  iconClassName: styles.icon,
  titleClassName: styles.title,
};

export default SidebarLink;
