/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React from 'react';
import {
  IconFolderFilled,
  IconRadio,
  IconWatchTogether,
} from '@plex/react-chroma-icon';
import * as metadataIcons from 'app/js/utils/constants/metadataIcons';
import iconsByMediaType from 'app/js/utils/metadata/iconsByMediaType';
import styles from './MetadataIcon.css';

function getIconByName(name: any) {
  switch (name) {
    case metadataIcons.RADIO:
      return IconRadio;
    case metadataIcons.FOLDER:
      return IconFolderFilled;
    case metadataIcons.WATCH_TOGETHER:
      return IconWatchTogether;
  }

  return undefined;
}

type MetadataIconProps = {
  className?: string;
  type?: string;
  icon?: string;
  width: number;
  height: number;
  isFolder?: boolean;
};

function MetadataIcon(props: MetadataIconProps) {
  const { className, type, width, height, icon } = props;

  // @ts-expect-error ts-migrate(2538) FIXME: Type 'undefined' cannot be used as an index type.
  const Icon = getIconByName(icon) || iconsByMediaType[type];

  if (!Icon) {
    return null;
  }

  const iconStyle = {
    fontSize: Math.max(14, Math.min(Math.round(width / 4), 42)),
    lineHeight: `${height}px`,
  };

  const containerStyle = {
    width: `${width}px`,
    height: `${height}px`,
  };

  return (
    <div
      className={classNames(className, styles.container)}
      style={containerStyle}
    >
      <div style={iconStyle}>
        <Icon />
      </div>
    </div>
  );
}

export default MetadataIcon;
