import React, { ReactElement, useCallback, useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import * as features from 'app/js/utils/constants/user/features';
import t from 'app/js/utils/t';
import { useSourceContext } from 'app/js/v4/core/servers/components/SourceContextProvider';
import Checkbox from 'app/js/v4/core/ui/forms/components/Checkbox';
import libraryManagementSlice from 'app/js/v4/features/libraryManagement/libraryManagementSlice';
import createMergeRecentlyAddedSettingSelector from 'app/js/v4/features/libraryManagement/selectors/createMergeRecentlyAddedSettingSelector';
import styles from './LibrarySettings.css';

export default function LibrarySettings(): ReactElement | null {
  const dispatch = useDispatch();
  const [server] = useSourceContext();

  const canMergeRecentlyAdded = server.ownerFeatures?.includes(
    features.MERGE_RECENTLY_ADDED
  );
  const setting = useSelector(
    createMergeRecentlyAddedSettingSelector(server.machineIdentifier)
  );

  const hasSetting = useMemo(
    () => setting?.isMergeRecentlyAddedEnabled != null,
    [setting]
  );

  const isDisabled = useMemo(
    () => !setting || setting.isUpdating || setting.isPending || !hasSetting,
    [hasSetting, setting]
  );

  //
  // Effects

  useEffect(() => {
    dispatch(
      libraryManagementSlice.actions.settingsFetchRequested({
        server,
      })
    );
  }, [dispatch, server]);

  //
  // Callbacks

  const onSettingChanged = useCallback(() => {
    if (!canMergeRecentlyAdded || isDisabled) {
      return;
    }

    dispatch(
      libraryManagementSlice.actions.settingsUpdateRequested({
        server,
        isMergeRecentlyAddedEnabled: !setting?.isMergeRecentlyAddedEnabled,
      })
    );
  }, [
    canMergeRecentlyAdded,
    dispatch,
    isDisabled,
    server,
    setting?.isMergeRecentlyAddedEnabled,
  ]);

  //
  // Render

  return canMergeRecentlyAdded && hasSetting ? (
    <div className={styles.container}>
      <label htmlFor="recentlyAddedMerged">
        <Checkbox
          checked={setting?.isMergeRecentlyAddedEnabled}
          disabled={isDisabled}
          name="recentlyAddedMerged"
          onChange={onSettingChanged}
        />
        <span className={styles.label}>{t('Merge Recently Added items')}</span>
      </label>
      <div className={styles.info}>
        {t(
          'Recently Added items from the same media type will be combined together on Home for libraries Recommended to Home. Only content from libraries pinned to the app sidebar will appear in Merged Recently Added recommendations.'
        )}
      </div>
    </div>
  ) : null;
}
