/* eslint-plugin-disable @typescript-eslint */

import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';
import getDisplayName from 'app/js/views/v3/helpers/getDisplayName';

export default function connectWithMetrics(...args: any[]) {
  // @ts-expect-error ts-migrate(2556) FIXME: Expected 0-4 arguments, but got 0 or more.
  const connectHOC = connect(...args);

  return (Component: any) => {
    const Connector = connectHOC(Component);

    function ConnectWithMetrics(
      props: any,
      { metricsPage, metricsContext }: any
    ) {
      const { metricsContext: propsMetricsContext, ...otherProps } = props;

      return (
        <Connector
          metricsContext={propsMetricsContext || metricsContext}
          metricsPage={metricsPage}
          {...otherProps}
        />
      );
    }

    ConnectWithMetrics.displayName = `ConnectWithMetrics(${getDisplayName(
      Component
    )})`;

    ConnectWithMetrics.contextTypes = {
      metricsPage: PropTypes.string,
      metricsContext: PropTypes.string,
    };

    ConnectWithMetrics.propTypes = {
      ...Connector.propTypes,
      metricsContext: PropTypes.string,
    };

    return ConnectWithMetrics;
  };
}
