/* eslint-plugin-disable @typescript-eslint */

import getPixelRatio from 'app/js/utils/constants/getPixelRatio';
import posterPlaceholders from 'app/js/utils/posterPlaceholders';
import path from 'app/js/utils/url/path';
import BaseImage from 'app/js/views/images/BaseImage';

const CompositeImage = BaseImage.extend({
  //
  // Properties

  background: true,

  //
  // Init

  setup: function (options: any) {
    this.model = options.model;

    if (!options.noPlaceholder) {
      this.placeholderClass = posterPlaceholders.playlist;
    }

    const pixelRatio = getPixelRatio();

    const params = {
      width: Math.round((options.width || options.height || 256) * pixelRatio),
      height: Math.round((options.height || options.width || 256) * pixelRatio),
      rows: options.rows || 2,
      cols: options.columns || 2,
      border: options.border || 0,
      media: options.media || 'thumb',
      repeat: options.unique ? 0 : 1,
    };

    const url = this.model.get('composite');
    if (url) {
      this.url = this.model.server.resolveUrl(path.join(url, params), {
        appendToken: true,
      });
    }
  },
});

export default CompositeImage;
