/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import { settingsTypes } from 'app/js/models/subscription/MediaSettingModel';
import CheckInputGroup from 'app/js/views/v3/components/core/forms/CheckInputGroup';
import NumberInputGroup from 'app/js/views/v3/components/core/forms/NumberInputGroup';
import SelectInputGroup from 'app/js/views/v3/components/core/forms/SelectInputGroup';
import SliderInputGroup from 'app/js/views/v3/components/core/forms/SliderInputGroup';
import TextInputGroup from 'app/js/views/v3/components/core/forms/TextInputGroup';
import { playbackSpeed } from 'app/js/views/v3/helpers/valueModifiers';

function mapEnumValues(enumValues = []) {
  return enumValues.map(({ label, value }) => ({ [value]: label }));
}

const valueModifiers = {
  playbackSpeed,
};

type SettingInputGroupProps = {
  id?: string;
  type?: string;
  label?: string;
  summary?: string;
  initialValueModifier?: (...args: any[]) => any;
  value: string | number | boolean;
  enumValues?: any[];
  min?: number;
  max?: number;
  step?: number;
};

function SettingInputGroup(props: SettingInputGroupProps) {
  const {
    id,
    type,
    label,
    summary,
    value,
    initialValueModifier,
    enumValues,
    min,
    max,
    step,
  } = props;

  const inputProps = {
    name: id,
    helpText: summary,
    // @ts-expect-error ts-migrate(2722) FIXME: Cannot invoke an object which is possibly 'undefin... Remove this comment to see the full error message
    initialValue: initialValueModifier(value),
  };

  if (Array.isArray(enumValues)) {
    return (
      <SelectInputGroup
        labelText={label}
        {...inputProps}
        // @ts-expect-error ts-migrate(2322) FIXME: Type '{ values: { [x: number]: never; }[]; name: s... Remove this comment to see the full error message
        values={mapEnumValues(enumValues)}
      />
    );
  }

  switch (type) {
    case settingsTypes.TEXT:
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ name: string | undefined; helpText: string... Remove this comment to see the full error message
      return <TextInputGroup labelText={label} {...inputProps} />;

    case settingsTypes.BOOL:
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ name: string | undefined; helpText: string... Remove this comment to see the full error message
      return <CheckInputGroup checkboxText={label} {...inputProps} />;

    case settingsTypes.INT:
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ name: string | undefined; helpText: string... Remove this comment to see the full error message
      return <NumberInputGroup labelText={label} {...inputProps} />;

    case settingsTypes.DOUBLE:
      return (
        <SliderInputGroup
          // @ts-expect-error ts-migrate(2322) FIXME: Type '{ name: string | undefined; helpText: string... Remove this comment to see the full error message
          labelText={label}
          max={max}
          min={min}
          stepSize={step}
          // @ts-expect-error ts-migrate(2538) FIXME: Type 'undefined' cannot be used as an index type.
          valueModifier={valueModifiers[id]}
          {...inputProps}
        />
      );

    default:
      return null;
  }
}

SettingInputGroup.defaultProps = {
  initialValueModifier(value: any) {
    return value;
  },
};

export default SettingInputGroup;
