/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import t from 'app/js/utils/t';
import DashSeparator from 'app/js/views/v3/components/core/base/DashSeparator';
import LegendLabel from './LegendLabel';
import styles from './ChartTooltip.css';

type ChartTooltipProps = {
  active: boolean;
  label?: string | number;
  payload?: any[];
  series: any[];
  showTotal?: boolean;
  labelFormatter?: (...args: any[]) => any;
  valueFormatter: (...args: any[]) => any;
  onActive?: (...args: any[]) => any;
  onInactive?: (...args: any[]) => any;
};

class ChartTooltip extends Component<ChartTooltipProps> {
  static defaultProps = {
    showTotal: true,
  };

  //
  // Lifecycle

  // @ts-expect-error ts-migrate(6133) FIXME: 'prevState' is declared but its value is never rea... Remove this comment to see the full error message
  componentDidUpdate(prevProps: ChartTooltipProps, prevState: any) {
    const { active, onActive, onInactive } = this.props;

    const { active: prevActive } = prevProps;

    if (!prevActive && active && onActive) {
      onActive();
    } else if (prevActive && !active && onInactive) {
      onInactive();
    }
  }

  //
  // Render

  render() {
    const {
      active,
      label,
      payload,
      series,
      showTotal,
      labelFormatter,
      valueFormatter,
    } = this.props;

    if (!active || !payload) {
      return null;
    }

    const finalLabel = labelFormatter ? labelFormatter(label) : label;

    const total = showTotal
      ? payload.reduce((acc, entry) => acc + entry.value, 0)
      : null;

    return (
      <div className={styles.container}>
        <div className={styles.label}>{finalLabel}</div>

        <div className={styles.separator} />

        <div>
          <div className={styles.values}>
            {payload.map((segment) => {
              const title = series.find(
                (points) => points.dataKey === segment.dataKey
              ).title;

              return (
                <div key={segment.dataKey} className={styles.valueRow}>
                  <LegendLabel
                    className={styles.LegendLabel}
                    labelColor={segment.color}
                    title={title}
                  />
                  <DashSeparator />
                  {valueFormatter(segment.value)}
                </div>
              );
            })}
          </div>

          {showTotal ? (
            <>
              <div className={styles.separator} />

              <div className={styles.total}>
                {t('Total')}
                <DashSeparator />
                {valueFormatter(total)}
              </div>
            </>
          ) : null}
        </div>
      </div>
    );
  }
}

export default ChartTooltip;
