import { createSelector, OutputSelector } from 'reselect';
import { MediaGrabberDevice } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import selectSelectedDVRDevices from 'app/js/v4/features/dvrManagement/selectors/selectSelectedDVRDevices';
import { GuideChannel } from 'app/js/v4/features/livetv/liveTVTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

function createDeviceIndicesMatchingGuideChannelSelector(
  vcn?: GuideChannel['vcn']
): OutputSelector<
  LegacyRootState,
  number[],
  (devices: MediaGrabberDevice[]) => number[]
> {
  return createSelector(selectSelectedDVRDevices, (devices) => {
    if (!vcn) {
      return [];
    }

    return devices.reduce<number[]>((acc, device, index) => {
      const { ChannelMapping } = device;

      if (
        ChannelMapping?.find(
          (mapping) => mapping.enabled && mapping.lineupIdentifier === vcn
        )
      ) {
        acc.push(index);
      }

      return acc;
    }, []);
  });
}

export default createDeviceIndicesMatchingGuideChannelSelector;
