/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { PropsWithChildren, useCallback } from 'react';
import t from 'app/js/utils/t';
import IconSelected from 'app/glyphs/selected-560.svg';
import MenuItem from './MenuItem';
import styles from './SelectedMenuItem.css';

interface SelectedMenuItemProps {
  className?: string;
  menuLabelClassName?: string;
  value?: number | string;
  iconComponent?: React.ReactElement;
  isSelected?: boolean;
  selectedIconAriaLabel?: string;
  onSelect?: (...args: any[]) => any;
  onPress?: (...args: any[]) => any;
}

function SelectedMenuItem(props: PropsWithChildren<SelectedMenuItemProps>) {
  const {
    children,
    className = styles.menuItemContainer,
    menuLabelClassName = styles.menuLabel,
    iconComponent: Icon = IconSelected,
    isSelected,
    selectedIconAriaLabel = t('Selected'),
    value,
    onPress,
    onSelect,
    ...otherProps
  } = props;

  const onMenuItemPress = useCallback(
    (event: any) => {
      if (onSelect) {
        onSelect(value);
      }

      if (onPress) {
        onPress(event);
      }
    },
    [onPress, onSelect, value]
  );

  return (
    <MenuItem
      className={classNames(className, isSelected && styles.isSelected)}
      {...otherProps}
      onPress={onMenuItemPress}
    >
      <div className={styles.menuItemContainer}>
        <div className={menuLabelClassName}>{children}</div>

        {isSelected ? (
          <div className={styles.selectedIcon}>
            {/* @ts-expect-error ts-migrate(2604) FIXME: JSX element type 'Icon' does not have any construc... Remove this comment to see the full error message */}
            <Icon aria-label={selectedIconAriaLabel} />
          </div>
        ) : null}
      </div>
    </MenuItem>
  );
}

export default SelectedMenuItem;
