import { createSelector, Selector } from 'reselect';
import { HubListState } from 'app/js/v4/features/libraryManagement/libraryManagementTypes';
import selectLibraryManagement from 'app/js/v4/features/libraryManagement/selectors/selectLibraryManagement';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

function createLibraryHubsSelector(
  sourceURI: string
): Selector<LegacyRootState, HubListState | undefined> {
  return createSelector(
    selectLibraryManagement,
    (libraryManagement) => libraryManagement.hubs[sourceURI]
  );
}

export default createLibraryHubsSelector;
