import React, { ChangeEvent, ReactElement, useCallback, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import t from 'app/js/utils/t';
import FormLabel from 'app/js/v4/core/ui/forms/components/FormLabel';
import Select from 'app/js/v4/core/ui/forms/components/Select';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import styles from './DVRSetupCountryRegionSelectInput.css';

interface CountryRegionOption {
  value: string;
  label: string;
}

interface DVRSetupCountryRegionSelectInputProps {
  isDisabled?: boolean;
  hasError?: boolean;
}

export default function DVRSetupCountryRegionSelectInput({
  isDisabled,
  hasError,
}: DVRSetupCountryRegionSelectInputProps): ReactElement | null {
  const dispatch = useDispatch();
  const {
    selectedRegion,
    regions: { epgCountryRegions },
  } = useSelector(selectEPGSettings);

  const regionsPopulated = epgCountryRegions.length > 0;
  const regionOptions = useMemo(
    () =>
      epgCountryRegions.map<CountryRegionOption>((region) => ({
        value: region.key,
        label: region.title,
      })),
    [epgCountryRegions]
  );

  //
  // Callbacks

  const onCountryRegionSelect = useCallback(
    (event: ChangeEvent<HTMLSelectElement>) => {
      dispatch(
        epgSettingsSlice.actions.epgCountryRegionSelected({
          regionKey: event.currentTarget.value,
        })
      );
    },
    [dispatch]
  );

  //
  // Render

  return regionsPopulated ? (
    <>
      <FormLabel className={styles.label} hasError={hasError}>
        {t('Region')}
      </FormLabel>

      <Select
        hasError={hasError}
        isDisabled={isDisabled}
        name="regionSelect"
        value={selectedRegion?.key}
        onChange={onCountryRegionSelect}
      >
        <option value="">{t('Select Region')}</option>
        {regionOptions.map(({ value, label }) => (
          <option key={value} value={value}>
            {label}
          </option>
        ))}
      </Select>
    </>
  ) : null;
}
