import classNames from 'classnames';
import React, {
  FocusEvent,
  InputHTMLAttributes,
  ReactElement,
  Ref,
} from 'react';
import Size from 'app/js/v4/core/ui/types/Size';
import styles from './TextInput.css';

interface InnerTextInputProps
  extends Omit<InputHTMLAttributes<HTMLInputElement>, 'size' | 'value'> {
  forwardedRef: Ref<HTMLInputElement>;
  className?: string;
  errorClassName?: string;
  disabledClassName?: string;
  name: string;
  value?: string;
  size?: Size;
  isDisabled?: boolean;
  hasFocusOnMount?: boolean;
  hasError?: boolean;
  onFocus?(event: FocusEvent): void;
}

export type TextInputProps = Omit<InnerTextInputProps, 'forwardedRef'>;

function TextInput({
  forwardedRef,
  className = styles.input,
  errorClassName = styles.hasError,
  disabledClassName = styles.isDisabled,
  isDisabled = false,
  hasFocusOnMount = false,
  hasError = false,
  name,
  size = Size.Medium,
  onFocus,
  ...inputProps
}: InnerTextInputProps): ReactElement {
  return (
    <input
      ref={forwardedRef}
      autoFocus={hasFocusOnMount}
      className={classNames(
        className,
        styles[size],
        isDisabled && disabledClassName,
        hasError && errorClassName
      )}
      disabled={isDisabled}
      name={name}
      onFocus={onFocus}
      {...inputProps}
    />
  );
}

export default React.forwardRef<HTMLInputElement, TextInputProps>(
  (props, ref) => {
    return <TextInput {...props} forwardedRef={ref} />;
  }
);
