import { Ref, useEffect, useRef } from 'react';
import { ResizeObserver as Polyfill } from '@juggle/resize-observer';

const ResizeObserver = window.ResizeObserver || Polyfill;

function useResizeObserver<Element extends HTMLElement>(
  callback: ResizeObserverCallback
): Ref<Element> {
  const elRef = useRef<Element>(null);
  const observeRef = useRef<ResizeObserver | null>(null);

  useEffect(() => {
    if (elRef.current) {
      observeRef.current = new ResizeObserver(callback);
      observeRef.current.observe(elRef.current);
    }

    return () => {
      observeRef.current?.disconnect();
      observeRef.current = null;
    };
  }, [callback]);

  return elRef;
}

export default useResizeObserver;
