/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import {
  clearDVRSettingsSaveError,
  saveDVRSettings,
} from 'app/js/stores/actions/ui/dvrSetupActions';
import createDVRSelector from 'app/js/stores/selectors/livetv/createDVRSelector';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import DVRSettingsModalContent from './DVRSettingsModalContent';

function createMapStateToProps() {
  return createSelector(createDVRSelector(), (dvr) => {
    const { prefs, isSaving, saveError } = dvr;

    return {
      prefs: prefs.filter((pref: any) => !pref.hidden),
      isSaving,
      saveError,
    };
  });
}

type DVRSettingsModalContentConnectorProps = {
  serverEntityID: string;
  dvrID: number;
  onModalClose: (...args: any[]) => any;
  saveDVRSettings: (...args: any[]) => any;
  clearDVRSettingsSaveError: (...args: any[]) => any;
};

class DVRSettingsModalContentConnector extends Component<DVRSettingsModalContentConnectorProps> {
  //
  // Listeners

  onSubmit = (prefs: any) => {
    const { serverEntityID, dvrID } = this.props;

    scheduleImmediateUpdate(() => {
      this.props.saveDVRSettings({ serverEntityID, dvrID, prefs });
    });
  };

  onModalClose = () => {
    this.props.clearDVRSettingsSaveError({ dvrID: this.props.dvrID });
    this.props.onModalClose();
  };

  //
  // Render

  render() {
    return (
      // @ts-expect-error ts-migrate(2769) FIXME: No overload matches this call.
      <DVRSettingsModalContent
        {...this.props}
        onModalClose={this.onModalClose}
        onSubmit={this.onSubmit}
      />
    );
  }
}

export default connect(createMapStateToProps, {
  saveDVRSettings,
  clearDVRSettingsSaveError,
})(DVRSettingsModalContentConnector);
