/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { claimServer } from 'app/js/stores/actions/server/claimServerActions';
import createUserAttributeSelector from 'app/js/stores/selectors/user/createUserAttributeSelector';
import ClaimServerButton from './ClaimServerButton';

const mapStateToProps = createSelector(
  createUserAttributeSelector('authToken'),
  (authToken) => {
    return {
      authToken,
    };
  }
);

const mapDispatchToProps = {
  dispatchClaimServer: claimServer,
};

type ClaimServerSignedInButtonConnectorProps = {
  serverEntityID: string;
  isBusy: boolean;
  isDisabled: boolean;
  authToken: string;
  dispatchClaimServer: (...args: any[]) => any;
};

class ClaimServerSignedInButtonConnector extends Component<ClaimServerSignedInButtonConnectorProps> {
  //
  // Listeners

  onClaimServerPress = () => {
    this.props.dispatchClaimServer({
      serverEntityID: this.props.serverEntityID,
      authToken: this.props.authToken,
    });
  };

  //
  // Render

  render() {
    const { isBusy, isDisabled } = this.props;

    return (
      <ClaimServerButton
        isDisabled={isDisabled}
        isSpinning={isBusy}
        onPress={this.onClaimServerPress}
      />
    );
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
  // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'typeof ClaimServerSignedInButton... Remove this comment to see the full error message
)(ClaimServerSignedInButtonConnector);
