/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import t from 'app/js/utils/t';

interface SubtitlesStreamTitleProps {
  separatorComponent: React.ReactElement;
  stream?: {
    language?: string;
    codec?: string;
    key?: string;
    forced?: boolean;
  };
}

function SubtitlesStreamTitle(props: SubtitlesStreamTitleProps) {
  const { stream, separatorComponent: Separator } = props;

  const details = [];

  // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
  if (stream.codec) {
    // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
    details.push(stream.codec.toUpperCase());
  }

  // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
  if (stream.key) {
    // External (sidecar, not embedded) subs have a `key` property.
    details.push(t('External'));
  }

  // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
  if (stream.forced) {
    details.push(t('Forced'));
  }

  // @ts-expect-error ts-migrate(2532) FIXME: Object is possibly 'undefined'.
  const label = stream.language || t('Unknown');

  const finalDetails = details && details.length > 0 ? details.join(' ') : null;

  const ariaLabel = finalDetails
    ? t('{label}, {details}', { label, details: finalDetails })
    : label;

  return (
    <span aria-label={ariaLabel} data-testid="subtitlesStream">
      {label}
      {/* @ts-expect-error ts-migrate(2604) FIXME: JSX element type 'Separator' does not have any con... Remove this comment to see the full error message */}
      {finalDetails ? <Separator /> : null}
      {finalDetails}
    </span>
  );
}

export default SubtitlesStreamTitle;
